import {Injectable} from '@angular/core';
import {getIDType, QueryEntity} from '@datorama/akita';
import {AssessmentsState, AssessmentsStore} from './assessments.store';
import {Assessment, AssessmentSortFn, AssessmentStatus, createAssessment} from './assessment.model';
import {map} from 'rxjs/operators';
import sort from 'fast-sort';

@Injectable({ providedIn: 'root' })
export class AssessmentsQuery extends QueryEntity<AssessmentsState> {

  constructor(protected store: AssessmentsStore) {
    super(store);
  }

  selectManySorted(ids: getIDType<Assessment>[]) {
    return this.selectAll({
      filterBy: assessment => ids.includes(assessment.id),
    }).pipe(
      map(assessments => this.sortBy(assessments))
    );
  }

  protected sortBy(assessments: Assessment[]): Assessment[] {
    const stateUI = this.getValue().ui;
    const sortMethod = this.getSortMethod(stateUI.sort.by);

    return sort([...assessments])[stateUI.sort.order](sortMethod);
  }

  protected getSortMethod(by: keyof Assessment): keyof Assessment | AssessmentSortFn {
    switch (by) {
      case 'status_new':
        return this.sortByStatusNew;
      default:
        return by;
    }
  }

  sortByStatusNew(assessment: Assessment): number {
    const orderBy = [AssessmentStatus.NOT_ASSESSED, AssessmentStatus.ASSESSING, AssessmentStatus.ASSESSMENT_DONE];

    return 1 - orderBy.indexOf(assessment.status_new);
  }

}
