export class ExportStudentsResults {

  constructor(Config, FetchData) {
    'ngInject';

    this.Config = Config;
    this.FetchData = FetchData;
  }

  asFile(fileFormat, courseId, studentId = null) {
    return this.getExportKey(
      courseId,
      studentId,
    )
      .then(response => response.key)
      .then(key => window.open(`${this.Config.apiUrl}/v3/classes/export-grades/download/${fileFormat}/${key}/`, '_blank'));
  }

  toGoogleDrive(fileFormat, courseId, studentId = null) {
    let promise;

    if (studentId) {
      promise = this.FetchData.v3
        .exportStudentResultsOnGoogleDrive(fileFormat, courseId, studentId);
    } else {
      promise = this.FetchData.v3
        .exportAllStudentsResultsOnGoogleDrive(fileFormat, courseId);
    }

    return promise;
  }

  checkGoogleDriveExportStatus(exportId, callback) {
    chrome.runtime.sendMessage(
      {
        action: 'checkGDExportStatus',
        delay: 3000,
        method: 'GET',
        url: this.getCheckGoogleDriveExportStatusURL(exportId),
      },
      callback,
    );
  }

  getCheckGoogleDriveExportStatusURL(exportId) {
    return `${this.Config.apiUrl}/v3/classes/export-grades/drive/result/${exportId}/`;
  }

  getExportKey(courseId, studentId = null) {
    return studentId ?
      this.getStudentExportKeyId(courseId, studentId) : this.getAllStudentsExportKeyId(courseId);
  }

  getAllStudentsExportKeyId(courseId) {
    return this.FetchData.v3.getExportKeyForAllStudents(courseId);
  }

  getStudentExportKeyId(courseId, studentId) {
    return this.FetchData.v3.getExportKeyForStudent(courseId, '', studentId);
  }
}
