export class UserSetupCheck {
  constructor(UserInfo, UserQuery) {
    'ngInject';

    this.UserInfo = UserInfo;
    this.UserQuery = UserQuery;
  }

  async getUserEmailDomain() {
    let userEmailDomain = '';

    if (this.UserQuery.getValue().email) {
      userEmailDomain = await Promise.resolve(/@\S+/.exec(this.UserQuery.getValue().email)[0]);
    } else {
      userEmailDomain = await new Promise((resolve) => {
        chrome.runtime.sendMessage({ action: 'getUserInfo' }, (userInfo) => {
          resolve(/@\S+/.exec(userInfo.email)[0]);
        });
      });
    }

    return userEmailDomain;
  }

  validateUser() {
    return Promise.resolve(this.UserInfo.gdpr);
  }
}
