/**
 * @module Observer/Service
 * @description
 *   Handle Observer - init and connect/disconnect observer.
 * @param {Object} Courses Courses service instance
 * @param {Object} FetchData FetchData service instance
 * @param {Object} Path Path service instance
 */

/*@ngInject*/
function Observer(Courses, FetchData, Path) {
  return {
    instance: null,

    config: {
      attributes: false,
      childList: true,
      characterData: false,
    },

    /**
     * Connect or disconnect observer to watch click event on div[name="ok"]
     * @memberof module:Observer/Service
     * @param {Function} checkState Check state function from Custom Routing directive
     */
    init(checkState) {
      this.instance = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
          if (!mutation.removedNodes.length) {
            return;
          }

          if (mutation.removedNodes[0].innerHTML.indexOf('name="ok"') >= 0) {
            FetchData
              .v3
              .getCourseDefinitions(Path.getClassroomId())
              .then((res) => {
                Courses.list = res;

                checkState();
              });
          }
        });
      });
    },

    /**
     * Connect or disconnect observer to watch click event on div[name="ok"]
     * @memberof module:Observer/Service
     */
    check() {
      if (Path.isClassesListPage()) {
        this.instance.observe(document.body, this.config);
      } else {
        this.instance.disconnect();
      }
    },
  };
}

module.exports = Observer;
