import React, {memo} from "react";


const inactiveOpacity = 0.15;
const activeOpacity = 0.6;

const Icon = ({file, style = {}, active = true, overrideOpacity = null}) => {
  let src = file;
  let opacity = active ? activeOpacity : inactiveOpacity;
  if (overrideOpacity) {
    opacity = overrideOpacity;
  }

  if (!process.env.REACT_APP_STANDALONE) {
    let filename = file.slice(file.indexOf('-')+1);
    src = `chrome-extension://${chrome.runtime.id}/images/png/${filename}`;
  }
  return (
    <img src={src} style={{...style, opacity: opacity}}/>
  );
}

export default memo(Icon);
