export class UserSetupSettings {
  static UPDATE_STATE = 'userSetup.setup.update';
  static COMPLETE_STATE = 'userSetup.setup.complete';
  static QUERY_LIMIT = 100;

  constructor($state, FetchData, Path, UserSetupDialog) {
    'ngInject';

    this.$state = $state;
    this.FetchData = FetchData;
    this.Path = Path;
    this.UserSetupDialog = UserSetupDialog;
  }

  isUpdateState() {
    return this.$state.current.name === UserSetupSettings.UPDATE_STATE;
  }

  getUserPreferences(educationalSystems) {
    const userPreferences = {};

    return this.getCourseSetup()
      .then((preferences) => {
        userPreferences.educationalSystem = educationalSystems
          .find(educationalSystem => educationalSystem.id === preferences.educational_system_setup);

        return preferences;
      })
      .then(preferences => this.getSchoolById(preferences.school))
      .then((school) => {
        userPreferences.school = school;

        return userPreferences;
      });
  }

  saveUserPreferences(userPreferences) {
    const userPreferenceData = this.mapUserPreferencesToSaveData(userPreferences);
    return this.FetchData.updateUserPreferences(userPreferenceData);
  }

  updateUserPreferences(userPreferences) {
    const classroomId = this.Path.getClassroomId();
    const userPreferenceData = this.mapUserPreferencesToSaveData(userPreferences);

    return this.FetchData.updateCourseSetup(classroomId, userPreferenceData);
  }

  getCourseSetup() {
    const classRoomId = this.Path.getClassroomId();

    return this.FetchData.getCourseSetup(classRoomId);
  }

  getSchoolById(schoolId) {
    return this.FetchData.getSchoolById(schoolId);
  }

  getSchoolByQuery(query, userPreferences) {
    return this.FetchData
      .getSchool(query, userPreferences.educationalSystem.id, UserSetupSettings.QUERY_LIMIT);
  }

  mapUserPreferencesToSaveData(userPreferences) {
    return {
      educational_system_setup: userPreferences.educationalSystem.id,
      school: userPreferences.school.id,
    };
  }

  comparePreferences(preferences1, preferences2) {
    return this.isPreferenceFilled(preferences1) && this.isPreferenceFilled(preferences2) &&
      preferences1.educationalSystem.id === preferences2.educationalSystem.id &&
      preferences1.school.id === preferences2.school.id;
  }

  isPreferenceFilled(preference) {
    return !!preference && !!preference.educationalSystem && !!preference.school;
  }

  goToSetupComplete() {
    this.$state.go(UserSetupSettings.COMPLETE_STATE);
  }

  showAfterUpdateDialog() {
    this.UserSetupDialog.showSaveInfo();
  }

  hideAfterUpdateDialog() {
    this.UserSetupDialog.hide();
  }
}
