import { IQService } from 'angular';
import { RequestType } from '../../shared/enums/requestType.enum';
import { HttpResponse } from '../../shared/models/httpResponse.model';
import { IInterceptor } from '../../shared/models/interceptor.model';
import { Interceptor } from '../decorators/interceptor.decoreator';
import UtilsModule from '../utils.module';
import {InitService} from '../../../../../src/app/core/init/init.service';

@Interceptor({
  module: UtilsModule,
})
export class AgreementSeenInterceptor implements IInterceptor {
  private readonly urlRegex: RegExp;

  constructor(private $q: IQService, private $injector: angular.auto.IInjectorService) {
    'ngInject';

    this.urlRegex = /\/users\/agreement-seen\/?$/;
  }

  public response(response: HttpResponse): PromiseLike<HttpResponse> {
    if (response.config.method === RequestType.GET && this.urlRegex.test(response.config.url)) {
      this.setIsAgreementSeen(response.data as any);
    }

    return this.$q.resolve(response);
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> {
    if (this.urlRegex.test(response.config.url)) {
      this.setIsAgreementSeen(false);
    }

    return this.$q.reject(response);
  }

  private setIsAgreementSeen(value: boolean): void {
    const userInfo: any = this.$injector.get('UserInfo');
    const initService: InitService = this.$injector.get('InitService');

    userInfo.agreementSeen = value;
    initService.updateUserInfo({agreement_seen: false});
  }
}
