import angular, { IComponentController, IPromise, IQService, IRootScopeService } from 'angular';
import { Events } from '../../../../common/global-events';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import { ICurriculumsService } from '../../common/services/curriculums.service';
import { compareClassAndCurriculumSettings } from '../shares/helper.classroom';
import { showCurriculumDetailsDialog, showEditCurriculumDialog, showMakeCurriculumPrivateDialog, showMakeCurriculumPublicDialog, showMakeCurriculumPublicToAllStudyBeeUsersDialog, showRemoveCurriculumFromLibrary, showWrongCurriculumSetupDialog } from '../shares/helpers.dialog';
import TeacherCourseManagementModule from '../teacherCourseManagement.module';
import template from './cmCurriculumLibrary.tpl.html';
import {getTranslation} from '../../../../../../../react_src/i18n';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'cm-curriculum-library',
  template: template,
})
export class CmCurriculumLibraryComponent implements IComponentController {
  @Input()
  public curriculums: any;

  public libraryCurriculums: any;

  constructor(private $rootScope: IRootScopeService, private $q: IQService,
              private $mdDialog: angular.material.IDialogService, private $mdToast: angular.material.IToastService,
              private CurriculumsService: ICurriculumsService, private FetchData: any, private Path: any,
              private Mixpanel: { track: (msg: string) => void }) {
    'ngInject';

    this.curriculums = null;
    this.libraryCurriculums = {
      shared: [],
      public: [],
      private: [],
    };
  }

  public $onInit(): void {
    Object.assign(this.libraryCurriculums, this.curriculums);
  }

  public addCurriculumToClassroom(curriculum: any): void {
    let promise: IPromise<void> = this.$q.when();
    // compare only when there is more then 0 curriculums
    if (this.CurriculumsService.curriculums.length > 0) {
      promise = compareClassAndCurriculumSettings(
        this.$q, this.Path.getClassroomId(), curriculum, this.FetchData,
      );
    }

    promise.then((): IPromise<any> =>
      this.FetchData.v3.addCurriculumToClassroom(curriculum, this.Path.getClassroomId()),
    )
      .then((): void => {
        this.$rootScope.$emit(Events.UPDATED_COURSES);
        this.showAddedToClassroomConfirm(curriculum);
      })
      .catch((err: { name: string }): void => {
        if (err.name === 'WRONG_EDU_STAGE') {
          showWrongCurriculumSetupDialog(this.$mdDialog);
        }
      });
  }

  public showAddedToClassroomConfirm(curriculum: any): void {
    this.$mdToast.show({
      templateUrl: 'utils/views/toast.html',
      parent: angular.element(document.body),
      hideDelay: 3000,
      position: 'bottom right',
      controller: class ShowAddedToClassroomConfirm {
        public information: string;

        constructor() {
          'ngInject';
          this.information = `${curriculum.name} ${getTranslation('curriculumAddedToClassroom')}`;
        }
      },
      controllerAs: 'to',
    });
  }

  public showEditCurriculumDialog(curriculum: any, type: string): void {
    showEditCurriculumDialog(
      this.$mdDialog, this.FetchData, this.Path, angular.copy(curriculum),
      curriculum.educational_system.id, curriculum.educational_stage.id,
    )
      .then((curriculumData: any): IPromise<any> => this.FetchData.v3
        .updateCurriculumInLibrary(
          Object.assign(curriculum, curriculumData, {
            course: {
              educational_stage: curriculumData.educational_stage,
            },
          }),
        ),
      )
      .then((updatedCurriculum: any): void => {
        const curriculumOnList: any = this.libraryCurriculums[type]
          .find(cur => cur.id === updatedCurriculum.id);

        Object.assign(curriculumOnList, updatedCurriculum);
        this.sortCurriculumList(type);
      });
  }

  public showMakeCurriculumPublicInMySchoolDialog(curriculum: any, type: string): void {
    showMakeCurriculumPublicDialog(this.$mdDialog, curriculum.name)
      .then(() => this.FetchData.v3.makeCurriculumPublic(curriculum))
      .then((updatedCurriculum: any): void => {
        const curriculumOnListIndex: number = this.findCurriculumOnListIndex(type, curriculum);

        this.libraryCurriculums[type].splice(curriculumOnListIndex, 1);
        this.libraryCurriculums.public.push(updatedCurriculum);
        this.sortCurriculumList('public');
      })
      .finally((): void => this.Mixpanel.track('Share curriculum with school'));
  }

  public showMakeCurriculumPublicToAllStudyBeeUsersDialog(curriculum: any, type: string): void {
    showMakeCurriculumPublicToAllStudyBeeUsersDialog(this.$mdDialog, curriculum.name)
      .then(() => this.FetchData.v3.makeCurriculumGlobal(curriculum))
      .then((updatedCurriculum: any): void => {
        const curriculumOnListIndex: number = this.findCurriculumOnListIndex(type, curriculum);

        this.libraryCurriculums[type].splice(curriculumOnListIndex, 1);
        this.libraryCurriculums.shared.push(updatedCurriculum);
        this.sortCurriculumList('shared');
      })
      .finally((): void => this.Mixpanel.track('Share curriculum with all StudyBee users'));
  }

  public showMakeCurriculumPrivateDialog(curriculum: any, type: string): void {
    const translations: Object = type === 'public' ?
      {} :
      {
        content1: 'makeCurriculumPrivateFromGlobalInfoPart1',
        content2: 'makeCurriculumPrivateFromGlobalInfoPart2',
      };

    showMakeCurriculumPrivateDialog(this.$mdDialog, curriculum.name, translations)
      .then(() => this.FetchData.v3.makeCurriculumPrivate(curriculum))
      .then((updatedCurriculum: any): void => {
        const curriculumOnListIndex: number = this.findCurriculumOnListIndex(type, curriculum);

        this.libraryCurriculums[type].splice(curriculumOnListIndex, 1);
        this.libraryCurriculums.private.push(updatedCurriculum);
        this.sortCurriculumList('private');
      })
      .finally((): void => this.Mixpanel.track('Unshare curriculum'));
  }

  public removeCurriculumFromLibrary(curriculum: any, type: string): void {
    showRemoveCurriculumFromLibrary(this.$mdDialog, curriculum.name)
      .then(() => this.FetchData.v3.removeCurriculumFromLibrary(curriculum))
      .then(() => {
        const curriculumOnListIndex: number = this.findCurriculumOnListIndex(type, curriculum);

        this.libraryCurriculums[type].splice(curriculumOnListIndex, 1);
      });
  }

  public openCurriculumDetails(curriculum: any): void {
    this.$mdDialog.cancel();
    showCurriculumDetailsDialog(this.$mdDialog, curriculum, this.FetchData);
  }

  public sortCurriculumList(curriculumType: string) {
    this.libraryCurriculums[curriculumType].sort((a: any, b: any): number =>
      a.name.toLowerCase().localeCompare(b.name.toLowerCase()),
    );
  }

  public findCurriculumOnListIndex(curriculumType: string, curriculum: any): number {
    return this.libraryCurriculums[curriculumType].findIndex(cur => cur.id === curriculum.id);
  }
}
