import { getTranslation } from "../i18n";
import { ReactComponent as SortIcon } from "../images/sort.svg";
import { colors } from "../theme";
import {GlobalContext} from '../utils';
import React, {useContext} from 'react';
import Tooltip from "./tooltip";

const SortStudentComponent = (props) => {
  const {style = {}} = props;
  const context = useContext(GlobalContext);
  const {sortingMethod, setSortingMethod} = context;

  const sortName = getTranslation(
    sortingMethod === "firstName"
      ? "studentsActionMenuSortByLastNameAsc"
      : "studentsActionMenuSortByFirstNameAsc"
  ).toLowerCase();

  return (
    <Tooltip
      title={`${getTranslation("studentsActionMenuSortByLabel")} ${sortName}`}
    >
      <div style={style}>
        <SortIcon
          style={{
            height: "25px",
            cursor: "pointer",
            transform: `rotate(${sortingMethod === "firstName" ? 0 : 180}deg)`,
            border: "0px solid blue",
          }}
          fill={colors.darkGrey}
          onClick={async () => {
            const newSort = sortingMethod === "firstName" ? "lastName" : "firstName";
            setSortingMethod(newSort)
          }}
        />
      </div>
    </Tooltip>
  );
};

export default SortStudentComponent;
