module.exports = function() {
  /*@ngInject*/
  function controller($scope, $element, Debounce) {
    var vm = this;

    vm.gradeSystem = $scope.gradeSystem;
    vm.savingMethod = $scope.savingMethod;
    vm.assignment = $scope.assignment;
    vm.counterGrades = vm.assignment.counter_grades;

    if ($element.data('disabled')) {
      vm.counterReadonly = true;
    }

    /**
     * Init function
     */
    vm.init = function() {
      vm.counterGrades.forEach(function(elem) {
        if (elem.points === null) { elem.points = '-'; }
      });
    };

    /**
     * Parse input value - if it's not integer set value to dash
     * @param {Number} index
     */
    vm.parseInput = function(index) {
      if (vm.isOnlyNumber(index)) {
        vm.counterGrades[index].points = parseInt(vm.counterGrades[index].points);
      } else {
        vm.counterGrades[index].points = '-';
      }
    };

    /**
     * Check if input value contains only number
     * @param {Number} index
     * @return {Boolean}
     */
    vm.isOnlyNumber = function(index) {
      var validTest = /^[0-9]{1,10}$/;

      return validTest.test(vm.counterGrades[index].points);
    };

    /**
     * Check if value is valid (bigger than zero and lower than max)
     * @param {Number} index
     * @param {String} direction
     * @return {Boolean} valid
     */
    vm.validation = function(index, direction) {
      var valid = true;

      switch (direction) {
        case 'up':
          if (vm.counterGrades[index].points >= vm.counterGrades[index].max) { valid = false; }
          break;
        case 'down':
          if (vm.counterGrades[index].points === '-') { valid = false; }
          break;
        case 'direct':
          if (vm.counterGrades[index].points >= vm.counterGrades[index].max) { vm.counterGrades[index].points = vm.counterGrades[index].max; }
          if (vm.counterGrades[index].points <= 0) { vm.counterGrades[index].points = 0; }
          break;
      }

      return valid;
    };

    /**
     * Handle increase counter value
     * @param {Number} index
     */
    vm.increase = function(index) {
      if (!vm.validation(index, 'up')) {
        return;
      }

      if (vm.counterGrades[index].points === '-') {
        vm.counterGrades[index].points = 0;
      } else {
        vm.counterGrades[index].points = parseInt(vm.counterGrades[index].points) + 1;
      }

      vm.sendToAPI();
    };

    /**
     * Handle decrease counter value
     * @param {Number} index
     */
    vm.decrease = function(index) {
      if (!vm.validation(index, 'down')) {
        return;
      }

      if (vm.counterGrades[index].points === 0) {
        vm.counterGrades[index].points = '-';
      } else {
        vm.counterGrades[index].points = parseInt(vm.counterGrades[index].points) - 1;
      }

      vm.sendToAPI();
    };

    /**
     * Handle direct value change
     * @param {Number} index
     */
    vm.directChange = function(index) {
      vm.parseInput(index);
      vm.validation(index, 'direct');

      vm.sendToAPI();
    };

    /**
     * Handle click on input
     * @param {Number} index
     */
    vm.selectInput = function($event) {
      if (vm.counterReadonly) {
        return;
      }

      $event.currentTarget.select();
    };

    /**
     * Send information to the API
     */
    vm.sendToAPI = Debounce(function() {
      if (vm.counterReadonly) {
        return;
      }

      vm.savingMethod([vm.assignment]);
    }, 500);

    vm.init();
  }

  return {
    restrict: 'E',
    scope: {
      assignment: '=',
      gradeSystem: '=',
      savingMethod: '='
    },
    templateUrl: 'utils/views/counters.html',
    controller: controller,
    controllerAs: 'vm'
  };
};
