/**
 * @module Courses/Service
 * @description
 *   Keep list of courses and allow to check if course exist
 */

export class Courses {
  constructor(FetchData) {
    'ngInject';

    this.FetchData = FetchData;
    this.list = [];
  }

  getCourseUsingDriveId(fileId, alternateLinkId) {
    return this.FetchData.getDriveCourse(fileId, alternateLinkId)
      .then((course) => {
        this.list.push(course);
        return course;
      });
  }

  /**
   *
   * @param courseId
   */
  getCourse(courseId) {
    return this.FetchData.getClassroomCourse(courseId)
      .then((course) => {
        this.list.push(course);
        return course;
      });
  }

  /**
   * Check if course exist on the courses list
   * @param {Number} courseId Selected course ID
   * @memberof module:Courses/Service
   */
  isCourseExist(courseId) {
    return !!this.list.find(course => !!course && course.id === courseId);
  }

  /**
   * Check if course has at least one course definition
   * @param {Number} courseId Selected course ID
   * @memberof module:Courses/Service
   */
  hasCourseDefinition(courseId) {
    let valid = false;

    this.list.forEach((courseElem) => {
      if (courseElem.id !== courseId) {
        return;
      }

      valid = courseElem.has_course_definitions;
    });

    return valid;
  }

  updateCourseDefinition(courseId, newValue) {
    this.list.forEach((courseElem) => {
      if (courseElem.id !== courseId) {
        return;
      }

      courseElem.has_course_definitions = newValue;
    });
  }
}
