import React, {useEffect, useState, memo} from 'react';
import {TextField} from '@material-ui/core/';
import {withStyles} from '@material-ui/core/styles';
import {colors, fonts} from '../theme';
import PublishIcon from './publishIcon';

// MuiInputBase-input MuiInput-input
const CssTextField = withStyles({
  root: {
    '& .MuiInputBase-input': {
      ...fonts.MEDIUM,
    },
    '& .MuiFormLabel-root': {
      ...fonts.SMALL,
    },
    '& label.Mui-focused': {
      color: colors.green,
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: colors.green,
    },
  },
})(TextField);

const FeedbackInput = (props) => {
  const {
    text: initialText,
    placeholder,
    hideEye,
    onTextChange,
    readonly = false,
    published = false,
    onPublish,
    id,
    assessment,
    style,
    helperText,
    type,
    warningText,
    multiEdit = false,
  } = props;

  const [text, setText] = useState(initialText);
  const [focused, setFocused] = useState(false);

  useEffect(() => {
    setText(initialText);
  }, [initialText]);

  const textChanged = ({target}) => {
    const {value: txt} = target;
    setText(txt);
    onTextChange(txt, id, assessment);
  };

  const onPublishChanged = (value) => {
    if (onPublish) {
      onPublish(id, value, assessment);
    }
  };

  return (
    <div
      style={{
        display: 'flex',
        marginBottom: '10px',
        marginTop: '10px',
        flexDirection: 'column',
        alignItems: 'flex-start',
        ...style,

      }}
    >
      <div style={{display: 'flex', width: '100%', alignItems: 'center'}}>
        <CssTextField
          type={type}
          onMouseEnter={() => setFocused(true)}
          onMouseLeave={() => setFocused(false)}
          label={placeholder}
          style={{width: '100%'}}
          onChange={textChanged}
          value={text || ''}
          disabled={readonly}
          multiline
          helperText={
            <span
              style={{
                ...fonts.TINY,
                color: colors.darkGrey,
                position: 'absolute',
                right: 0,
              }}
            >
            {focused ? helperText : helperText}
          </span>
          }
        />
        {!hideEye && (
          <PublishIcon
            size={14}
            multiEdit={multiEdit} published={published}
            onClick={onPublishChanged}/>
        )}
      </div>
      {warningText && text?.length > 0 &&
      <span style={{...fonts.SMALL, color: colors.red}}>{warningText}</span>}
    </div>
  );
};

export default memo(FeedbackInput);
