import { IComponentController, IRootScopeService } from 'angular';
import { StateService } from '@uirouter/core';
import { Events } from '../../../../common/global-events';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import StudentCourseManagementModule from '../studentCourseManagement.module';
import style from './cmStudentCourseDetails.scss';
import template from './cmStudentCourseDetails.tpl.html';

@Component({
  module: StudentCourseManagementModule,
  selector: 'cm-student-course-details',
  template: template,
  style: style,
})
export class CmStudentCourseDetailsComponent implements IComponentController {
  @Input()
  public course: any;

  constructor(private $rootScope: IRootScopeService, private $state: StateService) {
    'ngInject';

    this.course = null;
  }

  public closeCourseView(): void {
    this.$state.go('studentCourseManagement', { course: null });
    this.$rootScope.$emit(Events.CLOSE_COURSE_DETAILS_VIEW);
  }
}
