export const AppRouting = ($stateProvider) => {
  'ngInject';

  $stateProvider
    .state('loading', {
      templateUrl: 'utils/views/loading_state.html',
    })
    .state('not_logged_user', {
      templateUrl: 'utils/views/not_logged_state.html',
    })
    .state('wrong_user', {
      templateUrl: 'utils/views/wrong_user_state.html',
    })
    .state('not_configured', {
      themeClass: 'sb-theme-green',
      templateUrl: 'utils/views/not_configured_state.html',
    })
    .state('wrongGoogleDriveFile', {
      themeClass: 'sb-theme-green',
      templateUrl: 'utils/views/wrong_google_drive_file.html',
    })
    .state('noLearningObjectives', {
      themeClass: 'sb-theme-green',
      templateUrl: 'utils/views/noLearningObjectives.html',
    });
};
