import { UnbindableCtrlBase } from '../../../common/base-classes';
import { GroupedVisibilitySettings } from '../../../shared/models/groupedVisibilitySettings.model.ts';
import { GroupedVisibilityDialogType } from '../../../shared/enums/groupedVisibilityDialogType.enum.ts';

export class TeacherCourseProgressCtrl extends UnbindableCtrlBase {
  constructor(TeacherCourseProgress, GroupedVisibilityService, GroupedVisibilityDialogService, StudybeeHandle, NavigationHistory) {
    'ngInject';

    super();
    this.TeacherCourseProgress = TeacherCourseProgress;
    this.GroupedVisibilityService = GroupedVisibilityService;
    this.GroupedVisibilityDialogService = GroupedVisibilityDialogService;
    this.StudybeeHandle = StudybeeHandle;
    this.NavigationHistory = NavigationHistory;

    // variables injected from component's bindings
    this.students = [];
    this.courses = [];
    this.student = null;
    this.courseId = null;
  }

  $onInit() {
    this.courseId = this.TeacherCourseProgress.getCurrentCourseId();
    if (this.TeacherCourseProgress.isLastStateSavedExists()) {
      this.TeacherCourseProgress.navigateToLasStateSaved();
    } else if (this.students.length) {
      this.TeacherCourseProgress.navigateToNoStudentsSelected();
    }

    this.initEvents();

    // use map students to change the user's status more efficient
    this.studentsMap = this.TeacherCourseProgress.convertStudentsArrayToMap(this.students);
  }

  initEvents() {
    this.boundEvents.push(
      this.TeacherCourseProgress.addListenerUpdateUserStatusesForCoursesAndObjectives(
        () => {
          this.TeacherCourseProgress
            .updateWarningsForStudents(this.courseId, this.studentsMap)
            .then((studentsMap) => (this.studentsMap = studentsMap));
        },
      ),
    );
  }

  exportStudentsResultsToGoogleDrive(fileType) {
    this.TeacherCourseProgress.exportStudentsResultsToGoogleDrive(fileType, this.courseId);
  }

  shareAssessmentsWithColleagues() {
    this.TeacherCourseProgress.shareAssessmentsWithColleagues();
  }

  hasStudents() {
    return !!this.students.length;
  }

  setVisibility() {
    const settings = new GroupedVisibilitySettings(GroupedVisibilityDialogType.StudentProgressView);
    settings.students = [...this.students];
    settings.courseDefinitions = [...this.courses];

    this.GroupedVisibilityDialogService.show(settings).then((groupedVisibility) => {
      this.GroupedVisibilityService.patchCourseAssessmentsGroupedVisibility(groupedVisibility)
        .then(() => this.TeacherCourseProgress.reloadCurrentView());
    });
  }
}
