import { Observable, throwError } from 'rxjs';
import {
  retryWhen, delay, take, concatMap,
} from 'rxjs/operators';

/**
 * @module Path/Service
 * @description
 *   Allow to get Path and check current or previous page.
 * @param {Object} MainMenu Main Menu service instance
 */

/*@ngInject*/
function Path($window, MainMenu, $timeout) {
  return {
    classroomNode: null,
    driveNode: null,
    oldPath: null,
    oldCourseId: null,

    /**
     * Get an entire path
     * @memberof module:Path/Service
     * @return {String}
     */
    get path() {
      return $window.location.pathname;
    },

    /**
     * Get path array
     * @memberof module:Path/Service
     * @return {Array}
     */
    getPathArray() {
      return this.path.split('/');
    },

    /**
     * Get course ID
     * @memberof module:Path/Service
     * @return {Number}
     */
    getClassroomId() {
      const result = /[\da-zA-Z]{10,}/g.exec(this.path);

      return result ? result[0] : null;
    },

    /**
     * Get assignment ID
     * @memberof module:Path/Service
     * @return {Number}
     */
    getAssignmentId() {
      const pathArray = this.getPathArray();
      let assignmentId = pathArray[pathArray.indexOf('a') + 1];
      if (!assignmentId && MainMenu.selectedAssignment) {
        assignmentId = MainMenu.selectedAssignment.id;
      }
      return assignmentId;
    },

    /**
     * This is only temporary solution for bad design patterns
     * @returns {*}
     */
    getAssignmentAlternateLinkId(isDrive = false) {
      if (isDrive) {
        const result = /\/([\da-zA-Z]{5,})#?$/g.exec(this.path);

        return result ? result[1] : null;
      }
      const pathArray = this.getPathArray();
      let altLink = pathArray[pathArray.indexOf('a') + 1];
      if (!altLink) {
        altLink = pathArray[pathArray.indexOf('tc') + 1];
      }
      return altLink;
    },

    /**
     * Get student ID
     * @memberof module:Path/Service
     * @return {Number}
     */
    getStudentId() {
      const pathArray = this.getPathArray();
      const studentId = pathArray[pathArray.indexOf('student') + 1];
      return studentId;
    },

    /**
     * Get file ID
     * @memberof module:Path/Service
     * @return {Number}
     */
    getFileId(retryTimes = 1) {
      const getPathFromButton = () => {
        let documentId = null;
        const $button = $('[role=main] div[data-url^=https]');
        const URLRegex = /^(https?:\/\/)(www.)?drive.google.com\/open\?id=[\da-zA-Z-_]*&?/gm;

        if ($button.length && URLRegex.test(`${$button.data('url')}`)) {
          const url = new URL($button.data('url'));

          documentId = url.searchParams.get('id');
        } else {
          const links = Array.from(
            document.querySelectorAll('[role=main] [role=menuitem] [role=presentation] a[href]'),
          );
          const link = links.find((link) => URLRegex.test(link.href));

          if (link) {
            const url = new URL(link.href);

            documentId = url.searchParams.get('id');
          }
        }

        return documentId;
      };

      const getID = (observer) => {
        let pathArray = this.getPathArray();
        let fileId = pathArray[pathArray.indexOf('d') + 1];

        if (!fileId) {
          const iframe = $('iframe#material-iframe:last');

          if (iframe.length && iframe.attr('src')) {
            pathArray = iframe.attr('src').split('/');
            fileId = pathArray.includes('d') ? pathArray[pathArray.indexOf('d') + 1] : '';
          } else {
            fileId = getPathFromButton();
          }
        }

        if (fileId) {
          observer.next(fileId);
        } else {
          observer.error(fileId);
        }
      };

      const result = new Observable(getID);

      result.pipe(
        retryWhen((errors) => errors.pipe(
          delay(150),
          take(retryTimes),
          concatMap(throwError),
        )),
      );

      return new Promise((resolve, reject) => {
        result.subscribe(resolve, reject);
      });
    },

    /**
     * Is classroom instance
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isClassroomInstance() {
      let valid = false;
      if (this.classroomNode) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is drive instance
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isDriveInstance() {
      let valid = false;
      if (this.driveNode) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is classes list page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isClassesListPage() {
      return this.getPathArray().includes('h');
    },

    /**
     * Is settings page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isSettingsPage() {
      return this.getPathArray().includes('s');
    },

    /**
     * Is work page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isWorkPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (
        (pathArray.indexOf('ta') >= 0 || pathArray.indexOf('a') >= 0)
        && pathArray.indexOf('submissions') < 0
        && pathArray.indexOf('details') < 0
        && pathArray.indexOf('file') < 0
      ) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is calendar page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isCalendarPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('calendar') >= 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is stream page
     * Pattern: /c/[classId]
     * classId: A-Za-z0-9
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isStreamPage() {
      return /\/c\/[\w]+\/?$/g.test(this.path) || this.isClassworkPage();
    },

    /**
     * Is classwork page
     * Pattern: /w/[classID]/t/all
     * classId: A-Za-z0-9
     * @memberof module:Path/Service
     * @returns {Boolean}
     */
    isClassworkPage() {
      return /\/w\/[\w]+\/t\/all/g.test(this.path);
    },

    /**
     * Is students page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isStudentsPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('r') >= 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is classmates page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isClassmatesPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('r') >= 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is about page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isAboutPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('o') >= 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is student work page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isStudentWorkPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('submissions') >= 0 && pathArray.indexOf('sa') < 0) {
        valid = true;
      }
      if (pathArray.indexOf('tc') > 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Is instructions page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    isInstructionsPage() {
      const pathArray = this.getPathArray();
      let valid = false;
      if (pathArray.indexOf('details') >= 0 && pathArray.indexOf('pa') < 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Was student work page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    wasStudentWorkPage() {
      let valid = false;

      if (!this.oldPath || this.oldPath === this.path) {
        return valid;
      }

      const pathArray = this.oldPath.split('/');
      if (pathArray.indexOf('submissions') >= 0) {
        valid = true;
      }
      return valid;
    },

    /**
     * Was instructions page
     * @memberof module:Path/Service
     * @return {Boolean}
     */
    wasInstructionsPage() {
      let valid = false;

      if (!this.oldPath) {
        return valid;
      }

      const pathArray = this.oldPath.split('/');
      if (pathArray.indexOf('details') >= 0) {
        valid = true;
      }
      return valid;
    },

    isCourseChanged() {
      return this.oldCourseId && this.oldCourseId !== this.getClassroomId();
    },

    /**
     * Set old path
     * @memberof module:Path/Service
     * @param {String} oldPath Selected old path
     */
    setOldPath(oldPath) {
      this.oldPath = oldPath;
    },

    /**
     * Set old course id
     * @memberof module:Path/Service
     * @param {Number} oldCourseId Selected old course ID
     */
    setOldCourseId(oldCourseId) {
      this.oldCourseId = oldCourseId;
    },
  };
}

module.exports = Path;
