import { IRootScopeService } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { IObserver, IObserverNext, IUnsubscribe, Observer } from '../helpers/observer.service';
import { ISubject, Subject } from '../helpers/subject.service';
import SharedServicesModule from '../services.module';

export type HashChange = { previous: string, current: string };

export interface IHashObserverService {
  onNext(listener: IObserverNext<HashChange>): IUnsubscribe;
}

@Service({
  module: SharedServicesModule,
  name: 'HashObserverService',
})
export class HashObserverService implements IHashObserverService {
  public static DELAY: number = 500;

  private readonly subject: ISubject<HashChange>;
  private intervalId: number;
  private previous: string;

  constructor(private $rootScope: IRootScopeService) {
    'ngInject';

    this.subject = new Subject<HashChange>();
    this.previous = window.location.hash;
  }

  public onNext(listener: IObserverNext<HashChange>): IUnsubscribe {
    if (listener instanceof Function) {
      if (!this.intervalId) {
        this.setObserver();
        this.$rootScope.$on('$destroy', this.removeObserver.bind(this));
      }

      const observer: IObserver<HashChange> = new Observer<HashChange>(listener);

      return this.subject.subscribe(observer);
    }
  }

  private setObserver(): void {
    this.intervalId = window.setInterval(() => {
      const current: string = window.location.hash;

      if (this.previous !== current) {
        this.subject.next({ previous: this.previous, current });
        this.previous = current;
      }
    }, HashObserverService.DELAY);
  }

  private removeObserver(): void {
    window.clearInterval(this.intervalId);
  }
}
