import React, {useContext, useEffect, useRef, useState} from "react";
import {colors, fonts} from "../theme";
import {getGradeSystemValues, GlobalContext} from "../utils";
import CourseObjectiveAssessmentItem from "./courseObjectiveAssessmentItem";
import {getTranslation} from "../i18n";
import {InputBase, MenuItem, Select, withStyles} from "@material-ui/core";

const BootstrapInput = withStyles(() => ({
  input: {
    ...fonts.MEDIUM,
    fontSize: "16px",
    height: "34px",
    width: "0px",
    alignItems: "flex-end",
    display: "flex",
    borderBottom: `1px solid ${colors.borderColor}`,
    padding: "0px",
    margin: "0px",
  },
}))(InputBase);

const StyledSelect = withStyles(() => ({
  root: {
    width: "0px",
    padding: "0px",
    margin: "0px",
    border: "0px solid",
    borderLeft: `1px solid ${colors.borderColor}`,
  },
  input: {
    color: "transparent",
  },
  select: {
    paddingRight: "9px !important",
    color: "transparent",
  },
}))(Select);

const fields = `
   name
    courses {
      id
      name
      gradeSystem{
        name
        is_main
        is_final
        grades{
          id
          name
          is_passed
          color
          label
        }
      }
      objectives {
        id
        name
        description
        knowledgeRequirements {
          description
          grade_id
        }
      }
    }
     students {
      id
      first_name
      last_name
      assignments {
        gradeSystem{
          id
          name
          is_counter
          grades{
            id
            label
            order
            color
          }
        }
        objectiveAssessments {
        student_id
          objective_id
          grade {
            id
            label
            order
            color
          }
        }
        assignments {
          gradeSystem{
            id
            name
            is_counter
            grades{
              id
              label
              order
              color
            }
          }
          objectiveAssessments {
            student_id
            objective_id
            grade {
              id
              label
              order
              color
            }
          }
        }
      }
      courseAssessments {
        courseId
        courseName
        courseObjectiveAssessments {
          name
          id
          grade_published
          grade_id
          objective_id
          active
          grade_last_edit
          feedback
          feedback_published
          feedback_last_edit
          private_note
          private_note_last_edit
        }
      }
    }
  `;

const SIZE_THRESHOLD = 500;

const CourseObjectiveAssessmentCard = (props) => {
  const {studentId, courseId, onObjectiveSelected, multiEdit, onSave} = props;
  const context = useContext(GlobalContext);
  const {Api, networkTraffic} = context;
  const [course, setCourse] = useState();
  const [students, setStudents] = useState();
  const [saving, setSaving] = useState(false);
  const [proficiencyProfiles, setProficiencyProfiles] = useState(false);
  const [selectedGradeSystem, setSelectedGradeSystem] = useState(0);
  const gradeSystemsUsed = useRef([]);
  const ref = useRef();
  const [width, setWidth] = useState();
  const changedObjectiveAssessments = useRef({});
  const multieditRef = useRef(false);

  let assessment = {};

  const getObjectives = async () => {
    const response = await Api.getClassroom(fields);
    const classroom = response?.data?.classroom;
    const allAssignments = classroom?.students.find(s => s.id === studentId)?.assignments
      ?.map((a) => [a, ...(a.assignments || [])])
      ?.flat();

    gradeSystemsUsed.current = allAssignments?.reduce((acc, ass) => {
      if (
        ass?.gradeSystem?.id &&
        !acc.find((a) => a?.id === ass?.gradeSystem?.id) &&
        !ass.gradeSystem?.is_counter
      ) {
        acc.push(ass?.gradeSystem);
      }
      return acc;
    }, []);

    const prProfile = gradeSystemsUsed.current.map((grs) => {
      const assignments = allAssignments.filter(
        (a) => a?.gradeSystem?.id === grs.id
      );
      const prProfile = assignments?.reduce((acc, a) => {
        a?.objectiveAssessments?.filter(o => o.student_id === studentId)?.forEach((oa) => {
          if (!acc.hasOwnProperty(oa.objective_id)) {
            acc[oa.objective_id] = grs.grades.map((g) => ({
              quantity: 0,
              grade: g,
            }));
            acc[oa.objective_id].total = 0;
          }
          if (oa.grade) {
            acc[oa.objective_id].total += 1;
            if (oa.grade.order === 0) {
              const newQuantity = acc[oa.objective_id][0].quantity + 1;
              acc[oa.objective_id][0] = {
                ...acc[oa.objective_id][0],
                quantity: newQuantity,
              };
            }
            for (let x = oa.grade.order; x >= 1; x--) {
              const newQuantity = acc[oa.objective_id][x].quantity + 1;
              acc[oa.objective_id][x] = {
                ...acc[oa.objective_id][x],
                quantity: newQuantity,
              };
            }
          }
        });
        return acc;
      }, {});
      return {
        gradeSystemId: grs.id,
        gradeSystemName: grs.name,
        objectives: prProfile,
      };
    });
    setProficiencyProfiles(prProfile);

    setCourse(classroom?.courses.find((c) => c.id === courseId));
    if (!multieditRef.current) {
      setStudents(classroom?.students);
    }
  };

  // A little bit hacky, reload classroom if something has been saved
  useEffect(() => {
    if (networkTraffic === "POST" || networkTraffic === "PUT") {
      setSaving(true);
    } else if (saving) {
      getObjectives();
      setSaving(false);
    }
  }, [networkTraffic]);

  useEffect(() => {
    getObjectives();
    setWidth(ref?.current?.clientWidth);
  }, []);

  useEffect(() => {
    getObjectives();
  }, [studentId]);

  useEffect(() => {
    multieditRef.current = multiEdit;
    if (!multiEdit) {
      getObjectives();
      changedObjectiveAssessments.current = {};
    }
  }, [multiEdit]);

  const gradeSystemValues = getGradeSystemValues(
    course?.gradeSystem.find((g) => g.is_main)
  );

  if (!multiEdit) {
    assessment = students?.find(s => s.id === studentId).courseAssessments?.find((a) => a.courseId === courseId);
  }

  return (
    <div style={{marginTop: "30px", position: "relative"}} ref={ref}>
      <span
        style={{
          ...fonts.LARGE,
          color: colors.brandColorPink,
          marginLeft: "16px",
        }}
      >
        {getTranslation("objectivesText")}
      </span>
      {width > SIZE_THRESHOLD && gradeSystemsUsed.current.length > 0 && (
        <div
          style={{
            border: `1px solid ${colors.borderColor}`,
            backgroundColor: colors.white,
            position: "absolute",
            right: "244px",
            top: "0px",
            display: "flex",
            height: "30px",
            maxWidth: "205px",
          }}
        >
          <>
            {gradeSystemsUsed.current[selectedGradeSystem]?.grades?.map((g) => {
              return (
                <div
                  style={{
                    width: "30px",
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "center",
                    justifyContent: "center",
                    border: "0px solid",
                  }}
                >
                  <span style={{...fonts.SMALL, color: g.color}}>
                    {g.label}
                  </span>
                </div>
              );
            })}
            <StyledSelect
              value={selectedGradeSystem}
              onChange={({target}) => {
                setSelectedGradeSystem(target.value);
              }}
              input={<BootstrapInput/>}
            >
              {gradeSystemsUsed.current.map((g, i) => {
                return <MenuItem value={i}>{g.name}</MenuItem>;
              })}
            </StyledSelect>
          </>
        </div>
      )}
      <div style={{marginTop: "16px"}}>
        {course?.objectives.sort((a, b) => (a.name.localeCompare(b.name, undefined, {
          numeric: true,
          sensitivty: 'base'
        }))).map((o) => {
          const objectiveAssessment = assessment?.courseObjectiveAssessments?.find(
            (oa) => oa.objective_id === o.id
          );
          const defaultIndex = objectiveAssessment?.grade_id
            ? gradeSystemValues.findIndex(
              (g) => g.id === objectiveAssessment?.grade_id
            )
            : 0;
          return (
            <CourseObjectiveAssessmentItem
              multiEdit={multiEdit}
              onSave={(assessment) => {
                changedObjectiveAssessments.current[assessment.id] = assessment;
                onSave(changedObjectiveAssessments.current);
              }}
              key={o.id}
              gradeSystemValues={gradeSystemValues}
              defaultIndex={defaultIndex}
              assessment={objectiveAssessment}
              knowledgeRequirements={o.knowledgeRequirements}
              objective={o}
              id={objectiveAssessment?.id}
              selectedGradeSystem={
                gradeSystemsUsed.current[selectedGradeSystem]
              }
              onObjectiveSelected={onObjectiveSelected}
              proficiencyProfiles={
                proficiencyProfiles[selectedGradeSystem]?.objectives
              }
            />
          );
        })}
      </div>
    </div>
  );
};
export default CourseObjectiveAssessmentCard;
