import { Service } from '../../utils/decorators/service.decorator';
import TeacherAssessAssignmentDriveModule from './teacherAssessAssignmentDrive.module';

@Service({
  module: TeacherAssessAssignmentDriveModule,
  name: 'TeacherAssessAssignmentDriveService',
})
export class TeacherAssessAssignmentDriveService {
  constructor() {
    'ngInject';
  }

  public findVisibilityPropoerty(currentStudentEditingMode: string): string {
    const map: { [prop: string]: string } = {
      assessment: 'grade_published',
      objectives_feedback: 'feedback_published',
    };

    return map[currentStudentEditingMode];
  }

  public findTextPropoerty(currentStudentEditingMode: string): string {
    const map: { [prop: string]: string } = {
      objectives_feedback: 'feedback',
      objectives_private_notes: 'private_note',
    };

    return map[currentStudentEditingMode];
  }

  public findSelectedItems(currentStudentEditingMode: string, selectedAssignment: any): any[] {
    if (['assessment', 'objectives_feedback', 'objectives_private_notes'].includes(currentStudentEditingMode)) {
      return selectedAssignment.course_definitions.reduce((accCoursesDefinitions: any[], courseDefinition: any): any[] =>
          [
            ...accCoursesDefinitions,
            ...courseDefinition.objectives.filter((objectiveElem) => objectiveElem.checked),
          ],
        []);
    }

    return [];
  }

  public extractObjectiveAssessments(items: any): any[] {
    return items.map(item => item.objective_assessments);
  }
}
