/**
 * @module Sanitize/Filter
 * @description
 *   Display HTML content.
 * @param {Object} $sce Angular $sce object
 */

/*@ngInject*/
function Sanitize($sce) {
  return function(htmlCode) {
    return $sce.trustAsHtml(htmlCode);
  };
}

module.exports = Sanitize;
