import React, {
  memo,
  useContext, useEffect, useMemo, useRef, useState,
} from 'react';
import {debounce} from 'lodash';
import {fonts} from '../theme';
import {getLastModified, GlobalContext} from '../utils';
import CheckboxWithText from '../components/CheckboxWithText';
import {getTranslation} from '../i18n';

const ExtraAdjustmentComponent = (props) => {
  const {
    extraAdjustments: initialExtraAdjustments = [],
    extraAdjustmentsAvailable,
    studentId,
    courseId,
    multiEdit,
    isGrid,
    onChange = false,
    extraAdjustmentData = []
  } = props;
  const context = useContext(GlobalContext);
  const {Api} = context;

  const [extraAdjustments, setExtraAdjustments] = useState(initialExtraAdjustments);

  const added = useRef([]);
  const removed = useRef([]);

  useEffect(() => {
    if(extraAdjustmentData.length){
      setExtraAdjustments(extraAdjustmentData);
    } else {
      setExtraAdjustments([...initialExtraAdjustments]);
    }
    removed.current = [];
    added.current = [];
  }, [initialExtraAdjustments, studentId, multiEdit, extraAdjustmentData]);

  const saveChanges = useMemo(() => debounce(async (Api, adjustments, courseId, studentId) => {
    await Api.updateStudentExtraAdjustments2(adjustments.map(e => e.id), courseId, studentId);
  }, 1000), [studentId]);

  return (
    <div style={{marginTop: '30px', display: 'flex', flexDirection: isGrid ? 'column' : 'row'}}>
      <div style={{
        display: 'flex', border: '0px solid', minWidth: '160px', flexDirection: 'column'
      }}>
        <span style={fonts.MEDIUM}>{getTranslation('extraAdjustments')}</span>
        <span
          style={fonts.SMALL}>{getLastModified(extraAdjustments ? extraAdjustments[extraAdjustments.length - 1]?.modified : null)}</span>
      </div>
      <div style={{marginLeft: isGrid ? '20px' : '2px'}}>
        {extraAdjustmentsAvailable?.sort((a, b) => a.name.localeCompare(b.name)).map((e) => {
          let checked = multiEdit ? false : extraAdjustments?.findIndex((a) => a.id === e.id) >= 0 || false;
          let indeterminate = false;
          if (extraAdjustmentData.length > 0) {
            const adjustment = extraAdjustmentData.find(a => a.id === e.id);
            checked = adjustment?.status === 'check';
            indeterminate = adjustment?.status === 'partial';
          }
          const name = e.name;
          const maxCharactersInName = isGrid ? 39 : 54;
          return (
            <div key={e.id}>
              <CheckboxWithText
                textWidth={isGrid ? '240px' : '380px'}
                subText={''}
                indeterminate={indeterminate}
                toolTip={name.length > maxCharactersInName ? name : ''}
                id={e.id + ' ' + studentId + multiEdit}
                text={name}
                onCheck={(check) => {
                  let currentAdjustments;
                  currentAdjustments = extraAdjustments.filter(a => a.id !== e.id);
                  if (check) {
                    currentAdjustments = [...currentAdjustments, e];
                  }
                  setExtraAdjustments(currentAdjustments);
                  if (!multiEdit) {
                    saveChanges(Api, currentAdjustments, courseId, studentId);
                  }

                  if (onChange) {
                    onChange({currentAdjustments, courseId, studentId});
                  }
                }}
                checked={checked}
                style={{marginBottom: '-10px'}}
                controlled
              />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default memo(ExtraAdjustmentComponent);
