import moment, { Moment } from 'moment';

export class AssessmentStatusPreview {

  static updateDateProperties: { [prop: string]: string[] } = {
    assignment: ['feedback_last_edit', 'private_note_last_edit', 'result_last_edit'],
  };

  constructor(private GradeSystems: any, private UserInfo: any) {
    'ngInject';
  }

  isTeacher(): boolean {
    return this.UserInfo.isTeacher();
  }

  getGradeSystemById(gradeSystemId): any {
    return this.GradeSystems.getGradeSystemById(gradeSystemId);
  }

  getLastUpdateDate(assignment: any = {}, previousLastUpdate: string): string {
    let timestamps: Moment[] = previousLastUpdate ? [moment(previousLastUpdate)] : [];

    timestamps = [...timestamps, ...AssessmentStatusPreview.updateDateProperties.assignment
      .map((property): Moment =>
        assignment[property] ? moment(assignment[property]) : moment.invalid()
      )]
      .filter((timestamp: Moment): boolean => timestamp.isValid());

    const lastUpdate: Moment = timestamps.length ? moment.max(...timestamps) : moment.invalid();

    return lastUpdate && lastUpdate.isValid() ? lastUpdate.format() : '';
  }
}
