import { TransitionHandler } from '../../shared/models/transitionHandler.model';
import get from 'lodash.get';

export const registerOnBeforeTransition: ($transitionsProvider: any) => Function = ($transitionsProvider: any): Function => (constructor: new (...args: any[]) => TransitionHandler): void => {
  const instance: TransitionHandler = new constructor();
  instance.deregisterOnBefore = $transitionsProvider
    .onBefore(instance.getCriteria(), (transition: any): any => instance.onBefore(transition));
};

export function transitionOnce(
  target: Object,
  propertyName: string,
  propertyDescriptor: PropertyDescriptor): PropertyDescriptor {

  const method = propertyDescriptor.value;
  const deregisterMethodName = `deregister${propertyName.replace(/^on/, 'On')}`;

  propertyDescriptor.value = function (...args: any[]) {
    const transition: any = method.apply(this, args);

    get(this, deregisterMethodName, () => {}).call(this);

    return transition;
  };

  return propertyDescriptor;
};
