import { IPromise, IQService, IRootScopeService } from 'angular';
import { Events } from '../../../../../common/global-events';
import { IObjectiveAssignmentAssessment } from '../../../../../shared/models/objectiveAssignmentAssessment.model';
import { Service } from '../../../../../utils/decorators/service.decorator';
import TeacherCourseProgressModule from '../../teacher-course-progress.module';

export interface IAssignmentOverviewService {
  changeObjectiveAssignmentAssessment(objectiveAssignmentAssessment: IObjectiveAssignmentAssessment, courseId: number): IPromise<IObjectiveAssignmentAssessment>;
  updateListOfObjectiveAssignmentAssessments(newObjectiveAssignmentAssessment: IObjectiveAssignmentAssessment, objectiveAssignmentAssessments: IObjectiveAssignmentAssessment[]): IObjectiveAssignmentAssessment[];
}

@Service({
  module: TeacherCourseProgressModule,
  name: 'AssignmentOverviewService',
})
export class AssignmentOverviewService implements IAssignmentOverviewService {
  constructor(private $rootScope: IRootScopeService, private $q: IQService, private FetchData: any) {
    'ngInject';
  }

  public updateListOfObjectiveAssignmentAssessments(newObjectiveAssignmentAssessment: IObjectiveAssignmentAssessment, objectiveAssignmentAssessments: IObjectiveAssignmentAssessment[]): IObjectiveAssignmentAssessment[] {
    return objectiveAssignmentAssessments
      .map((objectiveAssignmentAssessment: IObjectiveAssignmentAssessment): IObjectiveAssignmentAssessment => {
        if (objectiveAssignmentAssessment.id === newObjectiveAssignmentAssessment.id) {
          // Dont overwrite feedback and private_note, it messes with the ui
          const { feedback, private_note, result, ...rest } = newObjectiveAssignmentAssessment;
          return {...objectiveAssignmentAssessment, ...rest};
        }

        return objectiveAssignmentAssessment;
      });
  }

  public changeObjectiveAssignmentAssessment(objectiveAssignmentAssessment: IObjectiveAssignmentAssessment, courseId: number): IPromise<IObjectiveAssignmentAssessment> {
    this.notifyRequestStart();
    return this.FetchData.v3.updateAssignmentAssessment(objectiveAssignmentAssessment, courseId)
      .then(this.notifyRequestSuccess.bind(this), this.notifyRequestFail.bind(this));
  }

  private notifyRequestStart(): void {
    this.$rootScope.$broadcast(Events.STARTED_NEW_REQUEST);
  }

  private notifyRequestSuccess(response: any): any {
    this.$rootScope.$broadcast(Events.REQUEST_FINISHED_SUCCESSFULLY);

    return response;
  }

  private notifyRequestFail(error: any): any {
    this.$rootScope.$broadcast(Events.REQUEST_FINISHED_FAILURE);

    return this.$q.reject(error);
  }
}
