import React, { useState } from 'react';
import {ButtonBase, Tooltip} from '@material-ui/core';
import { colors, fonts } from '../theme';

const StudentItem = (props) => {
  const {
    student, onClickStudent, style, selected,
  } = props;
  const [highlight, setHighlight] = useState(false);

  const highlightStudent = () => {
    setHighlight(true);
  };

  const unHighlightStudent = () => {
    setHighlight(false);
  };

  const baseColor = selected ? colors.borderColor : 'transparent';

  return (
  <div style={{ position: 'relative', height: '50px' }}>
    <ButtonBase
      focusRipple
      onClick={onClickStudent}
      key={student.id}
      onMouseEnter={highlightStudent}
      onMouseLeave={unHighlightStudent}
      style={{
        width: '100%',
        height: '50px',
        borderBottom: `1px solid ${colors.borderColor}`,
        backgroundColor: highlight ? colors.grey : baseColor,
        justifyContent: 'flex-start',
        paddingLeft: '16px',
        ...style,
      }}
    >
      <div
        style={{
          width: '30px',
          height: '30px',
          borderRadius: '15px',
          overflow: 'hidden',
          marginRight: '16px',
        }}
      >
        <img style={{ width: '30px' }} src={student.photo_url} />
      </div>
      <Tooltip title={student.email}>
      <span
        style={{
          ...fonts.MEDIUM,
          width: '210px',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          textAlign: 'start',
        }}
      >
        {student.first_name} {student.last_name}
      </span>
      </Tooltip>
    </ButtonBase>
  </div>);
};
export default StudentItem;
