import { IPromise, IQResolveReject, IQService } from 'angular';

export const compareClassAndCurriculumSettings: ($q: IQService, classroomId: number, curriculum: any, FetchData: any) => IPromise<void> = ($q: IQService, classroomId: number, curriculum: any, FetchData: any): IPromise<void> =>
  FetchData.getCourseSetup(classroomId).then((courseSetup: any): IPromise<void> =>
    $q((resolve: IQResolveReject<void>, reject: IQResolveReject<{ name: string }>) => {
      if (
        courseSetup.educational_system !== null &&
        courseSetup.educational_system !== curriculum.educational_system.id
      ) {
        reject({ name: 'WRONG_EDU_STAGE' });
      } else {
        resolve();
      }
    }),
  );
