import { IPromise } from 'angular';
import { AssignmentOrder } from '../../../assignments/teacherAssignments/shared/enums/assignmentOrder';
import { Service } from '../../../utils/decorators/service.decorator';
import { FetchData } from '../../../utils/services/fetchData.service';
import { RequestType } from '../../enums/requestType.enum';
import { IAssignment } from '../../models/assignment.model';
import { IAssignmentGroupedResponse } from '../../models/assignmentGroupedResponse.model';
import { IAssignmentRequestConfig } from '../../models/assignmentRequestConfig.model';
import { RequestParams } from '../../types/requestParams.type';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'AssignmentAssessmentService',
})
export class AssignmentAssessmentService {
  public static DEFAULT_REQUEST_CONFIG: IAssignmentRequestConfig = {
    ordering: AssignmentOrder.ASC_TYPE,
  };

  constructor(private FetchData: FetchData) {
    'ngInject';
  }

  public getAssignmentAssessmentByCourseDefiniton(studentId: number, courseDefinitionId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignment[]> {
    return this.fetchAssignmentsByCourseDefinition<IAssignment[]>(studentId, courseDefinitionId, requestConfig);
  }

  public getAssignmentAssessmentByCourseDefinitonGouped(studentId: number, courseDefinitionId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignmentGroupedResponse> {
    return this.fetchAssignmentsByCourseDefinition<IAssignmentGroupedResponse>(studentId, courseDefinitionId, requestConfig, true);
  }

  public getAssignmentAssessmentByObjective(studentId: number, objectiveId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignment[]> {
    return this.fetchAssignmentsByObjective<IAssignment[]>(studentId, objectiveId, requestConfig);
  }

  public getAssignmentAssessmentByObjectiveGouped(studentId: number, objectiveId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignmentGroupedResponse> {
    return this.fetchAssignmentsByObjective<IAssignmentGroupedResponse>(studentId, objectiveId, requestConfig, true);
  }

  private fetchAssignmentsByCourseDefinition<T>(studentId: number, courseDefinitionId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG, group: boolean = false): IPromise<T> {
    const _requestConfig: IAssignmentRequestConfig = Object
      .assign({}, AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG, requestConfig);
    const url: string = this.buildUrlByCourseDefinition(studentId, courseDefinitionId);
    const params: RequestParams = this.buildParams(_requestConfig, group);

    return this.fetchAssignments<T>(url, params);
  }

  private fetchAssignmentsByObjective<T>(studentId: number, objectiveId: number, requestConfig: IAssignmentRequestConfig = AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG, group: boolean = false): IPromise<T> {
    const _requestConfig: IAssignmentRequestConfig = Object
      .assign({}, AssignmentAssessmentService.DEFAULT_REQUEST_CONFIG, requestConfig);
    const url: string = this.buildUrlByObjective(studentId, objectiveId);
    const params: RequestParams = this.buildParams(_requestConfig, group);

    return this.FetchData.sendAPIRequestWithParams(params, url, RequestType.GET);
  }

  private fetchAssignments<T>(url: string, params: RequestParams): IPromise<T> {
    return this.FetchData.sendAPIRequestWithParams(params, url, RequestType.GET);
  }

  private buildUrlByCourseDefinition(studentId: number, courseDefinitionId: number): string {
    return `/v3/classes/students/${studentId}/courses-definitions/${courseDefinitionId}/assignment-assessments/`;
  }

  private buildUrlByObjective(studentId: number, objectiveId: number): string {
    return `/v3/classes/students/${studentId}/objectives/${objectiveId}/assignment-assessments/`;
  }

  private buildParams(requestConfig: IAssignmentRequestConfig, group: boolean = false): RequestParams {
    const params: RequestParams = { ordering: requestConfig.ordering };

    if (group) {
      params.group_by_assessability = true;
    }

    return params;
  }
}
