import React, {useState, memo} from 'react';
import ButtonBase from '@material-ui/core/ButtonBase';
import {ReactComponent as GridIcon} from '../images/grid.svg';
import {colors, fonts} from '../theme';
import MoreMenu from './moreMenu';
import {getTranslation} from '../i18n';
import {getAssignmentIcon} from '../utils';
import Tooltip from "./tooltip";

const menuOptionsConnected = [
  {
    title: getTranslation('editAssessmentCriteriaBtn'),
    name: 'ASSESSMENT_CRITERIA',
  },
  {
    title: getTranslation('disconnectFromStudyBee'),
    name: 'DISCONNECT',
  },
];

const menuOptionsNotConnected = [
  {
    title: getTranslation('connectToStudybee'),
    name: 'CONNECT',
  },
];

const AssignmentItem = (props) => {
  const {
    assignment,
    onAssignmentClicked,
    handleAssignmentMenuClick,
    inTopic = false,
    onGridClicked,
    isStudent,
  } = props;
  const [focused, setFocused] = useState(false);

  const click = () => {
    if(assignment.connected){
      onAssignmentClicked(assignment);
    }
  };

  return (
    <div
      onMouseEnter={() => setFocused(true)}
      onMouseLeave={() => setFocused(false)}
      style={{
        height: '70px',
        borderBottom: `1px solid ${colors.borderColor}`,
        alignItems: 'center',
        display: 'flex',
        paddingLeft: inTopic ? '30px' : '16px',
        backgroundColor: focused ? colors.borderColor : colors.white,
      }}
    >
      <ButtonBase
        onClick={click}
        focusRipple
        style={{
          display: 'flex',
          alignItems: 'center',
          flex: 1,
          justifyContent: 'flex-start',
        }}
      >
        {getAssignmentIcon(assignment)}
        <p
          style={{
            ...fonts.MEDIUM,
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            width: '190px',
            textAlign: 'start',
          }}
        >
          {assignment.title}
        </p>
      </ButtonBase>
      <div
        style={{
          display: 'flex',
          border: '0px solid',
          width: '100%',
          alignItems: 'center',
          justifyContent: 'flex-end',
        }}
      >
        {!isStudent && (
          <>
            {assignment.connected &&
            <Tooltip title={getTranslation('gridOverviewTooltip')}>
              <div>
                <GridIcon
                  style={{width: '20px', height: '20px', cursor: 'pointer'}}
                  fill={colors.darkGrey}
                  onClick={() => onGridClicked(assignment)}
                />
              </div>
            </Tooltip>
            }

            <MoreMenu
              iconStyle={{color: colors.darkGrey, marginBottom: '4px'}}
              options={assignment.connected?menuOptionsConnected:menuOptionsNotConnected}
              onClick={(option) => handleAssignmentMenuClick(assignment, option)
              }
            />
          </>
        )}
      </div>
    </div>
  );
};

export default memo(AssignmentItem);
