import { Events } from '../../../../common/global-events.ts';

export const StudentObjectiveDetails = {
  bindings: {
    objectiveSelected: '<',
    objectives: '<',
    objectiveAssessment: '<',
    course: '<',
  },
  templateUrl: 'components/course-progress/student-course-progress/student-objective-details/student-objective-details.html',
  controller: class {
    constructor($rootScope, NavigationHistory) {
      'ngInject';

      this.$rootScope = $rootScope;
      this.NavigationHistory = NavigationHistory;
    }

    $onInit() {
      this.$rootScope.$emit(
        Events.UPDATE_USER_STATUSES_FOR_COURSES_AND_OBJECTIVES,
      );
    }

    closeAssignmentsView() {
      this.NavigationHistory.go(
        'student-course-progress.details.learning-objectives',
        {
          course: this.course,
        },
      );
    }

    changeObjective() {
      this.NavigationHistory.go(
        'student-course-progress.objective-details.learning-assignments',
        {
          objective: this.objectiveSelected,
        },
      );
    }

    toggleAssessment() {
      this.objectiveAssessment.open = !this.objectiveAssessment.open;
    }
  },
};
