import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputBase from '@material-ui/core/InputBase';
import {colors, fonts} from '../theme';

const StyledSelect = withStyles(() => ({
  icon: {
    color: colors.white,
  },
}))(Select);

const BootstrapInput = withStyles(() => ({
  input: {
    ...fonts.MEDIUM,
    borderRadius: 0,
    color: colors.white,
    marginTop: '2px',
    // '&:focus': {
    //   borderRadius: 4,
    //   borderColor: '#80bdff',
    //   boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
    // },
  },
}))(InputBase);

const StudentDropDown = (props) => {
  const {
    students, defaultValue, label, onSelect,
  } = props;

  return (
    <div
      style={{
        display: 'flex',
        border: '0px solid red',
        marginTop: '-8px',
        marginLeft: '4px',
      }}
    >
      <div style={{display: 'flex', flexDirection: 'column'}}>
        <StyledSelect
          style={{
            border: '0px solid yellow', height: '25px', color: colors.white, width: '260px',
          }}
          input={<BootstrapInput/>}
          value={defaultValue}
        >
          {students.map((student) => {
            if (!student.student_name) {
              student.student_name = `${student.first_name} ${student.last_name}`;
            }
            return (
              <MenuItem
                onClick={() => {
                  onSelect(student.student_id);
                }}
                key={student.student_id}
                value={student.student_id}
                style={{...fonts.SMALL, color: colors.textColor}}
              >
                {student.student_name}
              </MenuItem>
            );
          })}
        </StyledSelect>
        <span style={{
          ...fonts.SMALL,
          color: colors.white,
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          width: '250px',
          marginTop:'-2px',
        }}>{label}</span>
      </div>
    </div>
  );
};

export default StudentDropDown;
