import get from 'lodash.get';
import {GroupedVisibilityDialogType} from '../../../shared/enums/groupedVisibilityDialogType.enum.ts';
import {GroupedVisibilitySettings} from '../../../shared/models/groupedVisibilitySettings.model.ts';
import {ListViewType} from '../../../shared/enums/listViewType.enum';
import {AssessmentStatus} from '../../../../../../src/app/shared/models/assessment.model';
import {combineQueries} from "@datorama/akita";
import {filter, map, switchMap} from 'rxjs/operators';
import {noop, Subject} from "rxjs";

const SingleCheckbox = require('../../../utils/components/single_checkbox');
const AssignmentGroupCheckboxes = require('../../../utils/components/assignment_group_checkboxes');

/**
 * @module TeacherAssessAssignment/Controller
 * @description
 *   Handle Assess Assignment feature for Teacher
 * @param {Object} $scope Angular $scope object
 * @param {Object} $stateParams Angular $stateParams object
 * @param {Object} $mdDialog Angular Material $mdDialog object
 * @param {Object} $mdToast Angular Material $mdToast object
 * @param {Object} Path Path service instance
 * @param {Object} FetchData Fetch Data service instance
 * @param {Object} Updates Updates service instance
 * @param {Object} Debounce Debounce service instance
 * @param {Object} Helpers Helpers service instance
 * @param {Object} LocalHelpers Local Helpers service instance
 * @param {Object} NavigationHistory Navigation History service instance
 * @param {Object} MainMenu Main Menu service instance
 * @param {Object} Mixpanel Mixpanel service instance
 * @param {Object} assignmentsObjectives Resolve from Config Service
 * @param {Object} assignmentsStudents Resolve from Config Service
 */

/*@ngInject*/
function MainCtrl(
  $scope, $stateParams, $state, $mdDialog, $filter, $timeout, Path, FetchData, Updates, Debounce,
  Helpers, TeacherAssessAssignmentService, NavigationHistory, MainMenu, Mixpanel,
  assignmentsObjectives, assignmentsStudents, assignment, GroupedVisibilityService,
  GroupedVisibilityDialogService, Students, ListViewMenuService, LocalStorageService,
    AssessmentsService, AssignmentService, AssignmentsQuery, AssessmentsQuery, AssessmentVisibilityDialogService,
    AssessmentsVisibilityDialogService, AssignmentsService,
) {
  const vm = this;

  const courseId = Path.getClassroomId();
  const storageViewMenuId = 'teacherAssessAssignment';

  vm.unsubscribes = [];
  vm.showLoading = FetchData.showLoading;
  vm.savingInProgress = 0;
  vm.savingIndicatorVisible = false;
  vm.savingError = false;
  vm.viewReady = true;
  vm.storageKeyGradeDescription = `${storageViewMenuId}GradeDescriptionVisible`;
  vm.isGradeDescriptionVisible = LocalStorageService.exists(vm.storageKeyGradeDescription);
  vm.AssessmentsService = AssessmentsService;
  vm.AssignmentListService = AssignmentService;
  vm.navigationHistory = NavigationHistory;

  // Set default values for Edit Multiple
  vm.editMultiple = {
    grade: 'not_changed',
    visibility: 'not_changed',
    open: false,
  };
  vm.statusMenuItems = [
    {
      status: AssessmentStatus.ASSESSMENT_DONE,
      label: 'teacherAssignmentsAssessmentsStudentMenuAssessed',
    },
    {
      status: AssessmentStatus.ASSESSING,
      label: 'teacherAssignmentsAssessmentsStudentMenuAssessing',
    },
    {
      status: AssessmentStatus.NOT_ASSESSED,
      label: 'teacherAssignmentsAssessmentsStudentMenuNotAssessed',
    },
  ];

  // array of opened students ids
  // if state is restored this should contain IDs of students which were opened
  vm.openedStudents = $stateParams.openedStudents;

  // Get data from API
  vm.assignmentTitle = assignmentsObjectives.title;
  vm.gradeSystem = assignmentsObjectives.grade_system;
  vm.coursesDefinitions = assignmentsObjectives.course_definitions;
  vm.resultOnly = assignmentsObjectives.result_only;
  vm.isConfigured = assignmentsObjectives.is_configured && assignmentsObjectives.course_definitions.length;
  vm.isGradeDescriptionEnable = assignmentsObjectives.enable_grade_texts && !vm.gradeSystem.is_counter;
  vm.students = assignmentsStudents;
  vm.viewMenuItems = ListViewMenuService.getListViewMenu(storageViewMenuId);
  vm.viewMode = vm.viewMenuItems.find(listViewMenuItem => listViewMenuItem.checked)
    .eventArgs.view;

  const assignment$ = combineQueries([
    AssignmentsQuery.selectActive().pipe(filter(Boolean)),
    AssessmentsQuery.selectAll({asObject: true})
  ]).pipe(
    map(([assignment, assessments]) => ({
        ...assignment,
        assessments: assignment.assessments.map(assessmentID => assessments[assessmentID]),
      })
    ))
    .subscribe(assignment => {
      vm.assignment = {...assignment};
      vm.viewReady = false;

      $timeout(() => {
        vm.updateCourseDefinition();
        vm.viewReady = true;
      });
    });

  vm.debounceFinished$ = new Subject();
  const debounceFinishedSub = vm.debounceFinished$.subscribe(assessmentID => {
    vm.AssessmentsService.synchronizeWarningStatus({id: assessmentID}).subscribe(() => {
      vm.updateLastEditTimestampForAssignment();
    });
  });

  vm.unsubscribes.push(() => assignment$.unsubscribe());
  vm.unsubscribes.push(() => debounceFinishedSub.unsubscribe());

  vm.assignment = assignment;

  $scope.$on('$destroy', () => {
    this.unsubscribes.forEach(unsubscribe => unsubscribe());
  });
  /**
   * Send changes to API for Objective Assessments
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} students Contains objects where each object represents single student
   */
  vm.changeObjectiveAssessments = function (students) {
    vm.savingInProgress += 1;
    vm.savingIndicatorVisible = true;
    vm.savingError = false;

    FetchData.putObjectiveAssessments(students).then((res) => {
      vm.savingInProgress -= 1;
      vm.updateObjectiveAssessmentsDates(res);
    }, () => {
      vm.savingInProgress -= 1;
      vm.savingError = true;
    })
      .finally(() => students.forEach(student => vm.debounceFinished$.next(student.assessment)));
  };

  /**
   * Send changes to API for Assignments Objectives
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} students Contains objects where each object represents single student
   */
  vm.changeAssignmentsObjectives = function (students) {
    vm.savingInProgress += 1;
    vm.savingIndicatorVisible = true;
    vm.savingError = false;

    FetchData.putAssignmentsObjectives(assignment.id, students).then((res) => {
      vm.savingInProgress -= 1;
      vm.updateAssignmentsObjectivesDates(res);
    }, () => {
      vm.savingInProgress -= 1;
      vm.savingError = true;
    })
      .finally(() => students.forEach(student => vm.debounceFinished$.next(student.assessment)));
  };

  vm.editMultipleAssignmentState = () => {
    AssessmentsVisibilityDialogService.open(vm.assignment.id).pipe(
      switchMap(assignmentVisibility => {
        return AssessmentsService.updateStatusForMany({
          ...vm.assignment
        }, assignmentVisibility).pipe(
          map(() => assignmentVisibility.status)
        );
      }),
    ).subscribe(status => {
      if (status === AssessmentStatus.ASSESSMENT_DONE) {
        $state.reload()
      }
    });
  };

  /**
   * Debounce send changes to API for Objective Assessments
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} students Contains objects where each object represents single student
   */
  vm.debounceObjectiveAssessments = Debounce((students) => {
    vm.changeObjectiveAssessments(students);
  }, 250);

  /**
   * Debounce send changes to API for Assignments Objectives
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} students Contains objects where each object represents single student
   */
  vm.debounceAssignmentsObjectives = Debounce((students) => {
    vm.changeAssignmentsObjectives(students);
  }, 250);

  /**
   * Choose send method
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} dataToSave Contains objects where each object represents single data to save
   */
  vm.matchEndpoint = function (dataToSave) {
    if (vm.currentEditingMode === 'assessment' || vm.currentEditingMode === 'objectives_feedback' || vm.currentEditingMode === 'objectives_private_notes') {
      vm.debounceObjectiveAssessments(dataToSave);
    } else if (vm.currentEditingMode === 'results' || vm.currentEditingMode === 'overall_feedback' || vm.currentEditingMode === 'overall_private_notes') {
      vm.debounceAssignmentsObjectives(dataToSave);
    }
  };

  /**
   * Update last modified dates for Objective Assessments
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} responseAssessmentsArray Response from the API, contains students information
   */
  vm.updateObjectiveAssessmentsDates = function (responseAssessmentsArray) {
    responseAssessmentsArray.forEach((resAssessment) => {
      vm.coursesDefinitions.forEach((courseDefinition) => {
        courseDefinition.students.forEach((student) => {
          student.objective_assessments.forEach((objectiveAssessment) => {
            if (resAssessment.id !== objectiveAssessment.id) {
              return;
            }

            objectiveAssessment.grade_last_edit = resAssessment.grade_last_edit;
            objectiveAssessment.feedback_last_edit = resAssessment.feedback_last_edit;
            objectiveAssessment.private_note_last_edit = resAssessment.private_note_last_edit;
          });
        });
      });
    });
  };

  /**
   * Update last modified dates for Assignments Objectives
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} responseAssessmentsArray Response from the API, contains students information
   */
  vm.updateAssignmentsObjectivesDates = function (responseAssessmentsArray) {
    responseAssessmentsArray.forEach((resAssessment) => {
      vm.students.forEach((student) => {
        if (resAssessment.id !== student.id) {
          return;
        }

        student.result_last_edit = resAssessment.result_last_edit;
        student.feedback_last_edit = resAssessment.feedback_last_edit;
        student.private_note_last_edit = resAssessment.private_note_last_edit;
      });
    });
  };

  /**
   * Init function
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.init = function () {
    // Check if is modal
    vm.isModal = $stateParams.isModal;

    if (!vm.isConfigured) {
      return;
    }

    // Generate structure for coursesDefinitions
    vm.generateStructure();

    // Fetch objectives for each student and push them to coursesDefinitions
    // vm.fetchingObjectives();

    // Set default values
    if ($stateParams.editingMode) {
      vm.editingMode = $stateParams.editingMode;
      vm.currentEditingMode = vm.editingMode;
    } else if (vm.resultOnly) {
      vm.editingMode = 'results';
      vm.currentEditingMode = 'results';
    } else {
      vm.editingMode = 'assessment';
      vm.currentEditingMode = 'assessment';
    }

    vm.selectedCourseDefinition = vm.getLastSelectedCourseDefinition();
    vm.openStudentsDetailsInCourse(vm.selectedCourseDefinition);
    vm.setTextForGradeSystem();
  };

  vm.setTextForGradeSystem = () => {
    vm.gradeSystem.grades.forEach((grade) => {
      const defaultGradeTexts = [{grade_id: grade.id, text: ''}];
      grade.text = get(vm, 'selectedCourseDefinition.objectives[0].grade_texts', defaultGradeTexts)
        .find(gradeText => gradeText.grade_id === grade.id).text;
    });
  };

  vm.getGradeText = (grade, objective, courseDefinition) => {
    const defaultGradeTexts = [{grade_id: grade.id, text: ''}];
    const currentObjective = courseDefinition.objectives
      .find(_objective => _objective.id === objective.objective);

    return get(currentObjective, 'grade_texts', defaultGradeTexts)
      .find(gradeText => gradeText.grade_id === grade.id).text;
  };

  vm.getLastSelectedCourseDefinition = () => {
    let courseDefinition;

    if ($stateParams.courseDefinitionId) {
      courseDefinition = vm.coursesDefinitions
        .find(item => item.id === $stateParams.courseDefinitionId);
    }

    return courseDefinition || vm.coursesDefinitions[0];
  };

  /**
   * Go to previous history entry
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.historyBack = function () {
    if (vm.historyBackClicked) {
      return;
    }

    vm.historyBackClicked = true;

    // Clear assignments details
    MainMenu.saveAssignmentDetails(null);

    NavigationHistory.back();
  };

  /**
   * Expands or collapses student entry
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Object} student Student object
   */
  vm.toggleStudent = function (student) {
    const id = student.id;
    const index = vm.openedStudents.indexOf(id);
    student.open = !student.open;

    if (student.open) {
      if (index === -1) {
        vm.openedStudents.push(id);
      }
    } else if (index !== -1) {
      vm.openedStudents.splice(index, 1);
    }

    NavigationHistory.updateParams({openedStudents: vm.openedStudents});
  };

  vm.assignmentCourseChanged = (selectedCourseDefinition = vm.selectedCourseDefinition) => {
    vm.setTextForGradeSystem();
    vm.openStudentsDetailsInCourse(selectedCourseDefinition);
    NavigationHistory.updateParams({courseDefinitionId: selectedCourseDefinition.id});
    $state.go('.', {courseDefinitionId: selectedCourseDefinition.id});
  };

  vm.openStudentsDetailsInCourse = (courseDefinition) => {
    courseDefinition.students.forEach((student) => {
      student.open = vm.openedStudents.includes(student.id);
    });
  };

  /**
   * Generate structure for vm.coursesDefinitions
   */
  vm.generateStructure = function () {
    vm.coursesDefinitions = vm.coursesDefinitions.filter(courseDefinitionElem => courseDefinitionElem.objectives.length);

    vm.coursesDefinitions.forEach((courseDefinitionElem) => {
      courseDefinitionElem.students = [];

        vm.students.forEach((courseDefinitionStudentElem) => {
        const assessment = vm.assignment.assessments.find(assessment => assessment.student_id === courseDefinitionStudentElem.student_id) || {};
        const student = {
          id: courseDefinitionStudentElem.student_id,
          full_name: courseDefinitionStudentElem.student_name,
          fetching_objectives: true,
          objective_assessments: [],
          open: vm.openedStudents.includes(courseDefinitionStudentElem.student_id),
          status: assessment.status_new || courseDefinitionStudentElem.new_status,
          warning: assessment.fail_warning,
        };

        courseDefinitionElem.students.push(student);
      });
    });
  };

  vm.updateCourseDefinition = () => {
    vm.coursesDefinitions = vm.coursesDefinitions.map(courseDefinition => {
      const students = courseDefinition.students.map(student => {
       const assessment = vm.assignment.assessments.find(assessment => assessment.student_id === student.id);

       return {
         ...student,
         status: assessment.status_new,
         warning: assessment.fail_warning,
       }
      });

      return {
        ...courseDefinition,
        students,
      }
    });
  };

  vm.changeAssessmentStatus = (studentId, status) => {
    const assessment = vm.assignment.assessments.find(assessment => assessment.student_id === studentId);
    let updateStatus$;
    if (status === AssessmentStatus.ASSESSMENT_DONE) {
      updateStatus$ = AssessmentVisibilityDialogService.open(vm.assignment.id).pipe(
        switchMap(assessmentVisibilityDialog => {
          const statusUpdateBody = {
            ...assessmentVisibilityDialog,
            status,
          };
          return AssessmentsService.updateStatus(vm.assignment.id, assessment, statusUpdateBody);
        })
      )
    } else {
      updateStatus$ = AssessmentsService.updateStatus(vm.assignment.id, assessment, {status});
    }

    updateStatus$.subscribe(() => {
      vm.updateLastEditTimestampForAssignment();
      if (status === AssessmentStatus.ASSESSMENT_DONE) {
        $state.reload();
      }
    });
  };

  /**
   * Fetch objectives for each student and push them to coursesDefinitions
   */
  vm.fetchingObjectives = function () {
    vm.students.forEach((studentElem) => {
      FetchData.v2.getObjectiveAssessments(assignment.id, studentElem.student_id).then((res) => {
        vm.coursesDefinitions.forEach((courseDefinitionElem) => {
          courseDefinitionElem.students.forEach((courseDefinitionStudentElem) => {
            if (studentElem.student_id !== courseDefinitionStudentElem.id) {
              return;
            }

            res.forEach((objectiveElem) => {
              courseDefinitionElem.objectives.forEach((courseDefinitionObjectiveElem) => {
                if (objectiveElem.objective !== courseDefinitionObjectiveElem.id) {
                  return;
                }

                const objective = Object.assign({}, objectiveElem, {
                  objective_name: courseDefinitionObjectiveElem.name,
                  description: courseDefinitionObjectiveElem.description,
                });

                courseDefinitionStudentElem.objective_assessments.push(objective);
              });
            });

            courseDefinitionStudentElem.fetching_objectives = false;
          });
        });
      });
    });
  };

  /**
   * Select student by clicking on student name
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Number} studentId Student ID of clicked student
   */
  vm.clickStudent = function (studentId) {
    const params = {studentId, assignment, courseDefinitionId: vm.selectedCourseDefinition.id};
    NavigationHistory.go('teacher_assess_assignment_student', params);
  };

  /**
   * Load view chosen in currentEditingMode select
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.loadView = function () {
    vm.currentEditingMode = vm.editingMode;

    vm.checkIfAnyCheckboxIsChecked();
  };

  /**
   * Check if there is any checkbox selected for Edit Multiple
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.checkIfAnyCheckboxIsChecked = function () {
    let anyCheckboxesChecked = false;

    const objectiveNormalEditing = ['assessment', 'objectives_feedback', 'objectives_private_notes'];
    const courseNormalEditing = ['results', 'overall_feedback', 'overall_private_notes'];

    if (Helpers.checkIfModeAvailable(vm.currentEditingMode, objectiveNormalEditing)) {
      anyCheckboxesChecked = vm.assignmentCheckboxesCheckIfAny(vm.coursesDefinitions);
    } else if (Helpers.checkIfModeAvailable(vm.currentEditingMode, courseNormalEditing)) {
      anyCheckboxesChecked = vm.checkboxCheckIfAny(vm.students);
    }

    if (anyCheckboxesChecked) {
      vm.editMultiple.anyCheckboxChecked = true;
    } else {
      vm.editMultiple.anyCheckboxChecked = false;
    }
  };

  /**
   * Check if there is any changes to save in Edit Multiple
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.checkEditMultiple = function () {
    let mode;
    let changesToSave = false;

    const visibilityNormalEditing = ['assessment', 'objectives_feedback', 'results', 'overall_feedback'];
    const gradeNormalEditing = ['assessment'];
    const textNormalEditing = [
      'results', 'objectives_feedback', 'objectives_private_notes', 'overall_feedback', 'overall_private_notes',
    ];

    mode = vm.editMultiple;

    if (Helpers.isVisibilityChanges(mode.visibility, vm.currentEditingMode, visibilityNormalEditing) ||
      Helpers.isGradeChanges(mode.grade, vm.currentEditingMode, gradeNormalEditing) ||
      Helpers.isTextChanges(mode.textField, vm.currentEditingMode, textNormalEditing)) {
      changesToSave = true;
    }

    if (changesToSave === true) {
      mode.changesToSave = true;
    } else {
      mode.changesToSave = false;
    }
  };

  /**
   * Save changes for Edit Multiple
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.saveEditMultiple = function () {
    const selectedItems = TeacherAssessAssignmentService
      .findSelectedItems(vm.currentEditingMode, vm.coursesDefinitions, vm.students);

    vm.changeMultipleTexts(selectedItems);
    vm.changeMultipleGrades(selectedItems);
    vm.changeMultipleVisible(selectedItems);

    vm.matchEndpoint(selectedItems);

    vm.clearEditMultiple();

    vm.updateLastEditTimestampForAssignment();

    if (vm.currentEditingMode === 'objectives_feedback') {
      Mixpanel.track('feedback_to_student_with_multiedit');
    }
  };

  /**
   * Set defaults values for Edit Multiple and clear changesToSave flag to false
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.clearEditMultiple = function () {
    vm.editMultiple.grade = 'not_changed';
    vm.editMultiple.visibility = 'not_changed';
    vm.editMultiple.textMode = 'new_text';
    vm.editMultiple.textField = '';
    vm.editMultiple.changesToSave = false;
  };

  /**
   * Close Edit Multiple
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.closeEditMultiple = function () {
    vm.editMultiple.open = false;
  };

  /**
   * Handle changing text of single item
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleText = function (savingMethod, savingItem, eventCode) {
    Helpers.changeSingleText(savingMethod, savingItem, eventCode);
    vm.updateLastEditTimestampForAssignment();
  };

  /**
   * Handle changing visible of single item
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {Object} editingItem Object that will be editing
   * @param {String} editingProperty Property that will be editing
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleVisible = function (savingMethod, savingItem, editingItem, editingProperty, eventCode) {
    Helpers.changeSingleVisible(savingMethod, savingItem, editingItem, editingProperty, eventCode);
    vm.updateLastEditTimestampForAssignment();
  };

  /**
   * Handle changing grade of single item
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleGrade = function (savingMethod, savingItem, eventCode) {
    Helpers.changeSingleGrade(savingMethod, savingItem, eventCode);
    vm.updateLastEditTimestampForAssignment();
  };

  /**
   * Handle changing text of multiple items
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleTexts = function (selectedItems) {
    let textProperty;
    let textMode;
    let textField;
    let pass;
    const normalEditing = [
      'objectives_feedback', 'objectives_private_notes', 'results', 'overall_feedback', 'overall_private_notes',
    ];

    if (!vm.editMultiple.textField) {
      return;
    }

    pass = Helpers.checkIfModeAvailable(vm.currentEditingMode, normalEditing);

    if (!pass) {
      return;
    }

    textMode = vm.editMultiple.textMode;
    textField = vm.editMultiple.textField;

    textProperty = TeacherAssessAssignmentService.findTextPropoerty(vm.currentEditingMode);

    selectedItems.forEach((itemElem) => {
      const oldValue = itemElem[textProperty];
      if (textMode === 'new_text') {
        itemElem[textProperty] = textField;
      } else if (textMode === 'append_text_first') {
        itemElem[textProperty] = `${textField} ${oldValue}`;
      } else if (textMode === 'append_text_last') {
        itemElem[textProperty] = `${oldValue} ${textField}`;
      }
    });
  };

  /**
   * Handle changing visible of multiple items
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleVisible = function (selectedItems) {
    const normalEditing = ['assessment', 'objectives_feedback', 'result', 'overall_feedback'];

    if (vm.editMultiple.visibility === 'not_changed') {
      return;
    }

    const pass = Helpers.checkIfModeAvailable(vm.currentEditingMode, normalEditing);

    if (!pass) {
      return;
    }


    const visibilityMode = vm.editMultiple.visibility;

    const visibilityProperty = TeacherAssessAssignmentService
      .findVisibilityPropoerty(vm.currentEditingMode);

    selectedItems.forEach((itemElem) => {
      if (visibilityMode === 'set_visible') {
        // eslint-disable-next-line no-param-reassign
        itemElem[visibilityProperty] = true;
      } else if (visibilityMode === 'set_hidden') {
        // eslint-disable-next-line no-param-reassign
        itemElem[visibilityProperty] = false;
      }
    });
  };

  /**
   * Handle changing grades of multiple items
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleGrades = function (selectedItems) {
    let grade;
    let pass;
    const normalEditing = ['assessment'];

    if (vm.editMultiple.grade === 'not_changed') {
      return;
    }

    pass = Helpers.checkIfModeAvailable(vm.currentEditingMode, normalEditing);

    if (!pass) {
      return;
    }

    grade = vm.editMultiple.grade;

    selectedItems.forEach((itemElem) => {
      if (itemElem.grade_system_is_counter) {
        return;
      }

      itemElem.grade_name = grade;
    });
  };

  /**
   * Handle changes dialog
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {String} triggerType Trigger type (changeEditingMode, cancelEditingMode, selectStudent,
   *   clickStudentInClassroom)
   * @param {Number} studentId Student ID of clicked student
   */
  // TODO Since we are not using a dialog anymore we should rename this function.
  vm.showChangesDialog = function (triggerType, studentId) {
    // Without changes
      if (triggerType === 'changeEditingMode') {
        NavigationHistory.updateParams({editingMode: vm.editingMode});
        vm.loadView();
      }
      if (triggerType === 'cancelEditingMode') {
        vm.closeEditMultiple();
      }
      if (triggerType === 'selectStudent') {
        vm.clickStudent(studentId);
      }
      if (triggerType === 'clickStudentInClassroom') {
        NavigationHistory.go('teacher_assess_assignment_student', {studentId, assignment});
      }
    vm.clearEditMultiple();
      return;
  };

  /**
   * Open action menu
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Object} $mdOpenMenu Angular Material $mdOpenMenu object
   * @param {Object} e Event object
   */
  vm.openActionMenu = function ($mdOpenMenu, e) {
    $mdOpenMenu(e);
  };

  /**
   * Edit scales and objectives option
   * @memberof module:TeacherAssessAssignment/Controller
   */
  vm.editScalesAndObjectivesOption = function (firstTime = false) {
    NavigationHistory.go('teacher_set_assignment_objectives', {assignment, firstTime});
  };

  vm.setVisibility = () => {
    const groupedVisibilitySettings =
      new GroupedVisibilitySettings(GroupedVisibilityDialogType.AssignmentView);

    groupedVisibilitySettings.courseDefinitions = [...vm.coursesDefinitions];

    groupedVisibilitySettings.students = groupedVisibilitySettings.courseDefinitions
      .map(courseDefinition => [...(courseDefinition.students || [])])
      .flat()
      .reduce((acc, student) =>
          (acc.some(_student => _student.id === student.id) ? acc : [...acc, student]),
        [])
      .map(student => ({...student, student_name: student.full_name}));

    groupedVisibilitySettings.type = groupedVisibilitySettings.students.length > 1 ?
      GroupedVisibilityDialogType.AssignmentView :
      GroupedVisibilityDialogType.AssignmentStudentView;

    GroupedVisibilityDialogService.show(groupedVisibilitySettings).then((groupedVisibility) => {
      GroupedVisibilityService.patchAssignmentAssessmentsGroupedVisibility(groupedVisibility)
        .then(() => $state.reload());
    });
  };

  vm.onClickMenuItem = (toolbarMenuItem) => {
    vm.viewMenuItems.forEach((menuItem) => {
      Object.assign(menuItem, {checked: false});
    });
    Object.assign(toolbarMenuItem, {checked: true});
    ListViewMenuService.saveListViewMenu(storageViewMenuId, vm.viewMenuItems);
    vm.viewMode = toolbarMenuItem.eventArgs.view;
  };

  vm.isTitleVisible = (title) =>
    vm.viewMode !== ListViewType.DESCRIPTION_ONLY && `${title}`.trim().length;

  vm.isDescriptionVisible = (description) =>
    vm.viewMode !== ListViewType.TITLE_ONLY && `${$filter('getFormattedOrEmpty')(description)}`.trim().length;

  // Listen on Clicked Student in Classroom event (trigger when user click on student link in classroom)
  // and redirect to students details for clicked student.
  // Use debounce because clickedStudentInClassroom is trigger twice from unknown reason.
  $scope.$on('clickedStudentInClassroom', Debounce((e, studentName) => {
    let studentId = Helpers.findStudentIdByName(vm.students, studentName);

    if (!studentId) {
      Updates.updateStudents(courseId, true).then((res) => {
        studentId = Helpers.findStudentIdByName(res, studentName);
        vm.showChangesDialog('clickStudentInClassroom', studentId);
      });
    } else {
      vm.showChangesDialog('clickStudentInClassroom', studentId);
    }
  }, 250));

  vm.updateDescription = (grade, objective, courseDefinition) => {
    $timeout(() => {
      if (grade) {
        objective.gradeText = vm.getGradeText(grade, objective, courseDefinition);
      } else {
        objective.gradeText = '';
      }
    });
  };

  vm.getGradeDescription = (objective, courseDefinition) => {
    const selectedGrade = {text: ''};
    const currentObjective = courseDefinition.objectives
      .find(_objective => _objective.id === objective.objective);

    if (currentObjective) {
      Object.assign(selectedGrade, currentObjective.grade_texts
        .find(grade => grade.grade_id === objective.grade_id));
    }


    return selectedGrade.text;
  };

  vm.showGradeDescription = () => {
    vm.isGradeDescriptionVisible = true;
    LocalStorageService.set(vm.storageKeyGradeDescription, 'true');
  };

  vm.hideGradeDescription = () => {
    vm.isGradeDescriptionVisible = false;
    LocalStorageService.remove(vm.storageKeyGradeDescription);
  };

  vm.isGradeTextVisible = objective => {
    return vm.isGradeDescriptionEnable && vm.isGradeDescriptionVisible && !vm.gradeSystem.is_counter && objective.gradeText;
  };

  vm.updateLastEditTimestampForAssignment = (id = vm.assignment.id) => {
    AssignmentsService.update({id});
  };

  vm.init();
}

MainCtrl.prototype = Object.assign(MainCtrl.prototype, AssignmentGroupCheckboxes.prototype, SingleCheckbox.prototype);

module.exports = MainCtrl;
