import { StateService } from '@uirouter/core';
import angular, { IComponentController, IPromise, IRootScopeService } from 'angular';
import { Events } from '../../../../common/global-events';
import { UnsubscribableComponent } from '../../../../shared/components/abstracts/unsubscribable.component';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import { ICurriculumsService } from '../../common/services/curriculums.service';
import style from '../../common/styles/courses-list.scss';
import { showDeleteCourseConfirmationDialog, showEditCurriculumDialog, showSaveCourseToLibraryDialog } from '../shares/helpers.dialog';
import TeacherCourseManagementModule from '../teacherCourseManagement.module';
import template from './cmTeacherCoursesList.tpl.html';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'cm-teacher-courses-list',
  template: template,
  style: style,
})
export class CmTeacherCoursesListComponent extends UnsubscribableComponent implements IComponentController {
  @Input()
  public curriculums: any[];

  public selectedCurriculum: any;

  constructor(private $rootScope: IRootScopeService, private $mdDialog: angular.material.IDialogService,
              private $state: StateService, private CurriculumsService: ICurriculumsService,
              private FetchData: any, private Path: any, private Mixpanel: any) {
    'ngInject';

    super();
    this.curriculums = [];
    this.selectedCurriculum = null;
  }

  public $onInit(): void {
    this.$$unsubscribes.push(
      this.$rootScope.$on(
        Events.UPDATED_COURSES,
        () => this.updateCoursesList(),
      ),
    );

    // When the name of the course is updated from the extended view,
    // it should be still possible to update the name on the list
    this.$$unsubscribes.push(
      this.$rootScope.$on(Events.COURSE_UPDATED, (_: any, courseUpdated: any): void => {
        const courseToUpdate: any = this.curriculums.find(course => course.id === courseUpdated.id);

        if (courseToUpdate) {
          courseToUpdate.name = courseUpdated.name;
        }
      }),
    );

    this.$$unsubscribes.push(this.$rootScope.$on(
      Events.CLOSE_COURSE_DETAILS_VIEW,
      (): void => (this.selectedCurriculum = null),
    ));
  }

  public navigateToCourseDetails(course: any): void {
    this.selectedCurriculum = course;
    this.$state.go('teacherCourseManagement.details.learningObjectives', { course });
  }

  public openSaveCourseToLibraryDialog(course: any): IPromise<void> {
    return showSaveCourseToLibraryDialog(this.$mdDialog, Object.assign({}, course))
      .then((newCourse: any): IPromise<any> => this.FetchData.v3
        .saveCourseToLibrary(course.id, newCourse.name, newCourse.code),
      )
      .then((): void => {
          this.Mixpanel.track('saving_curriculum_to_library');
        },
      );
  }

  public openEditCourseDialog(course: any): void {
    showEditCurriculumDialog(
      this.$mdDialog, this.FetchData, this.Path,
      angular.copy(course), course.educational_system,
      course.educational_stage, this.curriculums.length > 1,
    )
      .then((courseData: any): IPromise<any> => this.FetchData.v3.updateCourseDefinition(
        this.Path.getClassroomId(),
        Object.assign(course, courseData)),
      )
      .then((courseUpdated: any): void => {
        this.$rootScope.$emit(Events.COURSE_UPDATED, courseUpdated);
        this.$rootScope.$emit(Events.UPDATED_OBJECTIVES, courseUpdated);
      });
  }

  public deleteCourse(courseDefinition: any): void {
    showDeleteCourseConfirmationDialog(this.$mdDialog, courseDefinition)
      .then((): IPromise<any> => this.FetchData.v3.deleteCourse(this.Path.getClassroomId(), courseDefinition))
      .then((): void => {
        this.curriculums.splice(
          this.curriculums.findIndex(course => course.id === courseDefinition.id),
          1,
        );

        this.$rootScope.$emit(Events.COURSE_DELETED, { courseDefinition });
      });
  }

  private updateCoursesList(): void {
    this.CurriculumsService.updateCurriculums()
      .then((courseDefinitions: any): void => {
        this.curriculums = courseDefinitions;
      });
  }
}
