/**
 * @module Common/Components
 *
 * @description
 * This component is responsible fot showing tooltip on text wraped in span element when
 * it is too long and is truncated
 *
 * @example
 *
 *<long-text-tooltip text="$ctrl.course.name">
 *    <span class="truncate-text">{{ $ctrl.course.name }}</span>
 *</long-text-tooltip>
 *
 */

import { UnbindableCtrlBase } from '../../base-classes';
import { Events } from '../../global-events';

export class LongTextTooltipComponent extends UnbindableCtrlBase {
  static bindings = {
    text: '<',
    direction: '@?',
    query: '@?',
    disabled: '<ngDisabled',
    visible: '<?',
    watchText: '<',
  };
  static controller = LongTextTooltipComponent;
  static templateUrl = 'common/components/longTextTooltip/longTextTooltip.html';
  static transclude = true;

  constructor($rootScope, $element, $timeout, LongTextTooltip) {
    'ngInject';

    super();
    this.$rootScope = $rootScope;
    this.$element = $element;
    this.$timeout = $timeout;
    this.LongTextTooltip = LongTextTooltip;
    this.showTooltip = false;
    this.direction = 'top';
    this.query = null;
    this.disabled = false;
    this.visible = null;
    this.watchText = false;
  }

  $onInit() {
    this.boundEvents.push(
      this.$rootScope.$on(Events.COURSE_UPDATED, () => this.checkIfShowTooltip()),
      this.LongTextTooltip.addOnResizeHandler(() => this.checkIfShowTooltip()),
    );

    if (this.watchText) {
      this.boundEvents.push(
        this.$rootScope.$watch(() => this.text, () => this.checkIfShowTooltip()),
      );
    }

    this.$element.on('mouseover.longTextTooltip', () => {
      this.LongTextTooltip.preventOutdatedScope();
      this.$element.off('mouseover.longTextTooltip');
    });
  }

  $postLink() {
    this.checkIfShowTooltip();
  }

  $onDestroy() {
    super.$onDestroy();
    this.$element.off('mouseover.longTextTooltip');
  }

  checkIfShowTooltip() {
    this.$timeout(() => {
      this.showTooltip =
        !this.disabled &&
        !!this.text &&
        this.LongTextTooltip.isNeeded(this.$element, this.query);
    }, 0, false);
  }
}
