import { UnbindableCtrlBase } from '../../../../common/base-classes';
import { Observer } from '../../../../shared/services/helpers/observer.service.ts';

export class StudentCoursesDetailsComponent extends UnbindableCtrlBase {
  static bindings = {
    courses: '<',
    coursesDefinitions: '<?',
  };
  static controller = StudentCoursesDetailsComponent;
  static templateUrl =
    'components/course-progress/teacher-course-progress/studentCoursesDetails/studentCoursesDetails.html';

  constructor(StudentCoursesDetails, ColleagueEmailsService) {
    'ngInject';

    super();
    this.StudentCoursesDetails = StudentCoursesDetails;
    this.ColleagueEmailsService = ColleagueEmailsService;
    this.studentCoursesDefinitions = { archived: [], current: [], other: [] };
    this.courses = [];
    this.colleagueEmails = [];
    this.coursesDefinitions = null;
    this.pending = true;
    this.student = null;
    this.model = '';
    this.isOpen = false;
    this.isTeacher = this.StudentCoursesDetails.isTeacher;
    this.boundEvents.push(
      this.ColleagueEmailsService.colleagueEmails$
        .subscribe(new Observer(this.onColleagueEmailsUpdate.bind(this))),
    );
  }

  $onInit() {
    if (!this.colleagueEmails.length) {
      this.ColleagueEmailsService.get();
    }

    this.student = this.StudentCoursesDetails.getCurrentStudent();

    this.StudentCoursesDetails.extendStudentObject(this.student)
      .then((student) => (this.student = student))
      .finally(() => {
        if (this.coursesDefinitions) {
          this.pending = false;
        }
      });

    if (!this.coursesDefinitions) {
      this.StudentCoursesDetails.fetch()
        .then(studentCoursesDefinitions =>
          this.StudentCoursesDetails.extendByCourseDetails(
            studentCoursesDefinitions,
            this.courses,
          ),
        )
        .then(studentCoursesDefinitions =>
          this.StudentCoursesDetails.extendByOwner(studentCoursesDefinitions),
        )
        .then(studentCoursesDefinitions =>
          this.StudentCoursesDetails.splitByCategory(studentCoursesDefinitions),
        )
        .then(
          studentCoursesDefinitions =>
            (this.studentCoursesDefinitions = studentCoursesDefinitions),
        )
        .finally(() => (this.pending = false));
    } else {
      this.coursesDefinitions = this.StudentCoursesDetails
        .extendByCourseDetails(this.coursesDefinitions, this.courses);

      this.coursesDefinitions = this.StudentCoursesDetails
        .extendByOwner(this.coursesDefinitions);

      this.studentCoursesDefinitions = this.StudentCoursesDetails
        .splitByCategory(this.coursesDefinitions);
    }
  }

  onFeedbackChange($event) {
    const data = {
      feedback: $event.value,
      feedback_published: $event.isPublished,
    };
    this.updateStudent(data);
  }

  onPrivateNoteChange($event) {
    const data = { id: this.student.id, private_note: $event.value };
    this.updateStudent(data);
  }

  updateStudent(data) {
    this.StudentCoursesDetails.updateStudentDetails(this.student.id, data)
      .then((student) => this.StudentCoursesDetails.applyLastStudentUpdateDate(student))
      .then((student) => {
        // Dont overwrite feedback and private_note, it messes with the ui
        const { feedback, private_note, result, ...rest } = student;
        this.student = {
          ...this.student,
          ...rest,
        };
      });
  }

  getCurrentClassName() {
    return this.StudentCoursesDetails.findClassName(
      this.studentCoursesDefinitions.current,
    );
  }

  close() {
    this.StudentCoursesDetails.goToNoStudentSelectedState();
  }

  editStudentDetails() {
    this.StudentCoursesDetails.showStudentDetailsEditDialog(this.student).then(
      student => this.updateStudent(student),
    );
  }

  onColleagueEmailsUpdate(colleagueEmails) {
    this.colleagueEmails = colleagueEmails;
  }

  isOverallEmpty() {
    return !this.isTeacher && !this.student.feedback;
  }
}
