import React, { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { MoreVert, Check } from '@material-ui/icons';
import MenuItem from '@material-ui/core/MenuItem';
import { ClickAwayListener, Menu } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { colors, fonts } from '../theme';

const StyledMenu = withStyles({
  list: {
    padding: '0px',
  },
})(Menu);

const MoreMenu = (props) => {
  const {
    options, onClick, icon, iconStyle = {}, style = {}, closeOnClick = true,
  } = props;
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClick = (e, option) => {
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    if (onClick) {
      onClick(option);
    }
    if (closeOnClick) {
      setAnchorEl(null);
    }
  };

  const handleClickAway = () => {
    setTimeout(() => setAnchorEl(null));
  };

  return (
    <div style={style}>
      <ClickAwayListener onClickAway={handleClickAway}>
        <IconButton
          aria-label='more'
          aria-controls='long-menu'
          aria-haspopup='true'
          onClick={handleClick}
          style={{ color: colors.white, ...iconStyle }}
        >
          {icon || <MoreVert />}
        </IconButton>
      </ClickAwayListener>
      <StyledMenu open={Boolean(anchorEl)} anchorEl={anchorEl}>
        {options.map((option, i) => {
          if (option.type === 'separator') {
            return (
              <div
                key={i}
                style={{
                  height: '1px',
                  backgroundColor: colors.borderColor,
                }}
              />
            );
          }
          if (option.type === 'title') {
            return (
              <div key={option.title} style={{
                height: '30px', display: 'flex', alignItems: 'center', marginTop: '10px',
              }}>
              <span
                style={{
                  ...fonts.MEDIUM,
                  color: colors.green,
                  marginLeft: '20px',
                  paddingRight: '50px',
                }}
              >
                  {option.title}
                </span>
              </div>
            );
          }
          return (
            <div key={option.title} style={{ display: 'flex', alignItems: 'center' }}>
              <div style={{
                display: 'flex', width: '100%',
              }}>
                <MenuItem
                  style={{
                    ...fonts.MEDIUM,
                    flex: 1,
                  }}
                  onClick={(e) => handleMenuClick(e, option)}
                >
                  {option?.selectable && (
                    <Check
                      style={{
                        color: option.selected
                          ? colors.darkGrey
                          : 'transparent',
                        marginRight: '4px',
                      }}
                    />
                  )}
                  <span style={fonts.MEDIUM}>{option.title}</span>
                </MenuItem>
              </div>
            </div>
          );
        })}
      </StyledMenu>
    </div>
  );
};

export default MoreMenu;
