/**
 * @module Mixpanel/Service
 * @description
 *   Handle Mixpanel analytics script.
 */

export class Mixpanel {
  constructor($window, Config, FetchData, Courses) {
    'ngInject';

    this.$window = $window;
    this.Config = Config;
    this.FetchData = FetchData;
    this.Courses = Courses;
    this.instance = {};
  }

  isPanelDisable() {
    return DEVELOPMENT_ENV ||
      this.Config.apiUrl === 'http://dev.studybee.io' ||
      this.instance.distinct_id.includes('studybee.io');
  }

  get events() {
    return {
      toggle: 'Toggle studybee',
      edit_multiple: 'Multiedit done',
      course_summary: 'Course summary to student',
      course_private_note: 'Course private notes to student',
      result: 'Result to student',
      overall_feedback: 'Overall feedback to student',
      overall_private_note: 'Overall private notes to student',
      feedback: 'Feedback to student',
      private_note: 'Private notes to student',
      visibility: 'Item visibility changed',
      objective_slider: 'Objective slider moved',
      course_slider: 'Course slider moved',
      fetch_course_definition: 'Classroom assigned to course',
      assigned_objectives: 'Objectives assigned to assignment',
      setup_complete: 'Setup complete',
      menu_teacher_assignments: 'Menu item Assignments',
      menu_teacher_assess_course: 'Menu item Course assessments',
      menu_teacher_assignment_objectives: 'Menu item Assign course',
      menu_teacher_synchronize: 'Menu item Refresh',
      menu_teacher_user_setup: 'Menu item School setup',
      menu_student_assignments: 'Menu item Assignments',
      menu_student_course_progression: 'Menu item Course progression',
      menu_student_course_requirements: 'Menu item Course requirements',
      menu_get_help: 'Menu item Get help',
      saving_curriculum_to_library: 'Saving curriculum to library',
      new_assignment_popup: 'New assignment pop up',
      feedback_to_student_on_objective: 'Feedback to student on objective',
      feedback_to_student_on_assignment_objective: 'Feedback to student on objective',
      // this is different event, but it has to be send as the same as above
      feedback_to_student_on_overall_assignment: 'Feedback to student on overall assignment',
      feedback_to_student_on_course_progress: 'Feedback to student on course progress',
      feedback_to_student_with_multiedit: 'Feedback to student with multi edit',
      student_self_assessment_on_objective: 'Student self assessment on objective',
      moving_slider_on_objective_level_in_course: 'Moving slider on objective level in course view',
    };
  }

  /**
   * Init configuration for Mixpanel
   * @memberof module:Mixpanel/Service
   * @param {String} token Token used to identify extension in Mixed Panel
   * @param {String} userEmail Email of user that is logged in Chrome browser
   */
  init(token, userEmail) {
    this.instance.token = token;
    this.instance.distinct_id = userEmail;
  }

  /**
   * Allow to track events (if it's not dev server)
   * @memberof module:Mixpanel/Service
   * @param {String} eventCode Name of the event
   */
  track(eventCode) {
    if (this.isPanelDisable() || !eventCode) {
      return Promise.resolve();
    }

    const eventName = this.getEventName(eventCode);

    const payload = {
      event: eventName,
      properties: {
        distinct_id: this.instance.distinct_id,
        alias: this.instance.distinct_id,
        token: this.instance.token,
        browser: this.$window.navigator.userAgent,
      },
    };

    return this.FetchData.mixpanelTrack(
      this.$window.btoa(JSON.stringify(payload)),
    );
  }

  /**
   * Allow to handle users (if it's not dev server)
   * @memberof module:Mixpanel/Service
   * @param {String} type
   * @param userCountry
   * @param userEducationalSystem
   * @param userSchoolName
   */
  engage(type, userCountry, userEducationalSystem, userSchoolName) {
    if (this.isPanelDisable()) {
      return Promise.resolve();
    }

    let isTeacher = null;
    const currentTime = this.getCurrentDate();

    const payload = {
      $distinct_id: this.instance.distinct_id,
      $token: this.instance.token,
    };

    if (type === 'extension_run') {
      payload.$set = {
        $email: this.instance.distinct_id, // We used user email as distinct_id
      };
    }

    if (type === 'first_time_setup') {
      payload.$set_once = {
        'First time setup': currentTime,
      };

      payload.$set = {
        'User country': userCountry,
        'User educational system': userEducationalSystem,
        'User school name': userSchoolName,
      };
    }

    if (type === 'edit_setup') {
      payload.$set = {
        'Last setup update': currentTime,
        'User country': userCountry,
        'User educational system': userEducationalSystem,
        'User school name': userSchoolName,
      };
    }

    if (type === 'add_role') {
      isTeacher = this.Courses.list.some(courseElem => courseElem.role === 'teacher');

      payload.$set = {
        'User role': isTeacher ? 'Teacher' : 'Student',
      };
    }

    return this.FetchData.mixpanelEngage(
      this.$window.btoa(JSON.stringify(payload)),
    );
  }

  /**
   * Use event code to find event name
   * @memberof module:Mixpanel/Service
   */
  getEventName(eventCode) {
    return this.events[eventCode];
  }

  /**
   * Get current date in YYYY-MM-DD T HH:MM:SS
   * @memberof module:Mixpanel/Service
   */
  getCurrentDate() {
    return moment().toISOString();
  }
}
