export class IdService {
  public static ID_FRAGMENT: string = 's';
  private static pattern: RegExp = new RegExp(`${IdService.ID_FRAGMENT}`, 'g');

  public static getId(format: string = `${IdService.ID_FRAGMENT}${IdService.ID_FRAGMENT}`): string {
    return format.replace(this.pattern, IdService.generateIDFragment);
  }

  private static generateIDFragment(): string {
    return IdService.getRandomNumber().toString(16).slice(2);
  }

  private static getRandomNumber(base: number = Math.floor((1 + Math.random()) * 0x10000)): number {
    return base ^ window.crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> base / 4;
  }
}
