import React, { useEffect, useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import colors from '../theme/colors';
import fonts from '../theme/fonts';

const CssTextField = withStyles({
  root: {
    '& .MuiInputBase-input': {
      ...fonts.LARGE,
      padding: '2px',
    },
    '& .MuiFormLabel-root': {
      ...fonts.SMALL,
    },
    '& label.Mui-focused': {
      color: colors.green,
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: colors.green,
    },
  },
})(TextField);

const GradeCounter = (props) => {
  const {
    name, count: initialCount, id, max, onChange, gradeId, readonly,
  } = props;
  const [count, setCount] = useState(initialCount);

  useEffect(() => {
    setCount(initialCount);
  }, [initialCount]);

  return (
    <div style={{ display: 'flex', alignItems: 'center', marginRight: '8px' }}>
      <span style={{ ...fonts.LARGE, color: colors.green, marginRight: '6px' }}>{name}</span>
      <CssTextField disabled={readonly} type={'number'} style={{ width: '40px' }} value={count} onChange={({ target }) => {
        let { value } = target;
        if (value < 0) {
          value = 0;
        } else {
          value = value >= max ? max : value;
        }
        setCount(value);
        if (onChange) {
          onChange({ count: parseInt(value, 10), id, gradeId });
        }
      }}/>
      <span style={{ ...fonts.LARGE }}>/ {max}</span>

    </div>
  );
};

export default GradeCounter;
