import angular from 'angular';
import { IInputBindingsOptions } from 'angular';

export const Input: (options?: IInputBindingsOptions) => (prototype: object, property: string | symbol) => void = (options?: IInputBindingsOptions): (prototype: object, property: string | symbol) => void => {
  return (prototype: object & { $$bindings?: any }, property: string | symbol): void => {
    const _options: IInputBindingsOptions = angular.merge({}, {
      alias: '',
      bidirectional: false,
      collection: false,
      optional: false,
      string: false,
    }, options);
    const bindingDescription: string = `${_options.string ? '@' : _options.bidirectional ? '=' : '<'}${_options.collection && !_options.string ? '*' : ''}${_options.optional ? '?' : ''}${_options.alias}`;
    const binding: { [property: string]: string } = { [property]: bindingDescription };
    let $$bindings: { [boundProperty: string]: string } = prototype.$$bindings || {};

    $$bindings = angular.merge({}, $$bindings, binding);
    Object.assign(prototype, { $$bindings });
  };
};
