import React, {
  useEffect, useState, useRef, useReducer,
} from 'react';
import { Slider } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { colors, fonts } from '../theme';
import PublishIcon from './publishIcon';

const AirbnbSlider = withStyles({
  root: {
    color: '#3a8589',
    height: 3,
    padding: '13px 0',
    margin: '0px 6px 0px 6px',
  },
  thumb: {
    height: 27,
    width: 27,
    backgroundColor: colors.white,
    border: '1px solid currentColor',
    marginTop: -12,
    marginLeft: -13,
    boxShadow: '#ebebeb 0 2px 2px',
    '&:focus, &:hover, &$active': {
      boxShadow: '#ccc 0 2px 3px 1px',
    },
    '& .bar': {
      // display: inline-block !important;
      height: 9,
      width: 1,
      backgroundColor: 'currentColor',
      marginLeft: 1,
      marginRight: 1,
    },
    '&.Mui-disabled': {
      height: 27,
      width: 27,
      marginTop: -12,
      marginLeft: -13,
    },
    disabled: {
      width: '27px',
      backgroundColor: 'red',
    },
  },
  active: {},
  track: {
    height: 3,
  },
  rail: {
    color: '#d8d8d8',
    opacity: 1,
    height: 3,
  },
})(Slider);

function AirbnbThumbComponent(props) {
  return (
    <span {...props}>
      <span style={fonts.MEDIUM}>{props.label}</span>
    </span>
  );
}

const AssessmentSlider = (props) => {
  const {
    values,
    defaultIndex = 0,
    onChange,
    published,
    onPublish,
    helpText,
    showPublish = true,
    onChangeCommit,
    disabled = false,
    color,
    id = 0,
    multiEdit=false,
  } = props;

  const [currentIndex, setCurrentIndex] = useState(defaultIndex);
  const committedIndex = useRef(defaultIndex);
  const [hoover, setHoover] = useState(false);
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  useEffect(() => {
    setCurrentIndex(defaultIndex);
    committedIndex.current = defaultIndex;
    forceUpdate();
  }, [defaultIndex, id]);

  const handleChangeCommit = (event, value) => {
    if (committedIndex.current !== value) {
      committedIndex.current = value;
      if (onChangeCommit) {
        onChangeCommit(value);
      }
    }
  };
  const handleChange = (event, value) => {
    if (currentIndex !== value) {
      setCurrentIndex(value);
      if (onChange) {
        onChange(value);
      }
    }
  };

  const valueText = () => values[currentIndex]?.name;
  const ariaValueText = () => (hoover ? values[currentIndex]?.name : '');

  const valuesNoLabel = values.map((v) => ({ ...v, label: '' }));
  const markTexts = values.map((v) => ({
    ...v,
    label: <span style={{ ...fonts.TINY, fontSize: '9px' }}>{v.name}</span>,
  }));

  return (
    <div
      style={{
        display: 'flex',
        height: '26px',
        width: '100%',
        marginBottom: '10px',
        position: 'relative',
      }}
    >
      <AirbnbSlider
        ThumbComponent={(p) => AirbnbThumbComponent({ ...p, label: values[currentIndex]?.name })
        }
        onMouseEnter={() => setHoover(true)}
        onMouseLeave={() => setHoover(false)}
        style={{ color: color || values[currentIndex]?.color }}
        onChangeCommitted={handleChangeCommit}
        onChange={handleChange}
        defaultValue={0}
        value={currentIndex}
        valueLabelFormat={valueText}
        getAriaValueText={valueText}
        aria-labelledby="discrete-slider-restrict"
        step={null}
        valueLabelDisplay="auto"
        marks={hoover ? markTexts : valuesNoLabel}
        min={0}
        max={values.length - 1}
        disabled={disabled}
      />
      {showPublish && (
        <PublishIcon size={14} multiEdit={multiEdit} published={published} onClick={onPublish || null} />
      )}
      {!hoover && helpText && (
        <span
          style={{
            ...fonts.TINY,
            position: 'absolute',
            bottom: '-15px',
            right: '30px',
          }}
        >
          {helpText}
        </span>
      )}
    </div>
  );
};

export default AssessmentSlider;
