export const ExecuteGuard: (target: any, propertyKey: string, descriptor: PropertyDescriptor) => void = (target: any, propertyKey: string, descriptor: PropertyDescriptor): void => {
  target.$$isExecutable = descriptor.value;
};

export const Executable: (target: any, propertyKey: string, descriptor: PropertyDescriptor) => void = (target: any, propertyKey: string, descriptor: PropertyDescriptor): void => {
  const methodReference: (...args: any[]) => any = descriptor.value;

  descriptor.value = function (...args: any[]) {
    if (target.$$isExecutable.apply(this)) {
      methodReference.apply(this, args);
    }
  };
};

