import Header from "../../components/header";
import {getTranslation} from "../../i18n";
import {colors, fonts} from "../../theme";
import {Dialog} from "@material-ui/core";
import React from "react";

const styles = {
  dialogFont: {
    ...fonts.MEDIUM,
    color: colors.green,
    cursor: "pointer",
  },
};

const GradeWarningDialog = ({warningDialogText, close}) => {
  return (
    <Dialog open={!!warningDialogText}>
      <div
        style={{
          padding: "0px",
          display: "flex",
          flexDirection: "column",
          width: "250px",
        }}
      >
        <Header
          title={getTranslation("assignConfirmationModalTitle")}
          style={{margin: "0px"}}
        />
        <span style={{...fonts.MEDIUM, padding: "20px"}}>
            {warningDialogText}
          </span>
        <div
          style={{
            padding: "0 20px 20px 20px",
            display: "flex",
            justifyContent: "flex-end",
          }}
        >
            <span
              onClick={() => close()}
              style={styles.dialogFont}
            >
              {getTranslation("cancelBtn")}
            </span>
          <span
            onClick={async () => {
              close(true);
            }}
            style={{...styles.dialogFont, marginLeft: "20px"}}
          >
              {getTranslation("saveBtn")}
            </span>
        </div>
      </div>
    </Dialog>
  );
};

export default GradeWarningDialog;