import { IComponentController, INgModelController } from 'angular';
import { IGradeTextVisibilitySettings } from '../../../../../shared/models/gradeTextVisibilitySettings.model';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import { SiblingComponent } from '../../../../../utils/decorators/sibling-component';
import TeacherSetAssignmentObjectivesModule from '../../../teacherSetAssignmentObjectives.module';
import template from './objectiveGradeTextVisibilitySettings.tpl.html';
import style from './objectiveGradeTextVisibilitySettings.scss';

@Component({
  module: TeacherSetAssignmentObjectivesModule,
  selector: 'objective-grade-text-visibility-settings',
  template: template,
  style: style,
})
export class ObjectiveGradeTextVisibilitySettingsComponent implements IComponentController {

  @SiblingComponent() private ngModel: INgModelController;
  @Input({ optional: true }) private ngDisabled: boolean;

  constructor() {
    'ngInject';

    this.ngDisabled = false;
  }

  public update($viewValue: IGradeTextVisibilitySettings): void {
    this.ngModel.$setViewValue(Object.assign({}, $viewValue));
  }
}
