import { IPromise } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { IColleagueEmail, ColleagueEmail, IColleagueEmailResponse } from '../../models/colleagueEmail.model';
import { BehaviourSubject, IBehaviourSubject } from '../helpers/behaviourSubject.service';
import SharedServicesModule from '../services.module';

export interface IColleagueEmailsService {
  colleagueEmails$: IBehaviourSubject<IColleagueEmail[]>;
  add(email: string): IPromise<IColleagueEmail>;
  get(): IPromise<IColleagueEmail[]>;
  isEmailValid(email?: string): boolean;
  isUnique(email?: string): boolean;
  remove(colleagueEmail: IColleagueEmail): IPromise<void>;
}

export abstract class AbstractColleagueEmailsService implements  IColleagueEmailsService {
  static EMAIL_PATTERN: string = '[a-z0-9!#$%&\'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&\'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?';
  public abstract colleagueEmails$: IBehaviourSubject<IColleagueEmail[]>;
  protected constructor(protected FetchData: any) {};
  public abstract add(email: string): angular.IPromise<IColleagueEmail>;
  public abstract get(): angular.IPromise<IColleagueEmail[]>;
  public abstract isEmailValid(email?: string): boolean;
  public abstract isUnique(email?: string): boolean;
  public abstract remove(colleagueEmail: IColleagueEmail): angular.IPromise<void>;
}

@Service({
  module: SharedServicesModule,
  name: 'ColleagueEmailsService',
})
export class ColleagueEmailsService extends AbstractColleagueEmailsService  {
  public colleagueEmails$: IBehaviourSubject<IColleagueEmail[]>;

  constructor(protected FetchData: any) {
    'ngInject';
    super(FetchData);

    this.colleagueEmails$ = new BehaviourSubject<IColleagueEmail[]>([]);
  }

  public add(email: string): IPromise<IColleagueEmail> {
    return this.FetchData.addColleagueEmail(email)
      .then((responseColleagueEmail: IColleagueEmailResponse): IColleagueEmail => {
        this.get();

        return new ColleagueEmail(responseColleagueEmail);
      });
  }

  public get(): IPromise<IColleagueEmail[]>{
    return this.FetchData.getColleagueEmails()
      .then((responseColleagueEmails: IColleagueEmailResponse[]): IColleagueEmail[] => {
        const colleagueEmails: IColleagueEmail[] =
          this.mapResponseToColleagueEmails(responseColleagueEmails);

        this.colleagueEmails$.next(colleagueEmails);

        return colleagueEmails;
      });
  }
  isEmailValid(email: string = ''): boolean {
    const emailRegExp: RegExp = new RegExp(ColleagueEmailsService.EMAIL_PATTERN, 'i');

    return emailRegExp.test(email);
  }

  public isUnique(email: string = ''): boolean {
    return !this.colleagueEmails$.getValue()
      .find((colleagueEmail: IColleagueEmail): boolean => {
        return colleagueEmail.email.toLowerCase() === email.toLowerCase();
      });
  }

  public remove(colleagueEmail: IColleagueEmail): IPromise<void> {
    return this.FetchData.removeColleagueEmail(colleagueEmail.id).then(() => this.get());
  }

  private mapResponseToColleagueEmails(responseColleagueEmails: IColleagueEmailResponse[]): IColleagueEmail[] {
    return responseColleagueEmails.map(
      (colleagueEmail: IColleagueEmailResponse): IColleagueEmail =>
        new ColleagueEmail(colleagueEmail)
    );
  }
}
