import { Checkbox, Tooltip, withStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { colors, fonts } from "../theme";

const GreenCheckbox = withStyles({
  root: {
    color: colors.borderColorDark,
    paddingLeft: 0,
    marginLeft: "-6px",
    "&$checked": {
      color: colors.green,
    },
    "& .MuiSvgIcon-root": {
      fontSize: "1.5rem",
    },
  },

  checked: {},
})(Checkbox);

const CheckboxWithText = (props) => {
  const {
    subText,
    id,
    text,
    onCheck,
    checked: initialCheck,
    style = {},
    onClick,
    controlled = true,
    textWidth,
    toolTip = '',
    indeterminate:initialIndeterminate,
  } = props;
  const [checked, setChecked] = useState(initialCheck);
  const [indeterminate, setIndeterminate] = useState(initialIndeterminate);

  useEffect(() => {
    setChecked(initialCheck);
  }, [initialCheck, id]);

  useEffect(() => {
    setIndeterminate(initialIndeterminate);
  }, [initialIndeterminate, id]);

  const onChange = (e) => {
    if (controlled) {
      setChecked(e.target.checked);
    }
    if (onCheck) {
      onCheck(e.target.checked, id);
    }
    setIndeterminate(false);
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
  };

  const click = (e) => {
    if (onClick) {
      onClick(e);
    }
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        border: "0px solid",
        ...style,
      }}
    >
      <GreenCheckbox
        inputProps={{ "aria-label": "controlled" }}
        checked={checked}
        indeterminate={indeterminate}
        onChange={onChange}
      />
      <div
        onClick={click}
        style={{
          display: "flex",
          flexDirection: "column",
          cursor: onClick ? "pointer" : "default",
        }}
      >
        <Tooltip title={toolTip}>
          <span
            style={{
              ...fonts.MEDIUM,
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
              width: textWidth,
            }}
          >
            {text}
          </span>
        </Tooltip>
        {subText && (
          <div
            style={{ ...fonts.MEDIUM }}
            dangerouslySetInnerHTML={{
              __html: subText,
            }}
          />
        )}
      </div>
    </div>
  );
};

export default CheckboxWithText;
