import { IComponentController } from 'angular';
import { ListViewType } from '../../../../../../shared/enums/listViewType.enum';
import { Component } from '../../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../../utils/decorators/input.decorator';
import StudentCourseManagementModule from '../../../studentCourseManagement.module';
import style from './cmStudentObjectivesList.scss';
import template from './cmStudentObjectivesList.tpl.html';

@Component({
  module: StudentCourseManagementModule,
  selector: 'cm-student-objectives-list',
  template: template,
  style: style,
})
export class CmStudentObjectivesListComponent implements IComponentController {
  @Input()
  public course: any;
  @Input()
  public objectives: any[];
  @Input()
  public view: ListViewType;

  public readonly listViewType: any;
  constructor() {
    'ngInject';

    this.course = null;
    this.objectives = [];
    this.view = ListViewType.TITLE_AND_DESCRIPTION;
    this.listViewType = ListViewType;
  }
}
