import * as angular from 'angular';
import { IModule, IInterceptorOptions } from 'angular';
import { HttpInterceptorProvider } from '../providers/httpInterceptor.provider';

export const Interceptor: (options?: IInterceptorOptions) => any = (options: IInterceptorOptions = {}): <T extends { new(...args: any[]): {} }>(constructor: T) => any => {

  const getOrCreateInterceptorModule: () => IModule = (): IModule => {
    const moduleName: string = 'app';
    let module: IModule;

    if (options.module) {
      try {
        module = angular.module(`${options.module}`);
      } catch (err) {
        module = null;
      }
    }

    if (!module) {
      try {
        module = angular.module(`${moduleName}`);
      } catch (err) {
        console.error(`Error on register interceptor in module ${moduleName}. Module ${moduleName} is not exists.`, err);
        module = null;
      }
    }

    return module;
  };

  const buildFactoryFunction: (interceptorFactory: { new(...args: any[]): any } ) => any = (interceptorFactory: { new(...args: any[]): any } ): any => {
    const factory: Function = (...args: any[]): any => {
      const interceptor: any = new interceptorFactory(...args);

      Object.keys(Object.getPrototypeOf(interceptor)).forEach((key: string): void => {
        if (interceptor[key] instanceof Function) {
          interceptor[key] = interceptor[key].bind(interceptor);
        }
      });

      return interceptor;
    };

    return [...interceptorFactory.$inject || [], factory] as any;
  };

  return <T extends { new(...args: any[]): {} }>(constructor: T): T => {
    const module: IModule = getOrCreateInterceptorModule();

    if (module) {
      module.config((HttpInterceptorProvider: HttpInterceptorProvider) => {
        'ngInject';
        HttpInterceptorProvider.add(buildFactoryFunction(constructor), options.order);
      });
    }

    return constructor;
  };
};

