import { getTranslation } from '../../../../../react_src/i18n';

export const LocalizeFilter = ($sce) => {
  'ngInject';

  const translate = (input) => getTranslation(input) || input;

  const findTranslationKey = (input) => input.replace(
    /__MSG_(\w+)__/g,
    (match, v1) => (v1 ? translate(v1) : ''),
  );

  return (input = '', trustAs = 'trustAsHtml') => {
    const translation = input.startsWith('__MSG_')
      ? findTranslationKey(input)
      : translate(input);

    return trustAs && $sce[trustAs] ? $sce[trustAs](translation || input) : translation || input;
  };
};
