import angular, { IQService } from 'angular';
import { RequestType } from '../../shared/enums/requestType.enum';
import { HttpResponse } from '../../shared/models/httpResponse.model';
import { IInterceptor } from '../../shared/models/interceptor.model';
import { Interceptor } from '../decorators/interceptor.decoreator';
import { HttpStatusCode } from '../enums/httpStatusCode.enum';
import { UserInfo } from '../services/userInfo.service';
import UtilsModule from '../utils.module';
import {StateService} from '@uirouter/core';


@Interceptor({
  module: UtilsModule,
})
export class UserPreferencesInterceptor implements IInterceptor {

  private readonly urlRegex: RegExp;
  private UserInfo?: UserInfo;

  constructor(private $q: IQService, private $injector: angular.auto.IInjectorService, protected $state: StateService,) {
    'ngInject';

    this.urlRegex = /\/schools\/user_setup\/?$/;
  }

  public response(response: HttpResponse): PromiseLike<HttpResponse> {
    this.prepareUserInfoService();

    if (response.config.method === RequestType.GET && this.urlRegex.test(response.config.url)) {
      this.UserInfo.hasSetup = true;
    }

    return this.$q.resolve(response);
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> {
    this.prepareUserInfoService();

    if (response.status === HttpStatusCode.NOT_FOUND && this.urlRegex.test(response.config.url)) {
      this.UserInfo.hasSetup = false;
      this.$state.go('userSetup');
    }

    return this.$q.reject(response);
  }

  private prepareUserInfoService(): void {
    this.UserInfo = this.$injector.get('UserInfo');
  }
}
