import { IComponentController, IRootScopeService, IScope, ITimeoutService } from 'angular';
import { StateParams, StateService } from '@uirouter/core';
import { IBreadcrumbsService } from '../../../../../common/components/breadcrumbs/breadcrumbs.service';
import { Events } from '../../../../../common/global-events';
import { ListViewType } from '../../../../../shared/enums/listViewType.enum';
import { Breadcrumb } from '../../../../../shared/models/breadcrumb.model';
import { ICoverageMatrix } from '../../../../../shared/models/coverageMatrix.model';
import { ICoverageMatrixService } from '../../../../../shared/services/coverageMatrix/coverageMatrix.service';
import { IdService } from '../../../../../shared/services/helpers/id.service';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherCourseProgressModule from '../../teacher-course-progress.module';
import style from './objectivesList.scss';
import template from './objectivesList.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'objectivesListTeacher',
  template: template,
  style: style,
})
export class ObjectivesListComponent implements IComponentController {
  @Input()
  public assessments: any[];
  @Input()
  public courseSelected: any;

  public readonly listViewType: any;
  public student: any;
  public isEditable: boolean;
  public coverageMatrixes: ICoverageMatrix[];
  public coverageMatrix: ICoverageMatrix;
  public isCoverageMatrixView: boolean;
  public informationView: ListViewType;

  constructor(private $rootScope: IRootScopeService, private $scope: IScope, private $state: StateService,
              private readonly $stateParams: StateParams, private $timeout: ITimeoutService,
              private BreadcrumbsService: IBreadcrumbsService, private FetchData: any,
              private CoverageMatrixService: ICoverageMatrixService) {
    'ngInject';

    this.assessments = [];
    this.courseSelected = $stateParams.courseSelected;
    this.listViewType = ListViewType;
    this.student = $stateParams.student;
    this.isEditable = false;
    this.coverageMatrixes = [];
    this.coverageMatrix = null;
    this.isCoverageMatrixView = false;
    this.informationView = ListViewType.TITLE_AND_DESCRIPTION;
  }

  public $onInit(): void {
    this.setIdForAssessments();
    this.isEditable = this.$stateParams.courseDefinition &&
      this.$stateParams.courseDefinition.teacher_in_class;

    if (!this.student) {
      this.student = this.$stateParams.student;
    }

    this.coverageMatrixes = this.CoverageMatrixService.map(this.assessments);
    this.coverageMatrix = this.coverageMatrixes[0];
  }

  public navigateToLearningObjective(assessment: any): void {
    const breadcrumbs = [
      ...this.$stateParams.breadcrumbs,
      new Breadcrumb(
        this.courseSelected.course_definition_name,
        this.BreadcrumbsService.createGoToAction(),
      ),
    ];

    const params = Object.assign({}, this.$stateParams, {
      breadcrumbs,
      student: this.student || this.$stateParams.student,
      course: this.courseSelected,
      objective: assessment.objective,
      ordering: '',
      filters: null,
    });

    this.$state.go(
      'teacher-course-progress.objective-details.learning-assignments',
      params,
    );
  }

  public toggleAssessment(assessment: any): void {
    assessment.open = !assessment.open;
  }

  public updateAssessmentData(assessmentData: any): void {
    this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
    this.FetchData.v3
      .updateCourseDefinitionObjectiveAssessment(
        this.student.id,
        this.courseSelected.course_definition,
        assessmentData,
      )
      .then((assessmentResponse): void => {
        // update current assessments list
        this.assessments = this.assessments.map((assessment: any): any => {
          if (assessment.id === assessmentResponse.id) {
            // Dont overwrite feedback and private_note, it messes with the ui
            const {feedback, private_note, result, ...rest} = assessmentResponse;
            Object.assign(assessment, rest, { $id: IdService.getId() });
          }
          return assessment;
        });
      })
      .then((): void => {
        this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);
        this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
      });
  }

  public isNoteAvailable(assessment: any): boolean {
    return !!assessment && !!assessment.private_note;
  }

  public isFeedbackAvailable(assessment: any): boolean {
    return (!!assessment && assessment.feedback_published && !!assessment.feedback);
  }

  public onCoverageMatrixChange(coverageMatrix: ICoverageMatrix): void {
    this.coverageMatrix = coverageMatrix;
  }

  public onViewChange(listViewSwitchType: ListViewType): void {
    if ([ListViewType.COVERAGE_MATRIX, ListViewType.STATUS].includes(listViewSwitchType)) {
      this.isCoverageMatrixView = !!(listViewSwitchType & ListViewType.COVERAGE_MATRIX);
    } else {
      this.informationView = listViewSwitchType;
    }
  }

  public getCoverageMatrixFromAssessment(assessment: any): ICoverageMatrix {
    return assessment.coverage_matrix_grade_systems
      .find((coverageMatrixGradeSystem: ICoverageMatrix): boolean => {
        return this.coverageMatrix && coverageMatrixGradeSystem.id === this.coverageMatrix.id;
      });
  }

  public expandAll(): void {
    this.changeOpenStateForAll(true);
  }

  public collapseAll(): void {
    this.changeOpenStateForAll(false);
  }

  private changeOpenStateForAll(state): void {
    this.$timeout(() => {
      this.assessments = this.assessments.map((assessment) => {
        assessment.open = state;

        return assessment;
      });
    }, 0);
  }

  private setIdForAssessments(): void {
    this.assessments.forEach((assessment: any): void => {
      assessment.$id = IdService.getId();
    });
  }

  get hasAnyAssessmentOpened(): any[] {
    return this.assessments.find(assessment => assessment.open);
  }
}
