import Header from "../../components/header";
import {getTranslation} from "../../i18n";
import {colors, fonts} from "../../theme";
import {Dialog} from "@material-ui/core";
import React from "react";

const styles = {
  dialogFont: {
    ...fonts.MEDIUM,
    color: colors.green,
    cursor: "pointer",
  },
};

const SaveDialog = (props) => {
  const {open, onSave, onClose, onCancel} = props;
  return (
    <Dialog open={open}>
      <div
        style={{
          padding: "0px",
          display: "flex",
          flexDirection: "column",
          width: "250px",
        }}
      >
        <Header
          title={getTranslation("saveBtn").toUpperCase()}
          style={{margin: "0px"}}
        />
        <span style={{...fonts.MEDIUM, padding: "20px"}}>
            {getTranslation("saveChangesModalTitle")}
          </span>
        <div
          style={{
            padding: "0 20px 20px 20px",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
            <span
              onClick={onClose}
              style={{...styles.dialogFont}}
            >
              {getTranslation("dontSaveBtn")}
            </span>
          <div>
              <span
                onClick={onCancel}
                style={styles.dialogFont}
              >
                {getTranslation("cancelBtn")}
              </span>
            <span
              onClick={onSave}
              style={{...styles.dialogFont, marginLeft: "20px"}}
            >
                {getTranslation("saveBtn")}
              </span>
          </div>
        </div>
      </div>
    </Dialog>
  );
};

export default SaveDialog;