import React, { useContext } from "react";
import { Menu } from "@material-ui/icons";
import { colors, fonts } from "../theme";
import { ReactComponent as PaneIcon } from "../images/pane.svg";
import LinearProgress from "../components/LinearProgress";
import { GlobalContext } from "../utils";
import { getTranslation } from "../i18n";
import Tooltip from "../components/tooltip";

const AssignmentsListHeader = (props) => {
  const context = useContext(GlobalContext);
  const { toggleMenu, openDrawer } = context;
  const { title, gridOpen = false, toggleGrid, isTopic=true } = props;

  return (
    <div
      style={{
        width: "100%",
        height: "60px",
        display: "flex",
        backgroundColor: isTopic?colors.topic:colors.green,
        border: "0px solid blue",
        position: "relative",
      }}
    >
      <div
        style={{
          display: "flex",
          flex: 1,
          padding: "14px",
          border: "0px solid red",
        }}
      >
        <Menu
          style={{
            cursor: "pointer",
            color: colors.white,
            height: "25px",
            marginTop: "2px",
            marginRight: "4px",
          }}
          onClick={openDrawer}
        />
        <p
          style={{
            ...fonts.LARGE,
            color: colors.white,
            marginLeft: "14px",
            marginTop: "4px",
            fontWeight: "500",
          }}
        >
          {title}
        </p>
        {gridOpen && (
          <div
            onClick={toggleGrid}
            style={{
              position: "absolute",
              top: "20px",
              right: "16px",
              cursor: "pointer",
            }}
          >
            <Tooltip title={getTranslation("collapseGridTooltip")}>
              <div>
                <PaneIcon />
              </div>
            </Tooltip>
          </div>
        )}
      </div>
      <div
        style={{
          position: "absolute",
          bottom: 0,
          left: 0,
          right: 0,
        }}
      >
        <LinearProgress />
      </div>
    </div>
  );
};

export default AssignmentsListHeader;
