import { Service } from '../../utils/decorators/service.decorator';
import TeacherSetAssignmentObjectivesModule from './teacherSetAssignmentObjectives.module';

export interface ITeacherSetAssignmentObjectivesService {
  getStoredGradeSystemId(): number;
  setStoredGradeSystemId(id: string | number): void;
}

@Service({
  module: TeacherSetAssignmentObjectivesModule,
  name: 'TeacherSetAssignmentObjectivesService'
})
export class TeacherSetAssignmentObjectivesService implements ITeacherSetAssignmentObjectivesService {
  static localStorageGradeSystemIdKey: string = 'gradeSystemId';

  constructor() {
    'ngInject';
  }

  public getStoredGradeSystemId(): number {
    return +(localStorage.getItem(TeacherSetAssignmentObjectivesService.localStorageGradeSystemIdKey));
  }

  public setStoredGradeSystemId(id: string | number): void {
    localStorage.setItem(
      TeacherSetAssignmentObjectivesService.localStorageGradeSystemIdKey,
      `${id}`,
    );
  }
}
