export const MixpanelTextEvent = (Mixpanel) => {
  'ngInject';

  return {
    restrict: 'A',
    require: 'ngModel',
    link($scope, $element, $attrs, ngModel) {
      let feedbackUpdated = false;

      // let's watch the model for changes
      $scope.$watch(() => ngModel.$modelValue, (oldVal, newVal) => {
        feedbackUpdated = oldVal !== newVal;
      });

      const update = () => {
        if (feedbackUpdated) {
          feedbackUpdated = false;
          Mixpanel.track($attrs.mixpanelTextEvent);
        }
      };

      // remove focus event
      if ($element.is('input, textarea')) {
        $element.on('blur.mixpanelTextEvent', update);
      } else {
        $element.find('input, textarea').on('blur.mixpanelTextEvent', update);
      }
    },
  };
};
