import angular, { IChangesObject, IComponentController } from 'angular';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { HelpButtonService } from '../../services/helpButton/helpButton.service';
import { IPopupService } from '../../services/popup/popup.service';
import { TabService } from '../../services/tab/tab.service';
import SharedComponentsModule from '../components.module';
import style from './helpButton.scss';
import template from './helpButton.tpl.html';
import { getTranslation } from '../../../../../../react_src/i18n';

@Component({
  module: SharedComponentsModule,
  selector: 'help-button',
  template: template,
  style: style,
})
export class HelpButtonComponent implements IComponentController {

  @Input({ string: true }) public link: string;
  @Input({ optional: true }) public popup: boolean;

  public isVisible: boolean;

  private url: string;
  private window: Window;

  constructor(private HelpButtonService: HelpButtonService) {
    'ngInject';

    this.link = '';
    this.popup = false;
    this.isVisible = false;
    this.url = '';
    this.window = null;
  }

  public $onInit(): void {
    this.refreshUrlStatus();
  }

  public $onChanges(onChangesObj: angular.IOnChangesObject): void {
    const changedLink: IChangesObject<string> = onChangesObj['link'];

    if (changedLink && !changedLink.isFirstChange()) {
      this.refreshUrlStatus(changedLink.currentValue);
    }
  }

  public onClick(): void {
    this.HelpButtonService.open(this.url, this.popup);
  }

  private refreshUrlStatus(link: string = this.link) {
    this.url = getTranslation(link);
    this.isVisible = !!this.url;
  }
}
