import {colors, fonts} from "../../theme";
import {getTranslation} from "../../i18n";
import EditAssessmentCriteriaItem from "./editAssessmentCriteriaItem";
import React, {useState} from "react";
import Toast from "../../components/toast";

const CourseDefinitions = ({assignment, onChange}) => {
  const [showCurriculumToast, setShowCurriculumToast] = useState(false);

  return (
    <div style={{marginTop: "20px"}}>
      <span style={{...fonts.SMALL, color: colors.green}}>
        {getTranslation("courseSelect")}
      </span>
      {assignment?.courseDefinitions
        .sort((a, b) => a.name.localeCompare(b.name))
        .map((c) => {
            return (
              <EditAssessmentCriteriaItem
                text={c.name}
                checkbox
                checked={() => c.selected}
                onChange={
                  (checked) => {
                    if (assignment?.courseDefinitions.filter(cd => cd.selected)?.length === 1 && !checked) {
                      setShowCurriculumToast(true);
                    } else {
                      c.selected = checked;
                    }
                    onChange(assignment);
                  }
                }/>
            );
          }
        )}
      <Toast timeout={3000} onClose={() => setShowCurriculumToast(false)} open={showCurriculumToast}
             message={getTranslation("chooseAtLeastOneCurriculumTooltip")}/>
    </div>

  );
};

export default CourseDefinitions;
