import {Inject, Injectable, Optional} from '@angular/core';
import {forkJoin, from, Observable, of} from 'rxjs';
import {UserQuery} from './user.query';
import {UserStore} from './user.store';
import {HttpService} from '../../../core/http/http.service';
import {catchError, map, switchMap, take, tap} from 'rxjs/operators';
import {PRE_FETCH_USER_DATA, User, UserSetup} from './user.model';

@Injectable({providedIn: 'root'})
export class UserService {
  constructor(private userStore: UserStore,
              private userQuery: UserQuery,
              protected http: HttpService,
              @Inject(PRE_FETCH_USER_DATA) @Optional() protected preFetchUserData: User) {

    if (preFetchUserData !== null) {
      this.userStore.update(state => ({...state, ...preFetchUserData}));
      this.userStore.setHasCache(true);
    }
  }

  public fetchUser(): Observable<User> {
    let userDataSource: Observable<User>;

    if (this.userQuery.getHasCache()) {
      userDataSource = this.userQuery.select();
    } else {
      userDataSource = forkJoin({
        user: this.http.get<User>('/users/user_data/'),
        userInfo: from(new Promise<User>(resolve => chrome.runtime.sendMessage({action: 'getUserInfo'}, resolve)))
      }).pipe(
        map(sources => ({...sources.user, ...{google_id: sources.userInfo.id, email: sources.userInfo.email}} as User)),
        switchMap(user => {
            if (user.is_teacher) {
              return this.http.get<User>('/schools/user_setup/').pipe(
                catchError(() => of(user)),
                map(response => ({...user, ...response})),
              );
            }

            return of(user);
          }
        ),
        take(1),
        tap(user => this.userStore.update({...user})),
        tap(() => this.userStore.setHasCache(true)),
      );
    }

    return userDataSource;
  }

  public updateUser(user: Partial<User>): void {
    this.userStore.update(state => ({...state, ...user}));
  }

  public updateUserSetup(userSetup: UserSetup) {
    return this.http.post<UserSetup>('/schools/user_setup/', userSetup).pipe(
      take(1),
      tap(response => this.userStore.update(state => ({...state, ...response}))),
    );
  }
}
