import React, {useEffect, useRef, useState} from 'react';
import {colors, fonts} from './theme';
import {getTranslation} from './i18n';
import SecondaryButton from "./components/secondaryButton";
import PrimaryButton from "./components/primaryButton";

const imgSrc = process.env.REACT_APP_STANDALONE ? 'https://is2-ssl.mzstatic.com/image/thumb/Purple114/v4/be/59/64/be596489-6244-84bd-26c3-701bfcb3e3b3/source/256x256bb.jpg' : `chrome-extension://${chrome.runtime.id}/images/icon-128.png`;
const NewAssignmentDialog = () => {
  const [show, setShow] = useState();
  const lastStorageChange = useRef();

  const processStorage = async () => {
    chrome.storage.sync.get(
      ['newAssignment'],
      async (change) => {
        if (change?.newAssignment) {
          const title = change.newAssignment;
          setShow(title);
          chrome.storage.sync.set({newAssignment: 0});
        }
      },
    );
  };

  const storageListener = async (change) => {
    const changes = Object.keys(change).map((key) => {
      const obj = {};
      obj[key] = change[key].newValue;
      return obj;
    });
    const [firstChange] = changes;
    if (firstChange !== lastStorageChange.current) {
      lastStorageChange.current = firstChange;
      await processStorage(firstChange);
    }
  };

  useEffect(() => {
    if (!process.env.REACT_APP_STANDALONE) {
      chrome.storage.onChanged.addListener(storageListener);
      return function cleanup() {
        if (!process.env.REACT_APP_STANDALONE) {
          chrome.storage.onChanged.removeListener(storageListener);
        }
      };
    }
  }, []);

  if (!show) {
    return null;
  }
  return (
    <div style={{
      position: 'fixed',
      top: '0px',
      right: '0px',
      width: '310px',
      backgroundColor: colors.primary,
      borderRadius: '6px',
      margin: '20px',
      zIndex: '100000',
      boxShadow: '0px 5px 10px rgba(0,0,0,0.3)',
    }}>
      <div style={{display: 'flex', padding: '14px', alignItems: 'center'}}>
        <img src={imgSrc} style={{
          width: '25px', height: '25px', backgroundColor: colors.white, padding: '8px', borderRadius: '50px',
        }}/>
        <div style={{display: 'flex', flexDirection: 'column', marginLeft: '10px'}}>
          <span style={{...fonts.LARGE, fontWeight: '500'}}>
            {getTranslation('assignObjectivesToAssignmentToast')}
            <span style={{...fonts.LARGE, fontWeight: '800'}}>{show}</span>
            {getTranslation('assignObjectivesToAssignmentToast2')}
          </span>
        </div>
      </div>
      <div style={{borderTop: '1px solid ' + colors.borderOnPrimary, width: '100%', margin: '10px 0px 10px 0px'}}/>
      <div
        style={{
          marginBottom: '10px',
          display: 'flex',
          justifyContent: 'flex-end'
        }}>
        <SecondaryButton
          title={getTranslation('assignObjectivesToastPostponeButtonLabel')}
          onClick={() => {
            chrome.storage.sync.set({'disconnectNewAssignment': show}, () => console.log('disconnect assignment: ', show));
            setShow(false);
          }
          }/>
        <PrimaryButton
          title={getTranslation('addBtn')}
          onClick={() => {
            chrome.storage.sync.set({'connectNewAssignment': show}, () => console.log('connect assignment: ', show));
            setShow(false);
          }}/>
      </div>
    </div>
  );
};

export default NewAssignmentDialog;
