import angular from 'angular';

export interface IDialogService {
  readonly DEFAULT_OPTIONS: ng.material.IDialogOptions;
}

export abstract class DialogService implements IDialogService {
  public readonly DEFAULT_OPTIONS: ng.material.IDialogOptions = {
    bindToController: true,
    controller: class DialogController {
      constructor(public $mdDialog: ng.material.IDialogService) {
        'ngInject';
      }
    },
    controllerAs: '$ctrl',
    parent: angular.element(document.body),
  };

  constructor(protected $mdDialog: ng.material.IDialogService) {
    'ngInject';
  }

  protected buildOptions(options: ng.material.IDialogOptions = this.DEFAULT_OPTIONS): ng.material.IDialogOptions {
    return Object.assign({}, this.DEFAULT_OPTIONS, options);
  }
}
