import { Service } from '../../../utils/decorators/service.decorator';
import { ITabMessage, TabMessage } from '../../models/tabMessage.model';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'TabService',
})
export class TabService {
  public tabs: Map<string, chrome.tabs.Tab>;

  constructor() {
    this.tabs = new Map<string, chrome.tabs.Tab>();
  }

  public open(url: string): Promise<chrome.tabs.Tab> {
    const openTabMessage: ITabMessage = this.tabs.has(url) ? this.getFocusMessage(url) : this.getOpenTabMessage(url);

    return new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.runtime.sendMessage(openTabMessage, (tab: chrome.tabs.Tab): void => {
        this.onOpen(url, tab);
        this.listenOnRemove(tab);
        resolve(tab);
      });
    });
  }

  public update(tab: chrome.tabs.Tab, updateProperties: chrome.tabs.UpdateProperties): Promise<chrome.tabs.Tab> {
    const openTabMessage: ITabMessage = this.getUpdateMessage(tab, updateProperties);

    return new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.runtime.sendMessage(openTabMessage, resolve);
    });
  }

  public get(tab: chrome.tabs.Tab): Promise<chrome.tabs.Tab | null> {
    const openTabMessage: ITabMessage = this.getGetMessage(tab);

    return new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.runtime.sendMessage(openTabMessage, resolve);
    });
  }

  protected listenOnRemove(tab: chrome.tabs.Tab): void {
    const onRemoveListenerMessage: ITabMessage = this.getOnRemoveListenerMessage(tab);

    chrome.runtime.sendMessage(onRemoveListenerMessage, (tabId: number): void => {
      this.onClose(tabId);
    });
  }

  protected getOpenTabMessage(url: string): ITabMessage {
    const openTabMessage: ITabMessage = this.getTabMessage('openTab');
    openTabMessage.createProperties.url = url;

    return openTabMessage;
  }

  protected getFocusMessage(url: string): ITabMessage {
    const tab: chrome.tabs.Tab = this.tabs.get(url);
    const focusTabMessage: ITabMessage = this.getTabMessage('focusTab');
    focusTabMessage.updateProperties.active = true;
    focusTabMessage.tabId = tab.id;

    return focusTabMessage;
  }

  protected getUpdateMessage(tab: chrome.tabs.Tab, updateProperties: chrome.tabs.UpdateProperties): ITabMessage {
    const updateTabMessage: ITabMessage = this.getTabMessage('updateTab');
    updateTabMessage.tabId = tab.id;
    updateTabMessage.updateProperties = { ...updateProperties };

    return updateTabMessage;
  }

  protected getGetMessage(tab: chrome.tabs.Tab): ITabMessage {
    const updateTabMessage: ITabMessage = this.getTabMessage('getTab');
    updateTabMessage.tabId = tab.id;

    return updateTabMessage;
  }

  protected getOnRemoveListenerMessage(tab: chrome.tabs.Tab): ITabMessage {
    const onRemoveListenerMessage: ITabMessage = this.getTabMessage('registerOnRemovedTab');
    onRemoveListenerMessage.tabId = tab.id;

    return onRemoveListenerMessage;
  }

  protected getTabMessage(action: string): ITabMessage {
    return new TabMessage(action);
  }

  protected onOpen(url: string, tab: chrome.tabs.Tab) {
    this.tabs.set(url, tab);
  }

  protected onClose(tabId: number): void {
    this.tabs.forEach((tab: chrome.tabs.Tab, url: string): void => {
      if (tab.id === tabId) {
        this.tabs.delete(url);
      }
    });
  }
}
