export class UserSetupController {
  constructor($state) {
    'ngInject';

    this.$state = $state;
    this.showMenuToggle = false;
    this.backToState = null;
    this.stateContext = null;
  }

  onBackToState() {
    const params = Object.assign({}, this.stateContext, { backToState: null });

    this.$state.go(this.backToState, params);
  }
}
