import { getTranslation } from '../../../../../react_src/i18n';

const dialogParentElement = angular.element(document.body);

export const showHintDialog = ($mdDialog, $timeout, stateName, $cookies) => {
  const showedHint = $cookies.get(`${stateName}-hint`);

  $timeout(() => {
    if (showedHint === undefined) {
      $cookies.put(`${stateName}-hint`, '', {
        path: '/',
        expires: moment().add(1, 'year').toDate(),
      });

      let headerText;
      let contentText;

      switch (stateName) {
        case 'studentCourseManagement':
          headerText = getTranslation('studentManagementHintHeader');
          contentText = getTranslation('studentManagementHintContent');
          break;
        case 'student-course-progress':
          headerText = getTranslation('studentProgressHintHeader');
          contentText = getTranslation('studentProgressHintContent');
          break;
        default:
      }

      if (headerText && contentText) {
        $mdDialog.show({
          parent: dialogParentElement,
          templateUrl: 'common/dialogs/hint-dialog.html',
          controller: class {
            constructor() {
              'ngInject';

              this.$mdDialog = $mdDialog;
              this.$onInit();
            }

            $onInit() {
              this.headerText = headerText;
              this.contentText = contentText;
            }
          },
          controllerAs: '$ctrl',
        });
      }
    }
  }, 700);
};
