import {Injectable} from '@angular/core';
import {EntityState, EntityStore, EntityUIStore, runStoreAction, StoreActions, StoreConfig} from '@datorama/akita';
import {Assignment, AssignmentsUI, AssignmentUI, createAssignments, createAssignmentsUI, createAssignmentUI} from './assignment.model';

export interface AssignmentsState extends EntityState<Assignment> {
  ui: AssignmentsUI;
}

export interface AssignmentUIState extends EntityState<AssignmentUI> {
}

@Injectable({providedIn: 'root'})
@StoreConfig({name: 'assignments'})
export class AssignmentsStore extends EntityStore<AssignmentsState> {
  ui: EntityUIStore<AssignmentUIState>;

  constructor() {
    super(createAssignments({ui: createAssignmentsUI()}));
    this.createUIStore().setInitialEntityState(createAssignmentUI);
    this.setHasCache(false);
  }

  forceRefreshEntities() {
    runStoreAction(this.storeName, StoreActions.UpdateEntities, {
      payload: {
        data: {},
        entityIds: 0,
      },
    });
  }
}
