import { StateParams, StateService } from '@uirouter/core';
import angular, { IRootElementService, ITimeoutService, IWindowService } from 'angular';
import $ from 'jquery';
import { IdService } from '../../../shared/services/helpers/id.service';
import { Service } from '../../../utils/decorators/service.decorator';
import CommonComponentsModule from '../components.module';
import { IBreadcrumb, IBreadcrumbGoTo } from '../../../shared/models/breadcrumb.model';
import cloneDeep from 'lodash.clonedeep';

export interface IBreadcrumbsService {
  addOnResizeHandler(handler: Function & { id?: string }): Function;
  createGoToAction($stateParams?: StateParams, stateName?: string): IBreadcrumbGoTo;
  createHandler($element: IRootElementService): Function;
  hasEnoughBreadcrumbs(breadcrumbs?: IBreadcrumb[] | { length: number }): boolean;
  removeBreadcrumbsWithoutLabel(breadcrumbs?: IBreadcrumb[]): IBreadcrumb[];
}

@Service({
  module: CommonComponentsModule,
  name: 'BreadcrumbsService',
})
export class BreadcrumbsService implements IBreadcrumbsService {
  private onResizeHandlers: Function[];
  private debounce: number;

  constructor(private $state: StateService, private $stateParams: StateParams, private $timeout: ITimeoutService, private $window: IWindowService) {
    'ngInject';

    this.onResizeHandlers = [];
    this.debounce = 0;
    angular.element($window).on('resize', () => this.onResize());
  }

  public addOnResizeHandler(handler: Function & { id?: string }): Function {
    handler.id = IdService.getId();
    this.onResizeHandlers.push(handler);

    return this.createUnregisterFunction(handler.id);
  }

  public createGoToAction($stateParams: StateParams = this.$stateParams, stateName: string = this.$state.current.name): IBreadcrumbGoTo {
    const params = cloneDeep($stateParams);
    const goToAction: IBreadcrumbGoTo = () => {
      this.$state.go(`${stateName}`, params);
    };

    goToAction.params = params;

    return goToAction;
  }

  public createHandler($element: IRootElementService): Function {
    return () => {
      window.requestAnimationFrame(() => {
        const element: HTMLElement = $element[0];
        const $breadcrumbs: JQuery = $element.find('.breadcrumb');
        const $textContainers: JQuery = $breadcrumbs.find('span');

        if (this.hasEnoughBreadcrumbs($breadcrumbs)) {
          const availableWidth: number = element.clientWidth -
            (($breadcrumbs.length - 1) * $breadcrumbs.find('.breadcrumb-chevron')[0].clientWidth);
          let totalWidth: number = 0;

          $textContainers.each((index: number, span: HTMLSpanElement) => {
            totalWidth += span.scrollWidth;
          });

          if (totalWidth >= availableWidth) {
            const maxWidthPerBreadcrumb: number = availableWidth / $breadcrumbs.length;

            let remainingOffset: number = 0;
            const oversizeContainers: JQuery[] = [];

            $textContainers.each((index: number, item: HTMLElement) => {
              const $item: JQuery = $(item);

              if (item.scrollWidth >= maxWidthPerBreadcrumb) {
                $item.animate({ 'max-width': maxWidthPerBreadcrumb }, 0);
                oversizeContainers.push($item);
              } else {
                remainingOffset += (maxWidthPerBreadcrumb - item.scrollWidth);
                $item.removeAttr('style');
              }
            });

            const extraSize: number = (remainingOffset / oversizeContainers.length) - 1;
            oversizeContainers.forEach(($item: JQuery) => {
              $item.animate({ 'max-width': `+=${extraSize}` }, 0);
            });
          } else {
            $textContainers.removeAttr('style');
          }
        }
      });
    };
  }

  public hasEnoughBreadcrumbs(breadcrumbs: IBreadcrumb[] | { length: number } = []): boolean {
    return breadcrumbs.length > 1;
  }

  public removeBreadcrumbsWithoutLabel(breadcrumbs: IBreadcrumb[] = []): IBreadcrumb[] {
    return breadcrumbs.filter((breadcrumb: IBreadcrumb) => breadcrumb.label);
  }

  private onResize(): void {
    this.onResizeHandlers.forEach(handler => handler());
  }

  private createUnregisterFunction(handlerId: string): Function {
    return () => {
      this.onResizeHandlers = this.onResizeHandlers
        .filter((handler: Function & { id?: string }): any => handler.id !== handlerId);
    };
  }
}
