import Header from "../../components/header";
import {colors} from "../../theme";
import Card from "../../components/card";
import AccordionItem from "../../components/accordionItem";
import FeedbackInput from "../../components/feedbackInput";
import {getTranslation} from "../../i18n";
import CheckboxWithText from "../../components/CheckboxWithText";
import React, {useContext} from "react";
import {GlobalContext} from "../../utils";

const Objectives = ({assignment, selectedGradeSystem, onChange}) => {
  const context = useContext(GlobalContext);
  const {showObjectiveDetails} = context;

  const {courseDefinitions} = assignment;
  if (!courseDefinitions) {
    return null;
  }

  const handleObjectiveCheck = (checked, id) => {
    assignment.courseDefinitions.forEach(cd => {
      cd.objectives.forEach(o => {
        if (o.id === id) {
          o.selected = checked;
        }
      });
    });
    onChange({...assignment});
  };

  return courseDefinitions.filter(c => c.selected).sort((a, b) => a.name.localeCompare(b.name))
    .map((def) => {
      if (def.objectives.length === 0) {
        return null;
      }
      return (
        <div key={def.id} style={{marginLeft: "10px", marginRight: "10px"}}>
          <Header style={{backgroundColor: assignment?.is_topic ? colors.topic : colors.green}}
                  title={def.name}/>
          <Card style={{height: "100%", padding: 0}}>
            {def.objectives.sort((a, b) => (a.name.localeCompare(b.name, undefined, {
              numeric: true,
              sensitivty: 'base'
            }))).map((o) => {
              return (
                <div key={o.id} style={{borderBottom: `1px solid ${colors.borderColor}`}}>
                  {selectedGradeSystem?.is_counter && (
                    <AccordionItem
                      details={
                        <div>
                          {selectedGradeSystem?.grades?.map(
                            (g, i) => (
                              <FeedbackInput
                                key={g.id}
                                type={"number"}
                                style={{width: "300px"}}
                                text={o.max_counter_grades[i]}
                                placeholder={`${getTranslation("maxPointsInput")} ${g.name}`}
                                hideEye={true}
                                onTextChange={(text) => {
                                  o.max_counter_grades[i] = parseInt(text, 10);
                                  onChange({...assignment});
                                }}
                                id={g.id}
                              />
                            )
                          )}
                        </div>
                      }
                      summary={
                        <CheckboxWithText
                          onClick={() => {
                            showObjectiveDetails(o.id);
                          }}
                          id={o.id}
                          onCheck={handleObjectiveCheck}
                          subText={o.description}
                          text={o.name}
                          checked={o.selected}
                          style={{
                            padding: "0px 0px 0px 20px",
                            width: "270px",
                          }}
                        />
                      }
                    />
                  )}
                  {!selectedGradeSystem?.is_counter && (
                    <CheckboxWithText
                      onClick={() => showObjectiveDetails(o.id)}
                      id={o.id}
                      onCheck={handleObjectiveCheck}
                      subText={o.description}
                      text={o.name}
                      checked={o.selected}
                      style={{padding: "10px 20px 10px 20px"}}
                    />
                  )}
                </div>
              );
            })}
          </Card>
        </div>
      );
    });
};

export default Objectives;
