import { IComponentController, IFormController } from 'angular';
import { StateService } from '@uirouter/core';
import { UserService } from '../../../../../../src/app/shared/states/user/user.service';
import { ICountry } from '../../../shared/models/country.model';
import { ISchoolCategory } from '../../../shared/models/schoolCategory.model';
import { ISchoolRequestBody, SchoolRequestBody } from '../../../shared/models/schoolRequestBody.model';
import { CountryService } from '../../../shared/services/country/country.service';
import { SchoolService } from '../../../shared/services/school/school.service';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import UserSetupModule from '../../userSetup.module';
import style from './userSetupRegisterSchool.scss';
import template from './userSetupRegisterSchool.tpl.html';

@Component({
  module: UserSetupModule,
  selector: 'user-setup-register-school',
  template: template,
  style: style,
})
export class UserSetupRegisterSchoolComponent implements IComponentController {
  @Input() public countries: ICountry[];
  @Input() public schoolCategories: ISchoolCategory[];
  @Input({ optional: true }) public country: ICountry;
  @Input({ optional: true }) public state: string;

  public readonly DEFAULT_COUNTRY_CODE: string;
  public schoolRegisterForm: IFormController;
  public schoolRequest: ISchoolRequestBody;

  private requestPending: boolean;

  constructor(private $state: StateService, private CountryService: CountryService, private SchoolService: SchoolService, private UserService: UserService) {
    'ngInject';

    this.countries = [];
    this.schoolCategories = [];
    this.country = null;
    this.state = null;
    this.DEFAULT_COUNTRY_CODE = 'SE';
    this.schoolRegisterForm = null;
    this.schoolRequest = new SchoolRequestBody();
    this.requestPending = false;
  }

  public $onInit(): void {
    if (!this.country) {
      this.country = this.CountryService.findCountryByCode(this.countries, this.DEFAULT_COUNTRY_CODE);
    }
  }

  public sendSchoolRequest(): void {
    this.schoolRequest.country_code = this.country.country_code;
    this.schoolRequest.state = this.state;

    this.requestPending = true;
    this.SchoolService.sendSchoolRequest(this.schoolRequest)
      .then(this.schoolRequestSuccess.bind(this))
      .catch(this.schoolRequestFail.bind(this))
      .finally((): boolean => this.requestPending = false);
  }

  public isSubmitDisabled(): boolean {
    return this.schoolRegisterForm.$invalid || this.requestPending;
  }

  private clearStateInput(): void {
    this.state = null;
  }

  private schoolRequestSuccess(schoolRequest: ISchoolRequestBody): void {
    this.UserService.updateUser({ school_name: schoolRequest.school_name } as any);
    this.$state.go('userSetup.setup.complete');
  }

  private schoolRequestFail(): void {

  }
}
