import uiRouter from '@uirouter/angularjs';
import { UserInactive } from './user-inactive.component';

require('./user-inactive.scss');

export const UserInactiveModule = angular
  .module('userInactive', [
    uiRouter,
  ])
  .component('userInactive', UserInactive)
  .config(($stateProvider) => {
    'ngInject';

    $stateProvider
      .state('user-inactive', {
        component: 'userInactive',
        resolve: {
          message: (FetchData) => {
            'ngInject';

            return FetchData.getInactiveUserMessage();
          },
        },
      });
  })
  .name;
