/**
 * @module GradeSystems/Service
 * @description
 *   Keeps a list of grade systems
 */

export class GradeSystems {
  constructor($q, FetchData, Path) {
    'ngInject';

    this.$q = $q;
    this.FetchData = FetchData;
    this.Path = Path;
    this.gradeSystems = {};
  }


  /**
   * Update grade systems list.
   * @param {Array} updatedGradeSystems Updates grade systems array.
   * @memberof module:GradeSystems/Service
   */
  updateGradeSystems(updatedGradeSystems) {
    this.gradeSystems = updatedGradeSystems.reduce((gradeSystems, currentGradeSystem) => {
      Object.assign(gradeSystems, {
        [currentGradeSystem.id]: currentGradeSystem,
      });
      return gradeSystems;
    }, {});
  }

  /**
   * Clear grade systems list.
   * @param {Array} updatedGradeSystems Updates grade systems array.
   * @memberof module:GradeSystems/Service
   */
  clearGradeSystems() {
    this.gradeSystems = {};
  }

  /**
   * Fetch grade systems list or return already existing grade systems.
   * @memberof module:GradeSystems/Service
   */
  fetchGradeSystemsList() {
    const deferred = this.$q.defer();
    if (angular.equals(this.gradeSystems, {})) {
      this.FetchData.v2.getGradeSystems(this.Path.getClassroomId())
        .then((res) => {
          this.updateGradeSystems(res);
          deferred.resolve(this.gradeSystems);
        });
    } else {
      deferred.resolve(this.gradeSystems);
    }
    return deferred.promise;
  }

  getGradeSystemById(id) {
    return this.gradeSystems[id];
  }
}
