import { IdService } from '../../../shared/services/helpers/id.service';

export class LongTextTooltip {
  static defaultStyles = {
    position: 'absoulte',
    top: '-100px',
    whiteSpace: 'nowrap',
  };

  constructor($window, $timeout) {
    'ngInject';

    this.$timeout = $timeout;
    this.onResizeHandlers = [];
    this.timeoutPromise = null;
    this.debounce = 50;
    this.refreshScopeState = null;
    angular.element($window).on('resize', () => this.onResize());
  }

  onResize() {
    if (this.timeoutPromise) {
      this.$timeout.cancel(this.timeoutPromise);
    }

    this.timeoutPromise = this.$timeout(
      () => this.onResizeHandlers.forEach(handler => handler()),
      this.debounce,
    );
  }

  addOnResizeHandler(handler) {
    handler.id = IdService.getId();
    this.onResizeHandlers.push(handler);

    return this.createUnregisterFunction(handler.id);
  }

  createUnregisterFunction(handlerId) {
    return () => {
      this.onResizeHandlers = this.onResizeHandlers.filter(handler => handler.id !== handlerId);
    };
  }

  isNeeded($element, query = null) {
    let showTooltip = false;
    const contentElement = query ?
      $element.find(query).first() :
      $element.find('ng-transclude').children().first();

    if (contentElement.length) {
      showTooltip =
        Math.ceil(contentElement[0].scrollWidth) > Math.ceil(contentElement.innerWidth());
    }

    return showTooltip;
  }

  preventOutdatedScope() {
    if (!this.refreshScopeState) {
      this.refreshScopeState = this.$timeout(() => {
        this.refreshScopeState = null;
      }, 250);
    }
  }
}
