import {EntityState, ID, Order} from '@datorama/akita';

export type AssessmentSortFn = (assessment: Assessment) => number;

/**
 * @deprecated
 * Instead of this use AssessmentStatus
 */
export enum AssessmentStatusOld {
  ND = 'ND',
  NL = 'NL',
  NV = 'NV',
  NN = 'NN',
  AV = 'AV',
  AN = 'AN',
  WV = 'WV',
  WN = 'WN',
}

export enum AssessmentStatus {
  ASSESSING = 'ASSESSING',
  ASSESSMENT_DONE = 'ASSESSMENT_DONE',
  NOT_ASSESSED = 'NOT_ASSESSED',
  WARNING = 'WARNING',
}

export interface Assessment {
  assessment_status: AssessmentStatusOld;
  fail_warning: boolean;
  id: ID;
  status_new: AssessmentStatus;
  student_id: ID;
  student_name: string;
}

export interface AssessmentSort {
  by: keyof Assessment;
  order: Order;
}

export interface AssessmentSortItem extends AssessmentSort {
  label: string;
}

export interface AssessmentsUI {
  sort: AssessmentSort;
}

export interface AssessmentStatusUpdateBody {
  grades_visible?: boolean;
  objectives_feedback_visible?: boolean;
  overall_feedback_visible?: boolean;
  result_visible?: boolean;
  status?: AssessmentStatus;
}

export interface AssessmentVisibilityDialog extends AssessmentStatusUpdateBody {
  id?: ID;
  saved?: boolean;
}

export interface AssessmentStatusUpdateResponse {
  id?: ID;
  fail_warning?: boolean;
}

export interface AssessmentVisibility {
  grades_visible?: boolean;
  objectives_feedback_visible?: boolean;
  overall_feedback_visible?: boolean;
  result_visible?: boolean;
  status?: AssessmentStatus;
  student_ids?: ID[];
}

export function createAssessment(params: Partial<Assessment>) {
  return {
    ...params
  } as Assessment;
}

export function createAssessments(params: Partial<EntityState<Assessment>>) {
  return {
    ...params
  } as Assessment;
}

export function createAssessmentsUI(params?: Partial<AssessmentsUI>) {
  return {
    sort: {
      by: 'student_name',
      order: Order.ASC,
    },
    ...params
  } as AssessmentsUI;
}
