export class CourseListItemComponent {
  static bindings = {
    course: '<',
    selectedCourse: '<',
    onUpdate: '&',
  };
  static controller = CourseListItemComponent;
  static templateUrl = 'components/course-progress/student-course-progress/coursesList/courseListItem/courseListItem.html';

  constructor() {
    'ngInject';

    this.course = null;
    this.selectedCourse = null;
  }

  navigateToCourseDetails() {
    this.selectedCourse = this.course;
    this.onUpdate({ selectedCourse: this.selectedCourse });
  }
}
