import React, { useEffect, useRef, useState } from "react";
import { getTranslation } from "../i18n";
import {colors, fonts} from '../theme';

const savedText = getTranslation("savedStatusIndicator");

const SaveToast = (props) => {
  const { networkTraffic } = props;
  const state = useRef(false);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (networkTraffic && networkTraffic !== "GET") {
      state.current = "saving";
    } else if (!networkTraffic && state.current === "saving") {
      setOpen(true);
      state.current = null;
      setTimeout(() => {setOpen(false)}, 2000);
    }
  }, [networkTraffic]);

  if (!open) {
    return null;
  }
  return (
    <div
      style={{
        position: "absolute",
        bottom: "20px",
        right: "20px",
        backgroundColor: colors.darkGrey,
        padding:'5px 10px 5px 10px',
        borderRadius:'5px',
        zIndex:'1000',
      }}
    >
      <span style={{...fonts.MEDIUM, color:colors.white}}>{savedText}</span>
    </div>
  );
};

export default SaveToast;
