import angular, { IServiceProvider } from 'angular';
import { Interceptor } from '../../shared/models/interceptor.model';
import { Provider } from '../decorators/provider.decorator';
import UtilsModule from '../utils.module';

@Provider({
  module: UtilsModule,
  name: 'HttpInterceptor',
})
export class HttpInterceptorProvider implements IServiceProvider {

  public static readonly interceptors: Interceptor[] = [];

  public add(interceptorFactory: Interceptor, index: number = HttpInterceptorProvider.interceptors.length): void {
    HttpInterceptorProvider.interceptors.splice(index, 0, interceptorFactory);
  }

  public $get($injector: angular.auto.IInjectorService): Interceptor[] {
    'ngInject';

    return HttpInterceptorProvider.interceptors
      .reduce<Interceptor[]>(
        (acc: Interceptor[], interceptorFactory: Interceptor): Interceptor[] => [this.buildInterceptor($injector, interceptorFactory), ...acc], [],
      );
  }

  private buildInterceptor($injector: angular.auto.IInjectorService, interceptorFactory: Interceptor): Interceptor {
    return angular.isString(interceptorFactory) ?
      $injector.get<Interceptor>(interceptorFactory) : $injector.invoke<Interceptor>(interceptorFactory as any);
  }
}
