import React, { useContext, useEffect, useState } from 'react';
import { ArrowBack } from '@material-ui/icons';
import { colors, fonts } from '../theme';
import { useWindowDimensions } from '../hooks';
import { GlobalContext } from '../utils';

const GridHeader = (props) => {
  const {
    assignment, back,
  } = props;
  const context = useContext(GlobalContext);
  const { Api } = context;
  const window = useWindowDimensions();
  const [classroom, setClassroom] = useState();

  useEffect(() => {
    (async () => {
      const response = await Api.getClassroom('name');
      setClassroom(response?.data?.classroom);
    })();
  }, []);

  return (
    <div style={{ height: '60px' }}>
      <div style={{
        position: 'absolute',
        width: `${window.width - 360}px`,
        left: `${-window.width + 360}px`,
        height: '60px',
        display: 'flex',
        backgroundColor: assignment.is_topic ? colors.topic : colors.green,
        alignItems: 'center',
      }}>
        <ArrowBack
          style={{
            cursor: 'pointer',
            color: colors.white,
            height: '25px',
            marginLeft: '16px',
          }}
          onClick={back}
        />
        <div style={{
          display: 'flex',
          width: '100%',
          justifyContent: 'space-between',
          padding: '16px',
        }}>
          <div style={{ display: 'flex', flexDirection: 'column' }}>
            <span style={{ ...fonts.MEDIUM, color: colors.white }}>{classroom?.name}</span>
            <span style={{ ...fonts.LARGE, color: colors.white }}>{assignment.title}</span>
          </div>
        </div>
      </div>
    </div>
  );
};
export default GridHeader;
