import { IPromise } from 'angular';
import { StateParams } from '@uirouter/core';
import { Service } from '../../../utils/decorators/service.decorator';
import { GroupedVisibilityDialogType } from '../../enums/groupedVisibilityDialogType.enum';
import { GroupedVisibilityFeedbackType } from '../../enums/groupedVisibilityFeedbackType.enum';
import { IAssignment } from '../../models/assignment.model';
import { GroupedVisibility, IGroupedVisibility } from '../../models/groupedVisibility.model';
import SharedServicesModule from '../services.module';
import {getTranslation} from '../../../../../../react_src/i18n';

export interface IGroupedVisibilityService {
  getEmptyGroupedVisibilityObject(): IGroupedVisibility;
  getSelectedTypeOfFeedbackByType(type: GroupedVisibilityDialogType): { id: GroupedVisibilityFeedbackType, name: string, group: number }[];
  mapObjectToArrayOfIds(items: { id: number }[]): number[];
  patchAssignmentAssessmentsGroupedVisibility(groupedVisibility: IGroupedVisibility, assignment?: IAssignment): IPromise<void>;
  patchCourseAssessmentsGroupedVisibility(groupedVisibility: IGroupedVisibility): IPromise<void>;
}


@Service({
  module: SharedServicesModule,
  name: 'GroupedVisibilityService',
})
export class GroupedVisibilityService implements IGroupedVisibilityService {
  constructor(private $stateParams: StateParams, private Path: any, private FetchData: any) {
    'ngInject';
  }

  public getEmptyGroupedVisibilityObject(): IGroupedVisibility {
    return new GroupedVisibility(
      {
        overall: {
          feedback_published: true,
          grade_published: true,
          result_published: true,
          target_grade_published: true,
        },
        objective: {
          feedback_published: true,
          grade_published: true,
        },
      }, undefined, [], []);
  }

  public getSelectedTypeOfFeedbackByType(type: GroupedVisibilityDialogType): { id: GroupedVisibilityFeedbackType, name: string, group: number }[] {
    let selectedTypeOfFeedback: { id: GroupedVisibilityFeedbackType; name: string, group: number }[] = [
      {
        id: GroupedVisibilityFeedbackType.ObjectiveAssessment,
        name: getTranslation('objectiveAssessment'),
        group: 1,
      },
      {
        id: GroupedVisibilityFeedbackType.ObjectiveFeedback,
        name: getTranslation('objectiveFeedback'),
        group: 1,
      },
    ];

    if (type === GroupedVisibilityDialogType.StudentProgressView) {
      selectedTypeOfFeedback = [
        {
          id: GroupedVisibilityFeedbackType.OverallAssessment,
          name: getTranslation('overallAssessment'),
          group: 0,
        },
        {
          id: GroupedVisibilityFeedbackType.OverallTarget,
          name: getTranslation('overallTarget'),
          group: 0,
        },
        {
          id: GroupedVisibilityFeedbackType.OverallFeedback,
          name: getTranslation('overallFeedbackStudentProgress'),
          group: 0,
        },
        ...selectedTypeOfFeedback,
      ];
    } else {
      selectedTypeOfFeedback = [
        {
          id: GroupedVisibilityFeedbackType.OverallFeedback,
          name: getTranslation('overallFeedback'),
          group: 0,
        },
        {
          id: GroupedVisibilityFeedbackType.OverallResult,
          name: getTranslation('overallResult'),
          group: 0,
        },
        ...selectedTypeOfFeedback,
      ];
    }

    return selectedTypeOfFeedback;
  }

  public mapObjectToArrayOfIds(items: { id: number }[] = []): number[] {
    return items.reduce((acc: number[], item: { id: number }) => {
      return item ? [...acc, item.id] : acc;
    }, []);
  }

  public patchAssignmentAssessmentsGroupedVisibility(groupedVisibility: IGroupedVisibility, assignment: IAssignment = this.$stateParams.assignment): IPromise<void> {
    const courseId: number = this.Path.getClassroomId();
    const assignmentId: number = assignment.id;

    return this.FetchData.v3
      .patchAssignmentAssessmentsGroupedVisibility(courseId, assignmentId, groupedVisibility);
  }

  public patchCourseAssessmentsGroupedVisibility(groupedVisibility: IGroupedVisibility): IPromise<void> {
    const courseId: number = this.Path.getClassroomId();

    return this.FetchData.v3.patchCourseAssessmentsGroupedVisibility(courseId, groupedVisibility);
  }
}
