export const CounterGradeEditable = {
  bindings: {
    label: '<',
    grade: '<',
    maxGrade: '<',
    readOnly: '<',
    onUpdate: '&'
  },
  controller: class {
    updateGrade() {
      this.onUpdate({ grade: this.grade });
    }

    increaseGrade() {
      if (this.grade < this.maxGrade && this.grade !== null) {
        this.grade += 1;
        this.updateGrade();
      }
      if (this.grade === null) {
        this.grade = 0;
        this.updateGrade();
      }
    }

    decreaseGrade() {
      if (this.grade > 0) {
        this.grade -= 1;
        this.updateGrade();
      }
    }
  },
  template: `
<div
  class="counter-grade"
  layout="row"
  layout-align="start center">
  <span class="sb-label-left" flex="noshrink">
    {{$ctrl.label}}
  </span>
  <div
    flex="70"
    layout="row"
    layout-align="end center"
    class="input-underline">
    <input
      flex="55"
      type="number"
      placeholder="{{ $ctrl.grade || '-' }}"
      ng-model-options='{ debounce: 500, allowInvalid: false }'
      ng-readonly="$ctrl.readOnly"
      ng-model="$ctrl.grade"
      max="{{$ctrl.maxGrade}}"
      min="0"
      ng-change="$ctrl.updateGrade()">
    <span flex="40">/{{$ctrl.maxGrade}}</span>
  </div>
  <div
    class="counter-grade__arrows"
    flex="15"
    ng-if="!$ctrl.readOnly">
    <span class="arrow-up" ng-click="$ctrl.increaseGrade()"></span>
    <span class="arrow-down" ng-click="$ctrl.decreaseGrade()"></span>
  </div>
</div>
  `
};
