import angular, { IRootElementService } from 'angular';
import { Input } from '../../../utils/decorators/input.decorator';
import { Component } from '../../../utils/decorators/component.decorator';
import CommonComponentsModule from '../components.module';
import { IBreadcrumb } from '../../../shared/models/breadcrumb.model';
import { UnbindableCtrlBase } from '../../base-classes';
import { IBreadcrumbsService } from './breadcrumbs.service';
import template from './breadcrumbs.tpl.html';

@Component({
  module: CommonComponentsModule,
  template: template,
  selector: 'breadcrumbs',
})
export class BreadcrumbsComponent extends UnbindableCtrlBase {
  @Input({ alias: 'steps' })
  public breadcrumbs: IBreadcrumb[];

  public hasEnoughBreadcrumbs: boolean;
  public resizeHandler: Function;

  constructor(private $element: IRootElementService, private BreadcrumbsService: IBreadcrumbsService) {
    'ngInject';

    super();
    this.breadcrumbs = [];
    this.hasEnoughBreadcrumbs = false;
    this.resizeHandler = angular.noop;
  }

  private $onInit(): void {
    this.breadcrumbs = this.BreadcrumbsService.removeBreadcrumbsWithoutLabel(this.breadcrumbs);
    this.hasEnoughBreadcrumbs = this.BreadcrumbsService.hasEnoughBreadcrumbs(this.breadcrumbs);

    if (this.hasEnoughBreadcrumbs) {
      this.resizeHandler = this.BreadcrumbsService.createHandler(this.$element);
      this.boundEvents.push(this.BreadcrumbsService.addOnResizeHandler(() => this.resizeHandler()));
    }
  }

  private $postLink(): void {
    this.resizeHandler();
  }
}
