import angular, { IAttributes, IController, IDirective, IScope, ITranscludeFunction } from 'angular';
import { Directive } from '../../../utils/decorators/directive.decorator';
import SharedDirectivesModule from '../directives.module';
import style from './hint.scss';

@Directive({
  module: SharedDirectivesModule,
  selector: 'hint',
  restrict: 'A',
  transclude: 'element',
  style: style,
})
export class HintDirective implements IDirective {
  static classNamePrefix: string = 'sb-hint';
  static cookieValue: string = 'hide';
  private readonly body: JQuery;

  constructor(private $cookies: ng.cookies.ICookiesService, private UserInfo: { userId: number },
              private Hint: any, private StudybeeHandle: any) {
    'ngInject';

    this.body = angular.element(document.body);
  }

  public link(scope: IScope, instanceElement: JQLite, instanceAttributes: IAttributes, controller?: IController, transclude?: ITranscludeFunction): void {
    const cookieName: string = this.buildCookieName(instanceAttributes);

    if (!(this.isCookieExists(cookieName) || this.isHintBoxVisible(instanceAttributes))) {
      transclude(($element: JQLite): void => this.transcludeHandler($element, instanceAttributes));
    }
  }

  private transcludeHandler($hintBox: JQuery, instanceAttributes: IAttributes): void {
    this.increaseHintBoxCounter();
    this.addContentLock($hintBox, instanceAttributes);
    this.hide($hintBox, 0);
    this.addHintBox($hintBox, instanceAttributes);
    this.attachHintBox($hintBox, instanceAttributes);

    this.Hint.setRemoveCallback((): void => this.remove($hintBox, instanceAttributes));
    this.Hint.setDisplayToggleCallbacks((): void => this.displayToggle($hintBox));


    setTimeout(() => {
      const cookieName: string = this.buildCookieName(instanceAttributes);
      angular.element(window).trigger(`resize.${cookieName}`);

      if (this.StudybeeHandle.studybeeIsOpen()) {
        this.show($hintBox);
      }
    });
  }

  private addContentLock($hintBox: JQuery, instanceAttributes: IAttributes): JQuery {
    const $contentLock: JQuery = angular.element('<div></div>');

    if (!this.isContentActive(instanceAttributes)) {
      const cookieName: string = this.buildCookieName(instanceAttributes);
      $contentLock.addClass(`${HintDirective.classNamePrefix}-lock-content`);
      $contentLock.attr('id', `contentLock${cookieName}`);

      this.body.append($contentLock);
    }

    $hintBox.data('contentLock', $contentLock);

    return $contentLock;
  }

  private addHintBox($hintBox: JQuery, instanceAttributes: IAttributes): JQuery {
    const cookieName: string = this.buildCookieName(instanceAttributes);
    const $button: JQuery = $hintBox.find(`.${HintDirective.classNamePrefix}-close`);

    this.body.append($hintBox);

    $button.one({
      click: (event: MouseEvent): void => {
        this.onGotItClick(event, instanceAttributes);
        this.decreaseHintBoxCounter();
        this.hide($hintBox);
        this.remove($hintBox, instanceAttributes);
      },
    });

    angular.element(window).on(`resize.${cookieName}`, (): void => {
      window.requestAnimationFrame((): void => {
        this.setHeight($hintBox);
        this.attachHintBox($hintBox, instanceAttributes);
      });
    });

    return $hintBox;
  }

  private attachHintBox($hintBox: JQuery, instanceAttributes: IAttributes): void {
    if (instanceAttributes.attachTo) {
      const target: JQuery = angular.element(instanceAttributes.attachTo);
      const display: string = $hintBox.css('display');
      const visibility: string = $hintBox.css('visibility');

      if (display === 'none') {
        $hintBox.css({ display: 'block', visibility: 'hidden' });
      }

      if (target.length) {
        const targetBounding: ClientRect = (target[0] as HTMLElement).getBoundingClientRect();
        const hintBoxBounding: ClientRect = ($hintBox[0] as HTMLElement).getBoundingClientRect();
        const left = targetBounding.left - hintBoxBounding.width;
        const top = targetBounding.top - ((hintBoxBounding.height - targetBounding.height) / 2);

        $hintBox.css({ left, top, display, visibility });
      }
    }
  }

  private buildCookieName(instanceAttributes: IAttributes): string {
    return `VIEW${instanceAttributes.cookieName}USER${this.UserInfo.userId}`;
  }

  private isCookieExists(cookieName: string): boolean {
    return this.$cookies.get(cookieName) === HintDirective.cookieValue;
  }

  private isContentActive(instanceAttributes: IAttributes): boolean {
    return !!instanceAttributes.contentActive;
  }

  private isHintBoxVisible(instanceAttributes: IAttributes): boolean {
    return this.body.find(`#hintbox-${instanceAttributes.cookieName}`).length > 0;
  }

  private onGotItClick(event: MouseEvent, instanceAttributes: IAttributes): void {
    event.preventDefault();

    const cookieName: string = this.buildCookieName(instanceAttributes);
    const currentDate: Date = new Date();
    const nextDate: any = new Date();

    nextDate.setYear(currentDate.getFullYear() + 1);

    this.$cookies.put(cookieName, HintDirective.cookieValue, {
      path: '/',
      expires: nextDate,
    });
  }

  private setHeight($hintBox: JQuery): void {
    $hintBox.css('height', '');

    const windowHeight: number = angular.element(window).height();
    let hintHeight: number = $hintBox.outerHeight();

    if (hintHeight > (windowHeight - 64)) {
      hintHeight = windowHeight - 64;
    }

    $hintBox.css('height', hintHeight);
  }

  private increaseHintBoxCounter(): void {
    const hintBox: number = this.body.data('hintBox') || 0;
    this.body.data('hintBox', hintBox + 1);
  }

  private decreaseHintBoxCounter(): void {
    const hintBox: number = this.body.data('hintBox') || 1;
    this.body.data('hintBox', hintBox - 1);
  }

  private displayToggle($hintBox: JQuery): void {
    if ($hintBox.is(':visible')) {
      this.hide($hintBox);
    } else {
      this.show($hintBox);
    }
  }

  private hide($hintBox: JQuery, duration?: number): void {
    if (!$hintBox.data('isRemoved')) {
      if (!this.body.data('hintBox')) {
        this.body.removeClass(`${HintDirective.classNamePrefix}-disable-scroll`);
      }

      $hintBox.data('contentLock').css('display', 'none');
      $hintBox.fadeOut(duration, (): void => {
        $hintBox.css('display', 'none');
      });
    }
  }

  private show($hintBox: JQuery, duration?: number): void {
    if (!$hintBox.data('isRemoved')) {
      this.body.addClass(`${HintDirective.classNamePrefix}-disable-scroll`);
      $hintBox.data('contentLock').css('display', 'block');
      $hintBox.css('display', 'block').fadeIn(duration);
    }
  }

  private remove($hintBox: JQuery, instanceAttributes: IAttributes): void {
    const cookieName: string = this.buildCookieName(instanceAttributes);
    const contentLock: JQuery = $hintBox.data('contentLock');

    if (contentLock && contentLock.length) {
      contentLock.remove();
    }

    $hintBox.remove();
    $hintBox.data('isRemoved', true);
    angular.element(window).off(`resize.${cookieName}`);
  }
}
