class controller {
  constructor(StudybeeHandle) {
    'ngInject';

    this.StudybeeHandle = StudybeeHandle;
  }

  $onInit() {
    // just to be sure that the information is visible
    this.StudybeeHandle.studybeeActivate();
  }
}

export const UserInactive = {
  bindings: {
    message: '<',
  },
  templateUrl: 'components/user-inactive/user-inactive.html',
  controller,
};
