export const showSaveCourseToLibraryDialog = ($mdDialog, curriculum) => $mdDialog.show({
  parent: dialogParentElement,
  templateUrl: 'components/courseManagement/common/dialogs/save-curriculum-to-library.html',
  controller: class {
    constructor() {
      'ngInject';

      this.$mdDialog = $mdDialog;

      // set the name and the code
      this.courseName = curriculum.name;
      this.courseCode = curriculum.code;
    }
  },
  controllerAs: '$ctrl',
});

export const showCreateObjectiveDialog = $mdDialog => $mdDialog.show({
  parent: dialogParentElement,
  templateUrl: 'components/courseManagement/common/dialogs/create-objective.html',
  controller: class {
    constructor() {
      'ngInject';

      this.$mdDialog = $mdDialog;
    }

    createObjective() {
      this.$mdDialog.hide(this.objectiveName);
    }
  },
  controllerAs: '$ctrl',
  multiple: true,
});

export const showCurriculumDetailsDialog = ($mdDialog, curriculum, FetchData) => $mdDialog.show({
  parent: dialogParentElement,
  templateUrl: 'components/courseManagement/common/dialogs/curriculum-details.html',
  bindToController: true,
  multiple: true,
  controller: class {
    constructor($rootScope, $q, Path) {
      'ngInject';

      this.$rootScope = $rootScope;
      this.$q = $q;
      this.Path = Path;
    }

    openCurriculumLibraryDialog() {
      $mdDialog.cancel();
      showCarriculumLibraryDialog(this.$q, $mdDialog, FetchData);
    }

    openAddNewObjectiveDialog() {
      showCreateObjectiveDialog($mdDialog)
        .then(objectiveName => FetchData
          .v3
          .createObjective(
            curriculum.course,
            { name: objectiveName },
          ),
        )
        .then(() => {
          this.$rootScope.$emit(Events.UPDATED_OBJECTIVES);
        });
    }
  },
  resolve: {
    curriculumDetails: () => FetchData
      .v3
      .getCurriculumDetails(curriculum),
    curriculum: () => curriculum,
  },
  controllerAs: '$ctrl',
});

