import angular from 'angular';
import { from, combineLatest } from 'rxjs';
import { take } from 'rxjs/operators';
import { Events } from '../../common/global-events.ts';

export function CustomRouting(
  $rootScope, $timeout, $q, $interval, $state, $stateParams, $mdBottomSheet, $mdDialog, Path,
  FetchData, Courses, UserInfo, States, Observer, NavigationHistory, MainMenu,
  Updates, GradeSystems, BackendInfo, Mixpanel, StudybeeHandle, Helpers, Assignments,
  HashObserverService, UserService, AssignmentsStore, EnvironmentService, InitService,
) {
  'ngInject';

  const initConfig = {
    keepOpen: false,
    unsubscribe: angular.noop,
  };

  // Init Observer
  Observer.init(checkState);

  /**
   * Here we are checking all pre-init conditions
   * - User data and user permissions
   * - Whether a user has the user's preferences configured or not
   *
   * So the aim here is to be sure that before the main init process of our app
   * a user has all data configured and ready to use
   *
   * TODO This still needs to be changed and optimized
   */
  function preInit() {
    $state.go('loading', {}).then(() => {
      if (!InitService.hasAppSetup()) {
        $state.go('userSetup').then(() => {
          StudybeeHandle.studybeeActivate();
        });
      } else {
        combineLatest([UserService.fetchUser(), from(FetchData.getUsersAgreementSeen())])
          .pipe(take(1))
          .subscribe(([, agreementSeen]) => {
            UserInfo.agreementSeen = agreementSeen;

            if (agreementSeen) {
              return initGeneral();
            }
            $state.go('userSetup').then(() => {
              StudybeeHandle.studybeeActivate();
            });
          });
      }
    });
  }

  /**
   * Init - run on application init
   * Check if users logged in Chrome and Classroom/Google Drive page are the same,
   * get Courses list and run init functiion for Classroom or Google Drive page
   */
  function initGeneral() {
    UserInfo.checkUser();

    if (!UserInfo.loggedToBrowser) {
      // If user is not logged in the Chrome,
      // don't run extension and show information for the user
      $state.go('not_logged_user');
      StudybeeHandle.studybeeActivate();
      return;
    } if (!UserInfo.userMatched) {
      // If user use two different accounts in the Chrome and in the Classroom,
      // don't run extension and show information for the user
      $state.go('wrong_user');
      StudybeeHandle.studybeeActivate();
      return;
    }

    if (Path.isClassroomInstance()) {
      initClassroom();
    } else if (Path.isDriveInstance()) {
      initGoogleDrive();
    }
  }

  /**
   * Init for Classroom - run checkState function and add eventListeners
   */
  function initClassroom() {
    checkState();
    initClassroomUrlObservers();

    // Listen clicks on links
    $(document.body).on('click', 'a', function () {
      // If click on student name in assignment,
      // then set student in History to this student name and don't trigger checkState()
      const link = $(this);
      const href = link.attr('href') || '';
      const parent = link.closest('[data-student-id]').data('student-id');
      let studentName;

      if (parent) {
        studentName = link.find('div > div:not([data-submission-id]) > div').text();
      } else {
        studentName = link.closest('[data-include-submission-materials]').prev().text();
      }

      // don't try to go to `teacher_assess_assignment` when current stage is based on these stages
      const states = [
        'teacherCourseManagement',
        'teacher-course-progress',
        'userSetup',
      ];

      // Listen clicks on student name in Classroom
      if (href.indexOf('/submissions') >= 0 && href.indexOf('/student') >= 0 && studentName.length) {
        MainMenu.saveStudentDetails(studentName);
        // $rootScope.$broadcast('clickedStudentInClassroom', studentName);
        const indexOf = studentName.indexOf('"');
        studentName = studentName.substring(0, indexOf === -1 ? studentName.length : indexOf);
        chrome.storage.sync.set({ studentSelected: studentName }, () => console.log('studentSelected', studentName));
      } else if (
        href.indexOf('/submissions') >= 0
        && href.indexOf('/all') >= 0
        && !states.reduce((result, value) => result || $state.includes(value), false)) {
        const linkId = Path.getAssignmentAlternateLinkId();
        chrome.storage.sync.set({ assignmentSelected: linkId }, () => console.log('assignmentSelected: ', linkId));
      }
    });
  }

  /**
   * this is temporary and all this code should be rewritten
   */
  function initClassroomUrlObservers() {
    // get max one change per 500 ms
    let timeout;
    chrome.runtime.onMessage.addListener((request) => {
      if (request.action === 'urlChanged') {
        if (timeout) {
          $timeout.cancel(timeout);
        }
        timeout = $timeout(() => {
          checkState();
        }, 500);
      }
    });
  }

  function attachClickEventOnFiles() {
    const query = '[role=menu] content[role=menuitem][data-item-id]';
    const eventName = 'click.fileChange';

    $(query).parent().off(eventName).on(eventName, () => {
      $timeout(() => {
        initGoogleDrive();
      }, 100);
    });
  }

  function attachStudentChangeEvent() {
    const urlRegex = /\/g\/tg\/?/g;

    if (urlRegex.test(window.location.pathname)) {
      initConfig.unsubscribe();
      initConfig.unsubscribe = HashObserverService.onNext(() => {
        $state.go('loading')
          .then(() => {
            initGoogleDrive()
              .then(() => {
                // $state.go('teacher_google_drive');
                States.openDefaultState();
                chrome.storage.sync.set({ assignmentDriveSelected: Math.random() }, () => console.log('assignmentDriveSelected'));
              })
              .catch(() => {
                $state.go('wrongGoogleDriveFile');
              });
          });
      });
    }
  }

  /**
   * Init for Google Drive
   * we don't need to checkState in this case because
   * inside the google drive we only want to show one state,
   * and using that kind redirection
   */
  function initGoogleDrive() {
    const alternateLinkId = Path.getAssignmentAlternateLinkId(true);
    return Path.getFileId(5)
      .then((fileId) => Courses.getCourseUsingDriveId(fileId, alternateLinkId))
      .then((course) => {
        // these are 3 thing we need to do to correct open
        // google drive view
        UserInfo.setUserRole(course.role);
        States.openDefaultState();
        // States.openDriveState();
        chrome.storage.sync.set({ assignmentDriveSelected: Math.random() }, () => console.log('assignmentDriveSelected'));
      })
      .catch(() => {
        $state.go('wrongGoogleDriveFile');
      })
      .finally(() => {
        StudybeeHandle.studybeeActivate();

        attachClickEventOnFiles();
        attachStudentChangeEvent();
      });
  }

  /**
   * Check state - not perfect but better than before
   * This one is used only inside google classroom
   */
  function checkState() {
    $timeout(() => {
      //   // $location object is not updated, using window.location
      const path = window.location.pathname;
      const courseId = Path.getClassroomId();
      const assignmentAlternateLinkId = Path.getAssignmentAlternateLinkId();

      // eslint-disable-next-line consistent-return
      let checkStatePromise = $q.when().then(() => {
        // this is some type of workaround, when a user is switching between classes,
        // the loading state should be shown
        // ** IMPORTANT **
        // I don't have any idea why it's not possible to implements it as a chain
        // eg.
        // checkStatePromise.then(() => {
        //      return 46;
        //   });
        // simply doesn't work then ( but it should )
        if (Path.oldCourseId && Path.oldCourseId !== courseId) {
          $rootScope.$emit(Events.CLASS_CHANGED);
          // have to clear grade system list to be updated for switched class
          GradeSystems.clearGradeSystems();
          AssignmentsStore.setHasCache(false);
          return $state.go('loading');
        }
      });

      // Fallback if course doesn't exist on courses list,
      // try to update courses list and retry checkState
      if (courseId && !Courses.isCourseExist(courseId)) {
        checkStatePromise = Courses.getCourse(courseId)
          .then(() => {
            Mixpanel.engage('add_role');
          });
      }

      // close the extension if needed before fetching the data
      if (Path.oldCourseId !== courseId && !initConfig.keepOpen) {
        StudybeeHandle.studybeeClose();
        $mdBottomSheet.hide();
        $mdDialog.hide();
      }

      checkStatePromise
        .then(() => {
          // Connect or disconnect observer to watch click event on div[name="ok"]
          Observer.check();

          // Set user role in UserInfo service
          UserInfo.checkRole(courseId);

          // Set user configured in UserInfo service (used only on Classroom)
          UserInfo.checkConfig(courseId);

          if (Path.isClassroomInstance()) {
            if (
              !Path.isClassesListPage()
              && !Path.isSettingsPage()
              && !Path.isWorkPage()
              && !Path.isCalendarPage()
            ) {
              BackendInfo.show();
            } else {
              BackendInfo.hide();
            }
          }
        })
        .then(() => {
          if (
            !Path.isClassesListPage()
            && !Path.isSettingsPage()
            && !Path.isWorkPage()
            && !Path.isCalendarPage()
            && Path.isClassroomInstance()
            && UserInfo.userRole === 'teacher'
          ) {
            if (Path.oldCourseId !== courseId) {
              // Update Assignments and Students list when teacher enter on
              // new course or course has been changed
              //return Updates.updateAssignmentsAndStudents(courseId, true);
              return null;
            } if (Path.isStreamPage()) {
              // Update Assignments list when teacher enter on Stream page
              return Updates.updateAssignments(courseId, true);
            }
          }
        })
        .then(() => {
          if (
            (Path.isStudentWorkPage() || Path.isInstructionsPage())
            && !Path.wasStudentWorkPage()
            && !Path.wasInstructionsPage()
            && UserInfo.userRole === 'teacher'
          ) {
            const assignment = Assignments.getAssignmentByAlternateLink(
              assignmentAlternateLinkId,
            );
            if (assignment) {
              Updates.updateFiles(assignment.id, true);
            }
          }

          // If it's Student Work, Instructions or Assignment Details
          // and previous page was not Student Work page or Instructions page
          // then save assignment details for Main Menu.
          // Otherway clear assignment details and student details
          if (
            (Path.isStudentWorkPage() || Path.isInstructionsPage())
            && !Path.wasStudentWorkPage()
            && !Path.wasInstructionsPage()
          ) {
            const assignment = Assignments.getAssignmentByAlternateLink(
              assignmentAlternateLinkId,
            );
            if (assignment) {
              FetchData.getAssignmentsObjectives(assignment.id, courseId).then((assignment) => {
                MainMenu.saveAssignmentDetails(assignment);
              });
            }
          } else if (
            Path.oldCourseId !== courseId
            || ((Path.wasStudentWorkPage() || Path.wasInstructionsPage())
              && !Path.isStudentWorkPage() && !Path.isInstructionsPage())
          ) {
            MainMenu.saveAssignmentDetails(null);
            MainMenu.saveStudentDetails(null);
          }

          // If it's Class List page, Settings page, Work page or course has been changed
          // then unchecked selected menu state
          if (
            Path.isClassesListPage()
            || Path.isSettingsPage()
            || Path.isWorkPage()
            || Path.isCalendarPage()
            || Path.oldCourseId !== courseId
          ) {
            MainMenu.setSelectedMenuStateName(null);
          }

          // Decide what state should be loaded and close extension when
          // it is open for the first time or course has
          // been changed (closed is default state for the extension)
          serveState(courseId);

          // reset keep open - this is some kind of workaround, with the current structure
          // It's hard to implement it correctly
          if (initConfig.keepOpen) {
            initConfig.keepOpen = false;
          }

          // Overwrite oldCourseId and oldPath
          Path.setOldPath(path);
          Path.setOldCourseId(courseId);

          // Set handler icon state
          StudybeeHandle.setIconStatus();

          // Broadcast stateChecked event
          $rootScope.$broadcast(Events.STATE_CHECKED);
        });
    }, 250);
  }

  /**
   * Serve proper state
   * @param {Number} courseId
   */
  function serveState(courseId) {
    if (Path.isClassroomInstance()) {
      // It's Google Classroom
      if (
        Path.isClassesListPage()
        || Path.isSettingsPage()
        || Path.isWorkPage()
        || Path.isCalendarPage()
      ) {
        // It's Classes List page, Settings page or Work page
        // clear selected menu state and run loading state and close extension
        States.openLoadingState();
      } else if (
        !Path.isClassesListPage()
        && !Path.isSettingsPage()
        && !Path.isWorkPage()
        && !Path.isCalendarPage()
      ) {
        // It's not Classes List page, Settings page and Work page
        if (Path.oldCourseId !== courseId && !Courses.hasCourseDefinition(courseId)) {
          // Course has been changed (when user enter page for the first time or change class)
          // and there are no courses definitions assigned
          States.openNotConfiguredState();
        } else if (
          (Path.isStudentWorkPage() || Path.isInstructionsPage())
          && !Path.wasStudentWorkPage()
          && !Path.wasInstructionsPage()
          && (MainMenu.isStateNameSelected('teacherAssignments')
            || MainMenu.isStateNameSelected('studentAssignments')
            || MainMenu.isStateNameSelected(null))) {
          // It's Student Work page, Instructions page or Assignment Details page
          // and previous page was not Student Work page or Instructions page
          // and selected menu state is equal to list of assignments or it's null (for the first enter)
          // States.openAssignmentState();
          if (Path.oldCourseId !== courseId) {
            States.openDefaultState();
          }
          const linkId = Path.getAssignmentAlternateLinkId();
          chrome.storage.sync.set({ assignmentSelected: linkId }, () => console.log('assignmentSelected: ', linkId));
        } else if (
          Path.oldCourseId !== courseId
          || (Path.isStreamPage() && (Path.wasStudentWorkPage() || Path.wasInstructionsPage())
            && (MainMenu.isStateNameSelected('teacherAssignments')
              || MainMenu.isStateNameSelected('student_assignments')))
        ) {
          // Course has been changed (when user enter page for the first time or change class)
          // or it's Stream page
          // and previous page was Student Work page or Instructions page
          // and selected menu state is equal to list of assignments
          States.openDefaultState();
        }
      }
    }
  }

  const link = (scope) => {
    chrome.runtime.sendMessage({
      action: 'getUserInfo',
    }, (res) => {
      let interval;

      UserInfo.userEmail = res.email;

      UserService.updateUser({ email: res.email });

      if (res.id.length || res.email.length) {
        UserInfo.loggedToBrowser = true;
      }

      interval = $interval(() => {
        const isGoogleDocs = $(document.body).find('header[role=banner]');
        const classroomNav = $(document.body).find('nav[role="navigation"]');
        const driveViewerProfileImg = $(document.body).find('.drive-viewer-profile-icon');
        const docsHeader = $(document.body).find('#docs-header');

        if (
          classroomNav.length
          || driveViewerProfileImg.length
          || docsHeader.length
          || isGoogleDocs
        ) {
          $interval.cancel(interval);

          if (classroomNav.length) {
            Path.classroomNode = classroomNav;
          } else if (driveViewerProfileImg.length) {
            Path.driveNode = driveViewerProfileImg;
          } else if (docsHeader.length) {
            Path.driveNode = docsHeader;
          } else if (isGoogleDocs.length) {
            Path.driveNode = isGoogleDocs;
          }

          preInit();
        }
      }, 250);
    });

    scope.$on(Events.USER_SETUP_COMPLETED, () => {
      if (!Path.isClassesListPage()) {
        initConfig.keepOpen = true;
      }
      initGeneral();
    });

    chrome.runtime.onMessage.addListener((request) => {
      if (request.action === 'refreshStudybee' && [
        'teacher_assess_assignment_student', 'teacher_assess_assignment', 'teacher_google_drive',
      ].includes(Helpers.getCurrentStateName())
      ) {
        NavigationHistory.reloadCurrent();
      }
    });
  };

  return {
    restrict: 'A',
    link,
  };
}
