import {normalize, schema} from 'normalizr';
import {Assignment, NormalizeAssignmentsResponse} from './assignment.model';
import {ID} from '@datorama/akita';

const assessment = new schema.Entity('assessments');
const assignmentsForTopic = new schema.Entity('assignments_for_topic');
const assignment = new schema.Entity('assignments', {
  assessments: [assessment],
  assignments_for_topic: [assignmentsForTopic],
});

export const normalizeAssignmentsResponse = assignments =>
  normalize<Assignment, NormalizeAssignmentsResponse, ID[]>(assignments, [assignment]);

