import angular, { IPromise } from 'angular';

export class CourseProgressDialog {
  static DEFAULT_CONFIG: angular.material.IDialogOptions = {
    bindToController: true,
    controller($mdDialog: angular.material.IDialogService) {
      'ngInject';

      this.$mdDialog = $mdDialog;
    },
    controllerAs: 'dialog',
    parent: angular.element(document.body),
  };
  static TEMPLATE_PATH = 'components/course-progress/common/dialogs';

  constructor(protected $mdDialog: angular.material.IDialogService) {
    'ngInject';

    this.$mdDialog = $mdDialog;
  }

  show(config: angular.material.IDialogOptions): IPromise<any>{
    return this.$mdDialog.show(this.buildDialogConfig(config));
  }

  buildDialogConfig(config: angular.material.IDialogOptions = {}): angular.material.IDialogOptions {
    const $config = angular.copy(config);
    return angular.extend({}, CourseProgressDialog.DEFAULT_CONFIG, $config);
  }
}
