import React, {useContext} from 'react';
import {animated, useSpring} from 'react-spring';

import {
  Assignment, School, Book, Refresh,
} from '@material-ui/icons';
import {colors, fonts} from '../theme';
import {useWindowDimensions} from '../hooks';
import DrawerItem from './drawerItem';
import {getTranslation} from '../i18n';
import {GlobalContext} from '../utils';

const app = require('../../package.json');

const width = 300;

const Drawer = (props) => {
  const {
    close, selected, onNavigate, onRefresh, student,
  } = props;
  const context = useContext(GlobalContext);
  const {user} = context;

  const [opacityAnim, setOpacity] = useSpring(() => ({to: {opacity: 1}, from: {opacity: 0}}));
  const [animProp, setMargin] = useSpring(() => ({
    marginLeft: 0,
    from: {marginLeft: -width},
    config: {mass: 1, velocity: 0, friction: 26},
    onRest: (props) => {
      if (props.value.marginLeft === -width) close();
    },
  }));

  const animateClose = () => {
    setMargin({marginLeft: -width});
    setOpacity({opacity: 0});
  };

  const navigate = (view) => {
    onNavigate(view);
    animateClose();
  };

  const dimensions = useWindowDimensions();
  if (!user) {
    return null;
  }

  const school = user?.school || {name: '', city: ''};

  const refresh = () => {
    onRefresh();
    animateClose();
  };

  return (
    <div>
      <animated.div
        onClick={animateClose}
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: colors.greyedOut,
          ...opacityAnim,
        }}/>
      <animated.div style={{
        position: 'absolute',
        width,
        height: dimensions.height,
        top: 0,
        backgroundColor: colors.white,
        ...animProp,
      }}>
        <div style={{
          display: 'flex', height: '165px', backgroundColor: colors.primary, flexDirection: 'column',
        }}>
          <img src={`https://${user.photo_url}`} style={{
            display: 'flex',
            width: '50px',
            height: '50px',
            backgroundColor: colors.white,
            borderRadius: '50px',
            margin: '14px',
          }}/>
          <span style={{
            ...fonts.XLARGE,
            fontWeight: '800',
            margin: '8px 14px 0px 14px',
          }}>{user.first_name} {user.last_name}</span>
          <span style={{
            ...fonts.MEDIUM,
            margin: '0px 14px 14px 14px',
          }}>{school?.name}{school?.city ? ` - ${school.city}` : ''}</span>
        </div>

        <DrawerItem onClick={() => navigate('assignments')} selected={selected === 'assignments'}
                    iconObject={<Assignment style={{color: colors.brandColorGreen}}/>}
                    text={getTranslation('assignmentsPage')}/>
        <DrawerItem onClick={() => navigate('courseOverview')} selected={selected === 'courseOverview'}
                    iconObject={<School style={{color: colors.brandColorPink}}/>}
                    text={getTranslation('courseProgressionPage')}/>
        <DrawerItem onClick={() => navigate('courseRequirements')}
                    iconObject={<Book style={{color: colors.brandColorBlue}}/>}
                    text={getTranslation('courseRequirementsPage')}/>
        {!student
        && <>
          <div style={{borderBottom: `1px solid ${colors.borderColor}`}}/>
          <DrawerItem onClick={refresh} iconObject={<Refresh style={{color: colors.textColor}}/>}
                      text={getTranslation('synchronizeMainMenu')}/>
          <div style={{borderBottom: `1px solid ${colors.borderColor}`}}/>
        </>
        }
        <span style={{
          ...fonts.TINY,
          fontSize: '10px',
          position:  'absolute',
          bottom: '6px',
          right: '6px',
          color: '#0000007f',
        }}>Version: {app?.version}</span>
      </animated.div>
    </div>

  );
};

export default Drawer;
