import angular, { IModule } from 'angular';
import { BackendInfo } from './services/backend_info';
import { Mixpanel } from './services/mixpanel.service';
import { DynamicTheme } from './directives/dynamic-theme';
import { MainMenu } from './directives/main-menu';
import { MainMenu as MainMenuService } from './services/main_menu';
import { Courses } from './services/courses.service';
import { CustomRouting } from './directives/custom_routing';
import { UserInfo } from './services/userInfo.service';
import { Locale } from './services/locale';
import { GradeSystems } from './services/grade_systems';
import { FetchData } from './services/fetchData.service';
import { SlideToggleDirective } from './directives/slideToggle.directive';
import { LocalizeFilter } from './filters/localize';
import { SlideToggleAnimation } from './animations/slideToggle.animation';
import { PromiseQueue } from './services/promiseQueue.service';
import { LastModified } from './filters/last_modified';
import { Students } from './services/students';

export const UtilsModule: IModule = angular
  .module('app.utils', [])
  .animation('.slideToggleOpen', SlideToggleAnimation.create)
  .directive('customRouting', CustomRouting)
  .directive('mainMenu', MainMenu)
  .directive('slideToggle', SlideToggleDirective.create)
  .directive('localize', require('./directives/localize.js'))
  .directive('singleSlider', require('./directives/single_slider.js'))
  .directive('counters', require('./directives/counters.js'))
  .directive('knowledgeRequirements', require('./directives/knowledge_requirements.js'))
  .directive('stickySubheader', require('./directives/sticky_subheader.js'))
  .directive('lastModified', require('./directives/last_modified.js'))
  .directive('studybeeHandle', require('./directives/studybee_handle.js'))
  .directive('dynamicTheme', DynamicTheme)
  .directive('saveScroll', require('./directives/save_scroll.js'))
  .service('Locale', Locale)
  .service('GradeSystems', GradeSystems)
  .service('UserInfo', UserInfo)
  .service('FetchData', FetchData)
  .service('Mixpanel', Mixpanel)
  .service('Courses', Courses)
  .service('PromiseQueue', PromiseQueue)
  .factory('Assignments', require('./services/assignments.js'))
  .service('Students', Students)
  .factory('Config', require('./services/config.js'))
  .factory('Path', require('./services/path.js'))
  .factory('Debounce', require('./services/debounce.js'))
  .factory('Helpers', require('./services/helpers.js'))
  .factory('NavigationHistory', require('./services/navigation_history.js'))
  .factory('MainMenu', MainMenuService)
  .factory('States', require('./services/states.js'))
  .factory('Observer', require('./services/observer.js'))
  .factory('Hint', require('./services/hint.js'))
  .factory('Updates', require('./services/updates.js'))
  .factory('BackendInfo', BackendInfo)
  .factory('StudybeeHandle', require('./services/studybee_handle.js'))
  .filter('Sanitize', require('./filters/sanitize.js'))
  .filter('LastModified', LastModified)
  .filter('localize', LocalizeFilter);

export default UtilsModule.name;

export * from './providers/httpInterceptor.provider';
export * from './interceptors/agreementSeen.interceptor';
export * from './interceptors/checkDomain.interceptor';
export * from './interceptors/extractData.interceptor';
export * from './interceptors/inactiveUser.interceptor';
export * from './interceptors/missingAgreement.interceptor';
export * from './interceptors/userData.interceptor';
export * from './interceptors/userPreferences.interceptor';
