/*@ngInject*/
module.exports = function($timeout) {
  function link(scope, element, attr) {
    var config = [];
    var throttle = false;

    scope.initConfig = function() {
      $timeout(function() {
        scope.getConfig();
      });
    };

    scope.getConfig = function() {
      config = [];

      var startOffset = element
        .find('.sb-section-offset')
        .outerHeight();

      if (!startOffset) { startOffset = 0; }

      var sections = element
        .find('.sb-section-header')
        .parent()
        .toArray();

      sections.forEach(function(el, i) {
        var sectionHeight = $(el).outerHeight();
        var sectionStart;
        var sectionEnd;

        var subheader = $(el).find('.sb-section-header');
        var subheaderHeight = subheader.outerHeight();

        if (i !== 0) {
          sectionStart = config[config.length - 1].sectionEnd + config[config.length - 1].subheaderHeight;
        } else {
          sectionStart = startOffset;
        }

        sectionEnd = sectionStart + sectionHeight - subheaderHeight;

        config.push({
          'subheader': subheader,
          'subheaderHeight': subheaderHeight,
          'sectionStart': sectionStart,
          'sectionEnd': sectionEnd,
          'maxTransform': sectionEnd - sectionStart
        });
      });
    };

    element.on('scroll', function() {
      if (!throttle) {
        scope.getConfig();

        throttle = true;

        $timeout(function() {
          throttle = false;
        }, 1000);
      }

      var scrollTop = $(element).scrollTop();

      config.forEach(function(el, i) {
        var transformDistance = scrollTop - el.sectionStart;

        if (scrollTop > el.sectionStart && scrollTop < el.sectionEnd) {
          $(el.subheader)
            .css({
              'transform': 'translate3d(0px, ' + transformDistance + 'px, 0px)'
            })
            .addClass('md-whiteframe-1dp');
        } else if (scrollTop <= el.sectionStart) {
          $(el.subheader)
            .css({
              'transform': 'translate3d(0px, 0px, 0px)'
            })
            .removeClass('md-whiteframe-1dp');
        } else if (scrollTop >= el.sectionEnd) {
          $(el.subheader)
            .css({
              'transform': 'translate3d(0px, ' + el.maxTransform + 'px, 0px)'
            })
            .removeClass('md-whiteframe-1dp');
        }
      });
    });
  }

  return {
    restrict: 'A',
    link: link
  };
};
