import React, {useState, useEffect} from 'react';
import Alert from '@material-ui/lab/Alert';

export default function Toast(props) {
  const {
    timeout = 3000, severity = 'error', message, open, onClose = () => {
    }
  } = props;
  const [internalOpen, setInternalOpen] = useState(open);

  useEffect(() => {
    if (open) {
      setInternalOpen(true);
      setTimeout(() => {
        setInternalOpen(false);
        onClose();
      }, timeout);
    }
  }, [open]);

  if (!internalOpen) {
    return null;
  }
  return (
    <div style={{display: 'flex', position: 'absolute', bottom: 0, left: 0, right: 0, justifyContent:'center', boxShadow: "0px 5px 10px rgba(0,0,0,0.1)", padding:'20px', zIndex:10000}}>
      <Alert severity={severity}>{message}</Alert>
    </div>
  );
}
