import React, { useContext, useEffect, useState } from 'react';
import { GlobalContext } from '../utils';
import StudentListHeader from './studentListHeader';
import StudentItem from './studentItem';
import StudentOverview from './studentOverview';
import GridOverview from './gridview/gridOverview';
import SortStudentComponent from '../components/sortStudentComponent';
import { colors } from '../theme';

const classroomFields = `
  name
  courses {
    id
    name
    code
  }
  students {
      id
      last_name
      first_name
      photo_url
      email
    }`;
const CourseOverview = (props) => {
  const [loading, setLoading] = useState();
  const [selectedStudent, setSelectedStudent] = useState();
  const [selectedCourse, setSelectedCourse] = useState();
  const [selectedObjective, setSelectedObjective] = useState();

  const [showGrid, setShowGrid] = useState(false);
  const context = useContext(GlobalContext);
  const {
    Api, height, sortingMethod, openDrawer,
  } = context;
  const [classroom, setClassroom] = useState();

  const loadClassroom = async () => {
    setLoading(true);
    const response = await Api.getClassroom(classroomFields);
    setClassroom(response?.data?.classroom);
    setLoading(false);
  };
  useEffect(() => {
    loadClassroom();
  }, []);

  if (showGrid) {
    return (
      <div>
        <GridOverview
          course={selectedCourse}
          student={selectedStudent || classroom?.students[0]}
          onClose={() => setShowGrid(false)}
        />
        <StudentListHeader
          toggleMenu={openDrawer}
          toggleGrid={() => {
            if (classroom?.courses.length === 1) {
              setSelectedCourse(classroom?.courses[0]);
            }
            setShowGrid(!showGrid);
          }}
          showGridIcon={!!selectedCourse || classroom?.courses.length === 1}
          showGrid={showGrid}
        />
      </div>
    );
  }

  return (
    <div style={{ border: '0px solid green', height: '100%' }}>
      {selectedStudent && (
        <StudentOverview
          toggleGrid={() => setShowGrid(!showGrid)}
          selectedCourse={selectedCourse}
          student={selectedStudent}
          onClose={() => setSelectedStudent(null)}
          onCourseSelected={setSelectedCourse}
          classroomName={classroom?.name}
          onObjectiveSelected={setSelectedObjective}
          selectedObjective={selectedObjective}
        />
      )}
      <StudentListHeader
        toggleMenu={openDrawer}
        toggleGrid={() => {
          if (classroom?.courses.length === 1) {
            setSelectedCourse(classroom?.courses[0]);
          }
          setShowGrid(!showGrid);
        }}
        showGridIcon={!!selectedCourse || classroom?.courses.length === 1}
      />
      <div
        style={{
          overflow: 'scroll',
          height: height - 60,
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <div
          style={{
            borderBottom: `1px solid ${colors.borderColor}`,
            display: 'flex',
            justifyContent: 'flex-end',
            padding: '6px',
          }}
        >
          <SortStudentComponent style={{}} />
        </div>
        {classroom?.students
          .sort((a, b) => {
            if (sortingMethod === 'firstName') {
              return a.first_name.localeCompare(b.first_name);
            }
            return a.last_name.localeCompare(b.last_name);
          })
          .map((student) => (
            <div key={student.id}>
              <StudentItem
                selected={selectedStudent?.id === student.id}
                student={student}
                onClickStudent={() => {
                  setSelectedStudent(student);
                  if (classroom?.courses.length === 1) {
                    setSelectedCourse(classroom?.courses[0]);
                  }
                }}
              />
            </div>
          ))}
      </div>
    </div>
  );
};

export default CourseOverview;
