/**
 * @module StudybeeHandle/Directive
 */

module.exports = function() {
  /*@ngInject*/
  function controller($rootScope, $element, $mdSidenav, StudybeeHandle) {
    StudybeeHandle.setBeforeCloseCallback(function() {
      $element.removeClass('studybee-handle-opened-show');
    });

    StudybeeHandle.setInProgressCloseCallback(function() {
      $element.removeClass('studybee-handle-opened');
    });

    StudybeeHandle.setAfterCloseCallback(function() {
      $element.addClass('studybee-handle-closed studybee-handle-closed-show');
    });

    StudybeeHandle.setBeforeOpenCallback(function() {
      $element.removeClass('studybee-handle-closed-show');
    });

    StudybeeHandle.setInProgressOpenCallback(function() {
      $element.removeClass('studybee-handle-closed');
    });

    StudybeeHandle.setAfterOpenCallback(function() {
      $element.addClass('studybee-handle-opened studybee-handle-opened-show');
    });

    StudybeeHandle.setDisableCallback(function() {
      $element.addClass('studybee-handle-disabled');
    });

    StudybeeHandle.setEnableCallback(function() {
      $element.removeClass('studybee-handle-disabled');
    });

    this.toggleStudybee = () => {
      // can't assign it due to change context
      StudybeeHandle.studybeeToggle();

      // if (!!$mdSidenav('studybee-menu').isOpen()) {
      //   $rootScope.toggleMenu();
      // }
    };
  }

  return {
    scope: true,
    restrict: 'E',
    controller: controller,
    controllerAs: 'vm'
  };
};
