import React from 'react';
import { ArrowBack, Close as CloseIcon } from '@material-ui/icons';
import { useWindowDimensions } from '../hooks';
import { colors, fonts } from '../theme';

const StudentOverviewHeader = (props) => {
  const {
    studentName, courseName, onBack, onClose, classroomName, objectiveName,
  } = props;
  const dim = useWindowDimensions();

  return (
    <div
      style={{
        height: '60px',
        width: dim.width - 360,
        backgroundColor: colors.brandColorPink,
        alignItems: 'center',
        display: 'flex',
        minHeight: '58px',
        padding: '0px 0px 0px 0px',
        position: 'fixed',
      }}
    >
      {onBack && (
        <ArrowBack
          style={{
            cursor: 'pointer',
            color: colors.white,
            height: '25px',
            marginLeft: '16px',
          }}
          onClick={onBack}
        />
      )}
      {!onBack
        && <CloseIcon
          style={{
            cursor: 'pointer',
            color: colors.white,
            height: '25px',
            marginLeft: '16px',
          }}
        onClick={onClose}/>
      }
      <div style={{
        position: 'absolute', left: '50px', display: 'flex', alignItems: 'center',
      }}>
        <span style={{ ...fonts.MEDIUM, color: colors.white }}>{classroomName}</span>
      {courseName
      && <>
        <ArrowBack style={{ color: colors.white, transform: 'rotate(180deg)', height: '15px' }}/>
        <span style={{ ...fonts.MEDIUM, color: colors.white }}>{courseName}</span>
        <ArrowBack style={{ color: colors.white, transform: 'rotate(180deg)', height: '15px' }}/>
        <span style={{ ...fonts.MEDIUM, color: colors.white }}>{studentName}</span>
        <ArrowBack style={{ color: objectiveName ? colors.white : 'transparent', transform: 'rotate(180deg)', height: '15px' }}/>
        <span style={{ ...fonts.MEDIUM, color: colors.white }}>{objectiveName}</span>
      </>}
      </div>
    </div>
  );
};
export default StudentOverviewHeader;
