import { IComponentController } from 'angular';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { Output } from '../../../utils/decorators/output.decorator';
import { GroupedVisibilityDialogType } from '../../enums/groupedVisibilityDialogType.enum';
import { GroupedVisibilityFeedbackType } from '../../enums/groupedVisibilityFeedbackType.enum';
import { IEventEmitter } from '../../models/eventEmitter';
import { IGroupedVisibility } from '../../models/groupedVisibility.model';
import { IGroupedVisibilitySettings } from '../../models/groupedVisibilitySettings.model';
import { IGroupedVisibilityService } from '../../services/groupedVisibility/groupedVisibility.service';
import SharedComponentsModule from '../components.module';
import template from './groupedVisibility.tpl.html';
import style from './groupedVisibility.scss';
import {getTranslation} from '../../../../../../react_src/i18n';

@Component({
  module: SharedComponentsModule,
  template: template,
  selector: 'groupedVisibility',
  style: style,
})
export class GroupedVisibilityComponent implements IComponentController {
  @Output({ optional: true })
  public onChange: IEventEmitter<{ changes: IGroupedVisibility }>;

  @Input()
  public settings: IGroupedVisibilitySettings;

  public groupedVisibility: IGroupedVisibility;
  public i18n: { [key: string]: string };
  public isAllCourseDefinitionsSelected: boolean;
  public isAllStudentsSelected: boolean;
  public isAllTypeOfFeedbackSelected: boolean;
  public isAssignmentStudentView: boolean;
  public selectedTypeOfFeedback: { id: GroupedVisibilityFeedbackType, name: string }[];
  public typeOfFeedback: GroupedVisibilityFeedbackType[];

  constructor(private GroupedVisibilityService: IGroupedVisibilityService) {
    'ngInject';

    this.i18n = {
      typeOfFeedbackOverallLabel: 'typeOfFeedbackOverallSection',
      hintGroupedVisibilityVisibility: 'groupedVisibilityVisibilityHint',
      hintGroupedVisibilityStudents: 'groupedVisibilityStudentsHint',
      hintGroupedVisibilityCoursesSubjects: 'groupedVisibilityCoursesSubjectsHint',
      hintGroupedVisibilityFeedback: 'groupedVisibilityFeedbackHint',
    };
    this.isAllCourseDefinitionsSelected = true;
    this.isAllStudentsSelected = true;
    this.isAllTypeOfFeedbackSelected = true;
    this.isAssignmentStudentView = false;
    this.selectedTypeOfFeedback = [];
    this.typeOfFeedback = [
      GroupedVisibilityFeedbackType.ObjectiveAssessment,
      GroupedVisibilityFeedbackType.ObjectiveFeedback,
    ];
  }

  public $onInit(): void {
    this.groupedVisibility = this.GroupedVisibilityService.getEmptyGroupedVisibilityObject();
    this.groupedVisibility.students_ids = this.getIds(this.settings.students);
    this.groupedVisibility.course_definitions_ids = this.getIds(this.settings.courseDefinitions);
    this.isAssignmentStudentView = this.settings.type === GroupedVisibilityDialogType.AssignmentStudentView;
    this.selectedTypeOfFeedback = this.GroupedVisibilityService.getSelectedTypeOfFeedbackByType(this.settings.type);

    if (this.settings.type === GroupedVisibilityDialogType.StudentProgressView) {
      this.typeOfFeedback = [
        GroupedVisibilityFeedbackType.OverallAssessment,
        GroupedVisibilityFeedbackType.OverallTarget,
        GroupedVisibilityFeedbackType.OverallFeedback,
        ...this.typeOfFeedback,
      ];

      this.groupedVisibility.types_of_feedback.overall.result_published = null;
      this.i18n.typeOfFeedbackOverallLabel = 'typeOfFeedbackOverallAssessmentSectionStudentProgress';
      this.i18n.hintGroupedVisibilityVisibility = 'groupedVisibilityVisibilityHintStudentProgress';
      this.i18n.hintGroupedVisibilityStudents = 'groupedVisibilityStudentsHintStudentProgress';
      this.i18n.hintGroupedVisibilityCoursesSubjects = 'groupedVisibilityCoursesSubjectsHintStudentProgress';
      this.i18n.hintGroupedVisibilityFeedback = 'groupedVisibilityFeedbackHintStudentProgress';
    } else {
      this.typeOfFeedback = [
        GroupedVisibilityFeedbackType.OverallFeedback,
        GroupedVisibilityFeedbackType.OverallResult,
        ...this.typeOfFeedback,
      ];

      this.groupedVisibility.types_of_feedback.overall.target_grade_published = null;
      this.groupedVisibility.types_of_feedback.overall.grade_published = null;
    }
  }

  public selectAllStudents(): void {
    if (this.groupedVisibility.students_ids.length === this.settings.students.length) {
      this.groupedVisibility.students_ids = [];
      this.isAllStudentsSelected = false;
    } else {
      this.groupedVisibility.students_ids = this.getIds(this.settings.students);
      this.isAllStudentsSelected = true;
    }
    this.notifyChanges();
  }

  public selectAllCourseDefinitions(): void {
    if (this.groupedVisibility.course_definitions_ids.length === this.settings.courseDefinitions.length) {
      this.groupedVisibility.course_definitions_ids = [];
      this.isAllCourseDefinitionsSelected = false;
    } else {
      this.groupedVisibility.course_definitions_ids = this.getIds(this.settings.courseDefinitions);
      this.isAllCourseDefinitionsSelected = true;
    }
    this.notifyChanges();
  }

  public selectAllTypeOfFeedback(): void {
    this.isAllTypeOfFeedbackSelected = !(this.selectedTypeOfFeedback.length === this.typeOfFeedback.length);

    this.typeOfFeedback = this.isAllTypeOfFeedbackSelected ?
      this.getIds(this.selectedTypeOfFeedback) : [];

    this.setGroupedVisibilityFeedbackType(this.isAllTypeOfFeedbackSelected,
      this.groupedVisibility.types_of_feedback.objective);

    this.setGroupedVisibilityFeedbackType(this.isAllTypeOfFeedbackSelected,
      this.groupedVisibility.types_of_feedback.overall);

    this.selectTypeOfFeedback();
  }

  public selectTypeOfFeedback(): void {
    this.selectedTypeOfFeedback
      .forEach((typeOfFeedback: { id: GroupedVisibilityFeedbackType, name: string }): void => {
        const isSelected = (this.typeOfFeedback.indexOf(typeOfFeedback.id) > -1);
        switch (typeOfFeedback.id) {
          case GroupedVisibilityFeedbackType.OverallResult:
            this.groupedVisibility.types_of_feedback.overall.result_published = isSelected;
            break;
          case GroupedVisibilityFeedbackType.OverallFeedback:
            this.groupedVisibility.types_of_feedback.overall.feedback_published = isSelected;
            break;
          case GroupedVisibilityFeedbackType.OverallTarget:
            this.groupedVisibility.types_of_feedback.overall.target_grade_published = isSelected;
            break;
          case GroupedVisibilityFeedbackType.OverallAssessment:
            this.groupedVisibility.types_of_feedback.overall.grade_published = isSelected;
            break;
          case GroupedVisibilityFeedbackType.ObjectiveAssessment:
            this.groupedVisibility.types_of_feedback.objective.grade_published = isSelected;
            break;
          case GroupedVisibilityFeedbackType.ObjectiveFeedback:
            this.groupedVisibility.types_of_feedback.objective.feedback_published = isSelected;
            break;
        }
      });

    this.notifyChanges();
  }

  public getVisibilityLabel(): string {
    let label: string = getTranslation('chooseVisibility');

    if (this.groupedVisibility.visible === true) {
      label = getTranslation('visible');
    } else if (this.groupedVisibility.visible === false) {
      label = getTranslation('hidden');
    }

    return label;
  }

  public getStudentsSelectLabel(): string {
    this.updateSelectionsAll();

    let label: string = `${getTranslation('allStudents')}`;

    if (!this.isAllStudentsSelected) {
      label = `${this.groupedVisibility.students_ids.length} ${getTranslation('selected')}`;
    }

    return label;
  }

  public getCoursesSubjectsSelectLabel(): string {
    this.updateSelectionsAll();

    let label: string = `${getTranslation('allCoursesSubjects')}`;

    if (!this.isAllCourseDefinitionsSelected) {
      label = `${this.groupedVisibility.course_definitions_ids.length} ${getTranslation('selected')}`;
    }

    return label;
  }

  public getTypeOfFeedbackLabel(): string {
    this.updateSelectionsAll();

    let label: string = `${getTranslation('allFeedback')}`;

    if (!this.isAllTypeOfFeedbackSelected) {
      label = `${this.typeOfFeedback.length} ${getTranslation('selected')}`;
    }

    return label;
  }

  public notifyChanges(): void {
    const changes: IGroupedVisibility = Object.assign({}, this.groupedVisibility);
    this.onChange({ changes });
  }

  private updateSelectionsAll(): void {
    this.isAllStudentsSelected = this.groupedVisibility.students_ids.length === this.settings.students.length;
    this.isAllCourseDefinitionsSelected = this.groupedVisibility.course_definitions_ids.length === this.settings.courseDefinitions.length;
    this.isAllTypeOfFeedbackSelected = this.selectedTypeOfFeedback.length === this.typeOfFeedback.length;
  }

  private getIds(items: { id: number }[]): number[] {
    return this.GroupedVisibilityService.mapObjectToArrayOfIds(items);
  }

  private setGroupedVisibilityFeedbackType(value: boolean, object: { [prop: string]: any }): void {
    for (let prop in object) {
      if (object[prop] !== null) {
        object[prop] = value;
      }
    }
  }
}
