class Queue {
  constructor($q) {
    'ngInject';
    this.queue = $q.resolve();
  }

  push(promise) {
    this.queue = this.queue.then(promise, promise);

    return this;
  }
}

export class PromiseQueue {
  constructor($q) {
    'ngInject';

    this.$q = $q;
  }

  getInstance() {
    return new Queue(this.$q);
  }
}
