import { ICompileService, IComponentController, INgModelController, IRootElementService, IScope, ITimeoutService } from 'angular';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import { Output } from '../../../../utils/decorators/output.decorator';
import { ParentComponent } from '../../../../utils/decorators/parent-component.decorator';
import { IEventEmitter } from '../../../models/eventEmitter';
import SharedComponentsMaterialModule from '../material.module';
import style from './mdSelectAll.scss';
import template from './mdSelectAll.tpl.html';

@Component({
  module: SharedComponentsMaterialModule,
  selector: 'mdSelectAll',
  style: style,
})
export class MdSelectAllComponent implements IComponentController {
  @ParentComponent()
  public ngModel: INgModelController;

  @Output({ alias: 'onChange', optional: true })
  public notifyChanges: IEventEmitter<void>;

  @Input({ alias: 'mdDataSet' })
  public dataSet: any[];

  public isSelectedAll: boolean;

  constructor(private $scope: IScope, private $element: IRootElementService, private $timeout: ITimeoutService, private $compile: ICompileService) {
    'ngInject';
  }

  public $onInit(): void {
    this.refreshSelectAll(100);

    this.ngModel.$viewChangeListeners.push(() => {
      this.refreshSelectAll();
    });
  }

  public $postLink(): void {
    this.$timeout((): void => {
      const content: JQuery = this.$compile(template)(this.$scope);
      this.$element.append(content);
    }, 0);
  }

  public onChange() {
    this.notifyChanges();
    this.refreshSelectAll();
  }

  private refreshSelectAll(delay: number = 0): void {
    this.$timeout((): void => {
      this.isSelectedAll = this.ngModel.$modelValue.length === this.dataSet.length;
    }, delay);
  }
}
