import { IComponentController, ITimeoutService } from 'angular';
import { StateService } from '@uirouter/core';
import { showHintDialog } from '../../../common/dialogs/dialogs.helper';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import StudentCourseManagementModule from './studentCourseManagement.module';
import style from './studentCourseManagement.scss';
import template from './studentCourseManagement.tpl.html';

@Component({
  module: StudentCourseManagementModule,
  selector: 'student-course-management',
  template: template,
  style: style,
})
export class StudentCourseManagementComponent implements IComponentController {
  @Input()
  public coursesDefinitions: any[];

  constructor(private $state: StateService, private $cookies: ng.cookies.ICookiesService, private $timeout: ITimeoutService, private $mdDialog) {
    'ngInject';

    this.coursesDefinitions = [];
  }

  public $onInit(): void {
    showHintDialog(this.$mdDialog, this.$timeout, this.$state.current.name, this.$cookies);
  }
}
