import {StateService, TransitionPromise} from '@uirouter/core';
import {IQService, IRootScopeService} from 'angular';
import {HttpResponse} from '../../shared/models/httpResponse.model';
import {IInterceptor} from '../../shared/models/interceptor.model';
import {Interceptor} from '../decorators/interceptor.decoreator';
import {HttpStatusCode} from '../enums/httpStatusCode.enum';
import {UserInfo} from '../services/userInfo.service';
import UtilsModule from '../utils.module';

@Interceptor({
  module: UtilsModule,
})
export class MissingAgreementInterceptor implements IInterceptor {

  private readonly urlRegex: RegExp;
  private UserInfo?: UserInfo;

  constructor(private $rootScope: IRootScopeService,
              private $q: IQService,
              private $state: StateService,
              private $injector: angular.auto.IInjectorService) {
    'ngInject';

    this.urlRegex = /\/schools\/user_setup\/?$/;
    this.UserInfo = null;
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> | TransitionPromise {
    this.prepareUserInfoService();

    if (response.status === HttpStatusCode.NOT_FOUND && this.urlRegex.test(response.config.url)) {
      if (this.UserInfo.userId && this.UserInfo.isTeacher()) {
        if (this.UserInfo.agreementSeen) {
          this.$state.go('userSetup');
        } else {
          if (this.UserInfo.gdpr) {
            this.$state.go('userSetup');
          } else {
            this.$state.go('userSetup');
          }
        }

        this.openStudybeeIfClose();
      }
    }

    return this.$q.reject(response);
  }

  private openStudybeeIfClose(): void {
    const StudybeeHandle: any = this.$injector.get('StudybeeHandle');
    StudybeeHandle.studybeeActivate();
  }

  private prepareUserInfoService(): void {
    this.UserInfo = this.$injector.get('UserInfo');
  }
}
