import {getTranslation} from "../i18n";
import {colors, fonts} from "../theme";
import {Visibility, VisibilityOff} from "@material-ui/icons";
import {ReactComponent as FeedbackIcon} from "../images/sb-ico-feedback.svg";
import {ReactComponent as NoteIcon} from "../images/sb-ico-note.svg";
import React, {memo} from "react";
import Tooltip from "../components/tooltip";
import PublishedIcon from "../components/publishedIcon";

const styles = {
  item: {
    display: "flex",
    width: "30px",
    height: "40px",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    // border: '1px solid',
  },
};

const tt1 = getTranslation("courseProgressAssessmentLabel");
const tt2 = getTranslation("overallFeedback");
const tt3 = getTranslation("overallPrivateNotesMode");

const CourseObjectiveSummaryItem = (props) => {
  const {assessment, grade} = props;
  return (
    <div style={{
      display: "flex",
      alignItems: "center",
      borderLeft: `1px solid ${colors.borderColor}`,
      borderRight: `1px solid ${colors.borderColor}`,
      paddingRight: '6px'
    }}>
      <Tooltip title={tt1}>
        <div style={styles.item}>
          <span
            style={{
              ...fonts.LARGE,
              color: grade?.color || colors.iconColorLight,
              fontWeight: "800",
            }}
          >
            {grade?.name || "-"}
          </span>
          {grade?.name && (
            <PublishedIcon published={assessment?.grade_published} active={true}/>
          )}
        </div>
      </Tooltip>
      <Tooltip title={tt2}>
        <div style={styles.item}>
          <FeedbackIcon
            style={{height: "20px", width: "20px"}}
            fill={
              assessment?.feedback ? colors.iconColor : colors.iconColorLight
            }
          />
          <PublishedIcon published={assessment?.feedback_published} active={assessment?.feedback}/>
        </div>
      </Tooltip>
      <Tooltip title={tt3}>
        <div style={{display: "flex", justifyContent: "center"}}>
          <NoteIcon
            style={{height: "20px", width: "20px", marginBottom: "10px"}}
            fill={
              assessment?.private_note ? colors.iconColor : colors.iconColorLight
            }
          />
        </div>
      </Tooltip>
    </div>
  );
};

export default memo(CourseObjectiveSummaryItem);
