import { TransitionService, StateObject, IStateMatch, Transition, StateDeclaration} from '@uirouter/core';
import { StateProvider } from '@uirouter/angularjs';


export const CourseManagementRouting: Function = ($stateProvider: StateProvider, $transitionsProvider: TransitionService): void => {
  'ngInject';

  const predicateState: IStateMatch = (stateObject: StateObject): boolean => {
    return stateObject.name.includes('teacherCourseManagement') ||
      stateObject.name.includes('studentCourseManagement');
  };

  $transitionsProvider.onBefore({ to: predicateState }, (transition: Transition): any => {
    const transitionTo: StateDeclaration = transition.to();
    (transitionTo as any).themeClass = 'sb-theme-blue';

    return true;
  });
};
