/**
 * Represents a GroupCheckboxes.
 * @constructor
 */

function AssignmentGroupCheckboxes() {}
/**
 * Handle click on single checkbox - at the end function trigger checkboxesCheckAll
 * @param {Object} singleItem Single item
 * @param {Array} itemsInGroup List of all items in group
 * @param {Object} group Single group
 * @param {Array} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentGroupCheckboxes.prototype.assignmentCheckboxesSingle = function(singleItem, itemsInGroup, group, allGroups, editMultiple) {
  singleItem.checked = !singleItem.checked;

  var groupCheckboxChecked = itemsInGroup.every(function(elem, i, arr) {
    return elem.checked;
  });

  if (groupCheckboxChecked) {
    group.checked = true;
  } else {
    group.checked = false;
  }

  this.assignmentCheckboxesCheckAll(allGroups, editMultiple);
};

/**
 * Handle click on group checkbox - at the end function trigger checkboxesCheckAll
 * @param {Array} itemsInGroup List of all items in group
 * @param {Object} group Single group
 * @param {Array} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentGroupCheckboxes.prototype.assignmentCheckboxesGroup = function(itemsInGroup, group, allGroups, editMultiple) {
  group.checked = !group.checked;

  if (group.checked) {
    itemsInGroup.forEach(function(elem) {
      elem.checked = true;
    });
  } else {
    itemsInGroup.forEach(function(elem) {
      elem.checked = false;
    });
  }

  this.assignmentCheckboxesCheckAll(allGroups, editMultiple);
};

/**
 * Handle click on all checkbox which check or uncheck all checkboxes
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 * @param {Array} allGroups List of all groups
 */
AssignmentGroupCheckboxes.prototype.assignmentCheckboxesAll = function(editMultiple, allGroups) {
  editMultiple.checkboxesChecked = !editMultiple.checkboxesChecked;

  if (editMultiple.checkboxesChecked) {
    allGroups.forEach(function(courseDefinition) {
      courseDefinition.students.forEach(function(student) {
        student.checked = true;
        student.objective_assessments.forEach(function(objective) {
          objective.checked = true;
        });
      });
    });
  } else {
    allGroups.forEach(function(courseDefinition) {
      courseDefinition.students.forEach(function(student) {
        student.checked = false;
        student.objective_assessments.forEach(function(objective) {
          objective.checked = false;
        });
      });
    });
  }
};

/**
 * Check if all checkboxes are checked or unchecked
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 * @param {Array} allGroups List of all groups
 */
AssignmentGroupCheckboxes.prototype.assignmentCheckboxesCheckAll = function(allGroups, editMultiple) {
  var allCheckboxChecked = true;

  allGroups.forEach(function(courseDefinition) {
    courseDefinition.students.forEach(function(student) {
      if (!student.checked) {
        allCheckboxChecked = false;
      }
    });
  });

  if (allCheckboxChecked) {
    editMultiple.checkboxesChecked = true;
  } else {
    editMultiple.checkboxesChecked = false;
  }
};

/**
 * Check if any checkboxes are checked
 * @param {Array} allGroups List of all groups
 */
AssignmentGroupCheckboxes.prototype.assignmentCheckboxesCheckIfAny = function(allGroups) {
  var anyCheckboxChecked = false;

  for (var i = 0; i < allGroups.length; i++) {
    for (var j = 0; j < allGroups[i].students.length; j++) {
      anyCheckboxChecked = allGroups[i].students[j].objective_assessments.some(function(objective) {
        return objective.checked === true;
      });
      if (anyCheckboxChecked) { break; }
    }
    if (anyCheckboxChecked) { break; }
  }

  return anyCheckboxChecked;
};

/**
 * Clear all checkboxes
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 * @param {Array} allGroups List of all groups
 */
AssignmentGroupCheckboxes.prototype.assignmentClearCheckboxes = function(editMultiple, allGroups) {
  editMultiple.checkboxesChecked = false;

  allGroups.forEach(function(courseDefinition) {
    courseDefinition.students.forEach(function(student) {
      student.checked = false;
      student.objective_assessments.forEach(function(objective) {
        objective.checked = false;
      });
    });
  });
};

module.exports = AssignmentGroupCheckboxes;
