import get from 'lodash.get';
import { Events } from '../global-events.ts';
import { HostPage } from '../host-page-selectors.ts';

const ACTIONS = {
  CANCEL: 1,
  DELETE: 2,
  SAVE_DRAFT: 3,
  ASSIGN: 4,
  SCHEDULE: 5,
  ALERT_OK: 6,
  ALERT_CANCEL: 7,
  OPEN_CREATOR: 8,
};

const eventNamespace = 'hostPageObserver';


export class HostPageNewAssignmentObserver {
  constructor(
    $rootScope, $state, $timeout, Path, UserInfo, Updates, HostPageQualifiers,
    NavigationHistory, Mixpanel, StudybeeHandle,
  ) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
    this.$timeout = $timeout;
    this.Path = Path;
    this.UserInfo = UserInfo;
    this.Updates = Updates;
    this.HostPageQualifiers = HostPageQualifiers;
    this.NavigationHistory = NavigationHistory;
    this.Mixpanel = Mixpanel;
    this.StudybeeHandle = StudybeeHandle;
  }

  init() {
    this.EVENTS = [
      // modal
      ...HostPage.ASSIGNMENT_MODAL_ASSIGN_SEL.map((selector) => [selector, ACTIONS.ASSIGN]),
      ...HostPage.ASSIGNMENT_MODAL_ADD_TOPIC.map((selector) => [selector, ACTIONS.ASSIGN]),
      ...HostPage.ASSIGNMENT_MODAL_SAVE_SEL.map((selector) => [selector, ACTIONS.ASSIGN]),
      ...HostPage.ASSIGNMENT_MODAL_ASK_SEL.map((selector) => [selector, ACTIONS.ASSIGN]),
      ...HostPage.ASSIGNMENT_MODAL_DELETE_SEL.map((selector) => [selector, ACTIONS.DELETE]),
      // [HostPage.ASSIGNMENT_MODAL_CANCEL_SEL, ACTIONS.CANCEL],
      // // menu
      ...HostPage.ASSIGNMENT_MENU_ITEM_ASSIGN_SEL.map((selector) => [selector, ACTIONS.ASSIGN]),
      ...HostPage.ASSIGNMENT_MENU_ITEM_SCHEDULE_SEL.map((selector) => [selector, ACTIONS.SCHEDULE]),
      ...HostPage.ASSIGNMENT_MENU_ITEM_SAVE_DRAFT_SEL.map((selector) => [selector, ACTIONS.SAVE_DRAFT]),
      // alerts
      ...HostPage.ASSIGNMENT_ALERT_OK_SEL.map((selector) => [selector, ACTIONS.ALERT_OK]),
      //classwork
      ...HostPage.CLASSWORK_CREATE_NEW_ASSIGNMENT.map((selector) => [selector, ACTIONS.OPEN_CREATOR]),
      ...HostPage.CLASSWORK_CREATE_NEW_QUESTION.map((selector) => [selector, ACTIONS.OPEN_CREATOR]),
      ...HostPage.CLASSWORK_CREATE_NEW_REUSE_POST.map((selector) => [selector, ACTIONS.OPEN_CREATOR]),
      ...HostPage.CLASSWORK_CREATE_NEW_TOPIC.map((selector) => [selector, ACTIONS.OPEN_CREATOR]),
    ];

    this.$rootScope.$on(Events.STATE_CHECKED, () => {
      if (this.UserInfo.userRole === 'teacher' && this.Path.isStreamPage()) {
        this.register();
      } else {
        this.unregister();
      }
    });

    this.registered = false;
    this.registerPseudoSelector();
  }

  registerPseudoSelector() {
    //Addition pseudo-selector for jQuery, this comparator ignore case
    //Used to check text (and interact) on button and other elements on Google classroom page
    if (!$.expr[':'].textEquals) {
      $.expr[':'].textEquals = $.expr
        .createPseudo((text) => (element) => get(element, 'innerText', false) && !!element.innerText.match(new RegExp(`^${text}$`, 'i')));
    }
  }

  register() {
    if (!this.registered) {
      this.unregister();
      const eventName = `mousedown.${eventNamespace}`;

      this.EVENTS.forEach((event) => {
        const [selector, action] = event;
        $(document).on(eventName, selector, { action }, this.handler.bind(this));
      });

      this.registered = true;
    }
  }

  unregister() {
    $(document).off(`.${eventNamespace}`);
    this.registered = false;
  }

  /**
   * According to the restored logic, this should behave like this:
   * Should open modal for new assignments:
   *  - When new schedule is set up
   *  - when
   * @param event
   */
  handler(event) {
    event.stopPropagation();
    // this is the part of the old logic
    const [shouldRefreshAssignments, shouldShowModal] = this.shouldRefreshOrShowModal(event.data.action);
    const shouldHideStudybee = this.shouldHideStudybee(event.data.action);
    if (shouldShowModal) {
      try {
        //language=JQuery-CSS
        let title;
        const textArea = $('[role="dialog"] textarea');
        const input = $('[role="alertdialog"] input');
        if (textArea.length) {
          title = textArea[0].value.trim();
        } else if (input.length) {
          title = input[0].value.trim();
        }
        // probably we need to postpone that event a little
        // also , show only the title exists
        this.$timeout(() => {
          if (title) {
            // this.$rootScope.$emit(Events.OPEN_ASSIGNMENT_POPUP, {
            //   title,
            // });
            chrome.storage.sync.set({ newAssignment: title }, () => console.log('newAssignment', title));
            // trigger mixpanel action
            this.Mixpanel.track('new_assignment_popup');
          }
        }, 2000);
      } catch (err) {
        // in the future we should handle it better
        console.log(err);
      }
    }

    if (shouldRefreshAssignments) {
      this.Updates.updateAssignments(this.Path.getClassroomId(), true)
        .then(() => {
          this.$rootScope.$emit(Events.UPDATED_ASSIGNMENTS);
        });
    }

    if (shouldHideStudybee) {
      this.$timeout(() => this.StudybeeHandle.studybeeClose());
    }
  }

  shouldRefreshOrShowModal(action) {
    let refresh = false;
    let showModal = false;

    if (this.HostPageQualifiers.hasScheduleAlertDialog) {
      if (action === ACTIONS.ALERT_OK) {
        // 1. Post has been scheduled through 'Schedule' button
        // -> update list of assignments and open message.
        refresh = true;
        showModal = true;
      }
    } else if (this.HostPageQualifiers.hasAlertDialog) {
      if ([ACTIONS.ALERT_OK, ACTIONS.DELETE].includes(action)) {
        // 1. For all other alert dialogs the safest option
        // is to refresh list of assignments.
        refresh = true;
      }
    } else if (this.HostPageQualifiers.hasCreatingModal) {
      // actions for all events
      if ([ACTIONS.CANCEL, ACTIONS.ASSIGN, ACTIONS.SAVE_DRAFT].includes(action)) {
        refresh = true;
        showModal = true;
      }
    } else if (this.HostPageQualifiers.hasEditingPublishedModal) {
      if (action === ACTIONS.ASSIGN) {
        // 1. Post has been published through the 'Save' button
        refresh = true;
      }
    }
    return [refresh, showModal];
  }

  shouldHideStudybee(action) {
    return action === ACTIONS.OPEN_CREATOR;
  }
}
