import {Injectable} from '@angular/core';
import {AssignmentsStore} from './assignments.store';
import {HttpService} from '../../../core/http/http.service';
import {AssignmentsQuery} from './assignments.query';
import {ID, transaction} from '@datorama/akita';
import {map, tap} from 'rxjs/operators';
import {Assignment, AssignmentsUI, AssignmentType, NormalizeAssignmentsResponse} from './assignment.model';
import {Path} from '../../../ajs-upgraded-providers';
import {NormalizedSchema} from 'normalizr';
import {normalizeAssignmentsResponse} from './assignments.normalizer';

@Injectable({ providedIn: 'root' })
export class AssignmentsService {

  constructor(protected assignmentsStore: AssignmentsStore,
              protected assignmentsQuery: AssignmentsQuery,
              protected http: HttpService,
              protected path: Path) {
  }

  get() {
    return this.assignmentsQuery.getHasCache() ? this.assignmentsQuery.selectAll() : this.getFromAPI();
  }

  setFilters(filters: AssignmentsUI['filters']) {
    this.assignmentsStore.update(state => ({
      ...state,
      ui: {
        ...state.ui,
        filters,
      },
    }));

    this.assignmentsStore.forceRefreshEntities();
  }

  setSorting(sort: AssignmentsUI['sort']) {
    this.assignmentsStore.update(state => ({
      ...state,
      ui: {
        ...state.ui,
        sort,
      },
    }));

    this.assignmentsStore.forceRefreshEntities();
  }

  protected getFromAPI() {
    const courseID: ID = this.path.getClassroomId();
    const url = `/classes/student_assignments/${courseID}/`;

    return this.http.get<Assignment[]>(url).pipe(
      map(response => this.unifyDateTimeFormat(response)),
      map(response => this.updateAssignmentsType(response)),
      map(response => normalizeAssignmentsResponse(response)),
      tap(normalizeResponse => this.putAssignmentsIntoStorage(normalizeResponse)),
      map(normalizeResponse => Object.values(normalizeResponse.entities.assignments || {})),
    );
  }

  @transaction()
  protected putAssignmentsIntoStorage(normalizeResponse: NormalizedSchema<NormalizeAssignmentsResponse, ID[]>) {
    this.assignmentsStore.set({
      entities: normalizeResponse.entities.assignments || {},
      ids: normalizeResponse.result
    });
  }

  protected unifyDateTimeFormat(assignments: Assignment[]): Assignment[] {
    return assignments.map(assignment => {
      const assessmentLastChanged = (new Date(assignment.assessment_last_changed)).getTime() || assignment.assessment_last_changed;
      const assignmentCreated = (new Date(assignment.assignment_created)).getTime() || assignment.assignment_created;
      return {
        ...assignment,
        assessment_last_changed: assessmentLastChanged,
        assignment_created: assignmentCreated,
      };
    });
  }

  protected updateAssignmentsType(assignments: Assignment[]): Assignment[] {
    return assignments.map(assignment => {
      if (assignment.assignment_not_assessable || !assignment.assignment_type) {
        return {
          ...assignment,
          assignment_type: AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
        };
      }
      return { ...assignment };
    });
  }
}
