class controller {
  constructor(Config, FetchData) {
    'ngInject';

    this.Config = Config;
    this.FetchData = FetchData;
  }

  downloadFile(fileType) {
    this.FetchData.getDownloadKey(
      this.student.id,
      this.courseDefinition.course_definition,
    )
      .then(response => response.key)
      .then((key) => {
        window.open(`${this.Config.apiUrl}/classes/export_grades/${key}.${fileType}`, '_blank');
      });
  }
}

export const DownloadPdfMenu = {
  bindings: {
    student: '<',
    courseDefinition: '<',
  },
  controller,
  template: `
              <md-menu>
                <md-button
                    class="md-icon-button"
                    ng-click="$mdMenu.open($event)">
                    <md-icon>
                        more_vert
                    </md-icon>
                </md-button>

                <md-menu-content width="3">
                    <md-menu-item>
                        <md-button 
                            ng-click="$ctrl.downloadFile('pdf')"
                            localize>
                            __MSG_downloadPDFOption__
                        </md-button>
                    </md-menu-item>
                    <md-menu-item>
                        <md-button 
                            ng-click="$ctrl.downloadFile('csv')"
                            localize>
                            __MSG_downloadCSVOption__
                        </md-button>
                    </md-menu-item>
                </md-menu-content>
            </md-menu>
  `,
};
