import { getTranslation } from '../../../../../react_src/i18n';
/**
 * @module Update/Service
 * @description
 *   Handle update assignments, students and files information.
 * @param {Object} $q Angular $q object
 */

/*@ngInject*/
function Updates($q, $mdToast, FetchData, NavigationHistory, Students, Assignments) {
  return {
    assignmentsAndStudentsDate: null,

    updateAssignmentsLoading: false,
    updateStudentsLoading: false,
    updateFilesLoading: false,

    setSynchronizeDataCallbacks: [],

    /**
     * Update Assignments and Students list
     * @memberof module:Update/Service
     * @param {Number} courseId
     * @param {Boolean} showToast
     * @param {Boolean} viewReload
     * @return {Promise}
     */
    updateAssignmentsAndStudents(courseId, showToast, viewReload) {
      const deferred = $q.defer();

      return $q.all(
        [
          this.updateAssignments(courseId, false),
          this.updateStudents(courseId, false),
        ],
      )
        .then((res) => {
          deferred.resolve(res);

          this.updateAssignmentsAndStudentsDate();

          if (viewReload) {
            NavigationHistory.reloadCurrent();
          }

          if (showToast) {
            $mdToast.show({
              templateUrl: 'utils/views/toast.html',
              parent: $('body'),
              hideDelay: 3000,
              position: 'bottom right',
              controller() {
                this.information = getTranslation('assignmentsAndStudentsUpdatedToast');
              },
              controllerAs: 'to',
            });
          }
        });
    },

    /**
     * Update Assignments list
     * @memberof module:Update/Service
     * @param {Number} courseId
     * @param {Boolean} showToast
     * @return {Promise}
     */
    async updateAssignments(courseId, showToast) {
      this.updateAssignmentsLoading = true;
      return FetchData.updateAssignmentsList(courseId)
        .then((assignments) => {
          if (showToast) {
            $mdToast.show({
              templateUrl: 'utils/views/toast.html',
              parent: $('body'),
              hideDelay: 3000,
              position: 'bottom right',
              controller() {
                this.information = getTranslation('assignmentsUpdatedToast');
              },
              controllerAs: 'to',
            });
          }
          Assignments.updateAssignmentsList(assignments);

          this.updateAssignmentsLoading = false;
          return assignments;
        });
    },

    /**
     * Update Students list
     * @memberof module:Update/Service
     * @param {Number} courseId
     * @param {Boolean} showToast
     * @return {Promise}
     */
    updateStudents(courseId, showToast) {
      if (!courseId) {
        return;
      }

      const deferred = $q.defer();
      this.updateStudentsLoading = true;

      // Synchronization

      FetchData
        .getStudentsList(courseId)
        .then((res) => {
          if (showToast) {
            $mdToast.show({
              templateUrl: 'utils/views/toast.html',
              parent: $('body'),
              hideDelay: 3000,
              position: 'bottom right',
              controller() {
                const to = this;

                to.information = getTranslation('studentsUpdatedToast');
              },
              controllerAs: 'to',
            });
          }

          Students.updateStudentsList(res);

          this.updateStudentsLoading = false;
          deferred.resolve(res);
        }, (err) => {
          deferred.reject(err);
        });

      return deferred.promise;
    },

    /**
     * Update Files lists
     * @memberof module:Update/Service
     * @param {Number} assignmentId
     * @param {Boolean} showToast
     * @return {Promise}
     */
    updateFiles(assignmentId, showToast) {
      if (!assignmentId) {
        return;
      }

      const deferred = $q.defer();
      this.updateFilesLoading = true;

      FetchData.updateFilesListForAssignment(assignmentId).then((res) => {
        if (showToast) {
          $mdToast.show({
            templateUrl: 'utils/views/toast.html',
            parent: $('body'),
            hideDelay: 3000,
            position: 'bottom right',
            controller() {
              this.information = getTranslation('filesUpdatedToast');
            },
            controllerAs: 'to',
          });
        }

        this.updateFilesLoading = false;
        deferred.resolve(res);
      }, (err) => {
        deferred.reject(err);
      });

      return deferred.promise;
    },

    /**
     * Replace old date with current date and execute setSynchronizeDataCallbacks
     * @memberof module:Updates/Service
     */
    updateAssignmentsAndStudentsDate() {
      let date = new Date();
      date = date.toLocaleString();
      this.assignmentsAndStudentsDate = date;

      this.setSynchronizeDataCallbacks.forEach((elem) => {
        elem();
      });
    },

    /**
     * Set callback which is trigger on update assignments and students
     * @memberof module:Updates/Service
     * @param {Object} callback Callback function
     */
    setSynchronizeDataCallback(callback) {
      this.setSynchronizeDataCallbacks.push(callback);
    },
  };
}

module.exports = Updates;
