export interface ICoverageMatrixGrade {
  circle_label: string;
  color: string;
  coverage_of_assignments: number;
  id: number;
  is_passed: boolean;
  label: string;
  name: string;
  number_of_assingments: number;
  order: number;
}

export interface ICoverageMatrix {
  coverage_matrix_grades: ICoverageMatrixGrade[];
  id: number;
  is_counter: boolean;
  isSelected?: boolean;
  name: string;
  number_of_assingments: number;
}

export class CoverageMatrix implements ICoverageMatrix {
  public coverage_matrix_grades: ICoverageMatrixGrade[];
  public id: number;
  public is_counter: boolean;
  public name: string;
  public number_of_assingments: number;

  constructor(coverageMatrix?: ICoverageMatrix) {
    Object.apply(this, coverageMatrix || {});
  }
}
