import React from 'react';
import {PUBLISH_STATES} from "./publishIcon";
import {getTranslation} from "../i18n";
import {ReactComponent as VisibilityKeep} from '../images/visibilityKeep.svg';
import Tooltip from "./tooltip";
import PublishedIcon from "./publishedIcon";


const getTooltipText = (state) => {
  switch (state) {
    case PUBLISH_STATES.published:
      return getTranslation('visibility_tooltip_publish');
    case PUBLISH_STATES.notPublished:
      return getTranslation('visibility_tooltip_unpublish');
    case PUBLISH_STATES.dontChange:
      return getTranslation('visibility_tooltip_dont_change');
  }
};

const VisibilityIcon = (props) => {
  const {visible: state, style, size} = props;

  return (
    <Tooltip title={getTooltipText(state)}>
      {state !== PUBLISH_STATES.dontChange &&
      <PublishedIcon size={size} published={state === PUBLISH_STATES.published} active={true} style={style}/>}
      {state === PUBLISH_STATES.dontChange && <VisibilityKeep style={{width: size ? size + 'px' : '12px', ...style}}/>}
    </Tooltip>
  );
};
export default VisibilityIcon;