import { IComponentController } from 'angular';
import { IEventEmitter } from '../../../../../../shared/models/eventEmitter';
import { IObjectiveAssignmentAssessment } from '../../../../../../shared/models/objectiveAssignmentAssessment.model';
import { Component } from '../../../../../../utils/decorators/component.decorator';
import { Executable, ExecuteGuard } from '../../../../../../utils/decorators/executeGuard.decorator';
import { Input } from '../../../../../../utils/decorators/input.decorator';
import { Output } from '../../../../../../utils/decorators/output.decorator';
import TeacherCourseProgressModule from '../../../teacher-course-progress.module';
import style from './assignmentOverviewContent.scss';
import template from './assignmentOverviewContent.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'assignment-overview-content',
  template: template,
  style: style,
})
export class AssignmentOverviewContentComponent implements IComponentController {
  @Input()
  public objectiveAssignmentAssessment: IObjectiveAssignmentAssessment;
  @Output()
  public change: IEventEmitter<{ objectiveAssignmentAssessment: IObjectiveAssignmentAssessment }>;

  constructor() {
    'ngInject';

    this.objectiveAssignmentAssessment = null;
  }

  @Executable
  public onClickResultPublished(): void {
    this.objectiveAssignmentAssessment.result_published = !this.objectiveAssignmentAssessment.result_published;
    this.onChange();
  }

  @Executable
  public onClickFeedbackPublished(): void {
    this.objectiveAssignmentAssessment.feedback_published = !this.objectiveAssignmentAssessment.feedback_published;
    this.onChange();

  }

  @Executable
  public onChange(): void {
    this.change({ objectiveAssignmentAssessment: this.objectiveAssignmentAssessment });
  }

  @ExecuteGuard
  public isEditable(): boolean {
    return this.objectiveAssignmentAssessment.teacher_in_class;
  }
}
