import { SelectEducationalProperties } from '../controllers/select-educational-properties.controller';
import { getTranslation } from '../../../../../../../react_src/i18n';

export class SearchCurriculumDialogCtrl extends SelectEducationalProperties {
  constructor(
    $mdDialog, FetchData, Path,
    educationalSystemId,
    educationalSystems,
    isLocked,
  ) {
    'ngInject';

    super(FetchData);
    this.$mdDialog = $mdDialog;
    this.Path = Path;
    this.educationalSystemId = educationalSystemId;
    this.educationalSystems = educationalSystems;
    this.isLocked = isLocked;

    this.$onInit();
  }

  $onInit() {
    this.educationalSystem = this.educationalSystems.find(
      (educationalSystem) => educationalSystem.id === this.educationalSystemId,
    );

    if (this.educationalSystem) {
      this.updateEducationalStages(this.educationalSystem);
    }
    this.searchCourseText = '';
    this.searchCategories = [
      {
        name: getTranslation('categoryStudybeeCarriculumsLabel'),
        searchFunction: 'getStudybeeCurriculums',
        searchType: 1,
      },
      {
        name: getTranslation('categoryMyCarriculumsLabel'),
        searchFunction: 'getCurriculumLibrary',
        searchType: 2,
      },
      {
        name: getTranslation('categorySchoolCarriculumsLabel'),
        searchFunction: 'getSchoolCurriculums',
        searchType: 3,
      },
      {
        name: getTranslation('categoryAllUserStudybeeCarriculumsLabel'),
        searchFunction: 'allUsersCurriculums',
        searchType: 2,
      },
    ];
  }

  searchCourse() {
    const searchParams = {
      search: this.searchCourseText,
      educational_system: SearchCurriculumDialogCtrl.convertToSearchParam(this.educationalSystem),
      educational_stage: SearchCurriculumDialogCtrl.convertToSearchParam(this.educationalStage),
      scale: SearchCurriculumDialogCtrl.convertToSearchParam(this.scale),
      classroom_id: this.Path.getClassroomId(),
    };

    return this.FetchData
      .v3[this.searchCategory.searchFunction](searchParams);
  }

  static convertToSearchParam(model) {
    if (model === undefined) {
      return '';
    }
    return model.id;
  }

  addCurriculum() {
    const courseToAdd = this.searchCategory.searchFunction === 'getCurriculumLibrary'
      ? this.selectedItem.course : this.selectedItem;

    this.$mdDialog.hide(courseToAdd);
  }
}
