/**
 * Represents a SingleCheckbox.
 * @constructor
 */

function AssignmentSingleCheckbox() {}
/**
 * Handle click on single checkbox - at the end function trigger checkboxCheckAll
 * @param {Object} singleItem Single item
 * @param {Object} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentSingleCheckbox.prototype.assignmentCheckboxSingle = function(singleItem, allGroups, editMultiple) {
  singleItem.checked = !singleItem.checked;

  this.assignmentCheckboxCheckAll(allGroups, editMultiple);
};

/**
 * Handle click on all checkbox which check or uncheck all checkboxes
 * @param {Array} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentSingleCheckbox.prototype.assignmentCheckboxAll = function(allGroups, editMultiple) {
  editMultiple.checkboxChecked = !editMultiple.checkboxChecked;

  if (editMultiple.checkboxChecked) {
    allGroups.forEach(function(courseDefinition) {
      courseDefinition.objectives.forEach(function(objective) {
        objective.checked = true;
      });
    });
  } else {
    allGroups.forEach(function(courseDefinition) {
      courseDefinition.objectives.forEach(function(objective) {
        objective.checked = false;
      });
    });
  }
};

/**
 * Check if all checkboxes are checked or unchecked
 * @param {Array} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentSingleCheckbox.prototype.assignmentCheckboxCheckAll = function(allGroups, editMultiple) {
  var allCheckboxChecked = true;

  allGroups.forEach(function(courseDefinition) {
    courseDefinition.objectives.forEach(function(objective) {
      if (!objective.checked) {
        allCheckboxChecked = false;
      }
    });
  });

  if (allCheckboxChecked) {
    editMultiple.checkboxChecked = true;
  } else {
    editMultiple.checkboxChecked = false;
  }
};

/**
 * Check if any checkboxes are checked
 * @param {Array} allGroups List of all groups
 */
AssignmentSingleCheckbox.prototype.assignmentCheckboxCheckIfAny = function(allGroups) {
  var anyCheckboxChecked = false;

  for (var i = 0; i < allGroups.length; i++) {
    anyCheckboxChecked = allGroups[i].objectives.some(function(elem) {
      return elem.checked === true;
    });

    if (anyCheckboxChecked) { break; }
  }

  return anyCheckboxChecked;
};

/**
 * Clear all checkboxes
 * @param {Array} allGroups List of all groups
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
AssignmentSingleCheckbox.prototype.assignmentClearCheckbox = function(allGroups, editMultiple) {
  editMultiple.checkboxChecked = false;

  allGroups.forEach(function(courseDefinition) {
    courseDefinition.objectives.forEach(function(objective) {
      objective.checked = true;
    });
  });
};

module.exports = AssignmentSingleCheckbox;
