import {IPromise} from 'angular';
import {APICurriculumObject} from '../../../../common/types/api_types';
import {BehaviourSubject, IBehaviourSubject} from '../../../../shared/services/helpers/behaviourSubject.service';
import {Service} from '../../../../utils/decorators/service.decorator';
import CourseManagementModule from '../../courseManagement.module';

export interface ICurriculumsService {
  curriculums: APICurriculumObject[];
  curriculums$: IBehaviourSubject<APICurriculumObject[]>;
  updateCurriculums(): IPromise<APICurriculumObject[]>;
}

@Service({
  module: CourseManagementModule,
  name: 'CurriculumsService',
})
export class CurriculumsService implements ICurriculumsService {

  public curriculums: APICurriculumObject[];
  public curriculums$: IBehaviourSubject<APICurriculumObject[]>;

  constructor(private FetchData: any, private Path: any) {
    'ngInject';

    this.curriculums = [];
    this.curriculums$ = new BehaviourSubject(this.curriculums);
  }

  public updateCurriculums(): IPromise<APICurriculumObject[]> {
    return this.FetchData.v3.getCourseDefinitions(this.Path.getClassroomId())
      .then((curriculums: APICurriculumObject[]): APICurriculumObject[] => {
        this.curriculums = curriculums;
        this.curriculums$.next(this.curriculums);

        return this.curriculums;
      });
  }
}
