import { StateService } from '@uirouter/core';
import { AssignmentsListComponent } from '../../../../../shared/components/assignmentsList/assignmentsList.component';
import { GroupedVisibilityDialogType } from '../../../../../shared/enums/groupedVisibilityDialogType.enum';
import { IAssessment } from '../../../../../shared/models/assessment.model';
import { IAssignment } from '../../../../../shared/models/assignment.model';
import { IGroupedVisibilitySettings } from '../../../../../shared/models/groupedVisibilitySettings.model';
import { IStudent } from '../../../../../shared/models/student.model';
import { GroupedVisibilityService } from '../../../../../shared/services/groupedVisibility/groupedVisibility.service';
import { GroupedVisibilityDialogService } from '../../../../../shared/services/groupedVisibility/groupedVisibilityDialog.service';
import { IObserver, Observer } from '../../../../../shared/services/helpers/observer.service';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import { Students } from '../../../../../utils/services/students';
import TeacherAssignmentsModule, { ITeacherAssignmentsService } from '../../../teacherAssignments.module';
import { ITeacherAssignment } from '../../models/assignment.model';
import style from './teacherAssignmentsList.scss';
import template from './teacherAssignmentsList.tpl.html';

@Component({
  module: TeacherAssignmentsModule,
  selector: 'teacher-assignments-list',
  template: template,
  style: style,
})
export class TeacherAssignmentsListComponent extends AssignmentsListComponent {
  @Input({ string: true })
  public label: string;
  @Input()
  public assignments: IAssignment[];

  constructor(private $state: StateService, private Students: Students, private GroupedVisibilityService: GroupedVisibilityService,
              private GroupedVisibilityDialogService: GroupedVisibilityDialogService, private TeacherAssignmentsService: ITeacherAssignmentsService,
              private NavigationHistory: any) {
    'ngInject';

    super();
  }

  public $onInit(): void {
    const observer: IObserver<number[]> = new Observer<number[]>(this.onNext.bind(this));

    this.$$unsubscribes.push(this.TeacherAssignmentsService.openedAssignments$.subscribe(observer));
  }

  public toggleOpen(assignment: ITeacherAssignment): void {
    this.TeacherAssignmentsService.toggleAssignment(assignment);
  }

  public goToAssessAssignment(assignment: IAssignment): void {
    this.$state.go(
      'teacher_assess_assignment',
      {
        assignment,
        isModal: true,
      },
    ).then((): void => {
      //TODO: remove after refactor teacher_assess_assignment view
      this.NavigationHistory.addToHistory(
        'teacher_assess_assignment',
        {
          assignment,
          isModal: true,
        });
    });
  }

  public goToChangeVisibility(assignment: IAssignment): void {
    this.TeacherAssignmentsService.changeVisibility(assignment);
  }

  public goToEditAssignmentCriteria(assignment: IAssignment): void {
    this.$state.go(
      'teacher_set_assignment_objectives',
      {
        assignment,
        isModal: true,
      },
    ).then((): void => {
      //TODO: remove after refactor teacher_assess_assignment view
      this.NavigationHistory.addToHistory(
        'teacher_set_assignment_objectives',
        {
          assignment,
          isModal: true,
        });
    });
  }

  private onNext(): void {
    this.assignments = this.TeacherAssignmentsService.updateAssignmentsOpenState(this.assignments);
  }
}
