import {Inject, Injectable} from '@angular/core';
import {Store, StoreConfig} from '@datorama/akita';
import {User} from './user.model';
import {LOCATION_DETECTION, LocationDetection} from '../../models/location-detection.model';

export interface UserState extends User {
}

export function createInitialState(is_teacher: boolean): UserState {
  return {
    email: '',
    first_name: '',
    first_time_setup: true,
    gdpr_check: false,
    google_id: '',
    id: 0,
    is_teacher,
    last_name: '',
    photo_url: '',
  };
}

@Injectable({providedIn: 'root'})
@StoreConfig({name: 'user'})
export class UserStore extends Store<UserState> {
  constructor(@Inject(LOCATION_DETECTION) protected locationDetection: LocationDetection) {
    super(createInitialState(locationDetection.is_teacher));
  }
}
