import React, {useEffect, useState} from "react";
import {colors} from "../theme";
import VisibilityIcon from "./visabilityIcon";

export const PUBLISH_STATES = {
  dontChange: 'DONT_CHANGE',
  notPublished: 'NOT_PUBLISHED',
  published: 'PUBLISHED',
};

const getInitialValue = (value, multiEdit) => {
  if (typeof value === 'string') {
    return value;
  }
  const init = value ? PUBLISH_STATES.published : PUBLISH_STATES.notPublished;
  if (multiEdit) {
    return PUBLISH_STATES.dontChange;
  }
  return init;
};

const PublishIcon = (props) => {
  const {published: initialValue, onClick, style, iconStyle, multiEdit = false, size = null} = props;
  const [publishedState, setPublishedState] = useState();

  useEffect(() => {
    setPublishedState(getInitialValue(initialValue, multiEdit));
  }, [initialValue]);

  useEffect(() => {
    setPublishedState(getInitialValue(initialValue, multiEdit));
  }, [multiEdit]);

  const publish = () => {
    let newState;
    switch (publishedState) {
      case PUBLISH_STATES.notPublished:
        newState = PUBLISH_STATES.published;
        break;
      case PUBLISH_STATES.published:
        newState = multiEdit ? PUBLISH_STATES.dontChange : PUBLISH_STATES.notPublished;
        break;
      case PUBLISH_STATES.dontChange:
        newState = PUBLISH_STATES.notPublished;
        break;
    }
    setPublishedState(newState);
    if (multiEdit) {
      onClick(newState);
    } else {
      onClick(newState === PUBLISH_STATES.published);
    }
  };

  return (
    <div onClick={publish} style={{cursor: "pointer", zIndex: 100, marginLeft: '10px', ...style}}>
      <VisibilityIcon
        visible={publishedState}
        size={size}
        style={{
          color: publishedState === PUBLISH_STATES.published ? colors.green : colors.darkGrey,
          margin: "4px 0px 0px 0px",
          ...iconStyle,
        }}
      />
    </div>
  );
};

export default PublishIcon;
