import { Breadcrumb } from '../../../../../shared/models/breadcrumb.model.ts';
import style from './studentCoursesDetailsSection.scss';
import { getTranslation } from '../../../../../../../../react_src/i18n';

export class StudentCoursesDetailsSectionComponent {
  static bindings = {
    studentCoursesDefinitions: '<',
    extend: '<',
    label: '@?',
  };

  static controller = StudentCoursesDetailsSectionComponent;

  static require = { studentCoursesDetails: '^^' };

  static templateUrl =
    'components/course-progress/teacher-course-progress/studentCoursesDetails/studentCoursesDetailsSection/studentCoursesDetailsSection.html';

  static style = style;

  constructor($scope, $rootScope, $timeout, $stateParams, BreadcrumbsService, PromiseQueue, UserInfo) {
    'ngInject';

    this.$scope = $scope;
    this.$rootScope = $rootScope;
    this.$timeout = $timeout;
    this.$stateParams = $stateParams;
    this.BreadcrumbsService = BreadcrumbsService;
    this.UserInfo = UserInfo;
    this.studentCoursesDefinitions = [];
    this.isEmpty = true;
    this.extend = false;
    this.queue = PromiseQueue.getInstance();
  }

  $onInit() {
    this.studentCoursesDefinitions = angular
      .copy(this.studentCoursesDefinitions)
      .map((coursesDefinitions) => {
        coursesDefinitions.open = true;

        return coursesDefinitions;
      });

    this.isEmpty = this.studentCoursesDefinitions && !!this.studentCoursesDefinitions.length;
  }

  onUpdateCourseDefinition(courseDefinition) {
    this.queue.push(this.updateCourseDefinition(courseDefinition));
  }

  updateCourseDefinition(courseDefinition) {
    return this.studentCoursesDetails.StudentCoursesDetails.update(
      this.studentCoursesDetails.student,
      courseDefinition,
    ).then((newCourseDefinition) => {
      this.studentCoursesDefinitions = this.studentCoursesDefinitions.map(
        (item) => {
          if (item.id === newCourseDefinition.id) {
            // Dont overwrite feedback and private_note, it messes with the ui
            const {
              feedback, private_note, result, ...rest
            } = newCourseDefinition;
            return { ...item, ...rest };
          }
          return item;
        },
      );
    });
  }

  goToDetails(courseDefinition) {
    if (this.isAvailable(courseDefinition)) {
      const breadcrumbLabel = this.UserInfo.isTeacher()
        ? this.studentCoursesDetails.student.student_name : getTranslation('breadcrumbFirstItemLabel');

      this.studentCoursesDetails.StudentCoursesDetails.goToCourseDetails({
        courseDefinition,
        course: courseDefinition,
        student: this.studentCoursesDetails.student,
        breadcrumbs: [new Breadcrumb(breadcrumbLabel, this.BreadcrumbsService.createGoToAction())],
      });
    }
  }

  isAvailable(courseDefinition) {
    return !this.UserInfo.isTeacher()
      || courseDefinition.teacher_in_class
      || courseDefinition.shared_with_teacher
      || courseDefinition.is_mentor;
  }

  expandAll() {
    this.changeOpenStateForAll(true);
  }

  collapseAll() {
    this.changeOpenStateForAll(false);
  }

  changeOpenStateForAll(state) {
    this.$timeout(() => {
      this.studentCoursesDefinitions = this.studentCoursesDefinitions.map((courseDefinition) => {
        if (this.isAvailable(courseDefinition)) {
          courseDefinition.isOpen = state;
        }

        return courseDefinition;
      });
    }, 0);
  }
}
