/**
 * @module BackendInfo/Service
 * @description
 *   Handle Backend Info.
 */

export function BackendInfo($mdDialog, $cookies, UserInfo, FetchData) {
  'ngInject';

  return {
    modalExist: null,

    show() {
      const lang = chrome.i18n.getUILanguage();

      FetchData.getBackendInfo(lang).then((res) => {
        const modal = res;
        const cookieName = `USER${UserInfo.userId}TIMESTAMP${modal.timestamp}`;

        if (typeof $cookies.get(cookieName) !== 'undefined' || !modal.is_visible || this.modalExist) {
          return;
        }

        $mdDialog.show({
          parent: angular.element(document.querySelectorAll('[md-component-id="studybee"]')),
          templateUrl: 'utils/views/backend_info.html',
          disableParentScroll: false,
          controller() {
            const bi = this;

            bi.content = modal.message;

            bi.confirm = function () {
              const currentDate = new Date();
              const nextDate = new Date();
              nextDate.setYear(currentDate.getFullYear() + 1);

              $cookies.put(cookieName, 'hide', {
                path: '/',
                expires: nextDate,
              });

              $mdDialog.hide();
            };
          },
          controllerAs: 'bi',
          bindToController: true,
        });

        this.modalExist = true;
      });
    },

    hide() {
      if (!this.modalExist) {
        return;
      }

      $mdDialog.hide();
      this.modalExist = false;
    },
  };
}
