import React, {
  useContext, useEffect, useMemo, useReducer, useRef,
} from 'react';
import debounce from 'lodash.debounce';
import Card from './card';
import FeedbackInput from './feedbackInput';
import { getGradeSystemValues, getLastModified, GlobalContext } from '../utils';
import { getTranslation } from '../i18n';
import AssessmentSlider from './assessmentSlider';
import { fonts } from '../theme';
import {PUBLISH_STATES} from "./publishIcon";

const resultText = getTranslation('resultsInput');
const overallFeedbackText = getTranslation('overallFeedback');
const overallPrivateNoteText = getTranslation('overallPrivateNoteInput');
const teacherSelfAssessmentText = getTranslation('teacherSelfAssessmentLabel');
const studentSelfAssessmentLabel = getTranslation('studentSelfAssessmentLabel');

const AssignmentAssessment = (props) => {
  const {
    onSave,
    assignment,
    assessment,
    multiEdit = false,
    filter = [
      'result',
      'overallFeedback',
      'overallNote',
      'overallGrade',
      'selfAssessment',
    ],
    style,
  } = props;
  const context = useContext(GlobalContext);
  const { Api, isStudent } = context;
  const ref = useRef({ assessment });
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  useEffect(() => {
    if (multiEdit) {
      ref.current.assessment = {
        ...ref.current.assessment,
        feedback: null,
        result: null,
        result_published: PUBLISH_STATES.dontChange,
        grade_id: null,
        grade_published: PUBLISH_STATES.dontChange,
        feedback_published: PUBLISH_STATES.dontChange,
        private_note: null,
        status_new: 'NOT_ASSESSED',
      };
      forceUpdate();
    } else {
      ref.current.assessment = assessment;
      forceUpdate();
    }
  }, [multiEdit]);

  useEffect(() => {
    ref.current.assessment = assessment;
    forceUpdate();
  }, [assessment]);

  const debouncedUpdateAssignmentAssessment = useMemo(() => debounce(async (id, data, multiEdit, onSave, Api) => {
    if (multiEdit) {
      if (onSave) {
        const {
          // eslint-disable-next-line camelcase
          feedback, result, private_note, grade_id, feedback_published, result_published, grade_published,
        } = data[0];
        onSave({
          feedback, result, private_note, grade_id, feedback_published, result_published, grade_published,
        });
      }
      return;
    }
    if (onSave) {
      onSave(data);
    }
    await Api.updateAssignmentAssessment(data);
  }, 1000),[assessment?.id]);

  const saveGrade = async (grade) => {
    const { assessment: ass } = ref.current;
    ass.grade_id = grade?.id;
    ass.grade_name = grade?.name;
    ass.grade_color = grade?.color;
    ass.grade_last_edit = Date.now();
    ref.current.assessment = ass;
    await debouncedUpdateAssignmentAssessment(assignment.id, [ass],multiEdit, onSave, Api);
  };

  const publishGrade = async (publish) => {
    const { assessment: ass } = ref.current;
    ass.grade_published = publish;
    ref.current.assessment = ass;
    await debouncedUpdateAssignmentAssessment(assignment.id, [ass],multiEdit, onSave, Api);
  };

  const changeAssignmentText = async (text, name) => {
    const { assessment: ass } = ref.current;
    switch (name) {
      case 'result':
        ass.result = text;
        ass.result_last_edit = Date.now();
        break;
      case 'feedback_to_student_on_overall_assignment':
        ass.feedback = text;
        ass.feedback_last_edit = Date.now();
        break;
      case 'overall_private_note':
        ass.private_note = text;
        ass.private_note_last_edit = Date.now();
        break;
      case 'overall_self_assessment':
        ass.self_assessment = text;
        ass.self_assessment_last_edit = Date.now();
        break;
      default:
        break;
    }
    ref.current.assessment = ass;
    await debouncedUpdateAssignmentAssessment(assignment.id, [ass],multiEdit, onSave, Api);
  };

  const publishAssignmentValue = async (name, publish) => {
    const { assessment: ass } = ref.current;
    switch (name) {
      case 'result':
        ass.result_published = publish;
        break;
    case 'feedback_to_student_on_overall_assignment':
        ass.feedback_published = publish;
        break;
      default:
    }

    ref.current.assessment = ass;
    await debouncedUpdateAssignmentAssessment(assignment.id, [ass],multiEdit, onSave, Api);
  };
  const showResult = filter.find((f) => f === 'result');
  const showFeedback = filter.find((f) => f === 'overallFeedback');
  const showOverallNote = filter.find((f) => f === 'overallNote');
  const showGrade = filter.find((f) => f === 'overallGrade');
  const showSelfAssessment = filter.find((f) => f === 'selfAssessment');
  if (!showFeedback && !showOverallNote && !showResult) {
    return null;
  }
  const { gradeSystem } = assignment;
  const gradeSystemValues = getGradeSystemValues(gradeSystem);

  let defaultIndex = gradeSystemValues?.findIndex(
    (v) => v.id === ref.current.assessment?.grade_id,
  );
  defaultIndex = defaultIndex < 0 ? 0 : defaultIndex;

  return (
    <>
      <Card
        style={{
          margin: '10px 0 10px 0',
          paddingTop: '10px',
          paddingBottom: '20px',
          ...style,
        }}
      >
        {showFeedback && (
          <FeedbackInput
            id={'feedback_to_student_on_overall_assignment'}
            text={ref.current.assessment?.feedback}
            placeholder={overallFeedbackText}
            onTextChange={changeAssignmentText}
            published={ref.current.assessment?.feedback_published}
            helperText={getLastModified(ref.current.assessment?.feedback_last_edit) }
            onPublish={publishAssignmentValue}
            readonly={isStudent}
            hideEye={isStudent}
            warningText={multiEdit?getTranslation('warning_overwrite_text'):''}
            multiEdit={multiEdit}
          />
        )}
        {showSelfAssessment && !multiEdit && (
          <FeedbackInput
            id={'overall_self_assessment'}
            text={ref.current.assessment?.self_assessment}
            placeholder={isStudent ? studentSelfAssessmentLabel : teacherSelfAssessmentText}
            helperText={getLastModified(ref.current.assessment?.self_assessment_last_edit)}
            hideEye
            readonly={!isStudent}
            onTextChange={changeAssignmentText}
          />
        )}
        {showResult && (
          <FeedbackInput
            id={'result'}
            placeholder={resultText}
            onTextChange={changeAssignmentText}
            text={ref.current.assessment?.result}
            published={ref.current.assessment?.result_published}
            onPublish={publishAssignmentValue}
            helperText={getLastModified(ref.current.assessment?.result_last_edit) }
            readonly={isStudent}
            hideEye={isStudent}
            multiEdit={multiEdit}
            warningText={multiEdit?getTranslation('warning_overwrite_text'):''}
          />
        )}
        {showGrade && gradeSystemValues.length > 1 && (
          <>
            <span
              style={{
                ...fonts.SMALL,
                marginBottom: '10px',
                marginTop: '10px',
              }}
            >
              {getTranslation('overallAssessment')}
            </span>
            <AssessmentSlider
              id={ref.current.assessment.id}
              published={ref.current.assessment?.grade_published}
              helpText={getLastModified(ref.current.assessment?.grade_last_edit)}
              values={gradeSystemValues}
              defaultIndex={defaultIndex}
              onChangeCommit={(index) => {
                saveGrade(
                  gradeSystem.grades.find(
                    (g) => g.id === gradeSystemValues[index].id,
                  ),
                );
              }}
              onPublish={(value) => publishGrade(value)}
              showPublish={!isStudent}
              disabled={isStudent}
              multiEdit={multiEdit}
            />
          </>
        )}
        {(showOverallNote && !isStudent) && (
          <FeedbackInput
            id={'overall_private_note'}
            text={ref.current.assessment?.private_note}
            placeholder={overallPrivateNoteText}
            helperText={ getLastModified(ref.current.assessment?.private_note_last_edit) }
            hideEye
            onTextChange={changeAssignmentText}
            readonly={isStudent}
            warningText={multiEdit?getTranslation('warning_overwrite_text'):''}
          />
        )}
      </Card>
    </>
  );
};
export default AssignmentAssessment;
