import React, { useContext, useEffect, useState } from 'react';
import { Close } from '@material-ui/icons';
import { CircularProgress } from '@material-ui/core';
import { GlobalContext } from '../utils';
import { colors } from '../theme';
import AssessmentSlider from './assessmentSlider';
import fonts from '../theme/fonts';
import {getTranslation} from '../i18n';


const fields = `
  name
    courses {
      id
      name
      gradeSystem{
        id
        is_main
        is_final
        grades{
          id
          name
          label
          color
        }
      }
      objectives {
        id
        name
        knowledgeRequirements {
          grade_id
          description
        }
      }
    }
`;
const ObjectiveDescription = (props) => {
  const { objectiveId, defaultIndex = 0 } = props;
  const context = useContext(GlobalContext);

  const { Api, showObjectiveDetails } = context;
  const [currentGrade, setCurrentGrade] = useState(defaultIndex);
  const [objective, setObjective] = useState();

  const fetchObjective = async () => {
    const response = await Api.getClassroom(fields);
    const classroom = response?.data?.classroom;
    const objective = classroom?.courses.map(c => {
      return c.objectives.map(o => ({...o, courseId: c.id}));
    }).flat().find(o => o.id === objectiveId);
    objective.gradeSystem = classroom.courses.find(c => c.id === objective.courseId)?.gradeSystem.find(g => g.is_main);
    setObjective(objective);
  };

  useEffect(() => {
    fetchObjective();
  }, [objectiveId]);

  if (!objective || objective.length === 0) {
    return (
      <div style={{display:'flex', alignItems:'center', justifyContent:'center', height:'290px', border:'1px solid'}}>
      <CircularProgress />
    </div>);
  }

  const values = objective?.gradeSystem?.grades?.map((g, i) => ({
    id: g.id,
    label: g.label,
    color: g.color,
    value: i,
    name: g.name,
  }));

  return (
    <div
      style={{
        width: '320px',
        backgroundColor: colors.grey,
        borderRadius: '10px',
        padding: '40px 20px 20px 20px',
        borderTop: `1px solid ${colors.borderColorDark}`,
        display: 'flex',
        position: 'relative',
      }}
    >
      <div style={{ border: '0px solid red', width: '100%' }}>
        <span style={{
          ...fonts.LARGE,
          whiteSpace: 'nowrap',
          width: '80%',
          position: 'absolute',
          top: '16px',
          left: '20px',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
        }}>{getTranslation('assessmentCriteria')} {objective.name}</span>
        <AssessmentSlider
          defaultIndex={defaultIndex}
          showPublish={false}
          values={values}
          onChange={(c) => {
            setCurrentGrade(values[c].id);
          }}
        />
        <div
          style={{ marginTop: '20px', height: '185px', overflow: 'scroll' }}
          dangerouslySetInnerHTML={{
            __html: objective?.knowledgeRequirements?.find(k => k.grade_id === currentGrade)?.description,
          }}
        />
      </div>
      <Close
        onClick={() => showObjectiveDetails(false)}
        style={{
          position: 'absolute',
          right: '10px',
          top: '10px',
        }}
      />
    </div>
  );
};

export default ObjectiveDescription;
