import React, {memo} from "react";
import {colors, fonts} from "../../theme";

import happyImage from '../../images/png/happy-face.png';
import mediumImage from '../../images/png/medium-face.png';
import sadImage from '../../images/png/sad-face.png';
import extraImage from '../../images/png/extra-help.png';
import {getTranslation} from "../../i18n";
import VisibilityIcon from '../../components/visabilityIcon';
import {PUBLISH_STATES} from "../../components/publishIcon";
import Tooltip from "../../components/tooltip";
import Icon from "../../components/icon";

const getStatusIcon = (status, selected) => {
  const bgStyle = {
    width: "16px",
    height: "16px",
    borderRadius: "20px",
    backgroundColor: colors.ewsGreen,
    margin: "0px 4px 0px 4px",
  };
  if (!status) {
    return (
      <div
        style={{
          width: "14x",
          height: "14px",
          border: `1px solid ${selected ? colors.borderColorDark : colors.borderColor}`,
          borderRadius: "20px",
          maxWidth: "14px",
          minWidth: "14px",
          margin: "0px 4px 18px 4px",
        }}
      />
    );
  }
  switch (status) {
    case "10":
      return (
        <div style={bgStyle}>
          <Icon file={happyImage} style={{width: '16px'}}/>
        </div>
      );
    case "20":
      return (
        <div style={{...bgStyle, backgroundColor: colors.primary}}>
          <Icon file={mediumImage} style={{width: '16px'}}/>
        </div>
      );
    case "30":
      return (
        <div style={{...bgStyle, backgroundColor: colors.red}}>
          <Icon file={sadImage} style={{width: '16px'}}/>
        </div>
      );

    default:
      return (
        <div
          style={{
            width: "14x",
            height: "14px",
            border: `1px solid ${colors.borderColor}`,
            borderRadius: "20px",
            maxWidth: "14px",
            minWidth: "14px",
            margin: "0px 4px 18px 4px",
          }}
        />
      );
  }
};

const tt1 = getTranslation("currentWarningLevel");
const tt2 = getTranslation("extraAdjustments");

const EwsItem = (props) => {
  const {ews, selected} = props;

  return (
    <div
      style={{
        display: "flex",
        minWidth: "60px",
        alignItems: "center",
        justifyContent: "space-around",
      }}
    >
      <Tooltip title={tt1}>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            marginBottom: '-6px',
          }}
        >
          {getStatusIcon(ews?.status?.status, selected)}
          {ews?.status?.status && (
            <VisibilityIcon
              visible={ews?.status?.published ? PUBLISH_STATES.published : PUBLISH_STATES.notPublished}
              style={{
                color: ews?.status?.published ? colors.green : colors.darkGrey
              }}/>
          )}
        </div>
      </Tooltip>
      <Tooltip title={tt2}>
        <div style={{position: "relative", marginBottom: '8px'}}>
          <Icon file={extraImage} style={{width: '16px'}}/>
          <div
            style={{
              position: "absolute",
              bottom: 2,
              right: -2,
              backgroundColor: colors.black,
              width: "10px",
              height: "10px",
              borderRadius: "30px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <span
              style={{
                ...fonts.TINY,
                color: colors.white,
                marginTop: "1px",
                fontSize: "9px",
              }}
            >
              {ews?.extraAdjustments?.length}
            </span>
          </div>
        </div>
      </Tooltip>
    </div>
  );
};
export default memo(EwsItem);
