import angular from 'angular';
import { IModule, IProviderOptions } from 'angular';

export const Provider: (options: IProviderOptions) => any = (options: IProviderOptions): <T extends { new(...args: any[]): { $get: any } }>(constructor: T) => any => {

  const getOrCreateServiceModule: () => IModule = (): IModule => {
    const moduleName: string = 'app';
    let module: IModule;

    if (options.module) {
      try {
        module = angular.module(`${options.module}`);
      } catch (err) {
        module = null;
      }
    }

    if (!module) {
      try {
        module = angular.module(`${moduleName}`);
      } catch (err) {
        console.error(`Error on register service ${options.name} in module ${moduleName}. Module ${moduleName} is not exists.`, err);
        module = null;
      }
    }

    return module;
  };

  return <T extends { new(...args: any[]): { $get: any } }>(constructor: T): T => {
    const module: IModule = getOrCreateServiceModule();

    if (module) {
      module.provider(options.name, new constructor());
    }

    return constructor;
  };
};

