import React, {
  memo, useCallback,
  useContext, useEffect, useMemo, useReducer, useRef, useState,
} from 'react';
import Promise from 'bluebird';
import {Checkbox, CircularProgress, withStyles} from '@material-ui/core';
import debounce from 'lodash.debounce';
import {colors, fonts} from '../../theme';
import {useWindowDimensions} from '../../hooks';
import {filterAssignmentsOnCourseId, getPublishedStatus, GlobalContext} from '../../utils';
import EwsItem from './ewsItem';
import CourseOverviewComponent from './courseOverviewComponent';
import CourseObjectiveAssessmentsComponent from './CourseObjectiveAssessmentsComponent';
import GridOverviewHeader from './gridOverviewHeader';
import StudentCourseOverview from '../studentCourseOverview';
import AssignmentTable from './assignmentTable';
import {getTranslation} from '../../i18n';
import SortStudentComponent from '../../components/sortStudentComponent';
import CheckboxWithText from '../../components/CheckboxWithText';

const fields = `
  name
  assignments{
    title
    id
    is_topic
    connected
    state
    use_submitted_status
    courseDefinitions {
      id
    }
    assessments {
      first_name
      last_name
      student_id
      id
      feedback
      feedback_published
      private_note
      grade_published
      result
      result_published
      status
      grade_id
      grade_name
      grade_color
      grade_published
      grade_last_edit
      feedback_last_edit
      grade_last_edit
      private_note_last_edit
      status_new
      submitted_status
    }
    assignments {
      id
      title
      connected
      state
      use_submitted_status
      courseDefinitions {
        id
      }
      assessments{
        first_name
        last_name
        student_id
        id
        feedback
        feedback_published
        private_note
        grade_published
        result
        result_published
        status
        grade_id
        grade_name
        grade_color
        grade_published
        grade_last_edit
        feedback_last_edit
        grade_last_edit
        private_note_last_edit
        status_new
        submitted_status
      }
    }
  }
  courses{
    id
    gradeSystem{
      is_main
      is_final
      grades{
        id
        label
        color
      }
    }
  }
  students {
    id
    first_name
    last_name
    EWS {
    courseId
      status {
        status
        published
      }
      extraAdjustments {
        name
      }
    }
    courseAssessments {
      id
      courseId
      gradeName
      gradeColor
      grade_id
      grade_published
      target_grade_id
      target_grade_published
      feedback
      feedback_published
      private_note
      gradeSystem {
        is_main
        is_final
        grades {
          id
          label
          color
        }
      }
      courseObjectiveAssessments {
        id
        name
        objective_id
        feedback
        feedback_published
        private_note
        grade_id
        grade_published
      }
    }
  }
`;

const styles = {
  thText: {
    ...fonts.MEDIUM,
    fontWeight: '400',
    padding: '0px 8px 4px 8px',
    whiteSpace: 'nowrap',
    textAlign: 'center',
    height: '26px',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  tdText: {
    ...fonts.MEDIUM,
    display: 'inline-block',
    margin: '0px 16px 0px 16px',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    width: '150px',
    border: '0px solid',
    textAlign: 'start',
  },
  table: {
    boxShadow: '0px 5px 10px rgba(0,0,0,0.1)',
    margin: '50px 0px 16px 16px',
    backgroundColor: colors.grey,
    height: '0%',
  },
  td: {},
  rightPanel: {
    backgroundColor: colors.white,
    position: 'fixed',
    width: '330px',
    right: 0,
    top: 0,
    overflow: 'scroll',
    padding: '0px 30px 0px 0px',
    marginTop: '80px',
  },
};

const GreenCheckbox = withStyles({
  root: {
    color: colors.borderColorDark,
    paddingLeft: 0,
    marginLeft: '6px',
    '&$checked': {
      color: colors.green,
    },
    '& .MuiSvgIcon-root': {
      fontSize: '1.5rem',
    },
  },

  checked: {},
})(Checkbox);

const studentCourseOverviewStyle = {width: '315px'};

const GridOverview = (props) => {
  const {course, student: initialStudent, onClose} = props;
  const context = useContext(GlobalContext);
  const {Api, networkTraffic, sortingMethod} = context;
  const [classroom, setClassroom] = useState();
  const [student, setStudent] = useState(initialStudent);
  const [highlight, setHighlight] = useState();
  const dim = useWindowDimensions();
  const [saving, setSaving] = useState(false);
  const [multiEdit, setMultiEdit] = useState(false);
  const [selectedStudents, setSelectedStudents] = useState(false);
  const selectedStudentsBeforeSelectAll = useRef();
  const students = useRef([]);
  const [sortedStudents, setSortedStudents] = useState([]);
  const [extraAdjustmentData, setExtraAdjustmentData] = useState([]);
  const [sortedAssignments, setSortedAssignments] = useState([]);
  const [courseEwsMap, setCourseEwsMap] = useState({});
  const [courseAssessmentsMap, setCourseAssessmentsMap] = useState({});
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);
  const [renderAssignments, setRenderAssignments] = useState(false);
  const [loading, setLoading] = useState(true);


  const setStudentCB = useMemo(() => debounce((id) => {
    setStudent(id);
  }, 500, {trailing: false, leading: true}), [course.id]);

  const onLoading = useCallback((loading) => {
    setLoading(loading)
  }, [course.id]);

  const loadClassroom = debounce(async () => {
      const response = await Api.getClassroom(fields);
      const {classroom} = response?.data;
      classroom.assignments = filterAssignmentsOnCourseId(
        classroom?.assignments,
        course.id,
      );
      setClassroom(classroom);
      // Delay rendering of the tables 1s because this view is heavy to render
      setTimeout(() => setRenderAssignments(true), 1000);
    },
    1000);

  const multiSave = useCallback(async (changes) => {
    if (changes.ews?.ewsStatus) {
      const {ewsStatus} = changes.ews;
      await Promise.map(selectedStudents, async (id) => {
        const student = classroom?.students?.find((s) => s.id === id);
        const ews = student?.EWS.find((e) => e.courseId === course.id);
        const saveObj = {
          status: ewsStatus?.status,
          student_id: id,
          course_id: course.id,
          published: getPublishedStatus(ewsStatus.published, ews?.status?.published || false),
          modified: Date.now(),
        };
        await Api.updateStudentEWSStatus(saveObj);
      });
    }
    if (changes?.ews?.extraAdjustments) {
      const {extraAdjustments} = changes.ews;
      const {currentAdjustments, courseId} = extraAdjustments;
      await Promise.map(selectedStudents, async (id) => {
        const student = students.current.find((s) => s.id === id);
        const extra = student.EWS.find((e) => e.courseId === course.id)
          .extraAdjustments;
        const data = currentAdjustments.filter((c) => {
          if (c.status === 'partial') {
            if (!extra.find((e) => e.extra_adjustment_id === c.id)) {
              return false;
            }
          }
          return true;
        });
        await Api.updateStudentExtraAdjustments2(
          data.map((c) => c.id),
          courseId,
          id,
        );
      });
    }
    if (changes?.courseAssessment) {
      const saveObjs = selectedStudents.map((studentId) => {
        const {courseAssessment} = changes;
        const assessment = classroom?.students?.find((s) => s.id === studentId)
          ?.courseAssessments?.find((a) => a.courseId === course.id);
        return {
          ...assessment,
          feedback: courseAssessment?.feedback || assessment.feedback,
          feedback_published: getPublishedStatus(courseAssessment?.feedback_published, assessment.feedback_published),
          grade_id: courseAssessment?.grade_id || assessment.grade_id,
          grade_published: getPublishedStatus(courseAssessment?.grade_published, assessment.grade_published),
          target_grade_id: courseAssessment?.target_grade_id || assessment.target_grade_id,
          target_grade_published: getPublishedStatus(courseAssessment?.target_grade_published, assessment.target_grade_published),
          private_note: courseAssessment?.private_note || assessment.private_note,
        };
      });
      await Api.updateCourseAssessments(saveObjs);
    }
    if (changes?.objectiveAssessments) {
      const saveObjs = selectedStudents
        .map((studentId) => Object.keys(changes.objectiveAssessments).map((objectiveId) => {
          const change = changes.objectiveAssessments[objectiveId];
          const assessment = classroom?.students?.find((s) => s.id === studentId)?.courseAssessments?.find((a) => a.courseId === course.id);
          const currentObjectiveAssessment = assessment.courseObjectiveAssessments.find((co) => co.objective_id === parseInt(objectiveId, 10));
          return {
            ...currentObjectiveAssessment,
            ...change,
            grade_published: getPublishedStatus(change?.grade_published, currentObjectiveAssessment?.grade_published),
            feedback_published: getPublishedStatus(change?.feedback_published, currentObjectiveAssessment?.feedback_published),
            id: currentObjectiveAssessment.id,
          };
        }))
        .flat();
      Api.updateCourseObjectiveAssessment(saveObjs);
    }

    setMultiEdit(false);
  }, [course.id, selectedStudents]);

  // A little bit hacky, reload classroom if something has been saved
  useEffect(() => {
    if (networkTraffic === 'POST' || networkTraffic === 'PUT') {
      setSaving(true);
    } else if (saving) {
      loadClassroom();
      setSaving(false);
    }
  }, [networkTraffic]);

  useEffect(() => {
    loadClassroom();
  }, []);

  useEffect(() => {
    if (selectedStudents.length === 0) {
      setMultiEdit(false);
    }
  }, [selectedStudents]);

  useEffect(() => {
    if (!multiEdit) {
      setExtraAdjustmentData([]);
    }
  }, [multiEdit]);

  useEffect(() => {
    setSortedStudents(classroom?.students
      ?.sort((a, b) => {
        if (sortingMethod === 'firstName') {
          return a.first_name.localeCompare(b.first_name);
        }
        return a.last_name.localeCompare(b.last_name);
      }));
    setSortedAssignments(classroom?.assignments
      .filter(a => a.connected)
      .sort((a, b) => a.title.localeCompare(b.title)));
    forceUpdate();
  }, [classroom?.students, sortingMethod]);

  useEffect(() => {
    const coursesEws = classroom?.students?.map(s => ({
      studentId: s.id,
      EWS: s.EWS.find(e => e.courseId === course.id)
    }));
    const courseEwsMap = coursesEws?.reduce((acc, ce) => {
      acc[ce.studentId] = ce.EWS;
      return acc;
    }, {});
    setCourseEwsMap(courseEwsMap);

    const courseAssessments = classroom?.students?.map(s => ({
      studentId: s.id,
      assessments: s?.courseAssessments.find(ss => ss.courseId === course.id)
    }));
    const courseAssessmentsMap = courseAssessments?.reduce((acc, ca) => {
      acc[ca.studentId] = ca.assessments;
      return acc;
    }, {});
    setCourseAssessmentsMap(courseAssessmentsMap);
  }, [classroom?.students]);


  const objectiveGradeSystem = classroom?.courses
    .find((c) => c.id === course.id)
    .gradeSystem.find((g) => g.is_main);

  const getStudentExtraAdjustments = async (studentIds) => {
    const response = await Api.getClassroom(`
    name
    students {
      id
      EWS {
        courseId
        extraAdjustments {
          course_definition_id
          extra_adjustment_id
          student_id
        }
      }
    }
    `);
    students.current = response.data.classroom.students;
    calculateExtraAdjustments(studentIds);
  };

  const calculateExtraAdjustments = async (selectedStudentsNow) => {
    const allAdjustments = selectedStudentsNow
      .map((id) => students.current.find((s) => s.id === id)?.EWS.find((e) => e.courseId === course.id)
        .extraAdjustments.map((e) => e.extra_adjustment_id)).flat();
    const uniqueAdjustments = [...new Set(allAdjustments)];
    setExtraAdjustmentData(
      uniqueAdjustments.map((id) => ({
        id,
        status:
          allAdjustments.filter((a) => a === id).length
          === selectedStudentsNow.length
            ? 'check'
            : 'partial',
      })),
    );
  };

  const width = dim.width - 350;

  return (
    <div>
      <div
        style={{
          position: 'absolute',
          top: '0px',
          left: `${-width}px`,
          bottom: 0,
          right: 0,
          backgroundColor: 'black',
        }}
      >
        <GridOverviewHeader
          classroomName={classroom?.name}
          courseName={course.name}
          studentName={`${student.first_name} ${student.last_name}`}
          onCollapse={onClose}
        />
        <div
          style={{
            marginRight: '360px',
            backgroundColor: colors.grey,
            marginTop: '60px',
          }}
        >
          <div
            style={{
              display: 'flex',
              width: '100%',
              overflow: 'scroll',
              height: `${dim.height - 60}px`,
              backgroundColor: colors.grey,
              flexDirection: 'column',
            }}
          >
            <CheckboxWithText
              subText={''}
              id={'multiselect'}
              text={getTranslation('selectMany')}
              onCheck={(c) => {
                setMultiEdit(c);
                if (!c) {
                  setSelectedStudents([]);
                  setExtraAdjustmentData([]);
                } else {
                  setSelectedStudents([student.id]);
                  getStudentExtraAdjustments([student.id]);
                }
              }}
              checked={multiEdit}
              style={{
                marginLeft: '30px',
                marginBottom: '-30px',
              }}
            />
            <div style={{display: 'flex'}}>
              <div>
                <table style={styles.table}>
                  <tr style={{backgroundColor: colors.grey}}>
                    {multiEdit && (
                      <td>
                        <GreenCheckbox
                          onChange={(e) => {
                            if (e.target.checked) {
                              selectedStudentsBeforeSelectAll.current = selectedStudents;
                              const allStudents = classroom?.students.map(
                                (s) => s.id,
                              );
                              setSelectedStudents(allStudents);
                              calculateExtraAdjustments(allStudents);
                            } else {
                              setSelectedStudents(
                                selectedStudentsBeforeSelectAll.current,
                              );
                            }
                          }}
                        />
                      </td>
                    )}
                    <td style={styles.thText}>
                      <div style={{display: 'flex', alignItems: 'center'}}>
                        <SortStudentComponent style={{marginRight: '6px'}}/>
                        {getTranslation('student')}
                      </div>
                    </td>
                    <td style={styles.thText}>{getTranslation('EWS')}</td>
                    <td style={styles.thText}>
                      {getTranslation('overallResultHeaderTeacher')}
                    </td>
                    <td style={styles.thText}>
                      {getTranslation('objectivesText')}
                    </td>
                  </tr>
                  {sortedStudents?.map((s, i) => {
                    const ews = courseEwsMap[s.id];
                    const courseAssessment = courseAssessmentsMap[s.id];
                    let bgColor = s.id === highlight ? colors.grey : colors.white;
                    bgColor = s.id === student.id ? colors.borderColor : bgColor;
                    let selected = false;
                    if (multiEdit) {
                      selected = selectedStudents?.findIndex((id) => id === s.id) >= 0;
                    }
                    if (selected) {
                      bgColor = colors.borderColor;
                    } else if (!selected && multiEdit) {
                      bgColor = colors.white;
                    }

                    return (
                      <tr
                        onClick={() => {
                          if (!multiEdit) {
                            setStudentCB(s)
                          } else {
                            let selectedStudentsNow;
                            if (
                              selectedStudents.findIndex((id) => id === s.id)
                              >= 0
                            ) {
                              selectedStudentsNow = selectedStudents.filter(
                                (id) => id !== s.id,
                              );
                              setSelectedStudents(selectedStudentsNow);
                            } else {
                              selectedStudentsNow = [...selectedStudents, s.id];
                              setSelectedStudents(selectedStudentsNow);
                            }
                            calculateExtraAdjustments(selectedStudentsNow);
                          }
                        }}
                        onMouseEnter={() => setHighlight(s.id)}
                        onMouseLeave={() => setHighlight(null)}
                        key={s.id}
                        style={{
                          cursor: 'pointer',
                          height: '52px',
                          backgroundColor: bgColor,
                        }}
                      >
                        {multiEdit && (
                          <td>
                            <GreenCheckbox
                              inputProps={{'aria-label': 'controlled'}}
                              checked={selected || false}
                              onChange={(e) => {
                                if (e.target.checked) {
                                  setSelectedStudents([
                                    ...selectedStudents,
                                    s.id,
                                  ]);
                                } else {
                                  setSelectedStudents(
                                    selectedStudents.filter((id) => id !== s.id),
                                  );
                                }
                              }}
                            />
                          </td>
                        )}
                        <td style={styles.td}>
                          <span style={styles.tdText}>
                            {s.first_name} {s.last_name}
                          </span>
                        </td>
                        <td style={styles.td}>
                          <EwsItem ews={ews} selected={s.id === student.id}/>
                        </td>
                        <td style={styles.td}>
                          <CourseOverviewComponent
                            courseAssessment={courseAssessment}
                          />
                        </td>
                        <td style={styles.td}>
                          <CourseObjectiveAssessmentsComponent
                            gradeSystem={objectiveGradeSystem}
                            objectiveAssessments={courseAssessment?.courseObjectiveAssessments}
                            bgColor={bgColor}
                          />
                        </td>
                      </tr>
                    );
                  })}
                </table>
                <div style={{height: '30px', width: '50px', backgroundColor: 'transparent'}}/>
              </div>
              {!multiEdit && renderAssignments && (
                <div style={{display: 'flex', paddingRight: '16spx'}}>
                  {sortedAssignments?.map((assignment) => (
                    <AssignmentTable
                      key={assignment.id}
                      students={classroom?.students}
                      onStudentSelect={(id) => {
                        const stud = classroom?.students?.find(
                          (s) => s.id === id,
                        );
                        setStudentCB(stud);
                      }}
                      assignment={assignment}
                      highlight={highlight}
                      student={student}
                      setHighlight={setHighlight}
                      styles={styles}
                    />
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>
        <div
          style={{
            ...styles.rightPanel,
            height: `${dim.height - (multiEdit ? 30 : 80)}px`,
            boxShadow: '0px 5px 10px rgba(0,0,0,0.1)',
            marginTop: multiEdit ? '30px' : '80px',
          }}
        >
          <StudentCourseOverview
            style={studentCourseOverviewStyle}
            hideCourseCard
            student={student}
            course={course}
            isGrid
            multiEdit={multiEdit}
            onSave={multiSave}
            extraAdjustmentData={extraAdjustmentData}
            selectedStudents={selectedStudents}
            loading={() => {}}
          />
          {!multiEdit && (
            <div
              style={{
                position: 'fixed',
                top: 60,
                right: 0,
                height: '50px',
                backgroundColor: colors.borderColor,
                width: '330px',
                alignItems: 'center',
                padding: '0px 16px 0px 16px',
                display: 'flex',
              }}
            >
              <span style={{...fonts.LARGE, fontWeight: '800'}}>
                {student.first_name} {student.last_name}
              </span>
            </div>
          )}
        </div>
      </div>
      {loading && false &&
      <div style={{
        display: 'flex',
        position: 'fixed',
        top: 0,
        left: -dim.width,
        right: 0,
        bottom: 0,
        backgroundColor: colors.dimmed,
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1000,
      }}>
        <CircularProgress size={60}/>
      </div>
      }
    </div>
  );
};
export default memo(GridOverview);
