import angular from 'angular';
import { EnvironmentService } from '../../../src/app/core/environment/environment.service';

export function AppRun(
  $rootScope, $transitions, $trace, $timeout, Hint, Mixpanel, StudybeeHandle, Locale, MainMenu,
  FetchData, HostPageNewAssignmentObserver, Helpers, EnvironmentService,
) {
  'ngInject';

  // Run Mixpanel analytics
  chrome.runtime.sendMessage({
    action: 'getUserInfo',
  }, (res) => {
    if (!res.id.length || !res.email.length) {
      return;
    }

    Mixpanel.init('6850a4e3400b7fe7459103da40c5a5be', res.email);
    Mixpanel.engage('extension_run');
  });

  // Add listener for chrome messages.
  chrome.runtime.onMessage.addListener((request) => {
    if (request.action === 'toggleStudybee') {
      StudybeeHandle.studybeeToggle();
    }
  });

  // Add listener for click on body
  angular.element('body').on('keydown', (e) => {
    if (e.keyCode !== 27 || angular.element('[md-component-id="studybee"]').hasClass('md-closed')) {
      return;
    }

    StudybeeHandle.studybeeClose(0);
    Hint.displayToggleHint();
  });

  Locale.setMomentLocale();

  // ---------- global actions - will be moved to separated file ----

  // eslint-disable-next-line no-param-reassign
  $rootScope.toggleObject = (object) => {
    // eslint-disable-next-line no-param-reassign
    object.open = !object.open;
  };
  // eslint-disable-next-line no-param-reassign
  $rootScope.toggleMenu = MainMenu.toggleMenu;
  // eslint-disable-next-line no-param-reassign
  $rootScope.showLoading = FetchData.showLoading.bind(FetchData);
  // eslint-disable-next-line no-param-reassign
  Object.assign($rootScope, { get apiUrl() { return EnvironmentService.API; } });

  // auto check whether the extended view should be shown or not
  $transitions.onStart({ to: '**' }, ($transition$) => {
    // Here we have to find whether we use the extended view as a parent or not
    const { path } = $transition$.$to();
    let extendedViewEnable = false;
    for (const pathItem of path) {
      if (pathItem.views && pathItem.views.hasOwnProperty('extended@')) {
        extendedViewEnable = true;
        break;
      }
    }

    // Small delay to make animation more smooth
    const delayForAnimation = $rootScope.extendedViewEnable !== extendedViewEnable ? 400 : 0;
    // eslint-disable-next-line no-param-reassign
    $rootScope.extendedViewEnable = extendedViewEnable;

    return new Promise((resolve) => setTimeout(resolve, delayForAnimation));
  });

  HostPageNewAssignmentObserver.init();

  Helpers.registerThemeWatcher();
  Helpers.registerBackdropWatcher();
  $rootScope.$on('$destroy', () => {
    Helpers.disconnectThemeWatcher();
    Helpers.disconnectBackdropWatcher();
  });

  // ---------- section only for development ----------

  // if (DEVELOPMENT_ENV) {
  // $trace.enable('TRANSITION');
  // }
}
