module.exports = function() {
  /*@ngInject*/
  function controller($scope, $mdBottomSheet, FetchData, Helpers) {
    $scope.$watch('trigger', function() {
      if ($scope.trigger === undefined) {
        return;
      }

      FetchData.getSingleObjectiveRequirements($scope.objectiveId).then(function(res) {
        $mdBottomSheet.show({
          parent: angular.element(document.querySelectorAll('[md-component-id="studybee"]')),
          templateUrl: 'utils/views/knowledge_requirements.html',
          controller: function() {
            var vm = this;

            /**
             * Get data from API
             */
            vm.objective = res;

            vm.knowledgeRequirementGrade = Helpers.findFirstPositiveGrade(vm.objective.grade_system);

            /**
             * Show grade requirements
             */
            vm.showGradeRequirements = function() {
              vm.activeRequirement = {};

              vm.activeRequirement = vm.objective.knowledge_requirements.find(function(req) {
                  return req.grade_name === vm.knowledgeRequirementGrade;
              });
            };

            /**
             * Close knowledge requirements sheet
             */
            vm.close = function() {
              $mdBottomSheet.hide();
            };

            /**
             * Set vm.activeRequirement at knowledge requirements show
             */
            vm.showGradeRequirements();
          },
          controllerAs: 'vm'
        });
      });
    });
  }

  return {
    restrict: 'E',
    controller: controller,
    scope: {
      trigger: '=',
      objectiveId: '='
    }
  };
};
