import React, {memo, useContext, useEffect, useState} from "react";
import {colors, fonts} from "../../theme";
import {ReactComponent as ExpandIcon} from "../../images/collapse.svg";
import GridSummaryItem from "../../components/gridSummaryItem";
import {getAssessmentIcon, getStatusText, GlobalContext} from "../../utils";
import {getTranslation} from "../../i18n";
import Tooltip from "../../components/tooltip";

const AssignmentTable = (props) => {
  const {
    students,
    assignment,
    student,
    highlight,
    setHighlight,
    styles,
    onStudentSelect,
  } = props;

  const [expanded, setExpanded] = useState(false);
  const [sortedStudents, setSortedStudents] = useState([]);
  const context = useContext(GlobalContext);
  const {sortingMethod} = context;

  const hasAssignments = assignment?.assignments?.length > 0;

  const bgColor = assignment.is_topic ? colors.topic : colors.green;

  useEffect(() => {
    setSortedStudents(students
      .sort((a, b) => {
        if (sortingMethod === 'firstName') {
          return a.first_name?.localeCompare(b.first_name);
        } else {
          return a.last_name?.localeCompare(b.last_name);
        }

      }));

  }, [sortingMethod, students])

  const [assessmentMap, setAssessmentMap] = useState({});

  useEffect(() => {
    setAssessmentMap(assignment.assessments.reduce((acc, a) => {
      acc[a.student_id] = a;
      return acc;
    }, {}));
  }, [assignment]);

  return (
    <div
      style={{
        position: "relative",
        display: "flex",
        backgroundColor: expanded ? colors.topic : "transparent",
        margin: expanded ? "0px 4px 0px 4px" : 0,
      }}
    >
      <table style={{...styles.table}}>
        <tr style={{}}>
          <td
            colSpan={2}
            style={{
              backgroundColor: assignment?.state === "DRAFT" ? colors.draft : bgColor,
              height: "30px",
            }}
          >
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <span
                style={{
                  ...fonts.MEDIUM,
                  whiteSpace: "nowrap",
                  textOverflow: "ellipsis",
                  overflow: "hidden",
                  maxWidth: "200px",
                  color: colors.white,
                  margin: "0px 6px 0px 6px",
                }}
              >
                {assignment.title}
              </span>
              {assignment.is_topic && hasAssignments && !expanded && (
                <Tooltip title={getTranslation("expandTopicTooltip")}>
                  <div>
                    <ExpandIcon
                      fill={colors.white}
                      style={{cursor: "pointer", marginRight: "6px"}}
                      onClick={() => setExpanded(!expanded)}
                    />
                  </div>
                </Tooltip>
              )}
            </div>
          </td>
        </tr>
        {sortedStudents.map((stud) => {
          const assessment = assessmentMap[stud.id];
          const bgColor = stud.id === highlight ? colors.grey : colors.white;
          const backgroundColor =
            stud.id === student.id ? colors.borderColor : bgColor;
          if (!assessment) {
            return (
              <tr
                onClick={() => onStudentSelect(stud.id)}
                onMouseEnter={() => setHighlight(stud.id)}
                onMouseLeave={() => setHighlight(null)}
                key={stud.id}
                style={{
                  backgroundColor,
                  height: "50px",
                  cursor: "pointer",
                }}
              />
            );
          }

          return (
            <tr
              onClick={() => onStudentSelect(assessment.student_id)}
              onMouseEnter={() => setHighlight(assessment.student_id)}
              onMouseLeave={() => setHighlight(null)}
              key={assessment.id}
              style={{
                backgroundColor,
                height: "50px",
                cursor: "pointer",
              }}
            >
              <td>
                <Tooltip title={getStatusText(assessment?.status_new)}>
                  <div style={{width:'28px', display:'flex', alignItems:'center', justifyContent:'center'}}>
                    {getAssessmentIcon({status: assessment?.status_new, style:{maxHeight: '18px'}})}
                  </div>
                </Tooltip>
              </td>
              <td style={styles.td}>
                <GridSummaryItem assessment={assessment}/>
              </td>
            </tr>
          );
        })}
      </table>
      {expanded && (
        <div
          style={{
            display: "flex",
            backgroundColor: colors.topic,
            paddingRight: "16px",
          }}
        >
          {assignment.assignments
            .filter(a => a.connected)
            .sort((a, b) => a.title.localeCompare(b.title))
            .map((a) => (
              <AssignmentTable key={a.id} {...{...props, assignment: a}} />
            ))}
        </div>
      )}
      {expanded && (
        <ExpandIcon
          fill={colors.white}
          style={{
            cursor: "pointer",
            position: "absolute",
            right: "16px",
            top: "16px",
            transform: "rotate(180deg)",
          }}
          onClick={() => setExpanded(!expanded)}
        />
      )}
    </div>
  );
};

export default memo(AssignmentTable);
