import {Menu} from '@material-ui/icons';
import React from 'react';
import {ReactComponent as GridIcon} from '../images/grid.svg';
import {ReactComponent as PaneIcon} from '../images/pane.svg';
import {colors, fonts} from '../theme';
import {getTranslation} from '../i18n';
import LinearProgress from '../components/LinearProgress';
import Tooltip from "../components/tooltip";

const StudentListHeader = (props) => {
  const {toggleMenu, toggleGrid, showGridIcon, showGrid} = props;
  return (
    <div
      style={{
        position: 'relative',
        width: '100%',
        height: '60px',
        display: 'flex',
        backgroundColor: colors.brandColorPink,
        border: '0px solid blue',
        alignItems: 'center',
        padding: '0px 16px 0px 16px',
      }}
    >
      <Menu
        style={{
          cursor: 'pointer',
          color: colors.white,
          height: '25px',
          marginTop: '2px',
          marginRight: '4px',
        }}
        onClick={() => toggleMenu()}
      />
      <p
        style={{
          ...fonts.LARGE,
          color: colors.white,
          marginLeft: '14px',
          fontWeight: '500',
        }}
      >
        {getTranslation('courseProgressionPage')}
      </p>
      {showGridIcon &&
      <div onClick={toggleGrid}
           style={{
             position: 'absolute', top: '22px', right: '50px', cursor: 'pointer',
           }}>
        <Tooltip title={showGrid?getTranslation('collapseGridTooltip'):getTranslation('gridOverviewTooltip')}>
          <div>
            {!showGrid &&
            <GridIcon fill={colors.white} />
            }
            {showGrid &&
              <PaneIcon />
            }
          </div>
        </Tooltip>
      </div>
      }
      <div
        style={{
          position: 'absolute',
          bottom: 0,
          left: 0,
          right: 0,
        }}
      >
        <LinearProgress pink />
      </div>
    </div>
  );
};
export default StudentListHeader;
