import { IAttributes, IController, IDirective, IScope, ITimeoutService } from 'angular';
import get from 'lodash.get';
import { Directive } from '../../../utils/decorators/directive.decorator';
import AssignmentsSharedModule from '../assignmentsShared.module';

@Directive({
  module: AssignmentsSharedModule,
  selector: 'objectiveKnowledgeRequirements',
  restrict: 'AE',
  scope: false,
})
export class ObjectiveKnowledgeRequirementsDirective implements IDirective {
  constructor(private $timeout: ITimeoutService) {
    'ngInject';
  }

  public link(scope: IScope, instanceElement: JQLite, instanceAttributes: IAttributes): void {
    const controllerAs: string = get(instanceAttributes, 'controllerAs', '$ctrl');
    const parentController: IController = get(scope, `${controllerAs}`, null);
    const objectiveIdPath: string = get(instanceAttributes, 'objectiveIdPath', 'objective.objective');
    const objectiveId: number = get(scope, `${objectiveIdPath}`, null);

    if (parentController === null) {
      throw `ObjectiveKnowledgeRequirementsDirective: Could not find controller as ${controllerAs}.`;
    }

    if (objectiveId === null) {
      throw `ObjectiveKnowledgeRequirementsDirective: Missing property ${controllerAs}.${objectiveIdPath}`;
    }

    instanceElement.on('click.objectiveKnowledgeRequirements', () => {
      this.$timeout(() => {
        parentController.triggerKnowledgeRequirements = !parentController.triggerKnowledgeRequirements;
        parentController.objectiveKnowledgeRequirements = objectiveId;
      });
    });

    scope.$on('$destroy', (): void => {
      instanceElement.off('click.objectiveKnowledgeRequirements');
    });
  }
}
