export class AssessmentDetailsComponent {
  static bindings = {
    assessment: '<',
    assignment: '<',
    detailsButtonDisable: '<',
    showTargetGrade: '<',
    showPrivateNote: '<', // todo refactor readOnly binding to use teacherAccount
    readOnly: '<',
    teacherAccount: '<',
    showStudentSelfAssessment: '<',
    simpleView: '<',
    mixpanelFeedbackEvent: '@',
    mixpanelSelfAssessmentEvent: '@',
    mixpanelGradeEvent: '@', // events
    onAssessmentUpdate: '&',
    onAssessmentClose: '&',
    onNavigateToDetails: '&',
    onAssignmentChange: '&',
    disabled: '<ngDisabled',
    emptyMsg: '@?',
    isGradeTextsEnable: '<',
  };
  static controller = AssessmentDetailsComponent;
  static templateUrl =
    'components/course-progress/common/components/assessmentDetails/assessmentDetails.html';

  constructor(GradeSystems, $state) {
    'ngInject';

    this.GradeSystems = GradeSystems;
    this.$state = $state;
    this.disabled = false;
    this.assignment = null;
    this.assessment = null;
    this.emptyMsg = 'courseProgressObjectiveEmptyInfo';
  }

  $onInit() {
    if (!(this.assessment && this.assessment.grade_system)) {
      throw new Error('grade_system object for assessment is not provided.');
    }
    // this is to fix bug when grade nob is out of its scale
    this.assignment = this.assignment || {};
    this.componentInitialized = true;

    if (this.assessment && this.assignment) {
      this.assessment.seen = this.assignment.seen =
        this.assessment.seen && this.assignment.seen;
    }
  }

  toggleAssessmentGradePublished() {
    this.assessment.grade_published = !this.assessment.grade_published;
    this.onAssessmentUpdate({ assessment: this.assessment });
  }

  toggleAssessmentTargetGradePublished() {
    this.assessment.target_grade_published = !this.assessment
      .target_grade_published;
    this.onAssessmentUpdate({ assessment: this.assessment });
  }

  toggleAssessmentFeedbackPublished() {
    this.assessment.feedback_published = !this.assessment.feedback_published;
    this.onAssessmentUpdate({ assessment: this.assessment });
  }

  updateAssessmentProperty() {
    this.onAssessmentUpdate({ assessment: this.assessment });
  }

  onResultPublishChange() {
    this.assignment.result_published = !this.assignment.result_published;
    this.onAssignmentChange({ assignment: this.assignment });
  }

  onFeedbackPublishChange() {
    this.assignment.feedback_published = !this.assignment.feedback_published;
    this.onAssignmentChange({ assignment: this.assignment });
  }

  gradeUpdate(grade) {
    const assessment = angular.copy(this.assessment);

    if (grade) {
      this.assessment.grade = grade;
      assessment.grade = grade.id;
    } else {
      this.assessment.grade = null;
      assessment.grade = null;
    }

    this.onAssessmentUpdate({ assessment });
  }

  gradeChange(grade) {
    this.assessment.grade = grade || null;
  }

  counterGradeChange(value, index) {
    const assessment = angular.copy(this.assessment);
    assessment.counter_grades[index] = value;
    this.onAssessmentUpdate({ assessment });
  }

  targetGradeUpdate(targetGrade) {
    const assessment = angular.copy(this.assessment);

    if (targetGrade) {
      this.assessment.target_grade = targetGrade;
      assessment.target_grade = targetGrade.id;
    } else {
      this.assessment.target_grade = null;
      assessment.target_grade = null;
    }

    this.onAssessmentUpdate({ assessment });
  }

  // visibility
  get isGradeVisible() {
    return (
      (this.readOnly && this.assessment.grade && !this.assessment.counter_grades) ||
      (!this.readOnly && !this.assessment.grade_system.is_counter) ||
      (!this.readOnly && this.assessment.grade_system.is_counter && !this.assessment.counter_grades)
    );
  }

  get isTargetGradeVisible() {
    return (
      (this.readOnly && this.assessment.target_grade && this.showTargetGrade) ||
      (!this.readOnly && this.showTargetGrade)
    );
  }

  get isGradeTextsVisible() {
    return this.isGradeTextsEnable && this.isGradeVisible && this.assessment;
  }

  get gradeSystem() {
    return this.assessment.grade_system;
  }

  get isLearningAssignmentsView() {
    return !!this.$state.is('^.learning-assignments') || !!this.$state.is('^.assessments');
  }

  get isOverallAssessmentSection() {
    return !!this.assessment.objective && !this.assessment.assignment;
  }

  get isOverallAssessmentSectionVisible() {
    return (
      (this.teacherAccount ||
        this.assignment.feedback_published ||
        this.assignment.result_published) &&
      !this.isOverallAssessmentSection &&
      this.isLearningAssignmentsView
    );
  }

  get isPrivateNoteVisible() {
    return this.showPrivateNote && this.assessment.teacher_in_class;
  }

  get isOverallFeedbackAvailable() {
    return this.teacherAccount || this.assignment.feedback_published;
  }

  get isEmpty() {
    return !this.teacherAccount &&
      !this.assessment.feedback &&
      !this.assessment.target_grade &&
      !this.assessment.grade &&
      !this.assessment.self_assessment;
  }
}
