/**
 * @module Config/Service
 * @description
 *   Keep configuration for application.
 */

/*@ngInject*/
import {map} from 'rxjs/operators';
import {environment} from "../../../../../src/environments/environment";

function Config(EnvironmentService) {
  'ngInject';

  function updateIsRunning($q, Updates) {
    const deferred = $q.defer();

    const interval = setInterval(() => {
      if (
        !Updates.updateAssignmentsLoading &&
        !Updates.updateStudentsLoading &&
        !Updates.updateFilesLoading
      ) {
        clearInterval(interval);
        deferred.resolve();
      }
    }, 100);

    return deferred.promise;
  }


  return {
    get apiUrl() {
      return EnvironmentService.API;
    },
    get mixpanelApiUrl() {
      return environment.urls.mixPanel;
    },
    extensionUrl: `chrome-extension://${chrome.i18n.getMessage('@@extension_id')}/`,
    extensionId: chrome.i18n.getMessage('@@extension_id'),
    routes: [
      {
        // Teacher Assess Assignment
        // Location in the Classroom on the Student Work page (teacher)
        name: 'teacher_assess_assignment',
        role: 'teacher',
        templateUrl: 'assignments/teacherAssessAssignment/views/main.html',
        themeClass: 'sb-theme-green',
        ctrl: 'teacherAssessAssignment.MainCtrl',
        controllerAs: 'vm',
        params: {
          isModal: null,
          editingMode: null,
          openedStudents: [],
          courseDefinitionId: {
            value: null,
            dynamic: true,
          },
          assignment: null,
        },
        resolve: {
          assignments: (AssignmentsService) => {
            'ngInject';

            return new Promise(resolve => {
              AssignmentsService.get().subscribe(resolve);
            });
          },
          assignment($stateParams, Assignments, Path, FetchData, AssignmentsQuery, assignments, AssignmentsStore, AssessmentsQuery) {
            'ngInject';

            const assignment = $stateParams.assignment;
            const resolver = assignment ?
              Promise.resolve(AssignmentsQuery.getEntity(assignment.id)) :
              FetchData.v2.getAssignmentByAlternateLink(Path.getAssignmentAlternateLinkId())
                .then(assignment => AssignmentsQuery.getEntity(assignment.id));


            return resolver.then(assignment => {
              AssignmentsStore.setActive(assignment.id);
              return new Promise(resolve => {
                AssessmentsQuery.selectMany(assignment.assessments).pipe(
                  map(assessments => ({
                    ...assignment,
                    assessments,
                  }))
                ).subscribe(resolve);
              });
            })
          },

          assignmentsObjectives($q, $state, Path, FetchData, MainMenu, assignment) {
            'ngInject';

            const assignmentId = assignment.id;
            const courseId = Path.getClassroomId();

            return FetchData.v3.fetchAssignmentAssessments(courseId, assignmentId)
              .then(response => {
                const courseDefinition = response.course_definitions
                  .find(courseDefinition => courseDefinition.objectives.length);

                return courseDefinition ? response : {...response, course_definitions: []};
              })
              .catch(() => {
                MainMenu.saveAssignmentDetails(null);
                return $q.reject();
              });
          },
          assignmentsStudents($q, Path, FetchData, MainMenu, assignment) {
            'ngInject';

            const assignmentId = assignment.id;
            const courseId = Path.getClassroomId();
            return FetchData.getAssignmentsStudents(
              assignmentId, courseId,
            ).catch(() => {
              MainMenu.saveAssignmentDetails(null);
              return $q.reject();
            });
          },
          updateIsRunning($q, Updates) {
            'ngInject';

            return updateIsRunning($q, Updates);
          },
        },
      },
      {
        // Teacher Assess Assignment Student
        // Location in the Classroom on Student Work page on overlay (teacher)
        name: 'teacher_assess_assignment_student',
        role: 'teacher',
        templateUrl: 'assignments/teacherAssessAssignmentStudent/views/main.html',
        themeClass: 'sb-theme-green',
        ctrl: 'teacherAssessAssignmentStudent.MainCtrl',
        controllerAs: 'vm',
        params: {
          studentId: null,
          studentName: null,
          courseDefinitionId: {
            value: null,
            dynamic: true,
          },
          editingMode: null,
          assignment: null,
        },
        resolve: {
          assignments: (AssignmentsService) => {
            'ngInject';

            return new Promise(resolve => {
              AssignmentsService.get().subscribe(resolve);
            });
          },
          assignment($stateParams, Assignments, Path, FetchData, AssignmentsQuery, assignments, AssignmentsStore, AssessmentsQuery) {
            'ngInject';

            const assignment = $stateParams.assignment;
            const resolver = assignment ?
              Promise.resolve(AssignmentsQuery.getEntity(assignment.id)) :
              FetchData.v2.getAssignmentByAlternateLink(Path.getAssignmentAlternateLinkId())
                .then(assignment => AssignmentsQuery.getEntity(assignment.id));


            return resolver.then(assignment => {
              AssignmentsStore.setActive(assignment.id);

              return new Promise(resolve => {
                AssessmentsQuery.selectMany(assignment.assessments).pipe(
                  map(assessments => ({
                    ...assignment,
                    assessments,
                  }))
                ).subscribe(resolve);
              });
            });
          },
          assignmentsObjectives($q, Path, FetchData, MainMenu, assignment) {
            'ngInject';

            const courseId = Path.getClassroomId();
            return FetchData.v3.fetchAssignmentAssessments(courseId, assignment.id)
              .catch(() => {
                MainMenu.saveAssignmentDetails(null);
                MainMenu.saveStudentDetails(null);
                return $q.reject();
              });
          },
          updateIsRunning($q, Updates) {
            'ngInject';

            return updateIsRunning($q, Updates);
          },
        },
      },
      {
        // Teacher Google Drive
        // Location in the Drive on file preview (teacher)
        name: 'teacher_google_drive',
        role: 'teacher',
        templateUrl: 'assignments/teacherAssessAssignmentDrive/views/main.html',
        themeClass: 'sb-theme-green',
        ctrl: 'teacherAssessAssignmentDrive.MainCtrl',
        controllerAs: 'vm',
        params: {
          assignmentId: null,
          editingMode: null,
          courseDefinitionId: {
            value: null,
            dynamic: true,
          },
        },
        resolve: {
          courseId(Path, Courses, AssignmentsService) {
            'ngInject';

            let resolve;

            if (Courses.list.length) {
              resolve = Promise.resolve(Courses.list[0].id);
            } else {
              const alternateLinkId = Path.getAssignmentAlternateLinkId(true);

              resolve = Path.getFileId(5)
                .then(fileId => Courses.getCourseUsingDriveId(fileId, alternateLinkId))
                .then(course => course.id)
            }

            return resolve.then(courseID => new Promise(resolve =>
                AssignmentsService.get().subscribe(() => resolve(courseID)),
              ),
            );
          },
          studentAssignments($state, Path, FetchData, courseId, AssignmentsStore) {
            'ngInject';

            const assessmentId = Path.getAssignmentAlternateLinkId(true);
            return Path.getFileId(5)
              .then(fileId => FetchData.getAssessmentDrive(fileId, assessmentId))
              .then(response => {
                const gradeSystem = response.find(item => item.grade_system);

                if (!gradeSystem) {
                  return $state.go('teacher_assess_assignment', {
                    assignment: {id: response[0].assignment},
                  });
                }

                AssignmentsStore.setActive(response[0].assignment);

                return FetchData.v3.fetchAssignmentAssessments(courseId, response[0].assignment)
                  .then((assignmentAssessments) => {
                    const extendedResponse = {
                      enable_grade_texts: assignmentAssessments.enable_grade_texts,
                      show_grade_texts_for_student: assignmentAssessments.show_grade_texts_for_student,
                    };

                    return [Object.assign(response[0], extendedResponse)];
                  });
              }).catch(() => {
                $state.go('wrongGoogleDriveFile');
              });
          },
          assignment: (studentAssignments, AssignmentsQuery, AssessmentsQuery, $state) => {
            'ngInject';

            const resolver = new Promise((resolve, reject) => {
              const active = AssignmentsQuery.getActive();

              return active ? resolve(active) : reject(active);
            });

            return resolver.then(assignment => {
              return new Promise(resolve => {
                AssessmentsQuery.selectMany(assignment.assessments).pipe(
                  map(assessments => ({
                    ...assignment,
                    assessments,
                  }))
                ).subscribe(resolve);
              });
            }).catch(() => {
              $state.go('wrongGoogleDriveFile');
            });
          },
          assignments: (AssignmentsService) => {
            'ngInject';

            return new Promise(resolve => {
              AssignmentsService.get().subscribe(resolve);
            });
          },
        },
      },
    ],
  };
}

module.exports = Config;
