import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useReducer,
  useRef,
  useState,
} from "react";
import debounce from "lodash.debounce";
import {ButtonBase, CircularProgress} from "@material-ui/core";
import Promise from "bluebird";
import AssignmentObjectivesListHeader from "./assignmentObjectiveListHeader";
import {getPublishedStatus, GlobalContext} from "../utils";
import {getTranslation} from "../i18n";
import {GroupedVisibilityDialogType} from "../types";
import ObjectivesList from "../components/objectivesList";
import AssignmentAssessment from "../components/assignmentAssessment";
import Header from "../components/header";
import FeedbackInput from "../components/feedbackInput";
import {colors, fonts} from "../theme";
import AssessmentButton from "../components/assessmentButton";
import GridHeader from "../components/gridHeader";
import GridViewContent from "../components/gridViewContent";
import AssignmentsListHeader from "./assignmentsListHeader";
import MultiEditDescription from "../components/multiEditDescription";
import MultiEditStatusComponent from "./MultiEditStatusComponent";
import SubmissionStatusButton from "../components/submissionStatusButton";
import MultiEditSubmissionComponent from "../components/multiEditSubmissionComponent";
import {useWindowDimensions} from "../hooks";

const fields = `
    id
    title
    is_topic
    topic_id
    connected
    state
    use_submitted_status
    courseDefinitions {
      id
    }
    objectiveAssessments {
      id
      objective_id
      objectiveName
      objectiveDescription
      feedback
      feedback_published
      private_note
      grade_published
      assessment_id
      grade_id
      feedback_last_edit
      private_note_last_edit
      grade_last_edit
      self_assessment
      self_assessment_last_edit
      last_edit_for_student
      student_id
      counter_grade_0
      counter_grade_1
      counter_grade_2
      counter_grade_3
      counter_grade_4
      counter_grade_5
      counter_grade_6
      counter_grade_7
      counter_grade_8
      counter_grade_9
      max_counter_grade_0
      max_counter_grade_1
      max_counter_grade_2
      max_counter_grade_3
      max_counter_grade_4
      max_counter_grade_5
      max_counter_grade_6
      max_counter_grade_7
      max_counter_grade_8
      max_counter_grade_9
      grade {
        id
        name
        is_passed
        color
      }
    }
    assessments {
      id
      created
      modified
      feedback
      feedback_published
      private_note
      grade_published
      result
      result_published
      status
      assignment_id
      grade_id
      student_id
      assessment_status
      active
      feedback_last_edit
      private_note_last_edit
      result_last_edit
      grade_last_edit
      status_new
      last_edit_for_student
      status_new_last_edit
      self_assessment
      self_assessment_last_edit
      first_name
      last_name
      email
      grade_name
      grade_color
      student_id
      submitted_status
    }
    assignments {
      id
      title
      is_topic
      connected
      state
      use_submitted_status
      topic_id
      courseDefinitions {
       id
      }
      gradeSystem {
        name
        is_counter
        order
        grades {
          id
          name
          is_passed
          color
          label
          circle_label
          order
        }
      }
      assessments {
        id
        created
        modified
        feedback
        feedback_published
        private_note
        grade_published
        result
        result_published
        status
        assignment_id
        grade_id
        student_id
        assessment_status
        active
        feedback_last_edit
        private_note_last_edit
        result_last_edit
        grade_last_edit
        status_new
        last_edit_for_student
        status_new_last_edit
        self_assessment
        self_assessment_last_edit
        first_name
        last_name
        email
        grade_name
        grade_color
        student_id
        submitted_status
      }
    }
    gradeSystem {
      name
      is_counter
      order
      grades {
        id
        name
        is_passed
        color
        label
        circle_label
        order
      }
    }`;

const AssignmentObjectivesList = (props) => {
  const {
    students,
    assignment: initialAssignment,
    courseDefinitions: initialCourseDefinitions,
    studentId: initialStudentId,
    historyBack,
    assignmentAssessment: initialAssignmentAssessment,
    openGrid,
  } = props;

  const context = useContext(GlobalContext);
  const {
    ChangeVisibilityManyDialog,
    Api,
    height,
    showObjectiveDetails,
    isStudent,
    setVisibilityDialogData,
  } = context;
  const [studentId, setStudentId] = useState(initialStudentId);
  const [assignment, setAssignment] = useState(initialAssignment);
  const courseDefinitions = useRef(initialCourseDefinitions);
  const assignmentId = useRef(initialAssignment.id);
  const [textFilter, setTextFilter] = useState("");
  const [parentTopic, setParentTopic] = useState();
  const [loading, setLoading] = useState(false);
  const [assignmentAssessment, setAssignmentAssessment] = useState(
    initialAssignmentAssessment || []
  );
  const [failWarning, setFailWarning] = useState(
    () =>
      initialAssignment?.assessments.find((a) => a.student_id === studentId)
        ?.fail_warning
  );
  const [showGrid, setShowGrid] = useState(openGrid);
  const [multiEdit, setMultiEdit] = useState(false);
  const [selectedStudents, setSelectedStudents] = useState([]);
  const saveManyAssessment = useRef({});
  const saveManyObjectives = useRef();
  const dim = useWindowDimensions();

  const setFilterTextDebounced = debounce(
    (text) => setTextFilter(text.toLowerCase()),
    1000
  );
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  const refresh = async () => {
    setLoading(true);
    const response = await Api.getAssignmentWithAssessment(
      assignment.id,
      fields
    );

    setAssignment(response?.data?.assignment[0]);
    const assessment = response?.data?.assignment[0].assessments.find(
      (a) => a.student_id === studentId
    );
    setAssignmentAssessment(
      assessment || response?.data?.assignment[0].assessments[0]
    );
    forceUpdate();
    setLoading(false);
  };

  useEffect(() => {
    if (selectedStudents.length === 0) {
      setMultiEdit(false);
    }
  }, [selectedStudents]);

  useEffect(() => {
    setStudentId(initialStudentId);
  }, [initialStudentId]);

  useEffect(() => {
    refresh();
  }, [studentId]);

  useEffect(() => {
    if (assignment.id !== assignmentId.current) {
      assignmentId.current = assignment.id;
      refresh();
    }
  }, [assignment]);

  const statusUpdated = async (status) => {
    assignmentAssessment.status_new = status;
    assignmentAssessment.objectiveAssessments =
      assignment?.objectiveAssessments.filter(oa => oa.student_id === studentId);
    if (status === "ASSESSMENT_DONE") {
      setVisibilityDialogData({
        assignmentId: assignment.id,
        assessments: [assessment],
        callback: () => {
          refresh();
        },
      });
    }
    await Api.updateAssignmentAssessment([assessment]);
  };

  const submittedStatusChanged = async (status) => {
    assignmentAssessment.submitted_status = status;
    await Api.updateAssignmentAssessment([assignmentAssessment]);
  }

  const handleHeaderMenuClick = async ({name}) => {
    switch (name) {
      case "VISIBILITY_MANY": {
        const settings = {
          students: [
            students.find((s) => s.student_id === studentId),
          ].map((s) => ({...s, id: s.student_id})),
          courseDefinitions: courseDefinitions.current.filter(
            (courseDefinition) => !!courseDefinition.objectives.length
          ),
          type: GroupedVisibilityDialogType.AssignmentStudentView,
        };
        const result = await ChangeVisibilityManyDialog.show(settings);
        await Api.updateVisibilityMany(assignment.id, result);
        await refresh();
        break;
      }
      default:
        break;
    }
  };

  const assessment = useMemo(
    () => assignment?.assessments.find((a) => a.student_id === studentId),
    [assignment]
  );

  const numberOfObjectives = assignment?.objectiveAssessments?.filter((o) => o.student_id === studentId)?.length || 0;

  const saveMany = async () => {
    const response = await Api.getAssignmentWithAssessment(
      assignmentId.current,
      `assessments {
        id
        student_id
        grade_id
        grade_published
        feedback
        feedback_published
        result
        result_published
        private_note
        submitted_status
        }
      `
    );
    const {assessments} = response?.data?.assignment[0];

    let newAssignmentAssessments = [];
    if (saveManyAssessment.current) {
      newAssignmentAssessments = selectedStudents.map((sid) => {
        const ass = assessments.find((a) => a.student_id === sid);
        return {
          ...ass,
          id: ass.id,
          status_new: saveManyAssessment.current.status_new || ass.status_new,
          grade_id: saveManyAssessment.current.grade_id || ass.grade_id,
          grade_published: getPublishedStatus(saveManyAssessment.current.grade_published, ass.grade_published),
          feedback: saveManyAssessment.current.feedback || ass.feedback,
          feedback_published: getPublishedStatus(saveManyAssessment.current.feedback_published, ass.feedback_published),
          result: saveManyAssessment.current.result || ass.result,
          result_published: getPublishedStatus(saveManyAssessment.current.result_published, ass.result_published),
          private_note: saveManyAssessment.current.private_note || ass.private_note,
          submitted_status: saveManyAssessment.current.submitted_status || ass.submitted_status,
        };
      });
    }
    let newObjectiveAssessments = [];
    if (saveManyObjectives.current) {
      newObjectiveAssessments = selectedStudents.map((sid) => {
        const assessments = assignment?.objectiveAssessments?.filter(
          (o) => o.student_id === sid
        );
        return Object.keys(saveManyObjectives.current).map((key) => {
          const currentAssessment = saveManyObjectives.current[key];
          const assessment = assessments.find(
            (a) => a.objective_id === currentAssessment.objective_id
          );
          const newAssessment = {
            ...assessment,
            feedback_published: getPublishedStatus(currentAssessment?.feedback_published, assessment?.feedback_published),
            counter_grade_0:
              currentAssessment?.counter_grade_0 || assessment?.counter_grade_0,
            counter_grade_1:
              currentAssessment?.counter_grade_1 || assessment?.counter_grade_1,
            counter_grade_2:
              currentAssessment?.counter_grade_2 || assessment?.counter_grade_2,
            counter_grade_3:
              currentAssessment?.counter_grade_3 || assessment?.counter_grade_3,
            counter_grade_4:
              currentAssessment?.counter_grade_4 || assessment?.counter_grade_4,
            counter_grade_5:
              currentAssessment?.counter_grade_5 || assessment?.counter_grade_5,
            counter_grade_6:
              currentAssessment?.counter_grade_6 || assessment?.counter_grade_6,
            counter_grade_7:
              currentAssessment?.counter_grade_7 || assessment?.counter_grade_7,
            counter_grade_8:
              currentAssessment?.counter_grade_8 || assessment?.counter_grade_8,
            counter_grade_9:
              currentAssessment?.counter_grade_9 || assessment?.counter_grade_9,
            feedback: currentAssessment?.feedback || assessment?.feedback,
            grade_published: getPublishedStatus(currentAssessment?.grade_published, assessment?.grade_published),
            private_note:
              currentAssessment?.private_note || assessment?.private_note,
            self_assessment:
              currentAssessment?.self_assessment || assessment?.self_assessment,
          };
          if (currentAssessment?.grade_id) {
            newAssessment.grade_id =
              currentAssessment?.grade_id || assessment?.grade_id;
          }
          return newAssessment;
        });
      });
    }

    await Api.updateAssignmentAssessment(newAssignmentAssessments);
    await Promise.map(
      newObjectiveAssessments,
      (assessments) => {
        return Api.updateObjectiveAssessmentsGql(assessments);
      },
      {concurrency: 10}
    );

    if (saveManyAssessment.current.status_new === "ASSESSMENT_DONE") {
      const objectiveAssessments = assignment?.objectiveAssessments.map(oa => {
        const newOa = newObjectiveAssessments?.flat()?.find(noa => noa.id === oa.id);
        return newOa ? newOa : oa;
      });
      const dialogData = {
        assignmentId: assignment.id,
        assessments: newAssignmentAssessments.map((a) => {
          a.objectiveAssessments = objectiveAssessments?.filter((o) => a.student_id === o.student_id);
          return a;
        }),
        callback: async () => {
          setMultiEdit(false);
          setSelectedStudents([]);
          await refresh();
          saveManyAssessment.current = {};
          saveManyObjectives.current = {};
        },
        hideSaveSetting: true,
      };
      setVisibilityDialogData(dialogData);
    } else {
      setMultiEdit(false);
      setSelectedStudents([]);
      await refresh();
      saveManyAssessment.current = {};
      saveManyObjectives.current = {};
    }
  };
  const toggleGrid = () => {
    setShowGrid(!showGrid);
  };

  const assignmentClicked =  useCallback(async (selectedAssignment) => {
    setMultiEdit(false);
    const response = await Api.getClassroom(`
    courseDefinitions {
      id
      name
      course_id
      objectives{
        id
        name
        description
      }
    }`);
    const {courseDefinitions: cd} = response?.data?.classroom;
    courseDefinitions.current = cd;
    setParentTopic(assignment);
    setAssignment(selectedAssignment);
    const ass = selectedAssignment.assessments.find(
      (a) => a.student_id === studentId
    );
    setAssignmentAssessment(ass || selectedAssignment.assessments[0]);
    setStudentId(
      ass?.student_id || selectedAssignment.assessments[0].student_id
    );
  }, [studentId]);

  const goBack = async () => {
    if (parentTopic) {
      const {
        // eslint-disable-next-line camelcase
        course_definitions,
      } = await Api.getAssignmentAssessments(parentTopic.id);
      // eslint-disable-next-line camelcase
      courseDefinitions.current = course_definitions;
      setAssignment(parentTopic);
      setStudentId(parentTopic.assessments[0].student_id);
      setParentTopic(null);
    } else {
      historyBack();
    }
  };

  const student = students.find((s) => s.student_id === studentId);
  const content = (
    <>
      {showGrid && (
        <div style={{display: "flex"}}>
          <GridHeader
            handleMenuClick={handleHeaderMenuClick}
            assignment={assignment}
            onCollapse={toggleGrid}
            back={() => {
              showObjectiveDetails(false);
              goBack();
            }}
          />
          <AssignmentsListHeader
            isTopic={assignment?.is_topic}
            title={getTranslation("assignmentsPage")}
            gridOpen
            toggleGrid={toggleGrid}
          />
        </div>
      )}
      {showGrid && (
        <GridViewContent
          setMultiEdit={setMultiEdit}
          multiEdit={multiEdit}
          onStudentSelected={setStudentId}
          assignment={assignment}
          selectedStudentId={studentId}
          onAssignmentClicked={assignmentClicked}
          selectedStudents={selectedStudents}
          setSelectedStudents={setSelectedStudents}
        />
      )}
      {!showGrid && (
        <AssignmentObjectivesListHeader
          isStudent={isStudent}
          onStudentSelected={setStudentId}
          onStudent
          back={() => {
            showObjectiveDetails(false);
            historyBack();
          }}
          selectedStudentId={studentId}
          students={students}
          title={assignment.title}
          handleMenuClick={handleHeaderMenuClick}
          isTopic={assignment.is_topic}
          onToggleGrid={toggleGrid}
        />
      )}
      {showGrid && (
        <div
          style={{
            backgroundColor: colors.borderColor,
            display: "flex",
            alignItems: "center",
            padding: "16px",
            justifyContent: "space-between",
          }}
        >
          <div style={{display: "flex", flexDirection: "column"}}>
            <span style={{...fonts.LARGE}}>
              {(() =>
                multiEdit
                  ? `${selectedStudents.length}  ${getTranslation(
                    "studentsSelected"
                  )}`
                  : `${student?.first_name || ""} ${
                    student?.last_name || ""
                  } `)()}
            </span>
            {!multiEdit && student?.email && (
              <span style={{...fonts.SMALL}}>({student?.email})</span>
            )}
          </div>
          {multiEdit && (
            <ButtonBase
              onClick={saveMany}
              style={{
                border: `1px solid ${colors.borderColorDark}`,
                padding: "8px 12px 8px 12px",
                borderRadius: "4px",
                backgroundColor: colors.grey,
              }}
            >
              <span style={{...fonts.MEDIUM}}>
                {getTranslation("saveAll")}
              </span>
            </ButtonBase>
          )}
        </div>
      )}
      <div
        style={{
          padding: "10px",
          height: `${height - (showGrid ? 120 : 60)}px`,
          overflow: "scroll",
        }}
      >
        {multiEdit && <MultiEditDescription/>}
        <Header
          style={{
            backgroundColor: assignment?.is_topic ? colors.topic : colors.green,
            marginBottom: "16px",
          }}
          title={getTranslation("status")}
        />
        {!multiEdit && (
          <>
            <AssessmentButton
              initialState={multiEdit ? "NOT_ASSESSED" : assessment?.status_new}
              warning={failWarning}
              onStatusChange={(status) => statusUpdated(status)}
              readonly={isStudent}
            />
            {assignment.use_submitted_status &&
            <SubmissionStatusButton
              initialState={assessment?.submitted_status || "NOT_SUBMITTED"}
              onStatusChange={(status) => {
                submittedStatusChanged(status);
              }}
              readonly={isStudent}
              style={{marginTop: '10px'}}
            />}
          </>
        )}
        {multiEdit && (
          <>
            <MultiEditStatusComponent onStatusChange={(status) => saveManyAssessment.current.status_new = status}/>
            {assignment.use_submitted_status &&
            <div style={{borderTop: `1px solid ${colors.green}`}}>
              <MultiEditSubmissionComponent
                onStatusChange={(status => saveManyAssessment.current.submitted_status = status)}/>
            </div>
            }
          </>
        )}
        <Header
          style={{
            backgroundColor: assignment.is_topic ? colors.topic : colors.green,
            marginTop: "16px",
          }}
          title={getTranslation("overallResultHeaderTeacher")}
        />
        <AssignmentAssessment
          multiEdit={multiEdit}
          selectedStudents={selectedStudents}
          onSave={(ass) => {
            saveManyAssessment.current = {
              ...saveManyAssessment.current,
              ...ass,
            };
          }}
          assignment={assignment}
          assessment={assignmentAssessment}
        />
        {numberOfObjectives > 20 && (
          <FeedbackInput
            onTextChange={setFilterTextDebounced}
            hideEye
            placeholder={getTranslation("filterObjectives")}
            style={{margin: "20px", marginBottom: "20px", width: "300px"}}
          />
        )}

        <ObjectivesList
          multiEdit={multiEdit}
          textFilter={textFilter}
          assignmentAssessmentId={assignmentAssessment?.id}
          courseDefinitions={courseDefinitions.current}
          objectiveAssessments={
            assignment?.objectiveAssessments?.filter(
              (o) => o.student_id === studentId
            ) || []
          }
          gradeSystem={assignment.gradeSystem}
          failWarning={setFailWarning}
          isTopic={assignment.is_topic}
          onSave={(objectiveAssessments) => {
            saveManyObjectives.current = objectiveAssessments;
          }}
        />
      </div>
      {loading &&
      <div style={{
        display: 'flex',
        position: 'absolute',
        top: 60,
        left: -dim.width,
        right: 0,
        bottom: 0,
        backgroundColor: colors.dimmed,
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <CircularProgress size={60}/>
      </div>}
    </>
  );
  return content;
};

export default AssignmentObjectivesList;
