import React, { useContext, useEffect, useState } from 'react';
import { GlobalContext } from '../utils';
import { getTranslation } from '../i18n';
import { fonts } from '../theme';
import Card from '../components/card';
import CourseItem from './courseItem';
import FullscreenProgress from '../components/fullscreenProgress';

const fields = `
  name
  teachers {
    id
    first_name
    last_name
    email
    photo_url
  }
  courses {
    id
    name
    code
    description
  }
`;

const CoursesCard = (props) => {
  const { student, onCourseSelected } = props;
  const [classroom, setClassroom] = useState([]);
  const [loading, setLoading] = useState(false);
  const context = useContext(GlobalContext);
  const { Api } = context;

  const loadCourses = async () => {
    setLoading(true);
    const response = await Api.getClassroom(fields, student.id);
    setClassroom(response?.data?.classroom);
    setLoading(false);
  };

  useEffect(() => {
    loadCourses();
  }, []);

  useEffect(() => {
    loadCourses();
  }, [student]);

  if (loading) {
    return null;
  }

  return (
    <div style={{ padding: '16px', display:'flex', width:'100%', flexDirection:'column' }}>
      <span style={{ ...fonts.LARGE }}>{`${getTranslation('coursesIn')} ${
        classroom.name
      }`}</span>
      <Card style={{ marginTop: '10px'}}>
        {classroom?.courses?.map((c) => (
          <CourseItem course={c} key={c.id} teachers={classroom?.teachers} classroomName={classroom.name} onClick={() => onCourseSelected(c)}/>
        ))}
      </Card>
    </div>
  );
};
export default CoursesCard;
