import { IComponentController } from 'angular';

export abstract class UnsubscribableComponent implements IComponentController {

  protected $$unsubscribes: Function[];

  protected constructor() {
    'ngInject';
    this.$$unsubscribes = [];
  }

  public $onDestroy(): void {
    this.$$unsubscribes.forEach((unsubscribe: Function): void => {
      if (typeof  unsubscribe === 'function') {
        unsubscribe();
      }
    });
  }
}
