import { IPromise } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { ICoverageMatrix } from '../../models/coverageMatrix.model';
import SharedServicesModule from '../services.module';

export interface ICoverageMatrixService {
  get(studentId: number, courseDefinitionId: number): IPromise<ICoverageMatrix[]>;
  map(courseDefinitionObjectiveAssessments: { coverage_matrix_grade_systems: ICoverageMatrix[] }[]): ICoverageMatrix[];
}

@Service({
  name: 'CoverageMatrixService',
  module: SharedServicesModule,
})
export class CoverageMatrixService implements ICoverageMatrixService {

  constructor(protected FetchData: any) {
    'ngInject';
  }

  public get(studentId: number, courseDefinitionId: number): IPromise<ICoverageMatrix[]> {
    return this.FetchData.v3.getCoverageMatrixGradeSystem(studentId, courseDefinitionId)
      .then(this.map.bind(this));
  }

  public map(courseDefinitionObjectiveAssessments: { coverage_matrix_grade_systems: ICoverageMatrix[] }[]): ICoverageMatrix[] {
    return this.filterNonCounterCoverageMatrix(
      this.reduceUniqueCoverageMatrix(
        this.flattenCoverageMatrixArray(
          this.mapCourseDefinitionObjectiveAssessmentsToCoverageMatrixArray(courseDefinitionObjectiveAssessments),
        ),
      ),
    );
  }

  private mapCourseDefinitionObjectiveAssessmentsToCoverageMatrixArray(courseDefinitionObjectiveAssessments: any[]): ICoverageMatrix[][] {
    return courseDefinitionObjectiveAssessments
      .map((item: { coverage_matrix_grade_systems: ICoverageMatrix[] }) =>
        item.coverage_matrix_grade_systems,
      );
  }

  private flattenCoverageMatrixArray(coverageMatrixesArray: ICoverageMatrix[][]): ICoverageMatrix[] {
    return coverageMatrixesArray
      .reduce((acc: ICoverageMatrix[], coverageMatrix: ICoverageMatrix[]): ICoverageMatrix[] =>
          acc.concat(coverageMatrix),
        [],
      );
  }

  private reduceUniqueCoverageMatrix(coverageMatrixes: ICoverageMatrix[]): ICoverageMatrix[] {
    const ids: number[] = [];
    return coverageMatrixes
      .reduce((acc: ICoverageMatrix[], coverageMatrix: ICoverageMatrix): ICoverageMatrix[] => {
          if (!ids.includes(coverageMatrix.id)) {
            acc.push(coverageMatrix);
            ids.push(coverageMatrix.id);
          }

          return acc;
        },
        [],
      );
  }

  private filterNonCounterCoverageMatrix(coverageMatrixes: ICoverageMatrix[]): ICoverageMatrix[] {
    return coverageMatrixes
      .filter((coverageMatrix: ICoverageMatrix): boolean => !coverageMatrix.is_counter);
  }
}
