import { IPromise } from 'angular';
import get from 'lodash.get';
import { Service } from '../../../utils/decorators/service.decorator';
import { GroupedVisibilityDialogType } from '../../enums/groupedVisibilityDialogType.enum';
import { IGroupedVisibility } from '../../models/groupedVisibility.model';
import { IGroupedVisibilitySettings } from '../../models/groupedVisibilitySettings.model';
import { DialogService, IDialogService } from '../dialog/dialog.service';
import SharedServicesModule from '../services.module';

export interface IGroupedVisibilityDialogService extends IDialogService {
  show(settings: IGroupedVisibilitySettings): IPromise<IGroupedVisibility>;
}

@Service({
  module: SharedServicesModule,
  name: 'GroupedVisibilityDialogService',
})
export class GroupedVisibilityDialogService extends DialogService implements IGroupedVisibilityDialogService {
  protected readonly helpButtonLinks: { [type: string]: string };

  constructor(protected $mdDialog: ng.material.IDialogService) {
    'ngInject';

    super($mdDialog);
    this.helpButtonLinks = {
      [GroupedVisibilityDialogType.AssignmentStudentView]: 'teacherAssignmentStudentChangeVisibilityDialogHelpButtonUrl',
      [GroupedVisibilityDialogType.AssignmentView]: 'teacherAssignmentClassChangeVisibilityDialogHelpButtonUrl',
      [GroupedVisibilityDialogType.StudentProgressView]: 'teacherStudentProgressChangeVisibilityDialogHelpButtonUrl',
    };
  }

  public show(settings: IGroupedVisibilitySettings): IPromise<IGroupedVisibility> {
    const options: ng.material.IDialogOptions = this.buildOptions({
      locals: {
        settings,
        helpButtonLink: this.helpButtonLinks[settings.type],
      },
      controller: class GroupedVisibilityDialogController {
        public setVisibilityIsDisabled: boolean;
        public groupedVisibility: IGroupedVisibility;

        constructor(private $mdDialog: DialogService) {
          'ngInject';
          this.setVisibilityIsDisabled = true;
        }

        public onChange(groupedVisibility: IGroupedVisibility): void {
          const isTypeOfFeedbackSelected: boolean =
            this.checkIfAnySelected(get(groupedVisibility, 'types_of_feedback.objective', null)) ||
            this.checkIfAnySelected(get(groupedVisibility, 'types_of_feedback.overall', null));

          this.setVisibilityIsDisabled = !groupedVisibility || !isTypeOfFeedbackSelected ||
            !groupedVisibility.students_ids.length ||
            !groupedVisibility.course_definitions_ids.length ||
            typeof groupedVisibility.visible !== 'boolean';

          this.groupedVisibility = groupedVisibility;
        }

        private checkIfAnySelected(obj: { [prop: string]: boolean }) {
          for (const prop in obj) {
            if (get(obj, `${prop}`, false)) {
              return true;
            }
          }

          return false;
        }
      },
      template: `
<md-dialog class="grouped-visibility-dialog">
<!--  <help-button class="black"-->
<!--               link="{{ ::$ctrl.helpButtonLink }}"></help-button>-->
  <md-dialog-content class="sb-dialog-content">
    <div class="dialog__small--title" localize>__MSG_groupedVisibility__</div>
    <grouped-visibility settings="$ctrl.settings" on-change="$ctrl.onChange(changes)"></grouped-visibility>
    <footer class="dialog__small--button--container"
         layout="row"
         layout-align="end center">
      <md-button class="sb-btn-no-background md-primary sb-btn-no-min-width"
                 ng-click="$ctrl.$mdDialog.cancel()"
                 localize>__MSG_cancelBtn__</md-button>
      <md-button class="sb-btn-no-background md-primary sb-btn-no-min-width"
                 ng-click="$ctrl.$mdDialog.hide($ctrl.groupedVisibility)"
                 ng-disabled="$ctrl.setVisibilityIsDisabled"
                 localize>__MSG_setVisibilityButton__</md-button>
    </footer>
  </md-dialog-content>
</md-dialog>`,
    });

    return this.$mdDialog.show(options);
  }
}
