export class StudentProgressInputComponent {
  static templateUrl =
    'components/course-progress/common/components/studentProgressInput/studentProgressInput.html';
  static controller = StudentProgressInputComponent;
  static bindings = {
    disabled: '<?ngDisabled',
    isPublished: '<?',
    label: '@?',
    lastEdit: '<?',
    editBy: '<?',
    value: '<ngModel',
    onChange: '&?ngChange',
  };

  constructor(UserInfo) {
    'ngInject';

    this.UserInfo = UserInfo;
    this.isTeacher = false;
    this.disabled = false;
    this.value = null;
    this.lastEdit = null;
    this.isPublished = null;
  }

  $onInit() {
    this.isTeacher = this.UserInfo.isTeacher();
  }

  onClick() {
    this.isPublished = !this.isPublished;
    this.triggerOnChange();
  }

  triggerOnChange() {
    const $event = { value: this.value };

    if (this.isPublished !== null) {
      $event.isPublished = this.isPublished;
    }

    this.onChange({ $event });
  }
}
