import angular from 'angular';
import { ListViewType } from '../../../../../../shared/enums/listViewType.enum';
import { ICoverageMatrix } from '../../../../../../shared/models/coverageMatrix.model';
import { IListViewSwitch, ListViewSwitch } from '../../../../../../shared/models/ListViewSwitch.model';
import { DialogService } from '../../../../../../shared/services/dialog/dialog.service';
import { Service } from '../../../../../../utils/decorators/service.decorator';
import TeacherCourseProgressModule from '../../../teacher-course-progress.module';
import dialogTemplate from './listViewSwitchDialog.tpl.html';

@Service({
  name: 'ListViewSwitchService',
  module: TeacherCourseProgressModule,
})
export class ListViewSwitchService extends DialogService {
  constructor(protected $mdDialog: ng.material.IDialogService) {
    'ngInject';

    super($mdDialog);
  }

  public findFirstNonCoverageMatrixView(menuItems: IListViewSwitch[]): IListViewSwitch {
    return menuItems.find((menuItem: IListViewSwitch): boolean =>
      (menuItem.type & ListViewType.COVERAGE_MATRIX) === 0,
    );
  }

  public getCoverageMatrixById(coverageMatrixes: ICoverageMatrix[], id: number): ICoverageMatrix {
    return coverageMatrixes.find((coverageMatrix: ICoverageMatrix): boolean =>
      coverageMatrix.id === id,
    );
  }

  public getMenuItemById(menuItems: IListViewSwitch[], id: string): IListViewSwitch {
    return menuItems.find((menuItem: IListViewSwitch): boolean =>
      menuItem.id === id,
    );
  }

  public getViewMenuItems(coverageMatrixes: ICoverageMatrix[]): IListViewSwitch[] {
    let menuItems: IListViewSwitch[] = [
      {
        id: `${ListViewType.COVERAGE_MATRIX}`,
        label: 'showCoverageMatrix',
        type: ListViewType.COVERAGE_MATRIX,
      },
      {
        id: `${ListViewType.STATUS}`,
        label: 'showStatus',
        type: ListViewType.STATUS,
      },
    ];

    if (!coverageMatrixes.length) {
      menuItems = menuItems.map((menuItem: IListViewSwitch): IListViewSwitch => {
        menuItem.isDisabled = (menuItem.type & ListViewType.COVERAGE_MATRIX) > 0;

        return menuItem;
      });
    }

    return menuItems;
  }

  public getInformationMenuItems(): IListViewSwitch[] {
    return [
      {
        id: `${ListViewType.TITLE_ONLY}`,
        label: 'teacherCurriculumObjectivesToolbarMenuItemTitleOnly',
        type: ListViewType.TITLE_ONLY,
      },
      {
        id: `${ListViewType.TITLE_AND_DESCRIPTION}`,
        isSelected: true,
        label: 'teacherCurriculumObjectivesToolbarMenuItemTitleDescription',
        type: ListViewType.TITLE_AND_DESCRIPTION,
      },
      {
        id: `${ListViewType.DESCRIPTION_ONLY}`,
        label: 'teacherCurriculumObjectivesToolbarMenuItemDescriptionOnly',
        type: ListViewType.DESCRIPTION_ONLY,
      },
    ];
  }

  public isCoverageMatrixView(menuItems: IListViewSwitch[]): boolean {
    return !!menuItems
      .find((menuItem: IListViewSwitch): boolean =>
        menuItem.isSelected && (menuItem.type & ListViewType.COVERAGE_MATRIX) > 0,
      );
  }

  public setSelection(menuItem: IListViewSwitch, menuItems: IListViewSwitch[]): IListViewSwitch[] {
    return menuItems.map((item: IListViewSwitch): IListViewSwitch => {
      item.isSelected = item.id === menuItem.id;

      return item;
    });
  }

  public showDialogWithDisableMessage(): void {
    const options: ng.material.IDialogOptions = this.buildOptions({
      template: dialogTemplate,
    });

    this.$mdDialog.show(options);
  }
}
