import { IPromise } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { FetchData } from '../../../utils/services/fetchData.service';
import { RequestType } from '../../enums/requestType.enum';
import { SchoolCategoryType } from '../../enums/schoolCategoryType';
import { ISchool } from '../../models/school.model';
import { ISchoolCategory } from '../../models/schoolCategory.model';
import { ISchoolRequestBody } from '../../models/schoolRequestBody.model';
import { ISchoolRequestParams } from '../../models/schoolRequestParams.model';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'SchoolService',
})
export class SchoolService {
  constructor(private FetchData: FetchData) {
    'ngInject';
  }

  public getSchools(params: ISchoolRequestParams = {}): IPromise<ISchool[]> {
    return this.FetchData.sendAPIRequestWithParams(params, `/schools/school/`);
  }

  public getSchoolCategories(): ISchoolCategory[] {
    return [
      {
        type: SchoolCategoryType.K_12,
        name: 'SchoolCategoryNameK12',
      },
      {
        type: SchoolCategoryType.HIGHER_EDUCATION,
        name: 'SchoolCategoryNameHigherEducation',
      },
      {
        type: SchoolCategoryType.HOME_SCHOOL,
        name: 'SchoolCategoryNameHomeSchool',
      },
      {
        type: SchoolCategoryType.CORPORATION,
        name: 'SchoolCategoryNameCorporation',
      },
      {
        type: SchoolCategoryType.OTHER,
        name: 'SchoolCategoryNameOther',
      },
    ];
  }

  public filterSchools(schools: ISchool[], query: string): ISchool[] {
    const lowerQuery: string = query.toLocaleLowerCase();

    return schools.filter((school: ISchool): boolean => {
      const lowerTitle: string = school.title.toLocaleLowerCase();

      return lowerTitle.includes(lowerQuery);
    });
  }

  public getSchoolName(school: ISchool): string {
    return school.title.match(/(.*) - (.*)/)[1].trim();
  }

  public getCityName(school: ISchool): string {
    return school.title.match(/(.*) - (.*)/)[2].trim();
  }

  public sendSchoolRequest(data: ISchoolRequestBody): IPromise<any> {
    return this.FetchData.sendAPIRequest('/v3/schools/school-request/', RequestType.POST, data);
  }
}
