import {colors, fonts} from "../../theme";
import {Close} from "@material-ui/icons";
import {getTranslation} from "../../i18n";
import React from "react";

const EditAssessmentCriteriaHeader = (props) => {
  const {assignment, onClose, onSave, saveEnabled} = props;
  return (
    <div
      style={{
        padding: "0px 14px 0px 14px",
        height: "60px",
        display: "flex",
        backgroundColor: assignment.is_topic ? colors.topic : colors.green,
        position: "relative",
        alignItems: "center",
      }}
    >
      <div style={{alignItems: "center", display: "flex", height: "40px"}}>
        <Close
          onClick={onClose}
          style={{cursor: "pointer", color: colors.white}}
        />
        <span
          style={{
            ...fonts.LARGE,
            fontWeight: "500",
            color: colors.white,
            marginLeft: "6px",
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            width: '240px',
          }}
        >
          {assignment.title}
        </span>
      </div>
      <span
        onClick={onSave}
        style={{
          ...fonts.MEDIUM,
          color: saveEnabled ? colors.white : colors.darkGrey,
          position: "absolute",
          right: "20px",
          bottom: "20px",
          cursor: saveEnabled ? "pointer" : "default",
        }}
      >
        {getTranslation("saveBtn").toUpperCase()}
      </span>
    </div>
  );
};

export default EditAssessmentCriteriaHeader;