import React, {memo, useContext, useEffect, useMemo, useReducer, useRef, useState} from 'react';
import {debounce} from 'lodash';
import AssessmentSlider from '../components/assessmentSlider';
import {getLastModified, GlobalContext} from '../utils';
import FeedbackInput from '../components/feedbackInput';
import {getTranslation} from '../i18n';
import fonts from '../theme/fonts';

const CourseObjectiveAssessment = (props) => {
  const {
    assessment: initialAssessment,
    defaultIndex,
    id,
    gradeSystemValues,
    knowledgeRequirements,
    multiEdit = false,
    onSave
  } = props;
  const context = useContext(GlobalContext);
  const {Api} = context;
  const [gradeId, setGradeId] = useState(initialAssessment?.grade_id);
  const assessment = useRef(initialAssessment);
  const [initialIndex, setInitialIndex] = useState(defaultIndex);
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  useEffect(() => {
    setInitialIndex(defaultIndex);
  }, [defaultIndex, id]);

  useEffect(() => {
    assessment.current = initialAssessment;
    forceUpdate();
    setGradeId(initialAssessment?.grade_id);
  }, [id]);

  const saveAssessment = useMemo(() => debounce(async (Api, assessment) => {
    await Api.updateCourseObjectiveAssessment([assessment]);
  }, 1000), [initialAssessment?.id]);

  const save = () => {
    if (multiEdit) {
      onSave(assessment.current);
    } else {
      saveAssessment(Api, assessment.current);
    }
  };

  return (
    <div
      style={{display: 'flex', flexDirection: 'column', width: '100%'}}
    >
      <AssessmentSlider
        id={id}
        values={gradeSystemValues}
        defaultIndex={initialIndex}
        onChange={(index) => setGradeId(gradeSystemValues[index].id)}
        published={assessment.current?.grade_published}
        onPublish={(value) => {
          assessment.current = {
            ...assessment.current,
            grade_published: value,
          };
          save();
        }}
        helpText={getLastModified(assessment.current?.grade_last_edit)}
        showPublish={true}
        onChangeCommit={() => {
          assessment.current = {
            ...assessment.current,
            grade_id: gradeId,
          };
          save();
        }}
        disabled={false}
        multiEdit={multiEdit}
      />
      <div
        style={{
          marginTop: "20px",
          height: "100px",
          overflow: "scroll",
          ...fonts.MEDIUM,
        }}
        dangerouslySetInnerHTML={{
          __html: knowledgeRequirements?.find((k) => k.grade_id === gradeId)
            ?.description,
        }}
      />
      <FeedbackInput
        text={assessment.current?.feedback}
        placeholder={getTranslation('overallFeedback')}
        onTextChange={(text) => {
          assessment.current = {...assessment.current, feedback: text};
          save();
        }}
        published={assessment.current?.feedback_published}
        onPublish={(id, state) => {
          assessment.current = {...assessment.current, feedback_published: state};
          save();
        }}
        id={assessment.current?.id}
        assessment={assessment.current}
        helperText={getLastModified(assessment.current?.feedback_last_edit)}
        warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
        multiEdit={multiEdit}
      />
      <FeedbackInput
        text={assessment.current?.private_note}
        placeholder={getTranslation('overallPrivateNoteInput')}
        onTextChange={(text) => {
          assessment.current = {...assessment.current, private_note: text};
          save();
        }}
        hideEye
        id={assessment.current?.id}
        assessment={assessment.current}
        helperText={getLastModified(assessment.current?.private_note_last_edit)}
        warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
      />
    </div>
  );
};

export default memo(CourseObjectiveAssessment);
