import React, {memo, useContext, useEffect, useRef, useState} from "react";
import {GlobalContext} from "../utils";
import CourseAssessmentCard from "./courseAssessmentCard";
import EwsStudentCard from "./ewsStudentCard";
import CourseInfoCard from "./courseInfoCard";
import CourseObjectiveAssessmentCard from "./courseObjectiveAssessmentCard";
import AssignmentsCard from "./assignmentsCard";
import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import {ButtonBase, CircularProgress} from '@material-ui/core';
import MultiEditDescription from '../components/multiEditDescription';

const fields = `
  students{
    id
    first_name
    last_name
    email
    courseAssessments {
      id
      courseName
      courseId
      courseCode
      feedback
      feedback_published
      feedback_last_edit
      private_note
      private_note_last_edit
      grade_id
      gradeName
      gradeColor
      gradeIsPassed
      grade_published
      grade_last_edit
      target_grade_id
      target_grade_published
      target_grade_last_edit
      gradeSystem{
        name
        is_counter
        is_main
        is_final
        grades {
          id
          name
          label
          is_passed
          color
          order
        }
      }
    }
  }
`;

const StudentCourseOverview = (props) => {
  const {
    student,
    course,
    style,
    hideCourseCard = false,
    onObjectiveSelected,
    isGrid = false,
    multiEdit = false,
    onSave,
    extraAdjustmentData = false,
    selectedStudents = false,
    loading,
  } = props;

  const context = useContext(GlobalContext);
  const {Api} = context;

  const [classroom, setClassroom] = useState();
  const multiEditChanges = useRef({});

  const refresh = async () => {
    const response = await Api.getClassroom(fields);
    setClassroom(response?.data?.classroom);
  };

  const getClassroom = async () => {
    loading(true);
    await refresh();
    loading(false);
  };

  useEffect(() => {
    getClassroom();
  }, []);

  useEffect(() => {
    if (!multiEdit) {
      getClassroom();
    }
  }, [student.id, multiEdit]);

  if (!classroom) {
    return null;
  }

  const assessment = classroom?.students.find(s => s.id === student.id)?.courseAssessments?.find((a) => a.courseId === course.id);
  const cardStyle = isGrid ? {width: "315px"} : {minWidth: "501px"};

  const multiEditSave = () => {
    if (onSave) {
      onSave(multiEditChanges.current);
      multiEditChanges.current = {};
    }
  };

  return (
    <div
      style={{
        backgroundColor: colors.grey,
        display: "flex",
        flexDirection: "column",
        overflow: "hidden",
        position: "absolute",
        top: isGrid ? "30px" : "60px",
        left: 0,
        right: 0,
        bottom: 0,
      }}
    >
      <div
        style={{
          maxHeight: "50px",
          backgroundColor: colors.greyDarker,
          display: "flex",
          alignItems: "center",
          padding: "16px",
          borderBottom: `1px solid ${colors.borderColor}`,
        }}
      >
        <span
          style={{...fonts.XXLARGE, fontWeight: "400"}}
        >{`${getTranslation("coursePlan")} - ${course?.name}`}</span>
      </div>
      {multiEdit && (
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: '10px',
          borderBottom: '1px solid ' + colors.borderColor
        }}>
          <span style={{...fonts.LARGE}}>{selectedStudents.length} {getTranslation('studentsSelected')}</span>
          <ButtonBase
            style={{
              border: `1px solid ${colors.borderColorDark}`,
              padding: "8px 12px 8px 12px",
              borderRadius: "4px",
              backgroundColor: colors.white,
            }}
            onClick={multiEditSave}
          >
              <span style={fonts.MEDIUM}>
                {getTranslation("saveAll")}
              </span>
          </ButtonBase>
        </div>
      )}

      <div style={{position: "relative", overflow: "scroll"}}>
        <div style={{width: "600px", padding: "16px", ...style}}>
          {!hideCourseCard && <CourseInfoCard course={course}/>}
          {multiEdit && <MultiEditDescription/>}
          <EwsStudentCard
            student={student}
            course={course}
            multiEdit={multiEdit}
            isGrid={isGrid}
            onChange={(ews) => {
              multiEditChanges.current.ews = ews;
            }}
            extraAdjustmentData={extraAdjustmentData}
          />
          <CourseAssessmentCard
            studentId={student.id}
            assessment={assessment}
            onSave={(assessment) => {
              multiEditChanges.current.courseAssessment = assessment;
            }}
            multiEdit={multiEdit}
          />
          <CourseObjectiveAssessmentCard
            courseId={course.id}
            studentId={student.id}
            onObjectiveSelected={onObjectiveSelected}
            multiEdit={multiEdit}
            onSave={(changes) => {
              multiEditChanges.current.objectiveAssessments = changes;
            }}
          />
          {!multiEdit &&(
            <AssignmentsCard
              style={cardStyle}
              studentId={student.id}
              courseId={course.id}
              showObjectives={!isGrid}
              contentWidth={584}
            />
          )}
          {multiEdit && <div style={{height: "40px"}}/>}
        </div>
      </div>
    </div>
  );
};
export default memo(StudentCourseOverview);
