import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { AssignmentState } from '../../enums/assignmentStateType.enum';
import { AssignmentType } from '../../enums/assignmentType.enum';
import { IAssignment } from '../../models/assignment.model';
import SharedComponentsModule, { UnsubscribableComponent } from '../components.module';
import style from './assignmentsList.scss';
import template from './assignmentsList.tpl.html';
import {getTranslation} from '../../../../../../react_src/i18n';

@Component({
  module: SharedComponentsModule,
  selector: 'assignments-list-new',
  template: template,
  style: style,
})
export class AssignmentsListComponent extends UnsubscribableComponent {
  @Input({ string: true })
  public label: string;
  @Input()
  public assignments: IAssignment[];

  constructor() {
    'ngInject';

    super();
    this.label = '';
    this.assignments = [];
  }

  public getIconTooltip(assignment: IAssignment): string {
    const suffix: string = assignment.state === AssignmentState.DRAFT ?
      `(${getTranslation('assignmentStateIconTooltipDraft')})` : '';
    let tooltipText: string;

    if (assignment.assignment_not_assessable) {
      tooltipText = getTranslation('assignmentTypeIconTooltipWithoutAssessmentCriteria');
    } else if (assignment.assignment_type === AssignmentType.WORK_UNIT) {
      tooltipText = getTranslation('assignmentTypeIconTooltipWorkUnit');
    } else if (assignment.assignment_type === AssignmentType.STANDARD_ASSIGNMENT) {
      tooltipText = getTranslation('assignmentTypeIconTooltipStandardAssignment');
    }

    return tooltipText && suffix ? `${tooltipText} ${suffix}` : tooltipText;
  }


  public consumeEvent($event: Event): void {
    if ($event && $event.stopPropagation instanceof Function) {
      $event.stopPropagation();
    }
  }
}
