import React, { useContext, useEffect, useMemo, useState } from "react";
import debounce from "lodash.debounce";
import { colors, fonts } from "../theme";
import {getLastModified, GlobalContext} from "../utils";
import { ReactComponent as HappyIcon } from "../images/happy-face.svg";
import { ReactComponent as MediumIcon } from "../images/medium-face.svg";
import { ReactComponent as SadIcon } from "../images/sad-face.svg";
import PublishIcon from "../components/publishIcon";
import { getTranslation } from "../i18n";
import EwsStatusButton from "./ewsStatusButton";

const style = {
  statusButton: {
    display: "flex",
    width: "110px",
    height: "30px",
    borderRadius: "6px",
    cursor: "pointer",
    backgroundColor: colors.ewsGreen,
    alignItems: "center",
    justifyContent: "center",
    marginRight: "10px",
    marginTop: "4px",
  },
};
const getColor = (status) => {
  if (!status) {
    return "transparent";
  }
  switch (status) {
    case "10":
      return colors.ewsGreen;
    case "20":
      return colors.primary;
    case "30":
      return colors.red;
    default:
      return "transparent";
  }
};

const getIcon = (status) => {
  if (!status) {
    return null;
  }
  switch (status) {
    case "10":
      return <HappyIcon />;
    case "20":
      return <MediumIcon />;
    case "30":
      return <SadIcon />;
    default:
      return null;
  }
};

const StatusComponent = (props) => {
  const {
    status: initialStatus,
    studentId,
    courseId,
    multiEdit,
    onChange = false,
  } = props;
  const context = useContext(GlobalContext);
  const { Api } = context;
  const [status, setStatus] = useState(initialStatus || {});

  const save = useMemo(
    () =>
      debounce(async (data, Api) => {
        await Api.updateStudentEWSStatus(data);
      }, 1000),
    [studentId]
  );

  useEffect(() => {
    setStatus(multiEdit ? {} : initialStatus);
  }, [initialStatus, studentId]);

  useEffect(() => {
    setStatus(multiEdit ? {} : initialStatus);
  }, [multiEdit]);

  const saveStatus = async (newStatus) => {
    const saveObj = {
      ...status,
      status: newStatus.status,
      student_id: studentId,
      course_id: courseId,
      modified: Date.now(),
    };
    setStatus(saveObj);
    if (!multiEdit) {
      save(saveObj, Api);
    }
    if (onChange) {
      onChange(saveObj);
    }
  };

  const publish = async (published) => {
    const saveObj = {
      ...status,
      student_id: studentId,
      course_id: courseId,
      published: published,
    };
    if (!multiEdit) {
      save(saveObj, Api);
      setStatus(saveObj);
    }
    if (onChange) {
      onChange(saveObj);
    }
  };

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      {!multiEdit && (
        <div style={{ display: "flex", alignItems: "center" }}>
          <div style={{ display: "flex", width: "160px" }}>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <span style={fonts.MEDIUM}>
                {getTranslation("currentWarningLevel")}
              </span>
              <span style={fonts.SMALL}>
                {getLastModified(status?.modified)}
              </span>
            </div>
          </div>
          <div
            style={{
              ...style.statusButton,
              cursor: "default",
              backgroundColor: getColor(status?.status),
              border: status?.status
                ? 0
                : `1px solid ${colors.borderColorDark}`,
            }}
          >
            {getIcon(status?.status)}
          </div>
          {status?.status && (
            <PublishIcon
              size={14}
              multiEdit={multiEdit}
              published={status?.published}
              onClick={(status) => {
                publish(status);
              }}
            />
          )}
        </div>
      )}
      {multiEdit && (
        <div style={{ display: "flex", alignItems: "center" }}>
          <span style={{ ...fonts.MEDIUM, width: "140px" }}>
            {getTranslation("visibility")}
          </span>
          <PublishIcon
            multiEdit={multiEdit}
            published={status?.published}
            onClick={(status) => {
              publish(status);
            }}
          />
        </div>
      )}
      <div style={{ display: "flex", marginTop: "16px" }}>
        <span
          style={{
            ...fonts.MEDIUM,
            width: "160px",
            minWidth: multiEdit ? "140px" : "160px",
          }}
        >
          {getTranslation("setWarningLevel")}
        </span>
        <div
          style={{
            display: "flex",
            // width: '100%',
            // justifyContent: 'space-around',
            marginLeft: "0px",
            flexWrap: "wrap",
          }}
        >
          <EwsStatusButton
            value={"10"}
            selectedMulti={status?.status === "10"}
            saveStatus={saveStatus}
            multiEdit={multiEdit}
            status={status}
            icon={<HappyIcon />}
            selectedColor={`${colors.ewsGreen}80`}
            color={colors.ewsGreen}
          />
          <EwsStatusButton
            value={"20"}
            selectedMulti={status?.status === "20"}
            saveStatus={saveStatus}
            multiEdit={multiEdit}
            status={status}
            icon={<MediumIcon />}
            selectedColor={`${colors.primary}80`}
            color={colors.primary}
          />
          <EwsStatusButton
            value={"30"}
            selectedMulti={status?.status === "30"}
            saveStatus={saveStatus}
            multiEdit={multiEdit}
            status={status}
            icon={<SadIcon />}
            selectedColor={`${colors.red}80`}
            color={colors.red}
          />
        </div>
      </div>
    </div>
  );
};
export default StatusComponent;
