import { Service } from '../../utils/decorators/service.decorator';
import TeacherAssessAssignmentModule from './teacherAssessAssignment.module';

@Service({
  module: TeacherAssessAssignmentModule,
  name: 'TeacherAssessAssignmentService',
})
export class TeacherAssessAssignmentService {
  constructor() {
    'ngInject';
  }

  public findVisibilityPropoerty(currentEditingMode: string = ''): string {
    const map = {
      assessment: 'grade_published',
      objectives_feedback: 'feedback_published',
      results: 'result_published',
      overall_feedback: 'feedback_published',
    };

    return map[currentEditingMode];
  }

  public findTextPropoerty(currentEditingMode: string = ''): string {
    const map: { [prop: string]: string } = {
      objectives_feedback: 'feedback',
      overall_feedback: 'feedback',
      objectives_private_notes: 'private_note',
      results: 'result',
      overall_private_notes: 'private_note',
    };

    return map[currentEditingMode];
  }

  public findSelectedItems(currentEditingMode: string, coursesDefinitions: any[], students: any[]): any[] {
    if (['assessment', 'objectives_feedback', 'objectives_private_notes'].includes(currentEditingMode)) {
      return coursesDefinitions.reduce((accCoursesDefinitions, courseElem): any[] => [
        ...accCoursesDefinitions,
        ...courseElem.students.reduce((accStudents, studentElem): any[] => [
          ...accStudents,
          ...studentElem.objective_assessments
            .filter((objectiveElem): boolean => objectiveElem.checked),
        ], []),
      ], []);
    } else if (['results', 'overall_feedback', 'overall_private_notes'].includes(currentEditingMode)) {
      return students.filter((studentElem): boolean => studentElem.checked);
    }

    return [];
  }
}
