import CheckboxWithText from "../../components/CheckboxWithText";
import {fonts} from "../../theme";
import {Switch, Tooltip} from "@material-ui/core";
import {getTranslation} from "../../i18n";
import React from "react";

const EditAssessmentCriteriaItem = ({text, enabled = true, onChange, checked = true, checkbox = false}) => (
  <div
    style={{
      display: "flex",
      alignItems: "center",
      width: "100%",
      justifyContent: "space-between",
    }}
  >
    {checkbox && (
      <CheckboxWithText
        checked={checked}
        onCheck={onChange}
        text={text}
        controlled={true}
        textWidth={260}
      />
    )}
    {!checkbox && (
      <>
        <span style={{...fonts.MEDIUM}}>{text}</span>
        <Tooltip
          title={enabled ? "" : getTranslation("switch_default_on_tooltip")}
        >
          <div>
            <Switch
              onChange={onChange}
              checked={checked}
              color="primary"
              disabled={!enabled}
            />
          </div>
        </Tooltip>
      </>
    )}
  </div>
);

export default EditAssessmentCriteriaItem;
