import { IComponentController } from 'angular';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { AssignmentState } from '../../enums/assignmentStateType.enum';
import { AssignmentType } from '../../enums/assignmentType.enum';
import { IconsPath } from '../../enums/iconsPath.enum';
import { IAssignment } from '../../models/assignment.model';
import SharedComponentsModule from '../components.module';
import style from './assignmentIconType.scss';
import template from './assignmentIconType.tpl.html';

@Component({
  module: SharedComponentsModule,
  selector: 'assignment-icon-type',
  template: template,
  style: style,
})
export class AssignmentIconTypeComponent implements IComponentController {
  @Input()
  public assignment: IAssignment;

  constructor() {
    'ngInject';

    this.assignment = null;
  }

  public getIconName(): string {
    if (this.assignment.assignment_not_assessable || this.assignment.assignment_type === AssignmentType.WORK_UNIT) {
      return '';
    } else if (this.assignment.assignment_type === AssignmentType.STANDARD_ASSIGNMENT) {
      return 'assignment';
    }
  }

  public getSvgSrc(): string {
    if (this.assignment.assignment_not_assessable) {
      return chrome.runtime.getURL(`${IconsPath.SVG_WITHOUT_ASSESSMENT_CRITERIA}`);
    } else if (this.assignment.assignment_type === AssignmentType.STANDARD_ASSIGNMENT) {
      return '';
    } else if (this.assignment.assignment_type === AssignmentType.WORK_UNIT) {
      return chrome.runtime.getURL(`${IconsPath.SVG_WORK_UNIT}`);
    }
  }

  public getIconClass(): string {
    let iconClass: string;

    if (this.assignment.assignment_not_assessable) {
      iconClass = '';
    } else if (this.assignment.assignment_type === AssignmentType.WORK_UNIT) {
      iconClass = 'work-unit';
    } else if (this.assignment.assignment_type === AssignmentType.STANDARD_ASSIGNMENT) {
      iconClass = 'standard-assignment';
    }

    if (this.assignment.state === AssignmentState.DRAFT) {
      iconClass = `${iconClass} draft`;
    }

    return iconClass;
  }

}
