import React from 'react';
import { getLastModified } from '../utils';
import { fonts } from '../theme';
import { getTranslation } from '../i18n';

const InternalCommentsComponent = (props) => {
  const { comments } = props;
  let lastComment = getTranslation('noComments');
  let lastModified;
  if (comments && comments.length > 0) {
    lastComment = comments[comments.length - 1]?.comment;
    lastModified = getLastModified(comments[comments.length - 1]?.modified);
  }

  return (
    <div style={{ display: 'flex', marginTop: '30px' }}>
      <div style={{
        display: 'flex', flexDirection: 'column', minWidth: '160px', border: '0px solid', marginRight: '0px',
      }}>
        <span style={{ ...fonts.MEDIUM, whiteSpace: 'nowrap' }}>{getTranslation('internalComment')}</span>
        <span style={{ ...fonts.MEDIUM }}>{lastModified}</span>
      </div>
      <span style={fonts.MEDIUM}>{lastComment}</span>
    </div>

  );
};
export default InternalCommentsComponent;
