export const NOT_SUBMITTED = 'NOT_SUBMITTED';
export const NOT_SUBMITTED_LATE = 'NOT_SUBMITTED_LATE'

export const GlobalContext = React.createContext();

export const getFromLocalStorage = (identifier) => {
  if (process.env.REACT_APP_STANDALONE) {
    return localStorage.getItem(identifier);
  }
  return new Promise((resolve) => chrome.storage.sync.get(identifier, (result) => {
    resolve(result[identifier]);
  }));
};

export const setToLocalStorage = (identifier, data) => {
  if (process.env.REACT_APP_STANDALONE) {
    return localStorage.setItem(identifier, data);
  }
  const d = {};
  d[identifier] = data;
  return chrome.storage.sync.set(d);
};

const getToken = () => new Promise((resolve, reject) => {
  chrome.runtime.sendMessage({action: 'getToken'}, (token) => {
    if (!token) {
      reject();
    } else {
      resolve(token);
    }
  });
});

