const getSubmittedIcon = ({status, style = {height:'18px', width:'18px'}}) => {
  switch (status) {
    case SUBMITTED:
      return (<Icon file={submittedImage} style={style} overrideOpacity={1}/>);
    case SUBMITTED_LATE:
      return (<Icon file={submittedLateImage} style={style} overrideOpacity={1}/>);
    case NOT_SUBMITTED_LATE:
      return (<Icon file={notSubmittedLateImage} style={style} overrideOpacity={1}/>);
    case NOT_SUBMITTED:
      return (<Icon file={notSubmittedImage} style={style} overrideOpacity={0.4}/>);
    default:
      return (<Icon file={notSubmittedImage} style={style} overrideOpacity={0.4}/>);
  }
};
const submittedText = getTranslation('submitted');
const submittedLateText = getTranslation('submitted_late');
const notSubmittedText = getTranslation('not_submitted');
const notSubmittedLateText = getTranslation('not_submitted_late');
const getSubmittedStatusText = (state) => {
  switch (state) {
    case SUBMITTED:
      return submittedText;
    case SUBMITTED_LATE:
      return submittedLateText;
    case NOT_SUBMITTED:
      return notSubmittedText;
    case NOT_SUBMITTED_LATE:
      return notSubmittedLateText;
    default:
      return notSubmittedText;
  }
};

const getAssessmentIcon = ({status, warning = false, style = {height:'18px'}}) => {
  switch (status) {
    case NOT_ASSESSED:
      return <Icon file={notDoneImage} style={style} overrideOpacity={0.4}/>
    case ASSESSMENT_DONE:
      return (
        <div style={{position: 'relative'}}>
          <Icon file={doneImage} style={style} overrideOpacity={1}/>
          {warning && (
            <Icon file={warningImage} style={{position: 'absolute', left: '16px', bottom: '8px'}}/>
          )}
        </div>
      );
    case ASSESSING:
      return <Icon file={accessingImage} style={style} overrideOpacity={1}/>;
    default:
      return <Icon file={notDoneImage} style={style} overrideOpacity={0.4} />;
  }
};

const sortAssessmentsByStudentName = (ass, s) => ass
  .map((a) => ({
    ...a,
    firstName: a.student_name?.split(' ')[0] || a.first_name,
    lastName: a.student_name?.split(' ')[1] || a.last_name,
  }))
  .sort((a, b) => {
    if (s === 'firstName') {
      return a.firstName.localeCompare(b.firstName);
    }
    return a.lastName.localeCompare(b.lastName);
  });

const getLastModified = (lastModifiedDate) => {
  let outputDate;
  if (!lastModifiedDate) {
    // Empty string if date is equal to null or undefined
    outputDate = '';
  } else if (moment().isSame(lastModifiedDate, 'day')) {
    // Date equals today's date
    outputDate = moment(lastModifiedDate).format('k:mm');
  } else if (moment().isSame(lastModifiedDate, 'year')) {
    // Date equals the same year
    outputDate = moment(lastModifiedDate).format('D MMM');
  } else {
    // Previous year
    outputDate = moment(lastModifiedDate).format('D MMM YYYY');
  }

  return outputDate.toLocaleUpperCase();
};

const getStudentSortingMethod = async () => getFromLocalStorage('studentSort');
const setStudentSortingMethod = async (method) => setToLocalStorage('studentSort', method);

