const getStudentListFilterOptions = async () => {
  const options = getFromLocalStorage('studentFilterOptions');
  if (options && process.env.REACT_APP_STANDALONE) {
    return options.split(',');
  }
  return options;
};
const setStudentListFilterOptions = async (options) => setToLocalStorage('studentFilterOptions', options);

const getVisibilitySettingForAssignment = async (assignmentId) => {
  const setting = await getFromLocalStorage(`visibility_${assignmentId}`);
  if (setting && process.env.REACT_APP_STANDALONE) {
    return setting.split(',');
  }
  return setting;
};

const setVisibilitySettingForAssignment = (assignmentId, setting) => setToLocalStorage(`visibility_${assignmentId}`, setting);

const storeLastUsedGradeSystem = (scale) => setToLocalStorage('lastScale', scale);

const getLastUsedGradeSystem = () => {
  if (process.env.REACT_APP_STANDALONE) {
    return parseInt(getFromLocalStorage('lastScale'), 10);
  }
  return getFromLocalStorage('lastScale');
};

const getGradeSystemValues = (gradeSystem) => {
  if (!gradeSystem) {
    return [];
  }
  const gradeSystemValues = gradeSystem
    ? gradeSystem.grades.map((g, i) => ({
      value: i + 1,
      label: g.label,
      color: g.color,
      name: g.name,
      id: g.id,
    }))
    : [];
  gradeSystemValues.unshift({
    value: 0,
    label: '',
    color: colors.borderColor,
    name: '',
    id: 0,
  });
  return gradeSystemValues;
};

const getAssignmentIcon = (assignment) => {
  const style = {
    color: colors.white,
    backgroundColor: colors.green,
    padding: '8px',
    borderRadius: '30px',
    marginRight: '8px',
    height: '22px',
    width: '22px',
  };

  if (!assignment.connected) {
    if (assignment.is_topic) {
      return <Icon file={topicHiddenImage} style={{...style, backgroundColor: colors.blueGrey}} overrideOpacity={1}/>
    }
    return <Icon file={noCriteriaImage} style={{...style, backgroundColor: colors.blueGrey}} overrideOpacity={1}/>;
  }

  if (assignment.is_topic) {
    return (
      <div style={{...style, backgroundColor: colors.topic, alignItems:'center', justifyContent:'center', display:'flex'}}>
        <Icon file={topicImage} style={{width: '15px', height: '16px'}} overrideOpacity={1}/>
      </div>
    );
  }

  if (assignment.assignment_not_assessable) {
    return (
      <Icon file={noCriteriaImage} style={{...style, backgroundColor: colors.blueGrey}} overrideOpacity={1}/>
    );
  }

  if (assignment.assignment_type === 'WORK_UNIT') {
    return (
      <Icon file={workUnitImage} style={{...style, backgroundColor: colors.red}} overrideOpacity={1}/>
    );
  }

  switch (assignment.worktype) {
    case 'ASSIGNMENT':
      return (
        <Icon
          overrideOpacity={1}
          file={assignmentImage} style={{
          ...style,
          backgroundColor:
            assignment.state === 'PUBLISHED' ? colors.green : colors.draft,
        }}/>

      );
    case 'SHORT_ANSWER_QUESTION':
      return <LiveHelp style={style}/>;
    default:
      return (<Icon
        overrideOpacity={1}
        file={assignmentImage} style={{
        ...style,
        backgroundColor:
          assignment.state === 'PUBLISHED' ? colors.green : colors.draft,
      }}/>);
  }
};

const doneText = getTranslation('assessmentDoneStatusIconTooltip');
const acessingText = getTranslation('assessingStatusIconTooltip');
const notAssessedText = getTranslation('notAssessedStatusIconTooltip');

const getStatusText = (status) => {
  switch (status) {
    case ASSESSMENT_DONE:
      return doneText;
    case ASSESSING:
      return acessingText;
    case NOT_ASSESSED:
      return notAssessedText;
    default:
      return '';
  }
};

const filterAssignmentsOnCourseId = (assignments, courseId) => {
  let filtered = [...assignments];
  filtered.forEach((a) => {
    a.assignments = a.assignments?.filter((aa) => {
      const find = aa.courseDefinitions.find((c) => c.id === courseId);
      const b = aa.courseDefinitions.length === 0;
      return find || b;
    });
  });

  filtered = assignments.filter((a) => {
    const find = a.courseDefinitions.find((c) => c.id === courseId);
    const b = a.courseDefinitions.length === 0;
    const connected = find || b;
    if (!connected) {
      return a.assignments?.length > 0;
    }
    return connected;
  });
  return filtered;
};

const filterAssignmentsOnObjective = (assignments, objectiveId) => {
  let filtered = [...assignments];
  filtered.forEach((f) => {
    f.assignments = f?.assignments?.filter((a) => a.connected && a.objectiveAssessments?.find((o) => o.objective_id === objectiveId));
  });

  filtered = filtered?.filter((a) => {
    const connected = a?.objectiveAssessments?.find(
      (o) => o.objective_id === objectiveId,
    );
    return connected || a?.assignments?.length > 0;
  });
  return filtered;
};

const getPublishedStatus = (newStatus, originalStatus) => {
  switch (newStatus) {
    case PUBLISH_STATES.published:
      return true;
    case PUBLISH_STATES.notPublished:
      return false;
    case PUBLISH_STATES.dontChange:
      return originalStatus;
    default:
      return originalStatus;
  }
};

