import { IComponentController, IFormController, IPromise } from 'angular';
import { RawParams, StateService } from '@uirouter/core';
import get from 'lodash.get';
import { UserService } from '../../../../../../src/app/shared/states/user/user.service';
import { ICountry } from '../../../shared/models/country.model';
import { ISchool } from '../../../shared/models/school.model';
import { ISchoolRequestParams } from '../../../shared/models/schoolRequestParams.model';
import { IUserSetupResponse } from '../../../shared/models/userSetupResponse.model';
import { SchoolService } from '../../../shared/services/school/school.service';
import { UserSetupService } from '../../../shared/services/userSetup/userSetup.service';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { Mixpanel } from '../../../utils/services/mixpanel.service';
import UserSetupModule from '../../userSetup.module';
import style from './userSetupSchoolSelect.scss';
import template from './userSetupSchoolSelect.tpl.html';

@Component({
  module: UserSetupModule,
  selector: 'user-setup-school-select',
  template: template,
  style: style,
})
export class UserSetupSchoolSelectComponent implements IComponentController {
  @Input() public countries: ICountry[];
  @Input() public country: ICountry;
  @Input() public state: string;

  public school: ISchool;
  public schoolSelectForm: IFormController;
  public searchSchoolName: string;

  private schools: ISchool[];
  private requestPending: boolean;

  constructor(private $state: StateService, private SchoolService: SchoolService,
              private UserService: UserService,
              private UserSetupService: UserSetupService, private Mixpanel: Mixpanel) {
    'ngInject';

    this.countries = [];
    this.country = null;
    this.state = null;
    this.school = null;
    this.schoolSelectForm = null;
    this.searchSchoolName = null;
    this.schools = [];
    this.requestPending = false;
  }

  public $onInit(): void {
    if (!this.isSchoolInputDisable()) {
      this.refreshSchools();
    }
  }

  public registerSchool(): void {
    const params: RawParams = {
      country: this.country,
      state: this.state,
      backToState: this.$state.current.name,
    };

    this.$state.go('userSetup.setup.registerSchool', params);
  }

  public isSchoolInputDisable(): boolean {
    const hasStates: boolean = !!get(this, 'country.states', []).length;

    return !(hasStates ? get(this, 'state', false) : get(this, 'country', false));
  }

  public searchSchool(searchSchoolName: string): ISchool[] {
    return searchSchoolName ?
      this.SchoolService.filterSchools(this.schools, searchSchoolName) : this.schools;
  }

  public getSchoolName(school: ISchool): string {
    return this.SchoolService.getSchoolName(school);
  }

  public getCityName(school: ISchool): string {
    return this.SchoolService.getCityName(school);
  }

  public sendSaveRequest(): void {
    this.requestPending = true;

    this.UserSetupService.postUserSetup({ school: this.school.id })
      .then(this.userSetupRequestSuccess.bind(this))
      .finally((): boolean => this.requestPending = false);
  }

  public isSubmitDisabled(): boolean {
    return this.schoolSelectForm.$invalid || this.requestPending;
  }

  private refreshSchools(): void {
    const countryHasStates: boolean = !!get(this, 'country.states.length', false);
    const stateIsSelected: boolean = !!get(this, 'state', false);
    const requestParams: ISchoolRequestParams = {
      query: this.searchSchoolName,
      country_code: this.country.country_code,
    };
    let schoolsRequest: IPromise<ISchool[]> = Promise.resolve<ISchool[]>([]) as any;

    if (countryHasStates && stateIsSelected) {
      requestParams.state = get(this, 'state');
      schoolsRequest = this.SchoolService.getSchools(requestParams);
    } else if (!countryHasStates) {
      schoolsRequest = this.SchoolService.getSchools(requestParams);
    }

    schoolsRequest
      .then((schools: ISchool[]): ISchool[] => this.schools = schools)
      .catch((): ISchool[] => this.schools = []);
  }

  private clearStateInput(): void {
    this.state = null;
  }

  private clearSchoolInput(): void {
    this.school = null;
    this.searchSchoolName = null;
    this.schoolSelectForm.$setUntouched();
  }

  private userSetupRequestSuccess(userSetupResponse: IUserSetupResponse): void {
    this.UserService.updateUser(userSetupResponse);
    this.trackMixpanel(userSetupResponse.educational_system_setup);
    this.$state.go('userSetup.setup.complete');
  }

  private trackMixpanel(educationalSystemSetup: number): void {
    this.Mixpanel.track('setup_complete');
    this.Mixpanel.engage(
      'first_time_setup',
      this.country.country_code,
      educationalSystemSetup,
      this.school.id,
    );
  }
}
