import { Events } from '../../../../common/global-events.ts';
import { UnbindableCtrlBase } from '../../../../common/base-classes';
import { getTranslation } from '../../../../../../../react_src/i18n';

export class CoursesListComponent extends UnbindableCtrlBase {
  static bindings = { courses: '<' };

  static controller = CoursesListComponent;

  static templateUrl = 'components/course-progress/student-course-progress/coursesList/coursesList.html';

  constructor($rootScope, $state, FetchData, UserInfo, Path, BreadcrumbsService) {
    'ngInject';

    super();
    this.$rootScope = $rootScope;
    this.$state = $state;
    this.FetchData = FetchData;
    this.UserInfo = UserInfo;
    this.Path = Path;
    this.BreadcrumbsService = BreadcrumbsService;
    this.courses = [];
    this.selectedCourse = null;
    this.generalCourse = { id: -1, course_definition_name: getTranslation('allMyCourses') };
  }

  $onInit() {
    this.coursesMap = new Map();

    this.courses.forEach((course) => {
      this.coursesMap.set(course.id, course);
    });

    this.boundEvents.push(this.$rootScope.$on(
      Events.UPDATE_USER_STATUSES_FOR_COURSES_AND_OBJECTIVES,
      () => {
        this.FetchData
          .v3
          .getUserStatusesForCoursesAndObjectives(
            this.Path.getClassroomId(),
            this.UserInfo.userId,
          )
          .then((courses) => this.updateWarningsForCourses(courses));
      },
    ));

    this.boundEvents.push(this.$rootScope.$on(
      Events.CLOSE_COURSE_DETAILS_VIEW,
      () => (this.selectedCourse = null),
    ));

    this.boundEvents.push(this.$rootScope.$on(
      Events.SELECTED_COURSE_CHANGED,
      (event, selectedCourse) => (this.selectedCourse = selectedCourse),
    ));

    // emit by itself to check the status
    this.$rootScope.$emit(
      Events.UPDATE_USER_STATUSES_FOR_COURSES_AND_OBJECTIVES,
    );
  }

  updateWarningsForCourses(courses) {
    courses.forEach((course) => {
      if (this.coursesMap.has(course.id)) {
        this.coursesMap.get(course.id).has_warnings = course.has_warnings;
      }
    });
  }

  selectedNewCourse(selectedCourse) {
    this.selectedCourse = selectedCourse;

    if (selectedCourse.id === -1) {
      this.$state.go('student-course-progress.courses-overview', {
        coursesDefinitions: this.courses,
      });
    } else {
      this.$state.go('student-course-progress.courses-overview.learning-objectives', {
        course: selectedCourse,
        student: this.UserInfo,
        breadcrumbs: [],
      }, { reload: true });
    }
  }
}
