import angular, { IComponentController, IPromise, IRootScopeService } from 'angular';
import { StateService } from '@uirouter/core';
import { Events } from '../../../../common/global-events';
import { UnsubscribableComponent } from '../../../../shared/components/abstracts/unsubscribable.component';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import { ICurriculumsService } from '../../common/services/curriculums.service';
import style from '../../common/styles/course-details.scss';
import { showCreateObjectiveDialog, showEditCurriculumDialog, showSaveCourseToLibraryDialog } from '../shares/helpers.dialog';
import TeacherCourseManagementModule from '../teacherCourseManagement.module';
import template from './cmTeacherCourseDetails.tpl.html';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'cm-teacher-course-details',
  template: template,
  style: style,
  transclude: true,
})
export class CmTeacherCourseDetailsComponent extends UnsubscribableComponent implements IComponentController {
  @Input()
  public course: any;
  @Input()
  public hideToolbar: boolean;
  @Input({ optional: true })
  public curriculum: any;

  constructor(private $rootScope: IRootScopeService, private $state: StateService,
              private CurriculumsService: ICurriculumsService, private $mdDialog: angular.material.IDialogService,
              private FetchData: any, private Path: any, private Mixpanel: any) {
    'ngInject';

    super();
    this.course = null;
    this.hideToolbar = false;
    this.curriculum = null;
  }

  public $onInit(): void {
    this.$$unsubscribes.push(
      this.$rootScope.$on(Events.COURSE_UPDATED, (_: any, course: any): void => {
        this.course.name = course.name;
      }),
    );

    if (!this.curriculum) {
      this.curriculum = { name: this.course.name };
    }
  }

  public addNewObjective(): void {
    showCreateObjectiveDialog(this.$mdDialog)
      .then((objectiveName: string): IPromise<any> =>
        this.FetchData.v3.createObjective(this.course, { name: objectiveName }),
      )
      .then((): void => {
        this.$rootScope.$emit(Events.UPDATED_OBJECTIVES);
      });
  }

  public openSaveCourseToLibraryDialog(): IPromise<void> {
    return showSaveCourseToLibraryDialog(this.$mdDialog, Object.assign({}, this.course))
      .then((newCourse: any): IPromise<void> =>
        this.FetchData.v3.saveCourseToLibrary(this.course.id, newCourse.name, newCourse.code),
      )
      .then((): void => {
        this.Mixpanel.track('saving_curriculum_to_library');
      });
  }

  public openEditCourseDialog(): void {
    showEditCurriculumDialog(
      this.$mdDialog, this.FetchData, this.Path,
      angular.copy(this.course), this.course.educational_system,
      this.course.educational_stage, this.CurriculumsService.curriculums.length > 1,
    )
      .then((courseData: any): IPromise<void> => this.FetchData.v3.updateCourseDefinition(
        this.Path.getClassroomId(), Object.assign(courseData, { id: this.course.id }),
        )
          .then((courseUpdated: any): void => {
            this.$rootScope.$emit(Events.UPDATED_COURSES, courseUpdated);
            this.$rootScope.$emit(Events.UPDATED_OBJECTIVES, courseUpdated);
            Object.assign(this.course, courseUpdated);
          }),
      );
  }

  public update(course: any): void {
    this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
    this.FetchData.v3.updateCourseDefinition(this.Path.getClassroomId(), course)
      .finally((): void => {
        this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
      });
  }

  public closeCourseView(): void {
    this.$state.go('teacherCourseManagement', { course: null });
    this.$rootScope.$emit(Events.CLOSE_COURSE_DETAILS_VIEW);
  }
}
