import { IPromise } from 'angular';
import { AssignmentOrder } from '../../../assignments/teacherAssignments/shared/enums/assignmentOrder';
import { Service } from '../../../utils/decorators/service.decorator';
import { FetchData } from '../../../utils/services/fetchData.service';
import { UserInfo } from '../../../utils/services/userInfo.service';
import { AssignmentState } from '../../enums/assignmentStateType.enum';
import { AssignmentType } from '../../enums/assignmentType.enum';
import { RequestType } from '../../enums/requestType.enum';
import { IAssignment } from '../../models/assignment.model';
import { IAssignmentGroupedResponse } from '../../models/assignmentGroupedResponse.model';
import { IAssignmentRequestConfig } from '../../models/assignmentRequestConfig.model';
import { RequestParams } from '../../types/requestParams.type';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'AssignmentService',
})
export class AssignmentService {
  public static DEFAULT_REQUEST_CONFIG: IAssignmentRequestConfig = {
    ordering: AssignmentOrder.ASC_TYPE,
    assignment_state: [AssignmentState.PUBLISHED, AssignmentState.DRAFT],
  };

  constructor(private FetchData: FetchData, private UserInfo: UserInfo) {
    'ngInject';
  }

  public getAssignments(courseId: string, requestConfig: IAssignmentRequestConfig = AssignmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignment[]> {
    return this.fetchAssignments<IAssignment[]>(courseId, requestConfig);
  }

  public getAssignmentsGrouped(courseId: string, requestConfig: IAssignmentRequestConfig = AssignmentService.DEFAULT_REQUEST_CONFIG): IPromise<IAssignmentGroupedResponse> {
    return this.fetchAssignments<IAssignmentGroupedResponse>(courseId, requestConfig, true);
  }

  public updateAssignment(assignment: IAssignment): IPromise<IAssignment> {
    const url: string = `/v3/classes/assignment_details/${assignment.id}`;

    return this.FetchData.sendAPIRequest(url, RequestType.PATCH, assignment);
  }

  public filterAssignmentsByTypes(assignments: IAssignment[], types: AssignmentType[]): IAssignment[] {
    return assignments.filter((assignment: IAssignment): boolean =>
      assignment.assignment_not_assessable && types.includes(AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS) ||
      !assignment.assignment_not_assessable && types.includes(assignment.assignment_type),
    );
  }

  public filterAssignmentsByStates(assignments: IAssignment[], states: AssignmentState[]): IAssignment[] {
    return assignments.filter((assignments: IAssignment): boolean => states.includes(assignments.state));
  }

  private fetchAssignments<T>(courseId: string, requestConfig: IAssignmentRequestConfig = AssignmentService.DEFAULT_REQUEST_CONFIG, group: boolean = false): IPromise<T> {
    const _requestConfig: IAssignmentRequestConfig = Object
      .assign({}, AssignmentService.DEFAULT_REQUEST_CONFIG, requestConfig);
    const url: string = this.buildUrl(courseId);
    const params: RequestParams = this.buildParams(_requestConfig, group);

    return this.FetchData.sendAPIRequestWithParams(params, url, RequestType.GET);
  }

  private buildUrl(courseId: string): string {
    if (this.UserInfo.isTeacher()) {
      return `/v3/classes/${courseId}/assignments/`;
    } else {
      return `/classes/student_assignments/${courseId}/`;
    }
  }

  private buildParams(requestConfig: IAssignmentRequestConfig, group: boolean = false): RequestParams {
    return group ? Object.assign({ group_by_assessability: true }, requestConfig) : requestConfig;
  }
}
