import { Events } from '../../../../common/global-events.ts';

class controller {
  constructor($rootScope, FetchData, Path) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.FetchData = FetchData;
    this.Path = Path;
    this.objective = null;
    this.course = null;
    this.readOnly = false;
    this.objectiveName = '';
  }

  $onInit() {
    this.objectiveName = this.objective.name;
  }

  updateKnowledgeRequirementDescription(knowledgeRequirement) {
    this.update(knowledgeRequirement);
  }

  updateObjective() {
    this.update();
  }

  update(knowledgeRequirement = null) {
    if (!this.readOnly) {
      let request;
      this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);

      if (knowledgeRequirement) {
        const classroomId = this.Path.getClassroomId();

        request = this.FetchData.v3.updateKnowledgeRequirement(classroomId, knowledgeRequirement);
      } else {
        request = this.FetchData.v3.updateObjective(this.course, this.objective)
          .then((objective) => this.objectiveName = objective.name);
      }

      request
        .then(() => {
          this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
        })
        .catch(() => {
          this.$rootScope.$emit(Events.REQUEST_FINISHED_FAILURE);
        });
    }
  }

  onBlur() {
    if (!this.objective.name) {
      this.objective.name = this.objectiveName;
    }
  }
}

export const ObjectiveDetails = {
  bindings: {
    objective: '<',
    course: '<',
    readOnly: '<',
  },
  templateUrl: 'components/courseManagement/common/components/objective-details.html',
  controller,
};
