import React, {useContext, useEffect, useRef, useState} from "react";
import {ButtonBase, Checkbox, withStyles} from "@material-ui/core";
import {useWindowDimensions} from "../hooks";
import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import CheckboxWithText from "../components/CheckboxWithText";
import {
  getVisibilitySettingForAssignment,
  GlobalContext,
  setVisibilitySettingForAssignment,
} from "../utils";
import {ReactComponent as DoneIcon} from "../images/icon_bk_assessment_done.svg";
import PublishIcon from "../components/publishIcon";
import SecondaryButton from "../components/secondaryButton";
import PrimaryButton from "../components/primaryButton";

const GreenCheckbox = withStyles({
  root: {
    color: colors.borderColorDark,
    paddingLeft: 0,
    marginLeft: "-6px",
    "&$checked": {
      color: colors.green,
    },
    "& .MuiSvgIcon-root": {
      fontSize: "1.5rem",
    },
  },

  checked: {},
})(Checkbox);

const VisibilityDialog = (props) => {
  const {
    assignmentId,
    assessments,
    callback,
    hideSaveSetting = false,
  } = props;
  const context = useContext(GlobalContext);
  const {setVisibilityDialogData, Api} = context;
  const dimensions = useWindowDimensions();
  const [show, setShow] = useState(false);

  const selection = useRef([
    "result",
    "feedback",
    "assessment",
    "objectiveAssessment",
    "objectiveFeedback",
  ]);

  const save = async () => {
    const saveObj = assessments.map((a) => ({
      ...a,
      result_published: !!selection.current.find((s) => s === "result"),
      feedback_published: !!selection.current.find((s) => s === "feedback"),
      grade_published: !!selection.current.find((s) => s === "assessment"),
      objectiveAssessments: a.objectiveAssessments.map((oa) => ({
        ...oa,
        grade_published: !!selection.current.find(
          (s) => s === "objectiveAssessment"
        ),
        feedback_published: !!selection.current.find(
          (s) => s === "objectiveFeedback"
        ),
      })),
    }));

    await Api.updateAssignmentAssessment(saveObj);
    await Api.updateObjectiveAssessmentsGql(
      saveObj.map((a) => a.objectiveAssessments).flat()
    );
    setVisibilityDialogData(null);
    callback();
  };

  useEffect(() => {
    (async () => {
      const setting = await getVisibilitySettingForAssignment(assignmentId);
      if (setting && setting !== "") {
        selection.current = setting;
        save();
        setVisibilityDialogData(null);
      } else {
        setShow(true);
      }
    })();
  }, []);

  const onCheck = (checked, type) => {
    if (checked) {
      selection.current = [...selection.current, type];
    } else {
      selection.current = selection.current.filter((s) => s !== type);
    }
  };

  const useEyes = false;
  const attribute = (text, id, style = {}) => {
    if (useEyes) {
      return (
        <div style={{display: "flex", alignItems: "center", ...style}}>
          <PublishIcon
            style={{margin: "0px 10px 0px -4px"}}
            iconStyle={{width: "23px", height: "23px"}}
            published={selection.current.find((s) => s === id)}
            onClick={() => {
              const checked = selection.current.find((s) => s === id);
              onCheck(!checked, id);
            }}
          />
          <span style={fonts.MEDIUM}>{getTranslation(text)}</span>
        </div>
      );
    }
    return (
      <CheckboxWithText
        id={id}
        text={getTranslation(text)}
        onCheck={(checked) => {
          onCheck(checked, id);
        }}
        checked={selection.current.find((s) => s === id)}
        style={{...style, marginLeft: "4px"}}
      />
    );
  };

  if (!show) {
    return null;
  }

  const size = {width: 500, height: 450};
  return (
    <div
      style={{
        position: "absolute",
        left: -dimensions.width + 360,
        right: 0,
        bottom: 0,
        top: 0,
        backgroundColor: colors.dimmed,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        border: "0px solid red",
        zIndex: "1001",
      }}
    >
      <div
        style={{
          backgroundColor: colors.white,
          width: size.width,
          // height: size.height,
          border: "0px solid",
          borderRadius: "6px",
          // padding:'20px',
        }}
      >
        <div
          style={{
            padding: "20px",
            borderBottom: `1px solid ${colors.borderColor}`,
            display: "flex",
            flexDirection: "column",
          }}
        >
          <div style={{marginBottom: "10px"}}>
            <DoneIcon fill={colors.green} style={{}}/>
            <span
              style={{
                ...fonts.XLARGE,
                fontWeight: "500",
                marginLeft: "10px",
              }}
            >
              {getTranslation(
                "teacherAssignmentsAssessmentStudentVisibilityDialogTitle"
              )}
            </span>
          </div>
          <span style={{...fonts.LARGE}}>
            {getTranslation(
              "teacherAssignmentsAssessmentStudentVisibilityDialogHeader"
            )}
          </span>
        </div>
        <div
          style={{
            padding: "10px 20px 10px 20px",
          }}
        >
          <span style={{...fonts.MEDIUM}}>
            {getTranslation(
              "teacherAssignmentsAssessmentStudentVisibilityDialogAttributes"
            )}
          </span>
          {attribute(
            "teacherAssignmentsAssessmentStudentVisibilityDialogOptionResult",
            "result",
            {marginTop: "10px"}
          )}
          {attribute(
            "teacherAssignmentsAssessmentStudentVisibilityDialogOptionOverallFeedback",
            "feedback"
          )}

          {attribute(
            "teacherAssignmentsAssessmentStudentVisibilityDialogOptionAssignmentAssessment",
            "assessment"
          )}

          {attribute(
            "teacherAssignmentsAssessmentStudentVisibilityDialogOptionAssessment",
            "objectiveAssessment"
          )}

          {attribute(
            "teacherAssignmentsAssessmentStudentVisibilityDialogOptionFeedbackOnLearningObjectives",
            "objectiveFeedback"
          )}

          {!hideSaveSetting && (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                marginTop: "6px",
              }}
            >
              <span style={{...fonts.MEDIUM}}>
                {getTranslation(
                  "teacherAssignmentsAssessmentStudentVisibilityDialogSave"
                )}
              </span>
              <GreenCheckbox
                onChange={(e) => {
                  setVisibilitySettingForAssignment(
                    assignmentId,
                    e.target.checked ? selection.current : ""
                  );
                }}
              />
            </div>
          )}
        </div>
        <div
          style={{
            padding: "20px",
            display: "flex",
            justifyContent: "flex-end",
            // width: size.width,
          }}
        >
          <SecondaryButton
            title={getTranslation("teacherAssignmentsAssessmentStudentVisibilityDialogActionCancel")}
            onClick={() => {
              setVisibilityDialogData(null);
              callback();
            }}
          />
          <PrimaryButton
            inverted
            title={getTranslation("teacherAssignmentsAssessmentStudentVisibilityDialogActionGoAhead")}
            onClick={save}
          />
        </div>
      </div>
    </div>
  );
};

export default VisibilityDialog;
