export class SlideToggleAnimation {
  static className = 'slideToggleOpen';

  constructor() {
    'ngInject';
    this.addClass = this.addClass.bind(this);
    this.removeClass = this.removeClass.bind(this);
  }

  static create() {
    return new SlideToggleAnimation();
  }

  addClass(element, className, done) {
    if (className === SlideToggleAnimation.className) {
      setTimeout(() => {
        element.stop().slideDown(0, done);
      }, 0);
    }
    return this.animationFinished.bind({ element });
  }

  removeClass(element, className, done) {
    if (className === SlideToggleAnimation.className) {
      element.stop().slideUp(0, done);
    }

    return this.animationFinished.bind({ element });
  }

  animationFinished() {
    if (this.element && this.element.stop) {
      this.element.stop();
    }
  }
}
