import angular, { IComponentController, IFormController, INgModelController, IQService, IScope } from 'angular';
import { IColleagueEmail } from '../../../../../shared/models/colleagueEmail.model';
import { IColleagueEmailsService } from '../../../../../shared/services/colleagueEmails/colleagueEmails.service';
import { IUnsubscribe, Observer } from '../../../../../shared/services/helpers/observer.service';

export class ShareAssessmentsWithColleaguesComponent {
  static controller: IComponentController = ShareAssessmentsWithColleaguesComponent;
  static templateUrl: string =
    'components/course-progress/common/components/shareAssessmentsWithColleagues/shareAssessmentsWithColleagues.html';

  public shareAssessmentWithColleaguesForm: IFormController;
  public email: string;
  public colleaguesEmails: IColleagueEmail[];
  public readonly emailPattern: string;
  private readonly unsubscribe: IUnsubscribe;

  constructor(private $scope: IScope, private $q: IQService, private $mdDialog: angular.material.IDialogService,
              private ColleagueEmailsService: IColleagueEmailsService) {
    'ngInject';

    this.emailPattern = '[a-z0-9!#$%&\'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&\'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?';
    this.unsubscribe = this.ColleagueEmailsService.colleagueEmails$
      .subscribe(new Observer<IColleagueEmail[]>(this.next.bind(this)));
  }

  public $onInit(): void {
    if (!this.ColleagueEmailsService.colleagueEmails$.value.length) {
      this.ColleagueEmailsService.get();
    }

    this.$scope.$on('$destroy', () => this.unsubscribe);
  }

  public $postLink(): void {
    const emailModel: INgModelController = this.shareAssessmentWithColleaguesForm.email;

    emailModel.$validators.unique = (modelValue: any): boolean => {
      let isValid: boolean = true;

      if (this.ColleagueEmailsService.isEmailValid(modelValue)) {
        isValid = this.ColleagueEmailsService.isUnique(modelValue);
      }

      return isValid;
    };
  }

  public addColleagueEmail(): void {
    if (this.isFormValid()) {
      this.ColleagueEmailsService.add(this.email)
        .then(
          (): void => {
            this.email = '';
          },
          (): void => {
            this.shareAssessmentWithColleaguesForm.email.$setValidity('unique', false);
          });
    }
  }

  public removeColleagueEmail(colleagueEmail: IColleagueEmail): void {
    this.ColleagueEmailsService.remove(colleagueEmail);
  }

  public isFormValid(): boolean {
    return this.shareAssessmentWithColleaguesForm.$valid &&
      !this.shareAssessmentWithColleaguesForm.$pristine &&
      !!(this.shareAssessmentWithColleaguesForm.email as INgModelController).$viewValue;
  }

  public onKeyUp($event: chrome.input.ime.KeyboardEvent) {
    if ($event.keyCode === 13) {
      this.addColleagueEmail();
    }
  }

  private next(colleaguesEmails: IColleagueEmail[]): void {
    this.colleaguesEmails = colleaguesEmails;

    if (this.shareAssessmentWithColleaguesForm) {
      (this.shareAssessmentWithColleaguesForm.email as INgModelController).$validate();
    }
  }
}
