import { IPromise, IQService, ITimeoutService } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import SharedServicesModule from '../services.module';
import get from 'lodash.get';

@Service({
  module: SharedServicesModule,
  name: 'RetryPromiseService',
})
export class RetryPromiseService {
  constructor(private $q: IQService, private $timeout: ITimeoutService) {
    'ngInject';
  }

  public times<T = any>(promise: () => IPromise<T>, retries: number = 2, interval: number = 1500): IPromise<T> {
    const isAttempt: boolean = this.isAttempt(promise);

    return promise()
      .then((response: T): T => response)
      .catch((): IPromise<T> => {
        if (retries) {
          const delay: number = isAttempt ? interval : 0;

          this.setAttempted(promise, true);

          return this.$timeout<T>((): IPromise<T> => this.times(promise, retries -1, interval), delay);
        }
      });
  }

  private setAttempted<T>(promise: () => IPromise<T>, attempted: boolean): void {
    (promise as any).$$attempted = attempted;
  }

  private isAttempt<T>(promise: () => IPromise<T>): boolean {
    return get(promise, '$$attempted', false);
  }
}
