/**
 * @module States/Service
 * @description
 *   Contain funtions for state opening.
 * @param {Object} UserInfo User Info service instance
 * @param {Object} NavigationHistory Navigation History service instance
 * @param {Object} MainMenu Main Menu service instance
 * @param {Object} StudybeeHandle Studybee Handle service instance
 */

/*@ngInject*/
function States(UserInfo, NavigationHistory, MainMenu, Assignments, Path) {
  return {
    /**
     * Open Drive State and open extension
     * @memberof module:States/Service
     */
    openDriveState() {
      if (UserInfo.isTeacher()) {
        NavigationHistory.go('teacher_google_drive');
      } else {
        NavigationHistory.go('studentGoogleDrive');
      }
    },

    /**
     * Open Assignment State, open extension and close bottom sheet
     * @memberof module:States/Service
     */
    openAssignmentState() {
      const assignment = Assignments.getAssignmentByAlternateLink(
        Path.getAssignmentAlternateLinkId(),
      );
      if (UserInfo.userRole === 'teacher') {
        NavigationHistory.go('teacher_assess_assignment', {
          assignment, isModal: false,
        });
      } else if (UserInfo.userRole === 'student') {
        NavigationHistory.go('studentAssessed', {
          isModal: false, assignment: undefined,
        });
      }
    },

    /**
     * Open Default State, open extension and close bottom sheet
     * @memberof module:States/Service
     */
    openDefaultState() {
      const defaultStateForTeacher = 'teacherAssignments';
      const defaultStateForStudent = 'studentAssignments';

      if (MainMenu.selectedMenuStateName) {
        NavigationHistory.go(MainMenu.selectedMenuStateName);
        return;
      }

      if (UserInfo.userRole === 'teacher') {
        NavigationHistory.go(defaultStateForTeacher);
      } else if (UserInfo.userRole === 'student') {
        NavigationHistory.go(defaultStateForStudent);
      }
    },

    /**
     * Open Not Configured State, open extension and close bottom sheet
     * @memberof module:States/Service
     */
    openNotConfiguredState() {
      const notConfiguredStateForTeacher = 'teacherCourseManagement';
      const notConfiguredStateForStudent = 'not_configured';

      if (UserInfo.userRole === 'teacher') {
        MainMenu.setSelectedMenuStateName(notConfiguredStateForTeacher);
        NavigationHistory.go(notConfiguredStateForTeacher);
      } else if (UserInfo.userRole === 'student') {
        MainMenu.setSelectedMenuStateName(notConfiguredStateForStudent);
        NavigationHistory.go(notConfiguredStateForStudent);
      }
    },

    /**
     * Open Loading State, close extension and close bottom sheet
     * @memberof module:States/Service
     */
    openLoadingState() {
      NavigationHistory.go('loading', {}, true);
    },
  };
}

module.exports = States;
