import { Events } from '../../../common/global-events.ts';
import { DialogType } from '../../../utils/enums/dialogType.enum';
import { ExportState } from '../../../utils/enums/exportState.enum';

export class TeacherCourseProgress {

  constructor($rootScope, $state, $timeout, FetchData, CourseProgress, ExportStudentsResults,
    ExportStudentsResultsDialog, Path, ShareAssessmentsWithColleaguesDialog) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
    this.$timeout = $timeout;
    this.FetchData = FetchData;
    this.CourseProgress = CourseProgress;
    this.ExportStudentsResults = ExportStudentsResults;
    this.ExportStudentsResultsDialog = ExportStudentsResultsDialog;
    this.ShareAssessmentsWithColleaguesDialog = ShareAssessmentsWithColleaguesDialog;
    this.Path = Path;
  }

  addListenerUpdateUserStatusesForCoursesAndObjectives(callback) {
    return this.$rootScope.$on(
      Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES,
      callback,
    );
  }

  updateWarningsForStudents(courseId, studentsMap) {
    return this.fetchStudentsWarnings(courseId)
      .then((studentsWarnings) => {
        studentsWarnings.forEach((student) => {
          if (studentsMap.has(student.id)) {
            studentsMap.get(student.id).has_warnings = student.has_warnings;
          }
        });

        return studentsMap;
      });
  }

  fetchStudentsWarnings(courseId) {
    return this.FetchData.v3.getUsersStatusesForCoursesAndObjectives(courseId);
  }

  exportStudentsResultsToGoogleDrive(fileType, courseId) {
    this.ExportStudentsResults.toGoogleDrive(fileType, courseId)
      .then((response) => {
        this.ExportStudentsResultsDialog
          .show({ link: response.folder_link }, DialogType.CONFIRMATION);

        this.checkExportStatus(response.folder_link, response.task_id);
      })
      .catch(() => {
        this.ExportStudentsResultsDialog.show({}, DialogType.FAIL);
      });
  }

  checkExportStatus(link, exportId) {
    this.ExportStudentsResults.checkGoogleDriveExportStatus(
      exportId,
      (response) => {
        this.$timeout(() => {
          if (response.state === ExportState.SUCCESS) {
            this.ExportStudentsResultsDialog.show({ link }, DialogType.SUCCESS);
          } else {
            this.ExportStudentsResultsDialog.show({}, DialogType.FAIL);
          }
        });
      });
  }

  convertStudentsArrayToMap(students) {
    return students
      .reduce((studentsMap, student) => studentsMap.set(student.id, student), new Map());
  }

  navigateToNoStudentsSelected() {
    this.$state.go('teacher-course-progress');
  }

  navigateToLasStateSaved() {
    this.$state.go(
      this.CourseProgress.lastStateSaved.name,
      this.CourseProgress.lastStateSaved.params,
    );
  }

  isLastStateSavedExists() {
    return this.CourseProgress.hasLastStateSaved;
  }

  getCurrentCourseId() {
    return this.Path.getClassroomId();
  }

  shareAssessmentsWithColleagues() {
    this.ShareAssessmentsWithColleaguesDialog.show();
  }

  reloadCurrentView() {
    this.$state.reload();
  }
}
