import angular, { IPromise } from 'angular';
import { CourseProgressDialog } from '../courseProgressDialog.service';

export class StudentDetailsEditDialog extends CourseProgressDialog {
  constructor($mdDialog: ng.material.IDialogService) {
    'ngInject';

    super($mdDialog);
  }

  show(locals): IPromise<any> {
    const templateUrl: string =
      `${CourseProgressDialog.TEMPLATE_PATH}/studentDetailsEdit/studentDetailsEditDialog.html`;

    return super.show({
      locals,
      templateUrl,
    });
  }
}
