import {Component} from '../../../utils/decorators/component.decorator';
import {IComponentController} from 'angular';
import {Input} from '../../../utils/decorators/input.decorator';
import {School} from '../../../../../../src/app/shared/states/schools/school.interface';
import {SchoolService} from '../../services/school/school.service';
import style from './autocomplete.scss';
import template from './autocomplete.tpl.html';
import SharedUpgradesModule from '../upgrades.module';

@Component({
  module: SharedUpgradesModule,
  selector: 'ajs-autocomplete',
  style,
  template,
})
export class AutocompleteComponent implements IComponentController {
  @Input() school: School;
  @Input() schools: School[];
  @Input() disabled: boolean;
  @Input() change: Function;
  searchSchoolName: string;

  constructor(protected SchoolService: SchoolService) {
    'ngInject';
  }


  public searchSchool(searchSchoolName: string): School[] {
    return (searchSchoolName ?
      this.SchoolService.filterSchools(this.schools, searchSchoolName) : this.schools) as School[];
  }

  public getSchoolName(school: School): string {
    return this.SchoolService.getSchoolName(school);
  }

  public getCityName(school: School): string {
    return this.SchoolService.getCityName(school);
  }
}
