import React, {memo} from 'react';
import feedbackImage from '../../images/png/sb-ico-feedback.png';
import noteImage from '../../images/png/sb-ico-note.png';
import {colors, fonts} from '../../theme';
import {getTranslation} from '../../i18n';
import Tooltip from "../../components/tooltip";
import Icon from "../../components/icon";
import PublishedIcon from "../../components/publishedIcon";

const styles = {
  container: {
    display: 'flex',
    minWidth: '100px',
    alignItems: 'center',
    justifyContent: 'space-around',
  },
  item: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'column',
  },
  feedbackIcon: {height: '18px', width: '18px'},
  privateNoteIcon: {height: '20px', width: '20px', marginBottom: '10px'},
};

const tt1 = getTranslation('courseGradeInput');
const tt2 = getTranslation('courseProgressTargetGoalAvailableTooltip');
const tt3 = getTranslation('courseFeedbackInput');
const tt4 = getTranslation('coursePrivateNotesInput');

const CourseOverviewComponent = (props) => {
  const {courseAssessment} = props;
  const gradeSystem = courseAssessment?.gradeSystem.find((g) => g.is_final);
  const targetGrade = gradeSystem?.grades.find(
    (gr) => gr.id === courseAssessment?.target_grade_id,
  );

  return (
    <div style={styles.container}>
      <Tooltip title={tt1}>
        <div style={styles.item}>
          <span
            style={{
              ...fonts.LARGE,
              color: courseAssessment?.gradeColor || colors.iconColorLight,
              fontWeight: '800',
            }}
          >
            {courseAssessment?.gradeName || '-'}
          </span>
          <PublishedIcon published={courseAssessment.grade_published} active={!!courseAssessment?.gradeName}/>
        </div>
      </Tooltip>
      <Tooltip title={tt2}>
        <div style={styles.item}>
          <span
            style={{
              ...fonts.LARGE,
              fontWeight: '800',
              color: targetGrade?.label
                ? colors.brandColorBlue
                : colors.iconColorLight,
            }}
          >
            {targetGrade?.label || '-'}
          </span>
          <PublishedIcon published={courseAssessment?.target_grade_published} active={!!targetGrade?.label}/>
        </div>
      </Tooltip>
      <Tooltip title={tt3}>
        <div style={styles.item}>
          <Icon file={feedbackImage} active={courseAssessment?.feedback} style={styles.feedbackIcon}/>
          <PublishedIcon published={courseAssessment?.feedback_published} active={!!courseAssessment?.feedback}/>
        </div>
      </Tooltip>
      <Tooltip title={tt4}>
        <div>
          <Icon file={noteImage} active={!!courseAssessment?.private_note} style={styles.privateNoteIcon}/>
        </div>
      </Tooltip>
    </div>
  );
};
export default memo(CourseOverviewComponent);
