import React, {useContext, useEffect, useMemo, useReducer, useRef} from 'react';
import debounce from "lodash.debounce";
import Card from "../components/card";
import AssessmentSlider from "../components/assessmentSlider";
import {getGradeSystemValues, getLastModified, GlobalContext} from "../utils";
import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import FeedbackInput from "../components/feedbackInput";

const emptyAssessment = (assessment) => ({
  ...assessment,
  feedback: null,
  feedback_last_edit: null,
  feedback_published: false,
  gradeColor: null,
  gradeIsPassed: null,
  gradeName: null,
  grade_id: null,
  grade_last_edit: null,
  grade_published: false,
  private_note: null,
  private_note_last_edit: null,
  target_grade_id: null,
  target_grade_last_edit: null,
  target_grade_published: false,
});


const CourseAssessmentCard = (props) => {
  const {assessment, onSave, studentId, multiEdit} = props;
  const context = useContext(GlobalContext);
  const {Api} = context;
  const currentAssessment = useRef(assessment);
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  useEffect(() => {
    currentAssessment.current = multiEdit ? emptyAssessment(assessment) : assessment;
    forceUpdate();
  }, [assessment, studentId]);

  useEffect(() => {
    currentAssessment.current = multiEdit
      ? emptyAssessment(assessment)
      : assessment;
    forceUpdate();
  }, [multiEdit]);

  const saveCourseAssessment = useMemo(() => debounce(async (Api, assessment, onSave) => {
    await Api.updateCourseAssessments([assessment]);
    if (onSave) {
      onSave();
    }
  }, 1000), [assessment?.id]);

  const gradesSystemValues = getGradeSystemValues(
    currentAssessment.current?.gradeSystem?.find((g) => g.is_final)
  );

  const defaultIndex = gradesSystemValues.findIndex(
    (g) => g.id === currentAssessment.current.grade_id
  );

  const defaultIndexTarget = gradesSystemValues.findIndex(
    (g) => g.id === currentAssessment.current.target_grade_id
  );

  const save = () => {
    if (!multiEdit) {
      saveCourseAssessment(Api, currentAssessment.current, onSave);
    } else {
      onSave(currentAssessment.current);
    }
  };

  return (
    <div>
      <span
        style={{
          ...fonts.LARGE,
          color: colors.brandColorPink,
          marginLeft: "16px",
        }}
      >
        {getTranslation("overallResultHeaderTeacher")}
      </span>
      <Card style={{marginTop: "16px"}}>
        <span style={{...fonts.MEDIUM}}>
          {getTranslation("courseGradeInput")}
        </span>
        <AssessmentSlider
          id={studentId}
          values={gradesSystemValues}
          defaultIndex={defaultIndex < 0 ? 0 : defaultIndex}
          onChange={(index) => {
            currentAssessment.current.grade_id = gradesSystemValues[index].id;
          }}
          published={currentAssessment?.current?.grade_published}
          onPublish={(state) => {
            currentAssessment.current.grade_published = state;
            save();
          }}
          helpText={getLastModified(currentAssessment.current.grade_last_edit)}
          onChangeCommit={() => {
            save();
          }}
          multiEdit={multiEdit}
        />
        <span style={{...fonts.MEDIUM}}>
          {getTranslation("courseProgressTargetGradeLabel")}
        </span>
        <AssessmentSlider
          id={studentId}
          values={gradesSystemValues}
          defaultIndex={defaultIndexTarget < 0 ? 0 : defaultIndexTarget}
          onChange={(index) => {
            currentAssessment.current.target_grade_id =
              gradesSystemValues[index].id;
          }}
          published={currentAssessment.current.target_grade_published}
          onPublish={(state) => {
            currentAssessment.current.target_grade_published = state;
            save();
          }}
          helpText={getLastModified(currentAssessment.current.grade_last_edit)}
          onChangeCommit={() => {
            save();
          }}
          color={colors.brandColorBlue}
          multiEdit={multiEdit}
        />

        <FeedbackInput
          text={currentAssessment.current.feedback}
          placeholder={getTranslation("courseFeedbackInput")}
          onTextChange={(text) => {
            currentAssessment.current.feedback = text;
            save();
          }}
          published={currentAssessment.current.feedback_published}
          onPublish={(id, value) => {
            currentAssessment.current.feedback_published = value;
            save();
          }}
          id={currentAssessment.current.id}
          assessment={currentAssessment.current}
          helperText={getLastModified(
            currentAssessment.current.feedback_last_edit
          )}
          warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
          multiEdit={multiEdit}
        />
        <FeedbackInput
          text={currentAssessment.current.private_note}
          placeholder={getTranslation("coursePrivateNotesInput")}
          onTextChange={(text) => {
            currentAssessment.current.private_note = text;
            save();
          }}
          id={currentAssessment.current.id}
          assessment={currentAssessment.current}
          helperText={getLastModified(
            currentAssessment.current.private_note_last_edit
          )}
          hideEye
          warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
          multiEdit={multiEdit}
        />
      </Card>
    </div>
  );
};

export default CourseAssessmentCard;
