import { IPromise } from 'angular';
import { StateProvider } from '@uirouter/angularjs';

export const TeacherSetAssignmentObjectivesRouting: Function = ($stateProvider: StateProvider): void => {
  'ngInject';

  $stateProvider
    .state('teacher_set_assignment_objectives', {
      role: 'teacher',
      templateUrl: 'assignments/teacherSetAssignmentObjectives/views/main.html',
      themeClass: 'sb-theme-green',
      controller: 'teacherSetAssignmentObjectivesCtrl',
      controllerAs: 'vm',
      params: {
        hideExtensionOnAssign: null,
        assignment: null,
        firstTime: false,
      },
      resolve: {
        assignmentInformation($stateParams, Path, FetchData): IPromise<any> {
          'ngInject';

          return FetchData.getAssignedObjectivesToAssignment($stateParams.assignment.id).then(response => {
            return response;
          });
        },
        gradeSystems($stateParams, Path, FetchData): IPromise<any> {
          'ngInject';

          return FetchData.v2.getGradeSystems(Path.getClassroomId(), $stateParams.assignment.id);
        },
      },
    } as any);
};
