import React, { useState, useContext, useEffect } from 'react';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import AssignmentList from './topics_and_assignments/assignmentsList';
import { createApi } from './api';
import NetworkErrorToast from './components/networkErrorToast';
import SaveToast from './components/saveToast';
import { colors } from './theme';
import {
  getAssignmentSortingMethod,
  getFromLocalStorage,
  getStudentSortingMethod,
  GlobalContext, setAssignmentSortingMethod,
  setStudentSortingMethod, setToLocalStorage,
} from './utils';
import { useWindowDimensions } from './hooks';
import ObjectiveDescription from './components/objectiveDescription';
import CourseOverview from './course-overview';
import { ReactComponent as StudyBeeHandle } from './images/handlebg.svg';
import ErrorBoundary from './errorBoundry';
import VisibilityDialog from './topics_and_assignments/visibilityDialog';
import Drawer from './components/drawer';
import { getClassroomId } from './path';
import SyncToast from './components/syncToast';
import { getTranslation } from './i18n';
import NewAssignmentDialog from "./newAssignmentDialog";

const App = (props) => {
  const {
    data, ctrl, view = 'assignments', student = false,
  } = props;


  if(view === 'newsAssignmentPopup'){
    return <NewAssignmentDialog/>;
  }

  const {
    NavigationHistory: navigation,
    AssessmentVisibilityDialogService: ChangeVisibilityDialog,
    AssessmentsVisibilityDialogService: ChangeAssessmentsDialog,
    GroupedVisibilityDialogService: ChangeVisibilityManyDialog,
    StudybeeHandle: handle,
  } = ctrl;

  const { studybeeClose, studybeeOpen } = handle;

  const { toggleMenu } = data;
  return (
    <GlobalContext.Provider
      value={{
        navigation,
        toggleMenu,
        ChangeVisibilityDialog, // Change visibility after assess set to ASSESSMENT_DONE
        ChangeVisibilityManyDialog, // Change visibility for many students
        ChangeAssessmentsDialog, // Change assessment for many students, what happens to visibility?
        studybeeClose,
        studybeeOpen,
      }}
    >
      <ReactApp view={view} student={student}/>
    </GlobalContext.Provider>
  );
};

const theme = createMuiTheme({
  palette: {
    primary: {
      main: colors.green,
    },
    secondary: {
      main: colors.primary,
    },
  },
});

let context;
const ReactApp = (props) => {
  const { view, student } = props;
  context = useContext(GlobalContext);
  const [networkTraffic, setNetworkTraffic] = useState(false);
  const [networkError, setNetworkError] = useState(false);
  const [showObjectiveDetails, setShowObjectiveDetails] = useState();
  const dimensions = useWindowDimensions();
  const [currentView, setCurrentView] = useState(view);
  const [sortingMethod, setSortingMethod] = useState('firstName');
  const [assignmentSort, setAssignmentSort] = useState(false);
  const [visibilityDialogData, setVisibilityDialogData] = useState();
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [user, setUser] = useState();
  const [syncText, setSyncText] = useState();

  context.setNetworkTraffic = setNetworkTraffic;
  context.setNetworkError = setNetworkError;
  context.height = showObjectiveDetails
    ? dimensions.height - 300
    : dimensions.height;

  const Api = createApi(
    (m) => context.setNetworkTraffic(m),
    (e) => context.setNetworkError(e),
  );

  context.openDrawer = () => {
    setDrawerOpen(true);
  };
  context.networkTraffic = networkTraffic;
  context.Api = Api;
  context.showObjectiveDetails = setShowObjectiveDetails;
  context.isStudent = student;
  context.sortingMethod = sortingMethod;
  context.assignmentSortMethod = assignmentSort;
  context.setAssignmentSortMethod = (method) => {
    setAssignmentSortingMethod(method);
    setAssignmentSort(method);
  };
  context.user = user;
  context.setVisibilityDialogData = setVisibilityDialogData;
  context.setSortingMethod = (method) => {
    setStudentSortingMethod(method);
    setSortingMethod(method);
  };

  useEffect(() => {
    setToLocalStorage('currentView', currentView);
  }, [currentView]);

  const changeToAssignmentView = [
    'connectNewAssignment',
    'assignmentDriveSelected',
    'studentSelected',
    'assignmentSelected',
  ];
  const storageListener = async (change) => {
    if (Object.keys(change).find((k) => changeToAssignmentView.find((c) => c === k))) {
      setCurrentView('assignments');
      const v = await getFromLocalStorage('currentView');
      if (v !== 'assignments') {
        context.navigation.transitionTo('teacherAssignments', {});
      }
    }
  };

  const listenForChanges = () => {
    if (!process.env.REACT_APP_STANDALONE) {
      chrome.storage.onChanged.addListener(storageListener);
    }
  };

  useEffect(() => {
    if (!student) {
      listenForChanges();
    }
    (async () => {
      const method = await getStudentSortingMethod();
      setSortingMethod(method);
      const aMethod = await getAssignmentSortingMethod();
      setAssignmentSort(aMethod);
      const u = await context.Api.getUserInfo();
      setUser(u?.data?.userInfo);
    })();
  }, []);

  const navigate = (newView) => {
    if (newView === 'courseRequirements') {
      context.navigation.go(student ? 'studentCourseManagement' : 'teacherCourseManagement', { course: getClassroomId() });
      setCurrentView('courseManagement');
    } else if (newView === 'courseOverview' && student) {
      context.navigation.go('student-course-progress', { course: getClassroomId() });
      setCurrentView(newView);
    } else {
      setCurrentView(newView);
    }
  };

  return (
    <ErrorBoundary Api={Api}>
      <ThemeProvider theme={theme}>
        <div style={{ border: '0px solid', overflow: 'hidden', height: '100%' }}>
          <NetworkErrorToast networkError={networkError}/>
          <SaveToast networkTraffic={networkTraffic}/>
          <SyncToast text={syncText} setText={setSyncText}/>
          {currentView === 'assignments' && <AssignmentList/>}
          {currentView === 'courseOverview' && <CourseOverview/>}
          {showObjectiveDetails && (<ObjectiveDescription objectiveId={showObjectiveDetails}/>)}
          {visibilityDialogData && <VisibilityDialog {...visibilityDialogData} />}
          {!process.env.REACT_APP_STANDALONE && (
            <div
              style={{
                position: 'absolute',
                left: '-24px',
                top: '50%',
                marginTop: '-25px',
              }}
            >
              <StudyBeeHandle
                style={{
                  height: '52px',
                }}
              />
              <div
                style={{
                  display: 'flex',
                  position: 'absolute',
                  top: 0,
                  left: 1,
                  right: 0,
                  bottom: 8,
                  alignItems: 'center',
                  justifyContent: 'content',
                  cursor: 'pointer',
                }}
                onClick={context.studybeeClose}
              >
                <img
                  src={`chrome-extension://${chrome.runtime.id}/images/icon-128.png`}
                  style={{ width: '25px', height: '25px' }}
                />
              </div>
            </div>
          )}
          {drawerOpen
          && <Drawer
            student={student}
            onNavigate={navigate}
            selected={currentView}
            onRefresh={async () => {
              await Api.syncAssignments();
              await Api.syncStudents();
              setSyncText(getTranslation('assignmentsAndStudentsUpdatedToast'));
              const v = currentView;
              setCurrentView(null);
              setTimeout(() => {
                setCurrentView(v);
              }, 1);
            }}
            close={() => {
              setDrawerOpen(false);
            }}/>}
        </div>
      </ThemeProvider>
    </ErrorBoundary>
  );
};

export default App;
