/**
 * @module Hint/Service
 * @description
 *   Handle Hint.
 */

/*@ngInject*/
function Hint() {
  var displayToggleCallbacks = [];
  var removeCallbacks = [];

  return {
    /**
     * Set callback which is trigger on remove hint
     * @memberof module:Hint/Service
     * @param {Object} callback Callback function
     */
    setRemoveCallback: function (callback) {
      removeCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger on display toggle hint
     * @memberof module:Hint/Service
     * @param {Object} callback Callback function
     */
    setDisplayToggleCallbacks: function (callback) {
      displayToggleCallbacks.push(callback);
    },

    /**
     * Remove hint
     * @memberof module:Hint/Service
     */
    removeHint: function () {
      removeCallbacks.forEach(function (elem) {
        elem();
      });

      removeCallbacks = [];
    },

    /**
     * Display toggle hint
     * @memberof module:Hint/Service
     */
    displayToggleHint: function () {
      displayToggleCallbacks.forEach(function (elem) {
        elem();
      });
    }
  };
}

module.exports = Hint;
