import React, {useEffect, useState, useContext} from 'react';
import Toast from './toast';
import {GlobalContext} from '../utils';
import {getTranslation} from '../i18n';
import {LOG_LEVEL} from '../api';

const NetworkErrorToast = (props) => {
  const {networkError} = props;
  const context = useContext(GlobalContext);
  const {setNetworkError, Api} = context;
  const [open, setOpen] = useState(networkError);

  if (networkError) {
    console.log('network error: ', networkError);
    // Api.cloudLog(LOG_LEVEL.ERROR, networkError.toString());
  }

  useEffect(() => {
    if (networkError) {
      setOpen(true);
    }
  }, [networkError]);

  return (
    <Toast
      timeout={5000}
      onClose={() => {
        setOpen(false);
        setNetworkError(null);
      }}
      open={open}
      message={getTranslation('errorToast')}/>
  );
};

export default NetworkErrorToast;
