/**
 * Get an entire path
 * @memberof module:Path/Service
 * @return {String}
 */
import { Observable, throwError } from 'rxjs';
import {
  concatMap, retryWhen, take, delay,
} from 'rxjs/operators';

export const getPath = () => window.location.pathname;

/**
 * Get path array
 * @memberof module:Path/Service
 * @return {Array}
 */
export const getPathArray = () => getPath().split('/');

/**
 * Get course ID
 * @memberof module:Path/Service
 * @return {Number}
 */
export const getClassroomId = () => {
  const result = /[\da-zA-Z]{10,}/g.exec(getPath());

  return result ? result[0] : null;
};

/**
 * Get assignment ID
 * @memberof module:Path/Service
 * @return {Number}
 */
export const getAssignmentId = () => {
  const pathArray = this.getPathArray();
  const assignmentId = pathArray[pathArray.indexOf('a') + 1];
  // if (!assignmentId && MainMenu.selectedAssignment) {
  //   assignmentId = MainMenu.selectedAssignment.id;
  // }
  return assignmentId;
};

/**
 * This is only temporary solution for bad design patterns
 * @returns {*}
 */
export const getAssignmentAlternateLinkId = (isDrive = false) => {
  if (isDrive) {
    const result = /\/([\da-zA-Z]{5,})#?$/g.exec(getPath());

    return result ? result[1] : null;
  }
  const pathArray = this.getPathArray();

  let link = pathArray.indexOf('a') + 1;
  if (!link) {
    link = pathArray.indexOf('sa') + 1; // questions have /sa/ in the url not /a/
  }
  return isDrive ? getPath() : pathArray[link];
};

/**
 * Get student ID
 * @memberof module:Path/Service
 * @return {Number}
 */
export const getStudentId = () => {
  const pathArray = this.getPathArray();
  const studentId = pathArray[pathArray.indexOf('student') + 1];
  return studentId;
};

/**
 * Get file ID
 * @memberof module:Path/Service
 * @return {Number}
 */
export const getFileId = (retryTimes = 1) => {
  const getPathFromButton = () => {
    let documentId = null;
    const $button = $('[role=main] div[data-url^=https]');
    const URLRegex = /^(https?:\/\/)(www.)?drive.google.com\/open\?id=[\da-zA-Z-_]*&?/gm;

    if ($button.length && URLRegex.test(`${$button.data('url')}`)) {
      const url = new URL($button.data('url'));

      documentId = url.searchParams.get('id');
    } else {
      const links = Array.from(
        document.querySelectorAll('[role=main] [role=menuitem] [role=presentation] a[href]'),
      );
      const link = links.find((l) => URLRegex.test(l.href));

      if (link) {
        const url = new URL(link.href);

        documentId = url.searchParams.get('id');
      }
    }

    return documentId;
  };

  const getID = (observer) => {
    let pathArray = this.getPathArray();
    let fileId = pathArray[pathArray.indexOf('d') + 1];

    if (!fileId) {
      const iframe = $('iframe#material-iframe:last');

      if (iframe.length && iframe.attr('src')) {
        pathArray = iframe.attr('src').split('/');
        fileId = pathArray.includes('d') ? pathArray[pathArray.indexOf('d') + 1] : '';
      } else {
        fileId = getPathFromButton();
      }
    }

    if (fileId) {
      observer.next(fileId);
    } else {
      observer.error(fileId);
    }
  };

  const result = new Observable(getID);

  result.pipe(
    retryWhen((errors) => errors.pipe(
      delay(150),
      take(retryTimes),
      concatMap(throwError),
    )),
  );

  return new Promise((resolve, reject) => {
    result.subscribe(resolve, reject);
  });
};

/**
 * Is classroom instance
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isClassroomInstance = () => {
  let valid = false;
  if (this.classroomNode) {
    valid = true;
  }
  return valid;
};

/**
 * Is drive instance
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isDriveInstance = () => {
  let valid = false;
  if (this.driveNode) {
    valid = true;
  }
  return valid;
};

/**
 * Is classes list page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isClassesListPage = () => this.getPathArray().includes('h');

/**
 * Is settings page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isSettingsPage = () => this.getPathArray().includes('s');

/**
 * Is work page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isWorkPage = () => {
  const pathArray = this.getPathArray();
  let valid = false;
  if (
    (pathArray.indexOf('ta') >= 0 || pathArray.indexOf('a') >= 0)
    && pathArray.indexOf('submissions') < 0
    && pathArray.indexOf('details') < 0
    && pathArray.indexOf('file') < 0
  ) {
    valid = true;
  }
  return valid;
};

/**
 * Is calendar page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isCalendarPage = () => {
  const pathArray = this.getPathArray();
  let valid = false;
  if (pathArray.indexOf('calendar') >= 0) {
    valid = true;
  }
  return valid;
};

/**
 * Is stream page
 * Pattern: /c/[classId]
 * classId: A-Za-z0-9
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const isStreamPage = () => /\/c\/[\w]+\/?$/g.test(getPath()) || this.isClassworkPage();

/**
 * Is classwork page
 * Pattern: /w/[classID]/t/all
 * classId: A-Za-z0-9
 * @memberof module:Path/Service
 * @returns {Boolean}
 */
export const isClassworkPage = () => /\/w\/[\w]+\/t\/all/g.test(getPath());

