/**
 * Was student work page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const wasStudentWorkPage = () => {
  let valid = false;

  if (!this.oldPath || this.oldPath === getPath()) {
    return valid;
  }

  const pathArray = this.oldPath.split('/');
  if (pathArray.indexOf('submissions') >= 0) {
    valid = true;
  }
  return valid;
};

/**
 * Was instructions page
 * @memberof module:Path/Service
 * @return {Boolean}
 */
export const wasInstructionsPage = () => {
  let valid = false;

  if (!this.oldPath) {
    return valid;
  }

  const pathArray = this.oldPath.split('/');
  if (pathArray.indexOf('details') >= 0) {
    valid = true;
  }
  return valid;
};

export const isCourseChanged = () => this.oldCourseId && this.oldCourseId !== this.getClassroomId();

/**
 * Set old path
 * @memberof module:Path/Service
 * @param {String} oldPath Selected old path
 */
export const setOldPath = (oldPath) => {
  this.oldPath = oldPath;
};

/**
 * Set old course id
 * @memberof module:Path/Service
 * @param {Number} oldCourseId Selected old course ID
 */
export const setOldCourseId = (oldCourseId) => {
  this.oldCourseId = oldCourseId;
};
