import { AssignmentOrder } from '../../../assignments/teacherAssignments/shared/enums/assignmentOrder.ts';
import { AssignmentType } from '../../../shared/enums/assignmentType.enum.ts';

export const studentCourseProgressRouterConfig = ($stateProvider) => {
  'ngInject';

  $stateProvider
    .state('student-course-progress', {
      resolve: {
        courses: (FetchData, UserInfo) => {
          'ngInject';

          return FetchData.v3.getStudentCoursesDefinitions(UserInfo.id);
        },
      },
      views: {
        '@': { component: 'studentCourseProgress' },
      },
    })
    .state('student-course-progress.courses-overview', {
      params: {
        coursesDefinitions: null,
        breadcrumbs: [],
      },
      resolve: {
        coursesDefinitions: ($stateParams) => {
          'ngInject';

          return $stateParams.coursesDefinitions || null;
        },
        courses: (FetchData, Path) => {
          'ngInject';

          return FetchData.v3.getCourseDefinitions(Path.getClassroomId());
        },
      },
      views: {
        'extended@': 'studentCoursesDetails',
      },
    })
    .state('student-course-progress.courses-overview.learning-objectives', {
      params: {
        course: null,
        breadcrumbs: [],
        student: null,
      },
      resolve: {
        student: (UserInfo) => {
          'ngInject';

          return UserInfo.getUserObj();
        },
        course: ($stateParams) => {
          'ngInject';

          return $stateParams.course || null;
        },
        courseSelected: (course) => {
          'ngInject';

          return course;
        },
        assessments: (FetchData, UserInfo, course) => {
          'ngInject';

          return course ?
            FetchData.v3
              .getCourseDefinitionObjectiveAssessments(UserInfo.userId, course.course_definition) :
            null;
        },
        courseAssessment: (FetchData, course, UserInfo) => {
          'ngInject';

          return course ?
            FetchData.v3
              .getCourseDefinitionAssessment(UserInfo.userId, course.course_definition) :
            {};
        },
      },
      views: {
        'extended@': {
          component: 'studentDetails',
        },
        'details@student-course-progress.courses-overview.learning-objectives': {
          component: 'objectivesListStudent',
        },
      },
    })
    .state('student-course-progress.courses-overview.learning-objectives.assessments', {
      params: {
        assessment: null,
        breadcrumbs: [],
        objective: null,
        ordering: AssignmentOrder.ASC_TYPE,
        filters: [
          AssignmentType.WORK_UNIT,
          AssignmentType.STANDARD_ASSIGNMENT,
        ],
      },
      data: {
        statePrefix: 'StudentProgressLearningObjectiveAssignments',
      },
      resolve: {
        objectiveAssessment: ($stateParams) => {
          'ngInject';

          return $stateParams.assessment;
        },
        objectiveSelected: ($stateParams) => {
          'ngInject';

          return $stateParams.objective;
        },
        objectives: ($stateParams, FetchData) => {
          'ngInject';

          return FetchData.v3
            .getCourseDefinitionObjectives($stateParams.course.course_definition);
        },
        assessments: ($stateParams, FetchData, UserInfo) => {
          'ngInject';

          return FetchData.v3
            .getObjectiveAssessments(UserInfo.userId, $stateParams.objective.id);
        },
        assignments: (objectiveSelected, UserInfo, AssignmentAssessmentService, $stateParams, AssignmentService) => {
          'ngInject';

          const requestConfig = {
            ordering: $stateParams.ordering,
          };

          return AssignmentAssessmentService
            .getAssignmentAssessmentByObjective(UserInfo.userId, objectiveSelected.id, requestConfig)
            .then(assignmentsAssessments =>
              AssignmentService
                .filterAssignmentsByTypes(assignmentsAssessments, $stateParams.filters),
            );
        },
        gradeSystem: (GradeSystems) => {
          'ngInject';

          return GradeSystems.fetchGradeSystemsList();
        },
      },
      views: {
        'extended@': {
          component: 'objectiveDetails',
        },
        'details@student-course-progress.courses-overview.learning-objectives.assessments': {
          component: 'studentProgressAssignmentsList',
        },
      },
    });
};
