import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Tooltip,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import React, {useEffect, useRef, useState} from "react";
import {withStyles} from "@material-ui/core/styles";
import {getAssessmentIcon, getAssignmentIcon, getStatusText, getSubmittedIcon, getSubmittedStatusText} from "../utils";
import {colors, fonts} from "../theme";
import GridSummaryItem from "../components/gridSummaryItem";
import AssignmentAssessment from "../components/assignmentAssessment";
import GridObjectivesSummaryItem from "../components/GridObjectivesSummaryItem";
import ObjectivesList from "../components/objectivesList";
import AssignmentAccordionItemHeader from "./assignmentAccordionItemHeader";

const widthThreshold = 500;

const StyledAccordionDetails = withStyles({
  root: {
    backgroundColor: colors.grey,
  },
})(AccordionDetails);

const StyledAccordion = withStyles({
  root: {
    '&$expanded': {
      marginTop: '0px',
    },
  },
  expanded: {},
})(Accordion);

const StyledAccordionSummary = withStyles({
  root: {
    minHeight: "50px",
    padding: "0px",
    marginLeft: "10px",
    "&$expanded": {
      minHeight: "0px",
      marginTop: '0px',
    },
  },
  content: {
    // padding: '0px',
    margin: "0px",
    "&$expanded": {
      padding: "0px",
      margin: "0px",
      minHeight: "0px",
    },
  },
  expandIcon: {
    padding: '0px',
    margin: "0px 8px 0px 0px",
    "&$expanded": {},
  },
  expanded: {},
})(AccordionSummary);

const gridStyle = {
  borderLeft: `1px solid ${colors.borderColor}`,
  borderRight: `1px solid ${colors.borderColor}`,
  width:'178px',
};

const AssignmentAccordionItem = (props) => {
  const {
    assessment,
    assignment,
    courseDefinitions,
    style = {},
    objectiveAssessments = [],
    objectives = [],
    showObjectives = false,
    selectedObjective,
    indented = false,
    contentWidth = null,
    firstItem = false,
    onSave,
  } = props;

  const ref = useRef();
  const [width, setWidth] = useState();

  useEffect(() => {
    setWidth(ref?.current?.clientWidth);
  });

  if (!assessment) {
    return null;
  }

  const wideItem = width > widthThreshold;
  let itemWidth = wideItem ? 270 : 170;
  if (indented) {
    itemWidth -= 20;
  }
  if (showObjectives) {
    itemWidth -= 100;
  }

  return (
    <div style={{position: "relative"}}>
      {firstItem && wideItem && (
        <AssignmentAccordionItemHeader
          objectivesWidth={objectives?.length * 40}
        />
      )}
      <StyledAccordion
        square
        ref={ref}
        style={{...style, border: "0px solid green"}}
        TransitionProps={{unmountOnExit: true}}
      >
        <StyledAccordionSummary expandIcon={<ExpandMoreIcon/>}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              padding: "0px 10px 0px 10px",
              // border:'1px solid',
            }}
          >
            <div
              style={{
                // border: '1px solid',
                display: "flex",
                alignItems: "center",
                marginLeft: indented ? "20px" : 0,
              }}
            >
              {getAssignmentIcon(assignment)}
              <span
                style={{
                  ...fonts.MEDIUM,
                  textOverflow: "ellipsis",
                  overflow: "hidden",
                  whiteSpace: "nowrap",
                  // border: '1px solid',
                  width: `${itemWidth}px`,
                }}
              >
                {assignment.title}
              </span>
            </div>
            <div
              style={{
                display: "flex",
                alignItems: "center",
                borderLeft: `1px solid ${colors.borderColor}`,
              }}
            >
              <Tooltip title={getStatusText(assessment.status_new)}>
                <div style={{width: '44px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                  {getAssessmentIcon({
                    status: assessment.status_new,
                    style: {
                      maxHeight: "24px",
                      padding: "0px 10px 0px 10px",
                    },
                  })}
                </div>
              </Tooltip>
              {wideItem &&
              <Tooltip
                title={assignment.use_submitted_status ? getSubmittedStatusText(assessment.submitted_status) : ''}>
                <div style={{
                  borderLeft: `1px solid ${colors.borderColor}`,
                  width: '44px',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  height: '50px'
                }}>
                  {assignment.use_submitted_status && getSubmittedIcon({status: assessment.submitted_status})}
                </div>
              </Tooltip>
              }
              {wideItem && (
                <div style={{display: "flex", border: "0px solid"}}>
                  <GridSummaryItem assessment={assessment} style={gridStyle} />
                  {showObjectives && (
                    <GridObjectivesSummaryItem
                      objectives={objectives}
                      assessments={objectiveAssessments}
                      selectedObjective={selectedObjective}
                      gradeOnly={true}
                    />
                  )}
                </div>
              )}
            </div>
          </div>
        </StyledAccordionSummary>
        <StyledAccordionDetails>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              width: contentWidth ? `${contentWidth}px` : "100%",
            }}
          >
            <AssignmentAssessment
              assignment={assignment}
              assessment={assessment}
              onSave={onSave}
            />
            <ObjectivesList
              onSave={onSave}
              assignmentAssessmentId={assessment.id}
              courseDefinitions={courseDefinitions}
              objectiveAssessments={objectiveAssessments}
              gradeSystem={assignment?.gradeSystem}
              isTopic={assignment?.is_topic}
            />
          </div>
        </StyledAccordionDetails>
      </StyledAccordion>
    </div>
  );
};
export default AssignmentAccordionItem;
