import { StateService } from '@uirouter/core';
import { AssignmentsListComponent } from '../../../../../shared/components/assignmentsList/assignmentsList.component';
import { IAssignment } from '../../../../../shared/models/assignment.model';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherAssignmentsModule from '../../../teacherAssignments.module';
import style from './teacherAssignmentsDraftsList.scss';
import template from './teacherAssignmentsDraftsList.tpl.html';

@Component({
  module: TeacherAssignmentsModule,
  selector: 'teacher-assignments-drafts-list',
  template: template,
  style: style,
})
export class TeacherAssignmentsDraftsListComponent extends AssignmentsListComponent {
  @Input({ string: true })
  public label: string;
  @Input()
  public assignments: IAssignment[];

  constructor(private $state: StateService, private NavigationHistory: any) {
    'ngInject';

    super();
  }

  public goToEditAssignmentCriteria(assignment: IAssignment): void {
    this.$state.go(
      'teacher_set_assignment_objectives',
      {
        assignment,
        isModal: true,
      },
    ).then((): void => {
      //TODO: remove after refactor teacher_assess_assignment view
      this.NavigationHistory.addToHistory(
        'teacher_set_assignment_objectives',
        {
          assignment,
          isModal: true,
        });
    });
  }
}
