import { IComponentController } from 'angular';
import { StateParams, StateService } from '@uirouter/core';
import { IBreadcrumbsService } from '../../../../common/components/breadcrumbs/breadcrumbs.service';
import { Breadcrumb, IBreadcrumb } from '../../../../shared/models/breadcrumb.model';
import { IObjectiveAssignmentAssessment } from '../../../../shared/models/objectiveAssignmentAssessment.model';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import TeacherCourseProgressModule, { IAssignmentOverviewService } from '../teacher-course-progress.module';
import style from './assignmentOverviewDetails.scss';
import template from './assignmentOverviewDetails.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'assignment-overview-details',
  template: template,
  style: style,
})
export class AssignmentOverviewDetailsComponent implements IComponentController {
  @Input()
  public objectiveAssignmentAssessments: IObjectiveAssignmentAssessment[];
  @Input()
  public objectiveAssessment: IObjectiveAssignmentAssessment;

  public breadcrumbs: IBreadcrumb[];
  public courseName: string;

  constructor(private AssignmentOverviewService: IAssignmentOverviewService, private BreadcrumbsService: IBreadcrumbsService,
              private $stateParams: StateParams, private $state: StateService) {
    'ngInject';


    this.objectiveAssignmentAssessments = [];
    this.objectiveAssessment = null;
    this.breadcrumbs = [];
    this.courseName = '';
  }

  public $onInit(): void {
    this.updateBreadcrumbs();
    this.courseName = this.$stateParams.course.course_definition_name;
  }

  public onClickBackArrow(): void {
    this.$stateParams.breadcrumbs.pop().action();
  }

  public onClickClose(): void {
    this.objectiveAssessment.$$open = false;
  }

  public onChangeObjectiveAssessment(): void {
    this.$state.go(
      this.$state.current.name,
      Object.assign({}, this.$stateParams, { objectiveAssessment: this.objectiveAssessment }),
    );
  }

  public onChangeOverall(changedObjectiveAssessment: IObjectiveAssignmentAssessment): void {
    this.AssignmentOverviewService
      .changeObjectiveAssignmentAssessment(changedObjectiveAssessment, this.$stateParams.course.course_definition)
      .then((newObjectiveAssessment: IObjectiveAssignmentAssessment): void => {
        // Dont overwrite feedback and private_note, it messes with the ui
        const { feedback, private_note, result, ...rest } = newObjectiveAssessment;
        this.objectiveAssessment = { ...this.objectiveAssessment, ...rest};
      });
  }

  private updateBreadcrumbs(): void {
    this.breadcrumbs = [...this.$stateParams.breadcrumbs].concat(
      new Breadcrumb(
        this.objectiveAssessment.assignment_title,
        this.BreadcrumbsService.createGoToAction(),
      ),
    );
  }
}
