import get from 'lodash.get';
import { GroupedVisibilitySettings } from '../../../shared/models/groupedVisibilitySettings.model';
import { GroupedVisibilityDialogType } from '../../../shared/enums/groupedVisibilityDialogType.enum';
import { ListViewType } from '../../../shared/enums/listViewType.enum';
import { AssessmentStatus } from '../../../../../../src/app/shared/models/assessment.model';
import {combineQueries} from "@datorama/akita";
import {map, filter, switchMap} from "rxjs/operators";
import {noop, Subject} from "rxjs";

const AssignmentSingleCheckboxes = require('../../../utils/components/assignment_single_checkbox');

/**
 * @module TeacherAssessAssignmentStudent/Controller
 * @description
 *   Handle Assess Assignment Student feature for Teacher
 * @param {Object} $scope Angular $scope object
 * @param {Object} $stateParams Angular $stateParams object
 * @param {Object} $mdDialog Angular Material $mdDialog object
 * @param {Object} Path Path service instance
 * @param {Object} FetchData Fetch Data service instance
 * @param {Object} Debounce Debounce service instance
 * @param {Object} Helpers Helpers service instance
 * @param {Object} LocalHelpers Local Helpers service instance
 * @param {Object} NavigationHistory Navigation History service instance
 * @param {Object} MainMenu Main Menu service instance
 * @param {Object} Mixpanel Mixpanel service instance
 * @param {Object} assignmentsObjectives Resolve from Config
 * @param {Object} assignmentsStudents Resolve from Config
 */

/*@ngInject*/
function MainCtrl(
  $scope, $stateParams, $state, $mdDialog, $filter, $timeout, Path, FetchData, Updates, Debounce,
  Helpers, TeacherAssessAssignmentStudentService, NavigationHistory, MainMenu, Mixpanel,
  Students, assignmentsObjectives, assignment, GroupedVisibilityService,
  GroupedVisibilityDialogService, ListViewMenuService, LocalStorageService, $sce, $compile, AssignmentsService,
  AssignmentsQuery, AssessmentsService, AssignmentService, AssessmentsQuery, AssessmentVisibilityDialogService,
) {
  const vm = this;
  const courseId = Path.getClassroomId();
  const DEBOUNCE_TIME = 250;
  const storageViewMenuId = 'teacherAssessAssignmentStudent';

  vm.AssessmentsService = AssessmentsService;
  vm.savingInProgress = 0;
  vm.savingIndicatorVisible = false;
  vm.savingError = false;
  vm.storageKeyGradeDescription = `${storageViewMenuId}GradeDescriptionVisible`;
  vm.isGradeDescriptionVisible = LocalStorageService.exists(vm.storageKeyGradeDescription);

  // Set default values for Student Edit Multiple
  vm.studentEditMultiple = {
    grade: 'not_changed',
    visibility: 'not_changed',
    open: false,
  };

  // Get data from API
  vm.assignmentTitle = assignmentsObjectives.title;
  vm.gradeSystem = assignmentsObjectives.grade_system;
  vm.resultOnly = assignmentsObjectives.result_only;
  vm.isGradeDescriptionEnable = assignmentsObjectives.enable_grade_texts;
  vm.students = [];
  Students.getStudentsList().then((students) => vm.students = students);
  vm.viewMenuItems = ListViewMenuService.getListViewMenu(storageViewMenuId);
  vm.viewMode = vm.viewMenuItems.find(listViewMenuItem => listViewMenuItem.checked)
    .eventArgs.view;

  vm.assignment = assignment;
  vm.subscriptions = [];

  vm.subscriptions.push(combineQueries([
    AssignmentsQuery.selectActive().pipe(filter(Boolean)),
    AssessmentsQuery.selectAll({asObject: true})
  ]).pipe(
    map(([assignment, assessments]) => ({
        ...assignment,
        assessments: assignment.assessments.map(assessmentID => assessments[assessmentID]),
      })
    ),
  ).subscribe(assignment => {
    const studentID = +vm.studentSelect || $stateParams.studentId;
    vm.assignment = assignment || $stateParams.assignment;
    vm.assessment = vm.assignment.assessments.find(assessment => assessment.student_id === studentID);
    vm.selectedStatus = vm.assessment.status_new;
  }));

  vm.debounceFinished$ = new Subject();
  vm.subscriptions.push(vm.debounceFinished$.subscribe(() => {
    vm.AssessmentsService.synchronizeWarningStatus(vm.assessment).subscribe(() => {
      vm.updateLastEditTimestampForAssignment();
    });
  }));

  $scope.$on('$destroy', () => {
    vm.subscriptions.forEach(subscription => subscription.unsubscribe());
  });

  /**
   * Send changes to API for Objective Assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} objectiveAssessments Contains objects where each object represents objectives
   */
  vm.changeObjectiveAssessments = (objectiveAssessments) => {
    vm.savingInProgress += 1;
    vm.savingIndicatorVisible = true;
    vm.savingError = false;

    FetchData.putObjectiveAssessments(objectiveAssessments)
      .then(
        (res) => {
          vm.savingInProgress -= 1;
          vm.updateObjectiveAssessmentsDates(res);
        },
        () => {
          vm.savingInProgress -= 1;
        },
      )
      .finally(() => vm.debounceFinished$.next());
  };

  vm.statusChange = (status) => {
    const assessment = vm.assignment.assessments.find(assessment => assessment.student_id === +vm.studentSelect);
    let updateStatus$;

    if (status === AssessmentStatus.ASSESSMENT_DONE) {
      vm.selectedStatus = null;
      setTimeout(() => {
        vm.selectedStatus = assessment.status_new;
      });
      updateStatus$ = AssessmentVisibilityDialogService.open(vm.assignment.id).pipe(
        switchMap(assessmentVisibilityDialog => {
          const statusUpdateBody = {
            ...assessmentVisibilityDialog,
            status,
          };
          return AssessmentsService.updateStatus(vm.assignment.id, assessment, statusUpdateBody);
        })
      )
    } else {
      updateStatus$ = AssessmentsService.updateStatus(vm.assignment.id, assessment, {status});
    }

    updateStatus$.subscribe(() => {
      vm.updateLastEditTimestampForAssignment();
      if (status === AssessmentStatus.ASSESSMENT_DONE) {
        $state.transitionTo($state.current, NavigationHistory.getCurrentParams(), {
          reload: true, inherit: false, notify: false
        });
      }
    });
  };

  /**
   * Send changes to API for Assignments Assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} assignmentAssessments Contains objects where each object represents assignment
   * asssessments
   */
  vm.changeAssignmentAssessments = (assignmentAssessments) => {
    vm.savingInProgress += 1;
    vm.savingIndicatorVisible = true;
    vm.savingError = false;

    FetchData.v2.putAssignmentAssessments(assignment.id, assignmentAssessments)
      .then(
        (res) => {
          vm.savingInProgress -= 1;
          vm.updateAssignmentAssessmentsDates(res);
        },
        () => {
          vm.savingInProgress -= 1;
          vm.savingError = true;
        },
      )
      .finally(() => vm.debounceFinished$.next());
  };

  /**
   * Debounce send changes to API for Objective Assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} objectiveAssessments Contains objects where each object represents objectives
   */
  vm.debounceObjectiveAssessments = Debounce((objectiveAssessments) => {
    vm.changeObjectiveAssessments(objectiveAssessments);
  }, DEBOUNCE_TIME);

  /**
   * Debounce send changes to API for Assignments Assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} assignmentAssessments Contains objects where each object represents assignment
   * asssessments
   */
  vm.debounceAssignmentsAssessments = Debounce((assignmentAssessments) => {
    vm.changeAssignmentAssessments(assignmentAssessments);
  }, DEBOUNCE_TIME);

  /**
   * Choose send method
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} objectiveAssessments Contains objects where each object represents objectives
   */
  vm.matchEndpoint = (objectiveAssessments) => {
    vm.debounceObjectiveAssessments(objectiveAssessments);
  };

  /**
   * Update last modified dates for Objective Assessments
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} responseObjectiveAssessmentsArray Response from the API, contains students
   * information
   */
  vm.updateObjectiveAssessmentsDates = (responseObjectiveAssessmentsArray) => {
    responseObjectiveAssessmentsArray.forEach((resObjectiveAssessment) => {
      vm.selectedStudent.course_definitions.forEach((courseDefinition) => {
        courseDefinition.objectives.forEach((objective) => {
          if (resObjectiveAssessment.id !== objective.objective_assessments.id) {
            return;
          }

          // eslint-disable-next-line no-param-reassign
          objective.objective_assessments.grade_last_edit =
            resObjectiveAssessment.grade_last_edit;

          // eslint-disable-next-line no-param-reassign
          objective.objective_assessments.feedback_last_edit =
            resObjectiveAssessment.feedback_last_edit;

          // eslint-disable-next-line no-param-reassign
          objective.objective_assessments.private_note_last_edit =
            resObjectiveAssessment.private_note_last_edit;
        });
      });
    });
  };

  /**
   * Update last modified dates for Assignment Assessments
   * @memberof module:TeacherAssessAssignment/Controller
   * @param {Array} responseAssignmentAssessmentsArray Response from the API, contains students
   * information
   */
  vm.updateAssignmentAssessmentsDates = (responseAssignmentAssessmentsArray) => {
    vm.selectedStudent.assignmentAssessment.result_last_edit =
      responseAssignmentAssessmentsArray[0].result_last_edit;

    vm.selectedStudent.assignmentAssessment.feedback_last_edit =
      responseAssignmentAssessmentsArray[0].feedback_last_edit;

    vm.selectedStudent.assignmentAssessment.private_note_last_edit =
      responseAssignmentAssessmentsArray[0].private_note_last_edit;
  };

  /**
   * Init function
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.init = () => {
    // Set default values
    if ($stateParams.editingMode) {
      vm.studentEditingMode = $stateParams.editingMode;
      vm.currentStudentEditingMode = vm.studentEditingMode;
    } else {
      vm.studentEditingMode = 'all';
      vm.currentStudentEditingMode = 'all';
    }

    // Check if user clicked on student name in extension (get student id)
    // or in classroom (get student name) and return proper value.
    if ($stateParams.studentId) {
      vm.studentSelect = $stateParams.studentId;
    } else if ($stateParams.studentName) {
      vm.studentSelect = Helpers.findStudentIdByName(vm.students, $stateParams.studentName);
    }

    // Generate common structure
    vm.generateStructure();

    // Init change student
    vm.changeStudent();
    vm.selectedCourseDefinition = vm.getLastSelectedCourseDefinition();
    vm.setTextForGradeSystem();
  };

  vm.setTextForGradeSystem = () => {
    vm.gradeSystem.grades.forEach((grade) => {
      const defaultGradeTexts = [{ grade_id: grade.id, text: '' }];
      grade.text = get(vm, 'selectedCourseDefinition.objectives[0].grade_texts', defaultGradeTexts)
        .find(gradeText => gradeText.grade_id === grade.id).text;
    });
  };

  vm.getGradeText = (grade, objective, courseDefinition) => {
    const defaultGradeTexts = [{ grade_id: grade.id, text: '' }];
    const currentObjective = courseDefinition.objectives
      .find(_objective => _objective.id === objective.objective_assessments.objective);

    return get(currentObjective, 'grade_texts', defaultGradeTexts)
      .find(gradeText => gradeText.grade_id === grade.id).text;
  };

  vm.getLastSelectedCourseDefinition = () => {
    let courseDefinition;

    if ($stateParams.courseDefinitionId) {
      courseDefinition = vm.selectedStudent.course_definitions
        .find(item => item.id === $stateParams.courseDefinitionId);
    }

    return courseDefinition || vm.selectedStudent.course_definitions[0];
  };

  vm.generateStructure = () => {
    vm.selectedStudent = {};

    vm.selectedStudent.course_definitions = assignmentsObjectives.course_definitions
      .filter(courseDefinition =>
        courseDefinition.objectives.length ? courseDefinition.objectives : null,
      );
  };

  /**
   * Fetch information about assignment assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.fetchAssignmentAssessments = () => {
    vm.selectedStudent.assignmentAssessment = {};
    vm.selectedStudent.assignmentAssessmentFetching = true;

    FetchData.v2.getAssignmentAssessments(assignment.id, vm.studentSelect)
      .then((res) => {
        vm.selectedStudent.assignmentAssessment = Object
          .assign({}, vm.selectedStudent.assignmentAssessment, res);

        vm.selectedStudent.assignmentAssessmentFetching = false;
      });
  };

  /**
   * Fetch information about objective assessments
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.fetchObjectiveAssessments = () => {
    vm.selectedStudent.objectiveAssessmentFetching = true;

    FetchData.v2.getObjectiveAssessments(assignment.id, vm.studentSelect)
      .then((res) => {
        vm.selectedStudent.course_definitions.forEach((courseDefinitions) => {
          courseDefinitions.objectives.forEach((objective) => {
            res.forEach((resObjective) => {
              if (objective.id !== resObjective.objective) {
                return;
              }
              // eslint-disable-next-line no-param-reassign
              objective.objective_assessments = resObjective;
            });
          });
        });

        vm.selectedStudent.objectiveAssessmentFetching = false;
      });
  };

  /**
   * Go to previous history entry
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.historyBack = () => {
    if (vm.historyBackClicked) {
      return;
    }

    vm.historyBackClicked = true;

    // Clear students details
    MainMenu.saveStudentDetails(null);
    // reset only when we want to get beck to the assignments list
    // this is an ugly workaround ......
    if (NavigationHistory.getPreviousState() &&
      NavigationHistory.getPreviousState().stateName.startsWith('teacherAssignments')) {
      MainMenu.saveAssignmentDetails(null);
    }

    NavigationHistory.back({ courseDefinitionId: vm.selectedCourseDefinition.id });
  };

  /**
   * Change selected student using select
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.changeStudent = () => {
    vm.fetchAssignmentAssessments();
    vm.fetchObjectiveAssessments();
    vm.setTextForGradeSystem();

    vm.assessment = vm.assignment.assessments.find(assessment => assessment.student_id === +vm.studentSelect);
    vm.selectedStatus = vm.assessment.status_new;

    NavigationHistory.updateParams({ studentId: +vm.studentSelect });
  };

  /**
   * Load view chosen in currentStudentEditingMode select
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.loadView = () => {
    vm.currentStudentEditingMode = vm.studentEditingMode;
    vm.checkIfAnyCheckboxIsChecked();
  };

  /**
   * Check if there is any checkbox selected for Student Edit Multiple
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.checkIfAnyCheckboxIsChecked = () => {
    vm.studentEditMultiple.anyCheckboxChecked =
      vm.assignmentCheckboxCheckIfAny(vm.selectedStudent.course_definitions);
  };

  /**
   * Check if there is any changes to save in Student Edit Multiple
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.checkEditMultiple = () => {
    const visibilityStudentEditing = ['assessment', 'objectives_feedback'];
    const gradeStudentEditing = ['assessment'];
    const textStudentEditing = ['objectives_feedback', 'objectives_private_notes'];

    const mode = vm.studentEditMultiple;

    mode.changesToSave =
      Helpers.isVisibilityChanges(
        mode.visibility,
        vm.currentStudentEditingMode,
        visibilityStudentEditing) ||
      Helpers.isGradeChanges(mode.grade, vm.currentStudentEditingMode, gradeStudentEditing) ||
      Helpers.isTextChanges(mode.textField, vm.currentStudentEditingMode, textStudentEditing);
  };

  /**
   * Save changes for Student Edit Multiple
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.saveEditMultiple = () => {
    const selectedItems = TeacherAssessAssignmentStudentService
      .findSelectedItems(vm.currentStudentEditingMode, vm.selectedStudent);
    const objectiveAssessments = TeacherAssessAssignmentStudentService.extractObjectiveAssessments(selectedItems);

    vm.changeMultipleTexts(objectiveAssessments);
    vm.changeMultipleGrades(objectiveAssessments);
    vm.changeMultipleVisible(objectiveAssessments);

    vm.matchEndpoint(objectiveAssessments);

    vm.clearEditMultiple();

    Mixpanel.track('edit_multiple');
  };

  /**
   * Set defaults values for Student Edit Multiple and clear changesToSave flag to false
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.clearEditMultiple = () => {
    vm.studentEditMultiple.grade = 'not_changed';
    vm.studentEditMultiple.visibility = 'not_changed';
    vm.studentEditMultiple.textMode = 'new_text';
    vm.studentEditMultiple.textField = '';
    vm.studentEditMultiple.changesToSave = false;
  };

  /**
   * Close Student Edit Multiple
   * @memberof module:TeacherAssessAssignmentStudent
   */
  vm.closeEditMultiple = () => {
    vm.studentEditMultiple.open = false;
  };

  /**
   * Handle changing text of single item
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleText = (savingMethod, savingItem, eventCode) => {
    Helpers.changeSingleText(savingMethod, savingItem, eventCode);
    vm.updateLastEditTimestampForAssignment();
  };

  /**
   * Handle changing visible of single item
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {Object} editingItem Object that will be editing
   * @param {String} editingProperty Property that will be editing
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleVisible = (savingMethod, savingItem, editingItem, editingProperty, eventCode) => {
    Helpers.changeSingleVisible(savingMethod, savingItem, editingItem, editingProperty, eventCode);
  };

  /**
   * Handle changing grade of single item
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Function} savingMethod Function for saving changes
   * @param {Object} savingItem Object that should be saved
   * @param {String} eventCode Mixpanel event code
   */
  vm.changeSingleGrade = (savingMethod, savingItem, eventCode) => {
    Helpers.changeSingleGrade(savingMethod, savingItem, eventCode);
  };

  /**
   * Handle changing text of multiple items
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleTexts = (selectedItems) => {
    const studentEditing = ['objectives_feedback', 'objectives_private_notes'];

    if (!vm.studentEditMultiple.textField) {
      return;
    }


    const pass = Helpers.checkIfModeAvailable(vm.currentStudentEditingMode, studentEditing);

    if (!pass) {
      return;
    }

    const textMode = vm.studentEditMultiple.textMode;
    const textField = vm.studentEditMultiple.textField;
    const textProperty = TeacherAssessAssignmentStudentService
      .findTextPropoerty(vm.currentStudentEditingMode);

    selectedItems.forEach((itemElem) => {
      const oldValue = itemElem[textProperty];
      if (textMode === 'new_text') {
        // eslint-disable-next-line no-param-reassign
        itemElem[textProperty] = textField;
      } else if (textMode === 'append_text_first') {
        // eslint-disable-next-line no-param-reassign
        itemElem[textProperty] = `${textField} ${oldValue}`;
      } else if (textMode === 'append_text_last') {
        // eslint-disable-next-line no-param-reassign
        itemElem[textProperty] = `${oldValue} ${textField}`;
      }
    });
  };

  /**
   * Handle changing visible of multiple items
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleVisible = (selectedItems) => {
    const studentEditing = ['assessment', 'objectives_feedback'];

    if (vm.studentEditMultiple.visibility === 'not_changed') {
      return;
    }

    const pass = Helpers.checkIfModeAvailable(vm.currentStudentEditingMode, studentEditing);

    if (!pass) {
      return;
    }

    const visibilityMode = vm.studentEditMultiple.visibility;

    const visibilityProperty = TeacherAssessAssignmentStudentService
      .findVisibilityPropoerty(vm.currentStudentEditingMode);

    selectedItems.forEach((itemElem) => {
      if (visibilityMode === 'set_visible') {
        // eslint-disable-next-line no-param-reassign
        itemElem[visibilityProperty] = true;
      } else if (visibilityMode === 'set_hidden') {
        // eslint-disable-next-line no-param-reassign
        itemElem[visibilityProperty] = false;
      }
    });
  };

  /**
   * Handle changing grades of multiple items
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Array} selectedItems Contains selected items
   */
  vm.changeMultipleGrades = (selectedItems) => {
    const studentEditing = ['assessment'];

    if (vm.studentEditMultiple.grade === 'not_changed') {
      return;
    }

    const pass = Helpers.checkIfModeAvailable(vm.currentStudentEditingMode, studentEditing);

    if (!pass) {
      return;
    }

    const grade = vm.studentEditMultiple.grade;

    selectedItems.forEach((itemElem) => {
      if (itemElem.grade_system_is_counter) {
        return;
      }

      // eslint-disable-next-line no-param-reassign
      itemElem.grade_name = grade;
    });
  };

  /**
   * Handle changes dialog (using setTimeout for $scope.$apply function
   * because it's trigger by $scope.$on and this listener
   * doesn't trigger $scope.$apply function)
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {String} triggerType Trigger type (cancelEditingMode, changeEditingMode,
   * clickStudentInClassroom)
   * @param {Number} studentId Student ID of clicked student
   */
  vm.showChangesDialog = (triggerType, studentId) => {
    // Without changes
      if (triggerType === 'cancelEditingMode') {
        vm.clearEditMultiple();
        vm.closeEditMultiple();
      }
      if (triggerType === 'changeEditingMode') {
        NavigationHistory.updateParams({ editingMode: vm.studentEditingMode });
        vm.clearEditMultiple();
        vm.loadView();
        if (vm.studentEditingMode === 'all') {
          vm.studentEditMultiple.open = false;
        }
      }
      if (triggerType === 'clickStudentInClassroom') {
        vm.studentSelect = studentId;
        vm.changeStudent();
        $timeout(() => {
        });
        vm.closeEditMultiple();
        vm.clearEditMultiple();
      }
      if (triggerType === 'reloadState') {
        NavigationHistory.go('teacher_assess_assignment_student', { studentId, assignment });
      }
      return;
  };

  /**
   * Open action menu
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   * @param {Object} $mdOpenMenu Angular Material $mdOpenMenu object
   * @param {Object} e Event object
   */
  vm.openActionMenu = ($mdOpenMenu, e) => {
    $mdOpenMenu(e);
  };

  /**
   * Go to Assess Course option
   * @memberof module:TeacherAssessAssignmentStudent/Controller
   */
  vm.goToAssessCourseOption = () => {
    NavigationHistory.go('teacher_assess_course_student', { studentId: vm.studentSelect });
  };

  vm.assignmentCourseChanged = (selectedCourseDefinition = vm.selectedCourseDefinition) => {
    vm.setTextForGradeSystem();
    NavigationHistory.updateParams({ courseDefinitionId: selectedCourseDefinition.id });
    $state.go('.', { courseDefinitionId: selectedCourseDefinition.id });
  };

  // Listen on Clicked Student in Classroom event (trigger when user click on student link in
  // classroom)
  // and redirect to students details for clicked student.
  // Use debounce because clickedStudentInClassroom is trigger twice from unknown reason.
  $scope.$on('clickedStudentInClassroom', Debounce((e, studentName) => {
    let studentId = Helpers.findStudentIdByName(vm.students, studentName);

    if (!studentId) {
      Updates.updateStudents(courseId, true).then((res) => {
        studentId = Helpers.findStudentIdByName(res, studentName);
        vm.showChangesDialog('reloadState', studentId);
      });
    } else {
      vm.showChangesDialog('clickStudentInClassroom', studentId);
    }
  }, DEBOUNCE_TIME));

  vm.init();

  vm.setVisibility = () => {
    const settings =
      new GroupedVisibilitySettings(GroupedVisibilityDialogType.AssignmentStudentView);

    settings.students = [this.students.find(student => student.id === vm.studentSelect)];
    settings.courseDefinitions = assignmentsObjectives.course_definitions
      .filter(courseDefinition => !!courseDefinition.objectives.length);

    GroupedVisibilityDialogService.show(settings).then((groupedVisibility) => {
      GroupedVisibilityService.patchAssignmentAssessmentsGroupedVisibility(groupedVisibility)
        .then(() => $state.reload());
    });
  };

  vm.updateDescription = (grade, objective, courseDefinition) => {
    $timeout(() => {
      if (grade) {
        objective.gradeText = vm.getGradeText(grade, objective, courseDefinition);
      } else {
        objective.gradeText = '';
      }
    });
  };

  vm.onClickMenuItem = (toolbarMenuItem) => {
    vm.viewMenuItems.forEach((menuItem) => {
      Object.assign(menuItem, { checked: false });
    });
    Object.assign(toolbarMenuItem, { checked: true });
    ListViewMenuService.saveListViewMenu(storageViewMenuId, vm.viewMenuItems);
    vm.viewMode = toolbarMenuItem.eventArgs.view;
  };

  vm.isTitleVisible = (title) =>
    vm.viewMode !== ListViewType.DESCRIPTION_ONLY && `${title}`.trim().length;

  vm.isDescriptionVisible = (description) =>
    vm.viewMode !== ListViewType.TITLE_ONLY && `${$filter('getFormattedOrEmpty')(description)}`.trim().length;

  vm.showGradeDescription = () => {
    vm.isGradeDescriptionVisible = true;
    LocalStorageService.set(vm.storageKeyGradeDescription, 'true');
  };

  vm.hideGradeDescription = () => {
    vm.isGradeDescriptionVisible = false;
    LocalStorageService.remove(vm.storageKeyGradeDescription);
  };

  vm.isGradeTextMenuVisible = () => {
    return vm.isGradeDescriptionEnable && !vm.gradeSystem.is_counter;
  };

  vm.isGradeTextVisible = (objective) => {
    return vm.isGradeTextMenuVisible() && vm.isGradeDescriptionVisible &&
      !vm.gradeSystem.is_counter && objective.gradeText;
  };

  vm.updateLastEditTimestampForAssignment = (id = vm.assignment.id) => {
    AssignmentsService.update({id});
  };
}

MainCtrl.prototype = Object.assign(MainCtrl.prototype, AssignmentSingleCheckboxes.prototype);

module.exports = MainCtrl;
