import React, { useState } from 'react';

const useFetch = (url, setNetworkTraffic, networkError) => {
  const [error, setError] = useState(false);
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchData = async () => {
    const config = { url, method: 'GET', data: {} };
    const requestConfig = { ...config, action: 'fetchData' };
    // console.log('useFetch: ', config);
    setNetworkTraffic(config.method);
    setLoading(true);

    if (process.env.REACT_APP_STANDALONE) {
      const json = await (
        await fetch(url, {
          method: 'GET',
          headers: {
            Authorization: `StudybeeToken ${process.env.REACT_APP_TOKEN_FOR_DEBUG}`,
          },
        })
      ).json();
      setLoading(false);
      console.log('useFetch response: ', url, json);
      setData(json);
      setNetworkTraffic(false);
      return;
    }
    chrome.runtime.sendMessage(requestConfig, (response) => {
      // console.log('useFetch response: ', url, response);
      if (response.status < 400) {
        setData(response.data);
      } else {
        setError(response.status);
        networkError(response.status);
      }
      setLoading(false);
      setNetworkTraffic(false);
    });
  };

  return [loading, error, data, fetchData];
};

export default useFetch;
