import { StateService, RawParams } from '@uirouter/core';
import { AssignmentsListComponent } from '../../../../../shared/components/assignmentsList/assignmentsList.component';
import { IAssignment } from '../../../../../shared/models/assignment.model';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import StudentAssignmentsModule from '../../../studentAssignments.module';
import style from './studentAssignmentsList.scss';
import template from './studentAssignmentsList.tpl.html';

@Component({
  module: StudentAssignmentsModule,
  selector: 'student-assignments-list',
  template: template,
  style: style,
})
export class StudentAssignmentsListComponent extends AssignmentsListComponent {
  @Input({ string: true })
  public label: string;
  @Input()
  public assignments: IAssignment[];

  constructor(private $state: StateService) {
    'ngInject';

    super();
  }

  public goToAssignmentDetails(assignment: IAssignment): void {
    const params: RawParams = {
      assignment,
      isModal: true,
    };

    this.$state.go('studentAssessed', params);
  }
}
