import { StateService, TransitionPromise } from '@uirouter/core';
import { IQService } from 'angular';
import { HttpResponse } from '../../shared/models/httpResponse.model';
import { IInterceptor } from '../../shared/models/interceptor.model';
import { Interceptor } from '../decorators/interceptor.decoreator';
import { HttpStatusCode } from '../enums/httpStatusCode.enum';
import UtilsModule from '../utils.module';


@Interceptor({
  module: UtilsModule,
})
export class InactiveUserInterceptor implements IInterceptor {
  constructor(private $q: IQService, private $state: StateService) {
    'ngInject';
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> | TransitionPromise {
    if (response.status === HttpStatusCode.FORBIDDEN && response.data.detail) {
      return this.$state.go('user-inactive');
    }

    return this.$q.reject(response);
  }
}
