import React, {memo, useEffect, useMemo, useState} from "react";
import {colors, fonts} from "../../theme";
import Tooltip from "../../components/tooltip";
import PublishedIcon from "../../components/publishedIcon";

const styles = {
  item: {
    display: "flex",
    width: "40px",
    height: "40px",
    border: "0px solid red",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
  },
};

const CourseObjectiveAssessmentsComponent = (props) => {
  const {objectiveAssessments, gradeSystem, bgColor} = props;
  const [sortedAssessments, setSortedAssessments] = useState([]);
  const [gradeHash, setGradeHash] = useState({});

  useEffect(() => {
    setSortedAssessments(objectiveAssessments?.sort((a, b) => (a.name.localeCompare(b.name, undefined, {
      numeric: true,
      sensitivty: 'base'
    }))));
  }, [objectiveAssessments]);

  useEffect(() => {
    setGradeHash(gradeSystem?.grades?.reduce((acc, g) => {
      acc[g.id] = g;
      return acc;
    }, {}));
  }, [gradeSystem]);

  return (
    <div style={{display: "flex", width: "100%", justifyContent: "space-around"}}>
      {sortedAssessments?.map((assessment) => {
        const grade = assessment?.grade_id ? gradeHash[assessment.grade_id] : null;
        return (
          <div
            key={assessment.id}
            style={{
              display: "flex",
              flexDirection: "row",
              borderRight: `0px solid ${
                bgColor !== colors.white
                  ? colors.borderColorDark
                  : colors.borderColor
              }`,
            }}
          >
            <Tooltip key={assessment.id} title={assessment.name}>
              <div style={styles.item}>
        <span
          style={{
            ...fonts.MEDIUM,
            fontWeight: "800",
            color: grade?.color || colors.iconColorLight,
          }}
        >
      {" "}
          {grade?.label || "-"}
        </span>
                <PublishedIcon published={assessment?.grade_published} active={!!grade?.label} />
              </div>
            </Tooltip>
          </div>
        );
      })}
    </div>
  );
};
export default memo(CourseObjectiveAssessmentsComponent);
