import {IAngularEvent, IRootScopeService} from 'angular';
import {Events} from '../../../common/global-events';
import {Component} from '../../../utils/decorators/component.decorator';
import {UnsubscribableComponent} from '../abstracts/unsubscribable.component';
import SharedComponentsModule from '../components.module';
import style from './assignObjectivesPopup.scss';
import template from './assignObjectivesPopup.tpl.html';
import {AssignmentSimple, AssignmentsStore, AssignmentType} from '../../../../../../src/app/teacher/assignments/state';
import {HttpService} from '../../../../../../src/app/core/http/http.service';
import {delayedRetry} from '../../../../../../src/app/core/rxjs/delayed-retry.operator';
import {catchError, shareReplay, tap} from 'rxjs/operators';
import {Observable} from 'rxjs';
import {getTranslation} from '../../../../../../react_src/i18n';

@Component({
  module: SharedComponentsModule,
  selector: 'assign-objectives-popup',
  template: template,
  style: style,
})
export class AssignObjectivesPopupComponent extends UnsubscribableComponent {
  public assignment: any;
  public showLoading: boolean;
  public title: string;

  protected preFetchAssignment: Observable<AssignmentSimple>;

  constructor(protected $rootScope: IRootScopeService,
              protected NavigationHistory: any,
              protected Path: any,
              protected StudybeeHandle: any,
              protected AssignmentsStore: AssignmentsStore,
              protected HttpService: HttpService) {
    'ngInject';

    super();
    this.assignment = null;
    this.showLoading = false;
    this.title = null;
  }

  public $onInit(): void {
    this.$$unsubscribes.push(
      this.$rootScope.$on(Events.OPEN_ASSIGNMENT_POPUP, this.eventOpenAssignmentHandler.bind(this)),
    );
  }

  public assignObjectives(): void {
    this.showLoading = true;

    const subscription = this.preFetchAssignment.pipe(
      tap(() => this.postponeAssigningObjectives())
    ).subscribe(assignment => {
      this.StudybeeHandle.studybeeOpen();
      chrome.storage.sync.set({'editAssignment': assignment.id}, () => console.log('edit assignment: ', assignment.id));
    });
    this.$$unsubscribes.push(() => subscription.unsubscribe());
  }

  public postponeAssigningObjectives(): void {
    this.assignment = null;
    this.showLoading = false;
  }

  protected eventOpenAssignmentHandler(event: IAngularEvent, assignment: any): void {
    const subscription = this.fetchAssignment(assignment.title).subscribe(this.addAssignment.bind(this));
    this.$$unsubscribes.push(() => subscription.unsubscribe());
    this.assignment = assignment;
    this.title = getTranslation('assignObjectivesToAssignmentToast').replace('__title__', assignment.title);
  }

  protected fetchAssignment(assignmentTitle: string) {
    const courseID = this.Path.getClassroomId();
    const url = `/classes/assignment_id/?course_id=${courseID}&title=${encodeURI(assignmentTitle)}`;

    this.preFetchAssignment = this.HttpService.get<AssignmentSimple>(url).pipe(
      delayedRetry(1000, 5),
      catchError(error => {
        console.error(error);
        return null;
      }),
      shareReplay(),
    );

    return this.preFetchAssignment;
  }

  protected addAssignment(assignment: AssignmentSimple) {
    if (assignment) {
      const timestamp = (new Date()).getTime();

      this.AssignmentsStore.add({
        ...assignment,
        assessment_last_changed: timestamp,
        assignment_created: timestamp,
        assignment_type: AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
        is_topic: false,
        assessments: [],
        alternate_link_id: '',
        assignment_not_assessable: true,
        assignments_for_topic: [],
        classroom_name: '',
      });
    }
  }
}
