import { Events } from '../../../../common/global-events.ts';

export class CourseDetailsCtrl {
  constructor($rootScope, $state) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
  }

  $onInit() {
    this.$rootScope.$emit(
      Events.UPDATE_USER_STATUSES_FOR_COURSES_AND_OBJECTIVES,
    );
  }

  toggleAssessment() {
    this.courseAssessment.open = !this.courseAssessment.open;
  }

  closeCourseView() {
    this.$state.go('student-course-progress', { course: null });
    this.$rootScope.$emit(Events.CLOSE_COURSE_DETAILS_VIEW);
  }
}
