import { Events } from '../../../common/global-events.ts';

export class UserSetupCompleteController {
  constructor($rootScope, $state) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
  }

  closeUserSetup() {
    // this needs to be changed
    this.$rootScope.$broadcast(Events.USER_SETUP_COMPLETED);
    this.$state.go('loading');
  }
}
