import React, { useContext } from 'react';
import LinearProgress from '@material-ui/core/LinearProgress';
import { withStyles } from '@material-ui/core/styles';
import { colors } from '../theme';
import { GlobalContext } from '../utils';

const SBLinearProgress = withStyles({
  barColorPrimary: {
    backgroundColor: colors.green,
  },
})(LinearProgress);

const SBLinearProgressPink = withStyles({
  barColorPrimary: {
    backgroundColor: colors.brandColorPink,
  },
})(LinearProgress);

const StyledLinearProgress = (pink = false) => {
  const context = useContext(GlobalContext);
  const { networkTraffic } = context;
  if (!networkTraffic) {
    return null;
  }
  if (pink) {
    return <SBLinearProgressPink/>;
  }
  return (
    <SBLinearProgress />
  );
};

export default StyledLinearProgress;
