import {StateProvider} from '@uirouter/angularjs';
import {RawParams, StateDeclaration, StateService, Transition, TransitionService} from '@uirouter/core';
import {IPromise} from 'angular';
import {Assignment, AssignmentsService, AssignmentsStore} from '../../../../../src/app/teacher/assignments/state';
import {TransitionHandler} from '../../shared/models/transitionHandler.model';
import {TransitionAuth} from '../../shared/types/transitionAuth.type';
import {registerOnBeforeTransition} from '../../utils/decorators/registerOnBeforeTransition.decorator';

export const TeacherAssignmentsRouting: Function = ($stateProvider: StateProvider, $transitionsProvider: TransitionService): void => {
  'ngInject';

  @registerOnBeforeTransition($transitionsProvider)
  class AddToHistoryTeacherAssessAssignmentStudent extends TransitionHandler {
    public getCriteria(): any {
      return { to: 'teacher_assess_assignment_student' };
    }

    public onBefore(transition: Transition): TransitionAuth {
      const navigationHistory = transition.injector().get('NavigationHistory');
      const params: RawParams = transition.targetState().params();
      const isRefresh = transition.$from().name === transition.$to().name;

      if (!isRefresh && (params.studentName || params.studentId)) {
        navigationHistory.addToHistory('teacher_assess_assignment', {
          ...params,
        });
      }

      return super.onBefore(transition);
    }
  }

  @registerOnBeforeTransition($transitionsProvider)
  class AddToHistoryTeacherSetAssignmentObjectives extends TransitionHandler {
    public getCriteria(): any {
      return { to: 'teacher_set_assignment_objectives' };
    }

    public onBefore(transition: Transition): TransitionAuth {
      const navigationHistory = transition.injector().get('NavigationHistory');
      const params: RawParams = transition.targetState().params();
      const isRefresh = transition.$from().name === transition.$to().name;

      if (!isRefresh && params.assignment && params.isModal) {
        navigationHistory.addToHistory('teacher_set_assignment_objectives', {
          ...params,
        });
      }

      return super.onBefore(transition);
    }
  }

  @registerOnBeforeTransition($transitionsProvider)
  class AddToHistoryTeacherAssessAssignment extends TransitionHandler {
    public getCriteria(): any {
      return { to: 'teacher_assess_assignment' };
    }

    public onBefore(transition: Transition): TransitionAuth {
      const navigationHistory = transition.injector().get('NavigationHistory');
      const params: RawParams = transition.targetState().params();
      const isRefresh = transition.$from().name === transition.$to().name;

      if (!isRefresh && params.assignment && params.isModal) {
        navigationHistory.addToHistory('teacher_assess_assignment', {
          ...params,
        });
      }

      return super.onBefore(transition);
    }
  }

  @registerOnBeforeTransition($transitionsProvider)
  class GuardAuth extends TransitionHandler {
    public getCriteria(): any {
      return { to: state => state.name.startsWith('teacherAssignments') };
    }

    public onBefore(transition: Transition): TransitionAuth {
      const transitionTo: StateDeclaration = transition.to();
      (transitionTo as any).themeClass = 'sb-theme-green';
      (transitionTo as any).role = 'teacher';

      return true;
    }
  }

  $stateProvider
    .state('teacherAssignments', {
      resolve: {
        assignmentsGroup: (AssignmentsService: AssignmentsService, AssignmentsStore: AssignmentsStore, $state: StateService): IPromise<Assignment[]> => {
          'ngInject';

          AssignmentsStore.setActive(null);
          return new Promise(resolve => {
            AssignmentsService.get().subscribe(assignments => {
              if (assignments.length) {
                resolve(assignments);
              } else {
                $state.go('teacherAssignmentsNotConfigured');
              }
            });
          });
        },
      },
      views: {
        '@': {
          component: 'teacherAssignments',
        },
        'assignments@teacherAssignments': {
          component: 'appAssignments',
        },
      },
    })
    .state('teacherAssignmentsNotConfigured', {
      views: {
        '@': {
          component: 'teacherAssignments',
        },
        'assignments@teacherAssignmentsNotConfigured': {
          component: 'teacherAssignmentsNotConfigured',
        },
      },
    });
};
