const styles = {
  container: {
    display: 'flex',
    overflow: 'scroll',
    position: 'relative',
    width: '100%',
  },
  container2: {
    display: 'flex',
    height: '0%',
    justifyContent: 'flex-start',
    padding: '16px',
    marginTop: '60px',
  },
  header: {
    ...fonts.MEDIUM,
    fontWeight: '400',
    padding: '0px 10px 0px 10px',
    height: '30px',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
  },
  table: {
    marginRight: '10px',
    minWidth: '600px',
    height: '100%',
    boxShadow: '0px 5px 10px rgba(0,0,0,0.1)',
  },
  th: {
    ...fonts.MEDIUM,
    fontWeight: '400',
    padding: '0px 10px 0px 10px',
    height: '30px',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap', width: '220px', display: 'flex', alignItems: 'center', justifyContent: 'flex-start',
  },
  studentNameText: {
    ...fonts.MEDIUM,
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    display: 'inline-block',
    width: '200px',
    padding: '0px 16px 0px 16px',
  },
  container3: {display: 'flex', justifyContent: 'space-around'},
  iconContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  assessmentIcon: {height: '20px'},
  block: {height: '50px', width: '50px', backgroundColor: 'transparent'},
  shadow: {
    width: '30px',
    height: '100%',
    position: 'absolute',
    right: -30,
    top: 0,
    backgroundColor: 'transparent',
    boxShadow: '-5px 0px 6px rgba(0,0,0,0.1)',
  },
};

const GridViewContent = (props) => {
  const {
    assignment: initialAssignment,
    selectedStudentId,
    onStudentSelected,
    onAssignmentClicked,
    multiEdit,
    setMultiEdit,
    selectedStudents,
    setSelectedStudents,
  } = props;
  const window = useWindowDimensions();
  const [assignment, setAssignment] = useState(initialAssignment);
  const [highlight, setHighlight] = useState();
  const [renderAssignments, setRenderAssignments] = useState(false);
  const [sortedAssignments, setSortedAssignments] = useState([]);
  const [sortedAssessments, setSortedAssessments] = useState([]);
  const [students, setStudents] = useState([]);
  const originallySelectedStudent = useRef();
  const context = useContext(GlobalContext);
  const {Api, networkTraffic, sortingMethod} = context;
  const saving = useRef(false);

  const refresh = async () => {
    const response = await Api.getAssignmentWithAssessment(
      assignment.id,
      fields,
    );
    const newAssignment = response?.data?.assignment[0];
    setAssignment(newAssignment);
    // Delay rendering of the tables 1s because this view is heavy to render
    setTimeout(() => setRenderAssignments(true), 1000);
  };

  // A little bit hacky, reload classroom if something has been saved
  useEffect(() => {
    if (networkTraffic === 'POST' || networkTraffic === 'PUT') {
      saving.current = true;
    } else if (saving.current) {
      if (!multiEdit) {
        refresh();
        saving.current = false;
      }
    }
  }, [networkTraffic]);

  useEffect(() => {
    setAssignment(initialAssignment);
    setSortedAssignments(initialAssignment?.assignments?.filter(a => a.connected)?.sort((a, b) => a.title.localeCompare(b.title)));
    setSortedAssessments(initialAssignment.assessments
      .sort((a, b) => {
        if (sortingMethod === 'firstName') {
          return a.first_name.localeCompare(b.first_name);
        }
        return a.last_name.localeCompare(b.last_name);
      }));
    setStudents(initialAssignment?.assessments.map((a) => a.student_id));
  }, [initialAssignment, sortingMethod]);

  useEffect(() => {
    refresh();
  }, []);

  const studentClicked = useCallback((id) => {
    if (!multiEdit) {
      onStudentSelected(id);
    }
  }, [students, multiEdit]);


  return (
    <div
      style={{
        position: 'absolute',
        width: `${window.width - 360}px`,
        left: `${-window.width + 360}px`,
        height: `${window.height - 60}px`,
        display: 'flex',
        backgroundColor: colors.grey,
      }}
    >
      <div style={styles.container}>
        <CheckboxWithText
          subText={''}
          id={'multiselect'}
          text={getTranslation('selectMany')}
          onCheck={(c) => {
            setMultiEdit(c);
            if (!c) {
              setSelectedStudents([]);
            } else {
              setSelectedStudents([selectedStudentId]);
            }
          }}
          checked={multiEdit}
          style={{
            position: 'absolute',
            left: '30px',
            top: '6px',
          }}
        />
        <div style={styles.container2}>
          <div>
            <table style={styles.table}>
              <tr>
                {multiEdit && (
                  <th>
                    <CheckboxWithText
                      subText={''}
                      id={'multiselect'}
                      style={{marginLeft: '10px', height: '10px'}}
                      onCheck={(check) => {
                        if (check) {
                          originallySelectedStudent.current = selectedStudents;
                          setSelectedStudents(
                            assignment.assessments.map((a) => a.student_id),
                          );
                        } else {
                          setSelectedStudents(originallySelectedStudent.current);
                        }
                      }}
                      checked={
                        assignment.assessments.length === selectedStudents.length
                      }
                    />
                  </th>
                )}
                <th style={styles.th}>
                  <SortStudentComponent style={{marginRight: '6px'}}/>
                  {getTranslation('student')}
                </th>
                <th style={styles.header}>{getTranslation('status')}</th>
                <th style={styles.header}>
                  {getTranslation('overallResultHeaderStudent')}
                </th>
                {assignment?.objectiveAssessments?.length > 0 && (
                  <th style={styles.header}>
                    {getTranslation('objectivesText')}
                  </th>
                )}
              </tr>
              {sortedAssessments.map((assessment, i) => {
                const bgColor = i === highlight ? colors.grey : colors.white;
                let selected = assessment.student_id === selectedStudentId;
                if (multiEdit) {
                  selected = selectedStudents.find(
                    (id) => id === assessment.student_id,
                  );
                }
                return (
                  <tr
                    onMouseEnter={() => setHighlight(i)}
                    onMouseLeave={() => setHighlight(null)}
                    onClick={() => studentClicked(assessment.student_id)}
                    key={assessment.id}
                    style={{
                      backgroundColor: selected ? colors.borderColor : bgColor,
                      cursor: 'pointer',
                      height: '50px',
                    }}
                  >
                    {multiEdit && (
                      <td>
                        <div
                          style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            width: '36px',
                            border: '0px solid',
                          }}
                        >
                          <GreenCheckbox
                            inputProps={{'aria-label': 'controlled'}}
                            checked={selected || false}
                            onChange={(e) => {
                              if (e.target.checked) {
                                setSelectedStudents([
                                  ...selectedStudents,
                                  assessment.student_id,
                                ]);
                              } else {
                                setSelectedStudents(
                                  selectedStudents.filter(
                                    (id) => id !== assessment.student_id,
                                  ),
                                );
                              }
                            }}
                          />
                        </div>
                      </td>
                    )}
                    <td style={{}}>
                      <Tooltip title={assessment?.email}>
                      <span style={styles.studentNameText}>
                        {assessment.first_name} {assessment.last_name}
                      </span>
                      </Tooltip>
                    </td>
                    <td>
                      <div style={styles.container3}>
                        <Tooltip title={getStatusText(assessment.status_new)}>
                          <div style={styles.iconContainer}>
                            {getAssessmentIcon({status: assessment.status_new, style: styles.assessmentIcon})}
                          </div>
                        </Tooltip>
                        {assignment.use_submitted_status &&
                        <Tooltip title={getSubmittedStatusText(assessment.submitted_status)}>
                          <div style={styles.iconContainer}>
                            {getSubmittedIcon({status: assessment.submitted_status})}
                          </div>
                        </Tooltip>
                        }
                      </div>
                    </td>

                    <td>
                      <GridSummaryItem assessment={assessment}/>
                    </td>
                    {assignment?.objectiveAssessments?.length > 0 && (
                      <td>
                        <GridObjectivesSummaryItem
                          selected={selected}
                          assessments={assignment?.objectiveAssessments.filter(
                            (o) => o.student_id === assessment.student_id,
                          )}
                        />
                      </td>
                    )}
                  </tr>
                );
              })}
            </table>
            <div style={styles.block}/>
          </div>
          {renderAssignments &&
          <>
            {sortedAssignments?.map((ass) => {
              return (
                <AssignmentTable
                  students={students}
                  key={ass.id}
                  setHighlight={setHighlight}
                  studentClicked={studentClicked}
                  assignment={ass}
                  onAssignmentClicked={onAssignmentClicked}
                  selectedStudents={selectedStudents}
                  highlight={highlight}
                  selectedStudentId={selectedStudentId}
                />
              );
            })}</>}
        </div>
      </div>
      <div style={styles.shadow}/>
    </div>
  );
};

export default GridViewContent;
