import React, {useState} from 'react';
import { colors } from "../theme";
import { Radio } from "@material-ui/core";

const style = {
  statusButton: {
    display: "flex",
    width: "110px",
    height: "30px",
    borderRadius: "6px",
    cursor: "pointer",
    backgroundColor: colors.ewsGreen,
    alignItems: "center",
    justifyContent: "center",
    marginRight: "10px",
    marginTop: "4px",
  },
};



const EwsStatusButton = (props) => {
  const {value, saveStatus, status, icon, selectedColor, color, multiEdit, selectedMulti} = props;
  const [selected, setSelected] = useState(false);
  return (
    <div style={{ display: "flex" }}>
      {multiEdit && <Radio checked={selectedMulti} onClick={() => {
        saveStatus({ ...status, status: value });
      }}/>}
      <div
        onClick={() => {
          saveStatus({ ...status, status: value });
        }}
        onMouseEnter={() => setSelected(true)}
        onMouseLeave={() => setSelected(null)}
        style={{
          ...style.statusButton,
          backgroundColor:
            selected ? selectedColor : color,
        }}
      >
        {icon}
      </div>
    </div>
  );
};

export default EwsStatusButton;
