import { StateParams, StateService } from '@uirouter/core';
import { IScope, ITimeoutService } from 'angular';
import { IBreadcrumbsService } from '../../../../../common/components/breadcrumbs/breadcrumbs.service';
import { AssignmentsComponent } from '../../../../../shared/components/assignments/assignments.component';
import { AssignmentFilterGroup } from '../../../../../shared/enums/assignmentFilterGroup.enum';
import { AssignmentType } from '../../../../../shared/enums/assignmentType.enum';
import { Breadcrumb, IBreadcrumb } from '../../../../../shared/models/breadcrumb.model';
import { IObjectiveAssignmentAssessment } from '../../../../../shared/models/objectiveAssignmentAssessment.model';
import { LocalStorageService } from '../../../../../shared/services/localStorage/localStorage.service';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherCourseProgressModule, { IAssignmentOverviewService } from '../../teacher-course-progress.module';
import style from './assignmentsOverview.scss';
import template from './assignmentsOverview.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'assignments-overview',
  template: template,
  style: style,
})
export class AssignmentsOverviewComponent extends AssignmentsComponent {
  @Input()
  public objectiveAssignmentAssessments: IObjectiveAssignmentAssessment[];

  public filters: any[];

  protected readonly statePrefix: string = 'StudentProgressStudentDetailsAssignments';

  constructor(protected AssignmentOverviewService: IAssignmentOverviewService, protected BreadcrumbsService: IBreadcrumbsService,
              protected LocalStorageService: LocalStorageService, protected $state: StateService, protected $stateParams: StateParams,
              protected $scope: IScope, protected $timeout: ITimeoutService) {
    'ngInject';

    super($scope, $stateParams, $state, LocalStorageService);

    this.objectiveAssignmentAssessments = [];
    this.filters = [
      {
        group: AssignmentFilterGroup.TYPE,
        type: AssignmentType.WORK_UNIT,
        label: 'assignmentActionMenuFilterWorkUnit',
      },
      {
        group: AssignmentFilterGroup.TYPE,
        type: AssignmentType.STANDARD_ASSIGNMENT,
        label: 'assignmentActionMenuFilterAssignments',
      },
    ];
  }

  public toggleObjectiveAssessment(objectiveAssessment: IObjectiveAssignmentAssessment): void {
    objectiveAssessment.$$open = !objectiveAssessment.$$open;
  }

  public onClickHeader(objectiveAssessment: IObjectiveAssignmentAssessment): void {
    this.goToDetails(objectiveAssessment);
  }

  public onClickGoToDetails(objectiveAssessment: IObjectiveAssignmentAssessment): void {
    this.onClickClose(objectiveAssessment);
    this.goToDetails(objectiveAssessment);
  }

  public onClickClose(objectiveAssessment: IObjectiveAssignmentAssessment): void {
    objectiveAssessment.$$open = false;
  }

  public onChange(changedObjectiveAssignmentAssessment: IObjectiveAssignmentAssessment): void {
    this.AssignmentOverviewService
      .changeObjectiveAssignmentAssessment(changedObjectiveAssignmentAssessment, this.$stateParams.course.course_definition)
      .then((newObjectiveAssignmentAssessment: IObjectiveAssignmentAssessment): void => {
        this.objectiveAssignmentAssessments = this.AssignmentOverviewService
          .updateListOfObjectiveAssignmentAssessments(newObjectiveAssignmentAssessment, this.objectiveAssignmentAssessments);
      });
  }

  public expandAll(): void {
    this.changeOpenStateForAll(true);
  }

  public collapseAll(): void {
    this.changeOpenStateForAll(false);
  }

  private changeOpenStateForAll(state): void {
    this.$timeout(() => {
      this.objectiveAssignmentAssessments = this.objectiveAssignmentAssessments.map((assessment: IObjectiveAssignmentAssessment) => {
        assessment.$$open = state;

        return assessment;
      });
    }, 0);
  }

  private goToDetails(objectiveAssessment: IObjectiveAssignmentAssessment): void {
    const breadcrumbs: IBreadcrumb[] = [
      ...this.$stateParams.breadcrumbs,
      new Breadcrumb(
        this.$stateParams.course.course_definition_name,
        this.BreadcrumbsService.createGoToAction(),
      ),
    ];

    this.$state.go('teacher-course-progress.details.learning-objectives.assignmentOverview',
      Object.assign({}, this.$stateParams, { objectiveAssessment, breadcrumbs }));
  }
}
