import React, { useState, useEffect, useReducer } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { ArrowDropDown } from '@material-ui/icons';
import { ClickAwayListener } from '@material-ui/core';
import { colors, fonts } from '../theme';
import { ReactComponent as DoneIcon } from '../images/icon_bk_assessment_done.svg';
import { ReactComponent as WarningIcon } from '../images/icon_warning.svg';
import { ReactComponent as NotDoneIcon } from '../images/icon_bk_not_assessed.svg';
import { ReactComponent as AccessingIcon } from '../images/icon_bk_assessing.svg';
import {
  getAssessmentIcon,
  NOT_SUBMITTED,
  NOT_SUBMITTED_LATE,
  SUBMITTED,
  SUBMITTED_LATE
} from '../utils';
import { getTranslation } from '../i18n';
import {ReactComponent as SubmittedIcon} from "../images/submitted.svg";
import {ReactComponent as SubmittedLateIcon} from "../images/submitted-late.svg";
import {ReactComponent as NotSubmittedLateIcon} from "../images/not-submitted-late.svg";
import {ReactComponent as NotSubmittedIcon} from "../images/not-submitted.svg";

const ASSESSMENT_DONE = 'ASSESSMENT_DONE';
const ASSESSING = 'ASSESSING';
const NOT_ASSESSED = 'NOT_ASSESSED';

const options = [
  {
    title: getTranslation('teacherAssignmentsAssessmentsStudentMenuAssessed'),
    icon: (
      <DoneIcon fill={colors.green} style={{ border: '0px solid green' }} />
    ),
    value: ASSESSMENT_DONE,
  },
  {
    title: getTranslation('teacherAssignmentsAssessmentsStudentMenuAssessing'),
    icon: <AccessingIcon fill={colors.orange} />,
    value: ASSESSING,
  },
  {
    title: getTranslation('teacherAssignmentsAssessmentsStudentMenuNotAssessed'),
    icon: (
      <NotDoneIcon
        fill={colors.darkGrey}
        style={{ border: '0px solid green' }}
      />
    ),
    value: NOT_ASSESSED,
  },
  {
    title:'divider',
  },
  {
    title: getTranslation('mark_submitted_status_submitted'),
    icon: <SubmittedIcon/>,
    value: SUBMITTED,
  },
  {
    title: getTranslation('mark_submitted_status_submitted_late'),
    icon: <SubmittedLateIcon/>,
    value: SUBMITTED_LATE,
  },
  {
    title: getTranslation('mark_submitted_status_not_submitted_late'),
    icon: <NotSubmittedLateIcon/>,
    value: NOT_SUBMITTED_LATE,
  },
  {
    title: getTranslation('mark_submitted_status_not_submitted'),
    icon: <NotSubmittedIcon/>,
    value: NOT_SUBMITTED,
  }
];

// Only use submittedStatus with button without text
const AssessmentButton = (props) => {
  const {
    initialState, warning, onStatusChange, button, style = {}, readonly = false, submittedStatus = false,
  } = props;
  const [anchorEl, setAnchorEl] = useState(null);
  const [state, setState] = useState(initialState);
  const open = Boolean(anchorEl);

  const getAssessmentStatusTitle = () => {
    switch (state) {
      case ASSESSMENT_DONE:
        return getTranslation('teacherAssignmentsAssessmentManyStudentStatusLabel_ASSESSMENT_DONE');
      case NOT_ASSESSED:
        return getTranslation('teacherAssignmentsAssessmentManyStudentStatusLabel_NOT_ASSESSED');
      case ASSESSING:
        return getTranslation('teacherAssignmentsAssessmentManyStudentStatusLabel_ASSESSING');
      default:
        return getTranslation('teacherAssignmentsAssessmentManyStudentStatusLabel_NOT_ASSESSED');
    }
  };

  useEffect(() => {
    setState(initialState);
  }, [initialState]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleSelect = (option) => {
    setAnchorEl(null);
    setState(option.value);
    if (onStatusChange) {
      onStatusChange(option.value);
    }
  };

  const handleClickAway = (event) => {
    setTimeout(() => { setAnchorEl(null); });
  };

  if (readonly) {
    return (
      <div style={{ display: 'flex', alignItems: 'center' }}>
        {getAssessmentIcon({ status: state })}
        <span style={{ ...fonts.MEDIUM, marginLeft: '10px' }}>{getAssessmentStatusTitle()}</span>
      </div>
    );
  }

  const activeOptions = submittedStatus?options:options.slice(0,3);

  return (
    <div style={style}>
      <ClickAwayListener onClickAway={handleClickAway}>
        {button ? (
          <div onClick={handleClick}>{button}</div>
        ) : (
          <div
            onClick={handleClick}
            style={{
              display: 'flex',
              justifyContent: 'space-between',
              border: '0px solid red',
              flex: 1,
              height: '35px',
              backgroundColor: colors.white,
              borderRadius: '35px',
              cursor: 'pointer',
              boxShadow: '0px 2px 3px 0px rgba(0, 0, 0, 0.3)',
              alignItems: 'center',
              paddingLeft: '16px',
              paddingRight: '16px',
            }}
          >
            <div style={{ display: 'flex', alignItems: 'center' }}>
            {getAssessmentIcon({ status: state })}
            <span style={{ ...fonts.MEDIUM, marginLeft: '10px' }}>{getAssessmentStatusTitle()}</span>
            </div>
            <ArrowDropDown style={{ color: colors.darkGrey }} />
            {warning && state === ASSESSMENT_DONE && false && (
              <WarningIcon
                fill={colors.red}
                style={{ position: 'absolute', left: '25px', bottom: '6px' }}
              />
            )}
          </div>
        )}
      </ClickAwayListener>
      <Menu open={open} anchorEl={anchorEl}>
        {activeOptions.map((option) => {
          if(option.title === 'divider'){
            return <div style={{height:'1px', width:'100%', backgroundColor: colors.borderColor}}/>;
          }
          return (
            <MenuItem
              key={option.title}
              onClick={() => handleSelect(option)}
              style={{display: 'flex', alignItems: 'center', padding: '10px'}}
            >
              {option.icon}
              <span
                style={{
                  ...fonts.MEDIUM,
                  marginLeft: '16px',
                  border: '0px solid red',
                }}
              >
                {option.title}
              </span>
            </MenuItem>
          );
        })}
      </Menu>
    </div>
  );
};

export default AssessmentButton;
