/**
 * @module StudybeeHandle/Service
 * @description
 *   Studybee Handle.
 */

function StudybeeHandle($rootScope, $mdSidenav, Path, UserInfo, Hint, Mixpanel) {
  'ngInject';

  const beforeCloseCallbacks = [];
  const inProgressCloseCallbacks = [];
  const afterCloseCallbacks = [];
  const beforeOpenCallbacks = [];
  const inProgressOpenCallbacks = [];
  const afterOpenCallbacks = [];
  const disableCallbacs = [];
  const enableCallbacs = [];

  let firstInit = true;
  let animationInProgress = false;
  let extendedViewEnable = false;

  return {
    /**
     * Set callback which is trigger before extension close
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setBeforeCloseCallback(callback) {
      beforeCloseCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger in progress extension close
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setInProgressCloseCallback(callback) {
      inProgressCloseCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger after extension close
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setAfterCloseCallback(callback) {
      afterCloseCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger before extension open
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setBeforeOpenCallback(callback) {
      beforeOpenCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger in progress extension open
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setInProgressOpenCallback(callback) {
      inProgressOpenCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger after extension open
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setAfterOpenCallback(callback) {
      afterOpenCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger when extension is disable
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setDisableCallback(callback) {
      disableCallbacs.push(callback);
    },

    /**
     * Set callback which is trigger when extension is enable
     * @memberof module:StudybeeHandle/Service
     * @param {Object} callback Callback function
     */
    setEnableCallback(callback) {
      enableCallbacs.push(callback);
    },

    /**
     * Open extension
     * @memberof module:StudybeeHandle/Service
     * @param {Number} inProgressDelay The number of miliseconds that script should wait before trigger after callbacks
     * @param {Number} afterDelay The number of miliseconds that script should wait before trigger after callbacks
     */
    studybeeOpen(inProgressDelay = 250, afterDelay = 750) {
      animationInProgress = true;

      if (firstInit) {
        afterDelay = 1500;
        firstInit = false;
      } else {
        if (typeof inProgressDelay !== 'number') {
          inProgressDelay = 250;
        }
        if (typeof afterDelay !== 'number') {
          afterDelay = 750;
        }
      }

      beforeOpenCallbacks.forEach((elem) => {
        elem();
      });

      setTimeout(() => {
        inProgressOpenCallbacks.forEach((elem) => {
          elem();
        });

        $mdSidenav('studybee').open();
        $rootScope.extendedViewEnable = extendedViewEnable;
      }, inProgressDelay);

      setTimeout(() => {
        afterOpenCallbacks.forEach((elem) => {
          elem();
        });

        animationInProgress = false;
      }, afterDelay);
    },

    /**
     * Close extension
     * @memberof module:StudybeeHandle/Service
     * @param {Number} inProgressDelay The number of miliseconds that script should wait before trigger after callbacks
     * @param {Number} afterDelay The number of miliseconds that script should wait before trigger after callbacks
     */
    studybeeClose(inProgressDelay = 250, afterDelay = 750) {
      animationInProgress = true;

      if (firstInit) {
        afterDelay = 1500;
        firstInit = false;
      }

      beforeCloseCallbacks.forEach((elem) => {
        elem();
      });

      setTimeout(() => {
        inProgressCloseCallbacks.forEach((elem) => {
          elem();
        });

        $mdSidenav('studybee').close();
        extendedViewEnable = $rootScope.extendedViewEnable;
        $rootScope.extendedViewEnable = false;
      }, inProgressDelay);

      setTimeout(() => {
        afterCloseCallbacks.forEach((elem) => {
          elem();
        });

        animationInProgress = false;
      }, afterDelay);
    },

    /**
     * Toggle extension
     * @memberof module:StudybeeHandle/Service
     */
    studybeeToggle() {
      Mixpanel.track('toggle');
      if (!this.checkIfExtensionAvailable() || animationInProgress) {
        return;
      }

      if (this.studybeeIsOpen()) {
        // this needs to be optimized
        this.studybeeClose();
      } else {
        this.studybeeOpen();
      }

      Hint.displayToggleHint();
    },

    studybeeActivate() {
      if (this.studybeeIsOpen()) {
        this.studybeeOpen();
      } else {
        this.studybeeClose();
      }
    },

    /**
     * Check if extension is open
     * @memberof module:StudybeeHandle/Service
     * @return {Boolean}
     */
    studybeeIsOpen() {
      return $mdSidenav('studybee').isOpen();
    },

    /**
     * Check if extension should be available
     * @memberof module:StudybeeHandle/Service
     */
    checkIfExtensionAvailable() {
      let extensionAvailable = true;
      if (
        Path.isClassroomInstance() &&
        UserInfo.userMatched &&
        UserInfo.loggedToBrowser &&
        (
          Path.isClassesListPage() ||
          Path.isSettingsPage() ||
          Path.isWorkPage() ||
          Path.isCalendarPage())
      ) {
        extensionAvailable = false;
        this.studybeeClose();
      }

      return extensionAvailable;
    },

    /**
     * Set icon status (enable / disable)
     * @memberof module:StudybeeHandle/Service
     */
    setIconStatus() {
      if (this.checkIfExtensionAvailable()) {
        enableCallbacs.forEach((elem) => {
          elem();
        });
      } else {
        disableCallbacs.forEach((elem) => {
          elem();
        });
      }
    },
  };
}

module.exports = StudybeeHandle;
