import { IStudent } from '../../shared/models/student.model';

export class Students {
  public students: IStudent[];

  constructor(protected Path: any, protected FetchData: any) {
    'ngInject';

    this.students = [];
  }

  public updateStudentsList(updatedStudents: IStudent[]) {
    this.students = updatedStudents;
  }

  public getStudentsList(): Promise<IStudent[]> {
    return this.students.length ?
      Promise.resolve([...this.students]) :
      this.FetchData.getStudentsList(this.Path.getClassroomId())
        .then((students: IStudent[]): IStudent[] => {
          this.students = students;

          return [...students];
        });
  }

  public getStudentsIds(): number[] {
    return this.students.map(student => student.id);
  }
}
