/**
 * @module Locale/Service
 * @description
 *   Detect Chrome UI language and set Moment.js locale
 */

import moment from 'moment';

export class Locale {
  setMomentLocale() {
    moment.locale(chrome.i18n.getUILanguage());
  }

  static getISOLanguageCode(languageCode = chrome.i18n.getUILanguage()) {
    return languageCode.replace(/(\w+)-(\w+)/, '$1').toLocaleLowerCase();
  }
}
