import {Injectable} from '@angular/core';
import {AssessmentsStore} from './assessments.store';
import {
  Assessment,
  AssessmentStatusUpdateBody,
  AssessmentStatusUpdateResponse,
  AssessmentsUI,
  AssessmentVisibility
} from './assessment.model';
import {HttpService} from '../../../../../core/http/http.service';
import {Path} from '../../../../../ajs-upgraded-providers';
import {ID} from '@datorama/akita';
import {map, tap} from 'rxjs/operators';
import {Assignment} from '../../../state';
import {normalizeUpdateStatusForManyResponse} from './assessments.normalizer';

@Injectable({ providedIn: 'root' })
export class AssessmentsService {

  constructor(protected assessmentsStore: AssessmentsStore,
              protected path: Path,
              protected http: HttpService) {
  }

  update(assessment: Assessment) {
    this.assessmentsStore.update(assessment.id, state => ({
      ...state,
      ...assessment
    }));
  }

  updateStatus(assignmentID: ID, assessment: Assessment, statusUpdateBody: AssessmentStatusUpdateBody) {
    return this.updateStatusAPI(assignmentID, assessment.student_id, statusUpdateBody).pipe(
      map(response => {
       const updatedAssessment = {
         ...assessment,
         status_new: statusUpdateBody.status,
         fail_warning: response ? response.fail_warning : assessment.fail_warning,
       };
       this.update(updatedAssessment);

       return updatedAssessment;
     })
    );
  }

  updateStatusForMany(assignment: Assignment, assessmentVisibility: AssessmentVisibility) {
    return this.updateStatusForManyAPI(assignment.id, assessmentVisibility).pipe(
      map(response => {
        const updates = response || assignment.assessments
          .filter(assessment => assessmentVisibility.student_ids.includes(assessment.student_id))
          .map(assessment => ({id: assessment.id, fail_warning: assessment.fail_warning}));
        const normalizeUpdates = normalizeUpdateStatusForManyResponse(updates);

        return [normalizeUpdates.entities.updates, normalizeUpdates.result];
      }),
      tap(([updates, assessmentsID]) => {
        this.assessmentsStore.upsert(assessmentsID, state => {
          return {
            ...state,
            status_new: assessmentVisibility.status,
            fail_warning: (updates[state.id] || state).fail_warning,
          };
        });
      }));
  }

  synchronizeWarningStatus(assessment: Assessment) {
    return this.checkWarningStatusAPI(assessment.id).pipe(
      map(response => ({...assessment, ...response})),
      tap(updatedAssessment => this.update(updatedAssessment)),
    );
  }

  checkWarningStatus(assessment: Assessment) {
    return this.checkWarningStatusAPI(assessment.id).pipe(
      map(response => response.fail_warning),
    );
  }

  setSorting(sort: AssessmentsUI['sort']) {
    this.assessmentsStore.update(state => ({
      ...state,
      ui: {
        ...state.ui,
        sort,
      },
    }));

    this.assessmentsStore.forceRefreshEntities();
  }

  protected updateStatusAPI(assignmentID: ID, studentID: ID, statusUpdateBody: AssessmentStatusUpdateBody) {
    const courseID: ID = this.path.getClassroomId();
    const url = `/v3/classes/${courseID}/assignments/${assignmentID}/status/${studentID}/`;

    return this.http.patch<AssessmentStatusUpdateResponse>(url, {...statusUpdateBody});
  }

  protected updateStatusForManyAPI(assignmentID: ID, assignmentVisibility: AssessmentVisibility) {
    const courseID: string = this.path.getClassroomId();
    const url = `/v3/classes/${courseID}/assignments/${assignmentID}/status/`;

    return this.http.patch<AssessmentStatusUpdateResponse[]>(url, assignmentVisibility);
  }

  protected checkWarningStatusAPI(assessmentID: ID) {
    const url = `/v3/classes/fail-warning/${assessmentID}/`;

    return this.http.get<AssessmentStatusUpdateResponse>(url);
  }
}
