import { IObserver, IUnsubscribe } from './observer.service';

export interface ISubject<T = any> {
  observers: IObserver<T>[];
  subscribe(observer: IObserver<T>): IUnsubscribe;
  unsubscribe(observer: IObserver<T>): void;
  next(value: T): void;
  dispose(): void;
}

export class Subject<T = any> implements ISubject<T> {
  public observers: IObserver<T>[] = [];

  public subscribe(observer: IObserver<T>): IUnsubscribe {
    observer.closed = false;
    this.observers.push(observer);

    return (): void => {
      this.unsubscribe(observer);
    };
  }

  public unsubscribe(observer: IObserver<T>): void {
    observer.closed = false;
    this.observers = this.observers.filter((_observer: IObserver<T>): boolean => !_observer.closed && !(_observer === observer));
  }

  public next(value: T): void {
    this.observers.forEach((observer: IObserver<T>): void => {
      if (!observer.closed) {
        observer.next(value);
      }
    });
  }

  public dispose(): void {
    this.observers.forEach(this.unsubscribe.bind(this));
    this.observers = [];
  }
}
