import angular, { IPromise, IRootScopeService } from 'angular';
import { Events } from '../../../../../../common/global-events';
import { UnsubscribableComponent } from '../../../../../../shared/components/abstracts/unsubscribable.component';
import { ListViewType } from '../../../../../../shared/enums/listViewType.enum';
import { Component } from '../../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../../utils/decorators/input.decorator';
import commonStyle from '../../../../common/styles/objectives-list.scss';
import { showDeleteObjectiveDialog } from '../../../shares/helpers.dialog';
import TeacherCourseManagementModule from '../../../teacherCourseManagement.module';
import style from './cmTeacherObjectivesList.scss';
import template from './cmTeacherObjectivesList.tpl.html';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'cm-teacher-objectives-list',
  template: template,
  style: [commonStyle, style]
})
export class CmTeacherObjectivesListComponent extends UnsubscribableComponent {
  @Input()
  public course: any;
  @Input()
  public view: ListViewType;

  public readonly listViewType: any;
  public objectives: any[];

  constructor(private $rootScope: IRootScopeService, private $mdDialog: angular.material.IDialogService,
              private FetchData: any, private NavigationHistory: any) {
    'ngInject';

    super();
    this.course = null;
    this.view = ListViewType.TITLE_AND_DESCRIPTION;
    this.listViewType = ListViewType;
    this.objectives = [];
  }

  public $onInit(): void {
    this.updateObjectives();

    const updateObjectivesHandler = this.$rootScope.$on(
      Events.UPDATED_OBJECTIVES,
      () => this.updateObjectives(),
    );

    const deleteObjectivesHandler = this.$rootScope.$on(
      Events.COURSE_DELETED,
      (event, data) => {
        if (data.courseDefinition.id === this.course.id) {
          this.NavigationHistory.go('teacherCourseManagement');
        }
      },
    );

    this.$$unsubscribes.push(updateObjectivesHandler, deleteObjectivesHandler);
  }

  public onClickDelete(objective): void {
    showDeleteObjectiveDialog(this.$mdDialog, objective, this.course)
      .then((): IPromise<void> => this.FetchData.v3.deleteObjective(this.course, objective))
      .then((): void => {
        this.objectives = this.objectives.filter(item => item.id !== objective.id);
      });
  }

  private updateObjectives() {
    this.FetchData.v3.getObjectives(this.course).then(
      (objectives: any[]): void => {
        this.objectives = objectives;
      },
    );
  }
}
