  static ASSIGNMENT_MODAL_ASK_SEL: string[] = [...generateSelectors('ask', `[role="dialog"] span:textEquals("${HostPage.LABEL_PLACEHOLDER}")`)];

  // this is delete assignment from the stream
  static ASSIGNMENT_MODAL_DELETE_SEL: string[] = [...generateSelectors('delete', `[role="alertdialog"] span:textEquals("${HostPage.LABEL_PLACEHOLDER}")`)];

  static ASSIGNMENT_MENU_ITEM_ASSIGN_SEL: string[] = [...generateSelectors('assign', `[role="menu"] div:textEquals("${HostPage.LABEL_PLACEHOLDER}")`)];

  static ASSIGNMENT_MENU_ITEM_SCHEDULE_SEL: string[] = [...generateSelectors('schedule', `[role="menu"] div:textEquals("${HostPage.LABEL_PLACEHOLDER}")`)];

  static ASSIGNMENT_MENU_ITEM_SAVE_DRAFT_SEL: string[] = [...generateSelectors('saveDraft', `[role="menu"] div:textEquals("${HostPage.LABEL_PLACEHOLDER}")`)];

