import { Service } from '../../../utils/decorators/service.decorator';
import SharedServicesModule, { IPopupService } from '../services.module';
import { TabService } from '../tab/tab.service';

@Service({
  module: SharedServicesModule,
  name: 'HelpButtonService',
})
export class HelpButtonService {
  public tab: chrome.tabs.Tab;

  constructor(private PopupService: IPopupService, private TabService: TabService) {
    'ngInject';
  }

  public open(url: string, isPopup: boolean): void {
    if (isPopup) {
      this.openPopup(url);
    } else {
      this.openTab(url);
    }
  }

  protected openPopup(url: string): void {
    this.PopupService.open(url);
  }

  protected openTab(url: string): void {
    if (this.tab) {
      this.TabService.get(this.tab).then((tab: chrome.tabs.Tab): void => {
        if (tab) {
          this.TabService.update(tab, { active: true, url }).then(this.storeTab.bind(this));
        } else {
          this.TabService.open(url).then(this.storeTab.bind(this));
        }
      });
    } else {
      this.TabService.open(url).then(this.storeTab.bind(this));
    }
  }

  protected storeTab(tab: chrome.tabs.Tab): void {
    this.tab = tab;
  }
}
