import { GradeDescription } from '../services/gradeDescription.service';
import { UnbindableCtrlBase } from '../../../../common/base-classes';

class GradeDescriptionCtrl extends UnbindableCtrlBase {
  constructor($scope, $element) {
    'ngInject';

    super();
    this.$scope = $scope;
    this.$element = $element;
    this.description = null;
    this.gradesMap = new Map();
    this.animationTime = 0;
  }

  $onInit() {
    this.gradesMap = GradeDescription.mapAssessmentToGrades(this.assessment);
    this.setDescription();

    this.boundEvents.push(
      this.$scope.$watch(() => this.assessment.grade, this.setDescription.bind(this)),
    );
  }

  $onChanges(changes) {
    const filteredChanges = GradeDescription.filterNotFirstTimeChanges(changes);

    if (filteredChanges.assessment) {
      this.assessment = filteredChanges.assessment.currentValue;
      this.gradesMap = GradeDescription.mapAssessmentToGrades(this.assessment);
      this.setDescription();
    }
  }

  setDescription() {
    const currentGrade = this.assessment.grade ?
      this.gradesMap.get(this.assessment.grade.id) : null;

    this.description = currentGrade ? currentGrade.description : null;
    GradeDescription.animateContent(this.$element, this.animationTime);
    this.animationTime = 200;
  }
}

export const GradeDescriptionComponent = {
  bindings: {
    assessment: '<',
  },
  controller: GradeDescriptionCtrl,
  template:
    `
        <md-content class="grade-description__text"
                    ng-bind-html="$ctrl.description">
        </md-content>
    `,
};
