import React, {memo} from "react";
import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import noteImage from '../images/png/sb-ico-note.png';
import feedbackImage from '../images/png/sb-ico-feedback.png';
import Tooltip from "./tooltip";
import Icon from "./icon";
import PublishedIcon from "./publishedIcon";

const styles = {
  item: {
    display: "flex",
    width: "40px",
    height: "50px",
    // border: "0px solid red",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
  },
  icon: {height: "20px", width: "20px"},
  iconWMargin: {
    height: "20px",
    width: "20px",
    marginBottom: "10px",
  },
};

const GridObjectivesSummaryItem = (props) => {
  let {assessments, objectives, selected, selectedObjective = null, gradeOnly = true} = props;
  if (!objectives) {
    objectives = assessments.map((a) => ({
      name: a.objectiveName,
      id: a.objective_id,
    }));
  }
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-around",
        padding: "0px",
        borderRight: `1px solid ${colors.borderColor}`,
      }}
    >
      {objectives.sort((a, b) => (a.name.localeCompare(b.name, undefined, {
        numeric: true,
        sensitivty: 'base'
      }))).map((o) => {
        const assessment = assessments.find((a) => a.objective_id === o.id) || {
          id: Math.random(),
          grade: -1,
        };
        const connectedToObjective = assessment?.grade !== -1;
        const grade = assessment?.grade?.name || "-";
        const isSelectedObjective = selectedObjective
          ? o.id === selectedObjective?.id
          : true;
        return (
          <div
            key={o.id}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRight: `${gradeOnly ? 0 : 1}px solid ${
                selected ? colors.borderColorDark : colors.borderColor
              }`,
            }}
          >
            <Tooltip key={assessment.id} title={o.name}>
              <div style={{...styles.item, position: 'relative', width: '40px'}}>
                <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                <span
                  style={{
                    ...fonts.LARGE,
                    fontWeight: "800",
                    color: assessment?.grade?.color || colors.iconColorLight,
                  }}
                >
                  {connectedToObjective ? grade : ""}
                </span>
                  {connectedToObjective &&
                  <PublishedIcon published={assessment.grade_published} active={!!assessment?.grade?.name}/>
                  }
                </div>
                {!isSelectedObjective
                && <div style={{
                  position: 'absolute', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: '#888888CC',
                }}/>}
              </div>
            </Tooltip>
            {!gradeOnly &&
            <Tooltip title={getTranslation("overallFeedback")}>
              <div style={styles.item}>
                <Icon file={feedbackImage} active={assessment.feedback} style={styles.icon}/>
                <PublishedIcon published={assessment.feedback_published} active={!!assessment.feedback}/>
              </div>
            </Tooltip>}
            {!gradeOnly && <Tooltip title={getTranslation("overallPrivateNotesMode")}>
              <div style={{...styles.item}}>
                <Icon
                  file={noteImage}
                  active={assessment.private_note}
                  style={styles.iconWMargin}/>
              </div>
            </Tooltip>}
          </div>
        );
      })}
    </div>
  );
};

export default GridObjectivesSummaryItem;
