import React, { useState } from 'react';
import { colors, fonts } from '../theme';
import { getTranslation } from '../i18n';

const dimmedFont = {
  ...fonts.MEDIUM,
  color: colors.borderColorDark,
};

const CourseItem = (props) => {
  const {
    teachers, classroomName, course, onClick,
  } = props;

  const [selected, setSelected] = useState(false);

  return (
    <div
      onMouseEnter={() => setSelected(true)}
      onMouseLeave={() => setSelected(false)}
      style={{
        display: 'flex',
        flexDirection: 'column',
        border: `1px solid ${colors.borderColor}`,
        padding: '16px',
        margin: '4px',
        cursor: 'pointer',
        backgroundColor: selected ? colors.grey : colors.white,
      }}
      onClick={onClick}
    >
      <span style={{ ...fonts.MEDIUM, fontWeight: '800', marginBottom: '6px' }}>
        {course.name}
      </span>
      <span style={dimmedFont}>Id: {course.code}</span>
      <span style={dimmedFont}>
        {getTranslation('classroom')}: {classroomName}
      </span>
      <div>
        <span style={dimmedFont}>{getTranslation('teacher')}: </span>
        {teachers?.map((t) => (
          <span key={t.id} style={dimmedFont}>
            {t.first_name} {t.last_name}{' '}
          </span>
        ))}
      </div>
    </div>
  );
};
export default CourseItem;
