import { IPromise } from 'angular';
import { StateParams } from '@uirouter/core';
import { APICurriculumObject } from '../../../common/types/api_types';
import { ICurriculumsService } from '../common/services/curriculums.service';
import { StateProvider } from '@uirouter/angularjs';

export const routerConfig: Function = ($stateProvider: StateProvider): void => {
  'ngInject';

  $stateProvider
    .state('teacherCourseManagement', {
      component: 'teacherCourseManagement',
      resolve: {
        curriculums: (CurriculumsService: ICurriculumsService): IPromise<any[]> => {
          'ngInject';

          return CurriculumsService.updateCurriculums();
        },
      },
    })
    .state('teacherCourseManagement.details', {
      params: { course: null },
      resolve: {
        course: ($stateParams: StateParams, FetchData: any, Path: any): IPromise<any> => {
          'ngInject';

          return FetchData.v3.getCourseDefinition(Path.getClassroomId(), $stateParams.course.id);
        },
        isLocked: (curriculums: APICurriculumObject[]): boolean => {
          'ngInject';

          return curriculums.length > 1;
        },
      },
      views: {
        'extended@': {
          component: 'cmTeacherCourseDetails',
        },
      },
    })
    .state('teacherCourseManagement.details.learningObjectives', {
      views: {
        'details@teacherCourseManagement.details': {
          component: 'cmTeacherObjectives',
        },
      },
    });
};
