export class UserSetupSettingsController {
  constructor(Mixpanel, UserSetupSettings) {
    'ngInject';

    this.Mixpanel = Mixpanel;
    this.UserSetupSettings = UserSetupSettings;
  }

  $onInit() {
    // silly check for setup type - should be moved to the separated state but
    // right know I don't have time for that
    this.isUpdateSetupType = this.UserSetupSettings.isUpdateState();

    // current preferences model
    this.userPreferences = {
      lastEducationalSystem: null,
    };

    // fetch current settings when editing
    if (this.isUpdateSetupType) {
      this.fetchCurrentSetting();
    }
  }

  fetchCurrentSetting() {
    this.UserSetupSettings.getUserPreferences(this.educationalSystems).then(
      (preferences) => {
        this.userPreferences = preferences;

        if (this.isUpdateSetupType) {
          this.initialPreferences = Object.assign({}, this.userPreferences);
        }
      },
    );
  }

  /**
   * reset the school choice when the educational system is changed
   */
  changeEducationalSystem() {
    this.userPreferences.school = null;
  }

  filterSchool(query) {
    return this.UserSetupSettings.getSchoolByQuery(query, this.userPreferences);
  }

  savePreferences() {
    if (this.isUpdateSetupType) {
      this.updateCurrentPreferences();
    } else {
      this.saveUserPreferences();
    }
  }

  saveUserPreferences() {
    this.UserSetupSettings.saveUserPreferences(this.userPreferences).then(
      () => {
        // then move to the last slide
        this.Mixpanel.track('setup_complete');
        this.Mixpanel.engage(
          'first_time_setup',
          this.userPreferences.educational_system,
          this.userPreferences.school.title,
        );
        this.UserSetupSettings.goToSetupComplete();
      },
    );
  }

  updateCurrentPreferences() {
    this.UserSetupSettings.updateUserPreferences(this.userPreferences)
      .then(() => {
        this.initialPreferences = Object.assign({}, this.userPreferences);
        this.UserSetupSettings.hideAfterUpdateDialog();
        this.Mixpanel.engage(
          'edit_setup',
          this.userPreferences.country,
          this.userPreferences.educational_system,
          this.userPreferences.school.title,
        );
        this.UserSetupSettings.showAfterUpdateDialog();
      });
  }

  isSaveButtonDisabled() {
    let isDisabled = true;

    if (
      this.isUpdateSetupType &&
      this.UserSetupSettings.isPreferenceFilled(this.initialPreferences) &&
      this.UserSetupSettings.isPreferenceFilled(this.userPreferences)
    ) {
      isDisabled = this.UserSetupSettings.comparePreferences(
        this.initialPreferences,
        this.userPreferences,
      );
    } else {
      isDisabled = !this.UserSetupSettings.isPreferenceFilled(this.userPreferences);
    }

    return isDisabled;
  }
}
