import angular, { IPromise } from 'angular';
import { CourseProgressDialog } from '../courseProgressDialog.service';

export * from './shareAssessmentsWithColleagues.scss';

export class ShareAssessmentsWithColleaguesDialog extends CourseProgressDialog {
  constructor(protected $mdDialog: angular.material.IDialogService) {
    'ngInject';

    super($mdDialog);
  }

  show(): IPromise<any> {
    const config: angular.material.IDialogOptions = {
      templateUrl:
        `${CourseProgressDialog.TEMPLATE_PATH}/shareAssessmentsWithColleagues/shareAssessmentsWithColleagues.html`,
      controllerAs: '$ctrl',
    };

    return super.show(config);
  }
}
