/**
 * @module Helpers/Service
 * @description
 *   Global helpers functions.
 * @param {Object} $state Angular $state object
 * @param {Object} Config service instance
 */

/*@ngInject*/
function Helpers($state, Config, Mixpanel, Debounce) {
  let cachedAssignmentId = null;
  let themeWatcherObserver = null;
  let backdropWatcherObserver = null;

  return {
    /**
     * Check if changes is in right mode
     * @memberof module:Helpers/Service
     * @param {String} editingMode Selected editing mode
     * @param {Array} editing List of avaiable editing modes
     * @return {Boolean}
     */
    checkIfModeAvailable(editingMode, editing) {
      let passed = [];

      passed = editing.filter(val => editingMode === val);

      return passed.length;
    },

    /**
     * Check if there is visibility changes in Edit Multiple
     * @memberof module:Helpers/Service
     * @param {String} option Selected option
     * @param {String} editingMode Selected editing mode
     * @param {Array} editing List of avaiable editing modes
     * @return {Boolean}
     */
    isVisibilityChanges(option, editingMode, editing) {
      const pass = this.checkIfModeAvailable(editingMode, editing);
      let valid = false;

      if (pass && option !== 'not_changed') {
        valid = true;
      }

      return valid;
    },

    /**
     * Check if there is grade changes in Edit Multiple
     * @memberof module:Helpers/Service
     * @param {String} option Selected option
     * @param {String} editingMode Selected editing mode
     * @param {Array} editing List of avaiable editing modes
     * @return {Boolean}
     */
    isGradeChanges(option, editingMode, editing) {
      const pass = this.checkIfModeAvailable(editingMode, editing);
      let valid = false;

      if (pass && option !== 'not_changed') {
        valid = true;
      }

      return valid;
    },

    /**
     * Check if there is text changes in Edit Multiple
     * @memberof module:Helpers/Service
     * @param {String} option Selected option
     * @param {String} editingMode Selected editing mode
     * @param {Array} editing List of avaiable editing modes
     * @return {Boolean}
     */
    isTextChanges(option, editingMode, editing) {
      const pass = this.checkIfModeAvailable(editingMode, editing);
      let valid = false;

      if (pass && option) {
        valid = true;
      }

      return valid;
    },

    /**
     * Send event to Mixpanel
     * @memberof module:Helpers/Service
     * @param {Array} eventCode Contains event code
     */
    sendMixpanel(eventCode) {
      Mixpanel.track(eventCode);
    },

    /**
     * Debounce send event to Mixpanel
     * @memberof module:Helpers/Service
     * @param {Array} eventCode Contains event code
     */
    debounceMixpanel: Debounce((eventCode) => {
      Mixpanel.track(eventCode);
    }, 250),

    /**
     * Handle changing visible of single item
     * @memberof module:Helpers/Service
     * @param {Function} savingMethod Function for saving changes
     * @param {Object} savingItem Object that should be saved
     * @param {Object} editingItem Object that will be editing
     * @param {String} editingProperty Property that will be editing
     * @param {String} eventCode Mixpanel event code
     */
    changeSingleVisible(savingMethod, savingItem, editingItem, editingProperty, eventCode) {
      editingItem[editingProperty] = !editingItem[editingProperty];

      savingMethod(savingItem);

      this.sendMixpanel(eventCode);
    },

    /**
     * Handle changing text of single item
     * @memberof module:Helpers/Service
     * @param {Function} savingMethod Function for saving changes
     * @param {Object} savingItem Object that should be saved
     * @param {String} eventCode Mixpanel event code
     */
    changeSingleText(savingMethod, savingItem, eventCode) {
      savingMethod(savingItem);

      this.debounceMixpanel(eventCode);
    },

    /**
     * Handle changing grade of single item
     * @memberof module:Helpers/Service
     * @param {Function} savingMethod Function for saving changes
     * @param {Object} savingItem Object that should be saved
     * @param {String} eventCode Mixpanel event code
     */
    changeSingleGrade(savingMethod, savingItem, eventCode) {
      savingMethod(savingItem);

      this.debounceMixpanel(eventCode);
    },

    /**
     * Find matching grade system
     * @memberof module:Helpers/Service
     * @param {Array} gradeSystems List of grade systems
     * @param {String} gradeSystemId Selected grade system ID
     * @return {Object}
     */
    findGradeSystem(gradeSystems, gradeSystemId) {
      if (gradeSystems.main.grade_system.id === gradeSystemId) {
        return gradeSystems.main.grade_system;
      } else if (gradeSystems.final.grade_system.id === gradeSystemId) {
        return gradeSystems.final.grade_system;
      }
      return gradeSystems[gradeSystemId].grade_system;
    },

    /**
     * Find first positive grade in grade system
     * @memberof module:Helpers/Service
     * @param {Object} gradeSystem Single grade system
     * @return {String}
     */
    findFirstPositiveGrade(gradeSystem) {
      let firstPositiveGrade;

      gradeSystem.grades.forEach((elem) => {
        if (elem.is_passed) {
          if (!firstPositiveGrade) {
            firstPositiveGrade = elem;
          } else if (elem.order < firstPositiveGrade.order) {
            firstPositiveGrade = elem;
          }
        }
      });

      return firstPositiveGrade.name;
    },

    /**
     * Find student id by student name
     * @memberof module:Helpers/Service
     * @param {Array} students List of students
     * @param {String} studentName Selected student name
     * @return {Number}
     */
    findStudentIdByName(students, studentName) {
      let studentId;

      students.forEach((student) => {
        if (studentName !== student.student_name) {
          return;
        }

        studentId = student.student_id ? student.student_id : student.id;
      });

      return studentId;
    },

    registerBackdropWatcher() {
      if (!backdropWatcherObserver) {
        let throttle = null;
        backdropWatcherObserver = new MutationObserver(() => {
          clearTimeout(throttle);
          throttle = setTimeout(() => {
            requestAnimationFrame(() => {
              const backdropElement = document.body.querySelector('md-backdrop.md-click-catcher');
              const openItem = document.body.querySelector('#studybee [aria-expanded=true]');

              if (backdropElement && !openItem) {
                backdropElement.remove();
              }
            });
          }, 100);
        });

        backdropWatcherObserver.observe(document.getElementById('studybee'), {
          attributeFilter: ['aria-expanded'],
          attributes: true,
          subtree: true,
        });
      }
    },

    disconnectBackdropWatcher() {
      if (backdropWatcherObserver) {
        backdropWatcherObserver.disconnect();
      }
    },

    registerThemeWatcher() {
      if (!themeWatcherObserver) {
        let throttle = null;
        themeWatcherObserver = new MutationObserver(() => {
          clearTimeout(throttle);
          throttle = setTimeout(() => {
            requestAnimationFrame(() => {
              const currentTheme = $state.current.themeClass || 'sb-theme-green';
              document.body.setAttribute('sb-theme', currentTheme);
            });
          }, 100);
        });

        themeWatcherObserver.observe(document.getElementById('studybee'), {
          attributeFilter: ['class'],
          attributeOldValue: true,
        });
      }
    },

    disconnectThemeWatcher() {
      if (themeWatcherObserver) {
        themeWatcherObserver.disconnect();
      }
    },

    /**
     * Set theme class to StudyBee container
     * @memberof module:Helpers/Service
     * @param {String} containerName Container name for setting theme class
     */
    setTheme(containerName) {
      let themeClass;

      for (let i = 0; i < Config.routes.length; i++) {
        if (Config.routes[i].name === $state.current.name) {
          themeClass = Config.routes[i].themeClass;
          break;
        }
      }

      $(containerName)
        .removeClass((i, css) => (css.match(/(^|\s)sb-theme-\S+/g) || []).join(' ')).addClass(themeClass);
    },

    /**
     * Cache cached assignment ID
     * @memberof module:Helpers/Service
     * @param {String} assignmentId Selected assignment ID
     */
    setAssignmentIdCached(assignmentId) {
      cachedAssignmentId = assignmentId;
    },

    /**
     * Returns cached assignment ID
     * @memberof module:Helpers/Service
     * @param {Boolean} clearAssignmentIdCached
     * @return {String} assignment id or null
     */
    isAssignmentIdCached(clearAssignmentIdCached) {
      let assignmentId;

      if (!cachedAssignmentId) {
        return;
      }

      assignmentId = cachedAssignmentId;

      if (clearAssignmentIdCached) {
        cachedAssignmentId = null;
      }

      return assignmentId;
    },

    /**
     * Returns current state name
     * @memberof module:Helpers/Service
     * @return {String}
     */
    getCurrentStateName() {
      return $state.current.name;
    },
  };
}

module.exports = Helpers;
