import angular from 'angular';

export function AppConfig(
  $compileProvider, $locationProvider, $stateProvider, $qProvider, ConfigProvider, $mdAriaProvider,
  $sceDelegateProvider, $urlServiceProvider, $sanitizeProvider
) {
  'ngInject';

  const config = ConfigProvider.$get();

  // Disable error info on unhandled promise rejections
  $qProvider.errorOnUnhandledRejections(false);

  // Globally disables all ARIA warnings.
  $mdAriaProvider.disableWarnings();

  $compileProvider.debugInfoEnabled(DEVELOPMENT_ENV);

  //Enable SVG
  $sanitizeProvider.enableSvg(true);

  // Enable HTML5 mode which is based on using the HTML5 History API.
  $locationProvider.html5Mode({
    enabled: true,
    requireBase: false,
  });

  $urlServiceProvider.deferIntercept();

  // Run through all routes in config and create state for each one.
  angular.forEach(config.routes, (route) => {
    $stateProvider.state(route.name, {
      templateUrl: route.templateUrl,
      controller: route.ctrl,
      controllerAs: route.controllerAs,
      themeClass: route.themeClass,
      params: route.params || {},
      resolve: route.resolve,
    });
  });

  $sceDelegateProvider.resourceUrlWhitelist([
    'self',
    `chrome-extension://${chrome.runtime.id}/**`,
  ]);
}
