import {Component} from '../../utils/decorators/component.decorator';
import TeacherAssignmentsModule from './teacherAssignments.module';
import style from './teacherAssignments.scss';
import template from './teacherAssignments.tpl.html';
import {
  AssignmentFilterItem,
  AssignmentSort,
  AssignmentSortItem,
  AssignmentsQuery,
  AssignmentsService,
  AssignmentState,
  AssignmentsUI,
  AssignmentType
} from '../../../../../src/app/teacher/assignments/state';
import {Order} from '@datorama/akita';
import {
  AssessmentSort,
  AssessmentSortItem,
  AssessmentsQuery,
  AssessmentsService
} from '../../../../../src/app/teacher/assignments/assignment/assessments/state';

@Component({
  module: TeacherAssignmentsModule,
  selector: 'teacher-assignments',
  template: template,
  style: style,
})
export class TeacherAssignmentsComponent {

  filters: AssignmentFilterItem[] = [
    {
      label: 'assignmentActionMenuFilterWorkUnit',
      type: AssignmentType.WORK_UNIT,
    },
    {
      label: 'assignmentActionMenuFilterAssignments',
      type: AssignmentType.STANDARD_ASSIGNMENT,
    },
    {
      label: 'assignmentActionMenuFilterWithoutAssessmentCriterias',
      type: AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
    },
    null,
    {
      label: 'publishedStatus',
      state: AssignmentState.PUBLISHED,
    },
    {
      label: 'draftsStatus',
      state: AssignmentState.DRAFT,
    },
  ];

  assignmentsSort: AssignmentSortItem[] = [
    {
      by: 'assignment_type',
      label: 'assignmentsActionMenuSortByTypeAsc',
      order: Order.ASC,
    },
    {
      by: 'assignment_created',
      label: 'assignmentsActionMenuSortByCreationDateAsc',
      order: Order.ASC,
    },
    {
      by: 'assignment_created',
      label: 'assignmentsActionMenuSortByCreationDateDesc',
      order: Order.DESC,
    },
    {
      by: 'assessment_last_changed',
      label: 'assignmentsActionMenuSortByLastChanged',
      order: Order.DESC,
    },
    {
      by: 'title',
      label: 'assignmentsActionMenuSortByNameAsc',
      order: Order.ASC,
    },
  ];

  assessmentsSort: AssessmentSortItem[] = [
    {
      by: 'student_name',
      label: 'studentsActionMenuSortByFirstNameAsc',
      order: Order.ASC,
    },
    {
      by: 'status_new',
      label: 'studentsActionMenuSortByAssessmentDoneAsc',
      order: Order.ASC,
    },
    {
      by: 'status_new',
      label: 'studentsActionMenuSortByNotAssessedAsc',
      order: Order.DESC,
    },
  ];

  constructor(protected AssignmentsService: AssignmentsService,
              protected AssignmentsQuery: AssignmentsQuery,
              protected AssessmentsService: AssessmentsService,
              protected AssessmentsQuery: AssessmentsQuery,
              protected NavigationHistory: any,
              protected AssessmentVisibilityDialogService: any,
              protected AssessmentsVisibilityDialogService: any,
              protected GroupedVisibilityDialogService: any,
              protected StudybeeHandle: any) {
    'ngInject';
  }

  filter(assignmentFilterItem: AssignmentFilterItem) {
    const stateUI = this.AssignmentsQuery.getValue().ui;
    const filterType = assignmentFilterItem.state ? 'state' : 'type';
    const currentFilters: any[] = stateUI.filters[filterType];
    let filters: AssignmentsUI['filters'] = {
      ...stateUI.filters,
    };

    if (currentFilters.includes(assignmentFilterItem[filterType])) {
      filters = {
        ...filters,
        [filterType]: (filters[filterType] as any[]).filter(value => value !== assignmentFilterItem[filterType]),
      };
    } else {
      filters = {
        ...filters,
        [filterType]: [...filters[filterType], assignmentFilterItem[filterType]],
      };
    }

    this.AssignmentsService.setFilters(filters);
  }

  isFilterSelected(assignmentFilterItem: AssignmentFilterItem) {
    const stateUI = this.AssignmentsQuery.getValue().ui;
    const filterType = assignmentFilterItem.state ? 'state' : 'type';
    const currentFilters: any[] = stateUI.filters[filterType];

    return currentFilters.includes(assignmentFilterItem[filterType]);
  }

  sortAssignments(assignmentSortItem: AssignmentSortItem) {
    const sort: AssignmentSort = {
      by: assignmentSortItem.by,
      order: assignmentSortItem.order,
    };

    this.AssignmentsService.setSorting(sort);
  }

  isAssignmentSortSelected(assignmentSortItem: AssignmentSortItem) {
    const stateSortUI = this.AssignmentsQuery.getValue().ui.sort;

    return stateSortUI.by === assignmentSortItem.by &&
      stateSortUI.order === assignmentSortItem.order;
  }

  sortAssessments(assessmentSortItem: AssessmentSortItem) {
    const sort: AssessmentSort = {
      by: assessmentSortItem.by,
      order: assessmentSortItem.order,
    };

    this.AssessmentsService.setSorting(sort);
  }

  isAssessmentSortSelected(assessmentSortItem: AssessmentSortItem) {
    const stateSortUI = this.AssessmentsQuery.getValue().ui.sort;

    return stateSortUI.by === assessmentSortItem.by && stateSortUI.order === assessmentSortItem.order;
  }
}
