import { selectorWords } from '../host-page-selectors';

const isItTopic = () => {
  //language=JQuery-CSS
  let isTopic = $(`[role="alertdialog"] input[aria-label="${selectorWords.en.topic}"]`).length;
   isTopic = isTopic || $(`[role="alertdialog"] input[aria-label="${selectorWords.sv.topic}"]`).length;
   isTopic = isTopic || $(`[role="alertdialog"] input[aria-label="${selectorWords.nl.topic}"]`).length;
  return isTopic;
};
export class HostPageQualifiers {
  /**
   *
   */
  get hasScheduleAlertDialog() {
    //language=JQuery-CSS
    return $('[role="alertdialog"]').length && $('[data-initial-millis]').length;
  }

  get hasAlertDialog() {
    //language=JQuery-CSS
    return $('[role="alertdialog"]').length && !isItTopic();
  }

  get hasCreatingModal() {
    if (isItTopic()) {
      return true;
    }
    return $('[role="dialog"] [guidedhelpid="addOrCreateMaterial"]')
      && $('[role="dialog"] [guidedhelpid="streamItemShareWithOtherCourses"]')
      && $('[role="dialog"] [guidedhelpid="streamItem_edit_personalizationMenu"]');
  }

  get hasEditingPublishedModal() {
    //language=JQuery-CSS
    return $('[role="dialog"] [guidedhelpid="addOrCreateMaterial"]')
      && !$('[role="dialog"] [guidedhelpid="streamItem_edit_personalizationMenu"]');
  }
}
