import {Injectable} from '@angular/core';
import {HttpRequestType} from '../http.model';
import {Params} from '@angular/router';
import {Observable} from 'rxjs';
import {ID} from '@datorama/akita';
import SparkMD5 from 'spark-md5';
import {HttpCacheStore} from './http-cache.store';

@Injectable({ providedIn: 'root' })
export class HttpCacheService {

  constructor(protected httpCacheStore: HttpCacheStore) {
  }

  add<T = any>(source: Observable<T>, url: string, type: HttpRequestType, body?: any, params: Params = {}) {
    const id: ID = this.generateID(url, type, body, params);

    this.httpCacheStore.add({ id, source });
  }

  delete(id: ID) {
    this.httpCacheStore.remove(id);
  }

  generateID(url: string, type: HttpRequestType, body?: any, params: Params = {}): ID {
    return SparkMD5.hash(`${url}${type}${JSON.stringify(body)}${JSON.stringify(params)}`);
  }
}
