import { Events } from '../global-events';
import { UnbindableCtrlBase } from '../base-classes';

/**
 *
 * @type {{bindings: {visible: string}, template: string}}
 */
export const SavingIndicator = {
  controller: class extends UnbindableCtrlBase {
    constructor($rootScope, $timeout) {
      'ngInject';

      super();
      this.$rootScope = $rootScope;
      this.$timeout = $timeout;
    }

    $onInit() {
      this.savingInProgress = 0;
      this.visible = false;
      this.savingError = false;

      this.boundEvents.push(this.$rootScope.$on(
        Events.STARTED_NEW_REQUEST, () => {
          if (this.savingInProgress === 0) {
            this.visible = true;
          }
          this.savingInProgress += 1;
          this.savingError = false;
        },
      ));

      this.boundEvents.push(this.$rootScope.$on(
        Events.REQUEST_FINISHED_SUCCESSFULLY, () => {
          this.savingInProgress -= 1;
          this.savingError = false;
          if (this.savingInProgress === 0) {
            this.postponeHidingIndicator();
          }
        },
      ));

      this.boundEvents.push(this.$rootScope.$on(
        Events.REQUEST_FINISHED_FAILURE, () => {
          this.savingInProgress -= 1;
          this.savingError = true;
          if (this.savingInProgress === 0) {
            this.postponeHidingIndicator();
          }
        },
      ));
    }

    postponeHidingIndicator() {
      this.$timeout(() => {
        // hide only when current there are no saving in progress requests
        if (!this.savingInProgress) {
          this.visible = false;
          this.savingError = false;
        }
      }, 1000);
    }
  },
  //language=HTML
  template: `
    <div class="sb-saving-indicator"
         ng-if="$ctrl.visible">
      <span ng-show="$ctrl.savingInProgress > 0"
            localize>__MSG_savingStatusIndicator__</span>
      <span ng-show="$ctrl.savingInProgress === 0 && !$ctrl.savingError"
            localize>__MSG_savedStatusIndicator__</span>
      <span class="error"
            ng-show="$ctrl.savingInProgress === 0 && $ctrl.savingError"
            localize>__MSG_errorStatusIndicator__</span>
    </div>
  `,
};
