export type IUnsubscribe = () => void;
export type IObserverNext<T = any> = (value: T) => void;

export interface IObserver<T = any> {
  closed?: boolean;
  next: IObserverNext;
}

export class Observer<T = any> implements IObserver<T> {

  public closed: boolean = false;
  public next: IObserverNext<T>;

  constructor(next: IObserverNext<T>) {
    'ngInject';
    this.next = next;
  }
}



