import moment, { Moment } from 'moment';
import { IObjectiveAssignmentAssessment } from '../../../../../../shared/models/objectiveAssignmentAssessment.model';
import { Service } from '../../../../../../utils/decorators/service.decorator';
import TeacherCourseProgressModule from '../../../teacher-course-progress.module';

export interface IAssignmentOverviewStatusService {
  findLastUpdateDate(objectiveAssessment: IObjectiveAssignmentAssessment): string;
}

@Service({
  module: TeacherCourseProgressModule,
  name: 'AssignmentOverviewStatusService',
})
export class AssignmentOverviewStatusService implements IAssignmentOverviewStatusService {
  constructor() {
    'ngInject';
  }

  public findLastUpdateDate(objectiveAssignmentAssessment: IObjectiveAssignmentAssessment): string {
    const dates: string[] = [
      objectiveAssignmentAssessment.feedback_last_edit,
      objectiveAssignmentAssessment.private_note_last_edit,
      objectiveAssignmentAssessment.result_last_edit,
    ]
      .filter((date: string): boolean => !!date);

    if (!dates.length) {
      return '';
    } else if (dates.length === 1) {
      return dates[0];
    } else {
      const format: string = `${moment(dates[0]).creationData().format}`;

      return dates
        .map((date: string): Moment => moment(date))
        .sort((moment1: Moment, moment2: Moment): number => {
          if (moment1.isSame(moment2)) {
            return 0;
          } else {
            return moment1.isBefore(moment2) ? 1 : -1;
          }
        })
        .shift()
        .format(format);
    }
  }
}
