import { IDeferred, IPromise, IQService } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { IPopupMessage } from '../../models/popupMessage.model';
import SharedServicesModule from '../services.module';

export interface IPopupService {
  popups: Map<string, chrome.windows.Window>;
  open(url: string): IPromise<void>;
  close(url: string): void;
}

@Service({
  module: SharedServicesModule,
  name: 'PopupService',
})
export class PopupService implements IPopupService {

  public popups: Map<string, chrome.windows.Window>;

  constructor(private $q: IQService) {
    'ngInject';

    this.popups = new Map<string, chrome.windows.Window>();
  }

  public open(url: string): IPromise<void> {
    const message: IPopupMessage = this.popups.has(url) ?
      this.getMessageFocus(url) : this.getMessageOpen(url);
    const deferred: IDeferred<void> = this.$q.defer<void>();

    chrome.runtime.sendMessage(message, (window: chrome.windows.Window): void => {
        this.onOpen(url, window);
        this.listenOnClose(window);
        deferred.resolve();
      },
    );

    return deferred.promise;
  }

  public close(url: string): void {
    if (this.popups.has(url)) {
      const message: IPopupMessage = this.getMessageClose(url);

      chrome.runtime.sendMessage(message, () => {
        this.popups.delete(url);
      });
    }
  }

  protected listenOnClose(window: chrome.windows.Window): void {
    const message: IPopupMessage = this.getMessageListenOnClose(window);

    chrome.runtime.sendMessage(message, (windowId: number): void => {
      this.onClose(windowId);
    });
  }

  protected getMessageOpen(url: string): IPopupMessage {
    return {
      action: 'openPopup',
      createData: { url },
    };
  }

  protected getMessageFocus(url: string): IPopupMessage {
    return {
      action: 'focusPopup',
      window: this.popups.get(url),
    };
  }

  protected getMessageClose(url: string): IPopupMessage {
    return {
      action: 'closePopup',
      window: this.popups.get(url),
    };
  }

  protected getMessageListenOnClose(window: chrome.windows.Window): IPopupMessage {
    return {
      action: 'listenOnClosePopup',
      window,
    };
  }

  protected onOpen(url: string, window: chrome.windows.Window) {
    this.popups.set(url, window);
  }

  protected onClose(windowId: number): void {
    this.popups.forEach((window: chrome.windows.Window, url: string): void => {
      if (window.id === windowId) {
        this.popups.delete(url);
      }
    });
  }
}
