import uiRouter from '@uirouter/angularjs';
import ngSanitize from 'angular-sanitize';

import { AssessmentDetailsEmpty } from './assessment-details-empty.component';
import { CourseDetails } from './course-details/course-details.component';
import { CourseListItemComponent } from './coursesList/courseListItem/courseListItem.component';
import { CoursesListComponent } from './coursesList/coursesList.component';
import { CoursesListEmptyComponent } from './coursesList/coursesListEmpty/coursesListEmpty.component';
import { ObjectivesList } from './course-details/objectives-list/objectives-list.component';
import { StudentAssessmentDetails } from './student-assessment-details.component';
import { StudentAssignmentsList } from './student-objective-details/student-assignments-list/student-assignments-list.component';
import { StudentCourseProgressComponent } from './student-course-progress.component';
import { studentCourseProgressRouterConfig } from './student-course-progress.routing';
import { StudentObjectiveDetails } from './student-objective-details/student-objective-details.component';

export const StudentCourseProgressModule = angular
  .module('studentCourseProgressModule', [
    uiRouter,
    ngSanitize,
  ])
  .component('assessmentDetailsEmpty', AssessmentDetailsEmpty)
  .component('courseDetails', CourseDetails)
  .component('courseListItem', CourseListItemComponent)
  .component('coursesList', CoursesListComponent)
  .component('coursesListEmpty', CoursesListEmptyComponent)
  .component('objectivesListStudent', ObjectivesList)
  .component('studentAssessmentDetails', StudentAssessmentDetails)
  .component('studentProgressAssignmentsList', StudentAssignmentsList)
  .component('studentCourseProgress', StudentCourseProgressComponent)
  .component('studentObjectiveDetails', StudentObjectiveDetails)
  .config(studentCourseProgressRouterConfig)
  .name;
