import {
  showChangeCourseCorePropertyDoublecheckWarning,
  showChangeCourseCorePropertyWarning,
} from '../../teacherCourseManagement/shares/helpers.dialog';
import { SelectEducationalProperties } from '../controllers/select-educational-properties.controller';

/**
 * Controller for the edit curriculum modal ( which is also used for creating )
 */
export class EditCurriculumDialogCtrl extends SelectEducationalProperties {

  constructor(
    $q, FetchData, $mdDialog, Path, course,
    educationalSystems, isLocked,
    educationalSystemId, educationalStageId,
  ) {
    'ngInject';

    super(FetchData);
    this.$q = $q;
    this.$mdDialog = $mdDialog;
    this.educationalSystems = educationalSystems;
    this.course = course || {};
    this.isLocked = isLocked;
    this.educationalSystemId = educationalSystemId;
    this.educationalStageId = educationalStageId;

    this.$onInit();
  }

  /**
   *
   */
  $onInit() {
    this.educationalSystem = this.educationalSystems.find(
      educationalSystem => educationalSystem.id === this.educationalSystemId,
    );

    if (this.educationalSystem && !this.isLocked) {
      this.updateEducationalStages(this.educationalSystem);
    }

    this.createMode = Object.keys(this.course).length === 0;

    if (!this.createMode) {
      this.updateEducationalStagesAndScales()
        .then(() => {
          this.scale = this.scales.find(
            scale => scale.id === this.course.scale,
          );
        });
    }

    // Create curriculum based on current settings
    if (this.createMode && this.educationalSystemId) {
      this.updateEducationalStagesAndScales();
    }
  }

  changeEducationalStage() {
    if (this.educationalStage) {
      this.updateScales(this.educationalStage);
    } else {
      this.scales = [];
    }
    this.scale = undefined;
  }

  /**
   * Action triggered when the save button is pushed
   */
  saveChanges() {
    if (this.createButtonEnabled) {
      this.$mdDialog.hide({
        name: this.course.name,
        code: this.course.code,
        scale: this.scale.id,
        educational_stage: this.educationalStage.id,
        educational_system: this.educationalSystem.id,
      });
    }
  }

  /**
   * returns condition when the create button is enabled or not
   * @returns {boolean}
   */
  get createButtonEnabled() {
    return !!(this.course.name && this.educationalSystem && this.educationalStage && this.scale);
  }

  get basicConfigChanged() {
    return this.scale.id !== this.course.scale ||
      this.educationalStage.id !== this.educationalStageId ||
      this.educationalSystem.id !== this.educationalSystemId;
  }
}

