import {EntityState, HashMap, ID, Order} from '@datorama/akita';

export type AssignmentSortFn = (assignment: Assignment) => number;

/**
 * @deprecated
 * Instead of this use AssessmentStatus
 */
export enum AssessmentStatusOld {
  ND = 'ND',
  NL = 'NL',
  NV = 'NV',
  NN = 'NN',
  AV = 'AV',
  AN = 'AN',
  WV = 'WV',
  WN = 'WN',
}

export enum AssignmentState {
  DELETED = 'DELETED',
  DRAFT = 'DRAFT',
  PUBLISHED = 'PUBLISHED',
}

export enum AssessmentStatus {
  ASSESSING = 'ASSESSING',
  ASSESSMENT_DONE = 'ASSESSMENT_DONE',
  NOT_ASSESSED = 'NOT_ASSESSED',
  WARNING = 'WARNING',
}

export enum AssignmentType {
  STANDARD_ASSIGNMENT = 'STANDARD_ASSIGNMENT',
  WITHOUT_ASSESSMENT_CRITERIAS = 'WITHOUT_ASSESSMENT_CRITERIAS',
  WORK_UNIT = 'WORK_UNIT',
  EMPTY = '',
}

export interface NormalizeAssignmentsResponse {
  assignments?: HashMap<Assignment>;
}

export interface Assignment {
  assessment_last_changed: string | number;
  assessments_for_topic: (Assignment & ID)[];
  assignment_created: string | number;
  assignment_id: ID;
  assignment_is_topic: Boolean;
  assignment_not_assessable: boolean;
  assignment_title: string;
  assignment_type: AssignmentType;
  classroom_name: string;
  fail_warning: boolean;
  id: ID;
  result_last_edit: number | string;
  result_only: boolean;
  status: AssessmentStatusOld,
  status_new: AssessmentStatus;
}

export interface AssignmentMenuItem {
  label: string;
}

export interface AssignmentFilter {
  state: AssignmentState[];
  type: AssignmentType[];
}

export interface AssignmentFilterItem extends AssignmentMenuItem {
  state?: AssignmentState;
  type?: AssignmentType;
}

export interface AssignmentSort {
  by: keyof Assignment;
  order: Order;
}

export interface AssignmentSortItem extends AssignmentSort, AssignmentMenuItem {
}

export interface AssignmentsUI {
  filters: AssignmentFilter;
  sort: AssignmentSort;
}

export function createAssignments(params?: Partial<EntityState<Assignment>>) {
  return {
    ...params
  } as EntityState<Assignment>;
}

export function createAssignmentsUI(params?: Partial<AssignmentsUI>) {
  return {
    filters: {
      state: [],
      type: [
        AssignmentType.STANDARD_ASSIGNMENT,
        AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
        AssignmentType.WORK_UNIT,
        AssignmentType.EMPTY,
      ]
    },
    sort: {
      by: 'assignment_type',
      order: Order.ASC,
    },
    ...params
  } as AssignmentsUI;
}
