import { IQService } from 'angular';
import { StateService, TransitionPromise } from '@uirouter/angular';
import { RequestType } from '../../shared/enums/requestType.enum';
import { HttpResponse } from '../../shared/models/httpResponse.model';
import { IInterceptor } from '../../shared/models/interceptor.model';
import { Interceptor } from '../decorators/interceptor.decoreator';
import { HttpStatusCode } from '../enums/httpStatusCode.enum';
import { PermissionType } from '../enums/permissionType.enum';
import { UserInfo } from '../services/userInfo.service';
import UtilsModule from '../utils.module';

@Interceptor({
  module: UtilsModule,
})
export class UserDataInterceptor implements IInterceptor {

  private readonly urlRegex: RegExp;
  private UserInfo?: UserInfo;

  constructor(private $q: IQService, private $injector: angular.auto.IInjectorService, private $state: StateService) {
    'ngInject';

    this.urlRegex = /\/users\/user_data\/?$/;
  }

  public response(response: HttpResponse): PromiseLike<HttpResponse> {
    this.prepareUserInfoService();

    if (response.config.method === RequestType.GET && this.urlRegex.test(response.config.url)) {
      this.UserInfo.userId = response.data.id;
      this.UserInfo.hasCreateCoursePermission = false;
      this.UserInfo.applyDetails(response.data);

      if ('is_teacher' in response.data && response.data.is_teacher) {
        this.UserInfo.hasCreateCoursePermission = true;
      } else if ('permissions' in response.data) {
        this.UserInfo.hasCreateCoursePermission = !!response
          .data
          .permissions
          .find(permission => permission.permission === PermissionType.CREATE_COURSE);
      }
    }

    return this.$q.resolve(response);
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> {
    if (response.status === HttpStatusCode.FORBIDDEN && this.urlRegex.test(response.config.url)) {
      this.$state.go('user-inactive');

      return this.$q.resolve(response);
    }

    return this.$q.reject(response);
  }

  private prepareUserInfoService(): void {
    this.UserInfo = this.$injector.get('UserInfo');
  }
}
