import { IComponentController } from 'angular';
import { StateService } from '@uirouter/core';
import { IAssessment } from '../../../../../shared/models/assessment.model';
import { IAssignment } from '../../../../../shared/models/assignment.model';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherAssignmentsModule from '../../../teacherAssignments.module';
import style from './teacherAssessmentsList.scss';
import template from './teacherAssessmentsList.tpl.html';

@Component({
  module: TeacherAssignmentsModule,
  selector: 'teacher-assessments-list',
  template: template,
  style: style,
})
export class TeacherAssessmentsListComponent implements IComponentController {
  @Input()
  public assignment: IAssignment;
  @Input()
  public assessments: IAssessment[];

  constructor(private $state: StateService, private NavigationHistory: any) {
    'ngInject';

    this.assignment = null;
    this.assessments = [];
  }

  public goToAssessAssignment(studentId: number): void {
    if (this.assignment) {
      this.$state.go(
        'teacher_assess_assignment_student',
        {
          studentId,
          assignment: this.assignment,
        },
      ).then((): void => {
        //TODO: remove after refactor teacherAssessAssignmentStudent view
        this.NavigationHistory
          .addToHistory('teacher_assess_assignment', {
            isModal: true,
            assignment: this.assignment,
          });
      });
    }
  }
}
