export class UserSetupCheckProgressController {
  constructor(UserSetupCheck) {
    'ngInject';

    this.UserSetupCheck = UserSetupCheck;
    this.userEmailDomain = '';
    this.waitingForResponse = false;
  }

  async $onInit() {
    this.userEmailDomain = await this.UserSetupCheck.getUserEmailDomain();
  }

  validateUser() {
    this.waitingForResponse = true;

    this.UserSetupCheck.validateUser()
      .then(isValid => this.onValidation({ isValid }))
      .finally(() => (this.waitingForResponse = false));
  }
}
