import angular from 'angular';
import { ISiblingComponentOptions } from 'angular';

export const SiblingComponent: (options?: ISiblingComponentOptions) => (prototype: object, property: string | symbol) => void = (options?: ISiblingComponentOptions): (prototype: object, property: string | symbol) => void => {
  return (prototype: object & { $$require?: any }, property: string | symbol): void => {
    const _options: ISiblingComponentOptions = angular.merge({}, {
      alias: '',
      optional: false,
    }, options);
    const require: { [property: string]: string } = { [property]: `^${_options.optional ? '?' : ''}${_options.alias}` };
    let $$require: { [boundProperty: string]: string } = prototype.$$require || {};

    $$require = angular.merge({}, $$require, require);
    Object.assign(prototype, { $$require });
  };
};
