import React, {memo, useEffect, useReducer, useRef, useState} from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  ButtonBase,
  Tooltip,
} from "@material-ui/core";
import { colors, fonts } from "../theme";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import { getTranslation } from "../i18n";
import CourseObjectiveSummaryItem from "./courseObjectiveSummaryItem";
import CourseObjectiveAssessment from "./courseObjectiveAssessment";
import ProficiencyProfile from "./proficiencyProfile";

const widthThreshold = 500;

const CourseObjectiveAssessmentItem = (props) => {
  const {
    id,
    gradeSystemValues,
    defaultIndex,
    assessment: initialAssessment,
    knowledgeRequirements,
    objective,
    onObjectiveSelected,
    proficiencyProfiles,
    selectedGradeSystem,
    multiEdit = false,
    onSave,
  } = props;

  const ref = useRef();
  const assessment = useRef(initialAssessment);
  const [width, setWidth] = useState();
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);
  const [profileWidth, setProfileWidth] = useState(0);

  useEffect(() => {
    setWidth(ref?.current?.clientWidth);
  },[]);

  useEffect(() => {
    assessment.current = initialAssessment;
    forceUpdate();
  }, [id, initialAssessment]);

  const grade = gradeSystemValues.find(
    (g) => g.id === assessment.current?.grade_id
  );

  const name = objective?.name;
  const wideItem = width > widthThreshold;

  const empty = selectedGradeSystem?.grades.map(g => ({grade: {color:'transparent', label:''}, quantity: 1}))||{};
  empty.total = 0;

  return (
    <Accordion ref={ref} TransitionProps={{ unmountOnExit: true }}>
      <AccordionSummary expandIcon={<ExpandMoreIcon />}>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            width: "100%",
          }}
        >
          <Tooltip title={name.length > 15 ? name : ""}>
            <span
              style={{
                ...fonts.MEDIUM,
                overflow: "hidden",
                textOverflow: "ellipsis",
                whiteSpace: "nowrap",
                width: wideItem ? `${300 - profileWidth}px` : "150px",
              }}
            >
              {name}
            </span>
          </Tooltip>
          <div style={{ display: "flex", alignItems: "center" }}>
            {wideItem && proficiencyProfiles && (
              <ProficiencyProfile
                onWidth={setProfileWidth}
                profile={
                  proficiencyProfiles[objective?.id] ? proficiencyProfiles[objective?.id] : empty
                }
              />
            )}
            <CourseObjectiveSummaryItem
              assessment={assessment.current}
              grade={grade}
            />
            {wideItem && (
              <ButtonBase
                onClick={(e) => {
                  e.stopPropagation();
                  e.nativeEvent.stopImmediatePropagation();
                  onObjectiveSelected(objective);
                }}
                focusRipple
                style={{
                  border: `1px solid ${colors.grey}`,
                  padding: "10px",
                  marginLeft: "10px",
                  backgroundColor: colors.white,
                }}
              >
                <span style={{ ...fonts.MEDIUM, whiteSpace: "nowrap" }}>
                  {getTranslation("ShowDataButton")}
                </span>
              </ButtonBase>
            )}
          </div>
        </div>
      </AccordionSummary>
      <AccordionDetails>
        <CourseObjectiveAssessment
          assessment={initialAssessment}
          defaultIndex={defaultIndex}
          id={id}
          gradeSystemValues={gradeSystemValues}
          knowledgeRequirements={knowledgeRequirements}
          multiEdit={multiEdit}
          onSave={(assessment) => {
            onSave({id: objective.id, ...assessment})
          }}
        />
      </AccordionDetails>
    </Accordion>
  );
};
export default memo(CourseObjectiveAssessmentItem);
