import { AssignmentState } from '../enums/assignmentStateType.enum';
import { AssignmentType } from '../enums/assignmentType.enum';
import { IAssessment } from './assessment.model';

export interface IAssignment {
  alternate_link_id: string;
  assessments: IAssessment[];
  assignment_not_assessable: boolean;
  assignment_type: AssignmentType;
  classroom_name: string;
  id: number;
  state: AssignmentState;
  title: string;
  worktype: string;
  ui?: { isExpanded: boolean };
}

export class Assignment implements IAssignment {
  public alternate_link_id: string;
  public assessments: IAssessment[];
  public assignment_not_assessable: boolean;
  public assignment_type: AssignmentType;
  public classroom_name: string;
  public id: number;
  public state: AssignmentState;
  public title: string;
  public worktype: string;
  public ui?: { isExpanded: boolean };
}
