import React, {useEffect, useState} from 'react';
import {ButtonBase} from '@material-ui/core';
import {MoreVert} from '@material-ui/icons';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {withStyles} from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AssessmentButton from './assessmentButton';
import {getAssessmentIcon, getStatusText, getSubmittedIcon, getSubmittedStatusText} from '../utils';
import {colors, fonts} from '../theme';
import AssignmentAssessment from './assignmentAssessment';
import ObjectivesList from './objectivesList';
import Tooltip from "./tooltip";

const StudentButton = (props) => {
  const {
    onClickStudent,
    style,
    student,
    assessment,
    use_submitted_status = false,
    onStatusChange,
    showAssessmentButton = true,
  } = props;
  const [highlight, setHighlight] = useState(false);

  if (!student.student_name) {
    student.student_name = `${student.first_name} ${student.last_name}`;
  }
  const {
    student_id: id,
    student_name: fullName,
    fail_warning: warning,
  } = student;

  const {status_new: status} = assessment;
  const highlightStudent = () => {
    setHighlight(true);
  };

  const unHighlightStudent = () => {
    setHighlight(false);
  };

  return (
    <div style={{position: 'relative', height: '50px'}}>
      <ButtonBase
        focusRipple
        onClick={onClickStudent}
        key={id}
        onMouseEnter={highlightStudent}
        onMouseLeave={unHighlightStudent}
        style={{
          width: '100%',
          height: '50px',
          paddingTop: '5px',
          paddingRight: '20px',
          paddingBottom: '5px',
          paddingLeft: '20px',
          borderBottom: `1px solid ${colors.borderColor}`,
          backgroundColor: highlight ? colors.grey : 'transparent',
          ...style,
        }}
      >
        <div
          style={{
            width: '100%',
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <div style={{display: 'flex', justifyContent: 'space-between', width: '45px'}}>
            <Tooltip title={getStatusText(assessment.status_new)}>
              <div>
                {getAssessmentIcon({status: assessment.status_new, style: {maxHeight: '18px'}})}
              </div>
            </Tooltip>
            {use_submitted_status &&
            <div style={{marginLeft: '8px'}}>
              <Tooltip title={getSubmittedStatusText(assessment.submitted_status)}>
                <div>
                  {getSubmittedIcon({status: assessment.submitted_status})}
                </div>
              </Tooltip>
            </div>}
          </div>
          <Tooltip title={student?.email}>
            <p
              style={{
                ...fonts.MEDIUM,
                marginLeft: '16px',
                width: use_submitted_status ? '180px' : '210px',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                textAlign: 'start',
              }}
            >
              {fullName}
            </p>
          </Tooltip>
        </div>
      </ButtonBase>
      {showAssessmentButton && (
        <AssessmentButton
          style={{
            cursor: 'pointer',
            position: 'absolute',
            right: '10px',
            top: '14px',
            color: colors.darkGrey,
          }}
          onStatusChange={(newStatus) => onStatusChange({status: newStatus, student})
          }
          submittedStatus={use_submitted_status}
          button={<MoreVert/>}
        />
      )}
    </div>
  );
};

const StyledAccordion = withStyles({
  root: {
    '&$expanded': {
      marginTop: '0px',
    },
  },
  expanded: {},
})(Accordion);

const StyledAccordionSummary = withStyles({
  root: {
    padding: '0px',
    margin: '0px',
    '&$expanded': {
      minHeight: '0px',
    },
  },
  content: {
    padding: '0px',
    margin: '0px',
    '&$expanded': {
      padding: '0px',
      margin: '0px',
      minHeight: '0px',
    },
  },
  expandIcon: {
    position: 'absolute',
    right: '46px',
    '&$expanded': {},
  },
  expanded: {},
})(AccordionSummary);

const StyledAccordionDetails = withStyles({
  root: {
    padding: '0px',
  },
})(AccordionDetails);

export const StudentItem = (props) => {
  const {
    onClick,
    student,
    onStatusChange,
    assignment,
    expandable = false,
    filter,
    gradeSystem,
    courseDefinitions,
    expanded: initialExpanded,
    assessment,
    paddingLeft = '0px',
  } = props;

  const [expanded, setExpanded] = useState(initialExpanded);
  const [focused, setFocused] = useState(false);

  useEffect(() => {
    setExpanded(initialExpanded);
  }, [initialExpanded]);

  const onClickStudent = () => {
    onClick({student, objectiveAssessments: assessment?.objectiveAssessments, assessment});
  };

  const expandItem = (e, expand) => {
    setExpanded(expand);
  };

  if (!expandable) {
    return <StudentButton {...{...props, onClickStudent, style: {paddingLeft}}} />;
  }

  return (
    <div style={{position: 'relative'}}>
      <StyledAccordion
        TransitionProps={{unmountOnExit: true, enter: false, exit: false}}
        expanded={expanded}
        onChange={expandItem}
      >
        <StyledAccordionSummary
          onMouseEnter={() => setFocused(true)}
          onMouseLeave={() => setFocused(false)}
          style={{
            backgroundColor: focused ? colors.grey : colors.white,
            padding: '0px 0px 0px 16px',
            margin: '0px',
            borderBottom: `1px solid ${colors.borderColor}`,
          }}
          expandIcon={<ExpandMoreIcon/>}
        >
          <StudentButton
            {...{
              ...props,
              onClickStudent,
              showAssessmentButton: false,
              use_submitted_status: assignment.use_submitted_status,
              style: {border: '0px', paddingLeft},
            }}
          />
        </StyledAccordionSummary>
        <StyledAccordionDetails
          style={{
            display: 'flex',
            flexDirection: 'column',
            padding: '0px 10px 0px 10px',
          }}
        >
          <AssignmentAssessment
            filter={filter}
            assignment={assignment}
            assessment={assessment}
          />
          <ObjectivesList
            assignmentAssessmentId={assessment?.id}
            courseDefinitions={courseDefinitions}
            objectiveAssessments={assessment?.objectiveAssessments}
            gradeSystem={gradeSystem}
            filter={filter}
          />
        </StyledAccordionDetails>
      </StyledAccordion>
      <AssessmentButton
        style={{
          cursor: 'pointer',
          position: 'absolute',
          right: '10px',
          top: '16px',
          color: colors.darkGrey,
        }}
        onStatusChange={(newStatus) => onStatusChange({status: newStatus, student})
        }
        submittedStatus={assignment.use_submitted_status}
        button={<MoreVert/>}
      />
    </div>
  );
};
