import React, {useState} from 'react';
import {ButtonBase} from '@material-ui/core';
import {colors, fonts} from '../theme';


const DrawerItem = (props) => {
  const {
    onClick, icon, iconObject, text, selected = false,
  } = props;

  const [highlight, setHighlight] = useState(false);
  const bgColor = highlight ? colors.grey : colors.white;

  return (
    <ButtonBase
      onMouseOver={() => setHighlight(true)}
      onMouseLeave={() => setHighlight(false)}
      focusRipple
      onClick={onClick}
      style={{
        width: '100%',
        height: '56px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        cursor: 'pointer',
        backgroundColor: selected ? colors.greyDarker : bgColor,
      }}>
      {icon
      && <img style={{height: '25px', margin: '0px 20px 0px 10px'}} src={icon}/>
      }
      {iconObject && <div style={{margin: '0px 20px 0px 10px'}}>{iconObject}</div>}
      <span style={{...fonts.MEDIUM, fontWeight: '800'}}>{text}</span>
    </ButtonBase>
  );
};

export default DrawerItem;
