class controller {
  updateCourse() {
    this.onUpdate({ course: this.course });
  }
}

export const CourseDefinitionDetails = {
  bindings: {
    course: '<',
    readOnly: '<',
    onUpdate: '&',
  },
  controller,
  // language=HTML
  template: `
    <div class="sb-list-dropdown
                cm-course-definition-details--container">
      <div
        layout="column"
        layout-align="stretch">

        <div
          layout="row"
          layout-align="start start">
          <md-input-container
            class="md-block
                   md-input-has-placeholder"
            flex>
            <label localize>
              __MSG_courseDetailsPurposeHeader__
            </label>

            <ng-quill-editor
              class="absolute-btns focused-btns"
              toolbar="true"
              read-only="$ctrl.readOnly"
              toolbar-entries="bold"
              ng-model="$ctrl.course.description"
              ng-change="$ctrl.updateCourse()">
            </ng-quill-editor>
          </md-input-container>
        </div>

        <div class="sb-list-dropdown__footer"
             layout-align="center start space-around">
          <md-button
            class="sb-button sb-button--right"
            ng-click="$root.toggleObject($ctrl.course)"
            localize>
            __MSG_closeBtn__
          </md-button>
        </div>
      </div>
    </div>
  `,
};
