import angular from 'angular';
import uiRouter from '@uirouter/angularjs';
import ngCookies from 'angular-cookies';
import ngAnimate from 'angular-animate';
import ngMessages from 'angular-messages';
import ngMaterial from 'angular-material';
import { react2angular } from 'react2angular';

import { CommonModule } from './app/common/common.module';
import UtilsModule from './app/utils/utils.module.ts';
import { CourseProgressModule } from './app/components/course-progress/course-progress.module';
import UserSetupModule from './app/userSetup/userSetup.module.ts';
import { AppConfig } from './app/app.config';
import { AppRun } from './app/app.run';
import { AppRouting } from './app/app.routing.ts';
import { UserInactiveModule } from './app/components/user-inactive/user-inactive.module';
import SharedModule from './app/shared/shared.module.ts';
import CourseManagementModule from './app/components/courseManagement/courseManagement.module.ts';
import AssignmentsModule from './app/assignments/assignments.module.ts';
import App from '../../react_src/app';
// import {Provider} from 'react-redux';
// console.log('xxx Provider: ', Provider);

// this one doesn't support the export statement
require('./vendors/ngquill.js');

// include templates - webpack hack
function requireAll(requireContext) {
  requireContext.keys().map(requireContext);
}
// TODO (dydek) remove app.html from ng-cache
requireAll(require.context('./app/', true, /\.html$/));

if (!DEVELOPMENT_ENV) {
  Raven.config(RAVEN_URL, {
    release: RELEASE,
  })
    .addPlugin(require('raven-js/plugins/angular'), angular)
    .install();
}

const VENDOR_MODULES = [
  uiRouter,
  'ui.router.upgrade',
  ngCookies,
  ngMaterial,
  ngAnimate,
  ngMessages,
  'ngQuill',
];

const APP_MODULES = [
  CommonModule,
  UtilsModule,
  CourseManagementModule,
  CourseProgressModule,
  AssignmentsModule,
  UserSetupModule,
  UserInactiveModule,
  SharedModule,
];

if (!DEVELOPMENT_ENV) {
  APP_MODULES.unshift('ngRaven');
}
// import { Provider } from 'react-redux';
// import store from '../../react_src/redux/store';
angular
  .module('app', [...VENDOR_MODULES, ...APP_MODULES])
  .component(
    'rapp',
    react2angular(App, ['data', 'ctrl', 'view', 'student']),
  )
  .config(AppConfig)
  .config(AppRouting)
  .run(AppRun);

