export enum SchoolCategory {
  K_12,
  HIGHER_EDUCATION,
  HOME_SCHOOL,
  CORPORATION,
  OTHER,
}

export interface SchoolRequest {
  category: SchoolCategory;
  city: string;
  comment?: string;
  country_code: string;
  postal_code: string;
  school_name: string;
  state: string;
  street_address: string;
  website: string;
}

export interface School {
  readonly id: number;
  readonly is_intercom_enabled: boolean;
  readonly title: string;
  readonly url: string;
}


