import React, {useContext, useState} from 'react';
import ButtonBase from '@material-ui/core/ButtonBase';
import {colors, fonts} from '../theme';
import MoreMenu from './moreMenu';
import {getTranslation} from '../i18n';
import {ReactComponent as GridIcon} from '../images/grid.svg';
import AssignmentItem from './assignmentItem';
import {getAssignmentIcon, GlobalContext} from "../utils";
import Tooltip from "./tooltip";

const menuOptionsConnected = [
  {
    title: getTranslation('editAssessmentCriteriaBtn'),
    name: 'ASSESSMENT_CRITERIA',
  },
  {
    title: getTranslation('disconnectFromStudyBee'),
    name: 'DISCONNECT',
  },
];
const menuOptionsNotConnected = [
  {
    title: getTranslation('connectToStudybee'),
    name: 'CONNECT',
  },
];

const TopicItem = (props) => {
  const {
    assignment: topic,
    onAssignmentClicked,
    onGridClicked,
    handleAssignmentMenuClick,
    isStudent,
  } = props;

  const context = useContext(GlobalContext);
  const {assignmentSortMethod} = context;
  const [focused, setFocused] = useState(false);

  const click = () => {
    if(topic.connected){
      onAssignmentClicked(topic);
    }
  };

  return (
    <div>
      <div
        onMouseEnter={() => setFocused(true)}
        onMouseLeave={() => setFocused(false)}
        style={{
          height: '70px',
          width: '100%',
          display: 'flex',
          borderBottom: `1px solid ${colors.borderColor}`,
          backgroundColor: focused ? colors.borderColor : colors.white,
        }}
      >
        <ButtonBase
          onClick={click}
          focusRipple
          style={{
            display: 'flex',
            alignItems: 'center',
            // flex: 1,
            justifyContent: 'flex-start',
            paddingLeft: '16px',
          }}
        >
          {getAssignmentIcon(topic)}
          <p
            style={{
              ...fonts.MEDIUM,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap',
              width: '190px',
              textAlign: 'start',
              fontWeight:'800',
            }}
          >
            {topic.title}
          </p>
        </ButtonBase>
        <div
          style={{
            display: 'flex',
            border: '0px solid',
            justifyContent: 'flex-end',
            alignItems: 'center',
            width: '100%',
          }}
        >
          {!isStudent && (
            <>
              {topic.connected &&
              <Tooltip title={getTranslation('gridOverviewTooltip')}>
                <div>
                  <GridIcon
                    fill={colors.darkGrey}
                    style={{
                      width: '20px',
                      height: '20px',
                      border: '0px solid',
                      cursor: 'pointer',
                    }}
                    onClick={() => onGridClicked(topic)}
                  />
                </div>
              </Tooltip>
              }
              <MoreMenu
                style={{}}
                iconStyle={{color: colors.darkGrey, marginBottom: '4px'}}
                options={topic.connected?menuOptionsConnected:menuOptionsNotConnected}
                onClick={(option) => handleAssignmentMenuClick(topic, option)}
              />
            </>
          )}
        </div>
      </div>
      {topic?.assignments
        .filter(a => a.connected)
        .sort((a, b) => {
          if (assignmentSortMethod === 'alpha') {
            return a.title.localeCompare(b.title);
          }
          const ad = new Date(a.created);
          const bd = new Date(b.created);
          return bd - ad;
        })
        .map((assignment) => (
          <div key={assignment.id}>
            <AssignmentItem {...{...props, assignment, inTopic: true}} />
          </div>
        ))}
    </div>
  );
};

export default TopicItem;
