import { Events } from '../../common/global-events.ts';
import { getTranslation } from '../../../../../react_src/i18n';

export function MainMenu() {
  function controller(
    $scope, $mdSidenav, Path, UserInfo, NavigationHistory,
    Updates, MainMenu, Mixpanel,
  ) {
    'ngInject';

    const vm = this;

    /**
     * Set default option - set default if selectedMenuStateName is null
     */
    vm.setDefaultOption = function () {
      if (MainMenu.selectedMenuStateName) {
        return;
      }

      if (vm.userRole === 'teacher') {
        MainMenu.setSelectedMenuStateName('teacherAssignments');
      } else if (vm.userRole === 'student') {
        MainMenu.setSelectedMenuStateName('studentAssignments');
      }
    };

    /**
     * Toggle menu
     */
    vm.toggleMenu = function () {
      $mdSidenav('studybee-menu').toggle();

      vm.showBackdrop = !!$mdSidenav('studybee-menu').isOpen();
    };

    /**
     * Update assignment option
     */
    vm.updateAssignmentOption = () => {
      vm.streamStudentName = MainMenu.selectedStudent.name;
      if (MainMenu.selectedAssignment) {
        vm.streamAssignmentName = MainMenu.selectedAssignment.title;
        vm.streamAssignmentId = MainMenu.selectedAssignment.id;
      } else {
        vm.streamAssignmentName = undefined;
        vm.streamAssignmentId = undefined;
      }
    };

    vm.checkCurrentActive = (stateSectionName) => vm.selectedOption.startsWith(stateSectionName);

    /**
     * Update selected option
     */
    vm.updateSelectedOption = () => {
      vm.selectedOption = MainMenu.selectedMenuStateName;
    };

    /**
     * Update synchronize option
     */
    vm.updateSynchronizeDate = () => {
      vm.synchronizeDate = Updates.assignmentsAndStudentsDate;
    };

    /**
     * Go to specific website
     * @param {String} translationURLKey
     */
    vm.goToLink = (translationURLKey) => {
      const url = getTranslation(translationURLKey);
      window.open(url, '_blank');

      vm.showBackdrop = false;
      $mdSidenav('studybee-menu').close();
    };

    /**
     * Go to specific view
     * @param {String} stateName
     */
    vm.goToState = (stateName, stateParams) => {
      stateParams = stateParams || {};

      MainMenu.setSelectedMenuStateName(stateName);

      if (stateName.startsWith('teacherAssignments') || stateName.startsWith('studentAssignments')) {
        if (vm.streamStudentName && vm.userRole === 'teacher') {
          stateName = 'teacher_assess_assignment_student';
          stateParams.studentName = vm.streamStudentName;
          NavigationHistory.addToHistory('teacher_assess_assignment');
        } else if (vm.streamAssignmentName && vm.userRole === 'teacher') {
          stateName = 'teacher_assess_assignment';
          stateParams.assignment = MainMenu.selectedAssignment;
        } else if (vm.streamAssignmentName && vm.userRole === 'student') {
          stateName = 'studentAssessed.details';
        }
      }

      vm.showBackdrop = false;
      $mdSidenav('studybee-menu')
        .close()
        .then(() => {
          NavigationHistory.go(stateName, stateParams);
        });
    };

    /**
     * Synchronize Data
     */
    vm.synchronizeData = () => {
      Updates.updateAssignmentsAndStudents(Path.getClassroomId(), true, true);

      vm.showBackdrop = false;
      $mdSidenav('studybee-menu').close();
    };

    /**
     * Send info to Mixpanel
     * @param {String} eventCode Mixpanel event code
     */
    vm.mixpanelTrack = (eventCode) => {
      Mixpanel.track(eventCode);
    };

    /**
     * Set callback which is trigger on menu icon click
     */
    MainMenu.setToggleCallback(vm.toggleMenu);

    /**
     * Set callback which is trigger on set assignment
     */
    MainMenu.setAssignmentCallback(vm.updateAssignmentOption);

    /**
     * Set callback which is trigger on set selected menu state name
     */
    MainMenu.setSelectedMenuStateNameCallback(vm.updateSelectedOption);

    /**
     * Set callback which is trigger on synchronize data
     */
    Updates.setSynchronizeDataCallback(vm.updateSynchronizeDate);

    /**
     * Get user role after it's set by Custom Routing
     */
    $scope.$on(Events.STATE_CHECKED, () => {
      vm.userRole = UserInfo.userRole;
      vm.setDefaultOption();
    });
  }

  return {
    restrict: 'E',
    templateUrl: 'utils/views/main_menu.html',
    controller,
    controllerAs: 'vm',
  };
}
