import React, {memo, useContext, useEffect, useMemo, useRef, useState} from 'react';
import debounce from 'lodash.debounce';
import clonedeep from 'lodash.clonedeep';
import Header from './header';
import Card from './card';
import {colors, fonts} from '../theme';
import AssessmentSlider from './assessmentSlider';
import FeedbackInput from './feedbackInput';
import {getGradeSystemValues, getLastModified, GlobalContext} from '../utils';
import {getTranslation} from '../i18n';
import GradeCounter from './gradeCounter';
import PublishIcon from './publishIcon';

const objectiveFeedbackInputText = getTranslation('objectiveFeedbackInput');
const objectivePrivateNoteText = getTranslation('objectivePrivateNoteInput');
const teacherSelfAssessmentText = getTranslation('teacherSelfAssessmentLabel');

const ObjectivesList = (props) => {
  const context = useContext(GlobalContext);
  const {
    assignmentAssessmentId,
    courseDefinitions,
    objectiveAssessments: initialObjectiveAssessments,
    gradeSystem,
    onSave,
    failWarning,
    filter = ['grade', 'feedback', 'note', 'studentAssessment'],
    textFilter = '',
    isTopic,
    multiEdit = false,
  } = props;

  const {Api, showObjectiveDetails, isStudent} = context;
  const objectivesToSave = useRef({});
  const [objectiveAssessments, setObjectiveAssessments] = useState(initialObjectiveAssessments);

  useEffect(() => {
    if (multiEdit) {
      setObjectiveAssessments(objectiveAssessments.map(a => ({
        ...a,
        feedback_published: false,
        feedback_last_edit: null,
        counter_grade_0: 0,
        counter_grade_1: 0,
        counter_grade_2: 0,
        counter_grade_3: 0,
        counter_grade_4: 0,
        counter_grade_5: 0,
        counter_grade_6: 0,
        counter_grade_7: 0,
        counter_grade_8: 0,
        counter_grade_9: 0,
        feedback: null,
        grade: null,
        grade_id: null,
        grade_last_edit: null,
        grade_published: false,
        last_edit_for_student: null,
        private_note: null,
        private_note_last_edit: null,
        self_assessment: null,
        self_assessment_last_edit: null,
      })));
    } else {
      objectivesToSave.current = {};
      setObjectiveAssessments(initialObjectiveAssessments);
    }
  }, [multiEdit]);

  useEffect(() => {
    if (!multiEdit) {
      setObjectiveAssessments(initialObjectiveAssessments);
    }
  }, [assignmentAssessmentId, initialObjectiveAssessments]);

  const debouncedUpdateObjectiveAssessment = useMemo(() => debounce(async (multiEdit, onSave, objectivesToSave, Api) => {
    if (multiEdit) {
      onSave(objectivesToSave.current);
      return;
    }
    const objectives = clonedeep(objectivesToSave.current);
    objectivesToSave.current = {};
    if (onSave) {
      onSave();
    }
    await Api.updateObjectiveAssessmentsGql(Object.keys(objectives).map((key) => objectives[key]));
  }, 1000), [assignmentAssessmentId]);

  if (!objectiveAssessments?.length) {
    return null;
  }

  const saveObjective = (obj) => {
    objectivesToSave.current[obj.id] = obj;
    debouncedUpdateObjectiveAssessment(multiEdit, onSave, objectivesToSave, Api);
  };

  const changeObjectiveText = async (text, name, {id}) => {
    const ass = objectiveAssessments.find((a) => a.id === id);
    switch (name) {
      case 'feedback_to_student_on_objective':
        ass.feedback = text;
        ass.feedback_last_edit = Date.now();
        break;
      case 'private_note':
        ass.private_note = text;
        ass.private_note_last_edit = Date.now();
        break;
      case 'student_self_assessment_on_objective':
        ass.self_asessment = text;
        break;
      default:
        break;
    }
    await saveObjective(ass);
  };

  const changeObjectiveGrade = async (grade, {id}) => {
    const ass = objectiveAssessments.find((a) => a.id === id);
    ass.grade_id = grade?.id;
    ass.grade_name = grade?.name;
    ass.grade_last_edit = Date.now();
    ass.grade = grade;
    await saveObjective(ass);
  };

  const changeObjectiveGradeCount = async (data) => {
    const {count, id, gradeId} = data;
    const ass = objectiveAssessments.find((a) => a.id === id);
    ass[`counter_grade_${gradeId}`] = count;
    await saveObjective(ass);
  };

  const publishObjectiveValue = async (name, publish, {id}) => {
    const ass = objectiveAssessments.find((a) => a.id === id);
    switch (name) {
      case 'grade':
        ass.grade_published = publish;
        break;
      case 'feedback_to_student_on_objective':
        ass.feedback_published = publish;
        break;
      default:
        break;
    }
    await saveObjective(ass);
  };

  const gradeSystemValues = getGradeSystemValues(gradeSystem);

  const showGrade = !!filter.find((f) => f === 'grade');
  const showFeedback = !!filter.find((f) => f === 'feedback');
  const showNote = !!filter.find((f) => f === 'note');
  const showSelfAssessment = false; //filter.find((f) => f === 'studentAssessment');

  if (!showGrade && !showFeedback && !showNote && !showSelfAssessment) {
    return null;
  }

  return (
    <>
      {courseDefinitions?.map((courseDefinition) => {
        const {objectives} = courseDefinition;
        if (!objectives || objectives.length === 0) {
          return null;
        }

        const assessments = objectives.filter((o) => objectiveAssessments.find((oa) => oa.objective_id === o.id));
        if (!assessments.length) {
          return null;
        }
        const filteredObjectives = assessments.filter((o) => o.name.toLowerCase().includes(textFilter));
        if (filteredObjectives.length === 0) {
          return (
            <div>
              <Header
                style={{
                  backgroundColor: isTopic ? colors.topic : colors.green,
                }}
                title={courseDefinition.name}
              />
              <span style={{...fonts.MEDIUM, marginLeft: '10px'}}>
                {getTranslation('objectiveFilterNoMatch')}
              </span>
            </div>
          );
        }
        return (
          <div key={courseDefinition.id}>
            <Header
              style={{backgroundColor: isTopic ? colors.topic : colors.green}}
              title={courseDefinition.name}
            />
            {filteredObjectives.sort((a, b) => (a.name.localeCompare(b.name, undefined, {
              numeric: true,
              sensitivty: 'base'
            }))).map((objective) => {
              const assessment = objectiveAssessments.find(
                (a) => a.objective === objective.id
                  || a.objective_id === objective.id,
              );
              const defaultIndex = gradeSystemValues.findIndex(
                (v) => v.id === assessment?.grade_id,
              );

              return (
                <Card style={{margin: '10px 0 10px 0'}} key={objective.id}>
                  <span
                    onClick={() => showObjectiveDetails(objective.id)}
                    style={{
                      ...fonts.MEDIUM,
                      cursor: 'pointer',
                      marginBottom: '10px',
                    }}
                  >
                    {objective.name}
                  </span>
                  <div
                    style={{...fonts.MEDIUM}}
                    dangerouslySetInnerHTML={{
                      __html: objective.description,
                    }}
                  />
                  {showGrade && !gradeSystem?.is_counter && (
                    <AssessmentSlider
                      disabled={isStudent}
                      showPublish={!isStudent}
                      id={objective.id}
                      published={assessment?.grade_published}
                      helpText={getLastModified(assessment?.grade_last_edit)}
                      values={gradeSystemValues}
                      defaultIndex={defaultIndex < 0 ? 0 : defaultIndex}
                      onChangeCommit={(index) => {
                        changeObjectiveGrade(
                          gradeSystem.grades.find(
                            (g) => g.id === gradeSystemValues[index].id,
                          ),
                          assessment,
                        );
                      }}
                      onPublish={(value) => publishObjectiveValue('grade', value, assessment)
                      }
                      multiEdit={multiEdit}
                    />
                  )}
                  {showGrade && gradeSystem?.is_counter && (
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        marginBottom: '10px',
                      }}
                    >
                      <div
                        style={{
                          display: 'flex',
                          flexWrap: 'wrap',
                          justifyContent: 'space-between',
                        }}
                      >
                        {gradeSystem.grades.map((g, i) => (
                          <GradeCounter
                            readonly={isStudent}
                            id={assessment?.id}
                            gradeId={i}
                            key={g.name}
                            name={g.name}
                            count={assessment[`counter_grade_${i}`] || 0}
                            max={assessment[`max_counter_grade_${i}`]}
                            onChange={changeObjectiveGradeCount}
                          />
                        ))}
                      </div>
                      {!isStudent && (
                        <PublishIcon
                          multiEdit={multiEdit}
                          published={assessment?.grade_published}
                          onClick={(value) => publishObjectiveValue('grade', value, assessment)
                          }
                        />
                      )}
                    </div>
                  )}
                  {showFeedback && <FeedbackInput
                    id={'feedback_to_student_on_objective'}
                    assessment={assessment}
                    published={assessment?.feedback_published}
                    placeholder={objectiveFeedbackInputText}
                    onTextChange={changeObjectiveText}
                    helperText={getLastModified(assessment?.feedback_last_edit)}
                    text={assessment?.feedback}
                    onPublish={publishObjectiveValue}
                    hideEye={isStudent}
                    readonly={isStudent}
                    warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
                    multiEdit={multiEdit}
                  />}
                  {showNote && !isStudent && (
                    <FeedbackInput
                      id={'private_note'}
                      assessment={assessment}
                      placeholder={objectivePrivateNoteText}
                      text={assessment?.private_note}
                      helperText={getLastModified(
                        assessment?.private_note_last_edit,
                      )}
                      onTextChange={changeObjectiveText}
                      hideEye
                      warningText={multiEdit ? getTranslation('warning_overwrite_text') : ''}
                      multiEdit={multiEdit}
                    />
                  )}
                  {showSelfAssessment && (
                    <FeedbackInput
                      style={{marginBottom: '10px'}}
                      id={'student_self_assessment_on_objective'}
                      placeholder={teacherSelfAssessmentText}
                      onTextChange={changeObjectiveText}
                      text={assessment?.self_assessment}
                      hideEye
                      readonly
                    />
                  )}
                </Card>
              );
            })}
          </div>
        );
      })}
    </>
  );
};

export default memo(ObjectivesList);
