import moment from 'moment';
import { Events } from '../../../../common/global-events.ts';
import { BehaviourSubject } from '../../../../shared/services/helpers/behaviourSubject.service.ts';

export class StudentCoursesDetails {
  static STATE_PREFIX = {
    student: 'student-course-progress.courses-overview',
    teacher: 'teacher-course-progress.details',
  };

  static NO_STUDENT_SELECTED_STATE = {
    name: '',
    params: {},
  };

  static COURSE_DETAILS_STATE = {
    name: '.learning-objectives',
  };

  constructor($rootScope, $state, $stateParams, $q, FetchData, StudentDetailsEditDialog, UserInfo) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
    this.$stateParams = $stateParams;
    this.$q = $q;
    this.FetchData = FetchData;
    this.StudentDetailsEditDialog = StudentDetailsEditDialog;
    this.UserInfo = UserInfo;
    this.isTeacher = this.UserInfo.isTeacher();
    this.studentCoursesDefinitions$ = new BehaviourSubject(null);
  }

  fetch(student = this.getCurrentStudent()) {
    return this.FetchData.v3
      .getStudentCoursesDefinitions(student.id)
      .then((response) => {
        this.studentCoursesDefinitions$.next(response);

        return response;
      });
  }

  update(student, courseDefiniton) {
    if (courseDefiniton.target_grade && courseDefiniton.target_grade.id) {
      courseDefiniton.target_grade = courseDefiniton.target_grade.id;
    }

    this.showNewRequestToast();

    return this.FetchData.v3
      .updateCourseDefinitionAssessment(
        student.id,
        courseDefiniton.course_definition,
        courseDefiniton,
      )
      .then((response) => {
        this.showSuccessToast();
        return response;
      });
  }

  getCurrentStudent() {
    return this.isTeacher ? this.$stateParams.student : this.UserInfo.getUserObj();
  }

  extendByCourseDetails(studentCoursesDefinitions = [], courses = []) {
    if (!!studentCoursesDefinitions.length && !!courses.length) {
      return studentCoursesDefinitions.map((coursesDefinitions) => {
        const courseInClass = !!courses.find(
          course => course.id === coursesDefinitions.course_definition,
        );

        return Object.assign({}, coursesDefinitions, {
          courseInClass,
        });
      });
    }

    return studentCoursesDefinitions;
  }

  extendByOwner(studentCoursesDefinitions = []) {
    if (studentCoursesDefinitions.length) {
      return studentCoursesDefinitions.map((coursesDefinitions) => {
        return Object.assign({}, coursesDefinitions, {
          isOwner: false,
        });
      });
    }

    return studentCoursesDefinitions;
  }

  splitByCategory(studentCoursesDefinitions) {
    const archived = this.filterArchived(studentCoursesDefinitions);
    const current = this.filterCurrentClass(studentCoursesDefinitions);
    const other = this.filterOtherClass(studentCoursesDefinitions);

    return { archived, current, other };
  }

  filterCurrentClass(studentCoursesDefinitions) {
    return studentCoursesDefinitions.filter(
      studentCourseDefinitions =>
        !studentCourseDefinitions.google_classroom_data.archived &&
        studentCourseDefinitions.courseInClass,
    );
  }

  filterOtherClass(studentCoursesDefinitions) {
    return studentCoursesDefinitions.filter(
      studentCourseDefinitions =>
        !studentCourseDefinitions.google_classroom_data.archived &&
        !studentCourseDefinitions.courseInClass,
    );
  }

  filterArchived(studentCoursesDefinitions) {
    return studentCoursesDefinitions.filter(
      studentCourseDefinitions =>
        studentCourseDefinitions.google_classroom_data.archived,
    );
  }

  findClassName(studentCoursesDefinitions = []) {
    return studentCoursesDefinitions.length
      ? studentCoursesDefinitions[0].google_classroom_data.name
      : '';
  }

  filterNoneAccessibleCourses(studentCoursesDefinitions) {
    return studentCoursesDefinitions.filter((studentCourseDefinitions) =>
      studentCourseDefinitions.teacher_in_class || studentCourseDefinitions.shared_with_teacher || studentCourseDefinitions.is_mentor,
    );
  }

  goToNoStudentSelectedState() {
    const [stateName, ..._] = this.getStatePrefix().split('.');

    this.$state.go(
      stateName,
      StudentCoursesDetails.NO_STUDENT_SELECTED_STATE.params,
    );
  }

  goToCourseDetails(params) {
    const stateName = `${this.getStatePrefix()}${StudentCoursesDetails.COURSE_DETAILS_STATE.name}`;
    this.$state.go(stateName, params);
  }

  getStatePrefix() {
    return this.isTeacher ?
      StudentCoursesDetails.STATE_PREFIX.teacher : StudentCoursesDetails.STATE_PREFIX.student;
  }

  extendStudentObject(student = this.getCurrentStudent()) {
    return this.fetchStudentDetails(student.id)
      .then(
        additionalData => Object.assign({}, student, additionalData),
      )
      .then(extendedStudent => this.applyLastStudentUpdateDate(extendedStudent))
      .catch(() => student);
  }

  applyLastStudentUpdateDate(student) {
    let lastUpdate = null;

    if (this.isTeacher) {
      lastUpdate = moment(student.feedback_last_edit).isAfter(student.private_note_last_edit)
        ? student.feedback_last_edit
        : student.private_note_last_edit;
    } else if (student.feedback_last_edit) {
      lastUpdate = student.feedback_last_edit;
    }

    return Object.assign({}, student, { last_update: lastUpdate });
  }

  fetchStudentDetails(studentId) {
    return this.FetchData.getStudentDetails(studentId);
  }

  updateStudentDetails(studentId, data) {
    this.showNewRequestToast();
    return this.FetchData.updateStudentDetails(studentId, data)
      .then(response => {
        this.showSuccessToast();
        return response;
      });
  }

  showStudentDetailsEditDialog(student) {
    return this.StudentDetailsEditDialog.show({ student });
  }

  showNewRequestToast() {
    this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
  }

  showSuccessToast() {
    this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
  }
}
