import {Radio} from "@material-ui/core";
import {ReactComponent as DoneIcon} from "../images/icon_bk_assessment_done.svg";
import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import {ReactComponent as AccessingIcon} from "../images/icon_bk_assessing.svg";
import {ReactComponent as NotDoneIcon} from "../images/icon_bk_not_assessed.svg";
import React, {useEffect, useState} from "react";

const MultiEditStatusComponent = (props) => {
  const {onStatusChange} = props;
  const [status, setStatus] = useState();

  useEffect(() =>{
    onStatusChange(status);
  },[status]);

  return (
    <div style={{display: "flex", flexDirection: "column"}}>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === 'ASSESSMENT_DONE'}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus("ASSESSMENT_DONE");
            }
          }}
        />
        <DoneIcon fill={colors.green}/>
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation("teacherAssignmentsAssessmentsStudentMenuAssessed")}</span>
      </div>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === "ASSESSING"}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus("ASSESSING");
            }
          }}
        />
        <AccessingIcon fill={colors.orange}/>
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation("teacherAssignmentsAssessmentsStudentMenuAssessing")}</span>
      </div>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={
            status === "NOT_ASSESSED"
          }
          onChange={(e) => {
            if (e.target.checked) {
              setStatus("NOT_ASSESSED");
            }
          }}
        />
        <NotDoneIcon fill={colors.iconColor}/>
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation("teacherAssignmentsAssessmentsStudentMenuNotAssessed")}</span>
      </div>
    </div>
  );
};

export default MultiEditStatusComponent;