import { IComponentController } from 'angular';
import { ListViewType } from '../../../../../shared/enums/listViewType.enum';
import { IListViewMenuItem, ListViewMenuItem } from '../../../../../shared/models/listViewMenuItem.model';
import { IListViewMenuService } from '../../../../../shared/services/listViewMenu/listViewMenu.service';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherCourseManagementModule from '../../teacherCourseManagement.module';
import style from './cmTeacherObjectives.scss';
import template from './cmTeacherObjectives.tpl.html';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'cm-teacher-objectives',
  template: template,
  style: style,
})
export class CmTeacherObjectivesComponent implements IComponentController {
  @Input()
  public course: any;

  public view: ListViewType;
  public readonly toolbarMenuItems: ListViewMenuItem[];

  private readonly storageId: string;

  constructor(private ListViewMenuService: IListViewMenuService) {
    'ngInject';

    this.course = [];
    this.storageId = 'CmTeacherObjectives';
    this.view = ListViewType.TITLE_AND_DESCRIPTION;
    this.toolbarMenuItems = ListViewMenuService.getListViewMenu(this.storageId);
  }

  public $onInit(): void {
    this.view = this.toolbarMenuItems
      .find((listViewMenuItem: IListViewMenuItem): boolean => listViewMenuItem.checked)
      .eventArgs.view;
  }

  public onClickMenuItem(toolbarMenuItem: IListViewMenuItem): void {
    this.toolbarMenuItems.forEach((menuItem: any): void => {
      menuItem.checked = false;
    });
    toolbarMenuItem.checked = true;
    this.ListViewMenuService.saveListViewMenu(this.storageId, this.toolbarMenuItems);
    this.changeView(toolbarMenuItem.eventArgs);
  }

  private changeView(eventArgs: { [arg: string]: any }): void {
    this.view = eventArgs.view;
  }
}
