import { showHintDialog } from '../../../common/dialogs/dialogs.helper';

export class StudentCourseProgressComponent {
  static bindings = { courses: '<' };
  static templateUrl = 'components/course-progress/student-course-progress/student-course-progress.html';
  static controller = StudentCourseProgressComponent;

  constructor($state, $cookies, $timeout, $mdDialog, CourseProgress) {
    'ngInject';

    this.$state = $state;
    this.$cookies = $cookies;
    this.$timeout = $timeout;
    this.$mdDialog = $mdDialog;
    this.CourseProgress = CourseProgress;
    this.courseId = null;
    this.courses = [];
  }

  $onInit() {
    showHintDialog(this.$mdDialog, this.$timeout, this.$state.current.name, this.$cookies);

    if (this.CourseProgress.hasLastStateSaved) {
      this.$state.go(
        this.CourseProgress.lastStateSaved.name,
        this.CourseProgress.lastStateSaved.params,
      );
    }
  }
}
