import angular, { IRootElementService, IScope } from 'angular';
import { UnbindableCtrlBase } from '../../base-classes';
import { Accordion } from './accordion.service';

export class AccordionComponent extends UnbindableCtrlBase {
  static bindings = {
    open: '=?',
    onOpen: '&?',
    onClose: '&?',
    disabled: '<?ngDisabled',
  };
  static controller = AccordionComponent;
  static templateUrl = 'common/components/accordion/accordion.html';
  static transclude = {
    header: '?accordionHeader',
    footer: '?accordionFooter',
  };

  public open?: boolean;
  public disabled?: boolean;
  public onOpen?: (event?: { accordion: AccordionComponent }) => void;
  public onClose?: (event?: { accordion: AccordionComponent }) => void;
  public visible: boolean;

  constructor(private $scope: IScope, private $element: IRootElementService, private Accordion: Accordion) {
    'ngInject';

    super();
    this.open = false;
    this.disabled = false;
    this.onOpen = angular.noop;
    this.onClose = angular.noop;
    this.visible = false;
  }

  $onInit(): void {
    this.open = this.open && !this.disabled;

    this.boundEvents.push(
      this.$scope.$watch(
        () => this.open,
        () => {
          this.toggleState();
          this.triggerEvent();
        },
      ),
    );

    this.boundEvents.push(
      this.$scope.$watch(
        () => this.disabled,
        (disabled) => {
          this.disabled = disabled;
          this.open = this.open && this.disabled;
        },
      ),
    );

    this.toggleState();
  }

  toggle(): void {
    if (!this.disabled) {
      this.open = !this.open;
    }
  }

  toggleState(): void {
    if (!this.disabled) {
      if (this.open) {
        this.visible = true;
        this.Accordion.open(this.$element);
      } else {
        this.Accordion.close(this.$element).finally(() => {
          this.visible = false;
        });
      }
    }
  }

  triggerEvent(): void {
    if (!this.disabled) {
      if (this.open) {
        this.onOpen({ accordion: this });
      } else {
        this.onClose({ accordion: this });
      }
    }
  }
}
