import { IComponentController } from 'angular';
import { IObjectiveAssignmentAssessment } from '../../../../../../shared/models/objectiveAssignmentAssessment.model';
import { Component } from '../../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../../utils/decorators/input.decorator';
import TeacherCourseProgressModule, { IAssignmentOverviewStatusService } from '../../../teacher-course-progress.module';
import style from './assignmentOverviewStatus.scss';
import template from './assignmentOverviewStatus.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'assignment-overview-status',
  template: template,
  style: style,
})
export class AssignmentOverviewStatusComponent implements IComponentController {
  @Input()
  public objectiveAssignmentAssessment: IObjectiveAssignmentAssessment;

  constructor(private AssignmentOverviewStatusService: IAssignmentOverviewStatusService) {
    'ngInject';

    this.objectiveAssignmentAssessment = null;
  }

  public getLastUpdate(): string {
    return this.AssignmentOverviewStatusService
      .findLastUpdateDate(this.objectiveAssignmentAssessment);
  }
}
