import { IComponentController } from 'angular';
import get from 'lodash.get';
import { ListViewType } from '../../../../../../shared/enums/listViewType.enum';
import { ICoverageMatrix } from '../../../../../../shared/models/coverageMatrix.model';
import { IEventEmitter } from '../../../../../../shared/models/eventEmitter';
import { IListViewSwitch } from '../../../../../../shared/models/ListViewSwitch.model';
import { LocalStorageService } from '../../../../../../shared/services/localStorage/localStorage.service';
import { Component } from '../../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../../utils/decorators/input.decorator';
import { Output } from '../../../../../../utils/decorators/output.decorator';
import TeacherCourseProgressModule, { ListViewSwitchService } from '../../../teacher-course-progress.module';
import style from './listViewSwitch.scss';
import template from './listViewSwitch.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'list-view-switch',
  template: template,
  style: style,
  transclude: {
    left: '?leftSection',
    center: '?centerSection',
    right: '?rightSection',
  }
})
export class ListViewSwitchComponent implements IComponentController {
  @Output({ optional: true })
  public onViewChange: IEventEmitter<{ listViewSwitchType: ListViewType }>;
  @Output({ optional: true })
  public onCoverageMatrixChange: IEventEmitter<{ coverageMatrix: ICoverageMatrix }>;
  @Input()
  public coverageMatrixes: ICoverageMatrix[];

  public viewMenuItems: IListViewSwitch[];
  public informationMenuItems: IListViewSwitch[];
  public selectedCoverageMatrix: ICoverageMatrix;
  private readonly lastSelectedCoverageMatrixKey: string;
  private readonly currentSelectedViewKey: string;
  private readonly currentSelectedInformationKey: string;

  constructor(private ListViewSwitchService: ListViewSwitchService, private LocalStorageService: LocalStorageService) {
    'ngInject';

    this.coverageMatrixes = [];
    this.selectedCoverageMatrix = null;
    this.viewMenuItems = [];
    this.informationMenuItems = [];
    this.lastSelectedCoverageMatrixKey = 'lastSelectedMatrixCoverage';
    this.currentSelectedViewKey = 'currentSelectedView';
    this.currentSelectedInformationKey = 'studentProgressInformationMenu';
  }

  public $onInit(): void {
    this.viewMenuItems = this.ListViewSwitchService.getViewMenuItems(this.coverageMatrixes);
    this.informationMenuItems = this.ListViewSwitchService.getInformationMenuItems();

    if (this.LocalStorageService.exists(this.lastSelectedCoverageMatrixKey)) {
      const lastSelectedCoverageMatrixId: number = +this.LocalStorageService.get(this.lastSelectedCoverageMatrixKey);

      this.selectedCoverageMatrix = this.ListViewSwitchService
        .getCoverageMatrixById(this.coverageMatrixes, lastSelectedCoverageMatrixId);
    }

    if (this.LocalStorageService.exists(this.currentSelectedViewKey)) {
      const currentSelectedViewId: string = this.LocalStorageService.get(this.currentSelectedViewKey);
      this.restoreMainMenuSelection(currentSelectedViewId, this.viewMenuItems);
    }

    if (this.LocalStorageService.exists(this.currentSelectedInformationKey)) {
      const currentSelectedInformationViewId: string = this.LocalStorageService.get(this.currentSelectedInformationKey);
      this.restoreMainMenuSelection(currentSelectedInformationViewId, this.informationMenuItems);
    }

    if (!this.selectedCoverageMatrix) {
      this.selectedCoverageMatrix = get(this.coverageMatrixes, ['0'], null);
    } else {
      this.onSelectCoverageMatrix();
    }

    if (!this.isViewMenuActive()) {
      this.redirectToNonCoverageMatrixView();
    }
  }

  public onMenuClick(menuItem: IListViewSwitch): void {
    if ([ListViewType.COVERAGE_MATRIX, ListViewType.STATUS].includes(menuItem.type)) {
      this.onClickViewMenu(menuItem);
    } else {
      this.onClickInformationMenu(menuItem);
    }
  }

  public onSelectCoverageMatrix(): void {
    this.onCoverageMatrixChange({ coverageMatrix: this.selectedCoverageMatrix });
    this.LocalStorageService.set(this.lastSelectedCoverageMatrixKey, `${this.selectedCoverageMatrix.id}`);
  }

  public isCoverageMatrixView(): boolean {
    return this.ListViewSwitchService.isCoverageMatrixView(this.viewMenuItems);
  }

  private onClickViewMenu(menuItem: IListViewSwitch): void {
    if (!menuItem.isDisabled && this.isViewMenuActive()) {
      this.setMenuSelection(menuItem);
      this.onViewChange({ listViewSwitchType: menuItem.type });
    } else if (menuItem.isDisabled) {
      this.ListViewSwitchService.showDialogWithDisableMessage();
    } else {
      this.redirectToNonCoverageMatrixView();
    }
  }

  private onClickInformationMenu(menuItem: IListViewSwitch): void {
    this.informationMenuItems = this.ListViewSwitchService.setSelection(menuItem, this.informationMenuItems);
    this.LocalStorageService.set(this.currentSelectedInformationKey, menuItem.id);
    this.onViewChange({ listViewSwitchType: menuItem.type });
  }

  private setMenuSelection(menuItem: IListViewSwitch): void {
    this.viewMenuItems = this.ListViewSwitchService.setSelection(menuItem, this.viewMenuItems);
    this.LocalStorageService.set(this.currentSelectedViewKey, menuItem.id);
  }

  private isViewMenuActive(): boolean {
    return this.coverageMatrixes.length !== 0;
  }

  private redirectToNonCoverageMatrixView(): void {
    const nonCoverageMatrixView: IListViewSwitch =
      this.ListViewSwitchService.findFirstNonCoverageMatrixView(this.viewMenuItems);

    this.setMenuSelection(nonCoverageMatrixView);
    this.onViewChange({ listViewSwitchType: nonCoverageMatrixView.type });
  }

  private restoreMainMenuSelection(menuItemId: string, menu: IListViewSwitch[]): void {
    const menuItem: IListViewSwitch = this.ListViewSwitchService
      .getMenuItemById(menu, menuItemId);

    this.onMenuClick(menuItem);
  }
}
