import { IObserver, IUnsubscribe } from './observer.service';
import { ISubject, Subject } from './subject.service';

export interface IBehaviourSubject<T = any> extends ISubject<T> {
  value: T;
  getValue(): T;
}

export class BehaviourSubject<T = any> extends Subject<T> implements IBehaviourSubject<T> {

  public value: T;

  constructor(value?: T) {
    'ngInject';
    super();
    this.value = value;
  }

  public subscribe(observer: IObserver<T>): IUnsubscribe {
    observer.closed = false;
    observer.next(this.value);

    return super.subscribe(observer);
  }

  public next(value: T): void {
    this.value = value;
    super.next(value);
  }

  public getValue(): T {
    return this.value;
  }
}
