import {Radio} from "@material-ui/core";
import {fonts} from "../theme";
import {getTranslation} from "../i18n";
import React, {useEffect, useState} from "react";
import {getSubmittedIcon} from "../utils";

const SUBMITTED = 'SUBMITTED';
const SUBMITTED_LATE = 'SUBMITTED_LATE';
const NOT_SUBMITTED = 'NOT_SUBMITTED';
const NOT_SUBMITTED_LATE = 'NOT_SUBMITTED_LATE'

const MultiEditSubmissionComponent = (props) => {
  const {onStatusChange} = props;
  const [status, setStatus] = useState();

  useEffect(() => {
    onStatusChange(status);
  }, [status]);

  return (
    <div style={{display: "flex", flexDirection: "column"}}>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === SUBMITTED}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus(SUBMITTED);
            }
          }}
        />
        {getSubmittedIcon({status: SUBMITTED})}
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation('mark_submitted_status_submitted')}</span>
      </div>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === SUBMITTED_LATE}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus(SUBMITTED_LATE);
            }
          }}
        />
        {getSubmittedIcon({status: SUBMITTED_LATE})}
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation('mark_submitted_status_submitted_late')}</span>
      </div>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === NOT_SUBMITTED_LATE}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus(NOT_SUBMITTED_LATE);
            }
          }}
        />
        {getSubmittedIcon({status: NOT_SUBMITTED_LATE})}
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation('mark_submitted_status_not_submitted_late')}</span>
      </div>
      <div style={{display: "flex", alignItems: "center"}}>
        <Radio
          checked={status === NOT_SUBMITTED}
          onChange={(e) => {
            if (e.target.checked) {
              setStatus(NOT_SUBMITTED);
            }
          }}
        />
        {getSubmittedIcon({status: NOT_SUBMITTED})}
        <span style={{
          ...fonts.MEDIUM,
          marginLeft: "6px"
        }}>{getTranslation('mark_submitted_status_not_submitted')}</span>
      </div>
    </div>
  );
};

export default MultiEditSubmissionComponent;