import { Breadcrumb } from '../../../../../shared/models/breadcrumb.model.ts';
import { ListViewType } from '../../../../../shared/enums/listViewType.enum.ts';

export * from './objectives-list.scss';

export const ObjectivesList = {
  bindings: {
    assessments: '<',
    course: '<',
  },
  templateUrl: 'components/course-progress/student-course-progress/course-details/objectives-list/objectives-list.html',
  controller: class {
    constructor($state, $stateParams, $filter, $timeout, BreadcrumbsService, ListViewMenuService) {
      'ngInject';
      this.storageViewMenuId = 'studentStudentCourseProgressObjectiveList';
      this.$state = $state;
      this.$stateParams = $stateParams;
      this.$filter = $filter;
      this.$timeout = $timeout;
      this.BreadcrumbsService = BreadcrumbsService;
      this.ListViewMenuService = ListViewMenuService;
      this.viewMenuItems = [];
      this.viewMode = ListViewType.TITLE_ONLY;
      this.assessments = [];
      this.course = null;
    }

    $onInit() {
      this.viewMenuItems = this.ListViewMenuService.getListViewMenu(this.storageViewMenuId);
      this.viewMode = this.viewMenuItems.find(listViewMenuItem => listViewMenuItem.checked)
        .eventArgs.view;
    }

    toggleAssessment(assessment) {
      assessment.open = !assessment.open;
    }

    navigateToLearningObjective(assessment) {
      const breadcrumbs = [
        ...this.$stateParams.breadcrumbs,
        new Breadcrumb(
          this.course.course_definition_name,
          this.BreadcrumbsService.createGoToAction(),
        ),
      ];

      this.$state.go(
        'student-course-progress.courses-overview.learning-objectives.assessments',
        {
          assessment,
          breadcrumbs,
          course: this.course,
          objective: assessment.objective,
        },
      );
    }

    onClickMenuItem(toolbarMenuItem) {
      this.viewMenuItems.forEach((menuItem) => {
        Object.assign(menuItem, { checked: false });
      });
      Object.assign(toolbarMenuItem, { checked: true });
      this.ListViewMenuService.saveListViewMenu(this.storageViewMenuId, this.viewMenuItems);
      this.viewMode = toolbarMenuItem.eventArgs.view;
    }

    isTitleVisible(title) {
      return this.viewMode !== ListViewType.DESCRIPTION_ONLY && `${title}`.trim().length;
    }

    isDescriptionVisible(description) {
      return this.viewMode !== ListViewType.TITLE_ONLY &&
        `${this.$filter('getFormattedOrEmpty')(description)}`.trim().length;
    }

    expandAll() {
      this.changeOpenStateForAll(true);
    }

    collapseAll() {
      this.changeOpenStateForAll(false);
    }

    changeOpenStateForAll(state) {
      this.$timeout(() => {
        this.assessments = this.assessments.map((assessment) => {
          assessment.open = state;

          return assessment;
        });
      }, 0);
    }
  },
};
