import { Events } from '../../../../common/global-events.ts';
import { Breadcrumb } from '../../../../shared/models/breadcrumb.model.ts';
import { Observer } from '../../../../shared/services/helpers/observer.service.ts';
import { UnsubscribableComponent } from '../../../../shared/components/abstracts/unsubscribable.component.ts';

export * from './studentDetails.scss';

export class StudentDetailsComponent extends UnsubscribableComponent {
  static bindings = {
    courses: '<',
    courseSelected: '<',
    courseAssessment: '<',
  };
  static controller = StudentDetailsComponent;
  static templateUrl = 'components/course-progress/teacher-course-progress/studentDetails/studentDetails.html';

  constructor($rootScope, $scope, $state, $stateParams, $timeout, BreadcrumbsService, FetchData,
              StudentCoursesDetails) {
    'ngInject';

    super();
    this.$rootScope = $rootScope;
    this.$scope = $scope;
    this.$state = $state;
    this.$stateParams = $stateParams;
    this.$timeout = $timeout;
    this.BreadcrumbsService = BreadcrumbsService;
    this.FetchData = FetchData;
    this.StudentCoursesDetails = StudentCoursesDetails;
    this.student = $stateParams.student || {};
    this.courses = $stateParams.courses || [];
    this.courseSelected = $stateParams.course;
    this.courseAssessment = $stateParams.courseAssessment || {};
    this.breadcrumbs = [];
    this.broadcastDelay = 200;
    this.isEditable = false;
    this.courseDefinitions = null;
    this.isTeacher = this.StudentCoursesDetails.isTeacher;
    this.fetchStudentCoursesDetails = false;
  }

  $onInit() {
    const observer = new Observer(this.onNextStudentCoursesDefinitions.bind(this));

    this.$$unsubscribes
      .push(this.StudentCoursesDetails.studentCoursesDefinitions$.subscribe(observer));

    // show no-course view if courseSelected is undefined
    if (!this.courseSelected) {
      this.$state.go('teacher-course-progress.details.no-courses');
    } else {
      this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);

      this.breadcrumbs = [...this.$stateParams.breadcrumbs].concat(
        new Breadcrumb(
          this.courseSelected.course_definition_name,
          this.BreadcrumbsService.createGoToAction(),
        ),
      );
    }

    this.isEditable = this.$stateParams.courseDefinition &&
      this.$stateParams.courseDefinition.teacher_in_class;
  }

  changeCourse() {
    if (this.courseSelected.course_definition !== this.courseAssessment.course_definition) {
      this.$state.go(this.$state.current.name, {
        course: this.courseSelected,
        courseDefinition: this.courseSelected,
        breadcrumbs: this.breadcrumbs.slice(0, -1),
      }).then(() => {
        if (!this.isTeacher && this.breadcrumbs.length === 1) {
          this.$rootScope.$emit(Events.SELECTED_COURSE_CHANGED, this.courseSelected);
        }
      });
    }
  }

  toggleAssessment() {
    this.courseAssessment.open = !this.courseAssessment.open;
  }

  updateAssessmentData(assessmentData) {
    this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
    this.FetchData.v3
      .updateCourseDefinitionAssessment(
        this.student.id,
        this.courseSelected.course_definition,
        assessmentData,
      )
      .then((assessment) => {
        Object.assign(assessment, { open: assessmentData.open });
        // Dont overwrite feedback and private_note, it messes with the ui
        const { feedback, private_note, result, ...rest } = assessment;
        this.courseAssessment = { ...this.courseAssessment, ...rest };
      })
      .then(() => {
        this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);
        this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
      });
  }

  closeStudentView() {
    if (this.breadcrumbs.length > 1) {
      this.$stateParams.breadcrumbs.pop().action();
    } else {
      this.StudentCoursesDetails.goToNoStudentSelectedState();
    }
  }

  sizeOfCoursesDefinitions() {
    return this.courseDefinitions ?
      this.mergedCourseDefinitions().length :
      0;
  }

  getSingleCourseDefinition() {
    return this.mergedCourseDefinitions()[0];
  }

  mergedCourseDefinitions() {
    return this.courseDefinitions ?
      Object
        .keys(this.courseDefinitions)
        .reduce((acc, key) => acc.concat(this.courseDefinitions[key]), []) :
      [];
  }

  onNextStudentCoursesDefinitions(studentCoursesDefinitions) {
    if (studentCoursesDefinitions) {
      let extendedCourseDefinitions = this.StudentCoursesDetails.extendByOwner(
        this.StudentCoursesDetails.extendByCourseDetails(studentCoursesDefinitions, this.courses),
      );

      if (this.isTeacher) {
        extendedCourseDefinitions = this.StudentCoursesDetails.filterNoneAccessibleCourses(extendedCourseDefinitions);
      }

      this.courseDefinitions = this.StudentCoursesDetails.splitByCategory(
        extendedCourseDefinitions,
      );
    } else {
      this.fetchStudentCoursesDetails = true;
      this.StudentCoursesDetails.fetch().then((coursesDefinitions) => {
        this.courseSelected = coursesDefinitions.find((courseDefinition) =>
          courseDefinition.course_definition === this.courseSelected.course_definition,
        );

        if (!this.courseSelected) {
          const stateName = this.isTeacher ? 'teacher-course-progress.details.courses-overview' :
            'student-course-progress.courses-overview';

          this.$state.go(stateName, { student: this.student });
        }
      }).finally(() => {
        this.fetchStudentCoursesDetails = false;
      });
    }
  }
}
