import {StateProvider} from '@uirouter/angularjs';
import {StateDeclaration, StateService, Transition, TransitionService} from '@uirouter/core';
import {IPromise} from 'angular';
import {AssignmentsService, Assignment} from '../../../../../src/app/student/assignments/state';

export const StudentAssignmentsRouting: Function = ($stateProvider: StateProvider, $transitionsProvider: TransitionService): void => {
  'ngInject';

  $transitionsProvider.onBefore({ to: state => state.name.startsWith('studentAssignments') }, (transition: Transition): any => {
    const transitionTo: StateDeclaration = transition.to();
    (transitionTo as any).themeClass = 'sb-theme-green';
    (transitionTo as any).role = 'student';

    return true;
  });


  $stateProvider
    .state('studentAssignments', {
      resolve: {
        assignmentsGroup: (AssignmentsService: AssignmentsService, $state: StateService): IPromise<Assignment[]> => {
          'ngInject';

          return new Promise(resolve => {
            AssignmentsService.get().subscribe(assignments => {
              if (assignments.length) {
                resolve(assignments);
              } else {
                $state.go('studentAssignmentsNotConfigured');
              }
            });
          });
        },
      },
      views: {
        '@': {
          component: 'studentAssignments',
        },
        'assignments@studentAssignments': {
          component: 'appAssignments',
        },
      },
    })
    .state('studentAssignmentsNotConfigured', {
      views: {
        '@': {
          component: 'studentAssignments',
        },
        'assignments@studentAssignmentsNotConfigured': {
          component: 'studentAssignmentsNotConfigured',
        },
      }
    });
};
