import {StateService} from '@uirouter/core';
import {IQService} from 'angular';
import {APIErrorCode} from '../../../../../src/app/shared/enmus/api-error-code.enum';
import {HttpResponse} from '../../shared/models/httpResponse.model';
import {IInterceptor} from '../../shared/models/interceptor.model';
import {Interceptor} from '../decorators/interceptor.decoreator';
import {HttpStatusCode} from '../enums/httpStatusCode.enum';
import UtilsModule from '../utils.module';
import {InitService} from '../../../../../src/app/core/init/init.service';


@Interceptor({
  module: UtilsModule,
})
export class CheckDomainInterceptor implements IInterceptor {
  constructor(protected $q: IQService,
              protected $state: StateService,
              protected $injector: angular.auto.IInjectorService) {
    'ngInject';
  }

  public responseError(response: HttpResponse): PromiseLike<HttpResponse> {
    if (response.status === HttpStatusCode.FORBIDDEN) {
      if (response.data.error_code === APIErrorCode.WHITELISTED_DOMAIN && response.data.is_teacher) {
        this.$state.go('userSetup');
      } else {
        this.$state.go('userSetup');
      }

      this.openStudybeeIfClose();
    }

    return this.$q.reject(response);
  }

  private openStudybeeIfClose(): void {
    const StudybeeHandle: any = this.$injector.get('StudybeeHandle');
    StudybeeHandle.studybeeActivate();
  }
}
