import { Events } from '../../../../../common/global-events.ts';
import style from './assignmentsList.scss';
import { AssignmentsComponent } from '../../../../../shared/components/assignments/assignments.component.ts';
import { AssignmentFilterGroup } from '../../../../../shared/enums/assignmentFilterGroup.enum';
import { AssignmentType } from '../../../../../shared/enums/assignmentType.enum';

export const AssignmentsList = {
  bindings: {
    assessments: '<',
    assignments: '<',
    student: '<',
    objectiveSelected: '<',
  },
  templateUrl:
    'components/course-progress/teacher-course-progress/objective-details/assignments-list/assignments-list.html',
  controller: class extends AssignmentsComponent {
    constructor($rootScope, $scope, $timeout, $stateParams, $state, LocalStorageService, FetchData, AssignmentsList) {
      'ngInject';

      super($scope, $stateParams, $state, LocalStorageService);
      this.$rootScope = $rootScope;
      this.$scope = $scope;
      this.$timeout = $timeout;
      this.$stateParams = $stateParams;
      this.FetchData = FetchData;
      this.AssignmentsList = AssignmentsList;
      this.broadcastDelay = 200;
      this.isEditable = false;
      this.statePrefix = 'StudentProgressLearningObjective';
      this.filters = [
        {
          group: AssignmentFilterGroup.TYPE,
          type: AssignmentType.WORK_UNIT,
          label: 'assignmentActionMenuFilterWorkUnit',
        },
        {
          group: AssignmentFilterGroup.TYPE,
          type: AssignmentType.STANDARD_ASSIGNMENT,
          label: 'assignmentActionMenuFilterAssignments',
        },
      ];
    }

    $onInit() {
      super.$onInit();
      const counterGradeAssessment = this.assessments.find((assesment) => {
        return assesment.grade_system.is_counter;
      });
      this.hasCounterGrade = counterGradeAssessment !== undefined;
      this.isEditable = this.$stateParams.course &&
        this.$stateParams.course.teacher_in_class;

      this.assessments = this.assignments.map(assignment =>
        this.assessments.find(assessment => assessment.assignment.id === assignment.assignment_id),
      );
    }

    toggleAssessment(assessment) {
      Object.assign(assessment, { open: !assessment.open });

      if (assessment.open && !assessment.seen) {
        this.FetchData.v3
          .setObjectiveAssessmentSeen(
            this.student.id,
            this.objectiveSelected.id,
            assessment,
          )
          .then(() => {
            Object.assign(assessment, { seen: true });
          });

        this.FetchData.v3
          .patchObjectiveAssignmentAssessmentsSeen(
            this.student.id,
            this.objectiveSelected.id,
            { assignment_id: assessment.assignment.id },
          )
          .then(() => {
            Object.assign(assessment.assignment, { seen: true });
          });
      }
    }

    updateAssessmentData(assessmentData) {
      this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
      this.FetchData.v3
        .updateObjectiveAssessments(
          this.student.id,
          this.objectiveSelected.id,
          assessmentData,
        )
        .then((_assessment) => {
          this.assessments = this.assessments.map((assessment) => {
            if (assessment.id === _assessment.id) {
              // Dont overwrite feedback and private_note, it messes with the ui
              const { feedback, private_note, result, ...rest } = _assessment;
              return { ...assessment, ...rest, grade_system: assessment.grade_system };
            }
            return assessment;
          });
        })
        .finally(() => {
          this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
        });
    }

    getAssignmentForAssessment(assessment) {
      return this.AssignmentsList.getAssignmentForAssessment(this.assignments, assessment);
    }

    onAssignmentChange(assignment) {
      this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
      this.FetchData.v3
        .patchObjectiveAssignmentAssessments(
          this.student.id,
          this.objectiveSelected.id,
          assignment,
        )
        .then((response) => {
          this.assignments = this.assignments.map((_assignment) => {
            if (_assignment.id === assignment.id) {
              // Dont overwrite feedback and private_note, it messes with the ui
              const { feedback, private_note, result, ...rest } = response;
              return Object.assign({}, _assignment, assignment, rest);
            }
            return _assignment;
          });
        })
        .finally(() => this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY));
    }

    isNoteAvailable(assessment) {
      const assignment = this.getAssignmentForAssessment(assessment);

      return (
        (!!assessment && !!assessment.private_note) ||
        (!!assignment && !!assignment.private_note)
      );
    }

    isFeedbackAvailable(assessment) {
      const assignment = this.getAssignmentForAssessment(assessment);

      return (
        (!!assessment &&
          assessment.feedback_published &&
          !!assessment.feedback) ||
        (!!assignment && assignment.feedback_published && !!assignment.feedback)
      );
    }

    findAssignment(assessmentAssignment) {
      return this.assignments
        .find(assignment => assignment.assignment_id === assessmentAssignment.id);
    }

    expandAll() {
      this.changeOpenStateForAll(true);
    }

    collapseAll() {
      this.changeOpenStateForAll(false);
    }

    changeOpenStateForAll(state) {
      this.$timeout(() => {
        this.assessments = this.assessments.map((assessment) => {
          assessment.open = state;

          return assessment;
        });
      }, 0);
    }
  },
  style,
};
