import {Injectable} from '@angular/core';
import {EntityState, EntityStore, runStoreAction, StoreActions, StoreConfig} from '@datorama/akita';
import {Assessment, AssessmentsUI, createAssessments, createAssessmentsUI} from './assessment.model';

export interface AssessmentsState extends EntityState<Assessment> {
  ui: AssessmentsUI;
}

@Injectable({providedIn: 'root'})
@StoreConfig({name: 'assessments'})
export class AssessmentsStore extends EntityStore<AssessmentsState> {
  constructor() {
    super(createAssessments({ui: createAssessmentsUI()}));
    this.setHasCache(false);
  }

  forceRefreshEntities() {
    runStoreAction(this.storeName, StoreActions.UpdateEntities, {
      payload: {
        data: {},
        entityIds: 0,
      },
    });
  }

}

