import { IComponentController, INgModelController } from 'angular';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { SiblingComponent } from '../../../utils/decorators/sibling-component';
import SharedComponentsModule from '../components.module';
import style from './assignmentCourseSelect.scss';
import template from './assignmentCourseSelect.tpl.html';

@Component({
  module: SharedComponentsModule,
  selector: 'assignment-course-select',
  template: template,
  style: style,
})
export class AssignmentCourseSelectComponent implements IComponentController {
  @SiblingComponent({ alias: 'ngModel' }) ngModel: INgModelController;
  @Input() courseDefinitions: any[];

  constructor() {
    'ngInject';

    this.courseDefinitions = [];
  }

  public $onInit(): void {
    if (!this.ngModel && Array.isArray(this.courseDefinitions)) {
      this.ngModel.$viewValue = this.courseDefinitions[0];
    }
  }

  public isSingleView(): boolean {
    return Array.isArray(this.courseDefinitions) ? this.courseDefinitions.length < 2 : true;
  }
}
