import angular from 'angular';
import { IComponentOptions, IModule } from 'angular';

export const Component: (options: IComponentOptions) => any = (options: IComponentOptions): <T extends { new(...args: any[]): {} }>(constructor: T) => any => {

  const getOrCreateComponentModule: () => IModule = (): IModule => {
    const moduleName: string = 'app';
    let module: IModule;

    if (options.module) {
      try {
        module = angular.module(`${options.module}`);
      } catch (err) {
        module = null;
      }
    }

    if (!module) {
      try {
        module = angular.module(`${moduleName}`);
      } catch (err) {
        console.error(`Error on register component ${options.selector} in module ${moduleName}. Module ${moduleName} is not exists.`, err);
        module = null;
      }
    }

    return module;
  };

  const convertSelectorName: (selector: string) => string = (selector: string): string => {
    return selector.replace(
      /-([a-z])/g,
      (word: string): string => word[1].toUpperCase(),
    );
  };

  return <T extends { new(...args: any[]): {} }>(constructor: T): T => {
    const module: IModule = getOrCreateComponentModule();
    const bindings: { [boundProperty: string]: string } = constructor.prototype.$$bindings || {};
    const require: { [controller: string]: string } = constructor.prototype.$$require || {};

    if (!options.controller) {
      options.controller = constructor;
    }

    if (module) {
      const selector: string = convertSelectorName(options.selector);
      module.component(selector, angular.merge({}, options, { bindings, require }));
    }

    try {
      delete constructor.prototype.$$bindings;
      delete constructor.prototype.$$require;
    } catch {
    }

    return constructor;
  };
};

