import { getTranslation } from '../../../../../react_src/i18n';
/**
 * @module Localize/Directive
 * @description
 *   Handle localize mechanism.
 * @example
 *   <p localize>__MSG_saveBtn__</p>
 */

/*@ngInject*/
module.exports = function () {
  'ngInject';

  function compile($templateElement, $templateAttributes) {
    /**
     * Find translation using Chrome getMessage method
     * @memberof module:Localize/Directive
     * @param {String} localizeKey Text before localize
     * @return {String}
     */
    function findTranslation(localizeKey) {
      return localizeKey
        .replace(/__MSG_(\w+)__/g, (match, v1) => (v1 ? getTranslation(v1) : ''));
    }

    if ($templateAttributes) {
      Object.keys($templateAttributes).forEach((attribute) => {
        if (
          typeof $templateAttributes[attribute] === 'string'
          && $templateAttributes[attribute].startsWith('__MSG_')) {
          const translation = findTranslation($templateAttributes[attribute]);
          if (translation) {
            // eslint-disable-next-line no-param-reassign
            $templateAttributes[attribute] = translation;
          }
        }
      });
    }

    if ($templateElement.length !== 0) {
      const content = $templateElement[0].innerHTML.toString();
      // eslint-disable-next-line no-param-reassign
      $templateElement[0].innerHTML = findTranslation(content);
    }
  }

  return {
    priority: 201,
    restrict: 'A',
    compile,
  };
};
