import { Events } from '../../../common/global-events.ts';

export const StudentAssessmentDetails = {
  bindings: {
    assessment: '<',
    assignment: '<',
    objective: '<',
    detailsButtonDisable: '<',
    showTargetGrade: '<',
    showStudentSelfAssessment: '<',
    emptyInfo: '@',
    mixpanelSelfAssessmentEvent: '@',
    onAssessmentClose: '&',
    onNavigateToDetails: '&',
  },
  controller: class {
    constructor($rootScope, FetchData, UserInfo) {
      'ngInject';
      this.$rootScope = $rootScope;
      this.FetchData = FetchData;
      this.UserInfo = UserInfo;
    }

    get isDetailsEmpty() {
      return !(this.assessment.feedback ||
        this.assessment.grade ||
        this.assessment.counter_grades ||
        this.assessment.target_grade ||
        this.assessment.self_assessment);
    }

    updateAssessmentData(assessmentData) {
      this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
      this.FetchData.v3
        .updateObjectiveAssessments(
          this.UserInfo.userId,
          this.objective.id,
          assessmentData,
        )
        .finally(() => {
          this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
        });
    }
  },
  template: `
    <assessment-details
      ng-if="!$ctrl.isDetailsEmpty"
      assessment="$ctrl.assessment"
      assignment="$ctrl.assignment"
      details-button-disable="$ctrl.detailsButtonDisable"
      show-target-grade="$ctrl.showTargetGrade"
      read-only="true"
      show-student-self-assessment="$ctrl.showStudentSelfAssessment"
      mixpanel-self-assessment-event="{{$ctrl.mixpanelSelfAssessmentEvent}}"
      on-navigate-to-details="$ctrl.onNavigateToDetails()"
      on-assessment-close="$ctrl.onAssessmentClose()"
      on-assessment-update="$ctrl.updateAssessmentData(assessment)">
    </assessment-details>
    <assessment-details-empty
      empty-info="{{ $ctrl.emptyInfo }}"
      on-close="$ctrl.onAssessmentClose()"
      ng-if="$ctrl.isDetailsEmpty && $ctrl.assessment.open">
    </assessment-details-empty>
  `,
};
