import React, {useContext, useEffect, useRef, useState} from "react";
import {colors, fonts} from "../theme";
import Card from "../components/card";
import {GlobalContext} from "../utils";
import StatusComponent from "./statusComponent";
import ExtraAdjustmentComponent from "./extraAdjustmentComponent";
import InternalCommentsComponent from "./internalCommentsComponent";
import {getTranslation} from "../i18n";

const fields = `
    name
    extraAdjustments {
      id
      name
    }
    students {
      id
      first_name
      last_name
      email
      EWS {
        courseId
        courseName
        status {
          status
          published
          modified
        }
        internalComments{
          comment
          modified
        }
        extraAdjustments{
          id
          name
          modified
        }
      }
    }
`;

const EwsStudentCard = (props) => {
  const {student, course, multiEdit, isGrid, onChange = false, extraAdjustmentData} = props;
  const context = useContext(GlobalContext);
  const {Api} = context;
  const [classroom, setClassroom] = useState();
  const multiEditSaveObject = useRef({});

  const getEWSData = async () => {
    const response = await Api.getClassroom(fields);
    setClassroom(response?.data?.classroom);
  };

  useEffect(() => {
    getEWSData();
  }, [student.id, multiEdit]);

  useEffect(() => {
    getEWSData();
  }, []);

  const ews = classroom?.students.find(s => s.id === student.id)?.EWS.find((e) => e.courseId === course.id);
  const extraAdjustmentsAvailable = classroom?.extraAdjustments;
  return (
    <div>
      <span
        style={{
          ...fonts.LARGE,
          color: colors.brandColorPink,
          marginLeft: "16px",
        }}
      >
        {getTranslation("earlyWarningSystem")}
      </span>
      <Card style={{marginTop: "16px", marginBottom: "16px"}}>
        <StatusComponent
          status={ews?.status}
          studentId={student.id}
          courseId={course.id}
          multiEdit={multiEdit}
          onChange={(ewsStatus) => {
            multiEditSaveObject.current.ewsStatus = ewsStatus;
            if (onChange) {
              onChange(multiEditSaveObject.current);
            }
          }}
        />
        <ExtraAdjustmentComponent
          isGrid={isGrid}
          multiEdit={multiEdit}
          studentId={student.id}
          courseId={course.id}
          extraAdjustments={ews?.extraAdjustments}
          extraAdjustmentsAvailable={extraAdjustmentsAvailable}
          extraAdjustmentData={extraAdjustmentData}
          onChange={(adjustments) => {
            multiEditSaveObject.current.extraAdjustments = adjustments;
            if (onChange) {
              onChange(multiEditSaveObject.current);
            }
          }}
        />
        {!multiEdit && (
          <InternalCommentsComponent comments={ews?.internalComments}/>
        )}
      </Card>
    </div>
  );
};

export default EwsStudentCard;
