import { IPromise, IQService } from 'angular';
import { StateDeclaration, Transition, TransitionService, StateParams } from '@uirouter/core';
import { StateProvider } from '@uirouter/angularjs';
import { FetchData } from '../../utils/services/fetchData.service';

export const StudentAssessAssignmentRouting: Function = ($stateProvider: StateProvider, $transitionsProvider: TransitionService): void => {
  'ngInject';

  $transitionsProvider.onBefore({ to: 'studentAssessed.**' }, (transition: Transition): any => {
    const transitionTo: StateDeclaration = transition.to();
    (transitionTo as any).themeClass = 'sb-theme-green';
    (transitionTo as any).role = 'student';

    return true;
  });

  $stateProvider
    .state('studentAssessed', {
      component: 'studentAssessAssignment',
      params: {
        isModal: null,
        assignment: null,
      },
      resolve: {
        assignment: ($q: IQService, $stateParams: StateParams, Path: any, FetchData: FetchData, MainMenu: any) => {
          'ngInject';

          const assignmentPromise: IPromise<any> = $stateParams.assignment ?
            $q.when($stateParams.assignment) :
            FetchData.v2.getAssignmentByAlternateLink(Path.getAssignmentId());

          return assignmentPromise
            .then((assignment: any): IPromise<any> => {
              MainMenu.saveAssignmentDetails(assignment);
              return FetchData.getStudentAssignment(assignment.id);
            })
            .catch((): IPromise<never> => {
                MainMenu.saveAssignmentDetails(null);
                return $q.reject();
              },
            );
        },
      },
    });
};
