import { AssessmentStatusPreview } from './assessmentStatusPreview.service';

export class AssessmentStatusPreviewComponent {
  static bindings = {
    assessment: '<',
    assignment: '<',
    hasCounterGrade: '<',
    showFeedback: '<',
    showPrivateNote: '<',
    showStudentSelfAssessment: '<',
    showTargetGrade: '<',
    showVisibility: '<',
  };
  static controller = AssessmentStatusPreviewComponent;
  static templateUrl =
    'components/course-progress/common/components/assessmentStatusPreview/assessmentStatusPreview.html';

  public isTeacher: boolean;
  public gradeSystem?: any;
  public assessment?: any;
  public assignment?: any;
  public hasCounterGrade?: boolean;
  public showFeedback?: boolean;
  public showPrivateNote?: boolean;
  public showStudentSelfAssessment?: boolean;
  public showTargetGrade?: boolean;
  public showVisibility?: boolean;

  constructor(private AssessmentStatusPreview: AssessmentStatusPreview) {
    'ngInject';

    this.isTeacher = false;
    this.gradeSystem = null;
    this.assessment = {};
    this.assignment = {};
  }

  $onInit(): void {
    this.isTeacher = this.AssessmentStatusPreview.isTeacher();
    this.gradeSystem = this.AssessmentStatusPreview
      .getGradeSystemById(this.assessment.grade_system.id);

    this.findLastUpdateDate();
  }

  $onChanges(changes): void {
    if (changes.assignment && !changes.assignment.isFirstChange()) {
      this.findLastUpdateDate();
    }
  }

  findLastUpdateDate(): void {
    const lastUpdate: string = this.assessment ? this.assessment.last_update : null;

    this.assessment.last_update = this.AssessmentStatusPreview
      .getLastUpdateDate(this.assignment, lastUpdate);
  }

  get showFeedbackVisibility(): boolean {
    return this.showFeedback && this.showVisibility;
  }

  get hasFeedback(): boolean {
    return (
      (this.assessment && this.assessment.feedback) ||
      (this.assignment && this.assignment.feedback)
    );
  }

  get isFeedbackVisible(): boolean {
    return (
      this.isTeacher &&
      this.hasFeedback &&
      (
        (this.assessment && this.assessment.feedback_published) ||
        (this.assignment && this.assignment.feedback_published)
      )
    );
  }

  get hasTeacherNote(): boolean {
    return (
      this.isTeacher &&
      ((this.assessment && this.assessment.private_note) ||
        (this.assignment && this.assignment.private_note))
    );
  }

  get hasStudentsSelfAssessment(): boolean {
    return this.assessment && this.assessment.self_assessment;
  }

  get hasResult(): boolean {
    return this.assignment && this.assignment.result;
  }

  get isResultVisible(): boolean {
    return this.isTeacher &&
      this.hasResult &&
      this.assignment &&
      this.assignment.result_published;
  }

  get hasPrivateNote(): boolean {
    return this.assessment && this.assessment.private_note;
  }
}
