var noUiSlider = require('../../../vendors/nouislider.js');

/*@ngInject*/
module.exports = function ($timeout) {

  function link(scope, element, attr) {
    var disabledSlider;
    var squareSlider;
    var batchEditingSlider;
    var knowledgeRequirementsSlider;

    var sliderStartValue;
    var sliderRangeMin;
    var sliderRangeMax;
    var sliderValues = [];

    var containerNode;
    var handleNode;
    var valueLabelNode;

    var config;
    var labels;

    var ignoreWatch = false;
    var initRun = true;
    var initialized = false;

    var startValue;

    // Check slider type
    scope.checkSliderType = function () {
      if (attr.disabled === 'true') {
        disabledSlider = true;
      }

      if (attr.square === 'true') {
        squareSlider = true;
      }

      if (attr.batchediting === 'true') {
        batchEditingSlider = true;
      }

      if (attr.knowledgerequirements === 'true') {
        knowledgeRequirementsSlider = true;
      }
    };

    // Create slider wrapper
    scope.createWrapper = function () {
      element.append('<div class="container"></div>');
      containerNode = element.find('.container');
    };

    // Get slider start value
    scope.getSliderStartValue = function () {
      if (scope.ngModel === '') {
        // If grade isn't set yet, set sliderStartValue as -1 and leave function
        sliderStartValue = -1;
        startValue = sliderStartValue;
        return;
      } else if (scope.ngModel === 'not_changed') {
        // If grade isn't changed, set sliderStartValue as -2 and leave function
        sliderStartValue = -2;
        startValue = sliderStartValue;
        return;
      }

      // If grade is set, find matching value in config and set it as sliderStartValue
      config.forEach(function (configElem) {
        if (scope.ngModel !== configElem.name) {
          return;
        }

        sliderStartValue = configElem.order;
        startValue = sliderStartValue;
      });
    };

    // Get slider min and max range
    scope.getSliderRange = function () {
      if (batchEditingSlider) {
        // For batch editing slider set sliderRangeMin as -2
        sliderRangeMin = -2;
      } else {
        // For normal slider set sliderRangeMin as -1
        sliderRangeMin = -1;
      }

      sliderRangeMax = config.length - 1;
    };

    // Get slider array of values
    scope.getSliderValues = function () {
      // For batch editing slider push -2 value to the sliderValues
      if (batchEditingSlider) {
        sliderValues.push(parseInt(-2));
      }

      sliderValues.push(parseInt(-1));

      config.forEach(function (configElem) {
        sliderValues.push(parseInt(configElem.order));
      });

      sliderValues.sort();
    };

    // Create slider
    scope.initSlider = function () {
      noUiSlider.create(containerNode[0], {
        start: sliderStartValue,
        connect: 'lower',
        range: {
          'min': sliderRangeMin,
          'max': sliderRangeMax
        },
        pips: {
          mode: 'values',
          values: sliderValues
        }
      });
    };

    // Create value label
    scope.createValueLabel = function () {
      var html = [
        '<div class="value-label">',
        '<span></span>',
        '</div>',
        '<div class="arrow-indicator">',
        '<span></span>',
        '</div>',
      ].join('');

      handleNode = element.find('.noUi-handle');
      handleNode.append(html);
      valueLabelNode = element.find('.value-label');
    };

    // Replace slider labels
    scope.replaceSliderLabels = function () {
      labels = element.find('.noUi-value-large').toArray();
      labels.forEach(function (labelElem) {
        var labelValue = parseInt($(labelElem).text());

        // Set '' as label for not changed value
        if (labelValue === -2) {
          labelElem.innerText = '';
        }

        // Set '-' as label for not already set value
        if (labelValue === -1) {
          labelElem.innerText = '-';
        }
        config.forEach(function (configElem) {
          if (labelValue !== configElem.order) {
            return;
          }

          labelElem.innerText = configElem.label;
        });
      });
    };

    // Function that allows to make slider disable
    scope.disableSlider = function () {
      if (!disabledSlider) {
        return;
      }

      containerNode[0].setAttribute('disabled', true);
    };

    // Add keyboard support for arrow left and arrow right.
    // Only if slider is not disable
    scope.addKeyboardSupport = function () {
      if (disabledSlider) {
        return;
      }

      element[0].setAttribute('tabindex', 0);

      element[0].addEventListener('keydown', function (e) {
        var value = Number(containerNode[0].noUiSlider.get());

        switch (e.which) {
          case 37: // Arrow left key
            containerNode[0].noUiSlider.set(value - 1);
            $timeout(scope.ngChange, 0);
            break;
          case 39: // Arrow right key
            containerNode[0].noUiSlider.set(value + 1);
            $timeout(scope.ngChange, 0);
            break;
        }
      });
    };

    scope.setGradeStyles = function (label, background, border, color) {
      var valueTextNode = valueLabelNode[0].querySelectorAll('span');

      valueLabelNode[0].style.borderColor = border;
      valueTextNode[0].innerText = label;
      containerNode[0].style.backgroundColor = border;

      if (squareSlider) {
        valueLabelNode[0].style.backgroundColor = '#fff';
        valueTextNode[0].style.color = border;
      } else {
        valueLabelNode[0].style.backgroundColor = background;
        valueTextNode[0].style.color = color;
      }
    };

    scope.setSliderWidth = function () {
      // Do nothing if knowledge requirements slider - witdh is always 100% and it is set in CSS
      if (knowledgeRequirementsSlider || scope.autoWidth) {
        return;
      }

      var defaultDistanceBetweenGrades = 30;
      var realDistanceBetweenGrades = 30;

      var sliderLength = sliderValues.length - 1;
      var sliderWidth = sliderLength * defaultDistanceBetweenGrades;

      // Check if slider width isn't bigger than maximum slider width
      // For knowledge requirements slider width is always equal to 100%
      // Maximum for slider in batch editing is equal to 230px
      // Maximum for slider on the list is equal to 200px
      if ((batchEditingSlider && sliderWidth <= 230) || (!batchEditingSlider && sliderWidth <= 200)) {
        element[0].style.width = sliderWidth + 'px';
      } else {
        var width = (!batchEditingSlider) ? 200 : 230;
        element[0].style.width = width + 'px';
        realDistanceBetweenGrades = 200 / sliderLength;
      }

      // Indent slider in batch editing mode
      if (batchEditingSlider) {
        element[0].style.marginLeft = '-' + realDistanceBetweenGrades + 'px';
      }
    };

    // Watch ngModel
    scope.$watch('ngModel', function () {
      var value;

      if (initRun || ignoreWatch) {
        initRun = false;
        return;
      }

      if (scope.ngModel === 'not_changed') {
        value = -2;
      } else if (scope.ngModel === '') {
        value = -1;
      }

      config.forEach(function (configElem) {
        if (scope.ngModel !== configElem.name) {
          return;
        }

        value = configElem.order;
      });

      containerNode[0].noUiSlider.set(value);
    });

    // Watch gradeSystem
    scope.$watch('gradeSystem', function (newVal, oldVal) {
      if (!initialized && newVal && newVal.grades) {

        //Config
        config = newVal.grades;

        scope.checkSliderType();
        scope.getSliderStartValue();
        scope.getSliderRange();
        scope.getSliderValues();
        scope.createWrapper();
        scope.initSlider();
        scope.setSliderWidth();
        scope.createValueLabel();
        scope.replaceSliderLabels();
        scope.addKeyboardSupport();
        scope.disableSlider();

        // Look on slide event
        // On slide focus slider (user click on scale or slide slider manualy)
        containerNode[0].noUiSlider.on('slide', function (value) {
          if (element[0] !== document.activeElement) {
            element[0].focus();
          }
        });

        // Look on change event
        // On change find closest integer number and set it as slider value
        // and focus slider (user click on handle)
        containerNode[0].noUiSlider.on('change', function (value) {
          var roundValue = parseInt(Math.round(value));

          containerNode[0].noUiSlider.set(roundValue);

          if (element[0] !== document.activeElement) {
            element[0].focus();
          }

          if (startValue !== roundValue) {
            startValue = roundValue;
          } else {
            return;
          }

          $timeout(scope.ngChange, 0);
        });

        // Look on update event
        // On update change text in label circle and colors for slider
        containerNode[0].noUiSlider.on('update', function (value) {
          var roundValue = parseInt(Math.round(value));

          if (roundValue === -2) {
            scope.setGradeStyles('', '#ffffff', '#f44336', '#999999');
          } else if (roundValue === -1) {
            scope.setGradeStyles('-', '#ffffff', '#999999', '#999999');
          }

          const currentConfig = config.find(configElem => roundValue === configElem.order);

          (scope.ngUpdate || (() => {}))({ event: currentConfig || null });

          if (currentConfig) {
            scope.setGradeStyles(currentConfig.circle_label, currentConfig.color, currentConfig.color, '#ffffff');
          }
        });

        // Look on set event
        // On set update scope
        containerNode[0].noUiSlider.on('set', function (value) {
          ignoreWatch = true;

          var roundValue = parseInt(Math.round(value));

          scope.$evalAsync(function () {
            if (roundValue === -2) {
              scope.ngModel = 'not_changed';
              if (typeof scope.gradeId !== 'undefined') {
                scope.gradeId = null;
              }
            } else if (roundValue === -1) {
              scope.ngModel = '';
              if (typeof scope.gradeId !== 'undefined') {
                scope.gradeId = null;
              }
            }

            config.forEach(function (configElem) {
              if (roundValue !== configElem.order) {
                return;
              }

              scope.ngModel = configElem.name;

              if (scope.gradeId !== undefined) {
                scope.gradeId = configElem.id;
              }
            });
          });

          ignoreWatch = false;
        });

        initialized = true;
      }
    });
  }

  return {
    restrict: 'E',
    scope: {
      ngModel: '=',
      gradeId: '=',
      ngChange: '&',
      ngUpdate: '&?',
      gradeSystem: '=',
      autoWidth: '=',
    },
    link,
  };
};
