import {
  FormControl,
  InputBase,
  InputLabel,
  ListItemText,
  MenuItem,
  Select,
  withStyles,
} from '@material-ui/core';
import React from 'react';
import { colors, fonts } from '../theme';

const BootstrapInput = withStyles((theme) => ({
  input: {
    ...fonts.MEDIUM,
    fontSize: '18px',
    height: '35px',
    alignItems: 'flex-end',
    display: 'flex',
    borderBottom: `1px solid ${colors.borderColor}`,
    paddingBottom: '0px',
    '&:focus': {
      color: colors.green,
    },
  },
}))(InputBase);

const OptionsDropdown = (props) => {
  const {
    options, helpText, value, handleChange, style = {},
  } = props;
  return (
    <FormControl style={{ flex: 1, display: 'flex', ...style }}>
      <InputLabel id={helpText}>
        <span
          style={{
            ...fonts.SMALL,
            color: value === '' ? colors.red : colors.green,
          }}
        >
          {helpText}
        </span>
      </InputLabel>
      <Select
        labelId={helpText}
        style={{ width: '100%' }}
        value={value}
        onChange={handleChange}
        renderValue={(id) => (
          <span
            style={{
              ...fonts.MEDIUM,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
            }}
          >
            {options.find((o) => o.id === id)?.name}
          </span>
        )}
        input={<BootstrapInput />}
      >
        {options.map((option) => (
          <MenuItem key={option.id} value={option.id}>
            <ListItemText
              primary={<span style={{ ...fonts.MEDIUM }}>{option.name}</span>}
              secondary={<span style={fonts.TINY}>{option.description}</span>}
            />
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default OptionsDropdown;
