/**
 * @module LastModifed/Filter
 * @description
 *   Convert data to proper format.
 */

export const LastModified = () => {
  'ngInject';

  return (lastModifedDate) => {
    let outputDate;

    if (!lastModifedDate) {
      // Empty string if date is equal to null or undefined
      outputDate = '';
    } else if (moment().isSame(lastModifedDate, 'day')) {
      // Date equals today's date
      outputDate = moment(lastModifedDate).format('k:mm');
    } else if (moment().isSame(lastModifedDate, 'year')) {
      // Date equals the same year
      outputDate = moment(lastModifedDate).format('D MMM');
    } else {
      // Previous year
      outputDate = moment(lastModifedDate).format('D MMM YYYY');
    }

    return outputDate.toLocaleUpperCase();
  };
};
