import { IComponentController, INgModelController, IRootElementService } from 'angular';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { SiblingComponent } from '../../../utils/decorators/sibling-component';
import AssignmentsSharedModule from '../assignmentsShared.module';
import template from './studentAssignmentTextarea.tpl.html';
import style from './studentAssignmentTextarea.scss';

@Component({
  module: AssignmentsSharedModule,
  selector: 'student-assignment-textarea',
  template: template,
  style: style,
})
export class StudentAssignmentTextareaComponent implements IComponentController {
  @SiblingComponent() ngModel: INgModelController;
  @Input({ optional: true }) public isPublished: boolean | undefined;
  @Input() public label: string;
  @Input({ optional: true }) public readonly: boolean | undefined;

  constructor(private $element: IRootElementService) {
    'ngInject';

    this.label = '';
  }

  public $onInit(): void {
    if (this.readonly === undefined) {
      this.readonly = !!this.$element.attr('readonly');
    }

    this.isPublished = this.isPublished === undefined ? true : this.isPublished;
  }
}
