/*@ngInject*/
module.exports = function($timeout, Debounce, NavigationHistory) {
  function link(scope, element) {
    let currentParams = NavigationHistory.getCurrentParams();

    if(currentParams.scrollPosition) {
      $timeout(() => {
        element.scrollTop(currentParams.scrollPosition);
      });
    }

    element.on('scroll', Debounce((e) => {
      NavigationHistory.updateParams({
        scrollPosition: element.scrollTop()
      });
    }, 100));
  }

  return {
    restrict: 'A',
    link
  };
};
