export class NgVisibleDirective {
  static className = 'ng-hidden';
  static create() {
    'ngInject';

    return new NgVisibleDirective();
  }

  constructor() {
    'ngInject';
    this.multiElement = true;
    this.restrict = 'A';
  }

  link($scope, element, attrs) {
    const unsubscribe$watch = $scope.$watch(attrs.ngVisible, value =>
      element[value ? 'removeClass' : 'addClass'](NgVisibleDirective.className)
    );

    $scope.$on('$destroy', () => {
      unsubscribe$watch();
    });
  }
}
