import angular, { IModule } from 'angular';
import uiRouter from '@uirouter/angularjs';
import { UserSetupDialog } from './commons/dialogs/UserSetupDialog.service';
import { userSetupComponent } from './components/userSetup/userSetup.component';
import { userSetupAgreementComponent } from './components/userSetupAgreement/userSetupAgreement.component';
import { userSetupAgreementMissingComponent } from './components/userSetupAgreementMissing/userSetupAgreementMissing.component';
import { userSetupCheckComponent } from './components/userSetupCheck/userSetupCheck.component';
import { UserSetupCheck } from './components/userSetupCheck/userSetupCheck.service';
import { userSetupCheckInvalidComponent } from './components/userSetupCheck/userSetupCheckInvalid/userSetupCheckInvalid.component';
import { userSetupCheckProgressComponent } from './components/userSetupCheck/userSetupCheckProgress/userSetupCheckProgress.component';
import { userSetupCheckValidComponent } from './components/userSetupCheck/userSetupCheckValid/userSetupCheckValid.component';
import { userSetupCompleteComponent } from './components/userSetupComplete/userSetupComplete.component';
import { userSetupSettingsComponent } from './components/userSetupSettings/userSetupSettgins.component';
import { UserSetupSettings } from './components/userSetupSettings/usetSetupSettings.service';
import { userSetupSlidesComponent } from './components/userSetupSlides/userSetupSlides.component';
import { UserSetupRouting } from './userSetup.routing';


export const UserSetupModule: IModule = angular.module('app.userSetup', [uiRouter])
  .component('userSetup', userSetupComponent)
  .component('userSetupAgreement', userSetupAgreementComponent)
  .component('userSetupAgreementMissing', userSetupAgreementMissingComponent)
  .component('userSetupCheck', userSetupCheckComponent)
  .component('userSetupCheckInvalid', userSetupCheckInvalidComponent)
  .component('userSetupCheckProgress', userSetupCheckProgressComponent)
  .component('userSetupCheckValid', userSetupCheckValidComponent)
  .component('userSetupComplete', userSetupCompleteComponent)
  .component('userSetupSettings', userSetupSettingsComponent)
  .component('userSetupSlides', userSetupSlidesComponent)
  .service('UserSetupCheck', UserSetupCheck)
  .service('UserSetupDialog', UserSetupDialog)
  .service('UserSetupSettings', UserSetupSettings)
  .config(UserSetupRouting);

export default UserSetupModule.name;

export * from './components/userSetupSchoolSelect/userSetupSchoolSelect.component';
export * from './components/userSetupRegisterSchool/userSetupRegisterSchool.component';
