export class GradeDescription {

  static mapAssessmentToGrades(assessment) {
    return assessment.grade_system.grades
      .reduce((gradesMap, grade) => gradesMap.set(grade.id, grade), new Map());
  }

  static filterNotFirstTimeChanges(changes) {
    let filtered = {};

    for (const [key, value] of Object.entries(changes)) {
      if (!value.isFirstChange()) {
        filtered = Object.assign(filtered, { [key]: value });
      }
    }

    return filtered;
  }

  static animateContent($element, animationTime = 200) {
    window.setTimeout(() => {
      const $mdContent = $element.find('md-content');

      if ($mdContent.length) {
        window.requestAnimationFrame(() => {
          const prevHeight = $element.data('height') || 0;
          $mdContent.css('height', 'auto');
          const height = $mdContent.height();

          $mdContent
            .animate({ height: prevHeight }, 0)
            .animate({ height }, animationTime);

          $element.data('height', height);
        });
      } else {
        $element.data('height', 0);
      }
    });
  }
}
