import { UserSetupValidationState } from '../../../utils/enums/userSetupValidationState.enum';

export class UserSetupCheckController {
  constructor($state) {
    'ngInject';

    this.$state = $state;
    this.validationStatus = UserSetupValidationState.IN_PROGRESS;
  }

  onValidation(isValid) {
    this.validationStatus = isValid ?
      UserSetupValidationState.VALID : UserSetupValidationState.INVALID;
  }
}
