import get from 'lodash.get';
import { Events } from '../../../../common/global-events.ts';
import { UnsubscribableComponent } from '../../../../shared/components/abstracts/unsubscribable.component';

export class StudentsListCtrl extends UnsubscribableComponent {
  constructor($rootScope, $timeout, $state, $stateParams, StudentCoursesDetails) {
    'ngInject';

    super();
    this.$rootScope = $rootScope;
    this.$timeout = $timeout;
    this.$state = $state;
    this.$stateParams = $stateParams;
    this.StudentCoursesDetails = StudentCoursesDetails;
  }

  $onInit() {
    this.lastSelectedStudentId = null;
    if (this.selectedStudent) {
      this.lastSelectedStudentId = this.selectedStudent.id;
    }
    this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);
    this.$$unsubscribes.push(
      this.$rootScope.$watch(() => get(this, '$stateParams.student', null), (student) => {
        if (student) {
          this.selectedStudent = get(this, '$stateParams.student', null);
          this.lastSelectedStudentId = get(this, 'selectedStudent.id', null);
        }
      }),
    );
  }

  toggleStudentCourseProgress(student) {
    const stateName = this.$state.current.name;

    if (['teacher-course-progress', 'teacher-course-progress.details.courses-overview'].includes(stateName)) {
      this.goToOverview(student);
    } else {
      this.goToOtherStudent(student);
    }
  }

  goToOverview(student) {
    this.$timeout(() => {
      this.$state.go('teacher-course-progress.details.courses-overview', { student });
      this.lastSelectedStudentId = student.id;
    }, 400);
  }

  goToOtherStudent(student) {
    const stateName = this.$state.current.name;
    let stateParams = Object.assign({}, this.$stateParams, { student });
    stateParams = angular.copy(stateParams);

    this.setStudentForBreadcrumb(stateParams.breadcrumbs, student);

    this.$timeout(() => {
      this.$state.go(stateName, stateParams)
        .then(() => this.StudentCoursesDetails.studentCoursesDefinitions$.next(null))
        .catch(() => {
          this.goToOverview(student);
        });
      this.lastSelectedStudentId = student.id;
    }, 400);
  }

  setStudentForBreadcrumb(breadcrumbs, student) {
    breadcrumbs.forEach((breadcrumb) => {
      if (get(breadcrumb, 'action.params.student', false)) {
        Object.assign(breadcrumb.action.params.student, student);
      }

      if (breadcrumb.label === this.$stateParams.student.student_name) {
        Object.assign(breadcrumb, { label: student.student_name });
      }

      if (get(breadcrumb, 'action.params.breadcrumbs', false)) {
        this.setStudentForBreadcrumb(breadcrumb.action.params.breadcrumbs, student);
      }
    });
  }
}
