import { Events } from '../../../common/global-events.ts';

export class UserSetupAgreementController {
  constructor($rootScope, $state, $sce, FetchData, UserInfo) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$state = $state;
    this.$sce = $sce;
    this.FetchData = FetchData;
    this.UserInfo = UserInfo;
    this.agreementAccepted = false;
    this.isUpdateInProgress = false;
  }

  $onInit() {
    this.agreement.content = this.$sce.trustAsHtml(this.agreement.content);
  }

  goToUserSettings() {
    this.isUpdateInProgress = true;

    this.FetchData.updateUserAgreementSeen()
      .then(() => {
        this.UserInfo.agreementSeen = true;

        if (this.UserInfo.isTeacher() && !this.UserInfo.hasSetup) {
          this.$state.go('userSetup');
        } else {
          this.$rootScope.$broadcast(Events.USER_SETUP_COMPLETED);
          this.$state.go('loading');
        }
      })
      .finally(() => {
        this.isUpdateInProgress = false;
      });
  }
}
