import React, { useEffect, useState } from 'react';
import { colors, fonts } from '../theme';

const SyncToast = (props) => {
  const { text, setText } = props;
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (text) {
      setOpen(true);
      setTimeout(() => {
        setOpen(false);
        setText(null);
      }, 2000);
    }
  }, [text]);

  if (!open) {
    return null;
  }
  return (
    <div
      style={{
        position: 'absolute',
        bottom: '20px',
        right: '20px',
        backgroundColor: colors.darkGrey,
        padding: '5px 10px 5px 10px',
        borderRadius: '5px',
        zIndex: '1000',
      }}
    >
      <span style={{ ...fonts.MEDIUM, color: colors.white }}>{text}</span>
    </div>
  );
};

export default SyncToast;
