import { IPromise } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { FetchData } from '../../../utils/services/fetchData.service';
import { ICountry } from '../../models/country.model';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'CountryService',
})
export class CountryService {
  constructor(private FetchData: FetchData) {
    'ngInject';
  }

  public getAll(): IPromise<ICountry[]> {
    return this.FetchData.sendAPIRequest('/v3/schools/countries/');
  }

  public findCountryByCode(countries: ICountry[], countryCode: string): ICountry {
    return countries
      .find((country: ICountry): boolean => country.country_code === countryCode);
  }
}
