/**
 * This is not a classic solution in the angular apps, but the component is not the
 * the best solution here since the common part has only a few methods, and all
 * template layout is different. In this case I use this class as a base class
 * both for edit course modal and search course modal.
 */
export class SelectEducationalProperties {
  constructor(FetchData) {
    this.FetchData = FetchData;
  }

  /**
   *
   */
  changeEducationalSystem() {
    this.educationalStage = undefined;
    this.resetScales();
    this.updateEducationalStages(this.educationalSystem);
  }

  /**
   *
   */
  changeEducationalStage() {
    this.resetScales();
    this.updateScales(this.educationalStage);
  }

  resetScales() {
    this.scale = undefined;
    this.scales = [];
  }

  /**
   * Updates current educational stages based on the educational system
   * @param educationalSystem
   */
  updateEducationalStages(educationalSystem) {
    // reset educational stages
    this.educationalStages = [];
    return this.FetchData
      .v3
      .getEducationalStages({
        educational_system: educationalSystem.id,
      })
      .then((educationalStages) => {
        this.educationalStages = educationalStages;
      });
  }

  /**
   * Updates scales for modal window, based on actual educational stage
   * @param educationalStage
   */
  updateScales(educationalStage) {
    this.scales = [];
    //  fetch only if stage is not undefined ( case for all stages )
    if (!educationalStage) {
      return;
    }
    // eslint-disable-next-line consistent-return
    return this.FetchData
      .v3
      .getScales({
        educational_stage: educationalStage.id,
      })
      .then((scales) => {
        this.scales = scales;
      });
  }

  updateEducationalStagesAndScales() {
    return this.updateEducationalStages(this.educationalSystem)
      .then(() => {
        this.setEducationalStage(this.educationalStageId);
      })
      .then(() => this.updateScales(this.educationalStage));
  }

  setEducationalStage(educationalStageId) {
    this.educationalStage = this.educationalStages.find(
      educationalStage => educationalStage.id === educationalStageId,
    );
  }
}
