import { Type } from '@angular/core';

export interface IProviderUpgrade {
  provide: Type<any>;
  useFactory: (...args: any[]) => any;
  deps: string[];
}

export class UpgradedProviders {
  static providers: IProviderUpgrade[] = [];
}

export abstract class SimpleProvider {
  [porp: string]: any;
}

export class FetchData extends SimpleProvider {}

UpgradedProviders.providers.push({
  provide: FetchData,
  useFactory: ($injector: any): any => $injector.get('FetchData'),
  deps: ['$injector'],
});

