import { IAttributes, IDirective, IParseService, IScope } from 'angular';
import { Directive } from '../../../utils/decorators/directive.decorator';
import SharedDirectivesModule from '../directives.module';

@Directive({
  module: SharedDirectivesModule,
  selector: 'textarea',
  restrict: 'AE',
})
export class TextareaDirective implements IDirective {
  constructor(private $parse: IParseService) {
    'ngInject';
  }

  public link(scope: IScope, instanceElement: JQLite, instanceAttributes: IAttributes): void {
    const element: HTMLTextAreaElement = instanceElement[0] as HTMLTextAreaElement;
    const parent: HTMLElement = this.findScrollableParent.call(this, element.parentElement);

    if (parent) {
      instanceAttributes.$observe('mdDetectHidden', (): void => {
        const mdDetectHidden: boolean = this.$parse(instanceAttributes.mdDetectHidden)(scope);
        let eventListener: EventListener = null;

        if (mdDetectHidden) {
          eventListener = this.applyEvent.call(this, element, parent);
        } else {
          if (eventListener) {
            this.removeEvent.call(this, parent, eventListener);
            eventListener = null;
          }

          delete element.dataset.wasVisible;
        }
      });
    }
  }

  private applyEvent(element: HTMLTextAreaElement, parent: HTMLElement): EventListener {
    const isVisible: boolean = this.isVisible(element, parent);

    if (isVisible) {
      element.dataset.wasVisible = 'true';
    }

    const onScroll: EventListener = () => {
      const isVisible: boolean = this.isVisible(element, parent);
      const wasVisible: boolean = 'wasVisible' in element.dataset;

      if (isVisible !== wasVisible) {
        if (wasVisible) {
          delete element.dataset.wasVisible;
        } else {
          window.dispatchEvent(new Event('resize'));
          element.dataset.wasVisible = 'true';
        }
      }
    };

    parent.addEventListener('scroll', onScroll);

    return onScroll;
  }

  private removeEvent(parent: HTMLElement, eventListener: EventListener): void {
    parent.removeEventListener('scroll', eventListener);
  }

  private isVisible(element: HTMLTextAreaElement, container: HTMLElement): boolean {
    const rect: ClientRect = element.getBoundingClientRect();

    return rect.top >= 0 && rect.left >= 0 && rect.bottom <= container.clientHeight && rect.right <= container.clientWidth;
  }

  private findScrollableParent(node: HTMLElement): HTMLElement {
    if (node === null) {
      return null;
    }

    if (node.scrollHeight > node.clientHeight) {
      return node;
    } else {
      return this.findScrollableParent(node.parentElement);
    }
  }
}
