import React, {useContext, useEffect, useRef, useState} from "react";
import {colors, fonts} from "../theme";
import {StudentItem} from "../components/studentItem";
import AssignmentObjectivesList from "./assignmentObjectivesList";
import AssignmentStudentListHeader from "./assignmentStudentListHeader";
import {useWindowDimensions} from "../hooks";
import {
  getStudentListFilterOptions,
  GlobalContext, NOT_SUBMITTED, NOT_SUBMITTED_LATE, setStudentListFilterOptions,
  sortAssessmentsByStudentName, SUBMITTED, SUBMITTED_LATE,
} from "../utils";
import {getTranslation} from "../i18n";
import BatchAssessmentDialog from "../components/batchAssessmentDialog";
import FullscreenProgress from "../components/fullscreenProgress";
import EditAssessmentCriteria from "./editAssignment/editAssessmentCriteria";
import {classroomFields} from "./assignmentsList";
import {ReactComponent as FilterIcon} from "../images/filter.svg";
import {ReactComponent as ExpandIcon} from "../images/expand.svg";
import {ReactComponent as CollapseIcon} from "../images/collapse_all.svg";
import MoreMenu from "../components/moreMenu";
import SortStudentComponent from "../components/sortStudentComponent";
import Tooltip from "../components/tooltip";

const fields = `
    title
    connected
    state
    use_submitted_status
    course_id
    assessments{
      id
      student_id
      feedback
      feedback_published
      private_note
      grade_published
      grade_id
      grade_name
      grade_color
      result
      result_published
      status_new
      status_new_last_edit
      feedback_last_edit
      grade_last_edit
      last_edit_for_student
      self_assessment
      self_assessment_last_edit
      submitted_status
    }
    objectiveAssessments{
      id
      student_id
      objectiveName
      objective_id
      feedback
      feedback_published
      feedback_last_edit
      private_note
      private_note_last_edit
      grade_last_edit
      grade_id
      grade_published
      counter_grade_0
      counter_grade_1
      counter_grade_2
      counter_grade_3
      counter_grade_4
      counter_grade_5
      counter_grade_6
      counter_grade_7
      counter_grade_8
      counter_grade_9
      max_counter_grade_0
      max_counter_grade_1
      max_counter_grade_2
      max_counter_grade_3
      max_counter_grade_4
      max_counter_grade_5
      max_counter_grade_6
      max_counter_grade_7
      max_counter_grade_8
      max_counter_grade_9
    }
    gradeSystem {
      id
      name
      is_counter
      is_main
      is_final
      grades{
        id
        name
        is_passed
        color
        label
      }
    }`;

const filterOptions = [
  {title: getTranslation("selectContentInList"), type: "title"},
  {
    title: getTranslation(
      "teacherAssignmentsAssessmentStudentVisibilityDialogOptionOverallFeedback"
    ),
    name: "overallFeedback",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("teacherSelfAssessmentLabel"),
    name: "selfAssessment",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("overallResult"),
    name: "result",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("overallAssessment"),
    name: "overallGrade",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("overallPrivateNoteInput"),
    name: "overallNote",
    selected: false,
    selectable: true,
  },
  {type: "separator"},
  {
    title: getTranslation("assessmentMode"),
    name: "grade",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("objectivesFeedbackMode"),
    name: "feedback",
    selected: false,
    selectable: true,
  },
  {
    title: getTranslation("objectivesPrivateNotesMode"),
    name: "note",
    selected: false,
    selectable: true,
  },
];

const AssignmentStudentList = (props) => {
  const context = useContext(GlobalContext);
  const {
    Api,
    sortingMethod,
    setVisibilityDialogData,
  } = context;
  const {assignment: initialAssignment, back} = props;
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [
    editAssessmentCriteriaSelected,
    setEditAssessmentCriteriaSelected,
  ] = useState();
  const [showBatchDialog, setShowBatchDialog] = useState(false);
  const [assignment, setAssignment] = useState(initialAssignment);
  const [assessments, setAssessments] = useState([]);
  const [filter, setFilter] = useState([
    "overallFeedback",
    "overallNote",
    "selfAssessment",
    "overallGrade",
  ]);
  const [expandAll, setExpandAll] = useState(false);
  const [showExpandAllInfo, setShowExpandAllInfo] = useState(false);
  const [loading, setLoading] = useState(true);
  const [showTooltip, setShowTooltip] = useState(false);

  const gradeSystem = useRef([]);
  const courseDefinitions = useRef([]);

  const dimensions = useWindowDimensions();

  const getStudentAssessments = async () => {
    let response = await Api.getClassroom(`
    courseDefinitions {
      id
      name
      course_id
      objectives{
        id
        name
        description
      }
    }`);
    const {courseDefinitions: cd} = response?.data?.classroom;
    response = await Api.getAssignmentWithAssessment(assignment.id, fields);
    const {
      gradeSystem: gs,
      assessments,
      objectiveAssessments,
    } = response?.data?.assignment[0];
    courseDefinitions.current = cd;
    gradeSystem.current = gs;
    const all = assessments.map((a) => {
      return {
        ...a,
        objectiveAssessments: objectiveAssessments.filter(
          (oa) => oa.student_id === a.student_id
        ),
      };
    });
    setAssessments(all);
  };

  useEffect(() => {
    setLoading(true);

    (async () => {
      await getStudentAssessments();
      const f = await getStudentListFilterOptions();
      if (f) {
        setFilter(f);
      }

      setLoading(false);
    })();
  }, []);

  useEffect(() => {
    setAssignment(initialAssignment);
  }, [initialAssignment]);

  const refresh = async () => {
    setLoading(true);
    const response = await Api.getClassroom(classroomFields);
    const assignments = response?.data?.classroom?.assignments
      .map((a) => [a, ...(a.assignments || [])])
      .flat();
    setAssignment(assignments.find((a) => a.id === assignment.id));
    getStudentAssessments();
    setLoading(false);
  };

  const onStatusChange = async ({student, status}) => {
    const assessment = assessments.find(
      (a) => a.student_id === student.student_id
    );
    if([SUBMITTED, SUBMITTED_LATE, NOT_SUBMITTED_LATE, NOT_SUBMITTED].find(s => s === status)){
      assessment.submitted_status = status;
    } else {
      assessment.status_new = status;
    }

    if (status === "ASSESSMENT_DONE") {
      setVisibilityDialogData({
        assignmentId: assignment.id,
        assessments: [assessment],
        callback: () => {
          refresh();
        },
      });
    }
    await Api.updateAssignmentAssessment([assessment]);
  };

  const studentClicked = (data) => {
    const {
      student,
      objectiveAssessments,
      assessment,
      openGrid = false,
    } = data;
    const params = {
      openGrid,
      studentId: student.student_id,
      assignment,
      courseDefinitions: courseDefinitions.current,
      gradeSystem: gradeSystem.current,
      objectiveAssessments,
      assignmentAssessment: assessment,
      historyBack: async () => {
        setSelectedStudent(null);
        await refresh();
      },
      students: assignment.assessments,
    };
    setSelectedStudent(params);
  };

  const handleFilterMenu = (name) => {
    let newFilter;
    if (filter.find((f) => f === name)) {
      newFilter = filter.filter((n) => n !== name);
      setFilter(newFilter);
      setStudentListFilterOptions(newFilter);
    } else {
      newFilter = [...filter, name];
      setFilter(newFilter);
      setStudentListFilterOptions(newFilter);
    }
    const showObjectives = !!newFilter.find((f) =>
      ["grade", "feedback", "note", "studentAssessment"].find((a) => a === f)
    );
    if (showObjectives) {
      setExpandAll(false);
    }
  };

  if (selectedStudent) {
    return <AssignmentObjectivesList {...selectedStudent} />;
  }
  if (editAssessmentCriteriaSelected) {
    return (
      <EditAssessmentCriteria
        onClose={() => {
          setEditAssessmentCriteriaSelected(false);
          refresh();
        }}
        assignment={assignment}
      />
    );
  }

  const disableExpandAll = !!filter.find((f) =>
    ["grade", "feedback", "note", "studentAssessment"].find((a) => a === f)
  );
  const toggleExpandAll = () => {
    if (!disableExpandAll) {
      setExpandAll(!expandAll);
    } else {
      setShowExpandAllInfo(true);
      setTimeout(() => setShowExpandAllInfo(false), 3000);
    }
  };
  const empty = assessments ? assessments.length === 0 : true;
  const {assessments: students} = assignment;
  const sortedStudents = students
    ? sortAssessmentsByStudentName(students, sortingMethod)
    : [];

  return (
    <div style={{}}>
      <AssignmentStudentListHeader
        title={assignment.title}
        back={back}
        isTopic={assignment?.is_topic}
        gotToEdit={() => {
          setEditAssessmentCriteriaSelected(true);
        }}
        openGrid={() =>
          studentClicked({
            student: sortedStudents[0],
            objectiveAssessments: assessments?.objectiveAssessments,
            assessments,
            openGrid: true,
          })
        }
      />
      {loading && <FullscreenProgress/>}
      {empty && !loading && (
        <div style={{margin: "20px"}}>
          <span style={{...fonts.LARGE}}>
            {getTranslation("noStudentsYet")}
          </span>
        </div>
      )}
      {!loading && !empty && (
        <>
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              padding: "10px 10px 5px 10px",
              borderBottom: `1px solid ${colors.borderColor}`,
              height: "30px",
              alignItems: "center",
            }}
          >
            <SortStudentComponent/>
            <Tooltip
              open={showTooltip}
              title={getTranslation("assignmentsActionMenuFilterTooltip")}
            >
              <div
                onMouseUp={() => setShowTooltip(false)}
                onMouseEnter={() => setShowTooltip(true)}
                onMouseLeave={() => setShowTooltip(false)}
              >
                <MoreMenu
                  icon={
                    <FilterIcon
                      style={{height: "25px", border: "0px solid red"}}
                      fill={colors.darkGrey}
                    />
                  }
                  options={filterOptions.map((f) => ({
                    ...f,
                    selected: filter.find((name) => name === f.name),
                  }))}
                  onClick={(item) => handleFilterMenu(item.name)}
                  closeOnClick={false}
                />
              </div>
            </Tooltip>
            <Tooltip
              title={getTranslation("expandAllDisabledDescription")}
              open={showExpandAllInfo}
            >
              <Tooltip
                title={
                  expandAll
                    ? getTranslation("collapseAllButton")
                    : getTranslation("expandAllButton")
                }
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    cursor: "pointer",
                    height: "25px",
                    width: "25px",
                    alignItems: "center",
                    justifyContent: "center",
                    border: "0px solid",
                  }}
                  onClick={toggleExpandAll}
                >
                  {!expandAll && <ExpandIcon fill={colors.darkGrey}/>}
                  {expandAll && <CollapseIcon fill={colors.darkGrey}/>}
                </div>
              </Tooltip>
            </Tooltip>
          </div>
          <div style={{height: dimensions.height - 110, overflow: "scroll"}}>
            {sortedStudents.map((student) => (
              <StudentItem
                expanded={expandAll}
                filter={filter}
                expandable
                assignment={assignment}
                onStatusChange={onStatusChange}
                key={student.student_id}
                onClick={studentClicked}
                student={student}
                courseDefinitions={courseDefinitions.current}
                gradeSystem={gradeSystem.current}
                assessment={assessments.find(
                  (a) => a.student_id === student.student_id
                )}
              />
            ))}
          </div>
          <BatchAssessmentDialog
            assignment={assignment}
            students={assignment?.assessments}
            open={showBatchDialog}
            onClose={() => {
              refresh();
              setShowBatchDialog(false);
            }}
            courses={courseDefinitions.current.filter(
              (c) => c.objectives?.length > 0
            )}
            gradeSystem={gradeSystem.current}
          />
        </>
      )}
    </div>
  );
};

export default AssignmentStudentList;
