import angular, {IPromise, IRootElementService, ITimeoutService} from 'angular';

import {SlideToggleAnimation} from '../../../utils/animations/slideToggle.animation';

export class Accordion {
  static ACCORDION_CONTAINER_QUERY = '.accordion-container';
  static ACCORDION_CONTENT_QUERY = '.accordion-content';
  static ANIMATION_CLASSNAME = SlideToggleAnimation.className;
  static OPEN_CLASSNAME = 'open';
  static SCROLL_LOCK_CLASS_NAME = 'scroll-lock';

  constructor(private $animate: angular.animate.IAnimateService, private $timeout: ITimeoutService) {
    'ngInject';
  }

  open(accordionElement: IRootElementService): void {
    this.$timeout(
      () => {
        this.disabelContentScrollbars(accordionElement);
        this.addOpenClass(accordionElement);
        const accordionContainer: JQuery = accordionElement.find(Accordion.ACCORDION_CONTAINER_QUERY);
        this.stopAnimation(accordionElement);

        if (!accordionContainer.hasClass(Accordion.ANIMATION_CLASSNAME)) {
          accordionContainer.css('display', '');
        }

        const animationPromise = this.$animate
          .addClass(accordionContainer, Accordion.ANIMATION_CLASSNAME)
          .then(() => this.enableContentScrollbars(accordionElement));

        accordionContainer.data('$animate', animationPromise);
      },
      0,
    );
  }

  close(accordionElement: IRootElementService): IPromise<void> {
    this.disabelContentScrollbars(accordionElement);
    this.removeOpenClass(accordionElement);
    const accordionContainer: JQuery = accordionElement.find(Accordion.ACCORDION_CONTAINER_QUERY);
    this.stopAnimation(accordionContainer);

    if (!accordionContainer.hasClass(Accordion.ANIMATION_CLASSNAME)) {
      accordionContainer.css('display', 'none');
    }

    const animationPromise = this.$animate
      .removeClass(accordionContainer, Accordion.ANIMATION_CLASSNAME)
      .then(() => this.enableContentScrollbars(accordionElement));

    accordionContainer.data('$animate', animationPromise);

    return animationPromise;
  }

  addOpenClass(accordionElement: IRootElementService) {
    return this.$animate.addClass(accordionElement, Accordion.OPEN_CLASSNAME);
  }

  removeOpenClass(accordionElement: IRootElementService) {
    return this.$animate.removeClass(accordionElement, Accordion.OPEN_CLASSNAME);
  }

  stopAnimation(element: JQuery): void {
    if (element && element.data('$animate')) {
      this.$animate.cancel(element.data('$animate'));
      element.removeData('$animate');
    }
  }

  disabelContentScrollbars(accordionElement: IRootElementService): void {
    accordionElement
      .find(Accordion.ACCORDION_CONTENT_QUERY)
      .addClass(Accordion.SCROLL_LOCK_CLASS_NAME);
  }

  enableContentScrollbars(accordionElement: IRootElementService): void {
    accordionElement
      .find(Accordion.ACCORDION_CONTENT_QUERY)
      .removeClass(Accordion.SCROLL_LOCK_CLASS_NAME);
  }
}
