import {colors, fonts} from "../theme";
import React from "react";
import {getTranslation} from "../i18n";
import SecondaryButton from "../components/secondaryButton";
import PrimaryButton from "../components/primaryButton";

const imgSrc = process.env.REACT_APP_STANDALONE ? 'https://is2-ssl.mzstatic.com/image/thumb/Purple114/v4/be/59/64/be596489-6244-84bd-26c3-701bfcb3e3b3/source/256x256bb.jpg' : `chrome-extension://${chrome.runtime.id}/images/icon-128.png`;

const HideAssignmentsDialog = ({open, no, yes}) => {
  if (!open) {
    return null;
  }
  return (
    <div style={{
      position: 'absolute',
      display: 'flex',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      justifyContent: 'center',
      alignItems: 'center'
    }}>
      <div style={{
        width: '310px',
        backgroundColor: colors.primary,
        borderRadius: '6px',
        margin: '20px',
        zIndex: '100000',
        boxShadow: '0px 5px 10px rgba(0,0,0,0.3)',
        padding:'10px 0px 10px 0px',

      }}>
        <div style={{padding:'10px', display:'flex'}}>
          <img src={imgSrc} style={{
            width: '25px', height: '25px', backgroundColor: colors.white, padding: '8px', borderRadius: '50px'
          }}/>
        <span style={{...fonts.MEDIUM, fontWeight: '500', marginLeft:'10px'}}>{getTranslation('hideAssignmentsDialogText')}</span>
        </div>
        <div style={{borderTop:'1px solid ' + colors.borderOnPrimary, width:'100%', margin:'10px 0px 10px 0px'}}/>
        <div style={{display: 'flex', justifyContent: 'flex-end'}}>
          <SecondaryButton onClick={no} title={getTranslation('noDisconnectAssignments')}/>
          <PrimaryButton onClick={yes} title={getTranslation('yesDisconnectAssignments')} />
        </div>
      </div>
    </div>
  );
};
export default HideAssignmentsDialog;
