import { IPromise } from 'angular';
import { StateDeclaration, StateParams, TransitionService } from '@uirouter/core';
import { StateProvider } from '@uirouter/angularjs';
import { TransitionHandler } from '../../shared/models/transitionHandler.model';
import { registerOnBeforeTransition } from '../../utils/decorators/registerOnBeforeTransition.decorator';
import { FetchData } from '../../utils/services/fetchData.service';

export const StudentAssessAssignmentDriveRouting: Function = ($stateProvider: StateProvider, $transitionsProvider: TransitionService): void => {
  'ngInject';

  @registerOnBeforeTransition($transitionsProvider)
  class ThemeApply extends TransitionHandler {
    public getCriteria(): any {
      return { to: 'studentGoogleDrive.**' };
    }

    public onBefore(transition: any): boolean {
      const transitionTo: StateDeclaration = transition.to();
      (transitionTo as any).themeClass = 'sb-theme-green';
      (transitionTo as any).role = 'student';

      return true;
    }
  }

  $stateProvider
    .state('studentGoogleDrive', {
      component: 'studentAssessAssignmentDrive',
      resolve: {
        assignments: ($state: StateParams, Path: any, FetchData: FetchData): IPromise<any[]> => {
          'ngInject';

          const assessmentId = Path.getAssignmentAlternateLinkId(true);
          return Path.getFileId(5)
            .then(fileId => FetchData.getAssessmentDrive(fileId, assessmentId))
            .then((assessments: any[]) => {
              return FetchData.getStudentAssignment(assessments[0].id)
                .then((studentAssignment: any) => {
                  return assessments.map((assessment) => Object.assign(assessment, {
                    show_grade_texts_for_student: studentAssignment.show_grade_texts_for_student,
                  }));
                });
            })
            .catch(() => $state.target('wrongGoogleDriveFile'));
        },
      },
    });
};
