/**
 * @module Assignments/Service
 * @description
 *   Keeps a list of assignments
 */

/*@ngInject*/
function Assignments() {
  let assignments = [];
  return {
    /**
     * Update assignments list.
     * @param {Array} updatedAssignments Updates assignments array.
     * @memberof module:Assignments/Service
     */
    updateAssignmentsList(updatedAssignments) {
      assignments = updatedAssignments;
    },

    getAssignmentByAlternateLink(alternateLinkId) {
      return assignments.find(assignment => assignment.alternate_link_id === alternateLinkId);
    },
  };
}

module.exports = Assignments;
