import { GroupedVisibilityDialogType } from '../enums/groupedVisibilityDialogType.enum';
import { ICourseDefinition } from './courseDefinition.model';
import { IStudent } from './student.model';

export interface IGroupedVisibilitySettings {
  courseDefinitions: ICourseDefinition[];
  students: IStudent[];
  type: GroupedVisibilityDialogType;
}

export class GroupedVisibilitySettings implements IGroupedVisibilitySettings {
  courseDefinitions: ICourseDefinition[];
  students: IStudent[];
  type: GroupedVisibilityDialogType;

  constructor(type: GroupedVisibilityDialogType, courseDefinitions?: ICourseDefinition[], students?: IStudent[]) {
    this.type = type;
    this.courseDefinitions = courseDefinitions || [];
    this.students = students || [];
  }
}
