import angular from 'angular';
import { IDirectiveOptions, IModule } from 'angular';

export const Directive: (options: IDirectiveOptions) => any = (options: IDirectiveOptions): <T extends { new(...args: any[]): angular.IDirective }>(constructor: T) => any => {

  const getOrCreateComponentModule: () => IModule = (): IModule => {
    const moduleName: string = 'app';
    let module: IModule;

    if (options.module) {
      try {
        module = angular.module(`${options.module}`);
      } catch (err) {
        module = null;
      }
    }

    if (!module) {
      try {
        module = angular.module(`${moduleName}`);
      } catch (err) {
        console.error(`Error on register directive ${options.selector} in module ${moduleName}. Module ${moduleName} is not exists.`, err);
        module = null;
      }
    }

    return module;
  };

  const createDirectiveFactory: (constructor: { new(...args: any[]): angular.IDirective }) => angular.IDirectiveFactory = (constructor: { new(...args: any[]): angular.IDirective }): angular.IDirectiveFactory => {
    const directiveFactory: any = (...args: any[]): angular.IDirective =>  {
      const directive: angular.IDirective = new constructor(...args);
      Object.assign(directive, options);

      return directive;
    };

    if (constructor.$inject) {
      directiveFactory.$inject = constructor.$inject;
    }

    return directiveFactory;
  };

  const convertSelectorName: (selector: string) => string = (selector: string): string => {
    return selector.replace(
      /-([a-z])/g,
      (word: string): string => word[1].toUpperCase(),
    );
  };

  return <T extends { new(...args: any[]): angular.IDirective }>(constructor: T): T => {
    const module: IModule = getOrCreateComponentModule();

    const directiveFactory: angular.IDirectiveFactory = createDirectiveFactory(constructor);

    if (module) {
      const selector: string = convertSelectorName(options.selector);
      module.directive(selector, directiveFactory);
    }

    return constructor;
  };
};

