import { Service } from '../../../utils/decorators/service.decorator';
import SharedServicesModule from '../services.module';

export interface ILocalStorageService {
  clear(): void;
  exists(key: string): boolean;
  getArray<T = any>(key: string): T[];
  get(key: string): string;
  remove(key: string): void;
  setArray(key: string, value: any[]): void;
  set(key: string, value: string): void;
}

@Service({
  name: 'LocalStorageService',
  module: SharedServicesModule,
})
export class LocalStorageService implements ILocalStorageService {

  public clear(): void {
    window.localStorage.clear();
  }

  public exists(key: string): boolean {
    return window.localStorage.getItem(key) !== null;
  }

  public getArray<T = any>(key: string): T[] {
    return JSON.parse(this.get(key));
  }

  public get(key: string): string {
    return window.localStorage.getItem(key);
  }

  public remove(key: string): void {
    window.localStorage.removeItem(key);
  }

  public setArray(key: string, value: any[]): void {
    this.set(key, JSON.stringify(value));
  }

  public set(key: string, value: string): void {
    window.localStorage.setItem(key, value);
  }
}
