import React, { useEffect, useState } from 'react';
import { useWindowDimensions } from '../hooks';
import { colors } from '../theme';
import CoursesCard from './coursesCard';
import StudentOverviewHeader from './studentOverviewHeader';
import StudentCourseOverview from './studentCourseOverview';
import CourseObjectiveOverview from './courseObjectiveOverview';

const StudentOverview = (props) => {
  const {
    student,
    onClose,
    onCourseSelected,
    selectedCourse: initialSelectedCourse,
    toggleGrid,
    classroomName,
    onObjectiveSelected,
    selectedObjective,
  } = props;
  const window = useWindowDimensions();
  const [selectedCourse, setSelectedCourse] = useState(initialSelectedCourse);

  const studentName = `${student.first_name} ${student.last_name}`;

  useEffect(() => {
    onCourseSelected(selectedCourse);
  }, [selectedCourse]);

  return (
    <div
      style={{
        position: 'absolute',
        width: `${window.width - 360}px`,
        left: `${-window.width + 360}px`,
        height: `${window.height}px`,
        display: 'flex',
        backgroundColor: colors.grey,
        flexDirection: 'column',
        border: '0px solid blue',
      }}
    >
      <StudentOverviewHeader
        classroomName={classroomName}
        toggleGrid={toggleGrid}
        onClose={onClose}
        studentName={studentName}
        courseName={selectedCourse?.name}
        objectiveName={selectedObjective?.name}
        onBack={
          selectedObjective || selectedCourse
            ? () => {
              if (selectedObjective) {
                onObjectiveSelected(null);
              } else if (selectedCourse) {
                setSelectedCourse(null);
              }
            }
            : null
        }
      />
      {selectedObjective && (
        <CourseObjectiveOverview
          student={student}
          objective={selectedObjective}
          course={selectedCourse}
          onObjectiveSelected={onObjectiveSelected}
        />
      )}
      {selectedCourse && !selectedObjective && (
        <StudentCourseOverview
          hideCourseCard
          student={student}
          course={selectedCourse}
          onObjectiveSelected={onObjectiveSelected}
          selectedObjective={selectedObjective}
          loading={() => {}}
        />
      )}

      <div style={{ marginTop: '60px', overflow: 'scroll', display: 'flex' }}>
        {!selectedCourse && (
          <CoursesCard student={student} onCourseSelected={setSelectedCourse} />
        )}
      </div>
      <div
        style={{
          width: '30px',
          height: '100%',
          position: 'absolute',
          right: -30,
          top: 0,
          backgroundColor: 'transparent',
          boxShadow: '-5px 0px 6px rgba(0,0,0,0.1)',
        }}
      />
    </div>
  );
};
export default StudentOverview;
