import { Params } from '@angular/router';
import { AssignmentState } from '../../shared/enums/assignmentStateType.enum';
import { AssignmentType } from '../../shared/enums/assignmentType.enum';
import { GroupedVisibilityDialogType } from '../../shared/enums/groupedVisibilityDialogType.enum';
import { IAssessment } from '../../shared/models/assessment.model';
import { IAssignment } from '../../shared/models/assignment.model';
import { IGroupedVisibilitySettings } from '../../shared/models/groupedVisibilitySettings.model';
import { IStudent } from '../../shared/models/student.model';
import { GroupedVisibilityService } from '../../shared/services/groupedVisibility/groupedVisibility.service';
import { GroupedVisibilityDialogService } from '../../shared/services/groupedVisibility/groupedVisibilityDialog.service';
import { BehaviourSubject, IBehaviourSubject } from '../../shared/services/helpers/behaviourSubject.service';
import { ILocalStorageService } from '../../shared/services/localStorage/localStorage.service';
import { Service } from '../../utils/decorators/service.decorator';
import { Students } from '../../utils/services/students';
import { AssignmentOrder } from './shared/enums/assignmentOrder';
import { ITeacherAssignment } from './shared/models/assignment.model';
import TeacherAssignmentsModule from './teacherAssignments.module';

export interface ITeacherAssignmentsService {
  openedAssignments$: IBehaviourSubject<number[]>;
  closeAssignment(assignment: ITeacherAssignment): void;
  openAssignment(assignment: ITeacherAssignment): void;
  toggleAssignment(assignment: ITeacherAssignment): any;
  updateAssignmentsOpenState(assignmentsGroups: ITeacherAssignment[]): ITeacherAssignment[];
  changeVisibility(assignment: IAssignment): void;
  getRequestParams(): Params;
}

@Service({
  module: TeacherAssignmentsModule,
  name: 'TeacherAssignmentsService',
})
export class TeacherAssignmentsService implements ITeacherAssignmentsService {
  public openedAssignments$: IBehaviourSubject<number[]>;

  constructor(protected Path: any,
              protected FetchData: any,
              protected Students: Students,
              protected LocalStorageService: ILocalStorageService,
              protected GroupedVisibilityDialogService: GroupedVisibilityDialogService,
              protected GroupedVisibilityService: GroupedVisibilityService) {
    'ngInject';

    this.openedAssignments$ = new BehaviourSubject<number[]>([]);
  }

  public closeAssignment(assignment: ITeacherAssignment): ITeacherAssignment {
    const openedAssignments: number[] = this.openedAssignments$
      .getValue()
      .filter((_assignmentId: number): boolean => _assignmentId !== assignment.id);

    this.openedAssignments$.next(openedAssignments);

    return Object.assign({}, assignment, { $$open: false });
  }

  public openAssignment(assignment: ITeacherAssignment): ITeacherAssignment {
    const openedAssignments: number[] = [
      ...this.openedAssignments$.getValue(),
      assignment.id,
    ];

    this.openedAssignments$.next(openedAssignments);

    return Object.assign({}, assignment, { $$open: true });
  }

  public toggleAssignment(assignment: ITeacherAssignment): ITeacherAssignment {
    return assignment.$$open ? this.closeAssignment(assignment) : this.openAssignment(assignment);
  }

  public updateAssignmentsOpenState(assignments: ITeacherAssignment[]): ITeacherAssignment[] {
    return assignments.map((assignment: ITeacherAssignment): ITeacherAssignment => {
      const $$open: boolean = this.openedAssignments$.getValue().includes(assignment.id);

      return Object.assign({}, assignment, { $$open });
    });
  }

  public changeVisibility(assignment: IAssignment): PromiseLike<void> {
    return Promise.all([
      this.FetchData.v3.fetchAssignmentAssessments(this.Path.getClassroomId(), assignment.id),
      this.Students.getStudentsList(),
    ])
      .then((responses: any[]): PromiseLike<void> => {
        const assignmentsObjectives = responses[0];
        const assessmentStudentId: number[] = assignment.assessments.map((assessment: IAssessment): number => assessment.student_id);
        const students: IStudent[] = responses[1]
          .filter((student: IStudent): boolean => assessmentStudentId.includes(student.id));
        const settings: IGroupedVisibilitySettings = {
          students,
          courseDefinitions: assignmentsObjectives.course_definitions.filter((courseDefinition) => !!courseDefinition.objectives.length),
          type: students.length > 1 ? GroupedVisibilityDialogType.AssignmentView : GroupedVisibilityDialogType.AssignmentStudentView,
        };

        return this.GroupedVisibilityDialogService.show(settings).then((groupedVisibility) => {
          this.GroupedVisibilityService.patchAssignmentAssessmentsGroupedVisibility(groupedVisibility, assignment);
        });
      });
  }

  public getRequestParams(): Params {
    const savedOrdering: AssignmentOrder = this.LocalStorageService.get('teacherAssignmentsOrdering') as AssignmentOrder;
    const savedStates: AssignmentState[] = this.LocalStorageService.getArray<AssignmentState>('teacherAssignmentsStates');
    const savedFilters: AssignmentType[] = this.LocalStorageService.getArray<AssignmentType>('teacherAssignmentsFiltering');

    const ordering = savedOrdering || AssignmentOrder.ASC_TYPE;
    const states = savedStates || [AssignmentState.PUBLISHED, AssignmentState.DRAFT];
    const filters = savedFilters ||
      [AssignmentType.WORK_UNIT, AssignmentType.STANDARD_ASSIGNMENT, AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS];

    return { ordering, states, filters };
  }
}
