import { Events } from '../../../../common/global-events.ts';
import { Breadcrumb } from '../../../../shared/models/breadcrumb.model.ts';

export class ObjectiveDetailsCtrl {
  constructor($rootScope, $scope, $stateParams, $state, $timeout, BreadcrumbsService, FetchData, UserInfo) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$scope = $scope;
    this.$stateParams = $stateParams;
    this.$state = $state;
    this.$timeout = $timeout;
    this.BreadcrumbsService = BreadcrumbsService;
    this.FetchData = FetchData;
    this.breadcrumbs = [];
    this.objectives = [];
    this.broadcastDelay = 200;
    this.isEditable = false;
    this.isTeacher = UserInfo.isTeacher();
  }

  $onInit() {
    this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);

    this.breadcrumbs = [...this.$stateParams.breadcrumbs].concat(
      new Breadcrumb(
        this.$stateParams.objective.name,
        this.BreadcrumbsService.createGoToAction(),
      ),
    );

    this.isEditable = this.$stateParams.course && this.$stateParams.course.teacher_in_class;
  }

  closeAssignmentsView() {
    // also update the warnings for the students' list
    this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);
    this.$stateParams.breadcrumbs.pop().action();
  }

  changeObjective() {
    this.$state.go(
     this.$state.current.name,
      this.getRoutiongParams(),
    );
  }

  getRoutiongParams() {
    return Object.assign({}, this.$stateParams, {
      objective: this.objectiveSelected,
      student: this.student,
      course: this.course,
      breadcrumbs: this.breadcrumbs.slice(0, -1),
    });
  }

  toggleAssessment() {
    this.objectiveAssessment.open = !this.objectiveAssessment.open;
  }

  updateAssessmentData(assessmentData) {
    this.$rootScope.$emit(Events.STARTED_NEW_REQUEST);
    this.FetchData.v3
      .updateCourseDefinitionObjectiveAssessment(
        this.student.id,
        this.course.course_definition,
        assessmentData,
      )
      .then((assessmentResponse) => {
        Object.assign(assessmentResponse, { open: assessmentData.open });
        // Dont overwrite feedback and private_note, it messes with the ui
        const { feedback, private_note, result, ...rest } = assessmentResponse;
        this.objectiveAssessment = { ...this.objectiveAssessment, ...rest };
      })
      .then(() => {
        this.$rootScope.$emit(Events.UPDATE_USERS_STATUSES_FOR_COURSES_AND_OBJECTIVES);
        this.$rootScope.$emit(Events.REQUEST_FINISHED_SUCCESSFULLY);
      });
  }
}
