export interface ITabMessage {
  action: string;
  createProperties?: chrome.tabs.CreateProperties;
  tabId?: number;
  updateProperties?: chrome.tabs.UpdateProperties;
}

export class TabMessage implements ITabMessage {
  public action: string;
  public createProperties?: chrome.tabs.CreateProperties;
  public tabId: number;
  public updateProperties?: chrome.tabs.UpdateProperties;

  constructor(action: string) {
    this.action = action;
    this.createProperties = {};
    this.updateProperties = {};
  }
}
