import {Injectable} from '@angular/core';
import {FetchData} from '../../ajs-upgraded-providers';
import {from, Observable} from 'rxjs';
import {Params} from '@angular/router';
import {HttpRequestType} from './http.model';
import {HttpCacheQuery, HttpCacheService} from './state';
import {shareReplay, tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(protected fetchData: FetchData, protected httpCacheService: HttpCacheService, protected httpCacheQuery: HttpCacheQuery) {
  }

  request<T = any>(url: string, type: HttpRequestType, body?: any, params: Params = {}): Observable<T> {
    const id = this.httpCacheService.generateID(url, type, body, params);

    if (this.httpCacheQuery.hasEntity(id)) {
      return this.httpCacheQuery.getEntity(id).source;
    }

    const request: PromiseLike<T> = this.fetchData.sendAPIRequestWithParams(params, url, type, body);
    const source = from(request).pipe(
      tap(() => this.httpCacheService.delete(id)),
      shareReplay(1),
    );

    this.httpCacheService.add<T>(source, url, type, body, params);

    return source;
  }

  delete<T = any>(url: string, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.DELETE, null, params);
  }

  get<T = any>(url: string, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.GET, null, params);
  }

  head<T = any>(url: string, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.HEAD, null, params);
  }

  options<T = any>(url: string, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.OPTIONS, null, params);
  }

  patch<T = any>(url: string, body?: any, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.PATCH, body, params);
  }

  post<T = any>(url: string, body?: any, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.POST, body, params);
  }

  put<T = any>(url: string, body?: any, params: Params = {}): Observable<T> {
    return this.request<T>(url, HttpRequestType.PUT, body, params);
  }
}
