import { DialogType } from '../../../../../utils/enums/dialogType.enum';
import { CourseProgressDialog } from '../courseProgressDialog.service.ts';

export class ExportStudentsResultsDialog extends CourseProgressDialog {
  constructor($mdDialog) {
    'ngInject';

    super($mdDialog);
  }

  show(locals, dialogType) {
    const config = { locals };

    switch (dialogType) {
      case DialogType.SUCCESS:
        config.templateUrl = `${
          CourseProgressDialog.TEMPLATE_PATH
        }/exportStudentsResults/exportStudentsResultsDialogSuccess.html`;
        break;
      case DialogType.FAIL:
        config.templateUrl = `${
          CourseProgressDialog.TEMPLATE_PATH
        }/exportStudentsResults/exportStudentsResultsDialogFail.html`;
        break;
      case DialogType.CONFIRMATION:
        config.templateUrl = `${
          CourseProgressDialog.TEMPLATE_PATH
        }/exportStudentsResults/exportStudentsResultsDialogConfirmation.html`;
        break;
      default:
        break;
    }

    super.show(config);
  }
}
