import get from 'lodash.get';
import xor from 'lodash.xor';
import isEmpty from 'lodash.isempty';
import isEqual from 'lodash.isequal';
import { AssignConfirmationType } from './shared/enum/assignConfirmationType.enum.ts';
import { ListViewType } from '../../shared/enums/listViewType.enum.ts';
import { AssignmentType } from '../../shared/enums/assignmentType.enum.ts';
import { getTranslation } from '../../../../../react_src/i18n';

/**
 * @module TeacherSetAssignmentObjectives/Controller
 * @description
 *   Handle Assign Objectives to Assignment for Teacher
 * @param $stateParams
 * @param $mdToast
 * @param {Object} $mdDialog Angular Material $mdDialog object
 * @param {Object} Path Path service instance
 * @param {Object} FetchData Fetch Data service instance
 * @param Updates
 * @param {Object} Debounce Debounce service instance
 * @param {Object} NavigationHistory Navigation History service instance
 * @param {Object} MainMenu Main Menu service instance
 * @param {Object} Mixpanel Mixpanel service instance
 * @param StudybeeHandle
 * @param {Object} assignmentInformation Resolve from Config Service
 * @param gradeSystems
 */

export function TeacherSetAssignmentObjectiveCtrl($state, $timeout,
  $stateParams, $mdToast, $mdDialog, $filter, Path, FetchData, Updates, Debounce,
  NavigationHistory, Mixpanel, MainMenu, StudybeeHandle, assignmentInformation,
  gradeSystems, TeacherSetAssignmentObjectivesService, ListViewMenuService, AssignmentsStore) {
  'ngInject';

  const vm = this;

  const courseId = Path.getClassroomId();

  const { assignment } = $stateParams;
  const storageViewMenuId = 'teacherSetAssignment';

  vm.isFirstTime = get($stateParams, 'firstTime', false);

  // Get data from API
  vm.assignment = assignmentInformation;

  vm.gradeTextSettings = {
    isEnable: vm.assignment.enable_grade_texts,
    isEnableForStudents: vm.assignment.show_grade_texts_for_student,
  };

  // vm.assignmentTitle = assignmentInformation.title;
  vm.gradeSystems = gradeSystems;
  vm.courseDefinitions = assignmentInformation.course_definitions;
  vm.resultOnly = assignmentInformation.result_only;
  vm.viewMenuItems = ListViewMenuService.getListViewMenu(storageViewMenuId);
  vm.viewMode = vm.viewMenuItems.find((listViewMenuItem) => listViewMenuItem.checked)
    .eventArgs.view;
  vm.lastSelectedScaleId = null;
  vm.state = {
    type: {
      previous: {},
      get change() {
        return get(vm, 'selectedType.type', 0) !== get(this, 'previous.type', -1);
      },
      get isEmpty() {
        return isEmpty(vm.selectedType);
      },
    },
    scale: {
      previous: {},
      get change() {
        return get(vm, 'scaleSelect.id', 0) !== get(this, 'previous.id', 0);
      },
    },
    objectives: {
      previous: [],
      get change() {
        return !!xor(vm.getSelectedObjectivesId(), this.previous).length;
      },
    },
    counters: {
      previous: {},
      get change() {
        return get(vm, 'scaleSelect.is_counter', false) && !isEqual(get(this, 'previous'), vm.getFlatObjectivesCounters());
      },
    },
    gradeTextSettings: {
      previous: {},
      get change() {
        if (this.previous.enable_grade_texts !== vm.assignment.enable_grade_texts) {
          return true;
        } if (vm.assignment.enable_grade_texts) {
          return this.previous.show_grade_texts_for_student !== vm.assignment.show_grade_texts_for_student;
        }
      },
    },
  };
  vm.typeOfAssignments = [
    {
      description: 'teacherAssignmentObjectivesTypeOfAssignmentWorkUnitDesc',
      label: 'teacherAssignmentObjectivesTypeOfAssignmentWorkUnitLabel',
      type: AssignmentType.WORK_UNIT,
    },
    {
      description: 'teacherAssignmentObjectivesTypeOfAssignmentAssignmentDesc',
      label: 'teacherAssignmentObjectivesTypeOfAssignmentAssignmentLabel',
      type: AssignmentType.STANDARD_ASSIGNMENT,
    },
  ];
  vm.selectedType = {

    ...vm.typeOfAssignments
      .find((typeOfAssignment) => typeOfAssignment.type === assignment.assignment_type),
  };

  /**
   * Init function
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.init = function () {
    // Create list of selected elements that is used to compare changes
    vm.createListOfSelected();
    vm.setDefaultGradeSystem();
    vm.setState();

    $timeout(() => {
      vm.validationForm.$setSubmitted();
    }, 0);
  };

  vm.setState = () => {
    vm.state.scale.previous = { ...vm.scaleSelect };
    vm.state.objectives.previous = [...vm.getSelectedObjectivesId()];
    vm.state.type.previous = { ...vm.selectedType };
    vm.state.counters.previous = vm.getFlatObjectivesCounters();
    vm.state.gradeTextSettings.previous = {

      enable_grade_texts: vm.assignment.enable_grade_texts,
      show_grade_texts_for_student: vm.assignment.show_grade_texts_for_student,
    };
  };

  vm.getConfirmationType = () => {
    const currentState = [];

    if (vm.state.scale.change) {
      currentState.push(AssignConfirmationType.GRADE_SYSTEM);
    }

    if (vm.state.objectives.change) {
      currentState.push(AssignConfirmationType.OBJECTIVES);
    }

    if (vm.state.type.change) {
      currentState.push(AssignConfirmationType.ASSIGNMENT_TYPE);
    }

    if (vm.state.counters.change) {
      currentState.push(AssignConfirmationType.CHANGE_COUNTER_VALUE);
    }

    if (vm.state.gradeTextSettings.change) {
      currentState.push(AssignConfirmationType.GRADE_TEXT_SETTINGS);
    }

    return currentState;
  };

  vm.updateGradeTextSettings = () => {
    vm.assignment.enable_grade_texts = vm.gradeTextSettings.isEnable;
    vm.assignment.show_grade_texts_for_student = vm.gradeTextSettings.isEnable && vm.gradeTextSettings.isEnableForStudents;
  };

  vm.onClickMenuItem = (toolbarMenuItem) => {
    vm.viewMenuItems.forEach((menuItem) => {
      Object.assign(menuItem, { checked: false });
    });
    Object.assign(toolbarMenuItem, { checked: true });
    ListViewMenuService.saveListViewMenu(storageViewMenuId, vm.viewMenuItems);
    vm.viewMode = toolbarMenuItem.eventArgs.view;
  };

  vm.isTitleVisible = (title) => vm.viewMode !== ListViewType.DESCRIPTION_ONLY && `${title}`.trim().length;

  vm.isDescriptionVisible = (description) => vm.viewMode !== ListViewType.TITLE_ONLY && `${$filter('getFormattedOrEmpty')(description)}`.trim().length;

  vm.getAssignmentTypePlaceholder = () => get(vm, 'selectedType.label', 'teacherAssignmentObjectivesTypeOfAssignment');

  /**
   * Go to previous history entry
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.historyBack = function () {
    if (vm.historyBackClicked) {
      return;
    }

    if (NavigationHistory.getPreviousState()
      && NavigationHistory.getPreviousState().stateName.startsWith('teacherAssignments')) {
      MainMenu.saveAssignmentDetails(null);
    }

    vm.historyBackClicked = true;
    NavigationHistory.back();
  };

  /**
   * Set default grade system if there is no assigned yet
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.setDefaultGradeSystem = function () {
    if (vm.resultOnly) {
      return;
    }

    const gradeSystemId = TeacherSetAssignmentObjectivesService.getStoredGradeSystemId();
    let preSelectedGradeSystem;
    let selectedGradeSystem;
    let defaultGradeSystem;

    vm.gradeSystems.forEach((gradeSystem) => {
      if (gradeSystemId === gradeSystem.id) {
        preSelectedGradeSystem = gradeSystem;
      }

      if (vm.assignment.grade_system === gradeSystem.id) {
        selectedGradeSystem = gradeSystem;
      }

      if (gradeSystem.is_main && !defaultGradeSystem) {
        defaultGradeSystem = gradeSystem;
      }
    });

    // Resolution of selection:
    // Previously selected > First system with is_main selected > first system in list > none
    vm.scaleSelect = (selectedGradeSystem || preSelectedGradeSystem || defaultGradeSystem || vm.gradeSystems[0] || {});
    vm.scaleSelect.is_chosen = true;
    vm.lastSelectedScaleId = vm.scaleSelect.id;
    vm.state.scale.previous = vm.scaleSelect;
    vm.scaleSelect.grade_texts_present = get(vm, 'scaleSelect.grade_texts_present', true);
  };

  vm.isSaveButtonDisabled = () => vm.state.type.isEmpty || !vm.getConfirmationType().length;

  /**
   * Create list of selected objectives which is used to restore objectives
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.createListOfSelected = function () {
    vm.selectedObjectives = vm.courseDefinitions
      .filter((el) => el.is_chosen)
      .map((el) => el.id);
  };

  vm.getSelectedObjectivesId = () => vm.courseDefinitions
    .reduce((acc, courseDefinition) => [...acc, ...courseDefinition.objectives], [])
    .filter((objective) => objective.is_chosen)
    .map((objective) => objective.id);

  vm.getFlatObjectivesCounters = () => vm.courseDefinitions.reduce((accCD, courseDefinitionElem) => [...accCD, ...courseDefinitionElem.objectives
    .reduce((accO, objectiveElem) => {
      if (objectiveElem.is_chosen) {
        return [...accO, ...objectiveElem.max_counter_grades];
      }

      return accO;
    }, [])], []);

  /**
   * Check if it is init scale select and return if it is
   * Otherway close all objectives and run valid changes
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.changeScale = function () {
    vm.courseDefinitions.forEach((courseDefinitionElem) => {
      courseDefinitionElem.objectives.forEach((objectiveElem) => {
        // Close all objectives
        objectiveElem.open = false;

        // If selected scale is result only,
        // uncheck all objectives and clear max counter grades
        if (vm.scaleSelect === 'result_only') {
          objectiveElem.is_chosen = false;
          objectiveElem.max_counter_grades = [];
        }
      });
    });

    vm.validChanges();
  };

  /**
   * Toggle objective and run valid changes
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.toggleObjective = function (objective) {
    objective.is_chosen = !objective.is_chosen;

    vm.validChanges();
  };

  /**
   * Valid counters and set Changes to Save flag
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.validChanges = function () {
    vm.changesToSave = false;
    let isSelectedObjective = false;
    let validCounters = true;
    const countersTest = /^[0-9]{1,2}$/;

    // If selected scale is result only, set changesToSave to true and return
    if (vm.scaleSelect === 'result_only') {
      vm.changesToSave = true;
      return;
    }

    vm.courseDefinitions.forEach((courseDefinitionElem) => {
      courseDefinitionElem.objectives.forEach((objectiveElem) => {
        if (!objectiveElem.is_chosen) {
          return;
        }

        isSelectedObjective = true;

        objectiveElem.max_counter_grades.forEach((counterElem) => {
          if (!countersTest.test(counterElem)) {
            validCounters = false;
          }
        });
      });
    });

    // Return if there is no selected scale or if there is no selected objective
    if (!vm.scaleSelect || !isSelectedObjective) {
      return;
    }

    // Check changesToSave
    if (vm.scaleSelect.is_counter && validCounters) {
      vm.changesToSave = true;
    } else if (!vm.scaleSelect.is_counter) {
      vm.changesToSave = true;
    }
  };

  /**
   * Save changes - if any objective is removed than display confirmation
   * modal other way just save changes
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.saveChanges = function () {
    let removedItems = false;

    vm.courseDefinitions.forEach((courseDefinitionElem) => {
      courseDefinitionElem.objectives.forEach((objectiveElem) => {
        vm.selectedObjectives.forEach((selectedObjectiveElem) => {
          if (selectedObjectiveElem === objectiveElem.id && !objectiveElem.is_chosen) {
            removedItems = true;
          }
        });
      });
    });

    if (vm.scaleSelect) {
      TeacherSetAssignmentObjectivesService.setStoredGradeSystemId(vm.scaleSelect.id);
    }

    vm.assignConfirmation();
    Mixpanel.track('assigned_objectives');
  };

  /**
   * Create settings and send changes to API for assigned objectives to assignment
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.assignObjectives = Debounce(() => {
    const settings = {
      grade_system: vm.scaleSelect.id,
      assignment_objectives: [],
      result_only: vm.scaleSelect === 'result_only',
      studybee_type: vm.selectedType.type,
      enable_grade_texts: get(vm, 'assignment.enable_grade_texts'),
      show_grade_texts_for_student: get(vm, 'assignment.show_grade_texts_for_student'),
    };

    vm.courseDefinitions.forEach((courseDefinitionElem) => {
      courseDefinitionElem.objectives.forEach((objectiveElem) => {
        if (!objectiveElem.is_chosen) {
          return;
        }

        // Create temporary object
        const selectedObjective = {
          objective: objectiveElem.id,
          max_counter_grades: objectiveElem.max_counter_grades,
        };

        // Fill empty values with 0
        for (let i = 0; i <= 9; i++) {
          if (!selectedObjective.max_counter_grades[i]) {
            selectedObjective.max_counter_grades[i] = 0;
          }
        }

        settings.assignment_objectives.push(selectedObjective);
      });
    });

    FetchData.patchAssignedObjectivesToAssignment(assignment.id, settings)
      .then(() => {
        Updates.updateAssignments(courseId, true)
          .then(() => {
            vm.changesToSave = false;
            vm.lastSelectedScaleId = vm.scaleSelect.id;
            vm.createListOfSelected();
            vm.assignSuccess();
            AssignmentsStore.setHasCache(false);
            vm.isFirstTime = false;
          });
      });
  }, 500);

  /**
   * Show assign confirm modal
   * CAUTION: Display warning message only when user change grade system.
   * In other cases data is NOT deleted but set into inactive/archived mode.
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.assignConfirmation = function (confirmationType = vm.getConfirmationType()) {
    if (!vm.isFirstTime && confirmationType.includes(AssignConfirmationType.GRADE_SYSTEM)) {
      $mdDialog.show({
        parent: angular.element(document.querySelectorAll('[md-component-id="studybee"]')),
        templateUrl: 'assignments/teacherSetAssignmentObjectives/views/dialogs/assign_confirmation.html',
        controller() {
          this.text = 'assignGradeConfirmationModalText';
          this.closeDialog = () => $mdDialog.hide();
          this.confirmChanges = () => {
            vm.assignObjectives();
            $mdDialog.hide();
          };
        },
        controllerAs: 'ac',
      });
    } else {
      vm.assignObjectives();
    }
  };

  /**
   * Show assign success modal
   * @memberof module:TeacherSetAssignmentObjectives/Controller
   */
  vm.assignSuccess = function () {
    // this is the part of the merix's RP and I'll rewrite it later
    if ($stateParams.hideExtensionOnAssign) {
      StudybeeHandle.studybeeClose();
      NavigationHistory.transitionTo('teacherAssignments');
      MainMenu.setSelectedMenuStateName('teacherAssignments');

      $mdToast.show({
        templateUrl: 'utils/views/toast.html',
        parent: $('body'),
        hideDelay: 3000,
        position: 'bottom right',
        controller() {
          this.information = getTranslation('savedObjectivesToAssignmentToast');
        },
        controllerAs: 'to',
      });
    } else {
      $mdDialog.show({
        parent: angular.element(document.querySelectorAll('[md-component-id="studybee"]')),
        templateUrl: 'assignments/teacherSetAssignmentObjectives/views/dialogs/assign_success.html',
        controller() {
          vm.setState();
          this.closeDialog = function () {
            $mdDialog.hide();
          };
        },
        controllerAs: 'as',
      });
    }
  };


  vm.init();
}
