/**
 * @module NavigationHistory/Service
 * @description
 *   Handle Navigation History.
 *
 *   Allow to add state and state parameters to history
 *   and wrap $state.go and $state.transitionTo with custom methods.
 *   Add possibility to update state parameters for current state
 *   and handle back button which allow to back to the previous state.
 * @param {Object} $state Angular $state object
 * @param {Object} Hint Hint service instance
 */

/*@ngInject*/
function NavigationHistory($state, Hint) {
  return {

    // Keep history states
    history: [],

    /**
     * Push state information to the history array
     * @memberof module:NavigationHistory/Service
     * @param {String} stateName Selected state name
     * @param {Object} stateParams State params to save
     */
    addToHistory(stateName, stateParams) {
      stateParams = stateParams || {};

      if (this.history.length > 1 &&
        this.history[this.history.length - 1].stateName === stateName) {
        this.history[this.history.length - 1].stateParams = {
          ...this.history[this.history.length - 1].stateParams,
          ...stateParams,
        };
      } else {
        this.history.push({
          stateName,
          stateParams,
        });
      }
    },

    getPreviousState() {
      if (this.history.length <= 1) return;

      return this.history[this.history.length - 2];
    },

    /**
     * Wrapper for $state.go() which push state information to the history array
     * @memberof module:NavigationHistory/Service
     * @param {String} stateName Selected state name
     * @param {Object} stateParams State params to save
     * @param {Boolean} ignoreHistory Ignore history flag
     */
    go(stateName, stateParams, ignoreHistory) {
      stateParams = stateParams || {};

      Hint.removeHint();

      $state.go(stateName, stateParams);
      if (!ignoreHistory) {
        this.addToHistory(stateName, stateParams);
      }
    },

    /**
     * Wrapper for $state.transitionTo() which push state information to the history array
     * @memberof module:NavigationHistory/Service
     * @param {String} stateName Selected state name
     * @param {Object} stateParams State params to save
     * @param {Boolean} ignoreHistory Ignore history flag
     */
    transitionTo(stateName, stateParams, ignoreHistory) {
      stateParams = stateParams || {};

      Hint.removeHint();

      $state.transitionTo(stateName, stateParams, { reload: true });
      if (!ignoreHistory) {
        this.addToHistory(stateName, stateParams);
      }
    },

    /**
     * Handle arrow back
     * @memberof module:NavigationHistory/Service
     */
    back(overwritePrams = {}) {
      if (this.history.length <= 1) {
        return;
      }

      this.history.pop();

      const state = this.history[this.history.length - 1];
      const stateName = state.stateName;
      const stateParams = Object.assign({}, state.stateParams, overwritePrams);

      Hint.removeHint();
      $state.go(stateName, stateParams);
    },

    /**
     * Handle reload current entry with current params
     * @memberof module:NavigationHistory/Service
     */
    reloadCurrent() {
      $state.reload();
    },

    /**
     * Update stateParams for last state
     * @memberof module:NavigationHistory/Service
     * @param {Object} stateParams State params to save
     */
    updateParams(stateParams) {
      const currentState = this.history[this.history.length - 1];
      Object.assign(currentState, { ...currentState, stateParams: currentState.stateParams || {} }, {stateParams});
    },

    /**
     * Get stateParams for current state
     * @return {Object} stateParams State params to save
     */
    getCurrentParams() {
      const currentParams = this.history.slice(-1);

      return currentParams.length ? Object.assign({}, currentParams[0].stateParams) : {};
    },
  };
}

module.exports = NavigationHistory;
