/**
 * @module Common/BaseClasses
 *
 * @description
 * This class is responsible for auto-unbinding watchers added
 * in the $onInit method. There is no need to do it each time in the same way
 *
 * @example
 *
 * class Example extends UnbindableCtrlBase {
 *     constructor($rootScope) {
 *         'ngInject';
 *
 *         super();
 *         this.$rootScope = $rootScope;
 *     }
 *
 *  $onInit() {
 *   this.boundEvents.push(
 *     this.$rootScope.$on(
 *       Events.UPDATED_COURSES,
 *       () => console.log('event'),
 *     ),
 *   );
 * }
 *
 */
export class UnbindableCtrlBase {
  constructor() {
    'ngInject';
    this.boundEvents = [];
  }

  $onDestroy() {
    this.boundEvents.forEach(unbindFn => unbindFn());
  }
}
