export const LoaderComponent = {
  bindings: {
    type: '@',
  },
  template: `
    <md-progress-linear
        flex="nogrow"
        class="sb-progress-linear"
        ng-class="$ctrl.loaderClass"
        ng-show="$root.showLoading()"
        md-mode="indeterminate">
    </md-progress-linear>
  `,
  controller: class {
    $onInit() {
      this.loaderClass = `sb-progress-linear-${this.type || 'single'}-toolbar`;
    }
  },
};
