import React, { useContext, useEffect, useState } from "react";
import AssignmentsCard from "./assignmentsCard";
import CourseObjectiveAssessment from "./courseObjectiveAssessment";
import { getGradeSystemValues, GlobalContext } from "../utils";
import Card from "../components/card";
import { colors, fonts } from "../theme";
import { getTranslation } from "../i18n";
import OptionsDropdown from "../components/optionsDropdown";
import { useWindowDimensions } from "../hooks";

const fields = `
    name
    courses {
      id
      name
      objectives{
        id
        name
        description
        knowledgeRequirements{
          description
          grade_id
        }
      }
      gradeSystem {
        name
        is_main
        is_final
        grades {
          id
          name
          is_passed
          color
          label
        }
      }
    }
    students{
      id
      first_name
      last_name
      courseAssessments{
        id
        courseId
        courseName
        courseObjectiveAssessments{
          name
          id
          feedback
          feedback_published
          private_note
          grade_published
          grade_id
          objective_id
        }
      }
    }`;

const CourseObjectiveOverview = (props) => {
  const { objective, student, course, onObjectiveSelected } = props;
  const context = useContext(GlobalContext);
  const { Api } = context;
  const [classroom, setClassroom] = useState();
  const { width } = useWindowDimensions();

  const getData = async () => {
    const response = await Api.getClassroom(fields, student.id);
    setClassroom(response?.data?.classroom);
  };

  useEffect(() => {
    getData();
  }, [student.id, objective.id]);

  const s = classroom?.students?.find((s) => s.id === student.id);
  const courseAssessments = s?.courseAssessments?.find(
    (a) => a.courseId === course.id
  );
  const assessment = courseAssessments?.courseObjectiveAssessments?.find(
    (co) => co.objective_id === objective.id
  );

  const cor = classroom?.courses?.find((c) => c.id === course.id);
  const gradeSystemValues = getGradeSystemValues(
    cor?.gradeSystem?.find((g) => g.is_main)
  );
  const defaultIndex = assessment?.grade_id
    ? gradeSystemValues.findIndex((g) => g.id === assessment?.grade_id)
    : 0;

  const options =
    cor?.objectives?.map((o) => ({
      id: o.id,
      name: o.name,
    })) || [];

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        position: "absolute",
        marginTop: "60px",
        overflow: "hidden",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
      }}
    >
      <div
        style={{
          height: "50px",
          backgroundColor: colors.greyDarker,
          display: "flex",
          alignItems: "center",
          padding: "16px",
          borderBottom: `1px solid ${colors.borderColor}`,
        }}
      >
        <span
          style={{
            ...fonts.XXLARGE,
            fontWeight: "400",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            width: `${width - 630}px`,
            lineHeight: "30px",
          }}
        >{`${getTranslation("dataForObjectiveAssessmentTitle")} ${
          objective?.name
        }`}</span>
        <div
          style={{
            width: "200px",
            position: "absolute",
            right: "16px",
            padding: "2px 12px 2px 12px",
            borderRadius: "4px",
            backgroundColor: colors.white,
            boxShadow: "0px 5px 10px rgba(0,0,0,0.1)",
          }}
        >
          <OptionsDropdown
            options={options}
            helpText={getTranslation(
              "courseProgressLearningObjectivesListLabel"
            )}
            value={objective.id}
            handleChange={(e) => {
              onObjectiveSelected(
                cor?.objectives?.find((o) => o.id === e.target.value)
              );
            }}
            style={{ marginBottom: "10px", backgroundColor: colors.white }}
          />
        </div>
      </div>
      <div style={{ padding: "16px", overflow: "scroll", height: "100%" }}>
        <div style={{ width: "600px", marginBottom: "40px" }}>
          <span
            style={{
              ...fonts.LARGE,
              color: colors.brandColorPink,
              marginLeft: "16px",
            }}
          >
            {getTranslation("courseGradeMode")}
          </span>
          <Card style={{ marginTop: "16px" }}>
            <CourseObjectiveAssessment
              assessment={assessment}
              defaultIndex={defaultIndex}
              id={assessment?.id}
              gradeSystemValues={gradeSystemValues}
              knowledgeRequirements={objective?.knowledgeRequirements}
            />
          </Card>
        </div>
        <AssignmentsCard
          style={{ minWidth: "501px" }}
          studentId={student.id}
          courseId={course.id}
          showObjectives
          selectedObjective={objective}
          contentWidth={584}
        />
      </div>
    </div>
  );
};
export default CourseObjectiveOverview;
