import get from 'lodash.get';
import { AssignmentType } from '../../shared/enums/assignmentType.enum';
import { TransitionHandler } from '../../shared/models/transitionHandler.model';
import { IUserInfoService } from '../../shared/services/userInfo/userInfo.service';
import { TransitionAuth } from '../../shared/types/transitionAuth.type';
import { AssignmentOrder } from '../../assignments/teacherAssignments/shared/enums/assignmentOrder';
import { registerOnBeforeTransition } from '../../utils/decorators/registerOnBeforeTransition.decorator';

export const routerConfig = ($transitionsProvider) => {
  'ngInject';

  @registerOnBeforeTransition($transitionsProvider)
  class GuardAuth extends TransitionHandler {
    public getCriteria(): any {
      return {
        to: (state: any): boolean => /^(student|teacher)-course-progress/.test(state.name),
      };
    }

    public onBefore(transition: any): TransitionAuth {
      const UserInfo: IUserInfoService = transition.injector().get('UserInfo');
      const transitionTo = transition.to();
      let transitionAuth: TransitionAuth = true;

      transitionTo.themeClass = 'sb-theme-purple';

      if (/^teacher-/.test(transitionTo.name) && UserInfo.isTeacher()) {
        transitionTo.role = 'teacher';
      } else if (/^student-/.test(transitionTo.name) && !UserInfo.isTeacher()) {
        transitionTo.role = 'student';
      } else {
        transition.abort();
        transitionAuth = transition.router.stateService.transitionTo('wrong_user');
      }

      return transitionAuth;
    }
  }

  @registerOnBeforeTransition($transitionsProvider)
  class RestoreState extends TransitionHandler {
    public getCriteria(): any {
      return {
        to: state => !!get(state, 'data.statePrefix', false) && /^(student|teacher)-course-progress/.test(state.name),
      };
    }

    public onBefore(transition: any): TransitionAuth {
      const LocalStorageService = transition.injector().get('LocalStorageService');
      const transitionTo = transition.to();
      const targetParams = transition.targetState().params();
      const statePrefix = get(transitionTo, 'data.statePrefix', '');
      let ordering = get(targetParams, 'ordering');
      let filters = get(targetParams, 'filters');
      let transitionAuth: TransitionAuth = true;

      if (!ordering && !filters) {
        const savedOrdering = LocalStorageService.get(`${statePrefix}Ordering`);
        const savedFilters = LocalStorageService.getArray(`${statePrefix}Filtering`);

        ordering = savedOrdering || get(transitionTo, 'params.ordering', AssignmentOrder.ASC_TYPE);
        filters = savedFilters || get(transitionTo, 'params.filters',
          [
            AssignmentType.WORK_UNIT,
            AssignmentType.STANDARD_ASSIGNMENT,
          ],
        );

        if (ordering && filters) {
          const params = Object.assign({}, targetParams, { ordering, filters });

          transitionAuth = transition.router.stateService.target(transitionTo, params);
        }
      }

      return transitionAuth;
    }
  }
};
