import angular from 'angular';
import { IBindingsOptions } from 'angular';

export const Output: (options?: IBindingsOptions) => (prototype: object, property: string | symbol) => void = (options?: IBindingsOptions): (prototype: object, property: string | symbol) => void => {
  return (prototype: object & { $$bindings?: any }, property: string | symbol): void => {
    const _options: IBindingsOptions = angular.merge({}, { alias: '', optional: false }, options);
    const binding: { [property: string]: string } = { [property]: `&${_options.optional ? '?' : ''}${_options.alias}` };
    let $$bindings: { [boundProperty: string]: string } = prototype.$$bindings || {};

    $$bindings = angular.merge({}, $$bindings, binding);
    Object.assign(prototype, { $$bindings });
  };
};
