import React, {memo} from "react";
import resultImage from '../images/png/result-done.png'
import feedbackImage from '../images/png/icon_feedback.png'
import {colors, fonts} from "../theme";
import noteImage from '../images/png/sb-ico-note.png'
import assessmentImage from '../images/png/sb-ico-self-assessment.png'
import {getTranslation} from "../i18n";
import Tooltip from "./tooltip";
import Icon from "./icon";
import PublishedIcon from "./publishedIcon";

const item = {
  display: "flex",
  width: "40px",
  height: "50px",
  border: "0px solid red",
  alignItems: "center",
  justifyContent: "center",
  flexDirection: "column",
};

const styles = {
  gradeContaiener: {...item, marginRight: "8px"},
  gridStyle: {display: "flex", alignItems: "center", justifyContent: "space-around"},
  feedbackIcon: {width: '18px', height: '16px', marginBottom: "4px", marginTop: '2px'},
  assessmentIcon: {width: "20px", height: "20px", marginBottom: "10px"},
  resultIcon: {marginBottom: "3px", height: "20px", width: "18px"},
  noteIcon: {height: "18px", width: "16px", marginBottom: "10px", marginRight: "10px"},
  iconContainer: {display: "flex", justifyContent: "center"},
  gradeText: {...fonts.LARGE, fontWeight: "800"},
};

const tt = getTranslation("overallFeedback");
const tt2 = getTranslation("teacherSelfAssessmentLabel");
const tt3 = getTranslation("resultsInput");
const tt4 = getTranslation("courseProgressAssessmentLabel");
const tt5 = getTranslation("overallPrivateNotesMode");

const GridSummaryItem = ({assessment, style}) => {
  return (
    <div style={{...styles.gridStyle, ...style}}>
      <Tooltip title={tt}>
        <div style={item}>
          <Icon file={feedbackImage} active={!!assessment.feedback} style={styles.feedbackIcon}/>
          <PublishedIcon published={assessment.feedback_published} active={!!assessment.feedback}/>
        </div>
      </Tooltip>
      <Tooltip title={tt2}>
        <div style={styles.iconContainer}>
          <Icon file={assessmentImage} active={!!assessment.self_assessment} style={styles.assessmentIcon}/>
        </div>
      </Tooltip>
      <Tooltip title={tt3}>
        <div style={item}>
          <Icon file={resultImage} active={!!assessment.result} style={styles.resultIcon}/>
          <PublishedIcon published={assessment.result_published} active={!!assessment.result}/>
        </div>
      </Tooltip>
      <Tooltip title={tt4}>
        <div style={{...styles.gradeContaiener, backgroundColor: assessment?.grade_color}}>
          <span style={{...styles.gradeText, color: assessment?.grade_color ? colors.white : colors.iconColorLight}}>
            {assessment.grade_name || "-"}
          </span>
          {assessment.grade_name && (
            <PublishedIcon published={assessment.grade_published} active={true} white/>
          )}
        </div>
      </Tooltip>
      <Tooltip title={tt5}>
        <div style={styles.iconContainer}>
          <Icon file={noteImage} active={!!assessment.private_note} style={styles.noteIcon}/>
        </div>
      </Tooltip>
    </div>
  );
};

export default GridSummaryItem;
