export const CounterGradeSummary = {
  bindings: {
    assessments: '<',
  },
  controller: class {
    constructor(GradeSystems) {
      'ngInject';

      this.GradeSystems = GradeSystems;
    }

    $onChanges() {
      this.counterGradesSummarized = {};
      // get only assessments with counter grade system
      const assessments = this.assessments
        .filter(_assessment => _assessment.grade_system.is_counter);
      // summarize all grades, gruped by the grade system
      assessments.forEach((assessment) => {
        const id = assessment.grade_system.id;
        const gradeSystem = this.GradeSystems.getGradeSystemById(id);
        const gradesCount = gradeSystem.grades.length;
        // move to the next if counter_grades are null ( this situation might happen
        // to the student when grade is not visible
        if (!assessment.counter_grades) {
          return;
        }

        const values = assessment.counter_grades.slice(0, gradesCount);
        const maxValues = assessment.grade_system.max_counter_grades.slice(0, gradesCount);

        if (id in this.counterGradesSummarized) {
          const data = this.counterGradesSummarized[id];
          data.values = this.summarizeArrays(data.values, values);
          data.maxValues = this.summarizeArrays(data.maxValues, maxValues);
        } else {
          this.counterGradesSummarized[id] = {
            values,
            maxValues,
            labels: gradeSystem.grades.map(grade => grade.circle_label),
          };
        }
      });

      // switch it to the array ( it's not possible to use Object in the angular's templates
      this.counterGradesSummarized = Object.values(this.counterGradesSummarized);
    }

    summarizeArrays(array1, array2) {
      const sum = new Array(array1.length);
      // eslint-disable-next-line no-restricted-syntax
      for (const id in Object.values(array1)) {
        // fixing null + null == 0 error
        if (array1[id] === null && array2[id] === null) {
          sum[id] = null;
        } else {
          sum[id] = array1[id] + array2[id];
        }
      }
      return sum;
    }
  },
  //language=HTML
  template: `
<md-list-item
  ng-repeat="counterGrade in $ctrl.counterGradesSummarized">
  <p
    flex
    class="sb-list-item-name sb-list-item-name--blue sb-list-truncate-with-arrow"
    ng-if="$first">
    <span localize>
      __MSG_courseProgressECACounterSummaryLabel__
    </span>
  </p>

  <div flex
       class="sb-course-assessment__meta
            sb-course-assessment__meta--sm">

        <span class="sb-course-assessment__meta-grade
        sb-course-assessment__meta-grade--sm
        sb-course-assessment__meta-item
        sb-course-assessment__meta-grade--expanded
        sb-course-assessment__meta-item--sm">
            <counter-grade-preview
              ng-repeat="_ in counterGrade.maxValues track by $index"
              label="counterGrade.labels[$index]"
              grade="counterGrade.values[$index]"
              max-grade="counterGrade.maxValues[$index]">
            </counter-grade-preview>
        </span>
    <span class="sb-course-assessment__meta-item sb-course-assessment__meta-item--sm"></span>
  </div>
</md-list-item>
  `,
};
