import React, {
  useContext, useEffect, useRef, useState,
} from 'react';
import {colors, fonts} from '../theme';
import AssignmentStudentList from './assignmentStudentList';
import AssignmentsListHeader from './assignmentsListHeader';
import AssignmentObjectivesList from './assignmentObjectivesList';
import AssignmentItem from '../components/assignmentItem';
import {GlobalContext, sortAssessmentsByStudentName} from '../utils';
import {getTranslation} from '../i18n';
import FullscreenProgress from '../components/fullscreenProgress';
import {getAssignmentAlternateLinkId, getFileId} from '../path';
import TopicItem from '../components/topicItem';
import {ReactComponent as SortIcon} from '../images/sort.svg';
import EditAssessmentCriteria from './editAssignment/editAssessmentCriteria';
import AccordionItem from '../components/accordionItem';
import NotConnectedItem from '../components/notConnectedItem';
import HideAssignmentsDialog from './hideAssignmentsDialog';
import Tooltip from "../components/tooltip";

export const classroomFields = `
        name
        assignments {
          modified
          created
          connected
          use_submitted_status
          title
          state
          worktype
          id
          is_topic
          topic_order
          topic_id
          alternate_link_id
          courseDefinitions {
            id
            name
          }
          gradeSystem {
            id
            name
            is_counter
            order
            grades {
              id
              name
              is_passed
              color
              label
              circle_label
              order
            }
          }
          assessments {
            id
            student_id
            first_name
            last_name
            email
          }
          assignments {
            title
            id
            connected
            use_submitted_status
            modified
            created
            state
            worktype
            is_topic
            topic_order
            topic_id
            alternate_link_id
            courseDefinitions {
              id
              name
            }
            gradeSystem {
              id
              name
              is_counter
              order
              grades {
                id
                name
                is_passed
                color
                label
                circle_label
                order
              }
            }
            assessments {
              id
              student_id
              first_name
              last_name
              email
            }
          }
        }`;

const AssignmentList = () => {
  const context = useContext(GlobalContext);
  const {
    Api, height, isStudent, sortingMethod, assignmentSortMethod, setAssignmentSortMethod, studybeeOpen
  } = context;
  const [assignmentSelected, setAssignmentSelected] = useState();
  const [studentSelected, setStudentSelected] = useState();
  const [editAssessmentCriteria, setEditAssessmentCriteria] = useState();
  const [classroom, setClassroom] = useState();
  const [showHideAssignmentsDialog, setHowHideAssignmentsDialog] = useState(false);
  const lastStorageChange = useRef();

  const [loading, setLoading] = useState(true);
  const fetchAssignments = async () => {
    const response = await Api.getClassroom(classroomFields);
    setClassroom(response?.data?.classroom);
    setLoading(false);
    return response?.data?.classroom;
  };

  const assignments = classroom?.assignments || [];
  const routeWithLinkId = (courseId = null) => {
    const link = courseId || getAssignmentAlternateLinkId();
    if (!link) {
      return;
    }
    const allAssignments = assignments
      .map((a) => [a, ...(a.assignments || [])])
      .flat();
    const assignment = allAssignments.find((a) => a.alternate_link_id === link);
    if (assignment.connected) {
      setAssignmentSelected(assignment);
    }
  };

  const studentClicked = async (data, assignment, openGrid = false) => {
    const {student} = data;
    const promises = [];
    promises.push(
      Api.getClassroom(`
    courseDefinitions {
      id
      name
      course_id
      objectives{
        id
        name
        description
      }
    }`),
    );

    promises.push(
      Api.getAssignmentWithAssessment(
        assignment.id,
        `
    gradeSystem {
      id
      name
      is_counter
      is_main
      is_final
      grades{
        id
        name
        is_passed
        color
        label
      }
    }`,
      ),
    );
    const [resp1, resp2] = await Promise.all(promises);
    const {courseDefinitions} = resp1?.data?.classroom;
    const {gradeSystem} = resp2?.data?.assignment[0];
    const params = {
      openGrid,
      studentId: student?.student_id,
      assignment,
      assessment: assignment?.assessments.find(
        (a) => a.student_id === student?.student_id,
      ),
      courseDefinitions,
      gradeSystem,
      historyBack: () => {
        setStudentSelected(null);
        fetchAssignments();
      },
      students: assignment?.assessments,
    };
    setStudentSelected(params);
  };

  let listener;
  const processStorageChange = async (change) => {
    if (change?.studentSelected) {
      const link = getAssignmentAlternateLinkId();
      const allAssignments = assignments
        .map((a) => [a, ...(a.assignments || [])])
        .flat();
      const assignment = allAssignments.find(
        (a) => a.alternate_link_id === link,
      );
      if (!assignment.connected) {
        return;
      }
      const {assessments} = assignment;
      const student = assessments.find(
        (a) => `${a.first_name} ${a.last_name}` === change.studentSelected,
      );
      studentClicked({student}, assignment);
      chrome.storage.sync.set({studentSelected: 0});
    } else if (change?.assignmentSelected) {
      setStudentSelected(null);
      routeWithLinkId(change?.assignmentSelected);
      chrome.storage.sync.set({assignmentSelected: 0});
    }
    if (change?.editAssignment) {
      const newClassroom = await fetchAssignments();
      const assignmentId = change.editAssignment;
      const allAssignments = newClassroom?.assignments
        .map((a) => [a, ...(a.assignments || [])])
        .flat();
      const assignment = allAssignments.find((a) => a.id === assignmentId);
      setEditAssessmentCriteria(assignment);
      chrome.storage.sync.set({editAssignment: 0});
    }
    if (change?.connectNewAssignment) {
      studybeeOpen();
      const title = change.connectNewAssignment;
      chrome.storage.sync.set({connectNewAssignment: 0});
      const assignment = await Api.newAssignment(title);
      const newClassroom = await fetchAssignments();
      const assignmentId = assignment.id;
      const allAssignments = newClassroom?.assignments
        .map((a) => [a, ...(a.assignments || [])])
        .flat();
      const assignmentFromDb = allAssignments.find((a) => a.id === assignmentId);
      setEditAssessmentCriteria(assignmentFromDb);
    }
    if (change?.disconnectNewAssignment) {
      const title = change.disconnectNewAssignment;
      chrome.storage.sync.set({disconnectNewAssignment: 0});
      const assignment = await Api.newAssignment(title);
      const newClassroom = await fetchAssignments();
      const assignmentId = assignment.id;
      const allAssignments = newClassroom?.assignments
        .map((a) => [a, ...(a.assignments || [])])
        .flat();
      const assignmentFromDb = allAssignments.find((a) => a.id === assignmentId);
      await Api.updateAssignment({...assignmentFromDb, connected: false});
      await fetchAssignments();
    }
  };

  const processStorage = async () => {
    chrome.storage.sync.get(
      [
        'editAssignment',
        'assignmentDriveSelected',
        'studentSelected',
        'assignmentSelected',
        'connectNewAssignment',
        'disconnectNewAssignment',
      ],
      (storage) => {
        processStorageChange(storage);
      },
    );
  };

  const storageListener = async (change) => {
    const changes = Object.keys(change).map((key) => {
      const obj = {};
      obj[key] = change[key].newValue;
      return obj;
    });
    const [firstChange] = changes;
    if (firstChange !== lastStorageChange.current) {
      lastStorageChange.current = firstChange;
      await processStorageChange(firstChange);
    }
  };

  useEffect(() => {
    if (!assignments.length) {
      return;
    }
    if (listener) {
      return;
    }
    if (!process.env.REACT_APP_STANDALONE) {
      chrome.storage.sync.get(['assignmentDriveSelected'], async (selected) => {
        if (selected?.assignmentDriveSelected !== 0) {
          const fileId = await getFileId();
          if (!fileId) {
            return;
          }
          const temp = (await Api.getAssignmentDrive(fileId))[0];
          const allAssignments = assignments
            .map((a) => [a, ...(a.assignments || [])])
            .flat();
          const assignment = allAssignments.find(
            (a) => a.id === temp.assignment,
          );
          if (!assignment.connected) {
            return;
          }
          chrome.storage.sync.set({assignmentDriveSelected: 0});
          studentClicked(
            {student: {student_id: temp.student_id}},
            assignment,
          );
        }
      });

      processStorage();
      chrome.storage.onChanged.addListener(storageListener);
    }
    return function cleanup() {
      if (!process.env.REACT_APP_STANDALONE) {
        chrome.storage.onChanged.removeListener(storageListener);
      }
    };
  }, [assignments]);

  useEffect(() => {
    fetchAssignments();
  }, []);

  useEffect(() => {
    if (!showHideAssignmentsDialog) {
      fetchAssignments();
    }
  }, [showHideAssignmentsDialog]);

  const handleAssignmentMenuClick = async (assignment, {name}) => {
    switch (name) {
      case 'DISCONNECT':
        if (assignment.is_topic && assignment?.assignments?.filter((a) => a.connected).length > 0) {
          setHowHideAssignmentsDialog(assignment);
          return;
        }
        await Api.updateAssignment({...assignment, connected: false});
        await fetchAssignments();
        break;
      case 'CONNECT':
        await Api.updateAssignment({...assignment, connected: true});
        await fetchAssignments();
        break;
      case 'ASSESSMENT_CRITERIA':
        setEditAssessmentCriteria(assignment);
        break;
      default:
        break;
    }
  };

  const hideAllAssignments = async () => {
    const topic = showHideAssignmentsDialog;
    Api.updateAssignment({...topic, connected: false});
    await Promise.all(topic.assignments.filter((a) => a.connected).map((a) => Api.updateAssignment({
      ...a,
      connected: false,
    })));
    setHowHideAssignmentsDialog(false);
  };

  const assignmentClicked = async (assignment) => {
    if (isStudent) {
      studentClicked({student: assignment.assessments[0]}, assignment);
      return;
    }
    if (assignment.assignment_not_assessable) {
      setEditAssessmentCriteria(assignment);
    }
    setAssignmentSelected(assignment);
  };

  const openGridView = async (topic) => {
    const sortedAssessments = sortAssessmentsByStudentName(
      topic.assessments,
      sortingMethod,
    );
    studentClicked({student: sortedAssessments[0]}, topic, true);
  };

  if (editAssessmentCriteria) {
    return (
      <EditAssessmentCriteria
        assignment={editAssessmentCriteria}
        onClose={() => {
          fetchAssignments();
          setEditAssessmentCriteria(null);
          setAssignmentSelected(null);
        }}
      />
    );
  }

  if (studentSelected) {
    return <AssignmentObjectivesList {...studentSelected} />;
  }
  if (assignmentSelected) {
    return (
      <AssignmentStudentList
        assignment={assignmentSelected}
        back={() => {
          fetchAssignments();
          setAssignmentSelected(null);
        }}
      />
    );
  }

  const empty = assignments.length === 0;

  const sortByCreate = (a, b) => {
    const ad = new Date(a.created);
    const bd = new Date(b.created);
    return bd - ad;
  };

  let assignmentsNotInTopics = assignments
    .filter((a) => !a.is_topic)
    .sort(sortByCreate);
  const topics = assignments
    .filter((a) => a.is_topic)
    .sort((a, b) => a.topic_order - b.topic_order);

  if (assignmentSortMethod === 'alpha') {
    assignmentsNotInTopics = assignmentsNotInTopics.sort((a, b) => a.title.localeCompare(b.title));
  }
  const sortedAssignments = [...assignmentsNotInTopics, ...topics];
  let notConnected = [...assignmentsNotInTopics, ...topics.map((a) => a.assignments), ...topics].flat().filter((a) => !a.connected).filter(a => {
    if (a.is_topic) {
      const anyConnectedAssignments = a.assignments.filter(aa => aa.connected).length > 0;
      if (anyConnectedAssignments) {
        return false;
      }
    }
    return true;
  });
  if (notConnected.length === 0) {
    notConnected = [{text: getTranslation('noNotConnectedItemsPlaceholder'), placeholder: true}]
  }

  return (
    <div>
      <AssignmentsListHeader title={getTranslation('assignmentsPage')}/>
      {loading && empty && <FullscreenProgress/>}
      <div style={{display: 'flex', justifyContent: 'flex-end', borderBottom: `1px solid ${colors.borderColor}`}}>
        <Tooltip
          title={assignmentSortMethod === 'alpha' ? getTranslation('standardSortTooltip') : getTranslation('sortAssignmentAlphabetically')}
        >
          <div style={{margin: '6px 12px 4px 0px'}}>
            <SortIcon
              style={{
                height: '25px',
                cursor: 'pointer',
                transform: `rotate(${assignmentSortMethod === 'alpha' ? 0 : 180}deg)`,
              }}
              fill={colors.darkGrey}
              onClick={async () => {
                setAssignmentSortMethod(assignmentSortMethod === 'alpha' ? null : 'alpha');
              }}
            />
          </div>
        </Tooltip>
      </div>
      <div style={{height: height - 100, overflow: 'scroll'}}>
        {empty && !loading && (
          <div style={{margin: '20px'}}>
            <span style={{...fonts.LARGE}}>
              {getTranslation('assignmentOverviewEmptyList')}
            </span>
          </div>
        )}
        {sortedAssignments.map((assignment) => {
          let {connected} = assignment;
          if (assignment.is_topic) {
            connected = assignment.assignments.filter((a) => a.connected).length > 0 || assignment.connected;
            if (!connected) {
              return null;
            }
          }

          return (
            <div key={assignment.id}>
              {assignment.is_topic && (
                <TopicItem
                  isStudent={isStudent}
                  assignment={assignment}
                  onAssignmentClicked={assignmentClicked}
                  onGridClicked={openGridView}
                  handleAssignmentMenuClick={handleAssignmentMenuClick}
                  editAssessmentCriteria={editAssessmentCriteria}
                />
              )}
              {(!assignment.is_topic && connected) && (
                <AssignmentItem
                  isStudent={isStudent}
                  assignment={assignment}
                  onAssignmentClicked={assignmentClicked}
                  handleAssignmentMenuClick={handleAssignmentMenuClick}
                  editAssessmentCriteria={editAssessmentCriteria}
                  onGridClicked={openGridView}
                />
              )}
            </div>
          );
        })}
        {!isStudent
        && <AccordionItem
          summary={(
            <div style={{height: '50px', alignItems: 'center', display: 'flex'}}>
              <span style={{...fonts.MEDIUM, color: colors.topic}}>{getTranslation('notConnectedToStudyBee')}</span>
            </div>
          )}
          details={(
            <div style={{display: 'flex', flexDirection: 'column', width: '100%'}}>
              {notConnected.map((a) => {
                if (a.placeholder) {
                  return <span style={{
                    ...fonts.MEDIUM,
                    backgroundColor: colors.white,
                    padding: '10px 10px 10px 14px'
                  }}>{a.text}</span>
                }
                return (
                  <NotConnectedItem
                    assignment={a}
                    handleAssignmentMenuClick={handleAssignmentMenuClick}
                    topic={topics.find((t) => t.id === a.topic_id)}

                  />
                );
              })}
            </div>
          )}
        />
        }
      </div>
      <HideAssignmentsDialog
        open={showHideAssignmentsDialog}
        no={async () => {
          await Api.updateAssignment({...showHideAssignmentsDialog, connected: false});
          setHowHideAssignmentsDialog(false);
        }}
        yes={hideAllAssignments}/>
    </div>
  );
};

export default AssignmentList;
