import { IComponentController, ITimeoutService } from 'angular';
import { ListViewType } from '../../../../../shared/enums/listViewType.enum';
import { IListViewMenuItem, ListViewMenuItem } from '../../../../../shared/models/listViewMenuItem.model';
import { IObjectiveAssignmentAssessment } from '../../../../../shared/models/objectiveAssignmentAssessment.model';
import { ListViewMenuService } from '../../../../../shared/services/listViewMenu/listViewMenu.service';
import { Component } from '../../../../../utils/decorators/component.decorator';
import { Input } from '../../../../../utils/decorators/input.decorator';
import TeacherCourseProgressModule, { IAssignmentOverviewObjectivesListService } from '../../teacher-course-progress.module';
import style from './assignmentOverviewObjectivesList.scss';
import template from './assignmentOverviewObjectivesList.tpl.html';

@Component({
  module: TeacherCourseProgressModule,
  selector: 'assignment-overview-objectives-list',
  template: template,
  style: style,
})
export class AssignmentOverviewObjectivesListComponent implements IComponentController {
  @Input()
  public objectiveAssessment: IObjectiveAssignmentAssessment;
  @Input()
  public objectives: any[];
  @Input() isGradeTextsEnable: boolean;

  private readonly storageId: string;
  public readonly listViewType: any;
  public readonly toolbarMenuItems: ListViewMenuItem[];
  public view: ListViewType;

  constructor(private AssignmentOverviewObjectivesListService: IAssignmentOverviewObjectivesListService, private ListViewMenuService: ListViewMenuService,
              private $timeout: ITimeoutService) {
    'ngInject';

    this.objectiveAssessment = null;
    this.objectives = [];
    this.isGradeTextsEnable = false;
    this.storageId = 'teacherAssignmentOverviewObjectivesList';
    this.listViewType = ListViewType;
    this.toolbarMenuItems = ListViewMenuService.getListViewMenu(this.storageId);
    this.view = ListViewType.TITLE_AND_DESCRIPTION;
  }

  public $onInit(): void {
    this.view = this.toolbarMenuItems
      .find((listViewMenuItem: IListViewMenuItem): boolean => listViewMenuItem.checked)
      .eventArgs.view;
  }

  public onClickClose(objective: any): void {
    objective.open = false;
  }

  public onUpdateAssessment(changedObjective: any): void {
    this.AssignmentOverviewObjectivesListService.updateObjectiveAssessments(changedObjective)
      .then((newObjective: any): void => {
        this.objectives = this.objectives.map((objective: any): any => {
          // Dont overwrite feedback and private_note, it messes with the ui
          const { feedback, private_note, ...rest } = newObjective;
          return objective.id === newObjective.id ?
            {...objective, ...rest} :
            objective;
        });
      });
  }

  public onClickMenuItem(toolbarMenuItem: IListViewMenuItem): void {
    this.toolbarMenuItems.forEach((menuItem: any): void => {
      menuItem.checked = false;
    });
    toolbarMenuItem.checked = true;
    this.ListViewMenuService.saveListViewMenu(this.storageId, this.toolbarMenuItems);
    this.changeView(toolbarMenuItem.eventArgs);
  }

  public expandAll(): void {
    this.changeOpenStateForAll(true);
  }

  public collapseAll(): void {
    this.changeOpenStateForAll(false);
  }

  private changeOpenStateForAll(state): void {
    this.$timeout(() => {
      this.objectives = this.objectives.map((objective: any): any => {
        objective.open = state;

        return objective;
      });
    }, 0);
  }

  private changeView(eventArgs: { [arg: string]: any }): void {
    this.view = eventArgs.view;
  }
}
