const AssignmentsCard = (props) => {
  const {
    studentId,
    courseId,
    showObjectives = false,
    selectedObjective = null,
    noTitle = false,
    contentWidth = null,
    style = {},
  } = props;
  const context = useContext(GlobalContext);
  const {Api, networkTraffic} = context;
  const [classroom, setClassroom] = useState();
  const [assignments, setAssignments] = useState([]);
  const saving = useRef(false);
  const meSaving = useRef(false);

  const getClassroom = async () => {
    const response = await Api.getClassroom(fields);
    setClassroom(response?.data?.classroom);
    let {assignments: ass} = response?.data?.classroom;
    ass = filterAssignmentsOnCourseId(ass, courseId);
    if (selectedObjective) {
      ass = filterAssignmentsOnObjective(ass, selectedObjective?.id);
    }
    setAssignments(ass);

  };

  // A little bit hacky, reload classroom if something has been saved
  useEffect(() => {
    if (networkTraffic === "POST" || networkTraffic === "PUT") {
      saving.current = true;
    } else if (saving.current) {
      if (!meSaving.current) {
        getClassroom();
      }
      meSaving.current = false;
      saving.current = false;
    }
  }, [networkTraffic]);

  useEffect(() => {
    getClassroom();
  }, [selectedObjective?.id, studentId]);

  let firstItem = false;
  return (
    <div style={{marginTop: "36px"}}>
      {!noTitle && assignments?.length > 0 && (
        <span
          style={{
            ...fonts.LARGE,
            color: colors.brandColorPink,
            marginLeft: "16px",
          }}
        >
          {getTranslation("topicsAndAssignments")}
        </span>
      )}
      <div style={{display: "flex"}}>
        <div
          style={{
            marginTop: noTitle ? "-12px" : "16px",
            boxShadow: "0px 5px 10px rgba(0,0,0,0.1)",
          }}
        >
          {assignments
            ?.sort((a, b) => a.title.localeCompare(b.title))
            .map((assignment) => {
              const isConnected = assignment.connected || assignment?.assignments?.filter(a => a.connected).length > 0;
              if(!isConnected){
                return null;
              }
              const assessment = assignment.assessments.find(
                (a) => a.student_id === studentId
              );

              if (!firstItem && assessment) {
                firstItem = assessment.id;
              }
              return (
                <div style={{...style}} key={assignment.id}>
                  <AssignmentAccordionItem
                    onSave={() => {
                      meSaving.current = true;
                    }}
                    firstItem={assessment?.id === firstItem}
                    contentWidth={contentWidth}
                    selectedObjective={selectedObjective}
                    assessment={assessment}
                    assignment={assignment}
                    showObjectives={showObjectives}
                    objectiveAssessments={assignment?.objectiveAssessments.filter(
                      (oa) => oa.student_id === studentId
                    )}
                    objectives={
                      classroom?.courseDefinitions.find(
                        (c) => c.id === courseId
                      )?.objectives
                    }
                    courseDefinitions={classroom?.courseDefinitions.filter(
                      (cd) => cd.id === courseId
                    )}
                  />
                  {assignment.assignments?.filter(a => a.connected)?.map((as) => {
                    const ass = as.assessments.find(
                      (a) => a.student_id === studentId
                    );
                    return (
                      <AssignmentAccordionItem
                        onSave={() => {
                          meSaving.current = true;
                        }}
                        contentWidth={contentWidth}
                        selectedObjective={selectedObjective}
                        indented
                        key={as.id}
                        assessment={ass}
                        assignment={as}
                        showObjectives={showObjectives}
                        objectiveAssessments={as?.objectiveAssessments.filter(
                          (oa) => oa.student_id === studentId
                        )}
                        objectives={
                          classroom?.courseDefinitions.find(
                            (c) => c.id === courseId
                          )?.objectives
                        }
                        courseDefinitions={classroom?.courseDefinitions.filter(
                          (cd) => cd.id === courseId
                        )}
                      />
                    );
                  })}
                </div>
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default AssignmentsCard;
