import React, {memo, useEffect, useState} from 'react';
import {colors, fonts} from '../theme';
import {ReactComponent as GoToAssignmentIcon} from '../images/collapse.svg';
import {getAssessmentIcon, getStatusText, getSubmittedIcon, getSubmittedStatusText} from '../utils';
import GridSummaryItem from './gridSummaryItem';
import Tooltip from "./tooltip";

const header = {
  ...fonts.MEDIUM,
  fontWeight: '400',
  padding: '0px 10px 0px 10px',
  height: '30px',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
};

const submittedIcon = {
  height: '30px',
  width: '26px',
  marginLeft: '10px',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  padding: '0px 2px 0px 4px',
  borderLeft: `1px solid ${colors.borderColor}`,
};

const styles = {
  table: {
    boxShadow: '0px 5px 10px rgba(0,0,0,0.1)',
    minWidth: '180px',
    marginRight: '10px',
    height: '100%',
  },
  tableHeaderPublished: {
    ...header,
    backgroundColor: colors.green,
    color: colors.white,
    borderTopLeftRadius: '3px',
    borderTopRightRadius: '3px',
  },
  tableHeaderDraft: {
    ...header,
    backgroundColor: colors.draft,
    color: colors.white,
    borderTopLeftRadius: '3px',
    borderTopRightRadius: '3px',
  },
  assignmentTitle: {
    color: colors.white,
    width: '140px',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    marginRight: '10px',
  },
  assessmentIcon: {maxHeight: '18px'},
  submittedIconSelected: {
    ...submittedIcon,
    borderLeft: `1px solid ${colors.borderColorDark}`,
  },
  summeryContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-around',
    padding: '0px 6px 0px 6px',
  },
  assignmentTitleContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  noAssessment: {
    cursor: 'pointer',
    height: '52px'
  },
  summeryContainerInner: {
    height: '30px',
    width: '1px',
    margin: '0px 4px 0px 4px'
  },
  tr: {
    cursor: 'pointer',
    height: '50px'
  },
  assessmentIconContainer: {
    width: '20px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
};

const AssignmentTable = (props) => {
  const {
    setHighlight,
    studentClicked,
    assignment,
    onAssignmentClicked,
    selectedStudents,
    highlight,
    selectedStudentId,
    style,
    students,
  } = props;

  const [assessmentMap, setAssessmentMap] = useState({});

  useEffect(() => {
    setAssessmentMap(assignment.assessments.reduce((acc, a) => {
      acc[a.student_id] = a;
      return acc;
    }, {}));
  }, [assignment]);

  return (
    <table style={{...styles.table, ...style}} key={assignment.id}>
      <tbody>
      <tr>
        <th style={assignment.state === 'PUBLISHED' ? styles.tableHeaderPublished : styles.tableHeaderDraft}>
          <div
            onClick={() => {
              if (onAssignmentClicked) {
                onAssignmentClicked(assignment);
              }
            }}
            style={{...styles.assignmentTitleContainer, cursor: onAssignmentClicked ? 'pointer' : 'default'}}
          >
            <span style={styles.assignmentTitle}>
              {assignment.title}
            </span>
            {onAssignmentClicked && <GoToAssignmentIcon fill={colors.white}/>}
          </div>
        </th>
      </tr>
      {students.map((studentId, i) => {
        const assessment = assessmentMap[studentId];
        const bgColor = i === highlight ? colors.grey : colors.white;
        const selected = studentId === selectedStudentId || selectedStudents.find((id) => id === studentId);
        if (!assessment) {
          return (
            <tr
              key={studentId}
              style={{...styles.noAssessment, backgroundColor: selected ? colors.borderColor : bgColor}}>
              <td
                onMouseEnter={() => setHighlight(i)}
                onMouseLeave={() => setHighlight(null)}
                onClick={() => studentClicked(studentId)}
              />
            </tr>
          );
        }

        return (
          <tr style={{...styles.tr, backgroundColor: selected ? colors.borderColor : bgColor}} key={studentId}>
            <td
              onMouseEnter={() => setHighlight(i)}
              onMouseLeave={() => setHighlight(null)}
              onClick={() => studentClicked(assessment.student_id)}
            >
              <div style={styles.summeryContainer}>
                <Tooltip title={getStatusText(assessment.status_new)}>
                  <div style={{
                    ...styles.assessmentIconContainer,
                    marginRight: assignment.use_submitted_status ? '0px' : '6px'
                  }}>
                    {getAssessmentIcon({status: assessment.status_new, style: styles.assessmentIcon})}
                  </div>
                </Tooltip>
                {assignment.use_submitted_status &&
                <Tooltip title={getSubmittedStatusText(assessment.submitted_status)}>
                  <div style={{...submittedIcon, borderLeft: `1px solid ${selected?colors.borderColorDark:colors.borderColor}`}}>
                    {getSubmittedIcon({status: assessment.submitted_status})}
                  </div>
                </Tooltip>
                }
                <div
                  style={{
                    ...styles.summeryContainerInner,
                    backgroundColor: selected ? colors.borderColorDark : colors.borderColor
                  }}/>
                <GridSummaryItem assessment={assessment}/>
              </div>
            </td>
          </tr>
        );
      })}
      </tbody>
    </table>
  );
};

export default memo(AssignmentTable);
