import {Injectable} from '@angular/core';
import {EntityUIQuery, QueryEntity} from '@datorama/akita';
import {AssignmentsState, AssignmentsStore} from './assignments.store';
import {map} from 'rxjs/operators';
import {Assignment, AssignmentSortFn, AssignmentUI} from './assignment.model';
import sort from 'fast-sort';

@Injectable({providedIn: 'root'})
export class AssignmentsQuery extends QueryEntity<AssignmentsState> {
  ui: EntityUIQuery<AssignmentUI>;

  constructor(protected store: AssignmentsStore) {
    super(store);
    this.createUIQuery();
  }

  selectFilteredAndSorted() {
    return this.selectAll({filterBy: assignment => this.filterBy(assignment)}).pipe(
      map(assignments => this.sortBy(assignments)),
    );
  }

  protected filterBy(assignment: Assignment): boolean {
    const stateUI = this.getValue().ui;
    return stateUI.filters.type.includes(assignment.assignment_type) &&
      stateUI.filters.state.includes(assignment.state);
  }

  protected sortBy(assignments: Assignment[]): Assignment[] {
    const stateUI = this.getValue().ui;
    const sortMethod = this.getSortMethod(stateUI.sort.by);

    return sort([...assignments])[stateUI.sort.order](sortMethod);
  }

  protected getSortMethod(by: keyof Assignment): keyof Assignment | AssignmentSortFn {
    switch (by) {
      default:
        return by;
    }
  }

}
