import angular, { IComponentController, IQService, IRootScopeService, ITimeoutService } from 'angular';
import { StateService } from '@uirouter/core';
import { Events } from '../../../common/global-events';
import { APICurriculumObject } from '../../../common/types/api_types';
import { UnsubscribableComponent } from '../../../shared/components/abstracts/unsubscribable.component';
import { IObserver, Observer } from '../../../shared/services/helpers/observer.service';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import { ICurriculumsService } from '../common/services/curriculums.service';
import { showCarriculumLibraryDialog, showEditCurriculumDialog, showSearchCurriculumDialog } from './shares/helpers.dialog';
import TeacherCourseManagementModule from './teacherCourseManagement.module';
import style from './teacherCourseManagement.scss';
import template from './teacherCourseManagement.tpl.html';

@Component({
  module: TeacherCourseManagementModule,
  selector: 'teacher-course-management',
  template: template,
  style: style,
})
export class TeacherCourseManagementComponent extends UnsubscribableComponent implements IComponentController {

  @Input()
  public curriculums: APICurriculumObject[];

  constructor(private $rootScope: IRootScopeService, private $q: IQService, private $timeout: ITimeoutService,
              private $cookies: ng.cookies.ICookiesService, private $state: StateService, private $mdDialog: angular.material.IDialogService,
              private CurriculumsService: ICurriculumsService, private FetchData: any, private Path: any) {
    'ngInject';

    super();
    this.curriculums = [];
  }

  public $onInit(): void {
    const curriculumObserver: IObserver<APICurriculumObject[]> =
      new Observer<APICurriculumObject[]>(this.onNext.bind(this));

    this.$$unsubscribes.push(this.CurriculumsService.curriculums$.subscribe(curriculumObserver));
  }

  public openLibraryDialog(): void {
    showCarriculumLibraryDialog(this.$q, this.$mdDialog, this.FetchData);
  }

  public openSearchForCurriculumDialog(): void {
    this.FetchData.getCourseSetup(this.Path.getClassroomId())
      .then((courseSettings: any): any =>
        showSearchCurriculumDialog(
          this.$mdDialog, this.FetchData, this.Path,
          courseSettings.educational_system, this.isLocked(),
        ),
      )
      .then((selectedCourse: any): any => this.FetchData
        .v3
        .addCourseDefinitionToClassroom(
          this.Path.getClassroomId(),
          selectedCourse,
        ))
      .then((): void => {
        this.$rootScope.$emit(Events.UPDATED_COURSES);
      });
  }

  public openCreateCurriculumDialog(): void {
    this.FetchData.getCourseSetup(this.Path.getClassroomId())
      .then((courseSettings: any): any =>
        showEditCurriculumDialog(
          this.$mdDialog, this.FetchData, this.Path, null,
          courseSettings.educational_system, courseSettings.educational_stage,
          this.isLocked(),
        ),
      )
      .then((courseData: any): any => this.FetchData
        .v3
        .createCourseDefinition(this.Path.getClassroomId(), courseData)
        .then((): void => {
          this.$rootScope.$emit(Events.UPDATED_COURSES);
        }),
      );
  }

  public isLocked() {
    return this.curriculums.length > 0;
  }

  private onNext(curriculums: APICurriculumObject[]): void {
    this.curriculums = curriculums;
  }

}
