import { AssignmentOrder } from '../../../assignments/teacherAssignments/shared/enums/assignmentOrder.ts';
import { AssignmentType } from '../../../shared/enums/assignmentType.enum.ts';

export const routerConfig = ($stateProvider) => {
  'ngInject';

  $stateProvider
  // right panel, with students' list
    .state('teacher-course-progress', {
      resolve: {
        students: (FetchData, Path) => {
          'ngInject';

          return FetchData.getStudentsList(Path.getClassroomId());
        },
        courses: (FetchData, Path) => {
          'ngInject';

          return FetchData.v3.getCourseDefinitions(Path.getClassroomId());
        },
      },
      views: {
        '@': {
          component: 'teacherCourseProgress',
        },
      },
    })
    .state('teacher-course-progress.details', {
      params: {
        course: null,
        student: null,
        courseDefinition: null,
      },
      resolve: {
        courseSelected: ($stateParams) => {
          'ngInject';

          return $stateParams.course;
        },
        courseAssessment: (FetchData, $stateParams) => {
          'ngInject';

          if (!$stateParams.course) {
            return {};
          }

          return FetchData.v3.getCourseDefinitionAssessment(
            $stateParams.student.id,
            $stateParams.course.course_definition,
          );
        },
      },
      views: {
        'extended@': {
          component: 'studentDetails',
        },
      },
    })
    .state('teacher-course-progress.details.no-courses', {
      views: {
        'details@teacher-course-progress.details': {
          component: 'noCourses',
        },
      },
    })
    // learning objectives list, user's information
    .state('teacher-course-progress.details.courses-overview', {
      params: {
        course: null,
        student: null,
      },
      views: {
        'extended@': {
          component: 'studentCoursesDetails',
        },
      },
    })
    // bottom panel with learning objectives data
    .state('teacher-course-progress.details.learning-objectives', {
      params: {
        course: null,
        student: null,
        courseDefinition: null,
        breadcrumbs: [],
        ordering: AssignmentOrder.ASC_TYPE,
        filters: [
          AssignmentType.WORK_UNIT,
          AssignmentType.STANDARD_ASSIGNMENT,
        ],
      },
      data: {
        statePrefix: 'StudentProgressStudentDetailsAssignmentsAssignments',
      },
      resolve: {
        assessments: (FetchData, $stateParams) => {
          'ngInject';

          return FetchData.v3.getCourseDefinitionObjectiveAssessments(
            $stateParams.student.id,
            $stateParams.courseDefinition.course_definition,
          );
        },
        objectiveAssignmentAssessments: (FetchData, $stateParams, courseAssessment, AssignmentService) => {
          'ngInject';

          const requestConfig = {
            ordering: $stateParams.ordering,
          };

          return FetchData.v3
            .getAssignmentAssessment(
              $stateParams.student.id,
              courseAssessment.course_definition,
              requestConfig,
            )
            .then(assignments =>
              AssignmentService.filterAssignmentsByTypes(assignments, $stateParams.filters),
            );
        },
      },
      views: {
        'details@teacher-course-progress.details': {
          component: 'objectivesListTeacher',
        },
        'assignments@teacher-course-progress.details': {
          component: 'assignmentsOverview',
        },
      },
    })
    .state('teacher-course-progress.details.learning-objectives.assignmentOverview', {
      params: {
        course: null,
        student: null,
        courseDefinition: null,
        objectiveAssessment: null,
        breadcrumbs: [],
      },
      resolve: {
        objectiveAssessment: (objectiveAssignmentAssessments, $stateParams, $q) => {
          'ngInject';

          let objectiveAssessment = $stateParams.objectiveAssessment;

          if ($stateParams.student.id !== $stateParams.objectiveAssessment.student_id) {
            objectiveAssessment = objectiveAssignmentAssessments
              .find((objectiveAssignmentAssessment) =>
                objectiveAssignmentAssessment.assignment_id ===
                $stateParams.objectiveAssessment.assignment_id,
              );
          }

          return objectiveAssessment ? $q.resolve(objectiveAssessment) : $q.reject(null);
        },
        objectives: (FetchData, $stateParams) => {
          'ngInject';

          return FetchData.v3.getListObjectiveAssessments(
            $stateParams.student.id,
            $stateParams.courseDefinition.course_definition,
            $stateParams.objectiveAssessment.assignment_id,
          );
        },
        isGradeTextsEnable: (FetchData, $stateParams) => {
          'ngInject';

          return FetchData
            .getAssignedObjectivesToAssignment($stateParams.objectiveAssessment.assignment_id)
            .then(response => response.enable_grade_texts);
        }
      },
      views: {
        'extended@': {
          component: 'assignmentOverviewDetails',
        },
        'details@teacher-course-progress.details.learning-objectives.assignmentOverview': {
          component: 'assignmentOverviewObjectivesList',
        },
      },
    })

    // ---------------------------------------------------------------

    .state('teacher-course-progress.objective-details', {
      params: {
        objective: null,
        course: null,
        student: null,
        breadcrumbs: [],
      },
      resolve: {
        student: ($stateParams) => {
          'ngInject';

          return $stateParams.student;
        },
        course: ($stateParams) => {
          'ngInject';

          return $stateParams.course;
        },
        objectives: (FetchData, course) => {
          'ngInject';

          return FetchData.v3.getCourseDefinitionObjectives(course.course_definition);
        },
        objectiveSelected: ($stateParams, objectives) => {
          'ngInject';

          return objectives.find(objective => objective.id === $stateParams.objective.id);
        },
        objectiveAssessment: (
          FetchData,
          objectiveSelected,
          course,
          student,
        ) => {
          'ngInject';

          return FetchData.v3.getCourseDefinitionObjectiveAssessment(
            student.id,
            course.course_definition,
            objectiveSelected.id,
          );
        },
      },
      views: {
        'extended@': {
          component: 'objectiveDetails',
        },
      },
    })
    .state('teacher-course-progress.objective-details.learning-assignments', {
      params: {
        objective: null,
        course: null,
        student: null,
        breadcrumbs: [],
        ordering: AssignmentOrder.ASC_TYPE,
        filters: [
          AssignmentType.WORK_UNIT,
          AssignmentType.STANDARD_ASSIGNMENT,
        ],
      },
      data: {
        statePrefix: 'StudentProgressLearningObjectiveAssignments',
      },
      resolve: {
        assessments: (FetchData, objectiveSelected, student) => {
          'ngInject';

          return FetchData.v3.getObjectiveAssessments(student.id, objectiveSelected.id);
        },
        assignments: (objectiveSelected, student, AssignmentAssessmentService, $stateParams, AssignmentService) => {
          'ngInject';

          const requestConfig = {
            ordering: $stateParams.ordering,
          };

          return AssignmentAssessmentService
            .getAssignmentAssessmentByObjective(student.id, objectiveSelected.id, requestConfig)
            .then(assignmentsAssessments =>
              AssignmentService
                .filterAssignmentsByTypes(assignmentsAssessments, $stateParams.filters),
            );
        },
        gradeSystem: (GradeSystems) => {
          'ngInject';

          return GradeSystems.fetchGradeSystemsList();
        },
      },
      views: {
        'details@teacher-course-progress.objective-details': {
          component: 'assignmentsList',
        },
      },
    });
};
