import React from 'react';
import {ArrowBack} from '@material-ui/icons';
import {colors, fonts} from '../theme';
import StudentDropDown from '../components/studentDropDown';
import LinearProgress from '../components/LinearProgress';
import {ReactComponent as GridIcon} from '../images/grid.svg';
import {getTranslation} from '../i18n';
import Tooltip from "../components/tooltip";

const AssignmentObjectivesListHeader = (props) => {
  const {
    back,
    students,
    title,
    selectedStudentId,
    onStudentSelected,
    isTopic,
    onToggleGrid,
    isStudent,
  } = props;
  return (
    <div
      style={{
        position: 'relative',
        width: '100%',
        height: '60px',
        display: 'flex',
        backgroundColor: isTopic ? colors.topic : colors.green,
        border: '0px solid blue',
        zIndex: 1000,
      }}
    >
      <div
        style={{
          display: 'flex',
          flex: 1,
          padding: '14px',
          border: '0px solid red',
        }}
      >
        <ArrowBack
          style={{
            cursor: 'pointer',
            color: colors.white,
            height: '25px',
            marginTop: '2px',
            marginRight: '4px',
          }}
          onClick={back}
        />
        {!isStudent
        && <StudentDropDown
          onSelect={onStudentSelected}
          students={students}
          defaultValue={selectedStudentId}
          label={title}
        />
        }
        {isStudent
        && <div style={{display: 'flex', flexDirection: 'column'}}>
          <span style={{...fonts.LARGE, color: colors.white}}>{students[0].first_name} {students[0].last_name}</span>
          <span style={{
            ...fonts.MEDIUM,
            color: colors.white,
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
            width:'300px',
          }}>{title}</span>
        </div>
        }

        {!isStudent
        && <div style={{
          position: 'absolute', top: '20px', right: '16px', display: 'flex', alignItems: 'center',
        }}>
          <div style={{cursor: 'pointer', alignItems: 'center', display: 'flex'}} onClick={onToggleGrid}>
            <Tooltip title={getTranslation('gridOverviewTooltip')}>
              <div>
                <GridIcon fill={colors.white}/>
              </div>
            </Tooltip>
          </div>
        </div>
        }

      </div>
      <div style={{
        position: 'absolute', bottom: 0, left: 0, right: 0,
      }}>
        <LinearProgress/>
      </div>
    </div>
  );
};

export default AssignmentObjectivesListHeader;
