export interface IColleagueEmailResponse {
  id: number;
  email: string;
  in_database: boolean;
}

export interface IColleagueEmail {
  id: number;
  email: string;
  isValid: boolean;
}

export class ColleagueEmail implements IColleagueEmail {
  id: number;
  email: string;
  isValid: boolean;

  constructor(colleagueEmail: IColleagueEmailResponse) {
    this.id = colleagueEmail.id;
    this.email = colleagueEmail.email;
    this.isValid = colleagueEmail.in_database;
  }
}
