import {ID} from '@datorama/akita';
import {InjectionToken} from '@angular/core';

export interface UserSetup {
  educational_system_setup?: ID;
  school: ID;
}

export interface User {
  readonly educational_system_setup?: ID;
  readonly email: string;
  readonly first_name: string;
  readonly first_time_setup: boolean;
  readonly gdpr_check: boolean;
  readonly google_id: string;
  readonly id: ID;
  readonly is_teacher: boolean;
  readonly last_name: string;
  readonly photo_url: string;
  readonly school?: ID;
  readonly school_name?: string;
}

export const PRE_FETCH_USER_DATA = new InjectionToken<User>('app.pre-fetch-user-data');
