/**
 * @module MainMenu/Service
 * @description
 *   Handle Main Menu.
 * @param {Object} Helpers Helpers service instance
 */

/*@ngInject*/
import { MainMenuID } from '../../../../../src/app/shared/states/main-menu/main-menu.interface';

export function MainMenu(UiService, MainMenuService, UserQuery) {
  'ngInject';

  const toggleCallbacks = [];
  const assignmentCallbacks = [];
  const selectedMenuStateNameCallbacks = [];

  return {
    // Keep selected menu state name
    selectedMenuStateName: null,

    selectedAssignment: null,

    selectedStudent: {
      name: null,
    },

    /**
     * Set callback which is trigger on toggle main menu
     * @memberof module:MainMenu/Service
     * @param {Object} callback Callback function
     */
    setToggleCallback(callback) {
      toggleCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger on set assignment
     * @memberof module:MainMenu/Service
     * @param {Object} callback Callback function
     */
    setAssignmentCallback(callback) {
      assignmentCallbacks.push(callback);
    },

    /**
     * Set callback which is trigger on set selected menu state name
     * @memberof module:MainMenu/Service
     * @param {Object} callback Callback function
     */
    setSelectedMenuStateNameCallback(callback) {
      selectedMenuStateNameCallbacks.push(callback);
    },

    /**
     * Trigger toggle main menu
     * @memberof module:MainMenu/Service
     */
    toggleMenu() {
      toggleCallbacks.forEach((elem) => elem());
      UiService.updateToggleMainMenuVisible();
    },

    /**
     * Save assignment details
     * @memberof module:MainMenu/Service
     * @param assignment Assignment object
     */
    saveAssignmentDetails(assignment) {
      this.selectedAssignment = assignment;
      this.updateMainMenu();
      assignmentCallbacks.forEach((elem) => elem());
    },

    /**
     * Save student details
     * @memberof module:MainMenu/Service
     * @param {String} studentName Selected student name
     */
    saveStudentDetails(studentName) {
      this.selectedStudent.name = studentName;
      this.updateMainMenu();
      assignmentCallbacks.forEach((elem) => elem());
    },

    /**
     * Save selected menu state name
     * @param {String} stateName
     */
    setSelectedMenuStateName(stateName) {
      if (!stateName) {
        this.selectedMenuStateName = UserQuery.getValue().is_teacher ? 'teacherAssignments' : 'studentAssignments';
      } else {
        this.selectedMenuStateName = stateName;
      }

      selectedMenuStateNameCallbacks.forEach((elem) => elem());
    },

    /**
     * Is stateName selected
     * @memberof module:MainMenu/Service
     * @param {String} stateName Selected state name
     * @return {Boolean}
     */
    isStateNameSelected(stateName) {
      if (!this.selectedMenuStateName) {
        return this.selectedMenuStateName === stateName;
      }

      // This is some type of workaround for not consistent
      // states names and sill state checking
      const stateNameRoot = this.selectedMenuStateName.split('.')[0];

      return stateNameRoot === stateName || this.selectedMenuStateName === stateName;
    },

    updateMainMenu() {
      const isTeacher = UserQuery.getValue().is_teacher;

      if (isTeacher) {
        if (this.selectedStudent && this.selectedStudent.name) {
          // MainMenuService.update(MainMenuID.ASSIGNMENT, {
          //   state: 'teacher_assess_assignment_student',
          //   params: {
          //     studentName: this.selectedStudent.name
          //   }
          // });
        } else if (this.selectedAssignment) {
          MainMenuService.update(MainMenuID.ASSIGNMENT, {
            state: 'teacher_assess_assignment',
            params: {
              assignment: this.selectedAssignment,
            },
          });
        } else {
          MainMenuService.update(MainMenuID.ASSIGNMENT, {
            state: 'teacherAssignments',
            params: null,
          });
        }
      } else if (this.selectedAssignment) {
        MainMenuService.update(MainMenuID.ASSIGNMENT, {
          state: 'studentAssessed',
          params: null,
        });
      } else {
        MainMenuService.update(MainMenuID.ASSIGNMENT, {
          state: 'studentAssignments',
          params: null,
        });
      }
    },
  };
}
