/**
 * @module UserInfo/Service
 * @description
 *   Handle User Info - keep information about user
 *   and allow to check user matched, user role and user config.
 * @param {Object} Courses Courses service instance
 * @param {Object} Path Path service instance
 */

export class UserInfo {
  constructor(Courses, Path, UserQuery) {
    'ngInject';

    this.Courses = Courses;
    this.Path = Path;
    this.userMatched = null;
    this.userConfigured = null;
    this.userId = null;
    this.userEmail = null;
    this.userRole = null;
    this.loggedToBrowser = null;
    this.hasCreateCoursePermission = false;
    this.agreementSeen = false;
    this.hasSetup = null;
    this.student_name = null;
    this.firstName = '';
    this.lastName = '';
    this.photoUrl = '';
    this.googleId = '';
    this.gdpr = false;

    UserQuery.user$.subscribe(user => {
      this.applyDetails(user);
    });
  }

  applyDetails(userDetails) {
    this.student_name = `${userDetails.first_name} ${userDetails.last_name}`;
    this.first_name = userDetails.first_name || '';
    this.last_name = userDetails.last_name || '';
    this.photo_url = userDetails.photo_url || '';
    this.google_id = userDetails.google_id || '';
    this.gdpr = userDetails.gdpr_check || false;
    this.userId = userDetails.id || 0;
    this.userRole = userDetails.is_teacher ? 'teacher' : 'student';
    this.hasCreateCoursePermission = userDetails.is_teacher;
  }

  /**
   * Check if user logged in to Chrome and user logged in to Classroom/Drive is the same user
   * @memberof module:UserInfo/Service
   */
  checkUser() {
    const userEmail = this.userEmail.toLowerCase();
    let classroomStructure;
    let driveStructure;

    if (this.Path.isClassroomInstance()) {
      classroomStructure = this.Path.classroomNode[0].innerHTML.toLowerCase();
      if (userEmail.length && classroomStructure.indexOf(userEmail) >= 0) {
        this.userMatched = true;
      }
    } else if (this.Path.isDriveInstance()) {
      if (this.Path.driveNode[0].attributes['aria-label']) {
        driveStructure = this.Path.driveNode[0].attributes['aria-label'].value.toLowerCase();
      } else {
        driveStructure = this.Path.driveNode[0].innerHTML.toLowerCase();
      }

      if (userEmail.length && driveStructure.indexOf(userEmail) >= 0) {
        this.userMatched = true;
      }
    }
  }

  /**
   * Check user role.
   * @memberof module:UserInfo/Service
   * @param {Number} courseId Selected course ID
   */
  checkRole(courseId) {
    if (this.Path.isClassroomInstance()) {
      this.Courses.list.forEach((elem) => {
        if (elem.id === courseId && elem.role === 'teacher') {
          this.userRole = 'teacher';
        } else if (elem.id === courseId && elem.role !== 'teacher') {
          this.userRole = 'student';
        } else if (!courseId) {
          this.userRole = null;
        }
      });
    }
  }

  setUserRole(role) {
    this.userRole = role;
  }

  /**
   * Check if user config specific course
   * @memberof module:UserInfo/Service
   * @param {Number} courseId Selected course ID
   */
  checkConfig(courseId) {
    if (this.Path.isClassroomInstance()) {
      this.Courses.list.forEach((elem) => {
        if (elem.id === courseId) {
          this.userConfigured = elem.configured;
        } else if (!courseId) {
          this.userConfigured = null;
        }
      });
    }
  }

  isTeacher() {
    return this.userRole ? this.userRole === 'teacher' : this.hasCreateCoursePermission;
  }

  getUserObj() {
    const { Courses, Path, ...student } = this;

    student.id = this.id;
    student.email = this.email;

    return student;
  }

  get id() {
    return this.userId;
  }

  get email() {
    return this.userEmail;
  }
}
