import {Visibility, VisibilityOff} from '@material-ui/icons';
import React, {memo} from "react";
import colors from "../theme/colors";


const PublishedIcon = ({published, active, white = false, size = 12}) => {
  let iconStyle = {color: colors.black};
  iconStyle.width = `${size}px`;
  iconStyle.height = `${size}px`;
  if(published){
    iconStyle.color = colors.green;
  }
  if (white) {
    iconStyle.color = colors.white;
  }

  if (published) {
    return <Visibility style={iconStyle}/>
  } else {
    return <VisibilityOff style={iconStyle} opacity={active ? 0.8 : 0.1}/>
  }
};

export default memo(PublishedIcon);