import React, { useContext, useState } from 'react';
import {
  Button,
  CircularProgress,
  Dialog,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import StudybeeSelector from './studybeeSelector';
import Header from './header';
import Card from './card';
import FeedbackInput from './feedbackInput';
import { getGradeSystemValues, GlobalContext } from '../utils';
import { getTranslation } from '../i18n';
import { colors, fonts } from '../theme';
import AssessmentSlider from './assessmentSlider';

const overAllText = getTranslation('overallResultHeaderTeacher');
const resultText = getTranslation('resultsInput');
const overallFeedbackText = getTranslation('overallFeedback');
const overallPrivateNoteText = getTranslation('overallPrivateNoteInput');
const objectiveFeedbackInputText = getTranslation('objectiveFeedbackInput');
const objectivePrivateNoteText = getTranslation('objectivePrivateNoteInput');

const BatchAssessmentDialog = (props) => {
  const context = useContext(GlobalContext);
  const { Api } = context;

  const {
    open, onClose, students, courses, gradeSystem, assignment,
  } = props;
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [selectedObjectives, setSelectedObjectives] = useState([]);
  const [result, setResult] = useState();
  const [feedback, setFeedback] = useState();
  const [note, setNote] = useState();
  const [grade, setGrade] = useState();
  const [objectiveFeedback, setObjectiveFeedback] = useState();
  const [objectiveNote, setObjectiveNote] = useState();
  const [saving, setSaving] = useState(false);

  const overallAssessmentAvailable = selectedStudents.length > 0;
  const objectiveAssessmentAvailable = selectedObjectives.length > 0;

  if (!students || !gradeSystem) {
    return null;
  }
  const handleClose = () => {
    onClose();
  };

  const handleSave = async () => {
    setSaving(true);
    const objectives = (
      await Promise.all(
        selectedStudents.map((studentId) => Api.getObjectiveAssessments(assignment.id, studentId)),
      )
    )
      .map((s) => s
        .filter((o) => selectedObjectives.find((id) => id === o.objective))
        .map((o) => ({
          ...o,
          feedback: objectiveFeedback || o.feedback,
          private_note: objectiveNote || o.private_note,
          grade_id: grade?.id || o.grade_id,
          grade_name: grade?.name || o.grade_name,
        })))
      .flat();

    const assessments = (
      await Promise.all(
        selectedStudents.map((studentId) => Api.getAssignmentAssessment(assignment.id, studentId)),
      )
    ).map((assessment) => ({
      ...assessment,
      feedback: feedback || assessment.feedback,
      result: result || assessment.result,
      private_note: note || assessment.private_note,
    }));

    await Api.updateAssignmentAssessment(assignment.id, assessments);

    await Api.updateObjectiveAssessmentsGql(objectives);
    setSaving(false);
    handleClose();
  };

  const gradeSystemValues = getGradeSystemValues(gradeSystem);

  const objectives = courses.map((c) => c.objectives).flat();
  return (
    <Dialog onClose={handleClose} open={open}>
      <div
        style={{
          width: '550px',
          padding: '20px',
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <span
          style={{
            ...fonts.XLARGE,
            width: '100%',
            padding: '0px 0px 0px 0px',
            marginBottom: '20px',
          }}
        >
          {getTranslation('batchAssessmentDialogTitle')}
        </span>
        <StudybeeSelector
          selectedItems={selectedStudents}
          onSelect={(select) => {
            setSelectedStudents(select);
            if (select.length === 0) {
              setSelectedObjectives([]);
            }
          }}
          items={students.map((s) => ({
            id: s.student_id,
            label: s.student_name,
          }))}
          title={getTranslation('students')}
          inputName={getTranslation('students').toLowerCase()}
        />
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
          }}
        >
          <Header
            title={overAllText}
            style={{
              backgroundColor: overallAssessmentAvailable
                ? colors.green
                : colors.darkGrey,
            }}
          />
          <Card style={{ margin: '1px 2px 20px 2px' }}>
            <FeedbackInput
              readonly={!overallAssessmentAvailable}
              placeholder={resultText}
              onTextChange={(text) => setResult(text)}
              hideEye
            />
            <FeedbackInput
              readonly={!overallAssessmentAvailable}
              placeholder={overallFeedbackText}
              onTextChange={(text) => setFeedback(text)}
              hideEye
            />
            <FeedbackInput
              readonly={!overallAssessmentAvailable}
              placeholder={overallPrivateNoteText}
              hideEye
              onTextChange={(text) => setNote(text)}
            />
          </Card>

          <StudybeeSelector
            selectedItems={selectedObjectives}
            enabled={overallAssessmentAvailable}
            onSelect={(selected) => setSelectedObjectives(selected)}
            items={objectives.map((o) => ({ id: o.id, label: o.name }))}
            title={getTranslation('objectivesText')}
            inputName={getTranslation('objectivesText').toLowerCase()}
          />

          <Header
            title={getTranslation('objectivesText')}
            style={{
              backgroundColor: objectiveAssessmentAvailable
                ? colors.green
                : colors.darkGrey,
            }}
          />
          <Card style={{ margin: '1px 2px 1px 2px' }}>
            {/*<span*/}
            {/*  onClick={() => showObjectiveDetails(objective.id)}*/}
            {/*  style={{ ...fonts.MEDIUM, cursor: 'pointer' }}*/}
            {/*>*/}
            {/*  {objective.name}*/}
            {/*</span>*/}
            <AssessmentSlider
              showPublish={false}
              disabled={!objectiveAssessmentAvailable}
              values={gradeSystemValues}
              defaultIndex={0}
              onChangeCommit={(index) => {
                const selectedGrade = gradeSystem.grades.find((g) => g.name === gradeSystemValues[index].name);
                setGrade(selectedGrade);
              }}
            />
            <FeedbackInput
              readonly={!objectiveAssessmentAvailable}
              placeholder={objectiveFeedbackInputText}
              onTextChange={(text) => setObjectiveFeedback(text)}
              hideEye
            />
            <FeedbackInput
              placeholder={objectivePrivateNoteText}
              readonly={!objectiveAssessmentAvailable}
              onTextChange={(text) => setObjectiveNote(text)}
              hideEye
            />
          </Card>
        </div>
        <Button style={{ marginTop: '10px' }} onClick={handleSave}>
          <span style={fonts.LARGE}>{getTranslation('saveBtn').toUpperCase()}</span>
        </Button>
        <CloseIcon
          onClick={handleClose}
          style={{
            position: 'absolute',
            top: '20px',
            right: '20px',
            cursor: 'pointer',
          }}
        />
      </div>
      {saving && (
        <div
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: '#00000050',
            alignItems: 'center',
            justifyContent: 'center',
            display: 'flex',
            flexDirection: 'column',
          }}
        >
          <CircularProgress size={100} />
        </div>
      )}
    </Dialog>
  );
};
export default BatchAssessmentDialog;
