import { IPromise, IQService, IRootScopeService } from 'angular';
import { StateParams } from '@uirouter/core';
import { Events } from '../../../../../common/global-events';
import { Service } from '../../../../../utils/decorators/service.decorator';
import TeacherCourseProgressModule from '../../teacher-course-progress.module';

export interface IAssignmentOverviewObjectivesListService {
  updateObjectiveAssessments(objective): IPromise<any>;
}

@Service({
  module: TeacherCourseProgressModule,
  name: 'AssignmentOverviewObjectivesListService',
})
export class AssignmentOverviewObjectivesListService implements IAssignmentOverviewObjectivesListService {
  constructor(private $rootScope: IRootScopeService, private $q: IQService,
              private FetchData: any, private $stateParams: StateParams) {
    'ngInject';
  }

  public updateObjectiveAssessments(objective): IPromise<any> {
    this.notifyRequestStart();
    return this.FetchData.v3
      .updateObjectiveAssessmentsByCourseId(
        this.$stateParams.student.id,
        this.$stateParams.course.course_definition,
        objective,
      )
      .then(this.notifyRequestSuccess.bind(this), this.notifyRequestFail.bind(this));
  }

  private notifyRequestStart(): void {
    this.$rootScope.$broadcast(Events.STARTED_NEW_REQUEST);
  }

  private notifyRequestSuccess(response: any): any {
    this.$rootScope.$broadcast(Events.REQUEST_FINISHED_SUCCESSFULLY);

    return response;
  }

  private notifyRequestFail(error: any): any {
    this.$rootScope.$broadcast(Events.REQUEST_FINISHED_FAILURE);

    return this.$q.reject(error);
  }
}
