import { StateParams, StateService } from '@uirouter/core';
import { IScope } from 'angular';
import xor from 'lodash.xor';
import { AssessmentSort } from '../../../../../../src/app/shared/models/assessment.model';
import { AssignmentOrder } from '../../../assignments/teacherAssignments/shared/enums/assignmentOrder';
import { StudentOrder } from '../../../assignments/teacherAssignments/shared/enums/studentOrder.enum';
import { AssignmentFilterGroup } from '../../enums/assignmentFilterGroup.enum';
import { AssignmentState } from '../../enums/assignmentStateType.enum';
import { AssignmentType } from '../../enums/assignmentType.enum';
import { ILocalStorageService } from '../../services/localStorage/localStorage.service';
import { UnsubscribableComponent } from '../abstracts/unsubscribable.component';

export abstract class AssignmentsComponent extends UnsubscribableComponent {

  public sortingItems: any[];
  public studentSortingItems: any[];
  public filters: any[];

  protected abstract readonly statePrefix: string;
  protected refreshStateName: string;

  constructor(protected $scope: IScope, protected $stateParams: StateParams, protected $state: StateService,
              protected LocalStorageService: ILocalStorageService) {
    'ngInject';

    super();
    this.sortingItems = [{
      order: AssignmentOrder.ASC_TYPE,
      label: 'assignmentsActionMenuSortByTypeAsc',
    }, {
      order: AssignmentOrder.ASC_CREATED,
      label: 'assignmentsActionMenuSortByCreationDateAsc',
    }, {
      order: AssignmentOrder.DESC_CREATED,
      label: 'assignmentsActionMenuSortByCreationDateDesc',
    }, {
      order: AssignmentOrder.DESC_LAST_CHANGE,
      label: 'assignmentsActionMenuSortByLastChanged',
    }, {
      order: AssignmentOrder.ASC_TITLE,
      label: 'assignmentsActionMenuSortByNameAsc',
    }];
    this.studentSortingItems = [
      {
        order: AssessmentSort.ASC_FIRST_NAME,
        label: 'studentsActionMenuSortByFirstNameAsc',
      }, {
        order: AssessmentSort.ASSESSMENT_DONE,
        label: 'studentsActionMenuSortByAssessmentDoneAsc',
      }, {
        order: AssessmentSort.NOT_ASSESSED,
        label: 'studentsActionMenuSortByNotAssessedAsc',
      },
    ];
    this.filters = [
      {
        group: AssignmentFilterGroup.TYPE,
        type: AssignmentType.WORK_UNIT,
        label: 'assignmentActionMenuFilterWorkUnit',
      },
      {
        group: AssignmentFilterGroup.TYPE,
        type: AssignmentType.STANDARD_ASSIGNMENT,
        label: 'assignmentActionMenuFilterAssignments',
      },
      {
        group: AssignmentFilterGroup.TYPE,
        type: AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
        label: 'assignmentActionMenuFilterWithoutAssessmentCriterias',
      },
      {
        group: AssignmentFilterGroup.STATE,
        type: AssignmentState.PUBLISHED,
        label: 'publishedStatus',
      },
      {
        group: AssignmentFilterGroup.STATE,
        type: AssignmentState.DRAFT,
        label: 'draftsStatus',
      },
    ];
    this.refreshStateName = this.$state.current.name;
  }

  public $onInit(): void {
    this.$$unsubscribes.push(
      this.$scope.$on('$mdMenuClose', this.onFilterMenuClose.bind(this)),
    );
  }

  public isCurrentOrder(ordering: AssignmentOrder): boolean {
    return ordering === this.$stateParams.ordering;
  }

  public isSortAssessmentSelected(assessmentSort: AssessmentSort) {
    return true; // this.AppAssignmentsService.isSortAssessmentSelected(assessmentSort);
  }

  public isFilterSelected(filter: AssignmentType, group: AssignmentFilterGroup): boolean {
    return group === AssignmentFilterGroup.TYPE ?
      this.$stateParams.filters.includes(filter) : this.$stateParams.states.includes(filter);
  }

  public onClickOrder(ordering: AssignmentOrder): void {
    this.reloadState({ ordering });
  }

  public sortAssessments(assessmentSort: AssessmentSort): void {
    // this.AssignmentsService.updateSort({ assessments: assessmentSort });
  }

  public onClickFilter(filter: AssignmentType & AssignmentState, group: AssignmentFilterGroup): void {
    if (group === AssignmentFilterGroup.TYPE) {
      let filters: AssignmentType[] = this.$stateParams.filters;

      if (filters.includes(filter)) {
        filters = filters.filter((_filter: AssignmentType): boolean => _filter !== filter);
      } else {
        filters.push(filter);
      }

      Object.assign(this.$stateParams, { filters });
    } else if (group === AssignmentFilterGroup.STATE) {
      let states: AssignmentState[] = this.$stateParams.states;

      if (states.includes(filter)) {
        states = states.filter((_states: AssignmentState): boolean => _states !== filter);
      } else {
        states.push(filter);
      }

      Object.assign(this.$stateParams, { states });
    }
  }

  public isDividerVisible(index: number): boolean {
    return this.filters[index + 1] ? this.filters[index].group !== this.filters[index + 1].group : false;
  }

  protected reloadState(stateParams: { ordering?: AssignmentOrder, studentOrdering?: StudentOrder, states?: AssignmentState[], filters?: AssignmentType[] } = {}): void {
    const params: { ordering?: AssignmentOrder, states?: AssignmentState[], filters?: AssignmentType[] } = Object
      .assign({}, this.$stateParams, stateParams);

    this.saveFilters(params.filters);
    this.saveState(params.states);
    this.saveOrdering(params.ordering);
    this.$state.go(`${this.refreshStateName}`, params, { reload: true });
  }

  protected saveOrdering(ordering: AssignmentOrder): void {
    if (ordering) {
      this.LocalStorageService.set(`${this.statePrefix}AssignmentsOrdering`, `${ordering}`);
    }
  }

  protected saveState(states: AssignmentState[]): void {
    if (states) {
      this.LocalStorageService.setArray(`${this.statePrefix}AssignmentsStates`, states);
    }
  }

  protected saveFilters(filters: AssignmentType[]): void {
    if (filters) {
      this.LocalStorageService.setArray(`${this.statePrefix}AssignmentsFiltering`, filters);
    }
  }

  protected isFilterChange(): boolean {
    const currentFilters: AssignmentType[] = this.$stateParams.filters;
    const previousFilters: AssignmentType[] = this.LocalStorageService.getArray<AssignmentType>(`${this.statePrefix}AssignmentsFiltering`);

    return !!xor(previousFilters, currentFilters).length;
  }

  protected isStateChange(): boolean {
    const currentStates: AssignmentState[] = this.$stateParams.states;
    const previousStates: AssignmentState[] = this.LocalStorageService.getArray<AssignmentState>(`${this.statePrefix}AssignmentsStates`);

    return !!xor(previousStates, currentStates).length;
  }

  protected onFilterMenuClose(): void {
    const currentFilters: AssignmentType[] = this.$stateParams.filters;
    const currentStates: AssignmentState[] = this.$stateParams.states;

    if (this.isFilterChange() || this.isStateChange()) {
      this.reloadState({ filters: currentFilters, states: currentStates });
    }
  }
}
