import React, { useLayoutEffect, useRef } from "react";
import { colors, fonts } from "../theme";
import Tooltip from "../components/tooltip";

const ProficiencyProfile = (props) => {
  const { onWidth, profile } = props;
  const ref = useRef();

  useLayoutEffect(() => {
    if (ref.current) {
      if (onWidth) {
        onWidth(ref.current.offsetWidth);
      }
    }
  }, [profile]);

  return (
    <div ref={ref} style={{ display: "flex", maxWidth: "180px" }}>
      {profile?.map((p) => (
        <Tooltip title={p.grade.label}>
          <div
            style={{
              width: "30px",
              justifyContent: "center",
              alignItems: "center",
              border: "0px solid",
              display: "flex",
            }}
          >
            <span
              style={{
                ...fonts.SMALL,
                fontWeight: p.quantity ? 600 : 400,
                color: p.quantity ? p.grade.color : colors.borderColorDark,
              }}
            >
              {p.quantity}
              {profile.length < 8 ? `/${profile.total}` : ""}
            </span>
          </div>
        </Tooltip>
      ))}
    </div>
  );
};
export default ProficiencyProfile;
