import { Events } from '../../../common/global-events.ts';

export const GradeEditable = {
  bindings: {
    grade: '<',
    gradeSystem: '<',
    readOnly: '<',
    isTargetGrade: '<',
    mixpanelUpdateEvent: '@',
    onGradeUpdate: '&',
    onGradeChange: '&',
  },
  controller: class {
    constructor($scope, $timeout, $element, Mixpanel) {
      'ngInject';

      this.$scope = $scope;
      this.$timeout = $timeout;
      this.$element = $element;
      this.Mixpanel = Mixpanel;
      this.isTargetGrade = false;
      this.targetGradeColor = '#03A9F4';
      this.slider = { value: -1 };
    }

    $onInit() {
      this.$element
        .off('.GradeEditableAnimation')
        .on('mouseenter.GradeEditableAnimation', () => this.$timeout(() => {
          this.sliderOptions = { ...this.sliderOptions, disableAnimation: false };
        }))
        .on('mouseleave.GradeEditableAnimation', () => this.$timeout(() => {
          this.sliderOptions = { ...this.sliderOptions, disableAnimation: true };
        }));

      // find correct grade
      if (this.grade) {
        this.slider.value = this.gradeSystem.grades.findIndex(grade => grade.id === this.grade.id);
      }

      if (this.isTargetGrade) {
        this.gradeSystem = angular.copy(this.gradeSystem);
        const lastColor = this.gradeSystem.grades[this.gradeSystem.grades.length - 1].color;

        this.gradeSystem.grades.forEach((grade) => {
          if (grade.color === lastColor) {
            // eslint-disable-next-line no-param-reassign
            grade.color = this.targetGradeColor;
          }
        });
      }

      const ticksArray = [-1, ...this.gradeSystem.grades.map((value, index) => index)];

      this.sliderOptions = {
        ticksArray,
        enforceStep: true,
        floor: -1,
        showSelectionBar: true,
        showTicks: true,
        showTicksValues: true,
        ceil: this.gradeSystem.grades.length - 1,
        readOnly: this.readOnly,
        interval: 1,
        disableAnimation: false,
        getSelectionBarColor: (value) => {
          if (value === -1) {
            return '#9E9E9E';
          }
          return this.gradeSystem.grades[value].color;
        },
        getPointerColor: (value) => {
          if (value === -1) {
            return '#9E9E9E';
          }
          return this.gradeSystem.grades[value].color;
        },
        translate: (value, sliderId, model) => {
          if (value === -1) {
            return '';
          }
          return model === 'model' ?
            this.gradeSystem.grades[value].circle_label :
            this.gradeSystem.grades[value].label;
        },
        onStart: () => {
          this.isActive = true;
        },
        onChange: () => {
          this.onGradeChange({ grade: this.getGrade() });
        },
        onEnd: () => {
          this.isActive = false;
          this.onGradeUpdate({ grade: this.getGrade() });
          this.Mixpanel.track(this.mixpanelUpdateEvent);
        },
      };
    }

    $postLink() {
      this.$timeout(() => this.$scope.$broadcast(Events.RZ_SLIDER_FORCE_RENDER), 0)
        .then(() => {
          this.$timeout(() => this.$scope.$broadcast(Events.RZ_SLIDER_FORCE_RENDER), 50);
        });
    }

    $onDestroy() {
      this.$element.off('.GradeEditableAnimation');
    }

    getGrade() {
      if (this.slider.value !== -1) {
        return this.gradeSystem.grades[this.slider.value];
      }
      return null;
    }
  },
  template: `
    <rzslider
      ng-if="$ctrl.sliderOptions"
      ng-class="{
        'sb-course-assessment__slider--enabled-animations': !$ctrl.sliderOptions.disableAnimation,
        'sb-course-assessment__slider--hover': $ctrl.isHovered,
        'sb-course-assessment__slider--active': $ctrl.isActive,
        'sb-course-assessment__slider--readonly': $ctrl.readOnly,
      }"
      ng-mouseenter="$ctrl.isHovered = true"
      ng-mouseleave="$ctrl.isHovered = false"
      class="sb-course-assessment__slider animated-slider"
      rz-slider-model="$ctrl.slider.value"
      rz-slider-options="$ctrl.sliderOptions"></rzslider>
  `,
};
