import { TransitionAuth } from '../types/transitionAuth.type';

export abstract class TransitionHandler {
  deregisterOnBefore: Function = () => {};
  deregisterOnEnter: Function = () => {};
  deregisterOnError: Function = () => {};
  deregisterOnExit: Function = () => {};
  deregisterOnFinish: Function = () => {};
  deregisterOnRetain: Function = () => {};
  deregisterOnStart: Function = () => {};
  deregisterOnSuccess: Function = () => {};

  deregisterAll(): void {
    this.deregisterOnBefore();
    this.deregisterOnEnter();
    this.deregisterOnError();
    this.deregisterOnExit();
    this.deregisterOnFinish();
    this.deregisterOnRetain();
    this.deregisterOnStart();
    this.deregisterOnSuccess();
  }

  abstract getCriteria(): any;

  onBefore(transition: any): TransitionAuth {
    return true;
  }

  onStart(transition: any): TransitionAuth {
    return true;
  }

  onExit(transition: any): TransitionAuth {
    return true;
  }

  onRetain(transition: any): TransitionAuth {
    return true;
  }

  onEnter(transition: any): TransitionAuth {
    return true;
  }

  onFinish(transition: any): TransitionAuth {
    return true;
  }

  onSuccess(transition: any): TransitionAuth {
    return true;
  }

  onErro(transition: any): TransitionAuth {
    return true;
  }
}
