import { Events } from '../../common/global-events.ts';

export class CourseProgress {
  constructor($rootScope, $transitions) {
    'ngInject';

    this.$rootScope = $rootScope;
    this.$transitions = $transitions;

    this.initListeners();
  }

  initListeners() {
    this.lastState = null;

    this.$transitions.onSuccess({
      to: state => /^(teacher|student)-course-progress\.(no-student-selected|details|objective-details)\.?/.test(state.name),
    }, ($transition$) => {
      const toState = $transition$.$to();
      const params = angular.copy($transition$.router.stateService.params);
      this.lastState = {
        name: toState.name,
        params,
      };
    });

    this.$rootScope.$on(
      Events.CLASS_CHANGED, () => {
        this.lastState = null;
      },
    );
  }

  get hasLastStateSaved() {
    return !!this.lastState;
  }

  get lastStateSaved() {
    return this.lastState;
  }
}
