import { IPromise } from 'angular';
import { Service } from '../../../utils/decorators/service.decorator';
import { FetchData } from '../../../utils/services/fetchData.service';
import { RequestType } from '../../enums/requestType.enum';
import { IUserSetupRequestBody } from '../../models/userSetupRequestBody.model';
import { IUserSetupResponse } from '../../models/userSetupResponse.model';
import SharedServicesModule from '../services.module';

@Service({
  module: SharedServicesModule,
  name: 'UserSetupService',
})
export class UserSetupService {
  constructor(private FetchData: FetchData) {
    'ngInject';
  }

  public postUserSetup(userSetupRequestBody: IUserSetupRequestBody): IPromise<IUserSetupResponse> {
    return this.FetchData
      .sendAPIRequest('/schools/user_setup/', RequestType.POST, userSetupRequestBody);
  }
}
