import { Service } from '../../../utils/decorators/service.decorator';
import { ListViewType } from '../../enums/listViewType.enum';
import { IListViewMenuItem } from '../../models/listViewMenuItem.model';
import SharedServicesModule, { ILocalStorageService, IUserInfoService } from '../services.module';

export interface IListViewMenuService {
  getListViewMenu(storageId: string): IListViewMenuItem[];
  saveListViewMenu(storageId: string, listViewMenu: IListViewMenuItem[]): void;
}

@Service({
  module: SharedServicesModule,
  name: 'ListViewMenuService',
})
export class ListViewMenuService implements IListViewMenuService {
  private readonly userId: string;
  private readonly defaultMenuItems: IListViewMenuItem[];

  constructor(private LocalStorageService: ILocalStorageService, UserInfo: IUserInfoService) {
    'ngInject';

    this.userId = UserInfo.id;
    this.defaultMenuItems = [
      {
        id: ListViewType.TITLE_ONLY,
        label: 'teacherCurriculumObjectivesToolbarMenuItemTitleOnly',
        checked: false,
        eventArgs: { view: ListViewType.TITLE_ONLY },
      },
      {
        id: ListViewType.TITLE_AND_DESCRIPTION,
        label: 'teacherCurriculumObjectivesToolbarMenuItemTitleDescription',
        checked: true,
        eventArgs: { view: ListViewType.TITLE_AND_DESCRIPTION },
      },
      {
        id: ListViewType.DESCRIPTION_ONLY,
        label: 'teacherCurriculumObjectivesToolbarMenuItemDescriptionOnly',
        checked: false,
        eventArgs: { view: ListViewType.DESCRIPTION_ONLY },
      },
    ];
  }

  public getListViewMenu(storageId: string): IListViewMenuItem[] {
    const key: string = this.buildStorageKey(storageId);

    if (this.LocalStorageService.exists(key)) {
      return this.validateCheckedState(this.restoreListViewMenu(storageId));
    }

    return this.copyDefaultListViewMenu();
  }

  public saveListViewMenu(storageId: string, listViewMenu: IListViewMenuItem[]): void {
    const key: string = this.buildStorageKey(storageId);
    const listViewMenuItem: IListViewMenuItem = listViewMenu
      .find((listViewMenuItem: IListViewMenuItem): boolean => listViewMenuItem.checked);

    this.LocalStorageService.set(key, `${listViewMenuItem.id}`);
  }

  private copyDefaultListViewMenu(): IListViewMenuItem[] {
    return this.defaultMenuItems
      .reduce((acc: IListViewMenuItem[], listViewMenuItem: IListViewMenuItem): IListViewMenuItem[] => {
        const listViewMenuItemCopy: IListViewMenuItem = Object.assign({}, listViewMenuItem);

        listViewMenuItemCopy.eventArgs = Object.assign({}, listViewMenuItem.eventArgs);
        acc.push(listViewMenuItemCopy);

        return acc;
      }, []);
  }

  private restoreListViewMenu(storageId: string): IListViewMenuItem[] {
    const key: string = this.buildStorageKey(storageId);
    const lastSelectedId: string = `${this.LocalStorageService.get(key)}`;
    const listViewMenuItems: IListViewMenuItem[] = this.copyDefaultListViewMenu();

    listViewMenuItems.forEach((listViewMenuItem: IListViewMenuItem): void => {
      listViewMenuItem.checked = `${listViewMenuItem.id}` === lastSelectedId;
    });

    return listViewMenuItems;
  }

  private validateCheckedState(listViewMenu: IListViewMenuItem[]): IListViewMenuItem[] {
    const allCheckedItems: IListViewMenuItem[] = listViewMenu
      .filter((listViewMenuItem: IListViewMenuItem): boolean => listViewMenuItem.checked);

    if (!allCheckedItems.length) {
      listViewMenu[0].checked = true;
    } else if (allCheckedItems.length > 1) {
      listViewMenu[0].checked = true;
      listViewMenu.slice(1).forEach((listViewMenuItem: IListViewMenuItem): void => {
        listViewMenuItem.checked = false;
      });
    }

    return listViewMenu;
  }

  private buildStorageKey(storageId: string): string {
    return `${this.userId}LastSelectedListView${storageId}`;
  }
}
