/*@ngInject*/
module.exports = () => {
  'ngInject';

  function link() {
    const allowedTags = ['p', 'div'];

    const backwardCompatibility = (element) => {
      const $prevElement = $(element).prev();

      return $prevElement.length && allowedTags.includes($prevElement[0].localName) ? $prevElement : $('<div>');
    };

    const getLastModifyTag = (element) => {
      const $lastModifyTag = $(element)
        .closest('.last-modify-parent')
        .find('.last-modify-tag');

      return $lastModifyTag.length ? $lastModifyTag : backwardCompatibility(element);
    };

    $(document).on('mouseenter', '.last-modified-trigger', (e) => {
      getLastModifyTag(e.currentTarget).addClass('visible-date');
    });

    $(document).on('mouseleave', '.last-modified-trigger', (e) => {
      getLastModifyTag(e.currentTarget).removeClass('visible-date');
    });
  }

  return {
    restrict: 'A',
    link,
  };
};
