/**
 * @module SlideToggle/Directive
 * @description
 *   Handle slide toggle mechanism.
 *   Attrs:
 *   <ul>
 *     <li>slide-toggle {Boolean} - Slide toggle state. If true then content is visible, if false it is invisible.</li>
 *   </ul>
 * @example
 *  <p ng-click="objective.open = !objective.open">Toggle</p>
 *  <div slide-toggle="objective.open">
 *    <p>Hide content</p>
 *  </div>
 */

export class SlideToggleDirective {
  static create($animate) {
    'ngInject';

    return new SlideToggleDirective($animate);
  }

  constructor($animate) {
    'ngInject';
    this.$animate = $animate;
    this.restrict = 'A';
    this.multiElement = true;
    this.animationRunner = null;
  }

  link($scope, element, attrs) {
    const unsubscribeAnimationRunner = () => {
      if (this.animationRunner) {
        this.$animate.cancel(this.animationRunner);
      }
    };

    const unsubscribe$watch = $scope.$watch(
      attrs.slideToggle,
      (value) => {
        this.animationRunner = this.$animate[value ? 'addClass' : 'removeClass'](
          element,
          SlideToggleAnimation.className,
        );
      },
    );

    $scope.$on('$destroy', () => {
      unsubscribe$watch();
      unsubscribeAnimationRunner();
    });
  }
}
