import { IRootScopeService } from 'angular';
import { StateService } from '@uirouter/core';
import { Events } from '../../../../common/global-events';
import { UnsubscribableComponent } from '../../../../shared/components/abstracts/unsubscribable.component';
import { Component } from '../../../../utils/decorators/component.decorator';
import { Input } from '../../../../utils/decorators/input.decorator';
import StudentCourseManagementModule from '../studentCourseManagement.module';
import style from './cmStudentCoursesList.scss';
import template from './cmStudentCoursesList.tpl.html';

@Component({
  module: StudentCourseManagementModule,
  selector: 'cm-student-courses-list',
  template: template,
  style: style,
})
export class CmStudentCoursesListComponent extends UnsubscribableComponent {
  @Input()
  public courses: any[];

  public selectedCourse: any;

  constructor(private $rootScope: IRootScopeService, private $state: StateService) {
    'ngInject';
    super();

    this.courses = [];
    this.selectedCourse = null;
  }

  public $onInit(): void {
    this.$$unsubscribes.push(
      this.$rootScope.$on(Events.CLOSE_COURSE_DETAILS_VIEW, () => (this.selectedCourse = null)),
    );
  }

  public navigateToCourseDetails(course: any): void {
    this.selectedCourse = course;
    this.$state.go(
      'studentCourseManagement.details.learningObjectives', { course },
    );
  }
}
