export class UserSetupDialog {
  static DEFAULT_CONFIG = {
    bindToController: true,
    controller($mdDialog) {
      'ngInject';

      this.$mdDialog = $mdDialog;
    },
    controllerAs: 'dialog',
    parent: angular.element(document.body),
  };
  static TEMPLATE_PATH = 'userSetup/commons/dialogs';

  constructor($mdDialog) {
    'ngInject';

    this.$mdDialog = $mdDialog;
  }

  showSaveInfo() {
    const config = this.buildDialogConfig({
      templateUrl: `${UserSetupDialog.TEMPLATE_PATH}/saveInfo.html`,
      parent: angular.element('[ui-view="sideContent"]'),
    });

    this.show(config);
  }

  showWhatNowInfo() {
    const config = this.buildDialogConfig({
      templateUrl: `${UserSetupDialog.TEMPLATE_PATH}/whatNowInfo.html`,
    });

    this.show(config);
  }

  show(config) {
    this.$mdDialog.show(config);
  }

  hide() {
    this.$mdDialog.hide();
  }

  buildDialogConfig(config = {}) {
    return angular.extend({}, UserSetupDialog.DEFAULT_CONFIG, config);
  }
}
