import { IRootScopeService } from 'angular';
import { StateService } from '@uirouter/core';
import { Events } from '../../../common/global-events';
import { UnsubscribableComponent } from '../../../shared/components/abstracts/unsubscribable.component';
import { IAssignmentGroupedResponse } from '../../../shared/models/assignmentGroupedResponse.model';
import { Component } from '../../../utils/decorators/component.decorator';
import { Input } from '../../../utils/decorators/input.decorator';
import TeacherAssignmentsModule from '../teacherAssignments.module';
import style from './teacherAssignmentsPublished.scss';
import template from './teacherAssignmentsPublished.tpl.html';

@Component({
  module: TeacherAssignmentsModule,
  selector: 'teacher-assignments-published',
  template: template,
  style: style,
})
export class TeacherAssignmentsPublishedComponent extends UnsubscribableComponent {
  @Input()
  public assignmentsGroup: IAssignmentGroupedResponse;

  constructor(private $rootScope: IRootScopeService, private $state: StateService) {
    'ngInject';

    super();
    this.assignmentsGroup = {
      assessable: [],
      not_assessable: []
    };
  }

  public $onInit(): void {
    this.$$unsubscribes.push(this.$rootScope
      .$on(Events.UPDATED_ASSIGNMENTS, () => this.$state.reload()));
  }
}
