import { IconsPath } from '../../enums/iconsPath.enum';

export const IconURL = (): (iconName?: string) => string =>
  (iconName: string = ''): string => {
    const normalizeIconName: string  = `${iconName.trim()}`;
    const iconPath: string = IconsPath[normalizeIconName];

    return iconPath ? chrome.runtime.getURL(`${iconPath}`) : '';
  };
