import { IPromise } from 'angular';
import { StateParams } from '@uirouter/core';
import { StateProvider } from '@uirouter/angularjs';

export const routerConfig: Function = ($stateProvider: StateProvider): void => {
  'ngInject';

  $stateProvider
    .state('studentCourseManagement', {
      component: 'studentCourseManagement',
      resolve: {
        coursesDefinitions: (FetchData: any, Path: any): IPromise<any[]> => {
          'ngInject';

          return FetchData.v3.getCourseDefinitions(Path.getClassroomId());
        },
      },
    })
    .state('studentCourseManagement.details', {
      params: { course: null },
      resolve: {
        course: ($stateParams: StateParams, FetchData: any, Path: any): IPromise<any> => {
          'ngInject';

          return FetchData.v3.getCourseDefinition(Path.getClassroomId(), $stateParams.course.id);
        },
      },
      views: {
        'extended@': {
          component: 'cmStudentCourseDetails',
        },
      },
    })
    .state('studentCourseManagement.details.learningObjectives', {
      resolve: {
        objectives: (FetchData: any, course: any): IPromise<any[]> => {
          'ngInject';

          return FetchData.v3.getObjectives(course);
        },
      },
      views: {
        'details@studentCourseManagement.details': {
          component: 'cmStudentObjectives',
        },
      },
    });
};
