import {EntityState, HashMap, ID, Order} from '@datorama/akita';
import {Assessment} from '../assignment/assessments/state';
import {AssessmentStatus} from '../../../shared/models/assessment.model';

export type AssignmentSortFn = (assignment: Assignment) => number;

export enum AssignmentType {
  STANDARD_ASSIGNMENT = 'STANDARD_ASSIGNMENT',
  WITHOUT_ASSESSMENT_CRITERIAS = 'WITHOUT_ASSESSMENT_CRITERIAS',
  WORK_UNIT = 'WORK_UNIT',
  EMPTY = '',
}

export enum AssignmentState {
  DELETED = 'DELETED',
  DRAFT = 'DRAFT',
  PUBLISHED = 'PUBLISHED',
}

/**
 * @deprecated
 * Instead of this use AssignmentType
 */
export enum AssignmentWorktype {
  ASSIGNMENT = 'ASSIGNMENT',
  SHORT_ANSWER_QUESTION = 'SHORT_ANSWER_QUESTION',
}

export enum AssignmentSortBy {
  CREATED = 'CREATED',
  LAST_CHANGED = 'LAST_CHANGED',
  TITLE = 'TITLE',
  TYPE = 'TYPE',
}

export interface AssignmentSimple {
  google_id?: ID;
  id: ID;
  state: AssignmentState;
  title: string;
  worktype: AssignmentWorktype;
}

export interface Assignment extends AssignmentSimple {
  alternate_link_id: ID;
  assessment_last_changed: string | number;
  assessments: (Assessment & ID)[];
  assignment_created: string | number;
  assignment_not_assessable: boolean;
  assignment_type: AssignmentType;
  assignments_for_topic: (Assignment & ID)[];
  classroom_name: string;
  is_topic: boolean;
}

export interface AssignmentsResponse {
  without_topic: Assignment[];
  topics: Assignment[];
}

export interface NormalizeAssignmentsResponse {
  assignments?: HashMap<Assignment>;
  assessments?: HashMap<Assessment>;
  assignmentsForTopic?: HashMap<Assignment>;
}

export interface AssignmentMenuItem {
  label: string;
}

export interface AssignmentFilter {
  state: AssignmentState[];
  type: AssignmentType[];
}

export interface AssignmentFilterItem extends AssignmentMenuItem {
  state?: AssignmentState;
  type?: AssignmentType;
}

export interface AssignmentSort {
  by: keyof Assignment;
  order: Order;
}

export interface AssignmentSortItem extends AssignmentSort, AssignmentMenuItem {
}

export interface AssignmentUI {
  isExpanded: boolean;
}

export interface AssignmentsUI {
  filters: AssignmentFilter;
  sort: AssignmentSort;
}

export function createAssignmentUI(params: Partial<AssignmentUI>) {
  return {
    isExpanded: false,
    ...params
  } as AssignmentUI;
}

export function createAssignmentsUI(params?: Partial<AssignmentsUI>) {
  return {
    filters: {
      state: [AssignmentState.PUBLISHED, AssignmentState.DRAFT],
      type: [
        AssignmentType.STANDARD_ASSIGNMENT,
        AssignmentType.WITHOUT_ASSESSMENT_CRITERIAS,
        AssignmentType.WORK_UNIT,
        AssignmentType.EMPTY,
      ]
    },
    sort: {
      by: 'assignment_type',
      order: Order.ASC,
    },
    ...params
  } as AssignmentsUI;
}

export function createAssignments(params?: Partial<EntityState<Assignment>>) {
  return {
    ...params,
  } as EntityState<Assignment>;
}

export function createAssignment(params: Partial<Assignment>) {
  return {
    ...params
  } as Assignment;
}
