/**
 * Represents a SingleCheckbox.
 * @constructor
 */
function SingleCheckbox() {}

/**
 * Handle click on single checkbox - at the end function trigger checkboxCheckAll
 * @param {Object} singleItem Single item
 * @param {Object} group Single group
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
SingleCheckbox.prototype.checkboxSingle = function(singleItem, group, editMultiple) {
  singleItem.checked = !singleItem.checked;

  this.checkboxCheckAll(group, editMultiple);
};

/**
 * Handle click on all checkbox which check or uncheck all checkboxes
 * @param {Array} group Single group
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
SingleCheckbox.prototype.checkboxAll = function(group, editMultiple) {
  editMultiple.checkboxChecked = !editMultiple.checkboxChecked;

  if (editMultiple.checkboxChecked) {
    group.forEach(function(student) {
      student.checked = true;
    });
  } else {
    group.forEach(function(student) {
      student.checked = false;
    });
  }
};

/**
 * Check if all checkboxes are checked or unchecked
 * @param {Array} group Single group
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
SingleCheckbox.prototype.checkboxCheckAll = function(group, editMultiple) {
  var allCheckboxChecked = group.every(function (elem, i, arr) {
    return elem.checked;
  });

  if (allCheckboxChecked) {
    editMultiple.checkboxChecked = true;
  } else {
    editMultiple.checkboxChecked = false;
  }
};

/**
 * Check if any checkboxes are checked
 * @param {Array} group Single group
 */
SingleCheckbox.prototype.checkboxCheckIfAny = function(group) {
  var anyCheckboxChecked = false;

  anyCheckboxChecked = group.some(function(student) {
    return student.checked === true;
  });

  return anyCheckboxChecked;
};

/**
 * Clear all checkboxes
 * @param {Array} group Single group
 * @param {Object} editMultiple Object which keep information about Edit Multiple
 */
SingleCheckbox.prototype.clearCheckbox = function(group, editMultiple) {
  editMultiple.checkboxChecked = false;

  group.forEach(function(student) {
    student.checked = false;
  });
};

module.exports = SingleCheckbox;
